import React from 'react';
import { ReactComponent as DownIcon } from 'assets/img/icons/arrow-down.svg'
import { ReactComponent as UpIcon } from 'assets/img/icons/arrow-up.svg'

import './Resizer.scss';

class Resizer extends React.Component {
  render() {
    return (
      <React.Fragment>
        {/* <h3 className="resize-OPB-title">Resize block</h3> */}
        <div className="resize-OPB-container">
          { 
            this.props.show.h && 
            <div className="resize-OPB-box">
              <h4 className="resize-OPB-title">Height</h4>
              <div className="resize-OPB-editor">
                <button className="dope-button btn btn-primary" onClick={e=>this.props.onResizePick(e,1,0)} value={1}>
                  <UpIcon className="btn-icon"/>
                </button>
                <button className="dope-button btn btn-primary" onClick={e=>this.props.onResizePick(e,1,0)} value={-1}>
                  <DownIcon className="btn-icon"/>
                </button>         
              </div>
            </div>
          }
          { 
            this.props.show.w && 
            <div className="resize-OPB-box">
              <h4 className="resize-OPB-title">Width</h4>
              <div className="resize-OPB-editor">
                <button className="dope-button btn btn-primary" onClick={e=>this.props.onResizePick(e,0,1)} value={1}>
                  <UpIcon className="btn-icon"/>
                </button>
                <button className="dope-button btn btn-primary" onClick={e=>this.props.onResizePick(e,0,1)} value={-1}>
                  <DownIcon className="btn-icon"/>
                </button>         
              </div>
            </div>
          }
        </div>
      </React.Fragment>
    );
  }
}


export default Resizer;
