import React from 'react';
import { ModalFooter } from 'reactstrap';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModal } from 'components/DopeModal/DopeModal';
import './DeletePostcardModal.scss';

export default function DeletePostcardModal(props) {
  return (
    <div>
      <DopeModal
        className='save-template-modal'
        isOpen={props.open}
      >
        <DopeModalHeader>Delete Template</DopeModalHeader>
        <DopeModalBody>
          Delete this template?
        </DopeModalBody>
        <ModalFooter>
          <DopeButton color='white' 
          onClick={props.toggle}
          >
            Cancel
          </DopeButton>
          <DopeButton
            color='primary'
            onClick={() => props.deleteMyPostcard(props.id, {page: props.page, limit: 8, status: 'saved'}, props.images)}
          >
            Delete
          </DopeButton>
        </ModalFooter>
      </DopeModal>
    </div>
  );
}
