import {
  GET_CONTACTS,
  GET_CONTACTS_SUCCESS,
  GET_CONTACTS_FAILED,
  // GET_FILTERS_DATA,
  GET_FILTERS_DATA_SUCCESS,
  // GET_FILTERS_DATA_FAILED,
  DELETE_CONTACTS_SUCCESS,
  DELETE_ONE_CONTACT_SUCCESS,
  ON_CHANGE_NEW_CONTACT,
  ON_CHANGE_EDIT_CONTACT,
  GET_CONTACTS_ALL_LISTS_REQUEST_SUCCESS,
  ADD_ERRORS_NEW_CONTACT,
  ADD_ERRORS_EDIT_CONTACT,
  RESET_NEW_CONTACT_FORM,
  RESET_EDIT_CONTACT_FORM,
  TOGGLE_ADD_CONTACT_MODAL,
  TOGGLE_EDIT_CONTACT_MODAL,
  SET_EDIT_CONTACT_DATA,
  EDIT_CONTACT_REQUEST_SUCCESS,
  CHANGE_FILTERS,
  // GET_ALL_CONTACTS,
  GET_ALL_CONTACTS_SUCCESS,
  // GET_ALL_CONTACTS_FAILED,
  ADD_NEW_CONTACT_REQUEST_SUCCESS,
  RESET_CONTACTS_PAGE,
} from 'actions/contacts';

const initialState = {
  isLoading: false,
  data: {},  
  showAddContactModal: false,
  showEditContactModal: false,
  allLists: null,
  newContactData: {
    firstName: '',
    lastName: '',
    address1: '',
    address2: '',
    city: '',
    state: '',
    zip: '',
    listRequestID: '',
    name: '',
  },
  editContactData: {},
  editContactError: {},
  newContactError: {},
  filtersData: {}, //for data from backend to fill the filter's options
  filters: {
    lists: [],
    states: [],
    cities: [],
    zipCodes: [],
    startDate: '',
    endData: '',
    validation: 'allContacts',
    isFiltered: false,
  },
  totalSizeContacts: null,
  listContactIds: [],  
  updFlag: false,
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case GET_CONTACTS:
      return {
        ...state,
        isLoading: true,
      };
    case GET_CONTACTS_SUCCESS:  
      return {
        ...state,
        data: action.data,
        listContactIds: action.data.listContactIds,
        isLoading: false,
      };
    case GET_CONTACTS_FAILED:
      return {
        ...state,
        isLoading: false,
      };
    // case GET_FILTERS_DATA:
    //   return {
    //     ...state,
    //     isLoading: true,
    //   };
    case GET_FILTERS_DATA_SUCCESS:
      return {
        ...state,
        filtersData: action.data,                
      };
    // case GET_FILTERS_DATA_FAILED:
    //   return {
    //     ...state,        
    //   };
    case GET_ALL_CONTACTS_SUCCESS:         
      return {
        ...state,        
        totalSizeContacts: action.data.totalSize,           
      };

    case DELETE_CONTACTS_SUCCESS:
      const updatedContacts =
        state.data.data &&
        state.data.data.filter(
          contact => !action.data.ids.some(id => contact._id === id),
        );
      return {
        ...state,
        data: {
          ...state.data,
          data: updatedContacts,
          totalSize: state.data.totalSize - action.data.ids.length,          
        },
        updFlag: !state.updFlag,
      };
    case DELETE_ONE_CONTACT_SUCCESS:
      const newContacts =
        state.data.data &&
        state.data.data.filter(contact => action.data.id !== contact._id);
      return {
        ...state,
        data: {
          ...state.data,
          data: newContacts,
          totalSize: state.data.totalSize - 1,          
        },
        updFlag: !state.updFlag,
      };
    case ON_CHANGE_NEW_CONTACT:
      return {
        ...state,
        newContactData: {
          ...state.newContactData,
          [action.key]: action.value,
        },
      };
    case ON_CHANGE_EDIT_CONTACT:
      return {
        ...state,
        editContactData: {
          ...state.editContactData,
          [action.key]: action.value,
        },
      };
    case GET_CONTACTS_ALL_LISTS_REQUEST_SUCCESS:
      return {
        ...state,
        allLists: action.data.data,
      };
    case ADD_ERRORS_NEW_CONTACT:
      return {
        ...state,
        newContactError: action.data,
      };
    case ADD_ERRORS_EDIT_CONTACT:
      return {
        ...state,
        editContactError: action.data,
      };
    case RESET_NEW_CONTACT_FORM:
      return {
        ...state,
        newContactData: initialState.newContactData,
        newContactError: initialState.newContactError,
      };
    case RESET_EDIT_CONTACT_FORM:
      return {
        ...state,
        editContactData: initialState.editContactData,
        editContactError: initialState.editContactError,
      };
    case TOGGLE_ADD_CONTACT_MODAL:      
      return {
        ...state,
        showAddContactModal: action.data,
      };
    case TOGGLE_EDIT_CONTACT_MODAL:
      return {
        ...state,
        showEditContactModal: action.data,
      };
    case SET_EDIT_CONTACT_DATA:
      return {
        ...state,
        editContactData: action.data,
      };
    case EDIT_CONTACT_REQUEST_SUCCESS:
      const editedContacts = () => {
        const editedContact = action.data;
        const data = [...state.data.data];
        const res = data.map(item => {
          if (item._id === editedContact._id) {
            item = { ...item, ...editedContact };
          }
          return item;
        });
        return res
      }

      return {
        ...state,
        data: {
          ...state.data,
          data: editedContacts(),          
        },
        updFlag: !state.updFlag,
      };
    case CHANGE_FILTERS:      
      return {
        ...state,
        filters: action.data,
      };
    case ADD_NEW_CONTACT_REQUEST_SUCCESS:      
      return {
        ...state,        
        totalSizeContacts: state.totalSizeContacts + action.data.quantity,
        updFlag: !state.updFlag,  
      }; 
    case RESET_CONTACTS_PAGE:
      return {
        ...initialState,
        // Reset filters or save???
        // filters: state.filters,
      };     
    default:
      return state;
  }
}
