import { all, call, put, takeLatest } from 'redux-saga/effects'

import * as contactsActions from 'actions/contacts';
import * as generalActions from 'actions/general';
import { toast } from 'react-toastify';
import { 
  getContactsApi, 
  getFiltersDataApi,
  deleteContactsApi, 
  deleteOneContactApi, 
  createListApi, 
  getAddContactListsApi,
  addNewContactApi,
  editContactApi,
} from '../requests/contacts-requests'

export function* getContacts(action) {    
  try {
    const response = yield call(getContactsApi, action.data)    
    yield put({ type: contactsActions.GET_CONTACTS_SUCCESS, data: response.data })
  } catch(error) {
    yield put({ type: contactsActions.GET_CONTACTS_FAILED, error })
  }
}

export function* getFiltersData(action) {    
  try {
    const response = yield call(getFiltersDataApi, action.data)
    yield put({ type: contactsActions.GET_FILTERS_DATA_SUCCESS, data: response.data })
  } catch(error) {
    // yield put({ type: contactsActions.GET_FILTERS_DATA_FAILED, error })
    toast.error(`Can't get Lists/City/ZIP info.`)
  }
}

export function* getAllContacts(action) {  
  try {
    const response = yield call(getContactsApi, action.data)
    // console.log("getAllContacts response", response);
    yield put({ type: contactsActions.GET_ALL_CONTACTS_SUCCESS, data: response.data })
    // TODO   FILTERED ARRAY OF ID Contacts
    // const response2 = yield call(getContactsApi, {...action.data,limit:response.data.totalSize}) 
    // console.log("getAllContacts response2",response2);   
    // yield put({ type: contactsActions.GET_ALL_CONTACTS_SUCCESS, data: response2.data})
  } catch(e) {
    yield put({ type: contactsActions.GET_ALL_CONTACTS_FAILED, error: e })
  }
}

export function* deleteContacts(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    const response = yield call(deleteContactsApi, action.data)
    yield put({ type: contactsActions.DELETE_CONTACTS_SUCCESS, data: { message: response.data, ids: action.data.ids } })    
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.success(`Contacts has been successfully deleted!`)
  } catch(error) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.success(`Error. Contacts wasn't deleted!`)
    yield put({ type: contactsActions.DELETE_CONTACTS_FAILED, error })
  }
}

export function* deleteOneContact(action) {  
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    const response = yield call(deleteOneContactApi, action.data)   
    yield put({ type: contactsActions.DELETE_ONE_CONTACT_SUCCESS, data: { message: response.data, id: action.data.id } })
    //TODO yield put({ type: contactsActions.GET_TOTAL_SIZE_CONTACTS, data: {account: action.data.account, page: 0, limit: 10} }) 
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.success(`Contact has been successfully deleted!`)
  } catch(error) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.success(`Error. Contact wasn't deleted!`)
    yield put({ type: contactsActions.DELETE_ONE_CONTACT_FAILED, error })
  }
}

export function* createList(action) {
  try {
    const response = yield call(createListApi, action.data)
    yield put({ type: contactsActions.CREATE_LIST_SUCCESS, data: response.data })
    toast.success(`List has been successfully created!`)
  } catch(error) {
    toast.error(`Error. List has not been created.`)
    yield put({ type: contactsActions.CREATE_LIST_FAILED, error })
  }
}

export function* getAddContactLists(action) {
  try {
    const response = yield call(getAddContactListsApi, action.data)
    yield put({ type: contactsActions.GET_CONTACTS_ALL_LISTS_REQUEST_SUCCESS, data: response.data })
  } catch(error) {
    yield put({ type: contactsActions.GET_CONTACTS_ALL_LISTS_REQUEST_FAILED, error })
  }
}

export function* addNewContact(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    yield call(addNewContactApi, action.data);    
    //don't need there call all contacts without filters
    // yield put({ type: contactsActions.GET_CONTACTS, data: {account: action.data.query.account, page: 0, limit: 10} })
    yield put({ type: contactsActions.ADD_NEW_CONTACT_REQUEST_SUCCESS, data: { quantity: 1 } })
    // TODO  ... += _id  to allContacts

    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    yield put({ type: contactsActions.TOGGLE_ADD_CONTACT_MODAL, data: false })
    yield put({ type: contactsActions.RESET_NEW_CONTACT_FORM })
    toast.success(`New contact was added.`)
  } catch(error) {
    if(error.response && error.response.status === 422) toast.error(`Contact with these fields firstName, lastName, address 1 already exist.`)
    else toast.error(`Error new contact wasn't add.`)
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
  }
}

export function* editContact(action) {  
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    const response = yield call(editContactApi, action.data);
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    yield put({ type: contactsActions.EDIT_CONTACT_REQUEST_SUCCESS, data: response.data.data })
    yield put({ type: contactsActions.TOGGLE_EDIT_CONTACT_MODAL, data: false })
    yield put({ type: contactsActions.RESET_EDIT_CONTACT_FORM })
    toast.success(`The contact was changed.`)
  } catch(error) {
    if(error.response && error.response.status === 422) 
    toast.error(`Contact with these fields firstName, lastName, address 1 already exist.`)
    else toast.error(`Error the contact wasn't changed.`)
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
  }
}

export default all([
  takeLatest(contactsActions.GET_CONTACTS, getContacts),
  takeLatest(contactsActions.GET_FILTERS_DATA, getFiltersData),
  takeLatest(contactsActions.DELETE_CONTACTS, deleteContacts),
  takeLatest(contactsActions.DELETE_ONE_CONTACT, deleteOneContact),
  takeLatest(contactsActions.CREATE_LIST, createList),
  takeLatest(contactsActions.GET_CONTACTS_ALL_LISTS_REQUEST, getAddContactLists),
  takeLatest(contactsActions.ADD_NEW_CONTACT_REQUEST, addNewContact),
  takeLatest(contactsActions.EDIT_CONTACT_REQUEST, editContact),
  takeLatest(contactsActions.GET_ALL_CONTACTS, getAllContacts),
])