import React from 'react'
import {
  Input,
  Label
} from 'reactstrap'
import classnames from 'classnames'
import InputMask from 'react-input-mask'

import './DopeInput.scss'

export const DopeInput = ({ id, title, mask, maskChar, error, supText, ...rest}) => {
  
  const classNames = classnames({ 'dope-input': true, 'form-control': true, 'error': error})
  const InputComponent = mask ? 
    <InputMask className={classNames} mask={mask} maskChar={maskChar} id={id} { ...rest} /> : 
    <Input  maxLength="40" id={id} { ...rest} className={`${classNames} ${rest.className}`}/>

  return (<>
    { title && <Label className='input-title' htmlFor={id}>{title} {supText && <sup>{supText}</sup>}</Label> }    
    { InputComponent }
    { error && <div className='validation-message'>{error}</div> }    
  </>)
}