import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useRouteMatch, useHistory } from 'react-router-dom';
import { pathOr } from 'ramda';
import moment from 'moment';

import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeTable } from 'components/DopeTable/DopeTable';
import { columnsFunc } from './config';
import { getPriceByCost } from 'utils/helper';

import { getCampaignsDataRequest, setNewCampaignData, setReportCampaignData} from 'actions/campaigns';

import './CampaignsList.scss';

export default function CampaignsList({ postcardPrice }) {
  const campaignsDataIsLoading = useSelector(
    state => state.campaigns.campaignsDataIsLoading,
  );
  const campaignsData = useSelector(state => state.campaigns.campaignsData);
  const currentAccount = useSelector(state => state.account.currentAccount);

  useEffect(() => {    
    dispatch(getCampaignsDataRequest({ account: currentAccount._id, page: 0, limit: 20}));    
    return () => {};
  }, [currentAccount]); 

  let { url } = useRouteMatch();
  let history = useHistory();
  const dispatch = useDispatch();

  const getCampaigns = (pagination = { page: 1, limit: 20 }) => {
    let data = {
      account: currentAccount && currentAccount._id,
      ...pagination,
      page: pagination.page - 1,
    };

    const campaignsDataSort = campaignsData.hasOwnProperty('sort')
      ? campaignsData.sort
      : null;
    const paginationSort = pagination.hasOwnProperty('sort')
      ? pagination.sort
      : null;

    if (campaignsDataSort && !paginationSort) data.sort = campaignsData.sort;

    dispatch(getCampaignsDataRequest(data));
  };

  const paginationOptions = {
    onPageChange: getCampaigns,
    page: campaignsData.page + 1,
    limit: 20,
    totalSize: campaignsData.total,
  };

  const onSort = (field, order) => {
    const boolOrder = order === 'desc' ? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 20, sort: newOrder };
    getCampaigns(pagination);
  };

  const calculatePrice = (qty, postageType) => {
    if (!qty) return '0.00';

    let pricePerOne = getPriceByCost(postcardPrice, qty);

    if (postageType && postageType === 'usps_first_class')
      pricePerOne = pricePerOne['first-class-price'];
    else pricePerOne = pricePerOne.price;

    return (pricePerOne * qty / 100).toFixed(2);
  };

  const editCampaign = campaign => {
    const verifiedContacts = pathOr(
      0,
      ['list', 'listProperties', 'verifiedContacts'],
      campaign,
    );

    dispatch(
      setNewCampaignData({
        verifiedContacts,
        list: campaign.list._id,
        name: campaign.name,
        postage: campaign.postage,
        campaign: campaign._id,
      }),
    );
    history.push(`${url}/${campaign._id}`);
  };

  const reportCampaign = campaign => { 
    // useParams(); used in RepportCampaign.jsx for taking id    
    dispatch(
      setReportCampaignData({        
        campaignId: campaign._id,
        campaignName: campaign.name, 
        listName: campaign.listName,
      }),      
    );        
    history.push(`${url}/report/${campaign._id}`);    
  };

  return (
    <div className='campaigns-list-container'>
      <div className='dope-page-title'>
        <h2>Campaigns</h2>
        <div>
          <DopeButton
            color='primary'
            onClick={() => history.push(`${url}/create`)}
          >
            New campaign
          </DopeButton>
        </div>
      </div>
      <div className='campaigns-list-content'>
        <div className='campaigns-list-header'>
          {campaignsData.total || 0} campaigns
        </div>
        <DopeTable
          classes='campaigns-list-table'
          keyField='_id'
          columns={columnsFunc(onSort, reportCampaign, editCampaign)}
          paginationOptions={paginationOptions}
          data={
            (campaignsData.data &&
              campaignsData.data
              .filter(item => (item.list))
              .map(item => {
                const contactsQty = pathOr(
                  0,
                  ['list', 'listProperties', 'verifiedContacts'],
                  item,
                );

                const nextEvent = item.nextEvent? moment(item.nextEvent).format('MMM DD, YYYY') : '-';
                // const dollarsign = '&dollar;';

                return {
                  ...item,
                  name: item.name,
                  listName: item.list.name,
                  contact: contactsQty,
                  nextEvent,
                  price: `$${calculatePrice(contactsQty, item.postage)}`,
                };
              })) ||
            []
          }
          loading={campaignsDataIsLoading}
        />
      </div>
    </div>
  );
}
