import React, {useEffect, useState} from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import {DopeModalBody} from "components/DopeModal/DopeModalBody/DopeModalBody";
import 'react-input-range/lib/css/index.css'
import './NeighborhoodsFilter.scss'
import Select from "react-select";
import {useDispatch, useSelector} from "react-redux";
import {changeNeighborsFilters} from "actions/neighborhoodBlitz";
import { equals } from 'ramda';


export default function NeighborhoodsFilter({isOpen, toggle, getFilteredNeighbors}) {
  const dispatch = useDispatch();

  const filters = useSelector(state => state.neighborhoodBlitz.filters);
  const [state, setState] = useState(filters);
  const neighborhoodOptions = useSelector(state => state.dbConfigs.configs).find(item => item.name === "neighborhoodOptions").value;

  useEffect(() => {
    setState(filters);
  },[filters]);

  const keeperOptions = ['Owner', 'Renter'];

  const closeFilter =() => {
    toggle();
    setState(filters);
  };

  const applyFilter = () => {
    if(!equals(state, filters)){
      const {isFiltered, ...fields} = state;
      let flag = Object.values(fields).some(item => item);
      dispatch(changeNeighborsFilters({...state, isFiltered: flag}));

      const query = {};
      if(state.homeKeeper) query.owner = state.homeKeeper === 'Owner';
      if(state.homeValue) query.homeValue = state.homeValue.value;
      if(state.rangeValue) query.rangeValue = state.rangeValue;

      toggle();
      getFilteredNeighbors(query);
    }
  };

  const clearFilter = () => {
    const {isFiltered, ...fields} = state;
    let flag = Object.values(fields).some(item => item);
    if(flag){
      dispatch(changeNeighborsFilters({
        rangeValue: 0,
        homeValue: '',
        homeKeeper: '',
        isFiltered: false
      }));
      toggle();
      getFilteredNeighbors({})
    }
  };

  return (
    <DopeModal
      isOpen={isOpen}
      centered
      toggle={closeFilter}
      className='neighborhoodsFilter'
    >
      <DopeModalHeader className='dope-modal-header' toggle={closeFilter}>Filters</DopeModalHeader>
      <DopeModalBody>
        <div className="filter-section">
          <h4>Home Keeper</h4>
          <div className="filter-keeper">
            {keeperOptions.map((item, i) =>
              <label key={i}>
                <input
                  type="radio"
                  name="keeper"
                  checked={item === state.homeKeeper}
                  onChange={() => setState({...state, homeKeeper: item})}
                />
                <span>{item}</span>
              </label>
            )}
          </div>
        </div>        
        <div className="filter-section">
          <h4>Home Value</h4>
          <Select
            className='dope-select'
            classNamePrefix='dope'
            placeholder='Value'
            options={neighborhoodOptions.HomeValue}
            menuPlacement="auto"
            value={state.homeValue}
            onChange={option => setState({...state, homeValue: option})}
          />
        </div>
      </DopeModalBody>
      <DopeModalFooter>
        <DopeButton color='primary' onClick={applyFilter}>Apply filters</DopeButton>
        <DopeButton color='white' onClick={clearFilter}>Clear</DopeButton>
      </DopeModalFooter>
    </DopeModal>
  );
}
