import React from 'react';
import { DopeTable } from 'components/DopeTable';
import moment from 'moment';
import {columns} from './config';
import './AxleDataTable.scss';

export default function AxleDataTable({usingData,getData,sortData,loading}) {

  const onPageChange = (pagination) => {
    getData({...pagination});
  };
  const onSort = (field, order)=>{    
    sortData({field,order});
  };
  return (    
    <DopeTable
      keyField='_id'
      data={usingData.data.map((item) => (                    
        {
          _id: item._id,
          user: item.data.name||'user',
          timestamp: moment(item.data.lastItem).format('MMM DD, YYYY'),
          count: item.data.count,
          queriesDopeDB: item.data.totals.find(item=>item.type==='db')?item.data.totals.find(item=>item.type==='db').count:0,
          queriesAxleAPIDB: item.data.totals.find(item=>item.type==='api')?item.data.totals.find(item=>item.type==='api').count:0,
          purchased: `$${(item.data.priceTotal/100).toFixed(2)}`
        }
      ))}
      paginationOptions={{
        page: usingData.page + 1,
        limit: usingData.limit,
        totalSize: usingData.totalSize,
        onPageChange,
      }}
      columns={columns(onSort)}
      loading={loading}                
    />
  );
}
