import React from 'react';
import { Row, Col } from 'reactstrap';
import { useSelector } from 'react-redux';
import {withRouter} from 'react-router-dom'
import { ReactComponent as PostcardIcon } from 'assets/img/icons/postcards.svg';
import { ReactComponent as YardSignIcon } from 'assets/img/icons/yard-sign.svg';
import { ReactComponent as PrintProductsIcon } from 'assets/img/icons/printer.svg';
import { ReactComponent as VoiceMailsIcon } from 'assets/img/icons/voice.svg';
import './OrderBlock.scss';

function OrderBlock(props) {
  const accessGroup = useSelector(state => state.general.accessGroup);

  const goOrderPostcards = () => {
    props.history.push('/order-postcards')
  }

  const goOrderYardSigns = () => {
    props.history.push('/order-yard-signs')
  }

  const listOfSevices = () => {     
      switch(accessGroup){
        case 1:
          return(
            <React.Fragment>
              <Col md={3}>
                <div className='order-block-item postcard-block' onClick={goOrderPostcards}>
                  <PostcardIcon />
                  <div> Order postcards</div>
                </div>
              </Col>
              <Col md={3}>
                <div className='order-block-item yard-sign-block' onClick={goOrderYardSigns}>
                  <div className="ico-container">
                    <YardSignIcon />
                  </div>
                  
                  <div> Order Yard Signs</div>
                </div>
              </Col>
              <Col md={3}>
                <div className='order-block-item print-block' title="This functionality for the next release">
                  <PrintProductsIcon />
                  <div>Print Products</div>
                </div>
              </Col>
              <Col md={3}>
                <div className='order-block-item voice-block' title="This functionality for the next release">
                  <VoiceMailsIcon />
                  <div>Voicemails</div>
                </div>
              </Col>
            </React.Fragment>
          )        
        case 2:
          return(
            <React.Fragment>
              <Col md={3}>
                <div className='order-block-item postcard-block' onClick={goOrderPostcards}>
                  <PostcardIcon />
                  <div> Order postcards</div>
                </div>
              </Col>              
              <Col md={3}>
                <div className='order-block-item print-block' title="This functionality for the next release">
                  <PrintProductsIcon />
                  <div>Print Products</div>
                </div>
              </Col>
              <Col md={3}>
                <div className='order-block-item voice-block' title="This functionality for the next release">
                  <VoiceMailsIcon />
                  <div>Voicemails</div>
                </div>
              </Col>
            </React.Fragment>
          )          
        default :
          return(
            <React.Fragment>
              <Col md={3}>
                <div className='order-block-item postcard-block' onClick={goOrderPostcards}>
                  <PostcardIcon />
                  <div> Order postcards</div>
                </div>
              </Col>
              <Col md={3}>
                <div className='order-block-item yard-sign-block' onClick={goOrderYardSigns}>
                  <div className="ico-container">
                    <YardSignIcon />
                  </div>
                  
                  <div> Order Yard Signs</div>
                </div>
              </Col>
              <Col md={3}>
                <div className='order-block-item print-block' title="This functionality for the next release">
                  <PrintProductsIcon />
                  <div>Print Products</div>
                </div>
              </Col>
              <Col md={3}>
                <div className='order-block-item voice-block' title="This functionality for the next release">
                  <VoiceMailsIcon />
                  <div>Voicemails</div>
                </div>
              </Col>
            </React.Fragment>
          )           
      }     
  };

  return (
    <Row className='order-block-container'>
      {listOfSevices()}
    </Row>
  );
}

export default withRouter(OrderBlock)
