import React from 'react';
import enhanceWithClickOutside from 'react-click-outside';
import styled from 'styled-components';
import {connect} from "react-redux";

const StyledInput = styled.input``;
const TextArea = styled.textarea``;

let fontSizes = [];
for (let i = 6; i <= 96; i++) fontSizes.push(i);

const getTag = (tag) => {
    switch (tag){
        case 'textarea':
            return TextArea;

        default:
            return StyledInput
    }
};

const ComponentGenerator = enhanceWithClickOutside(class extends React.Component {
    state = {
        data: null,
        clicked: false,
        doubleClicked: false
    }

    self = React.createRef();

    renderContent = (props, options) => {
        if (this.props.editableType === 'image') return (
            <img
                {...props}
                style={{ ...props.style, border: 'none' /*pointerEvents: 'none'*/ }}
                src={this.props.content}
                alt='order'
            />
        );
        const Tag = getTag(props.element);

        if (this.state.doubleClicked && props.id !== 'phone') return (
            <Tag
                {...props}
                style={{
                    outline: 'none',
                    background: 'none',
                    fontFamily: 'Open Sans',
                    ...props.style,
                    cursor: options.disableSignEditing ? 'default' : 'auto',
                    border: 'none'
                }}
                autoFocus
                value={this.props.content}
                onChange={(e) => options.disableSignEditing ? {} : this.props.onChange(this.props.prop, 'content', e.target.value)}
            />
        );
        return <div {...props}>{this.props.children}</div>;
    }

    render() {
        let style = { ...this.props.style };
        const isEditable = !this.props.disableSignEditing && this.props.type === 'editable';
        if (this.props.borders) {
            if (this.state.clicked && isEditable) {
                style = {
                    ...style,
                    borderStyle: 'solid',
                    outline: 'none',
                    cursor: 'pointer',
                    padding: 0,
                    margin: 0,
                    resize: 'none'
                }
            } else if (isEditable) {
                style = {
                    ...style,
                    outline: 'none',
                    cursor: 'pointer',
                    padding: 0,
                    margin: 0,
                    resize: 'none'
                };
            } else {
                style = {
                    ...style,
                    cursor: 'default',
                }
            }
        }
        let props = {
            style,
            ref: this.self,
            element: this.props.element,
            onClick: (e) => {
                if (!this.props.disableSignEditing && this.props.type === 'editable') {
                    e.stopPropagation();
                    if (!this.state.clicked && this.props.onSelect) this.props.onSelect(this.props, this.self.current.getBoundingClientRect());
                    this.setState({ doubleClicked: true, clicked: true });
                }
            },
            id: this.props.contentType || this.props.id
        };
        return <div>{this.renderContent(props, {
            disableSignEditing: this.props.disableSignEditing
        })}</div>;
    }
});

const EditorTree = (props) => {
    const { tree, options } = props;
    return (
        <ComponentGenerator {...tree} {...options}>
            {Array.isArray(tree.content) ?
                tree.content.map((elm, inc) => (
                    <EditorTree
                        key={options.key + inc}
                        tree={elm}
                        options={options}
                    />)
                ) : tree.content
            }
        </ComponentGenerator>
    );
}

class Builder extends React.Component {
    state = {
        searchResult: null,
        modifyContentProps: null,
        tempInputData: '',
        tempFontSize: 16,
        colorPickerMarginTop: 0
    }

    originalTemplate = null;

    recursiveSearch(node, key, cb) {
        if (node.prop === key) {
            cb(node);
        } else if (Array.isArray(node.content)) {
            for (let subNode of node.content) this.recursiveSearch(subNode, key, cb)
        }
    }

    recursiveWalk(node, cb) {
        cb(node);
        if (Array.isArray(node.content)) {
            for (let subNode of node.content) this.recursiveWalk(subNode, cb)
        }
    }

    asyncRecursiveSearch(node, key) {
        return new Promise((resolve, reject) => {
            this.recursiveSearch(node, key, (result) => resolve(result))
        });
    }

    _searchTree = function(aTree, fCompair, bGreedy){
        let aInnerTree = []; // will contain the inner children
        let oNode; // always the current node
        let aReturnNodes = []; // the nodes array which will returned

        // 1. loop through all root nodes so we don't touch the tree structure
        for(let keysTree in aTree) {
            aInnerTree.push(aTree[keysTree]);
        }
        while(aInnerTree.length > 0) {
            oNode = aInnerTree.pop();
            // check current node
            if( fCompair(oNode) ){
                aReturnNodes.push(oNode);
                if(!bGreedy){
                    return aReturnNodes;
                }
            } else { // if (node.children && node.children.length) {
                // find other objects, 1. check all properties of the node if they are arrays
                for(let keysNode in oNode){
                    // true if the property is an array
                    if(oNode[keysNode] instanceof Array){
                        // 2. push all array object to aInnerTree to search in those later
                        for (let i = 0; i < oNode[keysNode].length; i++) {
                            aInnerTree.push(oNode[keysNode][i]);
                        }
                    }
                }
            }
        }
        return aReturnNodes; // someone was greedy
    };

    onColorChange = (color_id, hex) => {
        let tree = JSON.parse(JSON.stringify(this.props.data.template));

        this.recursiveWalk(tree.content, (node) => {
            if (node.color_id === color_id) {
                if (node.style.background) {
                    node.style.background = hex;
                } else {
                    node.style.color = hex;
                }
            }
        });
        this.props.onDataChange(tree);
    }

    onDataChange = (key, value, prop = 'content') => {
        if(this.props.data.template) {
            let tree = JSON.parse(JSON.stringify(this.props.data.template));
            let searchResult = null;
            this.recursiveSearch(tree.content, key, (node) => {
                node[prop] = value;
                searchResult = node;
            });
            this.props.onDataChange(tree);
        }
    }


    onSelect = async (props, position) => {
        const isEditable = props.type === 'editable';
        this.setState({
            colorPickerMarginTop: position.top - 238,
            tempInputData: isEditable ? props.content : '',
            tempFontSize: isEditable && props.style && props.style.fontSize ? props.style.fontSize : 16,
            modifyContentProps: props,
            searchResult: await this.asyncRecursiveSearch(this.props.data.template.content, props.prop)
        });
    }


    resetColors = () => {
        this.props.data.template = {...this.originalTemplate};
        this.props.onDataChange({...this.originalTemplate});
        this.onDataChange('phoneNumber', this.props.data.trackingNumber || this.props.data.phoneNumber);
    };

    componentDidUpdate(prevProps) {
        if (!this.props.borders && prevProps.borders) this.props.onPreparationDone();

    }

    componentDidMount(prevProps) {
        this.onDataChange('phoneNumber', this.props.data.trackingNumber || this.props.data.phoneNumber);
        this.originalTemplate = {...this.props.data.template};
    }
    render() {
        if (!this.props.data || !this.props.data.template) return <div/>;
        const { content } = this.props.data.template;

        return (
            <div style={{ display: 'flex', justifyContent: 'center' }}>
                <div style={{
                    display: 'flex',
                    flex: 1,
                    background: 'white',
                    padding: 20,
                    borderRadius: 10,
                    maxWidth: '8.5in'
                }}>
                    <div
                        style={{ height: 'auto' }}
                        ref={this.props.htmlRef}
                        id={this.props.id}
                    >
                        <EditorTree tree={content} options={{
                            onSelect: this.onSelect,
                            key: 0,
                            borders: this.props.borders,
                            onChange: (key, prop, data) => this.onDataChange(key, data, prop),
                            onDeselect: () => this.setState({
                                tempInputData: '',
                                tempFontSize: 16,
                                searchResult: null,
                                modifyContentProps: null
                            }),
                            disableSignEditing: this.props.disableSignEditing,
                            previewId: this.props.id
                        }}/>
                    </div>                    
                </div>
            </div>
        );
    }
}

Builder.defaultProps = {
    disableColorPickers: false,
    disableSignEditing: false,
}

const mapStateToProps = (state) => ({
    configs: state.dbConfigs.configs
});

export default connect(mapStateToProps)(Builder);
