import React, { Fragment, useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import { withRouter } from 'react-router-dom';
import QuarterlyPaymentModal from './QuarterlyPaymentModal';
import {PROFILE_PATH, JOBS_PATH} from 'utils/constants';
// import Logo from 'assets/img/header/logo.png';
import Logo from 'components/Logo';
import { JOB_DASHBOARD_PATH, DASHBOARD_PATH  } from 'utils/constants';
// import { ReactComponent as InfoIcon } from 'assets/img/icons/info.svg';
// import { ReactComponent as MessageIcon } from 'assets/img/icons/message.svg';
import { ReactComponent as OrderIcon } from 'assets/img/icons/orders.svg';
import { ReactComponent as UserIcon } from 'assets/img/icons/user.svg';
import { ReactComponent as Attention } from 'assets/img/icons/attention.svg';

import './AppHeader.scss';

function Header(props) {
  const [quarterlyPaymentModalShow, setQuarterlyPaymentModalShow] = useState(false);   
  const [quarterlyPaymentError, setQuarterlyPaymentError] = useState(false);
  const accessGroup = useSelector(state => state.general.accessGroup);
  // const quarterlyPaymentFailed = useSelector(state => state.account.currentAccount.quarterlyPaymentFailed);

  useEffect(() => {
    // TODO Uncomment when back will ready
    if(props.currentAccount&&props.currentAccount.quarterlyPaymentFailed){
      setQuarterlyPaymentModalShow(true);
      setQuarterlyPaymentError(true);
    }
    // for testing quarterlyPaymentFailed
    // if(true){
    //   setQuarterlyPaymentModalShow(true);
    //   setQuarterlyPaymentError(true);
    // }
  }, []);

  const toDashboard = () => {      
    switch(accessGroup){
      case 1:
        props.history.push(JOB_DASHBOARD_PATH);
        break;
      case 2:
        props.history.push(DASHBOARD_PATH);
        break;
      default :
        props.history.push(JOB_DASHBOARD_PATH);
        break;        
    }      
  };
  const toOrders = () => {    
    props.history.push(JOBS_PATH);
  };

  const toProfile = () => {
    props.history.push(PROFILE_PATH);
  };

  const toggleQuarterlyPaymentModal = () => {
    setQuarterlyPaymentModalShow(!quarterlyPaymentModalShow);   
  };
  const goQuarterlyRecharge = () => {
    setQuarterlyPaymentModalShow(!quarterlyPaymentModalShow);
    toProfile();    
  }

const path = props.location.pathname
  return (
    <Fragment>
      <ReactCSSTransitionGroup
        component='div'
        className='app-header'
        transitionName='HeaderAnimation'
        transitionAppear={true}
        transitionAppearTimeout={1500}
        transitionEnter={false}
        transitionLeave={false}
      >
        <div className='app-header__content'>
          <div className='app-header-container'>
            {/* <InfoIcon className='app-header-item' />
            <MessageIcon className='app-header-item' /> */}
            <div className='header-logo-container app-header-item'>
              {/* <img src={Logo} alt='Dope logo' onClick={toDashboard} /> */}
              <Logo onClick={toDashboard}/>
            </div>
            <OrderIcon 
              className={path === JOBS_PATH ? 'app-header-item active-header-item' : 'app-header-item'} 
              onClick={toOrders} 
            />
            <span className="user-icon-container">
              {quarterlyPaymentError && <Attention className="attention-icon"/>}
              <UserIcon 
                className={path === PROFILE_PATH ? 'app-header-item active-header-item' : 'app-header-item'} 
                onClick={toProfile}              
              />              
            </span>
          </div>
        </div>
      </ReactCSSTransitionGroup>
      <QuarterlyPaymentModal
        open={quarterlyPaymentModalShow}
        toggle={toggleQuarterlyPaymentModal}
        onQuarterlyRecharge={goQuarterlyRecharge}
      />
    </Fragment>
  );
}

export default withRouter(Header);
