import React from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import YardSignTracker from './YardSignTracker';
import { connect } from 'react-redux';
import * as dashboardActions from 'actions/dashboard';
import * as jobsActions from 'actions/jobs';
import * as mainActions from 'actions';
import { FadeLoader } from 'react-spinners';
import { pathOr } from 'ramda';
import { Container, Row, Col } from 'reactstrap';
import DashboardCard from './DashboardCard';
import { ReactComponent as PostcardIco } from 'assets/img/icons/postcards.svg';
import { ReactComponent as YardSignIco } from 'assets/img/icons/signpost.svg';
// import { ReactComponent as DoorHangerIco } from 'assets/img/icons/hangers.svg';
// import { ReactComponent as WebIco } from 'assets/img/icons/web.svg';
import { ReactComponent as PhoneIco } from 'assets/img/icons/telephone.svg';
import './Dashboard.scss';


class Dashboard extends React.Component {
  state = {
    jobs: [],
    currentJob: {},
  };

  componentDidMount() {
    this.updateData();        
  }

  componentDidUpdate(prevProps) {
    // New account was selected
    if (this.props.currentAccount._id !== prevProps.currentAccount._id) {
      this.updateData()
    }

    //All request are done
    if(!this.props.dashboard.incomingCalls.isLoading && !this.props.dashboard.statistic.isLoading && !this.props.jobs.isLoading ) {
      this.getMarkers();
    }
  }

  updateTable = (pagination = { limit: 10, page: 1 }) => {
    const { limit, page, sort } = pagination;
    let data = {
      params: {
        limit,
        page: page - 1,
        account: this.props.currentAccount._id,
        sort: sort
      },
    };
    this.props.createIncomingСallsRequest(data);
  };

  updateData = () => {
    const data = {
      account: this.props.currentAccount._id ,
    };
    this.props.createDashboardStatisticRequest(data);
    this.props.createAllIncomingСallsRequest(data);
    this.props.createJobsRequest(data)
    this.updateTable();
  };

  totalCalls = statistics => {
    var res = 0;
    for (let key in statistics) {
      if (statistics.hasOwnProperty(key)) {
        if(statistics[key].inboundCalls ) res += statistics[key].inboundCalls;
      }
    }
    return res;
  };

  getMarkers = () => {
    const calls = pathOr([], ['allIncomingCalls', 'calls'], this.props.dashboard);
    const lat = pathOr(null, ['currentAccount', 'companyAddress', 'lat'], this.props);
    const lng = pathOr(null, ['currentAccount', 'companyAddress', 'lng'], this.props);
    let markers = [];

    if (this.props.currentJob && this.props.currentJob.pins) {
      this.props.currentJob.pins.forEach(pin => {
        markers.push({
          _id: pin._id,
          latitude: pin.lat,
          longitude: pin.lng,
          createdAt: pin.createdAt,
          icon: 'yard',
        });
      });
    } else {
      const yardSigns = this.props.jobs.data.filter(job => {
        return job.type === 'Yard Sign';
      });

      for (let yard of yardSigns) {
        yard.pins &&
          yard.pins.forEach(pin => {
            markers.push({
              _id: pin._id,
              latitude: pin.lat,
              longitude: pin.lng,
              createdAt: pin.createdAt,
              icon: 'yard',
            });
          });
      }

      for (let call of calls) {
        if (!call.contact || !call.contact.latitude || !call.contact.longitude) continue;
        markers.push({
          ...call,
          latitude: call.contact.latitude,
          longitude: call.contact.longitude,
          icon: 'phone',
        });
    }

    if(lat && lng) {
      markers.push({
        latitude: lat,
        longitude: lng,
        icon: 'office',
      })
    }
  }

    this.props.setMarkers(markers);
  };

  render() {    
    const postcardQuantity = pathOr(
      0,
      ['postcards', 'sent'],
      this.props.statistic,
    );
    const yardSignQuantity = pathOr(
      0,
      ['yardSigns', 'sent'],
      this.props.statistic,
    );
    const postcardCalls = pathOr(
      0,
      ['postcards', 'inboundCalls'],
      this.props.statistic,
    );
    const yardSignsCalls = pathOr(
      0,
      ['yardSigns', 'inboundCalls'],
      this.props.statistic,
    );

    const incomingCalls = pathOr(
      {},
      ['incomingCalls', 'calls'],
      this.props.dashboard,
    );
    const jobs = pathOr({}, ['data'], this.props.jobs);
    const totalCalls = this.totalCalls(this.props.statistic);
    if (this.props.dashboard.allIncomingCalls.isLoading || 
      this.props.dashboard.incomingCalls.isLoading || 
      this.props.dashboard.statistic.isLoading || 
      this.props.jobs.isLoading)
      return (
        <div className='content'>
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <FadeLoader size={100} color={'#403d39'} />
          </div>
        </div>
      );
    return (      
      <ReactCSSTransitionGroup
        component='div'
        transitionName='TabsAnimation'
        transitionAppear={true}
        transitionAppearTimeout={0}
        transitionEnter={false}
        transitionLeave={false}
      >
        <div className='content dashboard-content'>
          <Container fluid={true}>
            <Row className='justify-content-center'>
              <Col lg={6}>
                <div className='total-phone-calls'>
                  <PhoneIco />
                  <p className='total-calls-count'>{totalCalls}</p>
                  <h5 className='total-phone-calls-title'>
                    Total phone calls received
                  </h5>
                </div>
              </Col>
            </Row>
            <Row className='work-result justify-content-center'>
              <Col className="col-xl-3 col-lg-6">
                <DashboardCard
                  countResult={postcardQuantity}
                  name='Postcards sent'
                  callsCount={postcardCalls}
                  themeColorBg
                  Img={PostcardIco}
                />
              </Col>
              <Col className="col-xl-3 col-lg-6">
                <DashboardCard
                  countResult={yardSignQuantity}
                  name='Yard signs placed'
                  callsCount={yardSignsCalls}
                  color='#515557'
                  Img={YardSignIco}
                />
              </Col>
              {/*
              <Col lg={3}>
                <DashboardCard
                  countResult='0'
                  name='Door hangers hung'
                  callsCount='0'
                  color='#7d7d7d'
                  Img={DoorHangerIco}
                  title='This functionality for the next release'
                />
              </Col>
              <Col lg={3}>
                <DashboardCard
                  countResult='0'
                  name='Web visitors pixeled'
                  callsCount='0'
                  color='#7d7d7d'
                  Img={WebIco}
                  title='This functionality for the next release'
                />
              </Col>
              */}
            </Row>
          </Container>
          <YardSignTracker
            incomingCalls={incomingCalls}
            jobs={jobs}
            updateTable={this.updateTable}
          />
        </div>
      </ReactCSSTransitionGroup>
    );
  }
}

const mapStateToProps = state => ({
  currentJob: state.job.currentJob,
  currentAccount: state.account.currentAccount,
  configs: state.dbConfigs.configs,
  statistic: state.dashboard.statistic,
  dashboard: state.dashboard,
  jobs: state.jobs,
});

const mapDispatchToProps = {
  ...dashboardActions,
  ...mainActions,
  ...jobsActions,
};

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);
