import React from 'react';
import { connect } from 'react-redux';
import BigCalendar from 'react-big-calendar';
import moment from 'moment';
import { Container, Row, Col } from 'reactstrap';
import { setMarkers, resetMapData, setCenter } from 'actions';
import * as jobsActions from 'actions/jobs';
import MapContainer from './MapContainer';
import YardSignTrackerList from './YardSignTrackerList';
import phoneIcon from 'assets/img/phone-small.svg';
import yardIcon from 'assets/img/home-small.svg';
import { ReactComponent as PhoneLogo } from 'assets/img/icons/telephone.svg';
import './YardSignTrackerMap.scss';
import CalendarToolbar from './CalendarToolbar/CalendarToolbar';
import 'moment-timezone';
import 'react-big-calendar/lib/css/react-big-calendar.css';

const localizer = BigCalendar.momentLocalizer(moment);

const CalendarEvent = ({ event }) => (
  <div style={{ display: 'flex', alignItems: 'center' }}>
    <img src={event.icon} style={{ marginRight: 10, width: 15, height: 15 }} alt="event" />
    <div>{event.title}</div>
  </div>
);

class YardSignTracker extends React.Component {
  state = {
    addPinMode: false,
  };

  componentDidMount() {
    this.props.resetMapData();
  }

  getEvent = () => {
    let events = [];
    const yardSigns = this.props.jobs.filter(job => {
      return job.type === 'Yard Sign';
    });
    for (let index in this.props.incomingCalls) {     
      let data = this.props.incomingCalls[index];

      events.push({
        title: 
          moment(data.callTime).tz(this.props.timeZone).format('hh:mm A') +
          (data.firstName && data.lastName
            ? ` ${data.firstName} ${data.lastName}`
            : ' Unnamed'),
        start: data.callTime,
        end: data.callTime,
        allDay: true,
        type: 'call',
        icon: phoneIcon,
      });
    }

    for (let yard of yardSigns) {
      yard.pins &&
        yard.pins.forEach(pin => {
          events.push({
            title: 'Yard Sign Placed',
            start: pin.createdAt,
            end: pin.createdAt,
            allDay: true,
            type: 'yard',
            icon: yardIcon,
          });
        });
    }
    return events;
  };

  showAddPinBtn = (data) => {
    let obj = {...this.state, addPinMode: !this.state.addPinMode}
    if(data) obj.addPinMode = false;
    this.setState(obj)
  }

  render() {
    this.getEvent();
    return (
      <div>
        <Container fluid={true}>
          <Row>
            <Col md={6} lg={12} className="map-wrapper">
              <MapContainer
                jobs={this.props.jobs}
                showAddPinBtn={this.showAddPinBtn}
                addPinMode={this.state.addPinMode}
              />
            </Col>
            <Col className ="no-incoming-calls" md={6} lg={12}>
              <h5>
                Incoming calls
              </h5>
              {this.props.incomingCalls.length > 0 ? (
                <YardSignTrackerList
                  callData={this.props.incomingCalls}
                  showAddPinBtn={this.showAddPinBtn}
                  updateTable={this.props.updateTable}
                />
              ): (
                <div className="no-incoming-calls">
                  <div className="circle">
                    <PhoneLogo/>
                  </div>
                  <p>There are no incoming calls yet.</p>
                </div>
              )}
            </Col>
          </Row>
        </Container>

        <div className='calendar-container'>
        <h5 className='calendar-header'>Calendar</h5>
          <BigCalendar
            events={this.getEvent()}
            localizer={localizer}
            views={['day', 'week', 'month']}
            drilldownView={'week'}
            eventPropGetter={e => {
              if (e.type === 'call')
                return {
                  className: 'call-label',
                };
              else if (e.type === 'yard')
                return {
                  className: 'yard-label',
                };
            }}
            components={{
              event: CalendarEvent,
              toolbar: CalendarToolbar,
            }}
          />
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  markers: state.map.markers,
  currentAccount: state.account.currentAccount,
  timeZone:  state.account.timeZone,
});

const mapDispatchToProps = {
  setMarkers,
  resetMapData,
  setCenter,
  ...jobsActions,
};

export default connect(mapStateToProps, mapDispatchToProps)(YardSignTracker);
