import React from 'react';
import { Map, TileLayer, Polyline, ZoomControl } from 'react-leaflet';
import { useSelector, useDispatch } from 'react-redux';
import { setPoligonProperties, resetPoligonProperties, resetPoligonPropertiesBack } from 'actions/eddm';
import './LeafletMap.scss';

export default function LeafletMap({center, zoom, handlePoligonClick, googleMapType}) {
  
  const dispatch = useDispatch();
  const featureCollection = useSelector(state => state.eddm.eddmRouteMap.featureCollection);
  const featureCollectionBack = useSelector(state => state.eddm.eddmRouteMap.featureCollectionBack);
  const selectedRoutes = useSelector(state => state.eddm.selectedRoutes);
  const mapPositionIsLoading = useSelector(state => state.eddm.eddmRouteMap.mapPositionIsLoading);
  const configs = useSelector(state => state.dbConfigs.configs);
  const colors = configs.filter(item => item.name === 'colorsEddm')[0].value;

  const reverseLatLng = (arr) => {    
    let arrRev=arr.map(el=> el.map(item => [...item].reverse()));
    return arrRev;
  }
  const handlePoligonMouseOver = (e,properties) => {
    dispatch(setPoligonProperties({properties, featureCollectionBack}));
  }
  const handlePoligonMouseOut = (e) => {
    dispatch(resetPoligonProperties());
    dispatch(resetPoligonPropertiesBack());
  }  
  const handlePoligonColor = (i, isSelected, route) => {    
    if(isSelected&&colors.length)      
      return colors[i%colors.length].hex;    
    else
      return '#2362d5';
  }
  
  return (     
    <Map      
      className={`${mapPositionIsLoading ? 'loading' : ''}`}
      center={center}
      zoomSnap={0.25}
      zoom={zoom}
      maxZoom={50}
      attributionControl={true}
      zoomControl={false}
      doubleClickZoom={true}
      scrollWheelZoom={true}
      dragging={true}      
      easeLinearity={0.35}                
    >
      <div className="loading-indicator">Processing...</div>
      <ZoomControl position="bottomleft"/>
      <TileLayer        
        url={`http://{s}.google.com/vt/lyrs=${googleMapType}&hl=en&x={x}&y={y}&z={z}`}        
        subdomains={['mt0','mt1','mt2','mt3']}            
      />      
      { 
        featureCollection.features.map((multiPoligon,i)=>{
          let isSelected = selectedRoutes.includes(multiPoligon.attributes.ZIP_CRID);
          return <Polyline
            key={i}
            weight={8}
            // color='#2362d5'
            color={handlePoligonColor(i,isSelected,multiPoligon.attributes.ZIP_CRID,selectedRoutes)}
            opacity={`${isSelected ? '0.8' : '0.2'}`}
            positions={reverseLatLng(multiPoligon.geometry.paths)}
            onClick={(e)=>handlePoligonClick(e, multiPoligon.attributes.ZIP_CRID, false)}
            onMouseover={(e)=>handlePoligonMouseOver(e, multiPoligon.attributes)}
            onMouseout={(e)=>handlePoligonMouseOut(e)}
          />          
        })
      }      
    </Map>   
  );
}
