import React from 'react';
import { DopeTable } from 'components/DopeTable/DopeTable';
import { pathOr } from 'ramda';
import moment from 'moment';
import 'moment-timezone';
import { ReactComponent as ReceiptIco } from 'assets/img/icons/receipt.svg';
// import { FadeLoader } from 'react-spinners';
import './PaymentHistory.scss';

export default function PaymentHistory(props) {
  // console.log("PaymentHistory: ", props);
  const page = pathOr(0, ['page'], props.histories);
  const pages = pathOr(0, ['pages'], props.histories);
  const totalSize = pathOr(0, ['totalSize'], props.histories);  

  const statusLayout = (cell, row, rowIndex, formatExtraData) => {        
    switch (cell) {
      case 'pending':
        return <span className='pending status-cell'>Pending</span>;
      case 'succeeded':
        return <span className='paid status-cell'>Paid</span>;
      case 'refunded':
        return <span className='refunded status-cell'>Refunded</span>;
      case 'error':
        return <span className='error status-cell'>Failed</span>;
      default:
        return <span className='refunded status-cell'>{cell}</span>;        
    }
  };

  const pdfLayout = (cell, row, rowIndex, formatExtraData) => {      
    return  cell ? <a href={cell} target="_blank" rel="noopener noreferrer"><ReceiptIco/></a> : null;
  };

  const idHistoryLayout = (cell, row, rowIndex, formatExtraData) => {
    return <span title={cell}>{cell.slice(-5)}</span>;
  };

  const cardLayout = (cell, row, rowIndex, formatExtraData) => {
    return (
      <div className='card-cell'>
        <span className={`card-cell-last4 ${cell.brand&&cell.brand.toLowerCase()}-ico`}>
          ****{cell.last4}
        </span>
        <span className='card-cell-exp-data'>
          {cell.expMonth}/{cell.expYear}
        </span>
      </div>
    );
  };

  const idHistory = {
    dataField: 'id',
    text: 'ID',
    sort: false,
    formatter: idHistoryLayout,
  };

  const card = {
    dataField: 'card',
    text: 'Card',
    sort: false,
    formatter: cardLayout,
  };

  const statusColumn = {
    dataField: 'status',
    text: 'Status',
    sort: false,
    formatter: statusLayout,
    attrs: { className: 'status-td' },
  };

  const pdfReportColumn = {
    dataField: 'pdfReport',
    text: '',
    sort: false,
    formatter: pdfLayout,
    attrs: { className: 'pdfReport-td' },
  };

  

  const formatedHistories = histories => {
    
    return histories.map( (item,i) => {
      // console.log("formatedHistories:", item);
      const id = pathOr(0, ['id'], item);
      const createdAt = pathOr(0, ['createdAt'], item);
      const dateCreated = moment(createdAt).tz(props.timeZone).format('DD MMM YYYY');
      const timeCreated = moment(createdAt).tz(props.timeZone).format('hh:mm:ss ');
      const description = pathOr('', ['message'], item);
      const card = pathOr('0000', ['card'], item);
      const totalPrice = '$' + (pathOr(0, ['charge', 'amount'], item) / 100).toFixed(2);
      let status = pathOr('', ['charge', 'status'], item);
      let pdfReport = pathOr('', ['invoice', 'invoice', 'pdf'], item);
      if(!pdfReport){
        pdfReport = pathOr('', ['invoice', 'invoice', 'url'], item);
      }
      if(!pdfReport){
        pdfReport = pathOr('', ['invoice', 'invoice', 'invoice', 'pdf'], item);
      }
      if(!pdfReport){
        pdfReport = pathOr('', ['invoice', 'invoice', 'invoice', 'url'], item);
      }

      const data = {
        id,
        dateCreated,
        timeCreated,
        description,
        card,
        totalPrice,
        status,
        pdfReport,
      };

      return data;
    });
  };
  const updateData = (pagination = { limit: 10, page: 1 }) => {
    const { limit, page } = pagination;
    const data = {
      account: props.currentAccount._id,
      params: { page: page - 1, limit: limit },
    };
    props.historiesReq(data);
  };

  let paginationOptions = {
    onPageChange: updateData,
    page: page + 1,
    paginationSize: pages,
    totalSize: totalSize,
  };

  const columns = [
    idHistory,
    {
      dataField: 'dateCreated',
      text: 'Date',
    },
    {
      dataField: 'timeCreated',
      text: 'Time',
    },
    {
      dataField: 'description',
      text: 'Description',
    },
    card,
    {
      dataField: 'totalPrice',
      text: 'Total price',
    },
    statusColumn,
    pdfReportColumn,
  ];

  if (props.histories && props.histories.data && props.histories.data.length)
    return (
      <div className='payment-history-tab account-form-container'>
        <DopeTable
          keyField='id'
          data={formatedHistories(props.histories.data)}
          columns={columns}
          rowClasses='table-row'
          paginationOptions={paginationOptions}
          loading={props.isHistoryLoading}
        />
      </div>
    );
  return null;
}
