import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import {
    Card,
    CardBody,
    CardTitle,
    FormGroup,
    Label,
    Input,
    InputGroup,
    InputGroupAddon,
    Button
} from 'reactstrap';
import { BeatLoader } from 'react-spinners';

import Toggle from "components/Toggle/Toggle";
import Palette from "./Palette/Palette";

import { ReactComponent as Plus } from 'assets/img/icons/add.svg';

import { makePalette } from "components/utils";
import { setDbConfigs } from 'actions';

const Options = ({ values }) => {
    return (
        <Fragment>
            <option value="null"> Select</option>
            {Array.isArray(values) && values.map((palette) => {
                return (
                    <option
                        key={palette.label}
                        label={palette.label}
                        value={palette.id}
                    >
                        {palette.label}
                    </option>
                )
            })}
        </Fragment>
    )
};

class SystemAdmin extends React.Component {
    state = {
        config: {
            directMailEnabled: false,
            addJob: false,
            pageOrders: false,
            pageLists: false,
            createLists: false,
            colorPalette: {
                value: []
            },
            adminEmail: ''
        },
    };

    static getDerivedStateFromProps({ configs }) {
        if (Array.isArray(configs)) {
            const config = configs.reduce((acc, { name, value }) => {
                acc[name] = value;
                return acc;
            }, {});

            return {
                config
            }
        }

        return {
            config: {}
        }
    }

    saveSetting(name) {
        this.onSettingChange()({
            target: {
                name,
                value: this.state.config[name]
            }
        });
    }

    onSettingChange = (withApiCall = true) => (event) => {
        const { name, value } = event.target;

        this.props.setDbConfigs({
            key: name,
            withApiCall,
            value,
        });
    };

    onCreatePalette = () => {
        const pallet = makePalette();

        this.onSettingChange()({
            target: {
                name: 'colorPalette',
                value: [
                    ...this.state.config.colorPalette || [],
                    pallet
                ]
            }
        })
    };

    onSavePalette = (event) => {
        const { name, value } = event.target;

        this.onSettingChange()({
            target: {
                name: 'colorPalette',
                value: [
                    ...this.state.config.colorPalette.map(item => {
                        if (item.id === name) {
                            return {
                                ...item,
                                colors: value
                            }
                        }
                        return item;
                    }),
                ]
            }
        })
    }

    render() {
        const { config } = this.state;

        return (
            <div id="setting" className='content setting'>
                <div className="container">
                    <Card className="col-12">
                        <CardBody>
                            <CardTitle style={{ display: 'flex' }}>
                                Mail
                              {this.props.loadingFieldName === 'directMailEnabled' &&
                                    <div style={{ marginLeft: 10 }}>
                                        <BeatLoader color={'#403d39'} size={5} />
                                    </div>
                                }
                            </CardTitle>
                            <Toggle
                                name="directMailEnabled"
                                value={config.directMailEnabled}
                                onToggle={this.onSettingChange()}
                                label="Direct Mail Feature"
                            />
                        </CardBody>
                    </Card>
                    <Card className="col-12">
                        <CardBody>
                            <CardTitle style={{ display: 'flex' }}>
                                Jobs
                              {(this.props.loadingFieldName === 'addJob' || this.props.loadingFieldName === 'pageOrders') &&
                                    <div style={{ marginLeft: 10 }}>
                                        <BeatLoader color={'#403d39'} size={5} />
                                    </div>
                                }
                            </CardTitle>
                            <Toggle
                                name="addJob"
                                value={config.addJob}
                                onToggle={this.onSettingChange()}
                                label="Add Job"
                            />
                            <Toggle
                                name="pageOrders"
                                value={config.pageOrders}
                                onToggle={this.onSettingChange()}
                                label="Orders Page"
                            />
                        </CardBody>
                    </Card>
                    <Card className="col-12">
                        <CardBody>
                            <CardTitle style={{ display: 'flex' }}>
                                Lists
                              {(this.props.loadingFieldName === 'createLists' || this.props.loadingFieldName === 'pageLists') &&
                                    <div style={{ marginLeft: 10 }}>
                                        <BeatLoader color={'#403d39'} size={5} />
                                    </div>
                                }
                            </CardTitle>
                            <Toggle
                                name="createLists"
                                value={config.createLists}
                                onToggle={this.onSettingChange()}
                                label="Create Lists"
                            />
                            <Toggle
                                name="pageLists"
                                value={config.pageLists}
                                onToggle={this.onSettingChange()}
                                label="Lists Page"
                            />
                        </CardBody>
                    </Card>
                    <Card className="col-12">
                        <CardBody>
                            <CardTitle style={{ display: 'flex' }}>
                                Admin Email
                              {this.props.loadingFieldName === 'adminEmail' &&
                                    <div style={{ marginLeft: 10 }}>
                                        <BeatLoader color={'#403d39'} size={5} />
                                    </div>
                                }
                            </CardTitle>
                            <InputGroup>
                                <Input
                                    type="email"
                                    name="adminEmail"
                                    id="adminEmail"
                                    value={config.adminEmail}
                                    onChange={this.onSettingChange(false)}
                                >
                                </Input>
                                <InputGroupAddon addonType="append"><Button onClick={() => this.saveSetting('adminEmail')}>Save</Button></InputGroupAddon>
                            </InputGroup>
                        </CardBody>
                    </Card>
                    <Card className="col-12">
                        <CardBody>
                            <CardTitle style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <div style={{ display: 'flex' }}>
                                    Color Palette
                                  {this.props.loadingFieldName === 'colorPalette' &&
                                        <div style={{ marginLeft: 10 }}>
                                            <BeatLoader color={'#403d39'} size={5} />
                                        </div>
                                    }
                                </div>
                                <Plus onClick={this.onCreatePalette} style={{'width': '18px', 'cursor': 'pointer'}}/>
                            </CardTitle>

                            {Array.isArray(config.colorPalette) && config.colorPalette.map((palette) => {
                                return (
                                    <Palette
                                        key={palette.label}
                                        label={palette.label}
                                        value={palette}
                                        colors={palette.colors}
                                        onSave={this.onSavePalette}
                                    />
                                )
                            })}
                        </CardBody>
                    </Card>
                    <Card className="col-12">
                        <CardBody>
                            <CardTitle style={{ display: 'flex' }}>
                                Active color schemas
                              {(this.props.loadingFieldName === 'yardSignsPalette' || this.props.loadingFieldName === 'postcardsPalette') &&
                                    <div style={{ marginLeft: 10 }}>
                                        <BeatLoader color={'#403d39'} size={5} />
                                    </div>
                                }
                            </CardTitle>
                            <FormGroup>
                                <Label for="yardSigns">Yard Signs</Label>
                                <Input
                                    type="select"
                                    name="yardSignsPalette"
                                    id="yardSigns"
                                    value={config.yardSignsPalette}
                                    onChange={this.onSettingChange()}
                                >
                                    <Options values={config.colorPalette} selected={config.yardSignsPalette} />
                                </Input>
                            </FormGroup>
                            <FormGroup>
                                <Label for="postcards">Postcards</Label>
                                <Input
                                    type="select"
                                    name="postcardsPalette"
                                    id="postcards"
                                    value={config.postcardsPalette}
                                    onChange={this.onSettingChange()}
                                >
                                    <Options values={config.colorPalette} />
                                </Input>
                            </FormGroup>
                        </CardBody>
                    </Card>
                    <Card className="col-12">
                        <CardBody>
                            <CardTitle style={{ display: 'flex' }}>
                                Postcard price per contact ($)
                              {this.props.loadingFieldName === 'postcardPricePerContact' &&
                                    <div style={{ marginLeft: 10 }}>
                                        <BeatLoader color={'#403d39'} size={5} />
                                    </div>
                                }
                            </CardTitle>
                            <InputGroup>
                                <Input
                                    type="number"
                                    name="postcardPricePerContact"
                                    id="postcardPricePerContact"
                                    value={config.postcardPricePerContact}
                                    onChange={this.onSettingChange(false)}
                                >
                                </Input>
                                <InputGroupAddon addonType="append">
                                    <Button onClick={() => this.saveSetting('postcardPricePerContact')}>Save</Button>
                                </InputGroupAddon>
                            </InputGroup>
                        </CardBody>
                    </Card>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    configs: state.dbConfigs.configs,
    loadingFieldName: state.dbConfigs.loadingFieldName,
});

const mapDispatchToProps = {
    setDbConfigs,
};

export default connect(mapStateToProps, mapDispatchToProps)(SystemAdmin);
