import service from './service';
import { API_URL } from '../config/config';

export const getAutomationListApi = data => {
  return service.get(
    // `${API_URL}/accounts/${data.accountId}/automation`, data.query
    `${API_URL}/automation`, data.query  // temporary
  )
};
export const getAutomationItemApi = data => {
  return service.get(
    `${API_URL}/automation/${data.sendingId}`, data.query
  )
};
export const deleteAutomationItemApi = data => {
  return service.delete(
    `${API_URL}/automation/${data.sendingId}`, data.query
  )
};
export const createAutomationApi = data => {  
  return service.post(
    `${API_URL}/automation/?account=${data.queries}`, data.body,
  )
};
export const updateAutomationApi = (data) => {
  return service.patch(
    `${API_URL}/automation/${data.automationId}?account=${data.queries}`,
    data.body,
  )
};
export const rechargeAutomationApi = (data) => {
  return service.post(
    `${API_URL}/automation/${data.automationId}/charge?account=${data.queries}`,
    data.body,
  )
};
export const changeStatusAutomationApi = (data) => {
  return service.patch(
    `${API_URL}/automation/${data.automationId}?account=${data.queries}`,
    data.body,
  )  
};
export const getAutomationPreviewContactsApi = data => {
  return service.get(
    `${API_URL}/contacts/?`, data.queries
  )
};
export const getAutomationPreviewImgApi = (data) => {
  return service.post(
    `${API_URL}/convert-image`,
    data,
  )
};
export const getRecentAutomationPostcardsApi = (data) => {  
  return service.get(
    `${API_URL}/accounts/${data.accountId}/automation-sendings`, data.query
  );
};
export const getMyAutomationPostcardsApi = (data) => {  
  return service.get(
    `${API_URL}/accounts/${data.accountId}/templates`, data.query
  );
};
export const deleteMyAutomationPostcardApi = (data) => {  
  return service.delete(
    `${API_URL}/accounts/${data.accountId}/templates/${data.templateId}`
  );
};