import React, {useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import Pagination from 'rc-pagination';
import { FadeLoader } from 'react-spinners';
import { ReactComponent as PrevIco } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as NextIco } from 'assets/img/icons/chevron-right.svg';
import { 
  getRecentNeighborhoodBlitzPostcardsRequest,
} from 'actions/neighborhoodBlitz';
import { S3_BUCKET_PUBLIC_NAME,
  REACT_APP_S3_ACCESS_KEY_ID,
  REACT_APP_S3_SECRET_ACCESS_KEY,
} from 'config/config';
const s3Config = {
  bucketName: S3_BUCKET_PUBLIC_NAME,
  accessKeyId: REACT_APP_S3_ACCESS_KEY_ID,
  secretAccessKey: REACT_APP_S3_SECRET_ACCESS_KEY,
  region: 'us-east-1'
};

export default function MyRecentPostcards({currentAccountId,onSelectTemplate,selectedTemplate}) {   
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getRecentNeighborhoodBlitzPostcardsRequest({accountId: currentAccountId, query:{page:0, limit: 10, templateStatus: 'auto-saved'}}));
  },[]);
  const templates = useSelector(state => state.neighborhoodBlitz.neighborhoodBlitzRecentTemplates.data);
  const loading = useSelector(state => state.neighborhoodBlitz.neighborhoodBlitzRecentTemplates.loading);
  const page = useSelector(state => state.neighborhoodBlitz.neighborhoodBlitzRecentTemplates.page);
  const total = useSelector(state => state.neighborhoodBlitz.neighborhoodBlitzRecentTemplates.totalSize);
  const limit = useSelector(state => state.neighborhoodBlitz.neighborhoodBlitzRecentTemplates.limit);
  const onPageChange = page => {
    dispatch(getRecentNeighborhoodBlitzPostcardsRequest({accountId: currentAccountId, query:{ page: page - 1, limit, templateStatus: 'auto-saved' }}));
  };
  return (
    <React.Fragment>
      <div className={`templates-preview-container${(templates&&templates.length<=5)?' oneRow':''}`}>
        {
          templates && templates.length ? (
            templates.map((template) => (             
              <div className='template-preview-item' key={template._id}>
                <div
                  className='template-item'
                  onClick={() => {                        
                    onSelectTemplate(template);                    
                  }}
                >
                  <div className={`template-preview-content${selectedTemplate === template._id ? ' checked':''}`}>
                    <img
                      className="template-preview-img"
                      src={template.front_thumbnail||`https://${s3Config.bucketName}.s3.amazonaws.com/nbhImages/custom-default-preview.jpg`}
                      alt='postcard template'
                    />                    
                  </div>
                </div>
              </div>
            ))
          ) : (            
            <div className="no-templates-container">
              No Templates
            </div>            
          )          
        }
        {
          loading && (
            <div className='loader-main'>
              <FadeLoader size={100} color={'#403d39'} />
            </div>
          )
        }
      </div> 
      {templates && templates.length ? (
        <Pagination
          className='dope-pagination'
          current={page + 1}
          defaultPageSize={+limit}
          total={total}
          hideOnSinglePage={true}
          onChange={onPageChange}          
          prevIcon={<PrevIco />}
          nextIcon={<NextIco />}
          jumpPrevIcon={<span>...</span>}
          jumpNextIcon={<span>...</span>}
          showTitle={false}
        />
      ) : null}     
    </React.Fragment>
  );
}