import React, {useEffect} from 'react';
import {
  Card,
  CardBody,
  CardHeader,
  FormGroup,
  Label,  
} from 'reactstrap';
// import {DopeCheckbox} from 'components/DopeForms/DopeCheckbox/DopeCheckbox';
import Select from 'react-select';
import StepTitle from 'components/StepTitle';
import ReactTooltip from 'react-tooltip';
import './OrderSummary.scss';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import DopeRadioGroup from 'components/DopeForms/DopeRadioGroup';
import { ReactComponent as HelpIcon } from 'assets/img/icons/help.svg';
import moment from "moment";

export default function OrderSummary(props) { 
  useEffect(() => {    
    props.onChangeOrderName(`Yard Signs - ${props.data.size} - ${moment(date).format('M/D/YY')}`);
  },[]); 
  const amountOptions = [10,25,50,100,250,500,1000].map(el=>{
    return {"value":el,"label":el}
  });
  const onChangeOrderName = (e) => {
    props.onChangeOrderName(e.target.value);
    props.addErrors('name', '');
  }

  const date = new Date();

  return (
    <Card className='order-summary-container'>      
      <CardHeader
        className="wizard-section-header"
        children={<StepTitle {...{
          marked: 'step 5',
          text: props.children
        }} />}
      />      
      <CardBody>
        <FormGroup className="order-summary-type-container">
          <div>
            <Label className='order-summary-label'>
              Yard Sign Size: 
            </Label> 
            <span className="summary-info">&nbsp;{props.data.size}</span>
          </div>         
          <Label className='order-summary-label'>
            Yard Sign Type
          </Label> 
          <div className="order-radio-container">
            <DopeRadioGroup
              data={[
                {
                  value: "one-sided",
                  label: "One-Sided"
                },
                {
                  value: "two-sided",
                  label: "Two-Sided"
                }
              ]}
              name="side"               
              selectedValue={props.data.side}
              onChange={e => props.onChange.setYardSignType(e)}
            />
          </div>

          <div>
            <Label className='order-summary-label'>
              Would you like stakes?&nbsp;&nbsp;<HelpIcon className="help-own-number-icon" data-tip data-for={`tooltips-stakes`} data-place="right" />	
            </Label>             
            <ReactTooltip          
              className="stakes-tooltip"          
              html={true}                       
              id={`tooltips-stakes`}
            >  
              {
                `<span>stakes $1 per one</span>`
              }
            </ReactTooltip>
            <div className="order-radio-container">
              <DopeRadioGroup
                data={[
                  {
                    value: true,
                    label: "Yes"
                  },
                  {
                    value: false,
                    label: "No"
                  }
                ]}
                name="stakes"               
                selectedValue={props.data.stakes}
                onChange={e => props.onChange.setYardSignStakes(e)}
              />  
            </div>
            {/* <DopeCheckbox
                className='dope-checkbox'
                label='with stakes'
                checked={props.data.stakes}                
                onChange={e=>props.onChange.setYardSignStakes(e.target.checked)}
            />
            <span data-tip data-for={`tooltips-stakes`}>&nbsp;(?)</span>
            <ReactTooltip          
              className="stakes-tooltip"          
              html={true}                       
              id={`tooltips-stakes`}
            >  
              {
                `<span>stakes $1 per one</span>`
              }
            </ReactTooltip> */}
          </div>

        </FormGroup>        
        {props.data && props.data.amount ? (
          <FormGroup className='order-summary-count'>
            <Label className='order-summary-label' for='amount'>
              Please select number of yard signs:
            </Label>            
            <div className="order-select-container">
              <Select
                className="dope-select"
                classNamePrefix="dope"
                options={amountOptions}
                value={amountOptions.filter(item => item.value === props.data.amount)}
                onChange={e => props.onChange.setYardSignAmount(e.value)}
              />
            </div>
          </FormGroup>
        ) : (
          null
        )}
        <div className='order-summary-label'>Price: <span className="t-price">${(props.price).toFixed(2)}</span></div>
        <div className='order-summary-label'>Shipping Price: <span className="t-price">${(props.shippingService).toFixed(2)}</span></div>
        <div className='order-summary-total'>Total Price: <span className="t-price">${(props.total).toFixed(2)}</span></div>
        <Label className="order-summary-label" for='name'>Order Name:</Label>
        <div className='order-summary-label'>
          <DopeInput
              type='text'              
              value={props.data.name}
              onChange={onChangeOrderName}
              // maxLength={30}
              error={props.errors.name}
          />
        </div>                
      </CardBody>
    </Card>
  );
}
