import {
  SET_CREATE_POSTCARD_PROPERTY,
  SET_TRACKING_NUMBER_POSTCARD,
  SET_SELECTED_NUMBER_POSTCARD,
	SET_OWN_NUMBER_POSTCARD,
  ADD_ERRORS_POSTCARD,
  GET_ALL_POST_CARD_TEMPLATE_REQUEST_SUCCESS,
  SELECT_POST_CARD_TEMPLATE,
  SELECT_MY_POST_CARD_TEMPLATE,
  SELECT_POST_CARD_TEMPLATE_BACK,
  RESET_POST_CARD_TEMPLATE,
  POST_CARD_NEXT_STEP,
  SET_POST_CARD_FRONT_BACK_IMG,
  SET_POSTCARD_ORDER_NAME,
  SET_SHIPPING_ADDRESS,
  CHANGE_RETURN_ADDRESS,
  SELECT_CREDIT_CARD_POSTCARD,
  CREATE_GET_CONTACTS_REQUEST,
  CREATE_GET_CONTACTS_REQUEST_SUCCESS,
  CONTACTS_LIST_ON_CHANGE,
  FINAL_MODAL_SHOW_POST_CARD,
  RESET_WIZARD_POST_CARD,
  CHANGE_POST_CARD_STATUS,
  CREATE_GET_LIST_REQUEST_SUCCESS,
  SET_POST_CARD_ID,
  GET_ALL_CONTACTS_REQUEST_SUCCESS,
  UPDATE_PHONE_NUMBERS_POSTCARD,
  DELETE_ERRORS_NUMBER_POSTCARD,
  POSTCARD_DISABLE_BTN,
  TOGGLE_SAVED_TEMPLATE_MODAL,
  GET_MY_POSTCARD_TEMPLATES_REQUEST_SUCCESS,
  GET_UNI_MY_POSTCARD_TEMPLATES_REQUEST_SUCCESS,
  CHANGE_POSTCARD_TEMPLATES_IS_LOADED,
  CHANGE_POSTCARD_TEMPLATE_TAB_INDEX,
  RESET_POSTCARD_TEMPLATES_DATA,
  RESET_ALL_POSTCARD_TEMPLATES,
  GET_POSTCARD_BY_ID_REQUEST_SUCCESS,
  TOGGLE_DELETE_TEMPLATE_MODAL,
  GET_TEMPLATES_WITH_CATEGORY_REQUEST_SUCCESS,
  CHANGE_CATEGORY_TEMPLATE_IS_LOADED, 
  RESET_CATEGORY_TEMPLATES, 
  RESET_POST_CARD_TEMPLATE_BACK_KEY,
  SET_USER_POSTCARD_IMAGES,
  RESET_TO_FIRST_STEP_POSTCARD,
  SET_POSTAGE_DATA,
  SET_SENDING_TO_MY_ADDRESS,
  GET_POSTCARD_PREVIEW_IMG_SUCCESS,
  MODIFICATE_POSTCARD_TEMPLATE,

} from '../actions/createPostCard';

let initialState = {
  step: 1,
  selectFromTemplates: false,
  allPostcardsTemplate: [],
  newTemplateIsLoaded: false,
  postcardTemplatesData: [],
  postcardTemplatesIsLoaded: false,
  categoryTemplates: [],
  categoryTemplatesIsLoaded: false,
  finalModalShow: false,
  changePostcardStatus: false,
  loader: false,
  contactsLists: [],
  selectedContactListId: '',
  errors: {},
  previewData: {},
  loading: false,
  updateAddresses: true,
  existingNumbers: [],
  disableSubmitBtn: false,
  showSaveTemplateModal: false,
  showDeleteTemplateModal: false,
  templateTabIndex: '1',  
  images: {
    front: {
      mini: '',
      full: ''
    },
    back: {
      mini: '',
      full: ''
    }
  }, 
  backMarkPlace: '<div class="morpheus-mark-bg" style="-webkit-box-sizing: inherit; background-color: #ffffff; bottom: 0; box-sizing: inherit; height: 800px; pointer-events: none; position: absolute; right: 0; width: 1300px;"/>',     
  data: {
    selectedNumber: 'trackingPhoneNumber',
    postcardId: null,
    trackingNumber: '',
    ownNumber: '',
    contacts: [],
    postage: 'standard',
    sendingToMyAddress: false,
    allContacts: [],
    name: '',
    cardId: '',
    billingAddress: {},
    shippingAddress: {
      address1: '',
      address2: '',
      city: '',
      state: '',
      zip: '',
    },
    postcardSize: '6x9',
    template: {
      name: '',
      front: '',
      back: '',
      thumbnailFront: '',
      thumbnailBack: '',
      backs: [],
      selectedBackKey: 0,
      createdAt: '',
    },
  },  
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case SET_CREATE_POSTCARD_PROPERTY: {
      if (action.group) {
        return {
          ...state,
          [action.group]: {
            ...state[action.group],
            [action.key]: action.value,
          },
        };
      } else {
        return {
          ...state,
          [action.key]: action.value,
        };
      }
    }
    case SET_TRACKING_NUMBER_POSTCARD:
      return {
        ...state,
        data: {
          ...state.data,
          trackingNumber: action.data,
        },
      };
    case SET_SELECTED_NUMBER_POSTCARD:
      return {
        ...state,
        data: {
          ...state.data,
          selectedNumber: action.value,
        },
      };
    case SET_OWN_NUMBER_POSTCARD:
      return {
        ...state,
        data: {
          ...state.data,
          ownNumber: action.value,
        },
      };
    case GET_ALL_POST_CARD_TEMPLATE_REQUEST_SUCCESS:
      return {
        ...state,
        allPostcardsTemplate: action.data,
      };
    case SELECT_POST_CARD_TEMPLATE:
      return {
        ...state,
        data: {
          ...state.data,
          template: {            
            ...action.template,
          },
        },
      };
    case SELECT_MY_POST_CARD_TEMPLATE:      
      return {
        ...state,
        data: {
          ...state.data,
          template: {            
            images: action.template.images,
            _id: action.template._id,
            front: action.template.templates.front,
            back: action.template.templates.back,
          },
        },
      };
    case SELECT_POST_CARD_TEMPLATE_BACK:
      return {
        ...state,
        data: {
          ...state.data,
          template: {
            ...state.data.template,
            selectedBackKey: action.data.key,
            back: action.data.back,
          },
        },
      };
    case MODIFICATE_POSTCARD_TEMPLATE:      
      return {
        ...state,
        data: {
          ...state.data,
          template: { 
            ...state.data.template,           
            ...action.data,
          },
        },
      };
    case RESET_POST_CARD_TEMPLATE_BACK_KEY:
      return {
        ...state,
        data: {
          ...state.data,
          template: {
            ...state.data.template,
            selectedBackKey: 0,
          },
        },
      };
    case RESET_POST_CARD_TEMPLATE:
      return {
        ...state,
        data: {
          ...state.data,
          template: {
            name: '',
            front: '',
            back: '',
            thumbnailFront: '',
            thumbnailBack: '',
          },
        },
      };
    case POST_CARD_NEXT_STEP:
      return {
        ...state,
        step: action.step,
      };
    case SET_POST_CARD_FRONT_BACK_IMG:
      return {
        ...state,
        data: {
          ...state.data,
          template: {
            ...state.data.template,
            front_thumbnail: action.front,
            back__thumbnail: action.back,
          },
        },
      };
    case SET_POSTCARD_ORDER_NAME:
      return {
        ...state,
        data: {
          ...state.data,
          name: action.name,
        },
      };
    case SET_SHIPPING_ADDRESS:
      return {
        ...state,
        data: {
          ...state.data,
          shippingAddress: action.address,
        },
      };
    case CHANGE_RETURN_ADDRESS:
      return {
        ...state,
        data: {
          ...state.data,
          shippingAddress: {
            ...state.data.shippingAddress,
            [action.key]: action.value,
          },
        },
      };
    case SELECT_CREDIT_CARD_POSTCARD:
      return {
        ...state,
        data: {
          ...state.data,
          cardId: action.id,
        },
      };
    case CHANGE_POST_CARD_STATUS:
      return {
        ...state,
        changePostcardStatus: action.payload,
      };
    case CREATE_GET_LIST_REQUEST_SUCCESS:
      return {
        ...state,
        contactsLists: action.data,
      };
    case CONTACTS_LIST_ON_CHANGE:
      return {
        ...state,
        selectedContactListId: action.list._id,
      };
    case FINAL_MODAL_SHOW_POST_CARD:
      return {
        ...state,
        finalModalShow: action.payload,
      };
    case RESET_WIZARD_POST_CARD:
      return {
        ...state,
        ...initialState,
      };
    case CREATE_GET_CONTACTS_REQUEST:
      return {
        ...state,
        data: {
          ...state.data,
          contacts: {
            ...state.data.contacts,
            loading: true,
          },
        },
      };
    case CREATE_GET_CONTACTS_REQUEST_SUCCESS:      
      return {
        ...state,
        data: {
          ...state.data,
          contacts: {
            ...action.data,
            loading: false,
          },
        },
      };
    case GET_ALL_CONTACTS_REQUEST_SUCCESS:      
      return {
        ...state,
        data: {
          ...state.data,
          allContacts: {
            ...action.data,
          },
        },
      };
    case SET_POST_CARD_ID:
      return {
        ...state,
        data: {
          ...state.data,
          postcardId: action.id,
        },
      };
    case UPDATE_PHONE_NUMBERS_POSTCARD:
      return {
        ...state,
        data: {
          ...state.data,
          [action.key]: action.value,
        },
      };
    case ADD_ERRORS_POSTCARD:
      return {
        ...state,
        errors: {
          ...state.errors,
          [action.key]: action.value,
        },
      };
    case DELETE_ERRORS_NUMBER_POSTCARD:
      const filtered = Object.keys(state.errors)
        .filter(item => item !== action.key)
        .reduce((obj, key) => {
          obj[key] = state.errors[key];
          return obj;
        }, {});
      return {
        ...state,
        errors: {
          ...filtered,
        },
      };

    case POSTCARD_DISABLE_BTN:
      return {
        ...state,
        disableSubmitBtn: action.data,
      };
    case TOGGLE_SAVED_TEMPLATE_MODAL:
      return {
        ...state,
        showSaveTemplateModal: !state.showSaveTemplateModal,
      };
    case GET_MY_POSTCARD_TEMPLATES_REQUEST_SUCCESS:
      return {
        ...state,
        postcardTemplatesData: action.data,
      };
    case GET_UNI_MY_POSTCARD_TEMPLATES_REQUEST_SUCCESS:
      return {
        ...state,
        postcardTemplatesData: action.data,
      };
    case CHANGE_POSTCARD_TEMPLATE_TAB_INDEX:
      return {
        ...state,
        templateTabIndex: action.data,
      };
    case CHANGE_POSTCARD_TEMPLATES_IS_LOADED:
      return {
        ...state,
        postcardTemplatesIsLoaded: action.data,
      };
    case RESET_POSTCARD_TEMPLATES_DATA:
      return {
        ...state,
        postcardTemplatesData: [],
      };
    case RESET_ALL_POSTCARD_TEMPLATES:
      return {
        ...state,
        allPostcardsTemplate: [],
      };
    case GET_POSTCARD_BY_ID_REQUEST_SUCCESS:
      return {
        ...state,
        data: {
          ...state.data,
          template: action.data,
        },
      };
    case TOGGLE_DELETE_TEMPLATE_MODAL:
      return {
        ...state,
        showDeleteTemplateModal: !state.showDeleteTemplateModal,
      };
    case GET_TEMPLATES_WITH_CATEGORY_REQUEST_SUCCESS:
      return {
        ...state,
        categoryTemplates: action.data,
      };
    case CHANGE_CATEGORY_TEMPLATE_IS_LOADED:
      return {
        ...state,
        categoryTemplatesIsLoaded: action.data,
      };
    case RESET_CATEGORY_TEMPLATES:
        return {
            ...state,
            categoryTemplates: [],
        };
    case SET_POSTAGE_DATA:            
      return {
          ...state,
          data: {
              ...state.data,
              postage: action.data
          },
      }; 
    case SET_USER_POSTCARD_IMAGES: {
      let arr = [...state.images];
      const newImg = {
          file: action.fileImg,
          nextImg: action.nextImg,
          prevImg: action.prevImg,
      }
      
      if(arr && arr.length) {
          //Remove old img
          arr = arr.filter(item => {
              return action.prevImg !== item.nextImg
          })
              arr.push(newImg);
      } else {
          arr = [
              newImg
          ]
      }
  
      return {
        ...state,
        images: [...arr]
      }
    }        
    case RESET_TO_FIRST_STEP_POSTCARD:
      return {
        ...state,
        images: [],
      }
    case SET_SENDING_TO_MY_ADDRESS:
      return {
        ...state,
        data: {
            ...state.data,
            sendingToMyAddress: action.data
        },
      }
    case GET_POSTCARD_PREVIEW_IMG_SUCCESS:
      return {
        ...state,
        data: {
          ...state.data,          
          images: action.data,
        },        
      };
    default:
      return state;
    }
}
