import {
  CREATE_NEW_CAMPAIGN_REQUEST_SUCCESS,
  RESET_CAMPAIGNS_PAGE,
  RESET_SENDING_DATA,
  RESET_NEW_CAMPAIGN_PAGE,
  GET_CAMPAIGNS_DATA_REQUEST,
  GET_CAMPAIGNS_DATA_REQUEST_SUCCESS,
  GET_CAMPAIGNS_DATA_REQUEST_FAILED,
  GET_CAMPAIGN_REQUEST_SUCCESS,
  GET_CAMPAIGN_LISTS_DATA_REQUEST,
  GET_CAMPAIGN_LISTS_DATA_REQUEST_SUCCESS,
  GET_CAMPAIGN_LISTS_DATA_REQUEST_FAILED,
  SET_NEW_CAMPAIGN_DATA,
  SET_SENDING_TRACKING_NUMBER,  
  SET_NEW_SENDING_DATA,
  GET_ALL_POST_CARD_TEMPLATE_FOR_CAMPAIGN_REQUEST,
  GET_ALL_POST_CARD_TEMPLATE_FOR_CAMPAIGN_REQUEST_SUCCESS,
  GET_ALL_POST_CARD_TEMPLATE_FOR_CAMPAIGN_REQUEST_FAILED,
  GET_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST,
  GET_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST_SUCCESS,
  GET_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST_FAILED,
  GET_UNI_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST,
  GET_UNI_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST_SUCCESS,
  GET_UNI_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST_FAILED,
  TOGGLE_POSTCARD_MODAL,
  TOGGLE_POSTCARD_APPROVAL_MODAL,
  SET_INDEX_SELECTED_POSTCARD_TEMPLATE_CAMPAIGN,
  GET_POSTCARD_BY_ID_CAMPAIGN_REQUEST_SUCCESS,
  SET_MY_POSTCARD_BY_INDEX_CAMPAIGN,  
  SET_SELECTED_POSTCARD_TEMPLATE,  
  GET_SENDING_DATA_REQUEST,
  GET_SENDING_DATA_REQUEST_SUCCESS,
  GET_SENDING_DATA_REQUEST_FAILED,
  RESET_POSTCARD_TEMPLATES_DATA_CAMPAIGN,
  SET_SELECTED_BACK_KEY,
  RESET_NEW_SENDING_PAGE,
  SET_EDITED_SENDING,
  TOGGLE_DELETE_SENDING_MODAL,
  SET_TAB_POSTCARD_MODAL,
  UPDATE_SENDING_REQUEST_SUCCESS,
  SAVE_EDITED_SENDING_TEMPLATE,
  TOGGLE_CLONE_SENDING_MODAL,
  GET_AVAILABLE_SENDING_DATA_REQUEST,
  GET_AVAILABLE_SENDING_DATA_REQUEST_SUCCESS,
  GET_AVAILABLE_SENDING_DATA_REQUEST_FAILED,
  RESET_CLONE_SENDING_MODAL,  
  CLONE_NEW_SENDING_REQUEST_SUCCESS,  
  SET_REPORT_CAMPAIGN_DATA,
  GET_REPORT_CAMPAIGN_NAMES_REQUEST,
  GET_REPORT_CAMPAIGN_NAMES_REQUEST_SUCCESS,
  GET_REPORT_CAMPAIGN_NAMES_REQUEST_FAILED,
  GET_REPORT_CAMPAIGN_STATISTIC_REQUEST,
  GET_REPORT_CAMPAIGN_STATISTIC_REQUEST_SUCCESS,
  GET_REPORT_CAMPAIGN_STATISTIC_REQUEST_FAILED,
  GET_REPORT_CAMPAIGN_CONVERSION_REQUEST,
  GET_REPORT_CAMPAIGN_CONVERSION_REQUEST_SUCCESS,
  GET_REPORT_CAMPAIGN_CONVERSION_REQUEST_FAILED,  
  RESET_REPORT_CAMPAIGNS_PAGE,
  GET_POSTCARD_CAMPAIGN_PREVIEW_IMG_SUCCESS,
} from 'actions/campaigns';

let initialState = {
  campaignsDataIsLoading: false,
  listsDataIsLoading: false,
  sendingDataIsLoading: false,
  showPostcardModal: false,
  showPostcardApprovalModal: false,
  showDeleteSendingModal: false,
  showCloneSendingModal: false,
  postcardTab: 1,
  indexSelectedPostcard: null,
  phoneNumberType: 'trackingNumber',
  editedTemplate: null,
  campaignsData: [],
  sendingData: [],
  availableSendingData: [],
  listsData: [],
  templatesData: '',
  allTemplatesIsLoading: false,
  myPostcardIsLoading: false,
  newCampaignData: {
    name: '',
    list: '',
    postage: 'usps_standard',
    verifiedContacts: 0,
    campaign: null,
  },
  reportCampaign: {    
    reportCampaignLoading: false,
    statisticIsLoading: false,
    conversionIsLoading: false,

    campaignId: '',
    campaignName: '', 
    listName: '',

    totalSendingsSent:0,
    totalPostcardsSent:0,
    callsReceived: 0,
    conversionRate: 0,

    totalSendings: 0,
    
    sentSendings: [],      
  },
  newSendingData: {
    trackingNumber: '',
    ownNumber: '',
    sendingDate: null,
  },
  postcardSize: '6x9',
  backMarkPlace: '<div class="morpheus-mark-bg" style="-webkit-box-sizing: inherit; background-color: #ffffff; bottom: 0; box-sizing: inherit; height: 800px; pointer-events: none; position: absolute; right: 0; width: 1300px;"/>',
  images: {
    front: {
      mini: '',
      full: ''
    },
    back: {
      mini: '',
      full: ''
    }
  },   
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case RESET_CAMPAIGNS_PAGE:
      return {
        ...initialState,
      };
    case RESET_SENDING_DATA:
      return {
        ...state,
        sendingData: [], 
      };
    case RESET_NEW_CAMPAIGN_PAGE:
      return {
        ...state,
        listsData: [],        
        newCampaignData: {
          ...initialState.newCampaignData,
        },
      };
    case CREATE_NEW_CAMPAIGN_REQUEST_SUCCESS:
      return {
        ...state,
        newCampaignData: {
          ...state.newCampaignData,
          campaign: action.data,
        },
      };
    case GET_CAMPAIGNS_DATA_REQUEST:
      return {
        ...state,
        campaignsDataIsLoading: true,
      };
    case GET_CAMPAIGNS_DATA_REQUEST_SUCCESS:
      return {
        ...state,
        campaignsData: action.data,
        campaignsDataIsLoading: false,
      };
    case GET_CAMPAIGNS_DATA_REQUEST_FAILED:
      return {
        ...state,
        campaignsDataIsLoading: false,
      };
    case GET_CAMPAIGN_LISTS_DATA_REQUEST:
      return {
        ...state,
        listsDataIsLoading: true,
      };
    case GET_CAMPAIGN_LISTS_DATA_REQUEST_SUCCESS:
      return {
        ...state,
        listsData: action.data,
        listsDataIsLoading: false,
      };
    case GET_CAMPAIGN_LISTS_DATA_REQUEST_FAILED:
      return {
        ...state,
        listsDataIsLoading: false,
      };
    case SET_NEW_CAMPAIGN_DATA:
      return {
        ...state,
        newCampaignData: action.data,
      };
    case GET_CAMPAIGN_REQUEST_SUCCESS:
      return {
        ...state,
        newCampaignData: action.data,
      };
    case SET_SENDING_TRACKING_NUMBER:
      return {
        ...state,
        phoneNumberType: action.data.type,
        newSendingData: {
          ...state.newSendingData,
          trackingNumber: action.data.trackingNumber,
          ownNumber: '',
        },
      };    
    case SET_NEW_SENDING_DATA:
      return {
        ...state,
        newSendingData: {
          ...state.newSendingData,
          [action.key]: action.value,
        },
      };
    case GET_ALL_POST_CARD_TEMPLATE_FOR_CAMPAIGN_REQUEST:
      return {
        ...state,
        allTemplatesIsLoading: true,
      };
    case GET_ALL_POST_CARD_TEMPLATE_FOR_CAMPAIGN_REQUEST_SUCCESS:
    case GET_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST_SUCCESS:
      return {
        ...state,
        templatesData: action.data,
        allTemplatesIsLoading: false,
        myPostcardIsLoading: false,
      };
    case GET_ALL_POST_CARD_TEMPLATE_FOR_CAMPAIGN_REQUEST_FAILED:
      return {
        ...state,
        allTemplatesIsLoading: false,
      };
    case GET_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST:
      return {
        ...state,
        myPostcardIsLoading: true,
      };
    case GET_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST_FAILED:
      return {
        ...state,
        myPostcardIsLoading: false,
      };      
    case GET_UNI_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST_SUCCESS:
      return {
        ...state,
        templatesData: action.data,
        allTemplatesIsLoading: false,
        myPostcardIsLoading: false,
      };      
    case GET_UNI_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST:
      return {
        ...state,
        myPostcardIsLoading: true,
      };
    case GET_UNI_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST_FAILED:
      return {
        ...state,
        myPostcardIsLoading: false,
      };
    case TOGGLE_POSTCARD_MODAL:
      return {
        ...state,
        showPostcardModal: action.data,
      };
    case TOGGLE_POSTCARD_APPROVAL_MODAL:      
      return {
        ...state,
        showPostcardApprovalModal: action.data,
      };
    case SET_INDEX_SELECTED_POSTCARD_TEMPLATE_CAMPAIGN:
      return {
        ...state,
        indexSelectedPostcard: action.index,
      };    
    case GET_POSTCARD_BY_ID_CAMPAIGN_REQUEST_SUCCESS:       
      return {
        ...state,
        editedTemplate: action.data,
      };

    case SET_MY_POSTCARD_BY_INDEX_CAMPAIGN:      
      return {
        ...state,
        editedTemplate: action.data,
      };    
    case GET_SENDING_DATA_REQUEST:
      return {
        ...state,
        sendingDataIsLoading: true,
      };
    case GET_SENDING_DATA_REQUEST_SUCCESS:
      return {
        ...state,
        sendingData: action.data,
        sendingDataIsLoading: false,
      };
    case GET_SENDING_DATA_REQUEST_FAILED:
      return {
        ...state,
        sendingDataIsLoading: false,
      };
    case RESET_POSTCARD_TEMPLATES_DATA_CAMPAIGN:
      return {
        ...state,
        templatesData: '',
        indexSelectedPostcard: null,
      };
    case RESET_NEW_SENDING_PAGE:
      return {
        ...state,
        editedTemplate: null,
        phoneNumberType: 'trackingNumber',
        indexSelectedPostcard: null,
        postcardTab: 1,
        newSendingData: {
          ...initialState.newSendingData,
        },
      };
    case SET_SELECTED_POSTCARD_TEMPLATE:
      return {
        ...state,
        editedTemplate: action.data,
      };
    
    case SET_SELECTED_BACK_KEY:
      return {
        ...state,
        editedTemplate: {
          ...state.editedTemplate,
          selectedBackKey: action.data.key,
          back: action.data.back,
        },
      };

    case SET_EDITED_SENDING: {      
      return {
        ...state,        
        editedTemplate: { ...action.data.template, previewUrls: {...action.data.previewUrls} },              
        phoneNumberType: (action.data.trackingNumber && action.data.trackingNumber.isOwn)
          ? 'ownNumber'
          : 'trackingNumber',
        newSendingData: {
          sendingDate: action.data.sendingDate,
          status: action.data.status,
          [`${(action.data.trackingNumber && action.data.trackingNumber.isOwn)
            ? 'ownNumber'
            : 'trackingNumber'}`]: action.data.trackingNumber? action.data.trackingNumber.trackingNumber : '',
        },
      };  
    }  

    case TOGGLE_DELETE_SENDING_MODAL:
      return {
        ...state,
        showDeleteSendingModal: !state.showDeleteSendingModal,
      };
    case SET_TAB_POSTCARD_MODAL:
      return {
        ...state,
        postcardTab: action.data,
      };
    case UPDATE_SENDING_REQUEST_SUCCESS:
      return {
        ...state,
        sendingData: [],
      };
    case SAVE_EDITED_SENDING_TEMPLATE:
      return {
        ...state,
        editedTemplate: {
          ...state.editedTemplate,
          front: action.data.front,
          back: action.data.back,
        },
      };
    case TOGGLE_CLONE_SENDING_MODAL:
      return {
        ...state,
        showCloneSendingModal: !state.showCloneSendingModal,
      };
    case GET_AVAILABLE_SENDING_DATA_REQUEST:
      return {
        ...state,
        availableSendingDataIsLoading: true,
      };
    case GET_AVAILABLE_SENDING_DATA_REQUEST_SUCCESS:
      return {
        ...state,
        availableSendingData: action.data,
        availableSendingDataIsLoading: false,
      };
    case GET_AVAILABLE_SENDING_DATA_REQUEST_FAILED:
      return {
        ...state,
        availableSendingDataIsLoading: false,
      };
    case RESET_CLONE_SENDING_MODAL:
      return {
        ...state,
        newSendingData: { ...initialState.newSendingData },
        availableSendingData: [],
      };    
    case CLONE_NEW_SENDING_REQUEST_SUCCESS:
      return {
        ...state,
        sendingData: [
          ...state.sendingData,
          action.data
        ],
      }; 
    case SET_REPORT_CAMPAIGN_DATA:
      return {
        ...state,
        reportCampaign: {
          ...state.reportCampaign,
          ...action.data
        },
      }; 
      
    case GET_REPORT_CAMPAIGN_NAMES_REQUEST:
      return {
        ...state,
        reportCampaign: {
          ...state.reportCampaign,
          statisticIsLoading: true,
        },
      };
    case GET_REPORT_CAMPAIGN_NAMES_REQUEST_SUCCESS:              
      return {
        ...state,
        reportCampaign: {          
          ...state.reportCampaign,
          statisticIsLoading: false,
          campaignName: action.data.name ? action.data.name : '',
          listName: action.data.list.name ? action.data.list.name : '',
        },        
      };
    case GET_REPORT_CAMPAIGN_NAMES_REQUEST_FAILED:
      return {
        ...state,
        reportCampaign: {          
          ...state.reportCampaign,
          statisticIsLoading: false,          
        }, 
      }; 

    case GET_REPORT_CAMPAIGN_STATISTIC_REQUEST:
      return {
        ...state,
        reportCampaign: {
          ...state.reportCampaign,
          statisticIsLoading: true,
        },
      };
    case GET_REPORT_CAMPAIGN_STATISTIC_REQUEST_SUCCESS:      
      return {
        ...state,
        reportCampaign: {          
          ...state.reportCampaign,
          statisticIsLoading: false,          
          ...action.data,
        },        
      };
    case GET_REPORT_CAMPAIGN_STATISTIC_REQUEST_FAILED:
      return {
        ...state,
        reportCampaign: {          
          ...state.reportCampaign,
          statisticIsLoading: false,                    
        }, 
      }; 

    case GET_REPORT_CAMPAIGN_CONVERSION_REQUEST:
      return {
        ...state,
        reportCampaign: {
          ...state.reportCampaign,
          conversionIsLoading: true,          
        },
      };
    case GET_REPORT_CAMPAIGN_CONVERSION_REQUEST_SUCCESS:      
      return {
        ...state,
        reportCampaign: {          
          ...state.reportCampaign,
          conversionIsLoading: false,          
          ...action.data,
        },        
      };
    case GET_REPORT_CAMPAIGN_CONVERSION_REQUEST_FAILED:
      return {
        ...state,
        reportCampaign: {          
          ...state.reportCampaign,
          conversionIsLoading: false,
        }, 
      }; 
    case RESET_REPORT_CAMPAIGNS_PAGE:      
      return {
        ...state,
        reportCampaign: {},
      };
    case GET_POSTCARD_CAMPAIGN_PREVIEW_IMG_SUCCESS:
      return {
        ...state,                 
        images: action.data,
      };

    default:
      return state;
  }
}
