import React from 'react';
import { Card, CardBody, Button, CardHeader } from 'reactstrap';
import { JsonEditor } from 'jsoneditor-react';
import { FadeLoader } from 'react-spinners';
import Builder from 'components/Builder/SignBuilder';
import 'jsoneditor-react/es/editor.min.css';

export default class extends React.Component {
    state = {
        category: 'yard-sign',
        jsonInp: {},
        template: {}
    }

    onJsonInputChanged = (e) => {
        this.setState({ newTemplateStr: e.target.value });
    }

    onSubmitJsonInput = () => {
        if (!this.state.jsonInp) return;
        this.setState({ template: JSON.parse(JSON.stringify(this.state.jsonInp)) });
    }

    render() {
        return (
            <Card>
                <CardHeader style={{ textAlign: 'center', fontWeight: 'bold', fontSize: 25 }}>
                    Add New Template
                    </CardHeader>
                <CardBody>
                    <div style={{ display: 'flex', flex: 1 }}>
                        <div style={{ flex: 1 }}>
                            <JsonEditor
                                value={this.state.jsonInp}
                                mode='text'
                                onChange={(jsonInp) => this.setState({ jsonInp })}
                            />
                            {this.props.loading ?
                                <FadeLoader color={'#403d39'} /> :
                                <div style={{ display: 'flex' }}>
                                    <Button onClick={this.onSubmitJsonInput}>Show preview</Button>
                                    <Button onClick={() => this.props.onSave(this.state.jsonInp)} style={{ marginLeft: 10 }}>Save</Button>
                                </div>
                            }
                        </div>
                        <div style={{ flex: 1, paddingLeft: 20, paddingRight: 20, display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                            {this.state.template.content &&
                                <Builder
                                    data={{ template: this.state.template }}
                                    disableColorPickers
                                    onDataChange={(data) => this.setState({ template: data, jsonInp: data })}
                                    id={this.props.id}
                                />
                            }
                        </div>
                    </div>
                </CardBody>
            </Card>
        );
    }
}