import React from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import './DeleteTrackingNumberModal.scss';

export default function RemoveTrackingNumberModal(props) {
  const onClose = () => {
    props.toggleRemoveTrackingNumberModal(false)
  }

  const resetModal = () => {
    props.setRemoveTrackingNumberData({trackingNumberId: '', trackingNumber: ''})
  }
  return (
    <div>
      <DopeModal
        isOpen={props.open}
        onClosed={resetModal}
        className='remove-tracking-number-modal'
        centered
      >
        <DopeModalHeader>Delete Tracking Number</DopeModalHeader>
        <DopeModalBody>
          <p>
            Are you sure you want to delete the tracking number? The tracking
            number will be automatically unbooked, which means that anyone else
            can book and use it. You cannot undo this action.
          </p>
        </DopeModalBody>
        <DopeModalFooter>
          <DopeButton 
            color='white'
            onClick={onClose}
          >Cancel</DopeButton>
          <DopeButton color='primary' onClick={props.deleteTrackingNumber}>Delete</DopeButton>
        </DopeModalFooter>
      </DopeModal>
    </div>
  );
}
