import React, {useEffect, useState} from 'react';
import { useHistory, useRouteMatch } from 'react-router-dom';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { EDDM_PATH } from 'utils/constants';
import './EddmList.scss';
import {useDispatch, useSelector} from "react-redux";
import {DopeTable} from "components/DopeTable";
import {columnsFunc} from "./config";
import {Form, FormGroup} from "reactstrap";
import { ReactComponent as SearchIco } from 'assets/img/icons/search.svg';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import {
  getEddmListRequest,
  deleteEddmItemRequest,  
} from "actions/eddm";
import moment from "moment";
import {FadeLoader} from "react-spinners";
// import { debounce } from 'utils/helper';
import DeleteEddmItemPopup from "./DeleteEddmItemPopup";

export default function EddmList({savedAccountId}) {  
  let { url } = useRouteMatch();
  let history = useHistory();
  const dispatch = useDispatch();
  const currentAccountId = useSelector(state => state.account.currentAccount._id); 

  useEffect(() => {
    const data = {
      accountId: currentAccountId,
      query: {
        page: 0,
        limit: 10,
      },
    };
    setSearchValue('');
    dispatch(getEddmListRequest(data));
  }, [currentAccountId]);
  
  const eddmList = useSelector(state => state.eddm.eddmList);  
  const isLoading = useSelector(state => state.eddm.isLoading);
  const [searchValue, setSearchValue] = useState('');
  const [deletePopupOpen, setDeletePopupOpen] = useState(false);
  const [rowInfo, setRowInfo] = useState({});
  const [searchTriggered, setSearchTriggered] = useState(false);

  

  const deleteEddmItem = () => {
    dispatch(deleteEddmItemRequest(rowInfo))
  };

  const onSort = (field, order)=>{    
    if(field === 'address') field = 'totalDeliveryAddresses';  //because in DB saved as totalDeliveryAddresses for every Eddm
    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder };
    updateData(pagination);
  };
  const columns = columnsFunc(onSort);

  const customColumn = {
    dataField: "remove",
    text: "",
    sort: false,
    formatter: () => <DeleteIcon className="del-icon"/>,
    attrs: { className: "icon-cell" }
  };

  const rowClasses = (route, index) => {
    if(route.status === 'sent') return 'table-row sent';
    if(route.status === 'in-progress') return 'table-row in-progress';
    return 'table-row';
  };

  const updateData = (pagination = { page: eddmList.query.page, limit: 10, sort: eddmList.query.sort }) => {    
    if(!pagination.sort && eddmList.query.sort) pagination.sort = eddmList.query.sort;
    if(pagination.zip===undefined && eddmList.query.zip) pagination.zip = eddmList.query.zip;     
    let data = {
      accountId: currentAccountId,
      query: {
        ...pagination,
        page: pagination.page - 1,
      }
    };    
    dispatch(getEddmListRequest(data))
  };

  const paginationOptions = {
    page: eddmList.query ? eddmList.query.page + 1 : 1,
    limit: eddmList.query ? eddmList.query.limit : 10,
    onPageChange: updateData,    
    totalSize: eddmList.total
  };

  // const debounceOnChange = React.useCallback(debounce(updateData, 700), []);

  const onChangeSearchValue = (e) => {    
    !searchTriggered&&setSearchTriggered(true);
    setSearchValue(e.target.value);
    //  donn't work with debounce (used prev first account in updateData)
    // debounceOnChange({ page: 1, limit: 10, sort: eddmList.query.sort, zip: e.target.value });   //zip there -- is search zip!!
    updateData({ page: 1, limit: 10, sort: eddmList.query.sort, zip: e.target.value });
  };

  return (
    <div className='EddmList'>
      <div className="title-container">
        <h1 className="title">Every Door Direct Mail</h1>
        <DopeButton
          color="primary"
          onClick={()=>{history.push(`${EDDM_PATH}/create`);}}
        >
          create New EDDM order
        </DopeButton>
      </div>
      <div className="eddmListTable">
        <div className="eddmList-header">
          <div>
            {eddmList.total > 0
              ? <span>{eddmList.total} EDDM orders</span>
              : <span>No EDDM’s Found</span>
            }
          </div>
          {((eddmList.total > 0)||searchTriggered) ? (
            <div className="zipSearch">
              <Form onSubmit={e => e.preventDefault()}>
                <FormGroup className='dope-form-group searchZip'>
                  <SearchIco />
                  <DopeInput
                    type="number"
                    value={searchValue}
                    min='0'
                    placeholder={'Search by ZIP Code'}
                    onChange={onChangeSearchValue}
                  />
                </FormGroup>
              </Form>
            </div>
          ):null
          }
        </div>
        { (eddmList.total > 0) ? (
            <DopeTable
              keyField="_id"
              columns={[...columns, customColumn]}
              data={eddmList.data.map((item) => (
                {
                  _id: item._id,
                  zip: item.zip,
                  eddmRoutes: item.eddmRoutes.map(el => <span key={el.route}>{`${el.route.slice(0,5)}-${el.route.slice(5)}`}</span>),
                  address: item.totalDeliveryAddresses,
                  status: item.status,
                  price: `$ ${item.price ? (item.price/100).toFixed(2) : 0}`,
                  createdAt: moment(item.createdAt).format('MMM DD, YYYY'),
                }
              ))}
              rowClasses={rowClasses}
              loading={isLoading}
              paginationOptions={paginationOptions}
              rowEvents={{
                onClick: (e, row, rowIndex) => {
                  if(e.target.tagName === 'svg' || e.target.tagName === 'path'){
                    setRowInfo({
                      sendingId: row._id,
                      accountId: currentAccountId
                    });
                    setDeletePopupOpen(true);
                    return;
                  }
                  const {_id, status='new'} = row;                  
                  switch(status) {                    
                    case 'new':                      
                      history.push(`${url}/create`);                      
                      break;
                    case 'draft-route':                      
                      history.push(`${url}/${_id}/edit-route`);                      
                      break;
                    case 'draft':
                    case 'design-request':                     
                      history.push(`${url}/${_id}/edit`);                      
                      break;
                    case 'sent': 
                    case 'in-progress':     
                      history.push(`${url}/${_id}/view`);
                      break;
                    default:                      
                      history.push(`${url}/create`);
                  } 
                }
              }}
            />
          ) : (
            <div className="noEddmList">
              {isLoading && (
                <div className='loader-container'>
                  <FadeLoader size={100} color={'#403d39'} />
                </div>
              )}
              <span>No Orders Found</span>
            </div>
          )
        }

      </div>
      <DeleteEddmItemPopup
        isOpen={deletePopupOpen}
        toggle={() => setDeletePopupOpen(!deletePopupOpen)}
        deleteEddmItem={deleteEddmItem}
      />
    </div>
  );
}
