import React from 'react';
import Toolbar from 'react-big-calendar/lib/Toolbar';

export default class CustomToolbar extends Toolbar {
  // componentDidMount() {
  //   const view = this.props.view;
  // }
  render() {
    // let { localizer: { messages }, label } = this.props;
    let { label } = this.props;
    return (
      <div className='rbc-toolbar'>
        <span className="rbc-toolbar-fill-block"/>
        <div className='rbc-toolbar-label-container'>
          <button
            className='arrow-left'
            type='button'
            onClick={this.navigate.bind(null, 'PREV')}
          />
          <span className='rbc-toolbar-label'>{label}</span>
          <button
            className='arrow-right'
            type='button'
            onClick={this.navigate.bind(null, 'NEXT')}
          />
        </div>
        <div className='calendar-period-btn'>
          <button
            onClick={this.view.bind(null, 'day')}
            type='button'
            className={this.props.view === 'day' ? 'rbc-active' : ''}
          >
            Day
          </button>
          <button
            className={this.props.view === 'week' ? 'rbc-active' : ''}
            type='button'
            onClick={this.view.bind(null, 'week')}
          >
            Week
          </button>
          <button
            className={this.props.view === 'month' ? 'rbc-active' : ''}
            type='button'
            onClick={this.view.bind(null, 'month')}
          >
            Month
          </button>
        </div>
      </div>
    );
  }
  navigate = action => {
    this.props.onNavigate(action);
  };
}
