import React from 'react';
import { Card, CardHeader, CardBody } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import './ReturnAddress.scss';

export default function ShippingAddress(props) {
  const address = [
    {
      title: 'Address 1',
      prop: 'address1',
    },
    {
      title: 'Address 2 - Optional',
      prop: 'address2',
    },
    {
      title: 'City',
      prop: 'city',
    },
    {
      title: 'State',
      prop: 'state',
    },
    {
      title: 'Postal Code',
      prop: 'zip',
    },
  ];

  return (
    <React.Fragment>
      {props.data.amount ? (
        <h4 className='wizard-block-header return-address-header'>
          Shipping Address - Where Do You Want Your Signs Sent?
        </h4>
      ) : (
        ''
      )}
      <Card className='shipping-address-container'>
        <CardHeader>{props.children}</CardHeader>
        <CardBody>
          {address.map((x, key) => (
            <div className='field' key={key}>
              <DopeInput
                type='text'
                name={x.prop}
                id={x.prop}
                placeholder={x.title}
                value={props.data.shippingAddress[x.prop] || ''}
                error={props.errors[x.prop]}
                onChange={e => {
                  props.onChange(x.prop, e.target.value);
                  props.addErrors(x.prop, '');
                }}
              />
            </div>
          ))}
        </CardBody>
      </Card>
    </React.Fragment>
  );
}