import React, {useEffect, useState} from 'react';
import { useSelector } from 'react-redux';
import { FormGroup} from "reactstrap";
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeTextarea } from 'components/DopeForms/DopeTextarea/DopeTextarea';
import {useDispatch} from "react-redux";
import {columnsFunc} from "./config";
import {DopeTable} from "components/DopeTable";
import moment from "moment";
import {
  sendSmsRequest,  
} from "actions/adminAccounts";
import {  
  getSmsHistoryRequest,
  resetSmsHistory,
} from "actions/account";

import './Sms.scss';

const Sms = (props) => {
  const dispatch = useDispatch();
  const [textSms, setTextSms] = useState('');
  const [textSmsError, setTextSmsError] = useState('');
  const smsHistory = useSelector(state=>state.account.currentAccount.smsHistory)||{};
  const [isLoadingHistory,setIsLoadingHistory] = useState('false');

  const {data:smsHistoryData=[],totalSize,page,limit}=smsHistory;

  useEffect(() => {
    setIsLoadingHistory(true);
    const sendData = {
      accountId: props.currentAccount && props.currentAccount._id,
      query: {
        page: 0,
        limit: 5,
      },
    };
    dispatch(getSmsHistoryRequest(sendData));
    setIsLoadingHistory(false);
    return () => {
      dispatch(resetSmsHistory());
    };
  }, []);

  
  const columns = columnsFunc();

  const updateDataSmsHistory = (pagination = {page,limit}) => {
    let sendData = {
      accountId: props.currentAccount && props.currentAccount._id,
      query: {
        ...pagination,
        page: pagination.page - 1,
      }
    };    
     dispatch(getSmsHistoryRequest(sendData));
  };
  const paginationOptions = {
    page: page+1,
    limit,
    onPageChange: updateDataSmsHistory,    
    totalSize: totalSize,
  };  

  const onChange = e => {
    setTextSms(e.target.value);
    setTextSmsError('');
  };
  const validate = ()=>{    
    if (!textSms.length){
      setTextSmsError('Please, enter some text into SMS');
      return false;
    }
    return true;    
  }
  const callbackSuccess=()=>{
    setTextSms('')
  }

  const onSubmit = () => {  
    if(!validate()){
      return;
    }
    const data = {
      accountId: props.currentAccount && props.currentAccount._id,
      body: {
        description: textSms,
      }
    };
    dispatch(sendSmsRequest(data, callbackSuccess)); 
  };

  return(
    <React.Fragment>
      <div className='sms-container'>
        <div className="send-sms-container">        
          <h6 className="block-title">SMS</h6>            
          <FormGroup className="dope-form-group">
            <DopeTextarea
              value={textSms}
              // title='textSms'
              name='textSms'                
              error={textSmsError}                
              onChange={onChange}
            />
          </FormGroup>            
          <div className='account-button-container'>
            <DopeButton
              disabled={false}
              color="primary"
              onClick={onSubmit}
            >
              Send SMS
            </DopeButton>
          </div>
        </div>
        {(smsHistory&&smsHistoryData.length)>0&&(
          <div className="sms-history-container">        
            <h6 className="block-title">SMS history</h6> 
            <DopeTable
              keyField="_id"
              columns={columns}
              data={smsHistoryData.map((item) => (
                {
                  _id: item._id,
                  description: item.description,                  
                  createdAt: moment(item.createdAt).format('MMM DD, YYYY'),
                }
              ))}              
              loading={isLoadingHistory}
              paginationOptions={paginationOptions}
              // rowEvents={{
              //   onClick: (e, row, rowIndex) => {
              //     console.log("SMSHistory row:",row);                
              //   }
              // }}
            />
          </div> 
        )} 
      </div>
    </React.Fragment>
  )
};

export default Sms;