import React, { useState } from 'react';
import { Container, Card, CardBody, InputGroupAddon, Row, Col } from 'reactstrap';
import { DopeInputGroup } from 'components/DopeForms/DopeInputGroup/DopeInputGroup';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeDatePicker } from 'components/DopeForms/DopeDatePicker/DopeDatePicker';

import 'react-datepicker/dist/react-datepicker.css';
import CalendarIcon from 'assets/img/contacts/calendar.png';
import './FilterPanel.scss';

const FilterPanel = ({
  startDate,
  endDate,
  jobName,
  minPrice,
  maxPrice,
  clearFilter,
  performFilter,
  handleChange,
}) => {
  const [sd, setSd] = useState(startDate);
  const [ed, setEd] = useState(endDate);
  const [jn, setJn] = useState(jobName);
  const [minP, setMinp] = useState(minPrice);
  const [maxP, setMaxp] = useState(maxPrice);

  return (
    <Card>
      <CardBody>
        <Container className='filter-panel-container'>
          <Row>
            <Col md={4}>
              <DopeInputGroup>
                <DopeDatePicker
                  selected={sd}
                  selectsStart
                  startDate={sd}
                  endDate={ed}
                  onChange={date => {
                    handleChange('startDate', date);
                    setSd(date);
                  }}
                  placeholderText='From (select start date)'
                />
                <InputGroupAddon addonType='append'>
                  <img
                    className='input-icon'
                    src={CalendarIcon}
                    alt='calendar_csv'
                  />
                </InputGroupAddon>
              </DopeInputGroup>
            </Col>
            <Col md={4}>
              <DopeInputGroup>
                <DopeDatePicker
                  selected={ed}
                  selectsEnd
                  startDate={sd}
                  endDate={ed}
                  onChange={date => {
                    handleChange('endDate', date);
                    setEd(date);
                  }}
                  minDate={sd}
                  placeholderText='To (select end date)'
                />
                <InputGroupAddon addonType='append'>
                  <img
                    className='input-icon'
                    src={CalendarIcon}
                    alt='calendar_csv'
                  />
                </InputGroupAddon>
              </DopeInputGroup>
            </Col>
            <Col md={4}>
              <DopeInputGroup>
                <DopeInput
                  value={jn}
                  onChange={e => {
                    handleChange('jobName', e.target.value);
                    setJn(e.target.value);
                  }}
                  placeholder='Job name'
                />
              </DopeInputGroup>
            </Col>
          </Row>
          <Row>
            <Col md={4}>
              <DopeInputGroup>
                <DopeInput
                  value={minP}
                  onChange={e => {
                    handleChange('minPrice', e.target.value);
                    setMinp(e.target.value);
                  }}
                />
              </DopeInputGroup>
            </Col>
            <Col md={4}>
              <DopeInputGroup>
                <DopeInput
                  value={maxP}
                  onChange={e => {
                    handleChange('maxPrice', e.target.value);
                    setMaxp(e.target.value);
                  }}
                />
              </DopeInputGroup>
            </Col>
            <Col md={4}>
              <div className="button-group">
                <DopeButton color='primary' onClick={performFilter}>
                  Search
                </DopeButton>
                <DopeButton
                  color='secondary'
                  onClick={() => {
                    setSd(null);
                    setEd(null);
                    setJn('');
                    setMaxp(0);
                    setMinp(0);
                    clearFilter();
                  }}
                >
                  Clear Filter
                </DopeButton>
              </div>
            </Col>
          </Row>
        </Container>
      </CardBody>
    </Card>
  );
};

export default FilterPanel;
