import axios from 'axios';
import history from '../history';

export default async function request(link, options) {
    
    if( !JSON.parse(localStorage.getItem('authdata'))) {
        return {};
    }
    if (options.queries) {
        let query = Object.keys(options.queries).map(key => `${key}=${options.queries[key]}`).join('&');
        let startCharacter = link.includes('?') ? '&' : '?';
        link += startCharacter + query;
    }

    let response;
    try {
        response = axios({
            method: options.method,
            url: link,
            data: options.body,
            headers: {
                'Authorization': `Bearer ${JSON.parse(localStorage.getItem('authdata')).token}`,
                'Access-Control-Allow-Origin': '*'
            }
        });                
    } catch (err) {
        // ??? 
        if (err.response && err.response.status === 401) {
            console.log("response.status === 401");
            localStorage.removeItem('authdata');  
            localStorage.removeItem('current_account');
            history.replace('/');          
        }
        throw err;
    }
    return response;
}
