import {
  RESET_LIST_DATA,
  SET_LIST_DATA_TYPE,
  // SET_LIST_DATA_DATA,
  TOGGLE_LIST_DATA_FINAL_MODAL,
} from 'actions/listData';

let initialState = {
  isLoading: false,
  listDataType: 'business',
  data: {},
  finalModalOpen: false,
};

export default function reducer(state = initialState, action) {
  switch (action.type) { 
    case RESET_LIST_DATA:
      return {
        ...initialState,
      };      
    case SET_LIST_DATA_TYPE:      
      return {
        ...state,
        listDataType: action.data,
      } 
    // case SET_LIST_DATA_DATA:
    //   return {
    //     ...state,
    //     data: {
    //       ...state.data,
    //       [action.key]: action.value,
    //     }
    //   } 
  case TOGGLE_LIST_DATA_FINAL_MODAL:
    return {
      ...state,
      finalModalOpen: action.data,
    } 
    default:
      return state;
  }
}
