import service from './service';
import { API_URL } from '../config/config';

export const getContactsApi = data => {  
  return service.get(`${API_URL}/contacts`, { ...data });
};

export const getFiltersDataApi = data => {  
  return service.get(`${API_URL}/contacts/filters-data?account=${data.account}`);
};

export const deleteContactsApi = data => {  
  let url = `${API_URL}/contact?account=${data.account}`;
  const ids = data.ids;

  for (var i = 0; i < ids.length; ++i) {
    url = `${url}&ids[]=${ids[i]}`;
  }
  if (data.list) {
    url = `${url}&list=${data.list}`;
  }
  return service.delete(url);
};

// TODO: Get real List Type
export const createListApi = data => {
  return service.post(`${API_URL}/list-request?account=${data.account}`, {
    ...data,
    listType: 'from contacts',
  });
};

export const deleteOneContactApi = data => {  
  let url = `${API_URL}/contact/${data.id}/?account=${data.account}`;
  if (data.list) {
    url = `${url}&list=${data.list}`;
  }
  return service.delete(url);
};

export const getAddContactListsApi = data => {
  return service.get(`${API_URL}/list-request`, data);
};

export const addNewContactApi = data => {
  return service.put(
    `${API_URL}/contact/create?accountID=${data.query
      .account}&listRequestID=${data.query.listRequestID}`,
    data.body,
  );
};

export const editContactApi = data => {  
  return service.put(
    `${API_URL}/contact/${data.params.contactId}?accountID=${data.query.account}`,
    data.body,
  );
};