import React,{ useEffect, useState } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import OnePictureBuilder from 'components/OnePictureBuilder/OnePictureBuilder';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { ReactComponent as LeftArrowIcon } from 'assets/img/icons/chevron-left.svg';
import { AUTOMATION_PATH } from 'utils/constants';
import { useSelector, useDispatch } from 'react-redux';
import { toast } from 'react-toastify';
import {
  getAutomationPreviewImgRequest,  
  setModificatedAutomationTemplateEdit,  
  createAutomationRequest,
  updateAutomationRequest,  
  setChangeAutomationPostcardStatus,  
  setNextStepAutomation,
} from 'actions/automation';
import {postcard_front_id, postcard_back_id} from 'views/Automation/config';
import S3 from 'aws-sdk/clients/s3';
import { S3_BUCKET_PUBLIC_NAME,
  REACT_APP_S3_ACCESS_KEY_ID,
  REACT_APP_S3_SECRET_ACCESS_KEY,
} from 'config/config';
import { showMainLoader } from 'actions/general';

import './AutomationEditor.scss';

const s3Config = {
  bucketName: S3_BUCKET_PUBLIC_NAME,
  accessKeyId: REACT_APP_S3_ACCESS_KEY_ID,
  secretAccessKey: REACT_APP_S3_SECRET_ACCESS_KEY,
  region: 'us-east-1'
};
const s3Client = new S3(s3Config);

export default function AutomationEditor(props) {
  const dispatch = useDispatch();
  const history = useHistory();
  useEffect(() => {   
    let domFront = document.getElementById(postcard_front_id);
    let domBack = document.getElementById(postcard_back_id);
    // wait while dom with template rendered and then select all !!!automation-image!!! images from aws
    if(domFront&&domBack&&(candidatesForDeleteFromAws===null)){
      let frontArray = [...domFront.querySelectorAll('img[data-image-changeable][src*="automation-image"]')].map((el)=>el.src);
      let backArray = [...domBack.querySelectorAll('img[data-image-changeable][src*="automation-image"]')].map((el)=>el.src);          
      // console.log("setCandidatesForDeleteFromAws----->>>>", [...frontArray,...backArray]);
      setCandidatesForDeleteFromAws([...frontArray,...backArray]);
      // console.log("useEffect setCandidatesForDeleteFromAws:", [...frontArray,...backArray]);
    } 
  });
  
  const { automationId } = useParams();
  const [candidatesForDeleteFromAws, setCandidatesForDeleteFromAws] = useState(null);  
  const automationSize = useSelector(state => state.automation.automationSize);
  const front = useSelector(state => state.automation.automationEditor.templateEdit.front);
  const back = useSelector(state => state.automation.automationEditor.templateEdit.back);
  const templateSample = useSelector(state => state.automation.automationEditor.templateSample);
  const templateEdit = useSelector(state => state.automation.automationEditor.templateEdit);  
  const currentAccountId = useSelector(state => state.account.currentAccount._id);      
  const templateTabIndex = useSelector(state => state.automation.automationEditor.templateTabIndex);
  const selectedTemplate = useSelector(state => state.automation.automationEditor.selectedTemplate);
  // const changeAutomationPostcardStatusFront = useSelector(state => state.automation.changeAutomationPostcardStatus.front);
  // const changeAutomationPostcardStatusBack = useSelector(state => state.automation.changeAutomationPostcardStatus.back);
  const backMarkPlace = useSelector(state => state.automation.automationEditor.backMarkPlace);
  const morpheusTemplates = useSelector(state => state.morpheusTemplates);
  const automationRecentTemplates = useSelector(state => state.automation.automationRecentTemplates);
  const automationMyTemplates = useSelector(state => state.automation.automationMyTemplates);
  const step = useSelector(state => state.automation.step);     
  // const [arrayImagesToDeleteFromAws, setArrayImagesToDeleteFromAws] = useState([]);  
  const [disableConfirmButton, setDisableConfirmButton] = useState(false);
  const [templateStatus, setTemplateStatus] = useState('auto-saved');
  
  // const onChangeSize = (e)=>{
  //   // TODO TEST TEMPLATE CHANGES front and back
  //   if( changeAutomationPostcardStatusFront || changeAutomationPostcardStatusBack ) {        
  //     saveImagesToAws().then(()=>{
  //       setModificatedAutomationTemplateToEdit(changeAutomationPostcardStatusFront, changeAutomationPostcardStatusBack);
  //       dispatch(setChangeAutomationPostcardStatus({front: false, back: false}));
  //       dispatch(setAutomationSize(e));        
  //     });
  //   }else{
  //     dispatch(setAutomationSize(e));
  //   }
  // }
  const dataURLtoFile = (dataurl, filename) => {
    let arr = dataurl.split(','),
      mime = arr[0].match(/:(.*?);/)[1],      
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);      
    while(n--){
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], filename, {type:mime});
  };
  const uploadImage = (Key, Body) => {
    const upload = s3Client.putObject({
      Bucket: `${s3Config.bucketName}/automationImages/${currentAccountId}`,
      Key,
      Body,
      ACL: "public-read"
    });
    return upload.promise();
  };
  // const deleteImage = (Key) => {
  //   const deleted = s3Client.deleteObject({
  //     Bucket: `${s3Config.bucketName}/automationImages/${currentAccountId}`,
  //     Key
  //   });
  //   return deleted.promise();
  // };
  // const deleteConvertImage = (Key) => {
  //   const deleted = s3Client.deleteObject({
  //     Bucket: `${s3Config.bucketName}/automationImages`,
  //     Key
  //   });
  //   return deleted.promise();
  // };
  const saveImagesToAws = async () => {
    // save to aws front images    
    let frontDomImages = document.getElementById(postcard_front_id).querySelectorAll('img[data-image-changeable][src^="data:image"]');    
    await Promise.all([...frontDomImages].map(async(el)=>{
      const filename = `automation-image-${currentAccountId}-${new Date().getTime()}.${el.src.split(',')[0].match(/:(.*?);/)[1].split('/')[1]}`;
      await uploadImage(filename, dataURLtoFile(el.src, filename));      
      el.src = `https://${s3Config.bucketName}.s3.amazonaws.com/automationImages/${currentAccountId}/${filename}`;           
    } ) );
    // save to aws back images
    let backDomImages = document.getElementById(postcard_back_id).querySelectorAll('img[data-image-changeable][src^="data:image"]');    
    await Promise.all([...backDomImages].map(async(el)=>{
      const filename = `automation-image-${currentAccountId}-${new Date().getTime()}.${el.src.split(',')[0].match(/:(.*?);/)[1].split('/')[1]}`;
      await uploadImage(filename, dataURLtoFile(el.src, filename));      
      el.src = `https://${s3Config.bucketName}.s3.amazonaws.com/automationImages/${currentAccountId}/${filename}`; 
    } ) );    
  }
  // const prepareArrayImagesToDeleteFromAws = () => {
  //   // CHECK images that should be deleted from aws as changed to new
  //   let allFinishImagesSRCFront = [...document.getElementById(postcard_front_id).querySelectorAll('img[data-image-changeable]')].map((el)=>el.src);      
  //   let allFinishImagesSRCBack = [...document.getElementById(postcard_back_id).querySelectorAll('img[data-image-changeable]')].map((el)=>el.src);  
  //   let allFinishImages = [...allFinishImagesSRCFront,...allFinishImagesSRCBack];
  //   let toDeleteArray = candidatesForDeleteFromAws.filter((el)=>!allFinishImages.includes(el));
  //   // console.log("Will be deleted after successfully sent to back:", toDeleteArray); 
  //   return toDeleteArray;
  // }
  const calcTemplateBodyStyle = (postcardSize)=>{
    switch(postcardSize){
      case '6x9':
        return 'width: 2775px; height: 1875px;'        
      case '6,5x9':
        return 'width: 2775px; height: 2025px;'        
      case '6,5x11':
        return 'width: 3375px; height: 1875px;'        
      case '8,5x11':
        return 'width: 3375px; height: 2625px;'        
      default:
        return 'width: 2775px; height: 1875px;'
    }    
  }
  const checkDefaultDomImages = ()=>{
    let checkDefDomImagesFront = document.getElementById(postcard_front_id).querySelectorAll('.morpheus-card-body img[data-image-changeable][src*="/templateimages/"]');
    let checkDefDomImagesBack = document.getElementById(postcard_back_id).querySelectorAll('.morpheus-card-body img[data-image-changeable][src*="/templateimages/"]');
    if(checkDefDomImagesFront.length||checkDefDomImagesBack.length){
      toast.error("You can't send the default image to print. Please, replace the example image.")
      return true;
    } 
    return false;  
  }
  // const callbackForDeleteImagesFromAws = async () => {    
  //   let toDeleteArray = arrayImagesToDeleteFromAws;    
  //   if(toDeleteArray.length){
  //     // console.log("Will be deleted after successfully sent approving to back:", toDeleteArray);
  //     await Promise.all([...toDeleteArray].map(async(el)=>{  
  //       // console.log(el.split('/').slice(-1)[0]);        
  //       // console.log(el.split('/').pop());         
  //       // eject just name from all url of file
  //       await deleteImage(el.split('/').pop()); 
  //     } ) ); 
  //   }
  // }
  const prepareTemplateSample = (size, side)=>{      
    if(selectedTemplate!==''){
      switch(templateTabIndex){
        case 1:
          return morpheusTemplates.templates.find((el)=>(el._id===selectedTemplate))[side] || '';
        case 2:
          return automationRecentTemplates.data.find((el)=>(el._id===selectedTemplate))[side] || '';
        case 3:
          return automationMyTemplates.data.find((el)=>(el._id===selectedTemplate))[side] || ''; 
        default: 
          return morpheusTemplates.templates.find((el)=>(el._id===selectedTemplate))[side] || '';      
      }      
    } 
    if(templateEdit[side]){
      return templateEdit[side] || '';
    }
    return templateSample[size][side] || '';
  }
  const setModificatedAutomationTemplateToEdit = (frontShouldChange, backShouldChange) =>{
    let front, back;
    if (frontShouldChange&&backShouldChange){
      front = document.getElementById(postcard_front_id).querySelector('.Morpheus-card-container').innerHTML;
      back = document.getElementById(postcard_back_id).querySelector('.Morpheus-card-container').innerHTML;
      dispatch(setModificatedAutomationTemplateEdit({
        front,
        back,
      }));
    }
    if (frontShouldChange&&!backShouldChange){
      front = document.getElementById(postcard_front_id).querySelector('.Morpheus-card-container').innerHTML;      
      dispatch(setModificatedAutomationTemplateEdit({
        front,        
      }));
    }
    if (!frontShouldChange&&backShouldChange){
      back = document.getElementById(postcard_back_id).querySelector('.Morpheus-card-container').innerHTML;      
      dispatch(setModificatedAutomationTemplateEdit({
        back,        
      }));
    }
  }
  const backStep = () => {
    dispatch(setNextStepAutomation(step-1));
  }
  const onEditorNext = ()=>{    
    if(checkDefaultDomImages()) return;  
    // setArrayImagesToDeleteFromAws(prepareArrayImagesToDeleteFromAws());  
    dispatch(showMainLoader(true));
    setDisableConfirmButton(true); 
    setTemplateStatus(templateStatus);
    saveImagesToAws().then(()=>{ 
      const newFront = `<html><body style="${calcTemplateBodyStyle(automationSize)} -webkit-box-sizing: border-box; box-sizing: border-box;">${document.getElementById(postcard_front_id).parentElement.innerHTML}</body></html>`;
      const newBack = `<html><body style="${calcTemplateBodyStyle(automationSize)} -webkit-box-sizing: border-box; box-sizing: border-box;">${document.getElementById(postcard_back_id).parentElement.innerHTML}</body></html>`;
      const data = {
        body: { 
          template:{
            front: newFront,
            back: newBack,
          },
          templateStatus,
          bucket: 'automationImages'
        },        
      };
      setModificatedAutomationTemplateToEdit(true, true);      
      dispatch(getAutomationPreviewImgRequest(data,onEditorNextCalback));
    });
  }
  const onEditorNextCalback = (previewImg) => {  
    // callbackForDeleteImagesFromAws();  
    const front = document.getElementById(postcard_front_id).querySelector('.morpheus-card-body').outerHTML;
    const back = document.getElementById(postcard_back_id).querySelector('.morpheus-card-body').outerHTML;   
    const data = {
      body: {          
        // size: automationSize, 
        account: currentAccountId, 
        name: '',
        description: '',        
        status: 'draft',          
        templates:{
          front,
          back,
        },
        images: previewImg,
        type: 'simple',
        neighborhoodCount: 0,
      },
      queries: currentAccountId,
    };      
    if(automationId==='create'){
      dispatch(createAutomationRequest(data));
    }else{
      data.automationId=automationId;
      dispatch(updateAutomationRequest(data,updateAutomationCallback));
    }        
  }  
  const updateAutomationCallback=()=>{    
    dispatch(setNextStepAutomation(step+1));
  }
  
  return (  
    <React.Fragment>
      <div className='back-btn-container'>
        <button className='back-btn' onClick={()=>history.push(`${AUTOMATION_PATH}`)}>
          <LeftArrowIcon className="back-icon"/>
          Back to automation
        </button>              
      </div>               
      <h2 className="status-title">New Automation</h2>
      <div className="automationEditorCard-v2">
        <div className="section">          
          <div className="title"><span className="badge-step">STEP 2</span>CUSTOMIZE YOUR POSTCARD</div>          
        </div>
        <div className="section">
          <div className="preview-header">
            <div className="sub-title">Front Side of Postcard</div>
            <DopeButton
              color="white"
              onClick={backStep}                
            >
              BACK TO POSTCARDS
            </DopeButton>
          </div>
          <OnePictureBuilder
            template={front || templateSample[automationSize].front}              
            templateSample={prepareTemplateSample(automationSize,'front')}                
            classPref={templateSample[automationSize].classPref || 'sm'}
            postcardSize={automationSize}
            postcardDpi={300}
            postcardBleed={0.25}
            awsPath={'automation'}
            side="front"            
            currentAccountId={currentAccountId}
            changePostCardStatus={ (data)=>{dispatch(setChangeAutomationPostcardStatus(data))} }                
            phoneNumber="+18888888888" //temporary TODO remove                
          />   
        </div>
        <div className="section">
          <div className="sub-title">Back Side of Postcard</div>                  
          <OnePictureBuilder                
            template={back || templateSample[automationSize].back}
            templateSample={prepareTemplateSample(automationSize,'back')} 
            classPref={templateSample[automationSize].classPref || 'sm'}
            postcardSize={automationSize}
            postcardDpi={300}
            postcardBleed={0.25}
            awsPath={'automation'}
            side="back"            
            currentAccountId={currentAccountId}
            changePostCardStatus={ (data)=>{dispatch(setChangeAutomationPostcardStatus(data))} }
            backMarkPlace={backMarkPlace}                  
            phoneNumber="+18888888888" //temporary TODO remove            
          />
        </div>
      </div> 
      <div className="bnts-container">
        <DopeButton
          color="primary"
          onClick={onEditorNext}
          disabled={disableConfirmButton}
        >
          Next
        </DopeButton>
      </div> 
    </React.Fragment>      
  );
}
