import React from 'react';
import ColorPicker from "../../../components/ColorPicker/ColorPicker";
import { makeColor } from "../../../components/utils";
import { ReactComponent as Plus } from 'assets/img/icons/add.svg';
import { ReactComponent as Save } from 'assets/img/icons/save.svg';

class Palette extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            colors: []
        }
    }

    static getDerivedStateFromProps({ colors }, state) {
        if (Array.isArray(state.colors) && !state.colors.length) {
            return {
                colors
            }
        } else {
            return state.colors
        }
    }

    onDelete = ({ target }) => {
        // delete particular item
        const colors = this.state.colors.filter(({ id }) => id !== target.name);
        this.setState({
            colors
        })
    };

    onChange = (event) => {
        const { name, value } = event.target;
        // update particular item
        this.setState({
            colors: [
                ...this.state.colors.map((color) => {
                    if (color.id === name) {
                        return {
                            ...color,
                            value
                        }
                    } else {
                        return color;
                    }
                })

            ]
        })
    };

    onAdd = () => {
        try {
            this.setState({
                colors: [
                    ...this.state.colors,
                    makeColor(),
                ]
            })
        } catch (e) {
            console.log('e', e);
        }
    };

    handleSave = () => {
        const { onSave, value } = this.props;
        onSave({
            target: {
                name: value.id,
                value: this.state.colors
            }
        })
    };

    render() {
        const { colors = [] } = this.state;
        return (
            <div className="palette">
                <div className="palette__label">{this.props.label}</div>
                <div className="palette__body">
                    {colors.map(({ id, value }) => {
                        return (
                            <ColorPicker
                                key={id}
                                value={value}
                                name={id}
                                onClose={this.onDelete}
                                onChange={this.onChange}
                            />
                        )
                    })}
                    <div className="palette__body__add" onClick={this.onAdd}>
                        <Plus style={{'width': '18px'}}/>
                    </div>
                    <div className="palette__body__add" onClick={this.handleSave}>
                        <Save style={{'width': '18px'}}/>
                    </div>
                </div>
            </div>
        )
    }
};

export default Palette;