import React from 'react';
import JobsTable from 'components/JobsTable';
import FilterPanel from 'components/FilterPanel';
import JobsModal from 'components/JobsModal';
import { connect } from 'react-redux';
import * as jobsActions from '../../actions/jobs';
import { pathOr } from 'ramda';

class AdminManageOrders extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      generalSearch: {
        startDate: null,
        endDate: null,
        jobName: '',
        minPrice: 0,
        maxPrice: 0,
      },
      isModalOpened: false,
      modalData: {},
    };
  }

  componentDidMount() {
    this.updateData();
  }

  componentWillUnmount() {
    this.props.resetJobs();
  }
  

  formatedOrders = orders => {
    return orders.map(item => {
      const totalPriceYardSign =
        pathOr(0, ['yardSign', 'totalPrice'], item) / 100;
      const totalPricePostcard = pathOr(0, ['postcard', 'price'], item) / 100;

      if (item.type === 'Yard Sign') {
        item.totalPrice = totalPriceYardSign;
      } else if (item.type === 'Postcard') {
        item.totalPrice = totalPricePostcard;
      }
      return item;
    });
  };

  handleFilterChange = (name, value) => {
    let generalSearch = this.state.generalSearch;

    this.setState({
      generalSearch: Object.assign(generalSearch, { [name]: value }),
    });
  };

  performFilter = () => {
    this.updateData();
  };

  clearFilter = () => {
    this.setState(
      {
        generalSearch: this.getInitialState(),
      },
      () => this.updateData(),
    );
  };

  onItemClicked = job => {
    this.setState({
      modalData: job,
      isModalOpened: true,
    });
  };

  onPreviewPdf = job => {
    const data = {
      id: job.id,
      params: {
        account: this.props.currentAccount._id,
      },
    };
    this.props.createPreviewPdfRequest(data);
  };

  updateData = (pagination = { limit: 10, page: 1 }) => {
    const { generalSearch } = this.state;
    const { limit, page } = pagination;

    let filter = {};
    Object.keys(generalSearch).forEach(
      key =>
        generalSearch[key] &&
        Object.assign(filter, { [key]: generalSearch[key] }),
    );
    const data = {
      account: this.props.currentAccount._id,
      page: page - 1,
      limit: limit,
      ...filter,
    };
    this.props.createJobsRequest(data);
  };

  onPageChange = pagination => {
    this.updateData(pagination);
  };

  toggleModal = () => {
    this.setState({
      modalData: {},
      isModalOpened: false,
    });
  };

  changeStatusJob = (payload, id, newJobs) => {
    const data = {
      id: id, 
      payload: {
        account: this.props.currentAccount._id, 
        ...payload}
    }
    this.props.createChangeStatusJobRequest(data, newJobs)
  }

  render() {
    let paginationOptions = {
      onPageChange: this.onPageChange,
      page: this.props.jobs.page + 1,
      paginationSize: this.props.jobs.pages,
      totalSize: this.props.jobs.totalSize,
    };

    const { generalSearch } = this.state;
    const { isLoading, data } = this.props.jobs;
    return (
      <div className='content order-container admin-block'>
        <FilterPanel
          handleChange={this.handleFilterChange}
          performFilter={this.performFilter}
          clearFilter={this.clearFilter}
          {...generalSearch}
        />
        <JobsTable
          isLoading={isLoading}
          jobs={data}
          paginationOptions={paginationOptions}
          onItemClicked={this.onItemClicked}
          currentAccount={this.props.currentAccount}
          onPreviewPdf={this.onPreviewPdf}
          changeStatusJob={this.changeStatusJob}
        />
        <JobsModal
          toggle={this.toggleModal}
          open={this.state.isModalOpened}
          data={this.state.modalData}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  currentAccount: state.account.currentAccount,
  jobs: state.jobs,
});

const mapDispatchToProps = {
  ...jobsActions,
};

export default connect(mapStateToProps, mapDispatchToProps)(AdminManageOrders);
