import React from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import './DeleteAccountModal.scss';

export default function DeleteAccountModal(props) {  
  const onSubmit = () => {
    props.onSubmit();
    props.toggle();
  }
  return (    
    <DopeModal className='DeleteAccountModal' isOpen={props.open} toggle={props.toggle} centered>      
      <DopeModalHeader className='dope-modal-header' toggle={props.toggle}>
        Delete Account?
      </DopeModalHeader>  
      <DopeModalBody>
        <p className="delete-acc-text">All information will be deleted.</p>
      </DopeModalBody>
      <DopeModalFooter>
        <DopeButton color='white' onClick={props.toggle}>
          Cancel
        </DopeButton>
        <DopeButton color='primary' onClick={onSubmit}>
          Delete
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>    
  );
}
