import React from 'react';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModal } from 'components/DopeModal/DopeModal';
import spects from 'assets/img/spects.jpg';
import './SpectsModal.scss';

export default function SpectsModal(props) {
  
  const toggle = () => {
    props.toggle();
  };

  const onClose = () => {
    props.toggle();    
  };

  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <DopeModal
        className='SpectsModal'
        isOpen={props.open}
        toggle={toggle}
        centered        
      >        
        <DopeModalHeader toggle={onClose}>
          Postcard spects details
        </DopeModalHeader>
        <DopeModalBody>
          <img src={spects} alt="Spects-img"/>
        </DopeModalBody>        
      </DopeModal>
    </div>
  );
}
