import service from './service';
import { API_URL } from 'config/config';

export const getAdminRequestsApi = data => {
  return service.get(`${API_URL}/account-requests?`, data.query);
};
// GET {api}/account-requests?limit=xxx&page=xxx&sort=xxx&account=xxx&status=xxx&productType=xxx

export const deleteRequestApi = data => {
  return service.delete(`${API_URL}/account-requests/${data}`);
};
// DELETE {api}/account-requests/{requestId}

export const changeRequestStatusApi = data => {
  return service.patch(`${API_URL}/account-requests/${data.id}`, data.body);
};
// PATCH {api}/account-requests/{requestId}


export const getAdminRequestApi = data => {
  return service.get(`${API_URL}/account-requests/${data.requestId}`);
};
// GET {api}/account-requests/{requestId}

export const getAdminListsUploadCsvApi = data => {
  return service.get(`${API_URL}/list-request`, data);
};

export const uploadAccountRequestsListApi = (data) => {
  return service.patch(
    `${API_URL}/account-requests/${data.requestId}/upload-csv`,
    data.body
  )
}
// PATCH: {api}/account-requests/{request id}/upload-csv




