import React, {useState} from 'react';
import Pagination from 'rc-pagination';
import localeInfo from 'rc-pagination/lib/locale/en_US';
import { ReactComponent as PrevIco } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as NextIco } from 'assets/img/icons/chevron-right.svg';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import { FadeLoader } from 'react-spinners';
import DeletePostcardModal from './DeletePostcardModal';
import {pathOr} from 'ramda';

export default function MyPostcardTemplates(props) {  
  // console.log("MyPostcardTemplates:" ,props);
  const [id, setId] = useState(null);
  const [images, setImages] = useState([]);
  const onPageChange = (page) => {
    props.getPostcards({page: page-1, limit:8, types: 'postcard,eddm,neighborhood'}, 'saved')
  };
  const postcards = pathOr([], ['postcardTemplatesData', 'data'], props);  
  return (
    <>
    <div className = {`${props.postcardTemplatesIsLoaded ? 'disabled postcards-preview-container' : 'postcards-preview-container'}`}>
      {postcards.length ? (
        
        postcards.map(template => (
          <div
            key={template._id}
            className='postcards-preview-item'
          >
            <div 
              className='postcards-preview-content'
              onClick={e => {
                props.resetPostCardTemplateBackKey();
                props.onSelectMyTemplate(template);
                props.addErrors('template', '');
              }}
            >
              <div>
                <img                  
                  src={
                    template.images.front&&template.images.front.mini                    
                  }
                  alt='template'
                />
              </div>
              
              {props.selectedPostcardId === template._id ? (
                <div className='template-checked' />
              ) : (
                ''
              )}
            </div>
            <div className='postcards-preview-name-block'>
              <span className="name-text">{template.name}</span>
              <DeleteIcon 
                className="del-icon"
                onClick={ ()=>{
                  setId(template._id)
                  setImages(template.images)
                  props.toggleDeleteTemplateModal()
                }
                }
              />
            </div>
          </div>
        ))
      ) : ''}

      {
        !props.postcardTemplatesIsLoaded && !postcards.length ?  <div className="no-templates-container">No Templates</div> : ''
      }

      
    </div>
    {postcards.length? <Pagination
      className='dope-pagination'
      current={props.postcardTemplatesData.page+1}
      defaultPageSize={props.postcardTemplatesData.limit}
      total={props.postcardTemplatesData.totalSize}
      hideOnSinglePage={true}
      onChange={onPageChange}
      locale={localeInfo}
      prevIcon={<PrevIco/>}
      nextIcon={<NextIco/>}
      jumpPrevIcon={<span>...</span>}
      jumpNextIcon={<span>...</span>}
      showTitle={false}
      /> : ''}
      {props.postcardTemplatesIsLoaded && (
        <div className='loader-main'>
                <FadeLoader size={100} color={'#403d39'} />
              </div>
      )}
      <DeletePostcardModal 
        open={props.showDeleteTemplateModal}
        toggle={props.toggleDeleteTemplateModal}
        deleteMyPostcard={props.deleteMyPostcard}
        id={id}
        page={props.postcardTemplatesData.page}
        images={images}
      />
    </>
  );
}
