import React from 'react';
import { connect } from 'react-redux';
import { FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeModal } from '../DopeModal/DopeModal';
import { DopeModalHeader } from '../DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from '../DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from '../DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import Ajv from 'ajv';
import * as newTrackingNumberActions from 'actions/newTrackingNumber';
import { friendlyFormatPhoneNumber, fullNumberFormat } from 'utils/phones';
import { CreateListSchema } from './config';
import './AddNumberModal.scss';
import Select from 'react-select';

const ajv = new Ajv({ allErrors: true });

class AddNumberModal extends React.Component {
  componentDidUpdate(prevProps) {
    if (
      !prevProps.newTrackingNumber.areaCode &&
      this.props.newTrackingNumber.areaCode &&
      this.props.newSearchNumbers.length
    ) {
      this.props.setNewSearchNumbers([]);
      this.props.toggleShowAddNewNumberDropDown(false);
    }

    if (
      prevProps.newSearchNumbersIsLoading &&
      !this.props.newSearchNumbersIsLoading
    ) {
      this.select.focus();
    }
  }

  validation = () => {
    const {
      trackingNumber,
      forwardingNumber,
      name,
      selectedFromDropDown,
    } = this.props.newTrackingNumber;
    const formattedForBE = {
      trackingNumber: fullNumberFormat(trackingNumber),
      forwardingNumber: fullNumberFormat(forwardingNumber),
      name: name,
      selectedFromDropDown: selectedFromDropDown,
    };

    const result = ajv.validate(CreateListSchema, formattedForBE);
    if (!result) {
      ajv.errors.map(error => {
        const errorKey = error.dataPath.split('.').pop();

        if (errorKey === 'selectedFromDropDown') {
          this.props.addErrorsNewTrackingNumber(
            [errorKey],
            'Select from drop down',
          );
        } else {
          this.props.addErrorsNewTrackingNumber(
            [errorKey],
            'This field is required',
          );
        }
      });
      return false;
    }

    return true;
  };

  getValidationMessage = name => {
    const message = this.props.newTrackingNumber.errors[name];

    if (message) {
      return <div className='validation-message'>{message}</div>;
    }
  };

  searchNumbers = trackingNumber => {
    const data = {
      areaCode: trackingNumber,
      account: this.props.currentAccount._id,
    };

    this.props.toggleShowAddNewNumberDropDown(false);
    this.props.searchNumbersRequest(data);
  };

  onAddNumberSubmit = () => {
    if (!this.validation()) return;

    let {
      trackingNumber,
      forwardingNumber,
      name,
    } = this.props.newTrackingNumber;

    trackingNumber = fullNumberFormat(trackingNumber);
    forwardingNumber = fullNumberFormat(forwardingNumber);

    //Set new tracking number as selected
    if (this.props.newNumberHandler) {
      this.props.newNumberHandler(trackingNumber);      
    }

    const data = {
      body: {
        trackingNumber,
        forwardingNumber,
        name,
      },
      query: {
        account: this.props.currentAccount._id,
      },
    };

    if (this.props.pagination) data.pagination = { ...this.props.pagination };

    if(!this.props.currentAccount.default_source){
      this.props.setShowAddingCard(true, data);      
    }else{
      this.props.createTrackingNumbersRequest(data);
    }
  };

  onClose = () => {
    this.props.toggleTrackingNumberModal(false);
    this.props.resetTrackingNumberModal();
  };

  onSelectInputChange = (e, action) => {
    if (action.action !== 'input-blur' && action.action !== 'menu-close') {
      if (action.action === 'input-change') {
        this.props.toggleShowAddNewNumberDropDown(false);
        this.props.setNewSearchNumbers([]);
        this.props.setNewTrackingNumber('trackingNumber', '');
      }

      this.props.setNewTrackingNumber('areaCode', e);
      this.props.addErrorsNewTrackingNumber('trackingNumber', '');
      this.props.addErrorsNewTrackingNumber('selectedFromDropDown', '');
      this.props.setNewTrackingNumber('selectedFromDropDown', false);
    } else {
      this.props.toggleShowAddNewNumberDropDown(false);
    }
  };

  onMenuOpen = () => {
    if (this.props.newSearchNumbers.length)
      this.props.toggleShowAddNewNumberDropDown(true);
  };

  getListsOptions = () => {
    return this.props.newSearchNumbers.map(item => ({
      value: item.phoneNumber,
      label: friendlyFormatPhoneNumber(item.phoneNumber),
    }));
  };

  getSelectedItem = () =>
    this.getListsOptions().filter(
      item => item.value === this.props.newTrackingNumber.trackingNumber,
    );

  onChangeSelect = e => {
    this.props.addErrorsNewTrackingNumber('selectedFromDropDown', '');
    this.props.setNewTrackingNumber('selectedFromDropDown', true);
    this.props.setNewTrackingNumber('trackingNumber', e.value);
  };

  render() {
    const {
      open,
      resetTrackingNumberModal,
      newTrackingNumber,
      newSearchNumbersIsLoading,
      showAddNewNumberDropDown,
    } = this.props;

    return (
      <DopeModal
        isOpen={open}
        onClosed={resetTrackingNumberModal}
        className='add-number-modal'
        centered
      >
        <DopeModalHeader>New Tracking Number </DopeModalHeader>
        <DopeModalBody>
          <p>
            Track your callers in Dope with your own custom tracking number $5 per month!
          </p>

          <FormGroup className='dope-form-group'>
            <div className='one-line'>
              <div
                className={`code-area-container ${newTrackingNumber.errors
                  .trackingNumber ||
                newTrackingNumber.errors.selectedFromDropDown
                  ? 'select-error'
                  : ''}`}
              >
                <Select
                  className='dope-select'
                  classNamePrefix='dope'
                  placeholder='Search by area code, e.g. 718'
                  isLoading={newSearchNumbersIsLoading}
                  menuIsOpen={showAddNewNumberDropDown}
                  onMenuOpen={this.onMenuOpen}
                  options={this.getListsOptions()}
                  inputValue={newTrackingNumber.areaCode}
                  value={this.getSelectedItem() || null}
                  onInputChange={this.onSelectInputChange}
                  onChange={this.onChangeSelect}
                  ref={ref => {
                    this.select = ref;
                  }}
                />
                {(!this.props.newTrackingNumber.areaCode &&
                  this.getValidationMessage('trackingNumber')) ||
                  this.getValidationMessage('selectedFromDropDown')}

                <div className='phone-numbers-container' />
              </div>
              <div className='code-area-btn'>
                <DopeButton
                  color='white'
                  onClick={() =>
                    this.searchNumbers(this.props.newTrackingNumber.areaCode)}
                  disabled={!this.props.newTrackingNumber.areaCode}
                >
                  Search
                </DopeButton>
              </div>
            </div>
          </FormGroup>

          <FormGroup className='dope-form-group'>
            <DopeInput
              title='Forwarding Number'
              supText='*'
              error={this.props.newTrackingNumber.errors.forwardingNumber}
              mask='(999)999-9999'
              placeholder="(___)___-____"
              value={this.props.newTrackingNumber.forwardingNumber}
              onChange={e => {
                this.props.setNewTrackingNumber(
                  'forwardingNumber',
                  e.target.value,
                );
                this.props.addErrorsNewTrackingNumber('forwardingNumber', '');
              }}
            />
          </FormGroup>
          <FormGroup className='dope-form-group'>
            <DopeInput
              title='Name'
              supText='*'
              error={this.props.newTrackingNumber.errors.name}
              value={this.props.newTrackingNumber.name}
              onChange={e => {
                this.props.setNewTrackingNumber('name', e.target.value);
                this.props.addErrorsNewTrackingNumber('name', '');
              }}
            />
          </FormGroup>
        </DopeModalBody>

        <DopeModalFooter>
          <DopeButton color='white' onClick={this.onClose}>
            Cancel
          </DopeButton>
          <DopeButton color='primary' onClick={this.onAddNumberSubmit}>
            Submit
          </DopeButton>
        </DopeModalFooter>
      </DopeModal>
    );
  }
}

const mapStateToProps = state => ({
  currentAccount: state.account.currentAccount,
  newSearchNumbers: state.newTrackingNumber.newSearchNumbers,
  newTrackingNumber: state.newTrackingNumber,
  newSearchNumbersIsLoading: state.newTrackingNumber.newSearchNumbersIsLoading,
  showAddNewNumberDropDown: state.newTrackingNumber.showAddNewNumberDropDown,
  open: state.newTrackingNumber.showAddNumberModal,
});

const mapDispatchToProps = {
  ...newTrackingNumberActions,
};

export default connect(mapStateToProps, mapDispatchToProps)(AddNumberModal);
