import React from 'react';
import {
  DopeModal,
  DopeModalHeader,
  DopeModalBody,
  DopeModalFooter,
} from 'components/DopeModal';
import { DopeButton } from 'components/DopeForms';
import { FormGroup } from 'reactstrap';
import { DopeDatePicker } from 'components/DopeForms/DopeDatePicker/DopeDatePicker';
import moment from 'moment';
import './CloneSendingModal.scss';

export default function CloneSendingModal({
  isOpen,
  toggle,
  selectedDates,
  availableSendingDataIsLoading,
  getAvailableSendingsData,
  calendarDate,
  onDataSelect,
  resetCloneSending,
  cloneSending,
}) {
  const startDate = moment()
    .startOf('month')
    .startOf('week')
    .format('YYYY-MM-DD');
  const endDate = moment().endOf('month').endOf('week').format('YYYY-MM-DD');

  return (
    <DopeModal
      className='delete-sending-modal'
      isOpen={isOpen}
      onOpened={() => getAvailableSendingsData({ startDate, endDate })}
      onClosed={resetCloneSending}
      centered
    >
      <DopeModalHeader toggle={toggle}>Duplicate Sending</DopeModalHeader>
      <DopeModalBody>
        <p>
          The selected sending will be duplicated with the same parameters to
          the chosen sending date.
        </p>
        <FormGroup className='postcard-other-number-container'>
          <span className='input-title'>
            Sending Date <sup>*</sup>
          </span>
          <DopeDatePicker
            selected={calendarDate}
            dateFormat='MMM d, yyyy'
            minDate={new Date()}
            highlightDates={selectedDates}
            onMonthChange={getAvailableSendingsData}
            sendingDataIsLoading={availableSendingDataIsLoading}
            onChange={onDataSelect}
            useWeekdaysShort={true} 
          />
        </FormGroup>
      </DopeModalBody>
      <DopeModalFooter>
        <DopeButton color='white' onClick={toggle}>
          Cancel
        </DopeButton>
        <DopeButton
          color='primary'
          onClick={cloneSending}
          disabled={!calendarDate}
        >
          Duplicate
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>
  );
}
