import React from 'react';
import AuthContainer from 'components/AuthContainer';
import { Label, FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import Select from 'react-select';
import { ReactComponent as ImageIcon } from 'assets/img/icons/image.svg';
import { STATES } from 'utils/constants';
import './CompanyInfoForm.scss';

const getOptions = () => {
  const states = STATES;

  return states.map(item => ({ value: item.name, label: item.name }));
};
const getSelectedItem = value => {
  const filtered = getOptions().filter(item => item.value === value);

  if (filtered.length) return filtered[0];
};

export default function CompanyInfoForm(props) {    
  return (
    <AuthContainer>
      <div className='account-details-form'>        
        <div className='company-information error-scroll-parent'>
          <div className='auth-form-title'>Contact Information</div>
          <FormGroup className="dope-form-group">
            <DopeInput
              value={props.data.person.firstName}
              name='firstName'
              data-group='person'
              title='First Name'
              supText='*'
              error={props.errors.firstName}
              onChange={props.onChange}
            />
          </FormGroup>
          <FormGroup className="dope-form-group">
            <DopeInput
              title='Last Name'
              supText='*'
              name='lastName'
              data-group='person'
              value={props.data.person.lastName}
              error={props.errors.lastName}
              onChange={props.onChange}
            />
          </FormGroup>
        </div>
        <div className='company-information error-scroll-parent'>
          <div className='auth-form-title'>Company Information</div>
          <FormGroup className="dope-form-group">
            <DopeInput
              value={props.data.name}
              name='name'
              title='Company Name'
              supText='*'
              error={props.errors.name}
              onChange={props.onChange}
            />
          </FormGroup>
          <FormGroup className="dope-form-group">            
            <DopeInput
              title='Company Phone Number'
              supText='*'
              name='phone'
              value={props.data.phone}
              mask='(999) 999 9999'
              error={props.errors.phone}
              onChange={props.onChange}
            />
          </FormGroup>
        </div>
        <div className='company-address error-scroll-parent'>
          <div className='auth-form-title'>Company Address</div>
          <FormGroup className="dope-form-group">
            <DopeInput
              value={props.data.companyAddress.address1}
              title='Address 1'
              supText='*'
              name='address1'
              data-group='companyAddress'
              error={props.errors.address1}
              onChange={props.onChange}
            />
          </FormGroup>
          <FormGroup className="dope-form-group">
            <DopeInput
              value={props.data.companyAddress.address2}
              name='address2'
              data-group='companyAddress'
              title='Address 2'
              error={props.errors.address2}
              onChange={props.onChange}
            />
          </FormGroup>
          <div className='one-line'>
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.companyAddress.city}
                title='City'
                supText='*'
                name='city'
                data-group='companyAddress'
                error={props.errors.city}
                onChange={props.onChange}
              />
            </FormGroup>
            <FormGroup className={`dope-form-group ${props.errors.state
                ? 'select-error'
                : ''}`}              
            >
              <span className='input-title'>
                State <sup>*</sup>
              </span>
              <div className='dope-select'>
                <Select
                  className="dope-select"
                  classNamePrefix="dope"
                  value={getSelectedItem(props.data.companyAddress.state)}
                  options={getOptions()}
                  placeholder=''
                  onChange={item => {
                    props.onChangeOnboarding('state', '', 'errors');
                    props.onChangeOnboarding(
                      'state',
                      item.value,
                      'companyAddress',
                    );
                  }}
                />
              </div>
              {props.errors.state && (
                <div className='validation-message'>{props.errors.state}</div>
              )}
            </FormGroup>
          </div>

          <div className='one-line'>
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.companyAddress.zip}
                title='ZIP Code'
                supText='*'
                name='zip'
                data-group='companyAddress'
                mask='99999'
                error={props.errors.zip}
                onChange={props.onChange}
              />
            </FormGroup>
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.companyAddress.zip4}
                title='ZIP 4 Code'
                name='zip4'
                data-group='companyAddress'
                mask='9999'
                error={props.errors.zip4}
                onChange={props.onChange}
              />
            </FormGroup>
          </div>
        </div>

        <div className='field logo-upload'>
          <div className='auth-form-title'>Company Logo</div>
          <div className='logo-upload-container'>
            <div className='logo-img'>
              {props.logo ? (
                <img
                  className='logo-image'
                  src={props.logo}
                  alt='company logo'
                />
              ) : (
                <div className='svg-container'>
                  <ImageIcon className='image-icon' />
                </div>
              )}
            </div>
            <div className='logo-link-block'>
              <Label for='file'>Upload a Company Logo</Label>
              {props.logo && <span onClick={props.removeImage}>Remove</span>}
            </div>
            <input
              className='logo-input'
              type='file'
              accept="image/jpeg,image/png,image/jpg"
              id='file'
              onChange={e => props.onImageChange(e, 'logo')}
            />

            {props.errors.logo && (
              <div className='validation-message'>{props.errors.logo}</div>
            )}
          </div>
        </div>

        <div className='form-footer'>
          <DopeButton color='primary' onClick={props.onSubmit}>
            Get Started
          </DopeButton>
        </div>
      </div>
    </AuthContainer>
  );
}
