export const getAccountApi = (data) => {
  const id = data ? data : 'me';  
  return service.get(
    `${API_URL}/accounts/${id}`
  );
};

export const getPackageListApi = () => {
  return service.get(
    `${API_URL}/subscription/plans`
  );
};

