export const updateAccountApi = (data) => {  
  return service.patch(
    `${API_URL}/accounts/${data.account}`, data.body
  )
}

export const createAccountApi = (data) => {
  return service.post(
    `${API_URL}/accounts`, data.body
  )
}

export const deleteAccountRequestApi = (data) => {
  return service.post(
    `${API_URL}/accounts/${data.accountId}/request-delete`
  )
}

export const cancelDeleteAccountRequestApi = (data) => {   
  return service.patch(    
    `${API_URL}/accounts/${data.accountId}`, data.body
  )
}

export const getAccountsApi = (data) => {
  return service.get(`${API_URL}/accounts`)
}

export const updateAccountSubscriptionApi = (data) => {
  return service.patch(
    `${API_URL}/accounts/${data.account}/subscription`,
    { packageId: data.mainPackageId }
  )
}

export const getAccountHistoriesApi = (data) => {
  return service.get(
    `${API_URL}/accounts/${data.account}/history`,
    data.params
  );
};

export const sendVerificationEmailApi = (data) => {
  return service.post(
    `${API_URL}/accounts/send-verification-email`, data.body
  )
}

export const getOwnPostcardPreferencesApi = data => {
  return service.get(`${API_URL}/ballance/${data}`);
};


// TODO sendRechargeApi
export const sendRechargeApi = (data) => {
  // console.log(data);
  return service.post(
    `${API_URL}/accounts/${data.accountId}/recharge`
  )
}

export const getSmsHistoryApi = (data) => {
  return service.get(
    `${API_URL}/accounts/${data.accountId}/sms`, data.query
  );
};