import React from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import {DopeModalBody} from "components/DopeModal";
import {FormGroup} from "reactstrap";
import {DopeInput} from "components/DopeForms";
import './ConfirmModal.scss';

export default function ConfirmModal(props) {
  const onSubmit = () => {
    props.onConfirm();
    props.toggle();
  };

  return (    
    <DopeModal className='ConfirmModal' isOpen={props.isOpen} toggle={props.toggle} centered>
      <DopeModalHeader className='dope-modal-header' toggle={props.toggle}>
        {props.titleText}
      </DopeModalHeader>
      <DopeModalBody>
        {props.accountData.email && props.selectedAccount.email !== props.accountData.email.trim() && (
          <FormGroup className="dope-form-group">
            <DopeInput
              value={props.accountData.email}
              title='account email'
              name='email'
              readOnly
            />
          </FormGroup>
        )}
        {props.accountData.accountPhone && props.selectedAccount.accountPhone !== props.accountData.accountPhone.trim() && (
          <FormGroup className="dope-form-group">
            <DopeInput
              value={props.accountData.accountPhone}
              title='account phone'
              name='accountPhone'
              readOnly
            />
          </FormGroup>
        )}
      </DopeModalBody>
      <DopeModalFooter>
        <DopeButton color='white' onClick={props.toggle}>
          Cancel
        </DopeButton>
        <DopeButton color='primary' onClick={onSubmit}>
          Save
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>    
  );
}
