import { all, call, put, takeLatest } from 'redux-saga/effects';
import { toast } from 'react-toastify';
import * as generalActions from 'actions/general';
import * as neighborhoodBlitzActions from '../actions/neighborhoodBlitz';
import {push} from "connected-react-router";
import {pathOr} from 'ramda';

import {
  getNeighborsApi,
  convertAddressToLatLonApi,
  sendNeighborsApi,
  updateNeighborsApi,
  getNeighborsPreviewImgApi,
  getNeighborsListApi,
  getNeighborsItemApi,
  deleteNeighborsItemApi,
  saveAsMyNeighborsTemplateApi,
  getRecentNeighborhoodBlitzPostcardsApi,
  getMyNeighborhoodBlitzPostcardsApi,
  deleteMyNeighborhoodBlitzPostcardApi,
  buyNeighborsListOfContactsApi,
  createNbhCampaignsApi,
} from 'requests/neighborhoodBlitz';

export function* getNeighbors(action) {
  try {    
    const response = yield call(getNeighborsApi, action.data);    
    if(response.data.count<action.data.count){
      toast.warning(`We have found ${response.data.count} address that covers your requirement within ${(Math.floor( (response.data.distance*1000)/1609.344 ) / 1000 )}ml around`);
    }
    yield put({
      type: neighborhoodBlitzActions.GET_NEIGHBORS_REQUEST_SUCCESS,
      data: response.data,
    });
  } catch (error) {
    yield put({
      type: neighborhoodBlitzActions.GET_NEIGHBORS_REQUEST_FAILED,
      error,
    });
  }
}

export function* convertAddressToLatLon(action) {
  try {
    const response = yield call(convertAddressToLatLonApi, action.data);
    yield put({
      type: neighborhoodBlitzActions.CONVERT_ADDRESS_TO_LAT_LON_REQUEST_SUCCESS,
      data: response.data,
    });

  } catch (error) {
    yield put({
      type: neighborhoodBlitzActions.CONVERT_ADDRESS_TO_LAT_LON_REQUEST_FAILED,
      error,
    });
  }
}

export function* saveNeighborsAsDraftSummary(action) {
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    const response = yield call(sendNeighborsApi, action.data);
    if(response && response.status === 200) {
      yield put(push(`/neighborhood-blitz/${response.data._id}/edit`));
      yield put(neighborhoodBlitzActions.changeStep(3));
    }
    yield put({
      type: neighborhoodBlitzActions.SAVE_NEIGHBORS_AS_DRAFT_SUMMARY_REQUEST_SUCCESS
    });
    toast.success(`You have successfully saved neighborhood-blitz settings`);
  } catch (e) {
    yield put({ type: neighborhoodBlitzActions.SAVE_NEIGHBORS_AS_DRAFT_SUMMARY_REQUEST_FAILED });
    let errorMessage = pathOr("Error. Cann't save neighborhood-blitz", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
}

export function* updateNeighborsSummary(action) {
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    const response = yield call(updateNeighborsApi, action.data);
    if(response && response.status === 200) {
      yield put(neighborhoodBlitzActions.changeStep(3));
    }
    yield put({
      type: neighborhoodBlitzActions.UPDATE_NEIGHBORS_SUMMARY_REQUEST_SUCCESS
    });
    toast.success(`You have successfully updated Neighborhood Blitz`);

  } catch (e) {
    if(true && action.callback) {  //todo check 400 answer or response
      action.callback();
    }
    console.log("error", e);
    yield put({ type: neighborhoodBlitzActions.UPDATE_NEIGHBORS_SUMMARY_REQUEST_FAILED, e });
    toast.error(`Error. Cann't update Neighborhood Blitz`)
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
}

export function* updateNeighbors(action) {
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    const response = yield call(updateNeighborsApi, action.data);
    if(response && response.status === 200 && action.callback) {
      action.callback();
    }
    yield put({
      type: neighborhoodBlitzActions.UPDATE_NEIGHBORS_REQUEST_SUCCESS
    });
    if(action.data.body.status==='in-progress'){
      toast.success(`You have successfully sent neighborhood-blitz`);
    }else{
      toast.success(`You have successfully updated neighborhood-blitz`);
    }
    // yield put(push('/neighborhood-blitz'));
  } catch (e) {
    if(true && action.callback) {  //todo check 400 answer or response
      action.callback();
    }
    console.log("error", e);
    yield put({ type: neighborhoodBlitzActions.UPDATE_NEIGHBORS_REQUEST_FAILED, e });
    if(action.data.body.status==='in-progress'){      
      // toast.error(`Error. Cann't sent neighborhood-blitz`)
      let errorMessage = pathOr("Error. Cann't sent neighborhood-blitz", ['response', 'data', 'error', 'dopeMessage'], e);
      toast.error(errorMessage);
      yield put(push('/neighborhood-blitz'));
    }else{
      // toast.error(`Error. Cann't update neighborhood-blitz`);
      let errorMessage = pathOr("Error. Cann't update neighborhood-blitz", ['response', 'data', 'error', 'dopeMessage'], e);
      toast.error(errorMessage);
    }
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
}

export function* getNeighborsPreviewImg(action) {
  try {
    const [response1,response2] = yield all([
      call(getNeighborsPreviewImgApi, {template: action.data.body.template.front, bucket:action.data.body.bucket, side: 'front'}),
      call(getNeighborsPreviewImgApi, {template: action.data.body.template.back, bucket:action.data.body.bucket, side: 'back'})
    ]);

    const previewImg = {
      front: {
        mini: response1.data.images.mini,
        full: response1.data.images.full
      },
      back: {
        mini: response2.data.images.mini,
        full: response2.data.images.full
      }
    };

    yield put({
      type: neighborhoodBlitzActions.GET_NEIGHBORS_PREVIEW_IMG_REQUEST_SUCCESS,
      data: previewImg
    });
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });

    yield put(neighborhoodBlitzActions.setShowConfirmPopup(true))

  } catch (e) {
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't get previewImg", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: neighborhoodBlitzActions.GET_NEIGHBORS_PREVIEW_IMG_REQUEST_FAILED, e });
  }
}

