export function* saveAsMyNeighborsTemplate(action) {  
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    const response = yield call(saveAsMyNeighborsTemplateApi, action.data);
    if(response && response.status === 200 && action.callback) {
      action.callback();
    }
    yield put({
      type: neighborhoodBlitzActions.GET_MY_NEIGHBORHOODBLITZ_POSTCARD_REQUEST,
      // data: {accountId: action.data.accountId, query:{ type: 'neighborhood', page:0, limit: 10 }}
      data: {accountId: action.data.accountId, query:{ page:0, limit: 10, types: 'postcard,eddm,neighborhood'}}
    });
    yield put({
      type: neighborhoodBlitzActions.SAVE_AS_MY_NEIGHBORS_TEMPLATE_REQUEST_SUCCESS
    });    
    toast.success(`You have successfully save Neighborhood Blitz template`);
  } catch (e) {
    if(true && action.callback) {  //todo check 400 answer or response
      action.callback();
    }
    console.log("error", e);
    yield put({ type: neighborhoodBlitzActions.SAVE_AS_MY_NEIGHBORS_TEMPLATE_REQUEST_FAILED, e });          
    let errorMessage = pathOr("Error. Cann't save Neighborhood Blitz template", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);    
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
}

export function* getRecentNeighborhoodBlitzPostcards(action) {
  // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true }); 
  yield put({ type: neighborhoodBlitzActions.SHOW_RECENT_NEIGHBORHOODBLITZ_POSTCARDS_LOADER, data: true });
  try {
    const response = yield call(getRecentNeighborhoodBlitzPostcardsApi, action.data);      
    let data = {};
    data = {
      ...response.data,
      data: response.data.data.filter( (el)=>(el.templateStatus==='auto-saved') ).map( (el)=>(
        {          
          _id: el._id,
          front: el.templates ? el.templates.front : '', 
          back: el.templates ? el.templates.back : '',
          front_thumbnail: el.images ? el.images.front.mini : '',
          size: el.size,
          createdAt: el.createdAt,
        }        
      ))
    };                
    yield put({
      type: neighborhoodBlitzActions.GET_RECENT_NEIGHBORHOODBLITZ_POSTCARD_REQUEST_SUCCESS,
      data: {...data, ...action.data.query}
    });    
  } catch (e) {
    console.log(e);
    let errorMessage = pathOr("Error.", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: neighborhoodBlitzActions.GET_RECENT_NEIGHBORHOODBLITZ_POSTCARD_REQUEST_FAILED });    
  }
  // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false }); 
  yield put({ type: neighborhoodBlitzActions.SHOW_RECENT_NEIGHBORHOODBLITZ_POSTCARDS_LOADER, data: false });
}

