export function* createNbhCampaigns(action) {
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    const response = yield call(createNbhCampaignsApi, action.data);
    if(response && response.status === 200 && response.data._id){
      yield put({
        type: neighborhoodBlitzActions.CREATE_NBH_CAMPAIGNS_SUCCESS
      });    
      yield put(push(`/campaigns/${response.data._id}`));
    }else{
      toast.error("Error. Cann't create campaigns");
    }
  } catch (e) {    
    console.log("error", e);
    yield put({ type: neighborhoodBlitzActions.CREATE_NBH_CAMPAIGNS_FAILED, e });
    let errorMessage = pathOr("Error. Cann't create campaigns", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);    
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
}

export default all([
  takeLatest(neighborhoodBlitzActions.GET_NEIGHBORS_REQUEST, getNeighbors),
  takeLatest(neighborhoodBlitzActions.CONVERT_ADDRESS_TO_LAT_LON_REQUEST, convertAddressToLatLon),
  takeLatest(neighborhoodBlitzActions.SAVE_NEIGHBORS_AS_DRAFT_SUMMARY_REQUEST, saveNeighborsAsDraftSummary),
  takeLatest(neighborhoodBlitzActions.UPDATE_NEIGHBORS_SUMMARY_REQUEST, updateNeighborsSummary),
  takeLatest(neighborhoodBlitzActions.UPDATE_NEIGHBORS_REQUEST, updateNeighbors),
  takeLatest(neighborhoodBlitzActions.GET_NEIGHBORS_PREVIEW_IMG_REQUEST, getNeighborsPreviewImg),
  takeLatest(neighborhoodBlitzActions.GET_NEIGHBORS_LIST_REQUEST, getNeighborsList),
  takeLatest(neighborhoodBlitzActions.GET_NEIGHBORS_ITEM_REQUEST, getNeighborsItem),
  takeLatest(neighborhoodBlitzActions.DELETE_NEIGHBORS_ITEM_REQUEST, deleteNeighborsItem),  
  takeLatest(neighborhoodBlitzActions.SAVE_AS_MY_NEIGHBORS_TEMPLATE_REQUEST, saveAsMyNeighborsTemplate),
  takeLatest(neighborhoodBlitzActions.GET_RECENT_NEIGHBORHOODBLITZ_POSTCARD_REQUEST, getRecentNeighborhoodBlitzPostcards), 
  takeLatest(neighborhoodBlitzActions.GET_MY_NEIGHBORHOODBLITZ_POSTCARD_REQUEST, getMyNeighborhoodBlitzPostcards),
  takeLatest(neighborhoodBlitzActions.DELETE_MY_NEIGHBORHOODBLITZ_POSTCARD_REQUEST, deleteMyNeighborhoodBlitzPostcard),  
  takeLatest(neighborhoodBlitzActions.BUY_NEIGHBORS_LIST_OF_CONTACTS_REQUEST, buyNeighborsListOfContacts),
  takeLatest(neighborhoodBlitzActions.CREATE_NBH_CAMPAIGNS_REQUEST, createNbhCampaigns),
]);