  getCroppedImg(image, crop, fileName) {
    const base64 = image.src;
    const block = base64.split(";"); // Get the content type of the image
    const contentType = block[0].split(":")[1];
    const canvas = document.createElement('canvas');
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    let ownScaleX, ownScaleY;
    switch(this.state.croperType){
      case 'imageContain':
        let ownMaxScales=Math.max(this.state.coverCrop.imgContainerWidth / crop.width, this.state.coverCrop.imgContainerHeight / crop.height);
        ownScaleX = ownMaxScales;
        ownScaleY = ownMaxScales;
        // console.log(Math.max(this.state.coverCrop.imgContainerWidth / crop.width, this.state.coverCrop.imgContainerHeight / crop.height) );
        break;   
      case 'aspectRatioImageCover':
        ownScaleX = this.state.coverCrop.imgContainerWidth / crop.width;
        ownScaleY = this.state.coverCrop.imgContainerHeight / crop.height; 
        break;      
      default:
        break; 
    }         
    // console.log("croper type:  ", this.state.croperType);
    // console.log("croper ownScaleX:  ", ownScaleX);
    // console.log("croper ownScaleY:  ", ownScaleY);    
    canvas.width = crop.width * ownScaleX;
    canvas.height = crop.height * ownScaleY;
    const ctx = canvas.getContext('2d');
    // console.log("----------------------------------------");
    // console.log("DON'T TOUCH.....FOR FUTURE TESTING OTHER TYPES OF FUCKED CROPPER");
    // console.log("crop getCroppedImg :", crop);
    // console.log("image.naturalWidth :", image.naturalWidth);
    // console.log("image.naturalHeight :", image.naturalHeight);
    // console.log("image.width :", image.width);
    // console.log("image.height :", image.height);    
    // console.log("imgContainerWidth: ",this.state.coverCrop.imgContainerWidth);
    // console.log("imgContainerHeight: ",this.state.coverCrop.imgContainerHeight);
    // console.log("ownScaleX: ", ownScaleX);
    // console.log("ownScaleY: ", ownScaleY);
    // console.log("canvas.width: ", canvas.width);
    // console.log("canvas.height: ", canvas.height);
    // console.log(
    //   "\n","\n",
    //   "cropper:",
    //   "\n",
    //   "crop.width: ",crop.width,"\n",
    //   "crop.height: ",crop.height,"\n",
    //   "crop.x * scaleX: ",crop.x * scaleX,"\n",
    //   "crop.y * scaleY: ",crop.y * scaleY,"\n",
    //   "crop.width * scaleX: ",crop.width * scaleX,"\n",
    //   "crop.width * scaleY: ",crop.width * scaleY,"\n",
    // );
    // console.log("----------------------------------------");
    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width * ownScaleX,
      crop.height * ownScaleY
    );
    // this code for injecting image to template(ready for HTMLToImage library)
    let jpeg = canvas.toDataURL(contentType, 0.9);
    // var jpeg = canvas.toDataURL("image/png");
    this.currentTargets.forEach(target => {
      if (target.type === "image") {
        target.el.src = jpeg;
      }
      this.setState({
        base64CroppedImage: jpeg,
      });
    });  
    // As a blob  (not using croppedImageUrl now)
    return new Promise((resolve, reject) => {
      canvas.toBlob(blob => {
        if (!blob) {
          console.error('Canvas is empty');
          return;
        }
        blob.name = fileName;
        window.URL.revokeObjectURL(this.fileUrl);
        this.fileUrl = window.URL.createObjectURL(blob);
        resolve(this.fileUrl);
      }, contentType);
    });
  }  
