import React, { useState } from 'react';
import {
  Container,
  Row,
  Col,
} from 'reactstrap';
import CompanyLogo from '../CompanyLogo';
import DeleteCardModal from './DeleteCardModal';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import Address from '../Address';
import CreditCards from './CreditCards';
import './CreditCardInfo.scss';

export default function CreditCardInfo(props) {
  const [deleteCardState, onSetDeleteCard] = useState({modalShow: false, id: ''});  
  const {billingAddress, createdAt, logo, name } = props.data;
  const {logo: newLogo = logo} = props.updatedAccount;
  const {billingAddress: newBillingAddress = billingAddress} = props.updatedAccount;
  const {errors} = props;
  
  const deleteCard = () => {     
    onSetDeleteCard({ ...deleteCardState, modalShow: !deleteCardState.modalShow })    
    props.onDeleteCard(deleteCardState.id);     
  };
  return (
    <div className='credit-card-tab account-form-container'>
      <Container fluid>
        <Row>
          <Col md={5}>
            <CompanyLogo timeZone={props.timeZone} createdAt={createdAt} name = {name} img={newLogo} onChange={props.onFileUpload} currentAccount={props.currentAccount._id}/>
          </Col>
          <Col md={7} className='account-form'>
            <Address
              data={newBillingAddress}
              type='billingAddress'
              onPlaceLoaded={props.onPlaceLoaded}
              title='Billing address'
              onChange={props.onChange}
              errors={errors}
              onErrorChange={props.onErrorChange}
            />
            <div className='account-button-container'>
              <DopeButton onClick={props.onSubmit} color='primary'>
                Save changes
              </DopeButton>
            </div>
            <CreditCards
              onDefaultCardChange = {props.onDefaultCardChange}
              onDeleteCard = {
                (cardId) => {
                  onSetDeleteCard({ ...deleteCardState, modalShow: !deleteCardState.modalShow, id: cardId })
                }
              }
              currentAccount={props.currentAccount}
            />
          </Col>
        </Row>
      </Container>
      <DeleteCardModal
        modalShow={deleteCardState.modalShow}
        onChange={() => {onSetDeleteCard({ ...deleteCardState, modalShow: !deleteCardState.modalShow})}}
        onConfirm={deleteCard}
      />
    </div>
  );
}
