import React, {useState} from 'react';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { useSelector, useDispatch } from 'react-redux';
import {changeStep} from 'actions/neighborhoodBlitz';
import {DopeTable} from "components/DopeTable";
import {columnsFunc} from "./config";
import { getPriceByCost } from 'utils/helper';
import './NeighborhoodBlitzSummary.scss';
import {  
  setNewDeliveryAddress,
  saveNeighborsAsDraftSummaryRequest,
  updateNeighborsSummaryRequest,
} from "actions/neighborhoodBlitz";
import PricingModal from "./PricingModal/PricingModal";
import ReturnAddress from "./ReturnAddress/ReturnAddress";
import SaveListPopup from "../NeighborhoodBlitzMap/SaveListPopup/SaveListPopup";

export default function NeighborhoodBlitzSummary({typePreview, neighborhoodBlitzId}) {

  const dispatch = useDispatch();

  const [showPricingModal, setShowPricingModal] = useState(false);
  const [tablePageNumber, setTablePageNumber] = useState(1);  
  const neighborhoods = useSelector(state => state.neighborhoodBlitz.neighborhoods);
  const currentAccount = useSelector(state => state.account.currentAccount);
  const postage = useSelector(state => state.neighborhoodBlitz.postage);  
  const returnAddress = useSelector(state => state.neighborhoodBlitz.returnAddress);
  const shippingAddress = returnAddress ? returnAddress : currentAccount.shippingAddress;
  const configs = useSelector(state => state.dbConfigs.configs);
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const searchAddress = useSelector(state => state.neighborhoodBlitz.searchAddress);
  const tablePageLimit = 10;
  const filters = useSelector(state => state.neighborhoodBlitz.filters);
  const status = useSelector(state => state.neighborhoodBlitz.status);
  const searchDistance = useSelector(state => state.neighborhoodBlitz.searchDistance);
  const amountAddresses = useSelector(state => state.neighborhoodBlitz.amountAddresses);
  const ownPrice = useSelector(state => state.general.ownPrice);
  const [saveListPopupOpen, setSaveListPopupOpen] = useState(false);
  const neighborhoodSize = useSelector(state => state.neighborhoodBlitz.neighborhoodSize);

  const onPlaceLoaded = (key, place, group) => {

    let data = currentAccount;

    let zip = place.address_components.find(x => x.types[0] === 'postal_code');
    zip = zip ? zip.long_name : '';

    let city = place.address_components.find(x => x.types[0] === 'locality');
    city = city ? city.long_name : '';

    let state = place.address_components.find(
      x => x.types[0] === 'administrative_area_level_1',
    );
    state = state ? state.long_name : '';

    let address1_street_number = place.address_components.find(
      x => x.types[0] === 'street_number',
    );
    let address1_route = place.address_components.find(
      x => x.types[0] === 'route',
    );
    let address1 = address1_street_number
      ? address1_street_number.long_name
      : '';
    address1 = address1_route ? address1 + address1_route.long_name : '';

    let address = {
      zip,
      city,
      state,
      address1,
    };

    data[group] = address;
    dispatch(setNewDeliveryAddress([group], address));
  };

  const [deliveryAddressError, setDeliveryAddressError] = useState({
    state: "", city: "", zip: "", address1: ""
  });

  const onErrorChange = (value) => {
    setDeliveryAddressError(value);
  };

  const calculatePrice = (qty, size, postage) => {
    if (!qty) return "0.00";    
    let pricePerOneClasses = getPriceByCost(getNeighborhoodBlitzPostcardPrices()[size], qty);       
    let pricePerOne = pricePerOneClasses.price;
    if (postage === 'first-class')
      pricePerOne = pricePerOneClasses['first-class-price'];    
    return (pricePerOne * qty / 100).toFixed(2);
  };

  const getNeighborhoodBlitzPostcardPrices = () => {
    const defaultPostcardPrice = configs.find(item => item.name === "neighborhoodPostcardsPrice").price;
    const customPostcardPrice = ownPrice?ownPrice.neighborhoodPrice:null;
    if (customPostcardPrice && Object.values(customPostcardPrice).some(arr => arr.length)){      
      return customPostcardPrice;
    }
    return defaultPostcardPrice;
  };

  const paginationOptions = {
    page: tablePageNumber,
    onPageChange: (value) => setTablePageNumber(value.page),
    limit: tablePageLimit,
    totalSize: neighborhoods.length
  };

  const saveUploadSummary = () => {
    const data = {
      body: {
        returnAddress: shippingAddress,
        postage: postage,
        // contacts: neighborhoods.map(item => item._id),
        dataToFetch: neighborhoods,
        size: neighborhoodSize,
        searchAddress: searchAddress,
        searchCount: amountAddresses,
        status: status,
        distance: searchDistance,
        filters: {...filters, homeValue: JSON.stringify(filters.homeValue)}
      },
      queries: currentAccountId,
    };
    dispatch(saveNeighborsAsDraftSummaryRequest(data));
  };

  const editUploadSummary = () => {
    const data = {
      body: {
        returnAddress: shippingAddress,
        postage: postage,
        // contacts: neighborhoods.map(item => item._id),
        dataToFetch: neighborhoods,
        size: neighborhoodSize,
        searchAddress: searchAddress,
        searchCount: amountAddresses,
        status: status,
        distance: searchDistance,
        filters: {...filters, homeValue: JSON.stringify(filters.homeValue)}
      },
      queries: currentAccountId,
    };
    data.neighborhoodBlitzId = neighborhoodBlitzId;
    dispatch(updateNeighborsSummaryRequest(data));
  };

  return (
    <div className="NeighborhoodBlitzSummary content postcard-wizard">
      <h1 className="main-title">Order Summary</h1>
      <div className="EddmSummaryCard">
        <div className="title-container t-shadow">
          <div className="title">Addresses ({neighborhoods.length})</div>
          <div>
            {/*<DopeButton*/}
            {/*  className='mr-3'*/}
            {/*  color="primary"*/}
            {/*  onClick={() => setSaveListPopupOpen(true)}*/}
            {/*  disabled={typePreview==='view'}*/}
            {/*>*/}
            {/*  Save List*/}
            {/*</DopeButton>*/}
            <DopeButton
              color="white"
              onClick={()=>{dispatch(changeStep(1));}}
              disabled={typePreview==='view'}
            >
              Edit
            </DopeButton>
          </div>
        </div>
        <DopeTable
          keyField="_id"
          columns={[...columnsFunc()]}
          data={neighborhoods.reduce((prev, current, index) => {
            if((index >= tablePageNumber * tablePageLimit - tablePageLimit) && (index < tablePageNumber * tablePageLimit)){
              return [...prev, {
                _id: `${current.street}-${index}`,
                index: index + 1,
                address: `${current.street} ${current.suite ? ', ' + current.suite : ''}`,
              }]
            }
            return prev;
          }, [])}
          paginationOptions={paginationOptions}
        />
      </div>
      {/*temporarily hidden*/}
      {/*<Card className="pricing-card">*/}
      {/*  <CardHeader>Pricing</CardHeader>*/}
      {/*  <CardBody>*/}
      {/*    <div className="pricing-radio-container">*/}
      {/*      <div className="pricing-radio-row">*/}
      {/*        <DopeRadio*/}
      {/*          name="pricing"*/}
      {/*          checked={postage === "standard"}*/}
      {/*          value="standard"*/}
      {/*          label="Standard Postage"*/}
      {/*          onChange={() => dispatch(setPostageData("standard"))}*/}
      {/*          disabled={typePreview==='view'}*/}
      {/*        />*/}
      {/*        <span className="price-per-text">${costPerOne("standard")} / postcard</span>*/}
      {/*        <span className="delivery-days">Estimated Delivery: {moment(new Date()).add(7, 'days').format('MMM DD')} - {moment(new Date()).add(21, 'days').format('MMM DD')}</span>*/}
      {/*        <span className="tooltips-text">*/}
      {/*          <span data-tip data-for={`tooltips-standard`}>What is Standard type?</span>*/}
      {/*          <ReactTooltip*/}
      {/*            className="pricing-tooltip"*/}
      {/*            html={true}*/}
      {/*            id={`tooltips-standard`}*/}
      {/*          >*/}
      {/*            {`<span>Standard mail is not forwarded or returned if undeliverable unless a special endorsement is used. Standard mail will typically be delivered in under 5 business days locally, but nationwide it could take 2 to 3 weeks. It should not be used for time-sensitive mail. As with Presorted First-class, to receive the lower postage rate, the addresses must be NCOA updated, CASS certified, and put into presort order.</span>`}*/}
      {/*          </ReactTooltip>*/}
      {/*        </span>*/}
      {/*      </div>*/}
      {/*      <div className="pricing-radio-row">*/}
      {/*        <DopeRadio*/}
      {/*          name="pricing"*/}
      {/*          checked={postage === "first-class"}*/}
      {/*          value="first-class"*/}
      {/*          label="First Class Postage"*/}
      {/*          onChange={() => dispatch(setPostageData("first-class"))}*/}
      {/*          disabled={typePreview==='view'}*/}
      {/*        />*/}
      {/*        <span className="price-per-text">${costPerOne("first-class")} / postcard</span>*/}
      {/*        <span className="delivery-days">Estimated Delivery: {moment(new Date()).add(4, 'days').format('MMM DD')} - {moment(new Date()).add(6, 'days').format('MMM DD')}</span>*/}
      {/*        <span className="tooltips-text">*/}
      {/*          <span data-tip data-for={`tooltips-first-class`} >What is First class type?</span>*/}
      {/*          <ReactTooltip*/}
      {/*            className="pricing-tooltip"*/}
      {/*            html={true}*/}
      {/*            id={`tooltips-first-class`}*/}
      {/*          >*/}
      {/*            {*/}
      {/*              `<span>USPS first-class delivery is given priority over standard mail, and local mail will typically be delivered in 2-3 days. Nationally, first-class mail should be delivered in about 4 days. First-class mail postage includes forwarding and return services with no additional charge.</span>`*/}
      {/*            }*/}
      {/*          </ReactTooltip>*/}
      {/*        </span>*/}
      {/*      </div>*/}
      {/*    </div>*/}
      {/*    <div className="pricing-link-container">*/}
      {/*        <span*/}
      {/*          className="view-pricing-link"*/}
      {/*          onClick={() => setShowPricingModal(true)}*/}
      {/*        >*/}
      {/*          View Pricing*/}
      {/*        </span>*/}
      {/*    </div>*/}
      {/*  </CardBody>*/}
      {/*</Card>*/}
      <div className="summaryGrid">
        <div className="delivGridCard">
          <div className="deliveryInfo-grid">
            <div className="title">Return Address</div>
            <ReturnAddress
              data={shippingAddress}
              errors={deliveryAddressError}
              onErrorChange={onErrorChange}
              type='shippingAddress'
              onPlaceLoaded={onPlaceLoaded}
              onChange={setNewDeliveryAddress}
              disabled={typePreview==='view'}
            />
          </div>
        </div>
        <div className="summaryGridCard">
          <div className="title-container t-shadow">
            <div className="title">Order Summary</div>
          </div>
          <div className="cardBodySection">
            <div className="sectionOrderItem">
              <span className="item-text">Total Delivery Addresses</span>
              <span className="item-value">{neighborhoods.length}</span>
            </div>
          </div>
          <div className="cardBodySection">
            <div className="sectionOrderItem">
              <span className="item-text">Pricing</span>
              <span className="item-value">{postage} Postage</span>
            </div>
          </div>
          <div className="totalSection">
            <span className="item-total-text">Total Price</span>
            <span className="item-total-text">{`$${calculatePrice(neighborhoods.length, neighborhoodSize, postage )}`}</span>
          </div>
        </div>
      </div>

      <div className="btnContainer">
        {
          typePreview ? (
            typePreview==='edit' ? (
              <DopeButton
                onClick={editUploadSummary}
                color='primary'
              >
                Save and upload postcard
              </DopeButton>
            ):(
              <DopeButton
                onClick={()=>{dispatch(changeStep(3));}}
                color='primary'
              >
                Continue
              </DopeButton>
            )
          ):(
            <DopeButton
              onClick={saveUploadSummary}
              color='primary'
            >
              Save and upload postcard
            </DopeButton>
          )
        }
      </div>
      <PricingModal
        open={showPricingModal}
        toggle={() => setShowPricingModal(!showPricingModal)}
        data={getNeighborhoodBlitzPostcardPrices()['6x9']} //temporary
      />
      <SaveListPopup
        isOpen={saveListPopupOpen}
        toggle={() => setSaveListPopupOpen(!saveListPopupOpen)}
        changeStep={changeStep}
        neighborhoods={neighborhoods}
      />
    </div>
  );
}