import React from 'react';
import ReactDOM from 'react-dom';

import LogRocket from 'logrocket';
import setupLogRocketReact from 'logrocket-react';

import { toast } from 'react-toastify';
import * as serviceWorker from './serviceWorker';
import { Switch } from 'react-router-dom';
import { ConnectedRouter } from 'connected-react-router'
import './assets/base.scss';
import configureStore, { history } from './config/configureStore';
import { Provider } from 'react-redux';
import { Elements, StripeProvider } from 'react-stripe-elements';
import auth from './utils/Auth';
import Dashboard from "./containers/Dashboard/Dashboard";
import ThemeCSSVar from "./HOC/ThemeCSSVar";
import Login from './views/Auth/LogInPage/LogInPage';
import Verification from './views/Auth/Verification/Verification';
import VerificationConfirm from './views/Auth/Verification/VerificationConfirm';

import LandingPage from './views/LandingPage';
import SignUp from './views/Auth/SignUp';
import CompanyInformation from './views/Auth/CompanyInformation';
import ForgotPassword from './views/Auth/ForgotPassword';
import NewPassword from './views/Auth/NewPassword';
import SignUpVerifyEmail from './views/Auth/SignUpVerifyEmail';
import SignUpVerifyEmailMessage from './views/Auth/SignUpVerifyEmailMessage';
import NotFound from 'views/NotFound';
import PrivateRoute from './HOC/PrivateRoute/PrivateRoute';
import PublicRoute from './HOC/PublicRoute/PublicRoute';
import { STRIPE_PK, NODE_ENV } from './config/config';
import {
  LANDING,
  DASHBOARD_ROOT,
  ACCOUNT_DETAILS_PATH,
  SIGN_UP_VERIFY_EMAIL_MESSAGE,   
  SIGN_UP_VERIFY_EMAIL,
  LOG_IN,
  FORGOT_PASSWORD,
  NEW_PASSWORD,
  SIGN_UP,
  P404,
  VERIFICATION,
  VERIFICATION_CONFIRM,
} from 'utils/constants';

import './styles/themes.scss';

import 'bootstrap/dist/css/bootstrap.css';
import './assets/scss/paper-dashboard.scss';
import './styles/styles.scss';
import 'react-toastify/dist/ReactToastify.css';
if(NODE_ENV==="production"){
    // lasofttest1@gmail.com 
  LogRocket.init(`hsh3mn/${NODE_ENV}-dope360`,{
    // lasofttest2@gmail.com 
  // LogRocket.init(`gacded/${NODE_ENV}-dope360-dvkxz`,{ 
    // lasofttest3@gmail.com
  // LogRocket.init(`9pumhn/${NODE_ENV}-dope`,{
    shouldAggregateConsoleErrors: true,
  });
  setupLogRocketReact(LogRocket);  
}

const store = configureStore();
const rootElement = document.getElementById('root');

toast.configure();

ReactDOM.render(  
  <Provider store={store} >    
    <StripeProvider apiKey={STRIPE_PK}>
      <Elements>
        <ThemeCSSVar>  
          <ConnectedRouter history={history}>
            <Switch>              

              <PublicRoute path={LANDING} auth={auth} component={LandingPage} />
              <PublicRoute path={LOG_IN} auth={auth} component={Login} />                
              <PublicRoute path={SIGN_UP_VERIFY_EMAIL_MESSAGE} auth={auth} component={SignUpVerifyEmailMessage} />
              <PublicRoute path={SIGN_UP_VERIFY_EMAIL} auth={auth} component={SignUpVerifyEmail} />
              <PublicRoute path={SIGN_UP} auth={auth} component={SignUp} />
              <PublicRoute path={FORGOT_PASSWORD} auth={auth} component={ForgotPassword} />
              <PublicRoute path={NEW_PASSWORD} auth={auth} component={NewPassword} />
              <PublicRoute path={VERIFICATION} auth={auth} component={Verification} />
              <PublicRoute path={VERIFICATION_CONFIRM} auth={auth} component={VerificationConfirm} />
              <PublicRoute path={P404} auth={auth} component={NotFound} />

              <PrivateRoute path={ACCOUNT_DETAILS_PATH} auth={auth} component={CompanyInformation} />
              <PrivateRoute path={DASHBOARD_ROOT} auth={auth} component={Dashboard} /> 
                            
            </Switch>              
          </ConnectedRouter>
        </ThemeCSSVar>
      </Elements>
    </StripeProvider>    
  </Provider>,  
  rootElement
);
serviceWorker.unregister();
