import { combineReducers } from 'redux';
import { connectRouter } from 'connected-react-router'

import general from './general';
import ThemeOptions from './ThemeOptions'
import account from './account'
import job from './job'
import map from './map'
import dbConfigs from './configs'
import builder from './builder'
import auth from './auth'
import coupons from './coupons'
import createYardSign from "./createYardSign"
import contacts from './contacts'
import newTrackingNumber from './newTrackingNumber'
import createPostCard from './createPostCard'
import dashboard from './dashboard';
import lists from './lists'
import jobs from './jobs';
import trackingNumbers from './trackingNumbers';
import signUp from './signUp';
import adminAccounts from './adminAccounts';
import adminUsingData from './adminUsingData';
import campaigns from './campaigns';
import neighborhoodBlitz from './neighborhoodBlitz';
import eddm from './eddm';
import listData from './listData';
import morpheusTemplates from './morpheusTemplates';
import handwritten from './handwritten';
import adminHandwritten from './adminHandwritten';
import adminRequests from './adminRequests';
import automation from './automation';

const createRootReducer = (history) =>combineReducers({
    adminAccounts,
    adminUsingData,
    general,
    account,
    job,
    map,
    builder,
    dbConfigs,
    auth,
    coupons,
    ThemeOptions,
    createYardSign,
    contacts,
    newTrackingNumber,
    createPostCard,
    campaigns,
    eddm,
    dashboard,
    lists,   
    jobs,
    trackingNumbers,
    signUp,
    neighborhoodBlitz,
    listData,
    morpheusTemplates,
    handwritten,
    adminHandwritten,
    adminRequests,
    automation,
    router: connectRouter(history),
});

export default createRootReducer;