import React from 'react';
import { withRouter } from 'react-router-dom';
import { DopeTable } from 'components/DopeTable/DopeTable';
import { Button } from 'reactstrap';
import { pathOr } from 'ramda';
import Select from 'react-select';
import { ReactComponent as EditIco } from 'assets/img/icons/edit.svg';
import { ReactComponent as TimeIco } from 'assets/img/icons/time.svg';
import { ReactComponent as DownloadIcon } from 'assets/img/icons/download.svg';
import { ReactComponent as FileIcon } from 'assets/img/icons/file.svg';
import { ReactComponent as DeliveredIcon } from 'assets/img/icons/select.svg';
import './JobsTable.scss';

const columnsFunc = (onSort) => [
  {
    dataField: 'name',
    text: 'Name',
    sort: true,
    sortFunc: () => {
      return false;
    },
    onSort: onSort,
  },
  {
    dataField: 'type',
    text: 'Type',
    sort: true,
    sortFunc: () => {
      return false;
    },
    onSort: onSort,
  },
  {
    dataField: 'date',
    text: 'Date',
    sort: true,
    sortFunc: () => {
      return false;
    },
    onSort: onSort,
  },
  {
    dataField: 'totalPrice',
    text: 'Price',
  },
];

function Job(props) {
  const actionLayout = (cell, row, rowIndex, formatExtraData) => {    
    let showDownloadPdf = ['Postcard','Postcard sending'].includes(row.type);
    return (
      <div className='flexbox'>
        {showDownloadPdf?(
          <Button 
            className={`icon-button ${row.type === 'Private'? 'disabled-ico' : ''}`} onClick={ () => props.onDownloadPdf(row)}>
            <DownloadIcon />
          </Button>
        ):(
          <Button 
            className={"icon-button disabled-ico"} onClick={ () => {}}>            
          </Button>
        )
        }
        <Button className={`icon-button ${row.type === 'Private'? 'disabled-ico' : ''}`} onClick={ () => props.onDetailsClicked(row)}>
          <FileIcon />
        </Button>
      </div>
    );
  };

  const formatedOrders = orders => {
    return orders.map(item => {
      let totalPriceYardSign = pathOr(0, ['yardSign', 'totalPrice'], item) / 100;
      let totalPricePostcard = pathOr(0, ['postcard', 'price'], item) / 100;
      let totalPriceEDDM = pathOr(0, ['eddmSending', 'price'], item) / 100;
      let totalPriceNeighborhood = pathOr(0, ['neighborhoodSending', 'price'], item) / 100;
      let totalPriceAutomation = pathOr(0, ['automation_price'], item) / 100;
      if (item.type === 'Postcard' || item.type === 'Postcard sending') {        
        totalPricePostcard = parseFloat(totalPricePostcard);
        if(totalPricePostcard) totalPricePostcard = totalPricePostcard.toFixed(2)
        else totalPricePostcard = '0.00'; 
        item.totalPrice = `$${totalPricePostcard}`;
      }
      if (item.type === 'Yard Sign') {
        totalPriceYardSign = parseFloat(totalPriceYardSign);
        if(totalPriceYardSign) totalPriceYardSign = totalPriceYardSign.toFixed(2)
        else totalPriceYardSign = '0.00'; 
        item.totalPrice = `$${totalPriceYardSign}`;
      }
      if (item.type === 'EDDMSending') {
        item.type='EDDM';
        totalPriceEDDM = parseFloat(totalPriceEDDM);
        if(totalPriceEDDM) totalPriceEDDM = totalPriceEDDM.toFixed(2)
        else totalPriceEDDM = '0.00';
        item.totalPrice = `$${totalPriceEDDM}`;        
      }
      if (item.type === 'neighborhoodSending') {
        item.type='Neighborhood Blitz';
        totalPriceNeighborhood = parseFloat(totalPriceNeighborhood);
        if(totalPriceNeighborhood) totalPriceNeighborhood = totalPriceNeighborhood.toFixed(2)
        else totalPriceNeighborhood = '0.00';
        item.totalPrice = `$${totalPriceNeighborhood}`; 
      } 
      if (item.type === 'Automation sending') {
        item.type='Automation sending';
        totalPriceAutomation = parseFloat(totalPriceAutomation);
        if(totalPriceAutomation) totalPriceAutomation = totalPriceAutomation.toFixed(2)
        else totalPriceAutomation = '0.00';
        item.totalPrice = `$${totalPriceAutomation}`; 
      }      
      return item;
    });
  };

  const onChange = (e, id) => {
    let jobs = props.jobs;
    let newJobs;

    jobs.forEach((item, i, arr) => {
      if (item.id === id) {
        arr[i].status = e.value;
        newJobs = arr;
      }
    });

    const data = {
      status: e.value,
    };
    
    props.changeStatusJob(data, id, newJobs);
  };

  const statusLayout = (cell, row, rowIndex, formatExtraData) => {
    if (props.changeStatusJob) {
      const mas = [
        { value: 'created', label: 'Created' },
        { value: 'processing', label: 'In processing' },
        { value: 'sent', label: 'Sent' },
      ];

      return (
        <Select
          className="dope-select"
          classNamePrefix="dope"
          value={mas.filter(option => option.value === cell)}
          options={mas}
          onChange={e => onChange(e, row.id)}
          isSearchable={false}
          menuPortalTarget={document.body}
          styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }}
        />
      );
    } else {
      switch (cell) {
        case 'created':
          return (
            <span className='created status-cell'>
              <EditIco alt='Create icon' />
              Created
            </span>
          );
        case 'processing':
          return (
            <span className='processing status-cell'>
              <TimeIco alt='Pending icon' />
              In processing
            </span>
          );
        case 'sent':
          return (
            <span className='sent status-cell'>
              <DeliveredIcon alt='Pending icon' />
              Sent
            </span>
          );
        default:
          return '';
      }
    }
  };

  const actionColumn = {
    dataField: 'edit',
    text: '',
    sort: false,
    formatter: actionLayout,
    attrs: { className: 'icon-cell' },
  };
  const statusColumn = {
    dataField: 'status',
    text: 'Status',
    sort: false,
    formatter: statusLayout,
    attrs: { className: 'status-cell' },
  };
  const onSort = (field, order)=>{
    if(field === 'date') field = 'createdAt';
    else if(field === 'address') field = 'address1';
    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder};
    props.paginationOptions.onPageChange( pagination );
  }
  const orders = formatedOrders(props.jobs);
  const columns = columnsFunc(onSort)

  return (
    <React.Fragment>
      <div className='orders-table-container'>
        <h2 className="title">Orders</h2>
          <DopeTable
            keyField='id'
            data={orders || []}
            columns={[...columns, statusColumn, actionColumn]}            
            rowClasses='table-row'
            paginationOptions={props.paginationOptions}
            loading={props.isLoading}
          />
        {
          !props.isLoading && !orders.length ? (
            <div className='order-table-message'>
              You don't have any orders yet.
            </div>
          ) : null
        }
      </div>
    </React.Fragment>
  );
}

export default withRouter(Job);
