import React from 'react';
import OrderYardSignsPreview from './/OrderYardSignsPreview';
import OrderPostcardsPreview from './OrderPostcardsPreview';
import OrderPostcardsSendingPreview from './OrderPostcardsSendingPreview';
import OrderEDDMPreview from './OrderEDDMPreview';
import OrderNeighborhoodPreview from './OrderNeighborhoodPreview';
import OrderAutomationPreview from './OrderAutomationPreview';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { pathOr } from 'ramda';
import './Modal.scss';

export default function OrderModal(props) {
  const orderType = pathOr('', ['type'], props.data).toLowerCase(); 
  return (
    <React.Fragment>
      {orderType && (
        <DopeModal
          className='modal-order-preview'
          isOpen={props.open}
          toggle={props.toggle}
          centered
        >
          <DopeModalHeader>
            Order Details
          </DopeModalHeader>
          <DopeModalBody>
            {orderType === 'yard sign' && (
              <OrderYardSignsPreview data={props.data} showHeader={false} />
            )}
            {orderType === 'postcard' && (
              <OrderPostcardsPreview data={props.data} showHeader={false} />
            )}  
            {orderType === 'postcard sending' && (
              <OrderPostcardsSendingPreview data={props.data} showHeader={false} />
            )} 
            {orderType === 'eddm' && (
              <OrderEDDMPreview data={props.data} showHeader={false} />
            )} 
            {orderType === 'neighborhood blitz' && (
              <OrderNeighborhoodPreview data={props.data} showHeader={false} />
            )} 
            {orderType === 'automation sending' && (
              <OrderAutomationPreview data={props.data} showHeader={false} />
            )}       
          </DopeModalBody>
          <DopeModalFooter>
            <DopeButton color='primary' onClick={props.toggle}>
              Close
            </DopeButton>
          </DopeModalFooter>
        </DopeModal>
      )}
    </React.Fragment>
  );
}
