import React from 'react';
import { connect } from 'react-redux'
import * as newTrackingNumberActions from 'actions/newTrackingNumber'
import * as trackingNumbersActions from 'actions/trackingNumbers';
import * as newTrackingNumberAction from 'actions/newTrackingNumber';
import AddNumberModal from 'components/AddNumberModal';
import DeleteTrackingNumberModal from './DeleteTrackingNumberModal';
import EditTrackingNumberModal from './EditTrackingNumberModal/EditTrackingNumberModal';
import ActivateTrackingNumberModal from './ActivateTrackingNumberModal/ActivateTrackingNumberModal';
import DeactivateTrackingNumberModal from './DeactivateTrackingNumberModal/DeactivateTrackingNumberModal';
import { DopeTable } from 'components/DopeTable/DopeTable'
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import DropdownMenu from './DropdownMenu/DropdownMenu';
import CreditCardsAfter from 'components/CreditCardsAfter';
import {columnsFunc} from './config';
import moment from 'moment';
import 'moment-timezone';
import './TrackingNumbers.scss';


class TrackingNumbers extends React.Component {
  constructor(props) {
    super(props)

    this.state = {     
      pagination: {
        page: 1,
        limit: 10,
        sort: null,        
      },
      showAddingCard: false,
      afterAddingCardData: {},
    }
  }

  componentDidMount() {
    this.getData()
  }

  componentDidUpdate(prevProps) {
    if (this.props.currentAccount._id !== prevProps.currentAccount._id) this.getData()
  }

  componentWillUnmount() {
    this.props.resetTrackingNumbersPage();
  }

  getData = (pagination = { page: 1, limit: 10}) => {
    let data = {
      account: this.props.currentAccount && this.props.currentAccount._id, ...pagination,  page: pagination.page-1};
    if(!pagination.sort) {
      delete data.sort
    };
    this.props.getTrackingNumbers(data)
  }

  addNewNumber = () => {
    this.props.toggleTrackingNumberModal(true)
  }

  goToContact = (number) => {
    this.props.history.push(`/contacts?job=${number.job._id}`)
  }
 
  customActions = (cell, row, rowIndex, formatExtraData) => {
    return (
      <div className="flexbox">
    <DropdownMenu
      data={row}
      activateTrackingNumberModal = {this.props.toggleActivateTrackingNumberModal}
      deactivateTrackingNumberModal = {this.props.toggleDeactivateTrackingNumberModal}
      editTrackingNumberModal = {this.props.toggleEditTrackingNumberModal}
      removeTrackingNumberModal = {this.props.toggleRemoveTrackingNumberModal}
      setActivateTrackingNumberData ={this.props.setActivateTrackingNumberData}
      setDeactivateTrackingNumberData ={this.props.setDeactivateTrackingNumberData}
      setRemoveTrackingNumberData={this.props.setRemoveTrackingNumberData}
      setEditTrackingNumberData={this.props.setEditTrackingNumberData}
      goToContact={()=>this.goToContact(row)}
    />
      </div>
    )
  }

  onSort = (field, order)=>{
    if(field === 'date') field = 'createdAt';
    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder};
    this.onPageChange( pagination );
  }

  onPageChange = (pagination) => {
    if(!pagination.sort && this.state.pagination.sort) pagination.sort = this.state.pagination.sort
    // this.setState({ pagination })
    this.getData(pagination)
  }

  columns = columnsFunc(this.onSort)

  activateTrackingNumber = () => {
    const data = {
      body: {
        ...this.props.activateTrackingNumberData
      },
      query: {
        account: this.props.currentAccount._id
      }
    }
    this.props.activateTrackingNumberRequest(data)
  }

  deactivateTrackingNumber = () => {
    const data = {
      body: {
        ...this.props.deactivateTrackingNumberData
      },
      query: {
        account: this.props.currentAccount._id
      }
    }
    this.props.deactivateTrackingNumberRequest(data)
  }

  deleteTrackingNumber = () => {
    const data = {
      params: {
        ...this.props.removeTrackingNumberData
      },
      query: {
        account: this.props.currentAccount._id
      },
      pagination: {
        page: this.props.data.page,
        limit: this.props.data.limit,
      }
    }

    if(this.props.data.sort) data.pagination.sort = this.props.data.sort;

    this.props.deleteTrackingNumber(data)
  }

  editTrackingNumber = () => {
    const data = {
      params: {
        ...this.props.editTrackingNumberData
      },
      query: {
        account: this.props.currentAccount._id
      },
      body: {
        ...this.props.editTrackingNumberData
      }
    }
    
    this.props.editTrackingNumberRequest(data)
  }

  setShowAddingCard = (valueBoolean, afterAddingCardData={}) => { 
    this.props.toggleTrackingNumberModal(false)
    this.props.resetTrackingNumberModal()     
    this.setState({ 
      showAddingCard: valueBoolean,
      afterAddingCardData: afterAddingCardData
    });
  }

  onAfterAddCreditCardTrackingNumber = () => {
    this.setState({ 
      showAddingCard: false
    });
    this.props.createTrackingNumbersRequest(this.state.afterAddingCardData);
  } 

  render() {    
    const { data } = this.props
    const { page, pages, totalSize, limit } = this.props.data;
    const customColumn = {
      dataField: "edit", 
      text: "",
      sort: false,
      formatter: this.customActions,
      headerAttr: { width: 80 },
      // attrs: { className: "icon-cell" } 
    }
    const paginationOptions = {
      onPageChange: this.onPageChange,
      page: page + 1,
      paginationSize: pages,
      totalSize: totalSize,
    }

    return (
      <div className={`phones-page${this.state.showAddingCard?" addCard":""}`}>
        {!this.state.showAddingCard ?(
          <React.Fragment>
            <div className='phones-page-title'>
              <h2>
                Tracking Numbers
              </h2> 
              <div className='button-group'>
                <DopeButton color="primary" onClick={this.addNewNumber}>
                  New tracking number
                </DopeButton>
              </div>
            </div>
            <div className='phones-table'>
              <DopeTable
                keyField="_id"
                columns={[...this.columns
                  , customColumn
                ]}
                paginationOptions={paginationOptions}
                data={
                  (data.data && data.data.map((item, i) =>{
                    return {
                      ...item,
                      createdAt: moment(item.createdAt).tz(this.props.timeZone).format('MMM DD, YYYY'),
                    }
                  })) || []
                }
                loading={this.props.isLoading}
              />
              <AddNumberModal
                pagination={{page, limit}}                
                setShowAddingCard={this.setShowAddingCard}
              />
              <DeleteTrackingNumberModal
                open={this.props.showRemoveModal}
                toggleRemoveTrackingNumberModal={this.props.toggleRemoveTrackingNumberModal}
                deleteTrackingNumber={this.deleteTrackingNumber}
                setRemoveTrackingNumberData={this.props.setRemoveTrackingNumberData}
              />
              <EditTrackingNumberModal
                open={this.props.showEditModal}
                toggleEditTrackingNumberModal={this.props.toggleEditTrackingNumberModal}
                editTrackingNumber={this.editTrackingNumber}
                editTrackingNumberData={this.props.editTrackingNumberData}
                setEditTrackingNumberData={this.props.setEditTrackingNumberData}
              />
              <ActivateTrackingNumberModal
                open={this.props.showActivateModal}
                toggleActivateTrackingNumberModal={this.props.toggleActivateTrackingNumberModal}
                activateTrackingNumber={this.activateTrackingNumber}
                activateTrackingNumberData={this.props.activateTrackingNumberData}
                setActivateTrackingNumberData={this.props.setActivateTrackingNumberData}
              />
              <DeactivateTrackingNumberModal
                open={this.props.showDeactivateModal}
                toggleDeactivateTrackingNumberModal={this.props.toggleDeactivateTrackingNumberModal}
                deactivateTrackingNumber={this.deactivateTrackingNumber}
                deactivateTrackingNumberData={this.props.deactivateTrackingNumberData}
                setDeactivateTrackingNumberData={this.props.setDeactivateTrackingNumberData}
              />
            </div>
          </React.Fragment>
        ):(
          <CreditCardsAfter
            onAfterAddCreditCard={this.onAfterAddCreditCardTrackingNumber}
            onBack={()=>this.setShowAddingCard(false)}
          />
        )}
      </div>  
    )
  }
}

const mapStateToProps = (state) => ({
  currentAccount: state.account.currentAccount,
  isLoading: state.trackingNumbers.isLoading,
  data: state.trackingNumbers.data,
  showAddNumberModal: state.newTrackingNumber.showAddNumberModal,
  showEditModal: state.trackingNumbers.showEditModal,
  showActivateModal: state.trackingNumbers.showActivateModal,
  showDeactivateModal: state.trackingNumbers.showDeactivateModal,
  showRemoveModal: state.trackingNumbers.showRemoveModal,
  editTrackingNumberData: state.trackingNumbers.editTrackingNumberData,
  activateTrackingNumberData: state.trackingNumbers.activateTrackingNumberData,
  deactivateTrackingNumberData: state.trackingNumbers.deactivateTrackingNumberData,
  removeTrackingNumberData: state.trackingNumbers.removeTrackingNumberData,
  timeZone:  state.account.timeZone,
})

const mapDispatchToProps = {
  ...trackingNumbersActions,
  ...newTrackingNumberActions,
  ...newTrackingNumberAction,
}

export default connect(mapStateToProps, mapDispatchToProps)(TrackingNumbers)
