export function* getPackageListRequest() {
  try {
    const packageList = yield call(getPackageListApi);
    yield put({
      type: generalActions.GET_PACKAGE_LIST_REQUEST_SUCCESS,
      data: packageList.data,
    });
  } catch (e) {
    toast.error(`Can't download Dope packages list.`);
    yield put({
      type: generalActions.GET_PACKAGE_LIST_REQUEST_FAILED,
      error: e,
    });
  }
}

export function* getCategory(action) {
  try {
    const response = yield call(getCategoryApi)
    yield put({ type: generalActions.GET_CATEGORY_REQUEST_SUCCESS, data: response.data})
  } catch(e) {
    yield put({ type: generalActions.GET_CATEGORY_REQUEST_FAILED, error: e })
    toast.error(`Can't download category.`)
  }
}

export function* getOwnPrice(action) {
  try {
    const response = yield call(getOwnPriceApi, action.data)
    yield put({ type: generalActions.GET_OWN_PRICE_REQUEST_SUCCESS, data: response.data})
  } catch(e) {
    yield put({ type: generalActions.GET_CATEGORY_REQUEST_FAILED, error: e })
    toast.error(`Can't download custom price.`)
  }
}

export default all([
  takeLatest(generalActions.GET_PACKAGE_LIST_REQUEST, getPackageListRequest),
  takeLatest(generalActions.GET_CATEGORY_REQUEST, getCategory),
  takeLatest(generalActions.GET_OWN_PRICE_REQUEST, getOwnPrice),
  // takeLatest(generalActions.SUBMIT_NEW_CARD, submitNewCard),  
]);
