import {
  SET_CURRENT_ACCOUNT,
  SET_ACCOUNT_LIST,
  SET_IS_ADMIN,  
} from '../actions';

import { UPDATE_ACCOUNT_REQUEST_SUCCESS,
  CREATE_ACCOUNT_REQUEST_SUCCESS,
  GET_ACCOUNT_REQUEST_SUCCESS, 
  SET_ACCOUNT, 
  SET_ACCOUNT_LOADING, 
  SET_ACCOUNT_UPDATING,
  SET_HISTORY_LOADING,  
  GET_ACCOUNT_HISTORIES_REQUEST_SUCCESS,
  TOGGLE_SIGN_UP_SUCCESS_MODAL,
  SELECT_PACKAGE,
  SET_NEW_ACCOUNT_DATA,
  RESET_NEW_ACCOUNT_DATA,
  SET_TIME_ZONE,
  SET_DEFAULT_SOURCE,
  REMOVE_CARD,
  GET_USER_OWN_POSTCARD_PRICE_REQUEST_SUCCESS,
  SEND_RECHARGE_REQUEST_SUCCESS,
  RESET_ACCOUNT_USER_OWN_PRICE,
  DELETE_ACCOUNT_REQUEST_SUCCESS,
  CANCEL_DELETE_ACCOUNT_REQUEST_SUCCESS,
  GET_SMS_HISTORY_SUCCESS,
  RESET_SMS_HISTORY,
} from '../actions/account';



let initialState = {

  currentAccount: null,
  updatedAccount: {},
  accountList: null,
  isAdmin: false,
  isLoading: false,
  isUpdating: false,
  isHistoryLoading: false,
  histories: null,
  showSignUpSuccessModal: false,
  timeZone: null,
  userOwnPrice: null,  
}

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case SET_ACCOUNT_LOADING:
      return {
        ...state,
        isLoading: action.loading,
      };
    case SET_ACCOUNT_UPDATING:
      return {
        ...state,
        isUpdating: action.loading,
      };
    case SET_HISTORY_LOADING:
      return {
        ...state,
        isHistoryLoading: action.loading,
      };
      
    case SET_CURRENT_ACCOUNT:      
      return {
        ...state,
        currentAccount: {
          ...action.currentAccount
        },        
      };
    case RESET_ACCOUNT_USER_OWN_PRICE:      
      return {
        ...state,          
        userOwnPrice: null,
      };
    case SET_ACCOUNT_LIST:
      return {
        ...state,
        accountList: action.accountList,
      };
    case SET_IS_ADMIN:      
      return {
        ...state,
        isAdmin: action.isAdmin,
      };
    case GET_ACCOUNT_HISTORIES_REQUEST_SUCCESS:
      return {
        ...state,
        histories: action.data,
      };
    case GET_ACCOUNT_REQUEST_SUCCESS:
      return {
        ...state,
        currentAccount: {
          ...state.currentAccount,
          ...action.data
        }
      };
      case SET_ACCOUNT:
        if (action.group) 
        return {
          ...state,
          currentAccount: {
            ...state.currentAccount,
            [action.group]: {
              ...state.currentAccount[action.group],
              [action.key]: action.value
            }
          }
        }
      else return {
        ...state,
        currentAccount: {
          ...state.currentAccount,
          [action.key]: action.value
        }
      };
      case SET_NEW_ACCOUNT_DATA:
        if (action.group)
        return {
          ...state,
          updatedAccount: {
            ...state.updatedAccount,
            [action.group]: {
              ...state.updatedAccount[action.group],
              [action.key]: action.value
            }
          }
        }
      else return {
        ...state,
        updatedAccount: {
          ...state.updatedAccount,
          [action.key]: action.value
        }
      };
    case RESET_NEW_ACCOUNT_DATA:
      return {
        ...state,
        updatedAccount: {}
      };
    case CREATE_ACCOUNT_REQUEST_SUCCESS:
    case UPDATE_ACCOUNT_REQUEST_SUCCESS:
      return {
          ...state,
          ...action.data
      }
    case DELETE_ACCOUNT_REQUEST_SUCCESS:           
      return {
        ...state,
        currentAccount: {
          ...state.currentAccount,
          requestToDelete: true,                    
        }        
      }
    case CANCEL_DELETE_ACCOUNT_REQUEST_SUCCESS:
      return {
        ...state,
        currentAccount: {
          ...state.currentAccount,
          requestToDelete: false,                    
        }        
      }
    case TOGGLE_SIGN_UP_SUCCESS_MODAL:
      return {
        ...state,
        showSignUpSuccessModal: !state.showSignUpSuccessModal
      }
    case SELECT_PACKAGE:
      return {
        ...state,
        packageId: action.data
      }
    case SET_TIME_ZONE:
      return {
        ...state,
        timeZone: action.data
      }
    case SET_DEFAULT_SOURCE:      
      return{
        ...state,
        currentAccount: {
          ...state.currentAccount,
          default_source: action.value
        }
      }
    case REMOVE_CARD:      
      return{
        ...state,
        currentAccount: {
          ...state.currentAccount,          
          cards: state.currentAccount.cards.filter(card => card.id !== action.value)
        }
      }  
    // DONT USED
    case GET_USER_OWN_POSTCARD_PRICE_REQUEST_SUCCESS:   
      return {
        ...state,
        userOwnPrice: action.data,        
      };
    // TODO  
    case SEND_RECHARGE_REQUEST_SUCCESS:      
      return {
        ...state,
        currentAccount: {
          ...state.currentAccount,                    
        }
      };
    case GET_SMS_HISTORY_SUCCESS:      
      return {
        ...state,
        currentAccount: {
          ...state.currentAccount,  
          smsHistory: action.data                  
        }
      };
    case RESET_SMS_HISTORY:      
      return {
        ...state,
        currentAccount: {
          ...state.currentAccount,  
          smsHistory: {},
        }
      };   

      
      
      
    default:
      return state;
  }
}
