import React, { Fragment } from 'react';
import LogRocket from 'logrocket';
import { Route, Redirect, Switch } from 'react-router-dom';
import {isMobile} from 'react-device-detect';
import tzlookup from "tz-lookup";
import ToastifyContainer from 'components/ToastifyContainer';
import AppHeader from 'Layout/AppHeader';
import AppFooter from 'Layout/AppFooter';
import AppSidebar from '../../Layout/AppSidebar';
import request from '../../utils/request';
import {defaultTimeZone} from 'utils/constants';
import Loader from 'components/Loader/Loader';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { ReactComponent as AttentionIcon } from 'assets/img/icons/attention.svg';
import LoadingGif from 'components/LoadingGif';
import { getAccessGroup } from 'utils/helper';
import { connect } from 'react-redux';
import {
  setCurrentAccount,
  setAccountList,
  getDbConfigs,
  setIsAdmin
} from '../../actions';
import * as accountActions from 'actions/account';
import * as generalActions from 'actions/general';
import dashRoutes from '../../routes/dashboard.jsx';
import NotFound from 'views/NotFound';

import {  
  NOTFOUND,
  // P404,
} from 'utils/constants';

import { API_URL } from '../../config/config';

import './Dashboard.scss';


class Dashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      backgroundColor: 'black',
      activeColor: 'info',
      pages: [],
      sideBarElms: [],
      loading: true,
      currentAccount: {}, 
      isMobile: isMobile,      
    };
  }

  componentDidMount() {      
    this.props.setDomain({
      domain: window.location.host,
      accessGroup: getAccessGroup(window.location.host)
    });
    this.props.setWindowWidth(window.innerWidth);
    this.props.getDbConfigs();
    this.props.getPackageListRequest();
    this.props.getCategoryRequest();    
    this.getSidBarContent(); 
    window.addEventListener("resize", this.handleResize);    
  }
  handleResize = (e) => {      
    this.props.setWindowWidth(window.innerWidth);
  };
  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }

  // FOR ADMIN ACCOUNTS LIST
  setAccounts = async (userId) => {    
    try {
      // NEW CORRECT FILL current_Account for admin
      let currentAccountRes = await request(`${API_URL}/accounts/${userId}`,{
        method: 'get'
      });  
      let currentAccount = currentAccountRes.data;
      localStorage.setItem('current_account', JSON.stringify(currentAccount));
      await this.props.setCurrentAccount(currentAccount);
      LogRocket.identify(currentAccount._id, {
        name: currentAccount.name||'',
        email: currentAccount.email||'',
        role: currentAccount.role||'',
        logo: currentAccount.logo||''
      });
      let userAccountsRes = await request(`${API_URL}/accounts`, {
        method: 'get'
      });     
      let accountList = userAccountsRes.data.data;      
      await this.props.setAccountList(accountList);
      return true;
    } catch (err) {      
      LogRocket.captureException(err);      
      console.log(err);
    }
  };

  setAccount = async () => {
    try {      
      let userAccountRes = await request(`${API_URL}/accounts/me`, {
        method: 'get'
      }); 
      // TODO GET CORRECT BALLANCE
      let currentAccount = userAccountRes.data;
      localStorage.setItem('current_account', JSON.stringify(currentAccount));      
      await this.props.setCurrentAccount(currentAccount);
      LogRocket.identify(currentAccount._id, {
        name: currentAccount.name||'',
        email: currentAccount.email||'',
        role: currentAccount.role||'',
        logo: currentAccount.logo||''
      });
      return true;
    } catch (err) {
      LogRocket.captureException(err);      
      console.log(err);
    }
  };  

  getDashboardRoutes() {
    const disabledPages = [];
    if (this.props.configs) {
      this.props.configs.forEach((config) => {
        if (config.name && config.name.includes('page') && !config.value) {
          disabledPages.push(config.name.replace('page', ''));
        }
      });
    }    
    return dashRoutes.filter((route) => {      
      return !disabledPages.includes(route.name) && route.group.includes(this.props.accessGroup) ;      
    });
  }

  getPages() {    
    let pages = [];
    pages = this.getDashboardRoutes().slice();   
    if(this.props.isAdmin) {
      let adminSection = pages.find((item)=>(item.section==="Admin"));      
      for (let item of adminSection.items)
        pages.splice(pages.length - 2, 0, item);
    }      
    pages = pages.filter((item)=>(!item.section));    
    return pages;
  }

  async getSideBarElms() {    
    let sideBarElms = [];     
    let statement;    
    const authdata = JSON.parse(localStorage.getItem('authdata'));    
    if (authdata.role === 'user') {
      statement = await this.setAccount();
    } 
    if (authdata.role === 'admin') {
      const currentAccount = localStorage.getItem('current_account');
      const userId = currentAccount ? JSON.parse(currentAccount)._id : authdata.userId;
      this.props.setIsAdmin(true)
      statement = await this.setAccounts(userId);
    }        
    sideBarElms = this.getDashboardRoutes().filter((elm) => {      
      let state = !elm.hideInSidebar && elm.name !== 'Login';
      if (!this.props.isAdmin && elm.section === 'Admin') {
        state = false;
      }
      return state;
    });        
    if (!statement || !this.props.currentAccount) {
      console.log('current account not set');
    }    
    return sideBarElms;
  }

  

  getSidBarContent = () => {    
    this.getSideBarElms().then((sideBarElms) => {
      const stateObj = {};
      if (this.props.currentAccount) {
        stateObj.currentAccount = this.props.currentAccount;
      }
      this.setState({
        ...stateObj,
        pages: this.getPages(),
        sideBarElms,
        loading: false
      });                      
    });    
  };

  componentDidUpdate(prevProps) {     
    const {currentAccount, ownPriceChecked} = this.props;    
    if ((!this.props.timeZone && this.props.currentAccount) ||
     (this.props.currentAccount && prevProps.currentAccount && 
      this.props.currentAccount._id !== prevProps.currentAccount._id) ) {        
     this.getTimeZone()
    }    
    if(currentAccount && currentAccount._id && currentAccount.ownPrice && !ownPriceChecked) { 
      this.props.getOwnPriceRequest(currentAccount.ballance.id);
    }

    if (this.props.isAdmin) {      
      if(prevProps.currentAccount&&prevProps.currentAccount._id!==this.props.currentAccount._id){ 
        this.props.getOwnPriceRequest(currentAccount.ballance.id);
      }
    }      
  }

  getBrowserGeolocation = () => {
    if ("geolocation" in navigator) {
      // If browser support geolocation
      this.props.setTimeZone(defaultTimeZone);
      navigator.geolocation.getCurrentPosition((position) => {
        // If user allow geolocation
        const timeZone = tzlookup(position.coords.latitude, position.coords.longitude) || defaultTimeZone
      this.props.setTimeZone(timeZone)
      },(err) => {
        // If user block geolocation
        this.props.setTimeZone(defaultTimeZone)
      })
    } else {
       // If browser doesn't support geolocation
      this.props.setTimeZone(defaultTimeZone)
    }
  }

  coordinatesValidation = (lat, lng) => {
    if((lat > -90 || lat < 90)  && (lng > -180 || lng < 180))  return true
  }

  getTimeZone = () => {
    if(!this.props.currentAccount.companyAddress) {
      this.props.setTimeZone(defaultTimeZone)
      return
    }
    const {lat , lng} = this.props.currentAccount.companyAddress;
    if(!lat || !lng || !this.coordinatesValidation(lat, lng)) {
      // Get time zone by geolocation
      this.getBrowserGeolocation();
    } else {
      // Get time zone by company geolocation coordinates
      const timeZone = tzlookup(lat, lng)
      this.props.setTimeZone(timeZone)
    }
  }

  toggleMobileModal = () => {       
    this.setState((prevState) => ({
      isMobile: !prevState.isMobile,
    }));     
  };

  render() {        
    return (
      <Fragment>
        <Loader show={this.props.isLoading} text={this.props.loadingText}/>
        {
          !this.state.loading ? (
            <React.Fragment>            
              <AppHeader {...this.props} />
              <div className="app-main">
                <ToastifyContainer/>
                <AppSidebar />
                <div className="app-main__outer">
                  <div className="app-main__inner">
                    <Switch>
                      {this.state.pages.map((prop, key) => {                         
                        if (prop.section) return;
                        if (prop.pro) {
                          return null;
                        }
                        return (
                          <Route
                            path={prop.to}
                            key={key}
                            exact = {prop.exact}
                            render={(props) => {
                              if (prop.redirect) {
                                return (
                                  <Redirect
                                    from={prop.path}
                                    to={prop.pathTo}
                                    key={key}
                                  />
                                );
                              }
                              const Component = prop.component;
                              return <Component {...this.props} {...props} />;
                            }}
                          />
                        );
                      })}                      
                      <Route
                        path={NOTFOUND}
                        render={(props) => {
                          return <NotFound />;
                        }}
                      />                      
                    </Switch>
                  </div> 
                  {
                    isMobile&&(
                      <DopeModal
                        className='isMobileModal'
                        isOpen={this.state.isMobile}
                        toggle={this.toggleMobileModal}
                        centered                
                      >                        
                        <DopeModalBody>
                          <div className="icon-container">
                            <AttentionIcon className="icon"/>
                          </div>
                          <h4 className="modal-title">Info</h4>
                          <p className="modal-text">Please use a desktop version for the best user experience</p>
                        </DopeModalBody>   
                        <DopeModalFooter>                          
                          <DopeButton color='primary' onClick={this.toggleMobileModal}>
                            Ok
                          </DopeButton>
                        </DopeModalFooter>     
                      </DopeModal>
                    )
                  }               
                  <AppFooter domain={this.props.domain}/>
                </div>
              </div>
            </React.Fragment>  
          ):( 
            <div className="startloader">                
                {/* <img src={LoadingGif} alt='loading' /> */}
                <LoadingGif/>
            </div>
          )
        }
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => ({
  currentAccount: state.account.currentAccount,  
  accountList: state.account.accountList,
  configs: state.dbConfigs.configs,
  isAdmin: state.account.isAdmin,
  timeZone: state.account.timeZone,
  isLoading: state.general.isLoading,
  loadingText: state.general.loadingText,
  ownPriceChecked: state.general.ownPriceChecked,
  adminAccounts: state.adminAccounts,
  accessGroup: state.general.accessGroup,
  domain: state.general.domain,
});

const mapDispatchToProps = {
  ...accountActions,
  ...generalActions,
  setCurrentAccount,
  setAccountList,
  getDbConfigs,
  setIsAdmin
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Dashboard);
