export const friendlyFormatPhoneNumber = phone => {
  if (!phone) return '';
  phone = phone.replace(/[^0-9]/g, '');
  return `(${phone.slice(-10, -7)})${phone.slice(-7, -4)}-${phone.slice(-4)}`;
};

export const fullNumberFormat = phone => {
  if (!phone) return '';
  phone = phone.replace(/[^0-9]/g, '');
  return `+1${phone.slice(-10)}`;
};
