import React from 'react';
import ReactJson from 'react-json-view';
// import phoneLookupData from '../phoneLookupData.json';
import request from 'utils/request';
import { connect } from 'react-redux';
import { FadeLoader } from 'react-spinners';
import { API_URL } from 'config/config';

class PhoneLookupData extends React.Component {
  state = {
    phoneLookupData: '',
    lookupDataRender: '',
    loading: true
  };

  componentDidMount() {
    this.getData();
  }

  componentDidUpdate(prevProps) {
    if (this.props.currentAccount !== prevProps.currentAccount) this.getData();
  }

  getData = async () => {
    this.setState({ loading: true });
    try {
      let link = `${API_URL}/phone-lookups`;
      let response = await request(link, {
        method: 'get',
        queries: { account: this.props.currentAccount._id }
      });
      let responseJson = response.data;

      this.setState(
        {
          phoneLookupData: responseJson
        },
        () => {
          let lookupDataRender = this.state.phoneLookupData.map(
            (lookup, key) => {
              let name;
              if (lookup.result) {
                if (
                  lookup.dataSource === 'WhitePages' &&
                  lookup.result.belongs_to[0]
                ) {
                  name = lookup.result.belongs_to[0].name;
                } else if (
                  lookup.dataSource === 'Webula' &&
                  lookup.result.first_name
                ) {
                  name =
                    lookup.result.first_name + ' ' + lookup.result.last_name;
                } else if (
                  lookup.dataSource === 'DataFinder' &&
                  lookup.result.FirstName
                ) {
                  name = lookup.result.FirstName + ' ' + lookup.result.LastName;
                }
              } else name = 'Unknown Caller';
              // console.log('name', name);
              return (
                <div key={key}>
                  <pre>
                    Caller Name:{' '}
                    <span style={{ fontSize: '120%' }}>
                      <b>{name}</b>
                    </span>
                  </pre>
                  <ReactJson src={lookup} />
                  <hr style={{ borderTop: '2px solid #000' }}></hr>
                </div>
              );
            }
          );
          this.setState({ lookupDataRender });
        }
      );
    } catch (err) {
      console.log(err);
    }
    this.setState({ loading: false });
  };

  render() {
    if (this.state.loading)
      return (
        <div className="content">
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <FadeLoader size={100} color={'#403d39'} />
          </div>
        </div>
      );
    return (
      <div style={{ marginTop: '100px', marginLeft: '40px', width: '90%' }}>
        {this.state.lookupDataRender}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  currentAccount: state.account.currentAccount
});

export default connect(mapStateToProps)(PhoneLookupData);
