import React, {useEffect, useState} from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import {DopeModalBody} from "components/DopeModal";
import {saveHandwrittenRequest, updateHandwrittenRequest} from "actions/handwritten";
import {useDispatch, useSelector} from "react-redux";
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { FormGroup } from 'reactstrap';

export default function HandwrittenSavingModal({isOpen, toggle, handwrittenId, typePreview, handwrittenOrder, state}) {

  const dispatch = useDispatch();
  const currentAccount = useSelector(state => state.account.currentAccount);
  const [handwrittenName, setHandwrittenName] = useState('');
  const [handwrittenNameError, setHandwrittenNameError] = useState('');

  useEffect(() => {
    if(handwrittenOrder) setHandwrittenName(handwrittenOrder.name);
    return () => {
      setHandwrittenNameError('');
    };
  }, [isOpen]);

  const saveHandwritten = () => {
    if(handwrittenName === ''){
      setHandwrittenNameError("Handwritten's name can't be empty");
      return;
    }
    const data = {
      body: {
        ...state,
        name: handwrittenName,
        status: 'draft'
      },
      accountId: currentAccount._id,
      handwrittenId
    };
    if(typePreview === 'edit') dispatch(updateHandwrittenRequest(data));
    else dispatch(saveHandwrittenRequest(data));
  };


  return (    
    <DopeModal className='HandwrittenModal' isOpen={isOpen} toggle={toggle} centered>
      <DopeModalHeader className='dope-modal-header' toggle={toggle}>
        {typePreview === 'edit' ? 'Edit' : 'Save'}
      </DopeModalHeader>
      <DopeModalBody>
        <FormGroup className="dope-form-group">
          <DopeInput
            value={handwrittenName}
            title='Handwritten name'
            error={handwrittenNameError}
            onChange={e => {
              setHandwrittenNameError('');
              setHandwrittenName(e.target.value);
            }}
          />
        </FormGroup>
      </DopeModalBody>
      <DopeModalFooter>
        <DopeButton color='primary' onClick={saveHandwritten}>
          {typePreview === 'edit' ? 'Edit' : 'Save'}
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>    
  );
}
