import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {DopeTable} from "components/DopeTable";
import {FadeLoader} from "react-spinners";
import Select from 'react-select';
import {columnsFunc} from "./config";
import moment from "moment";
import {
	getAdminHandwrittenListRequest,
	updateAdminHandwrittenRequest,
	deleteAdminHandwrittenOrderRequest
} from 'actions/adminHandwritten';
import './AdminManageHandwrittenList.scss';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import DeleteAdminHandwrittenItemModal from "./DeleteAdminHandwrittenItemModal";
import {DopeTextarea} from "components/DopeForms";


export default function AdminManageHandwrittenList() {
	const dispatch = useDispatch();
	
	useEffect(() => {
		dispatch(getAdminHandwrittenListRequest({ query:{page:0, limit: 10} }));
	}, []);

	const loading = useSelector(state => state.adminHandwritten.loading);
	const handwrittenList = useSelector(state => state.adminHandwritten.handwrittenList);
	const [deletePopupOpen, setDeletePopupOpen] = useState(false);
	const [rowId, setRowId] = useState({});

	const onSort = (field, order)=>{
		if (field==="account") field= "name";
    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder };
    updateData(pagination);
	};
	
	const columns = columnsFunc(onSort);

	const delColumn = {
		dataField: "remove",
		text: "",
		sort: false,
		formatter: () => <DeleteIcon className="del-icon"/>,
		attrs: { className: "icon-cell" }
	};

	const rowClasses = () => {
    return 'table-row';
	};

	const updateData = (pagination = { page: handwrittenList.query.page, limit: 10, sort: handwrittenList.query.sort }) => {
		if(!pagination.sort && handwrittenList.query.sort) pagination.sort = handwrittenList.query.sort;
		let data = {
			query: {
				...pagination,
				page: pagination.page - 1,
			}
		};
		dispatch(getAdminHandwrittenListRequest(data))
	};

	const paginationOptions = {
		page: handwrittenList.query ? handwrittenList.query.page + 1 : 1,
		limit: handwrittenList.query ? handwrittenList.query.limit : 10,
		onPageChange: updateData,
		totalSize: handwrittenList.total
	};

	const statusFormatter = (cell, row) => {
		const statusArr = [
			{ value: 'paid', label: 'Paid' },
			{ value: 'sent', label: 'Sent' },
			{ value: 'draft', label: 'Draft' },
		];
		return (
			<span onClick={e=>e.stopPropagation()}>
					<Select
						className="dope-select"
						classNamePrefix="dope"
						value={statusArr.filter(option => option.value === cell)}
						options={statusArr}
						onChange={e => changeHandwrittenStatus(e, row._id)}
						menuPortalTarget={document.body}
						styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }}
						isSearchable={false}
					/>
				</span>
		);
	};

	const contentFormatter = (cell) => {
		return (
			<DopeTextarea
				value={cell}
				name='content'
				onChange={e => e.preventDefault()}
			/>
		)
	};
	
	const statusColumn = {
    dataField: 'status',
    text: 'Status',
		sort: true,
		sortFunc: function(){
      return false
    },
    onSort: onSort,
		formatter: statusFormatter,
    attrs: { className: 'status-cell-td' },
	};

	const contentColumn = {
    dataField: 'content',
    text: 'Content',
		formatter: contentFormatter,
    attrs: { className: 'status-cell-td' },
	};

	const accountColumn = {
		dataField: "account",
		text: "Account",
		sort: false,
		formatter: (cell, row) =>{
			return (
				<>
					<div className="acc-cell-box">
						<span>{row.account.name}</span>
						<span>{row.account.email}</span>
					</div>
				</>
			)
		},
		attrs: { className: "acc-cell" }
	};

	const changeHandwrittenStatus = (e, id)=>{
		let data = {
			body: {
				status: e.value,
			},
			handwrittenId: id,
		};
		dispatch(updateAdminHandwrittenRequest(data));
	};

	const deleteHandwrittenOrder =()=>{
		dispatch(deleteAdminHandwrittenOrderRequest(rowId));
	};

	return (
		<div className='AdminManageHandwrittenList'>
			<div className='page-title-block'>
        <h2 className='page-title'>Manage Handwritten</h2>
      </div>
			<div className="handwrittenTable">
				<div className="handwrittenList-header">
					<div>
						{handwrittenList.total > 0
							? <span>{handwrittenList.total} Handwritten orders</span>
							: <span>No Handwritten Found</span>
						}
					</div>
				</div>
				{ (handwrittenList.total > 0) ? (
					<DopeTable
						keyField="_id"
						columns={[accountColumn, ...columns, contentColumn, statusColumn, delColumn]}
						data={handwrittenList.data.map((item) => (
							{
								_id: item._id,
								price: `$ ${item.price ? (item.price/100).toFixed(2) : 0}`,
								status: item.status,
								account: item.account,
								content: item.content,
								name: item.name,
								size: item.size,
								contacts: item.list.listProperties.verifiedContacts,
								list: item.list.name,
								font: item.font ? item.font.name : '',
								createdAt: moment(item.createdAt).format('MMM DD, YYYY'),
							}
						))}
						rowClasses={rowClasses}
						loading={loading}
						rowEvents={{
							onClick: (e, row) => {
								if(e.target.tagName === 'svg' || e.target.tagName === 'path') {
									setRowId(row._id);
									setDeletePopupOpen(true);
								}
							}
						}}
						paginationOptions={paginationOptions}
					/>
				) : (
					<div className="nohandwrittenData">
						{loading && (
							<div className='loader-container'>
								<FadeLoader size={100} color={'#403d39'} />
							</div>
						)}
						<span>No Handwritten Found</span>
					</div>
				)}
			</div>
			<DeleteAdminHandwrittenItemModal
				isOpen={deletePopupOpen}
				toggle={() => setDeletePopupOpen(!deletePopupOpen)}
				deleteHandwrittenOrder={deleteHandwrittenOrder}
			/>
		</div>
	);
}