import React from 'react';
import { ReactComponent as Logo } from 'assets/img/icons/image.svg';
import { Label } from 'reactstrap';
import './CompanyLogo.scss';
import moment from 'moment';
import 'moment-timezone';

export default function CompanyLogo(props) {
  const {name} = props;
  return (
    <div className='company-logo-container'>
      <h4 className="company-name">{name}</h4>
      <span className="created-label">Created on Dope 360: {moment(props.createdAt).tz(props.timeZone).format('DD MMM YYYY')}</span>
      
      {props.img ? (
        <div className='logo-container'>
          <img
            src={props.img}
            alt='Company logo'
          />
        </div>
      ) : (
        <div className='default-logo-container'>
          <Logo />
        </div>
      )}
      <Label className={props.edit? 'upload-logo' :  'upload-logo editable'}>
        Update company logo
        <input
          name='logo'
          type='file'
          className='inputfile'
          accept="image/jpeg,image/png,image/jpg"
          onChange={e => {
            props.onChange(e);
          }}
        />
      </Label>
    </div>
  );
}
