import React from 'react';
import SendingCell from '../SendingCell/SendingCell';

import { DopeButton } from 'components/DopeForms';

import { ReactComponent as PlusIcon } from 'assets/img/icons/plus.svg';
import { ReactComponent as ViewIcon } from 'assets/img/icons/view.svg';
import { ReactComponent as CopyIcon } from 'assets/img/icons/copy.svg';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import { ReactComponent as EditIcon } from 'assets/img/icons/edit.svg';

import './AddSending.scss';

export default function AddSending({
  day,
  event,
  openPostcardTemplatesModal,
  toggleDeleteSending,
  toggleCloneSending,
  editSending,
  viewSending,
  getStartEndDate,
}) {
  return (
    <React.Fragment>
      {event && <SendingCell event={event} />}
      <div className={`bg ${event ? 'sending-event' : ''}`}>
        <div
          className={`sending-block ${event
            ? event.status + '-block'
            : 'no-sending-block'}`}
        />
        <div className='sending-btn'>
          {!event ? (
            <DopeButton
              color='white'
              onClick={() => openPostcardTemplatesModal(day)}
            >
              <PlusIcon />
            </DopeButton>
          ) : event.status === 'unsent' || event.status === 'sent' || event.status === 'payment-failed'  ? (
            <React.Fragment>
              <DopeButton color='white' onClick={() => viewSending(event)}>
                <ViewIcon />
              </DopeButton>
              <DopeButton color='white' onClick={() => toggleCloneSending(event)}>
                <CopyIcon />
              </DopeButton>
            </React.Fragment>
          ) : event.status === 'draft' || event.status === 'confirmed' ? (
            <React.Fragment>
              <DopeButton color='white' onClick={() => editSending(event)}>
                <EditIcon />
              </DopeButton>
              <DopeButton color='white' onClick={() => toggleCloneSending(event)}>
                <CopyIcon />
              </DopeButton>
              <DopeButton
                color='white'
                onClick={() => toggleDeleteSending(event._id, getStartEndDate())}
              >
                <DeleteIcon />
              </DopeButton>
            </React.Fragment>
          ) : (
            ''
          )}
        </div>
      </div>
    </React.Fragment>
  );
}
