import {
    CREATE_INCOMING_CALLS_REQUEST,
    CREATE_INCOMING_CALLS_REQUEST_SUCCESS,
    CREATE_INCOMING_CALLS_REQUEST_FAILED,
    CREATE_DASHBOARD_STATISTIC_REQUEST_SUCCESS,
    CREATE_ALL_INCOMING_CALLS_REQUEST_SUCCESS,
    SET_DASHBOARD_PROPERTY,
} from '../actions/dashboard';

let initialState = {
    incomingCalls: {
        page: 1,
        pages: null,
        totalSize: null,
        calls: [],
        isLoading: false,
        isPaginate: false,
    },
    statistic: {
        isLoading: false,
        postcards: {
            sent: 0,
            inboundCalls: 0,
        },
        yardSigns: {
            sent: 0,
            inboundCalls: 0,
        },
    },
    allIncomingCalls: {
        isLoading: false,
        calls: null
    }
};

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case SET_DASHBOARD_PROPERTY: {
            if (action.group) {
                return {
                    ...state,
                    [action.group]: {
                        ...state[action.group],
                        [action.key]: action.value
                    }
                }
            }
            else {
                return {
                    ...state,
                    [action.key]: action.value
                }
            }
        }

        case CREATE_INCOMING_CALLS_REQUEST:
            return {
                ...state,
                incomingCalls: {
                    ...state.incomingCalls,
                },
            };
        case CREATE_INCOMING_CALLS_REQUEST_SUCCESS:
            return {
                ...state,
                incomingCalls: {
                    ...action.data,
                },
            }; 
        case CREATE_ALL_INCOMING_CALLS_REQUEST_SUCCESS:
                return {
                    ...state,
                    allIncomingCalls: {
                        ...action.data,
                        isLoading: false,
                    },
                }; 
        case CREATE_INCOMING_CALLS_REQUEST_FAILED:
            return {
                ...state,
                incomingCalls: {
                    ...state.incomingCalls,
                    isLoading: false,
                },
            };
        case CREATE_DASHBOARD_STATISTIC_REQUEST_SUCCESS:
            return {
                ...state,
                statistic: {
                    ...action.data,
                }
            };
        default:
            return state;
    }
}
