import {
  GET_ADMIN_ACCOUNTS_REQUEST,
  GET_ADMIN_ACCOUNTS_REQUEST_SUCCESS,
  GET_ADMIN_ACCOUNTS_REQUEST_FAILED,  
  GET_ADMIN_ACCOUNT_REQUEST_SUCCESS,  
  RESET_ADMIN_ACCOUNTS_PAGE,  
  RESET_SELECTED_ACCOUNT,
  SET_VIEW_ADMIN_ACCOUNTS_PAGE,
  ON_CHANGE_ADMIN_ACCOUNTS_SEARCH_INPUT,
  GET_ADMIN_OWN_POSTCARD_PRICE_REQUEST_SUCCESS,
  SET_OWN_POSTCARD_PRICE_REQUEST_SUCCESS,
  RESET_OWN_POSTCARD_PRICE_REQUEST_SUCCESS,
  RESET_OWN_PRICE_DATA,  
  SET_ADMIN_ACCOUNT_LOADING,
  CHANGE_ACCOUNT_DATA_SUCCESS,
} from 'actions/adminAccounts';

let initialState = {
  view: 'accounts',
  accountsDataIsLoaded: false,
  accountDataIsLoaded: false,
  accountsData: {},
  selectedAccount: {},
  searchValue: '',
  ownPriceData: null,
  updatedFlag: false,
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case GET_ADMIN_ACCOUNTS_REQUEST:
      return {
        ...state,
        accountsDataIsLoaded: true,
      };
    case GET_ADMIN_ACCOUNTS_REQUEST_SUCCESS:     
      return {
        ...state,
        accountsData: action.data,
        accountsDataIsLoaded: false,
      };
    case GET_ADMIN_ACCOUNTS_REQUEST_FAILED:
      return {
        ...state,
        accountsDataIsLoaded: false,
      };
    case RESET_ADMIN_ACCOUNTS_PAGE:
      return {
        ...state,
        ...initialState,
      };    
    case GET_ADMIN_ACCOUNT_REQUEST_SUCCESS:
      return{
        ...state,        
        selectedAccount: action.data,
      };    
    case RESET_SELECTED_ACCOUNT:
      return {
        ...state,
        selectedAccount: {},
      };
    case SET_VIEW_ADMIN_ACCOUNTS_PAGE:
      return {
        ...state,
        view: action.data,
      };
    case ON_CHANGE_ADMIN_ACCOUNTS_SEARCH_INPUT:
      return {
        ...state,
        searchValue: action.data,
      };
    case GET_ADMIN_OWN_POSTCARD_PRICE_REQUEST_SUCCESS:
      return {
        ...state,
        ownPriceData: action.data,
      };
    case SET_OWN_POSTCARD_PRICE_REQUEST_SUCCESS:
      return {
        ...state,
        ownPriceData: {
          ...state.ownPriceData,
          postcardPrice: action.data.postcardPrice,
          yardSignPrice: action.data.yardSignPrice,
          neighborhoodPrice: action.data.neighborhoodPrice,
          eddmPrice: action.data.eddmPrice,
        },
        selectedAccount: {
          ...state.selectedAccount,
          printPartner: action.data.printPartner,
          ownPrice: action.ownPrice,
        },
      };
    case RESET_OWN_POSTCARD_PRICE_REQUEST_SUCCESS:      
      return {
        ...state,
        ownPriceData: {
          ...state.ownPriceData,
          postcardPrice: action.data.postcardPrice,
          yardSignPrice: action.data.yardSignPrice,
          neighborhoodPrice: action.data.neighborhoodPrice,
          eddmPrice: action.data.eddmPrice,
        },
        selectedAccount: {
          ...state.selectedAccount,
          ownPrice: action.ownPrice,
        },
      };
    case RESET_OWN_PRICE_DATA:
      return {
        ...state,
        ownPriceData: null,
      };  
    case SET_ADMIN_ACCOUNT_LOADING:      
      return {
        ...state,
        accountDataIsLoaded: action.loading,
      }; 
    case CHANGE_ACCOUNT_DATA_SUCCESS:
      return {
        ...state,
        selectedAccount: {
          ...state.selectedAccount,
          ...action.data,
        }
      };       
    default:
      return state;
  }
}
