import {
  GET_TRACKING_NUMBERS,
  GET_TRACKING_NUMBERS_SUCCESS,
  GET_TRACKING_NUMBERS_FAILED,
  RESET_TRACKING_NUMBERS_PAGE,
  TOGGLE_ACTIVATE_TRACKING_NUMBER_MODAL,
  TOGGLE_DEACTIVATE_TRACKING_NUMBER_MODAL,
  TOGGLE_EDIT_TRACKING_NUMBER_MODAL,
  TOGGLE_REMOVE_TRACKING_NUMBER_MODAL,
  SET_ACTIVATE_TRACKING_NUMBER_DATA,
  SET_DEACTIVATE_TRACKING_NUMBER_DATA,
  SET_REMOVE_TRACKING_NUMBER_DATA,
  SET_EDIT_TRACKING_NUMBER_DATA,
  ACTIVATE_TRACKING_NUMBER_REQUEST_SUCCESS,
  DEACTIVATE_TRACKING_NUMBER_REQUEST_SUCCESS,
  EDIT_TRACKING_NUMBER_REQUEST_SUCCESS,
} from '../actions/trackingNumbers';

import { fullNumberFormat } from 'utils/phones';

let initialState = {
  showEditModal: false,
  showActivateModal: false,
  showDeactivateModal: false,
  showRemoveModal: false,
  isLoading: false,
  activateTrackingNumberData: {
    name: '',
    trackingNumber: '',
  },
  deactivateTrackingNumberData: {
    trackingNumber: '',
  },
  editTrackingNumberData: {
    name: '',
    forwardingNumber: '',
    trackingNumberId: '',
    trackingNumber: '',
  },
  removeTrackingNumberData: {
    trackingNumberId: '',
    trackingNumber: '',
  },
  data: [],

}

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case GET_TRACKING_NUMBERS:
      return {
        ...state,
        isLoading: true
      }
    case GET_TRACKING_NUMBERS_SUCCESS:
      return {
        ...state,
        data: action.data,
        isLoading: false
      }
    case GET_TRACKING_NUMBERS_FAILED:
      return {
        ...state,
        isLoading: false
      }
    case RESET_TRACKING_NUMBERS_PAGE:
      return {
        ...state,
        ...initialState,
      }
    case TOGGLE_ACTIVATE_TRACKING_NUMBER_MODAL:
      return {
        ...state,
        showActivateModal: action.data
      }
    case TOGGLE_DEACTIVATE_TRACKING_NUMBER_MODAL:
      return {
        ...state,
        showDeactivateModal: action.data
      }
    case TOGGLE_EDIT_TRACKING_NUMBER_MODAL:
      return {
        ...state,
        showEditModal: action.data
      }
    case TOGGLE_REMOVE_TRACKING_NUMBER_MODAL:
      return {
        ...state,
        showRemoveModal: action.data
      }
    case SET_ACTIVATE_TRACKING_NUMBER_DATA:
      return {
        ...state,
        activateTrackingNumberData: action.data
      }
    case SET_DEACTIVATE_TRACKING_NUMBER_DATA:
      return {
        ...state,
        deactivateTrackingNumberData: action.data
      }
    case SET_EDIT_TRACKING_NUMBER_DATA:
      return {
        ...state,
        editTrackingNumberData: action.data
      }
    case SET_REMOVE_TRACKING_NUMBER_DATA:
      return {
        ...state,
        removeTrackingNumberData: action.data
      }
    case ACTIVATE_TRACKING_NUMBER_REQUEST_SUCCESS: {
      let data = [...state.data.data];
      data.find((item, i)=>{
        const trackingNumber = fullNumberFormat(item.trackingNumber);
        if(trackingNumber === action.data){
          data[i].orderStatus = 'private-order'
        }})
      return {
        ...state,
        data: {
          ...state.data,
          data: [...data]
        }
      }
    }
    case DEACTIVATE_TRACKING_NUMBER_REQUEST_SUCCESS: {
      let data = [...state.data.data];
      data.find((item, i)=>{
        const trackingNumber = fullNumberFormat(item.trackingNumber);
        if(trackingNumber === action.data){
          data[i].orderStatus = 'reserved'
        }})
      return {
        ...state,
        data: {
          ...state.data,
          data: [...data]
        }
      }
    }
    case EDIT_TRACKING_NUMBER_REQUEST_SUCCESS: {
      let data = [...state.data.data];
      data.map((item, i)=>{
        if(item._id === action.data){
          data[i] = {
            ...data[i], 
            name: state.editTrackingNumberData.name, 
            forwardingNumber: state.editTrackingNumberData.forwardingNumber}
        }
        return item;
      })
      return {
        ...state,
        data: {
          ...state.data,
          data: [...data]
        }
      }
    }
    default:
      return state
    }
}
