import service from './service'
import { API_URL } from '../config/config'

export const getListsApi = (data) => {
  return service.get(
    `${API_URL}/list-request`,
    data
  )
}

export const createListApi = (data) => {
  return service.post(
    `${API_URL}/list-request?addToCrmAutomatically=${data.addToCrmAutomatically}&account=${data.account}`,
    data
  )
}

export const uploadListApi = (data) => {
  return service.post(
    `${API_URL}/list-request?addToCrmAutomatically=${data.addToCrmAutomatically}&account=${data.account}`,
    data
  )
}

export const downloadListApi = (data) => {
  console.log("downloadListApi :",data);
  return service.get(
    `${API_URL}/get-list-csv?account=${data.account}&listId=${data.listId}`
  )
}

export const getListsUploadCsvApi = data => {
  return service.get(`${API_URL}/list-request`, data);
};

export const editListApi = data => {
  console.log("editListApi: ",data);
  return service.patch(
    `${API_URL}/list-request/${data.listId}?account=${data.account}`,
    data.body
  );
};

export const getZipBordersApi = zip => {
  return service.extGet(`https://emapdata.adc4gis.com/zips?zips=${zip}`);
};

export const getUniqueZipsApi = ({lists, account}) => {
  return service.get(`${API_URL}/contacts/zip-data/${lists}?account=${account}`);
};
