import React, { Component } from 'react';
import {CardHeader, Table } from 'reactstrap';
import {pathOr} from 'ramda';
// import './OrderYardSignsPreview.scss';

class Order extends Component {
  // builderRef = React.createRef();

  render() {    
    const img = pathOr('', ['yardSign', 'images', 'front', 'mini'], this.props.data);
    const trackingNumber = pathOr('', ['callTracking', 'trackingNumber', 'number'], this.props.data);
    const forwardingNumber = pathOr('', ['callTracking', 'forwardingNumber'], this.props.data);
    const phoneNumber = pathOr('', ['phoneNumber'], this.props.data);
    const amount = pathOr('', ['yardSign', 'amount'], this.props.data);
    const stakes = pathOr('', ['yardSign', 'stakes'], this.props.data);
    const address1 = pathOr('', ['yardSign', 'deliveryAddress', 'address1'], this.props.data);
    const address2 = pathOr('', ['yardSign', 'deliveryAddress', 'address2'], this.props.data);
    const zip = pathOr('', ['yardSign', 'deliveryAddress', 'zip'], this.props.data);
    const city = pathOr('', ['yardSign', 'deliveryAddress', 'city'], this.props.data);
    const state = pathOr('', ['yardSign', 'deliveryAddress', 'state'], this.props.data);
    const totalPrice = (pathOr(0, ['yardSign', 'totalPrice'], this.props.data))/100;    
    return (
      <React.Fragment>
        {this.props.showHeader !== false && (
          <CardHeader className='header'>Yard Sign Order</CardHeader>
        )}
        <div className="order-preview-container"
        >
          {img&&(
            <div className="order-preview-img">
              <img src={img} alt="YardSign" />
            </div>
          )}
          <Table striped>
            <tbody>
              {this.props.data.callTracking && (
                <tr>
                  <td>Tracking Number</td>
                  <td>{trackingNumber}</td>
                </tr>
              )}

              {this.props.data.callTracking && (
                <tr>
                  <td>Forwarding Number</td>
                  <td>{forwardingNumber}</td>
                </tr>
              )}

              {this.props.data.phoneNumber && (
                <tr>
                  <td>Phone Number</td>
                  <td>{phoneNumber}</td>
                </tr>
              )}
              <tr>
                <td>Signs Amount</td>
                <td>{amount}</td>
              </tr>
              <tr>
                <td>Stakes</td>
                <td>{stakes?amount:0}</td>
              </tr>
              <tr>
                <td>Address 1</td>
                <td>{address1}</td>
              </tr>
              <tr>
                <td>Address 2</td>
                <td>{address2}</td>
              </tr>
              <tr>
                <td>Postal Code</td>
                <td>{zip}</td>
              </tr>
              <tr>
                <td>City</td>
                <td>{city}</td>
              </tr>
              <tr>
                <td>State</td>
                <td>{state}</td>
              </tr>
              <tr>
                <td>
                  <b>Total Amounts</b>
                </td>
                <td>
                  <b>${totalPrice}</b>
                </td>
              </tr>
            </tbody>
          </Table>
        </div>
      </React.Fragment>
    );
  }
}

export default Order;
