const isBlank = (str)=>{
  return (!str || /^\s*$/.test(str))
}

const fullNameLayout = (cell, row, rowIndex, formatExtraData) => {  
  return (
    <div className={`verified-cell ${!cell.verified?'small-cell':''} ${isBlank(`${cell.firstName} ${cell.lastName}`)?'isBlank':'full'}`}>
      <div className='fullName-cell'>
        {`${cell.firstName} ${cell.mi ? cell.mi : ''} ${cell.lastName}`}
      </div>
      {
        !cell.verified && 
        <div className='invalidated-contact'>
          Invalidated Contact
        </div>
      }
    </div>
  );
};

const listsLayout = (cell, row, rowIndex, formatExtraData) => {     
  let listStr = cell.map(x=>x.name||'').join(', ') || '';
  let vertlistStr =  cell.map((x,i,arr)=>(`<span>${x.name}${i<arr.length-1 ? ',' : ''}</span>`)).join('') || '';  
  return ( 
    listStr ?       
      <div className='list-cell' >            
        <span className="list-text"  data-tip data-for={`tooltips-${row._id}`} > {listStr} </span>         
        <ReactTooltip          
          className="contact-list-tooltip"          
          html={true} 
          id={`tooltips-${row._id}`}
        >  
          {vertlistStr}
        </ReactTooltip>
      </div>
     : null    
  );
};

