import service from './service';
import { API_URL } from '../config/config';

export const createAllIncomingСallsApi = (data) => {
  return service.get(
    `${API_URL}/call-tracking/get-calls-by-job?account=${data.account}`,
  );
};

export const createIncomingСallsApi = (data) => {
  return service.get(
    `${API_URL}/call-tracking/get-calls-by-job`,
    data.params
  );
};

export const createDashboardStatisticApi = (data) => {
  return service.get(
    `${API_URL}/statistic?account=${data.account}`,
  );
};


export const createAddPinApi = (data) => {
  return service.post(
    `${API_URL}/yard-sign/pin`,
    data
  )
}

export const createRemovePinApi = (data) => {
  return service.post(
    `${API_URL}/yard-sign/pin/remove`,
    data
  )
}