import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Switch, Route, useRouteMatch } from 'react-router-dom';
import AutomationList from './AutomationList';
import AutomationCreate from './AutomationCreate';
import AutomationPreview from './AutomationPreview';
import { resetAutomation } from 'actions/automation';
import './Automation.scss';

export default function Automation(props) {
  const dispatch = useDispatch();
  const { url } = useRouteMatch();
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const [savedAccountId, setSavedAccountId] = useState('');  

  useEffect(() => {       
    return () => {      
      dispatch(resetAutomation());
    };
  }, []);

  useEffect(() => {         
    setSavedAccountId(currentAccountId);
  }, [currentAccountId]);
  
  return (        
    <Switch>
      <Route exact path={`${url}`}>
        <AutomationList/>
      </Route>
      <Route exact path={`${url}/:automationId/view`}>
        <AutomationPreview savedAccountId={savedAccountId}/>
      </Route>
      <Route exact path={`${url}/:automationId/:typePreview?`}>
        <AutomationCreate savedAccountId={savedAccountId}/>
      </Route>
    </Switch>    
  );
}
