import React from 'react';
import { NavLink, TabPane } from 'reactstrap';
import { DopeNav } from 'components/DopeNav/DopeNav';
import { DopeNavItem } from 'components/DopeNav/DopeNavItem';
import { DopeTabContent } from 'components/DopeNav/DopeTabContent';
import AllTemplates from './AllTemplates';
import MyRecentYardSigns from './MyRecentYardSigns';
import MyYardSignTemplates from './MyYardSignTemplates';
import './Templates.scss';

export default function Templates(props) {   
  const {
    tab,
    ...rest
  } = props;  
  return (
    <div className='yardSigns-templates-container'>      
      <DopeNav tabs>
        <DopeNavItem>
          <NavLink
            className={`${tab === '1' ? 'active' : 'w1'}`}
            onClick={() => {
              props.changeYardSignTemplateTabIndex('1');              
            }}
          >
            All
          </NavLink>
        </DopeNavItem>        
        <DopeNavItem>
          <NavLink
            className={`${tab === '2' ? 'active' : 'w2'}`}
            onClick={() => {            
              props.changeYardSignTemplateTabIndex('2');              
            }}
          >
            My Recent YardSigns
          </NavLink>
        </DopeNavItem>       
        <DopeNavItem>
          <NavLink
            className={`${tab === '3' ? 'active' : 'w3'}`}
            onClick={() => {
              props.changeYardSignTemplateTabIndex('3');              
            }}
          >
            My YardSign Templates
          </NavLink>
        </DopeNavItem>
      </DopeNav>
      <DopeTabContent activeTab={tab}>
        <TabPane tabId='1'>
          <AllTemplates            
            {...rest}
          />
        </TabPane>        
        <TabPane tabId='2'>
          <MyRecentYardSigns            
            {...rest}            
          />
        </TabPane>
        <TabPane tabId='3'>
          <MyYardSignTemplates
            {...rest}
          />
        </TabPane>          
      </DopeTabContent>
    </div>
  );
}
