import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Switch, Route, useRouteMatch } from 'react-router-dom';

import CampaignPage from './CampaignPage';
import CampaignsList from './CampaignsList';
import SendingPage from './SendingPage';
import PostcardTemplatesModal from './PostcardTemplatesModal';
import ReportCampaign from './ReportCampaign';
// import ReactDeviceDetect from 'components/ReactDeviceDetect';



import { resetCampaignsPage } from 'actions/campaigns';

import './Campaigns.scss';

export default function Campaigns(props) {  
  const configs = useSelector(state => state.dbConfigs.configs);
  const ownPriceData = useSelector(state => state.general.ownPrice); //??? Dima
  // const ownPriceData = useSelector(state => state.account.userOwnPrice);
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const [savedAccountId, setSavedAccountId] = useState('');
  
  const { url } = useRouteMatch();
  const dispatch = useDispatch();

  useEffect(() => {    
    setSavedAccountId(currentAccountId);
    return () => {      
      dispatch(resetCampaignsPage());
    };
  }, []);

  useEffect(() => {        
    setSavedAccountId(currentAccountId);    
  }, [currentAccountId]); 

  const getPostcardPrices = () => {
    const defaultPostcardPrice = configs.filter(
      item => item.name === 'postcardPrice',
    )[0].value;
    const customPostcardPrice = ownPriceData
      ? ownPriceData.postcardPrice
      : null;

    if (customPostcardPrice && customPostcardPrice.length)
      return customPostcardPrice;
    else return defaultPostcardPrice;
  };

  const [showAddingCard, setShowAddingCard] = useState(false);

  return (
    <div className={`campaigns-page${showAddingCard?" addCard":""}`}>
      {/* <ReactDeviceDetect 
        // titleText = "don't support functionality that using in our Postcard Builder in Campaigns"
      /> */}
      <Switch>
        <Route exact path={`${url}`}>
          <CampaignsList postcardPrice={getPostcardPrices() } />
        </Route>
        <Route exact path={`${url}/report/:campaignId`}>
          <ReportCampaign savedAccountId={savedAccountId}/>
        </Route>
        <Route exact path={`${url}/:campaignId`}>
          <CampaignPage postcardPrice={getPostcardPrices()} savedAccountId={savedAccountId}/>
        </Route>
        <Route exact path={`${url}/:campaignId/:sendingId/:typePreview?`}>
          <SendingPage 
            savedAccountId={savedAccountId}
            showAddingCard={showAddingCard}
            setShowAddingCard={setShowAddingCard}
          />
        </Route>
      </Switch>
      <PostcardTemplatesModal />      
    </div>
  );
}
