import service from './service';
import { API_URL } from 'config/config';

export const getAdminAccountsApi = data => {
  return service.get(`${API_URL}/accounts`, data.query);
};

export const getAdminAccountApi = (data) => {    
  return service.get(
    `${API_URL}/accounts/${data}`
  );
};

export const setOwnPostcardPreferencesApi = data => {
  return service.patch(`${API_URL}/ballance/${data.params.id}?account=${data.query.account}`, data.body);
};

export const getOwnPostcardPreferencesApi = data => {
  return service.get(`${API_URL}/ballance/${data}`);
};

export const deleteAccountAdmRequestApi = (data) => {  
  return service.delete(
    `${API_URL}/accounts/${data.accountId}`
  )
};

export const getCustomPaymentApi = data => {
  return service.post(`${API_URL}/accounts/${data.accountId}/charge`, data.body);
};

export const sendSmsApi = data => {
  return service.post(`${API_URL}/accounts/${data.accountId}/send-sms`, data.body);
};

export const changeAccountDataApi = data => {
  console.log("changeAccountDataApi body:", data.body);
  return service.patch(`${API_URL}/accounts/${data.accountId}`,
    data.body
  );  
};

