import React from 'react';
import DatePicker from 'react-datepicker';
import moment from 'moment';
import { ReactComponent as LeftArrIcon } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as RightArrIcon } from 'assets/img/icons/chevron-right.svg';
import { FadeLoader } from 'react-spinners';

import './DopeDatePicker.scss';

export const DopeDatePicker = ({
  onMonthChange,
  sendingDataIsLoading,
  ...props
}) => {
  const getStartEndDates = (selectedDate, direction) => {
    let newDate = null;

    if (direction === 'PREV') {
      newDate = moment(selectedDate).subtract('month', 1);
    } else if (direction === 'NEXT') {
      newDate = moment(selectedDate).add('month', 1);
    }

    const startDate = newDate
      .clone()
      .startOf('month')
      .startOf('week')
      .format('YYYY-MM-DD');
    const endDate = newDate
      .clone()
      .endOf('month')
      .endOf('week')
      .format('YYYY-MM-DD');
    return { startDate, endDate };
  };
  return (
    <DatePicker
      className='dope-input form-control'
      calendarClassName={`dope-date-picker ${sendingDataIsLoading
        ? 'disabled-calendar'
        : ''}`}
      {...props}
      renderCustomHeader={({ date, decreaseMonth, increaseMonth }) => (
        <div className='dope-calendar-header'>
          <button
            onClick={() => {
              onMonthChange(getStartEndDates(date, 'PREV'));
              decreaseMonth();
            }}
          >
            <LeftArrIcon />
          </button>
          {moment(date).format('MMMM')}, {moment(date).format('YYYY')}
          <button
            onClick={() => {
              onMonthChange(getStartEndDates(date, 'NEXT'));
              increaseMonth();
            }}
          >
            <RightArrIcon />
          </button>
        </div>
      )}
    >
      {sendingDataIsLoading && (
        <div className='loader-container'>
          <FadeLoader size={100} color={'#403d39'} />
        </div>
      )}
    </DatePicker>
  );
};
