import React from 'react';
import { Map, TileLayer, Marker, Circle, ZoomControl } from 'react-leaflet';
import './LeafletMap.scss'
import { useSelector } from 'react-redux';

export default function LeafletMap({center, googleMapType, neighborhoods, searchAddress}) {

  const isLoading = useSelector(state => state.neighborhoodBlitz.isLoading);
  const searchDistance = useSelector(state => state.neighborhoodBlitz.searchDistance);
  const foundRealAddress = useSelector(state => state.neighborhoodBlitz.foundRealAddress);

  let dynamicZoom = 4;
  if(foundRealAddress){
    if(searchDistance < 100) dynamicZoom = 19;
    else if(searchDistance < 150) dynamicZoom = 18;
    else if(searchDistance < 300) dynamicZoom = 17;
    else if(searchDistance < 600) dynamicZoom = 16;
    else if(searchDistance < 1200) dynamicZoom = 15;
    else if(searchDistance < 2100) dynamicZoom = 14;
    else if(searchDistance < 4200) dynamicZoom = 13;
    else if(searchDistance < 7500) dynamicZoom = 12;
    else dynamicZoom = 11;
  }

  return (
    <Map
      className={`${isLoading ? 'loading' : ''}`}
      center={center}
      zoom={dynamicZoom}
      maxZoom={50}
      attributionControl={true}
      zoomControl={false}
      doubleClickZoom={true}
      scrollWheelZoom={true}
      dragging={true}
      easeLinearity={0.35}
    >
      <div className="loading-indicator">Processing...</div>
      <ZoomControl position="bottomleft"/>
      <TileLayer
        url={`http://{s}.google.com/vt/lyrs=${googleMapType}&hl=en&x={x}&y={y}&z={z}`}
        subdomains={['mt0','mt1','mt2','mt3']}
      />
      {searchAddress && (
        <Circle center={center} radius={searchDistance} />
      )}
      {neighborhoods && neighborhoods.map((item, key) =>
        <Marker position={{ lat: item.geocoordinate.lat, lng: item.geocoordinate.lon }} key={key}/>
      )}
    </Map>
  );
}