import service from './service';
import { API_URL } from '../config/config';

export const getPackageListApi = () => {
  return service.get(`${API_URL}/subscription/plans`);
};

export const getCategoryApi = () => {
  return service.get(`${API_URL}/category`);
};

export const getOwnPriceApi = data => {
  return service.get(`${API_URL}/ballance/${data}`);
};

export const submitNewCardApi = data => {
  // console.log("submitNewCardApi data", data);
  return service.post(
    `${API_URL}/save-new-card/?account=${data.queries.account}`, data.body,
  )
};
