import { all, call, put, takeEvery } from 'redux-saga/effects'
import * as generalActions from 'actions/general';
import * as listsActions from 'actions/listRequests';
import {
  createListApi,
  uploadListApi,
  downloadListApi,
  getListsApi,
  getListsUploadCsvApi,
  editListApi,
  getZipBordersApi,
  getUniqueZipsApi,
} from '../requests/list-requests'
import { toast } from 'react-toastify';

export function* getListsRequest(action) {
  try {
    const response = yield call(getListsApi, action.data)
    yield put({ type: listsActions.GET_LISTS_REQUEST_SUCCESS, data: response.data })
  } catch(e) {
    yield put({ type: listsActions.GET_LISTS_REQUEST_FAILED, error: e })
  }
}

export function* createListRequest(action) {
  try {
    const response = yield call(createListApi, action.data)
    yield put({ type: listsActions.CREATE_LIST_REQUEST_SUCCESS, data: response.data })
    yield put({ type: listsActions.GET_LISTS_REQUEST, data: action.data })
  } catch(e) {
    yield put({ type: listsActions.CREATE_LIST_REQUEST_FAILED, error: e })
  }
}

export function* uploadListRequest(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    yield call(uploadListApi, action.data)
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    yield put({ type: listsActions.UPLOAD_LIST_REQUEST_SUCCESS })
    const {page, limit, account} = action.data;
    let data = {
      page, limit, account
    }
    
    yield put({ type: listsActions.TOGGLE_UPLOAD })
    yield put({ type: listsActions.GET_LISTS_REQUEST, data })
    toast.success(`Your list has successfully started loading. It may take a few minutes.`, {
      position: toast.POSITION.BOTTOM_CENTER
    })
  } catch(e) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    yield put({ type: listsActions.UPLOAD_LIST_REQUEST_FAILED, error: e.response.data.message })
    toast.error(`Error list wasn't add.`, {
      position: toast.POSITION.BOTTOM_CENTER
    })
  }
}

export function* downloadListRequest(action) {
  try {
    const response = yield call(downloadListApi, action.data)
    yield put({ type: listsActions.DOWNLOAD_LIST_REQUEST_SUCCESS, data: response.data })
  } catch(e) {
    yield put({ type: listsActions.DOWNLOAD_LIST_REQUEST_FAILED, error: e })
  }
}

export function* getListsUploadCsv(action) {
  try {
    const response = yield call(getListsUploadCsvApi, action.data)
    yield put({ type: listsActions.GET_LISTS_UPLOAD_CSV_REQUEST_SUCCESS, data: response.data.data })
  } catch(error) {
    
  }
}

export function* editList(action) {  
  try {    
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    yield call(editListApi, action.data);
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    yield put({ type: listsActions.EDIT_LIST_REQUEST_SUCCESS, data: action.data })
    yield put({ type: listsActions.TOGGLE_EDIT_LIST_MODAL, data: false })
    yield put({ type: listsActions.RESET_EDIT_LIST_FORM })    
    toast.success(`The list was changed.`)
  } catch(error) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.error(`Error the list wasn't changed.`)
  }
}

export function* getZipBorders(action) {
  try {
    const zips = Object.keys(action.data).toString();
    const contactsInZip = Object.values(action.data);
    const response = yield call(getZipBordersApi, zips);

    if(response.data.features.length){
      const x = [];
      const y = [];
      const markers = [];
      let clientWidth = document.querySelector('.leaflet-container') ? document.querySelector('.leaflet-container').clientWidth : 500;
      let clientHeight = document.querySelector('.leaflet-container') ? document.querySelector('.leaflet-container').clientHeight : 462;//389;//changed positioning
      response.data.features.map((multiPoligon,i)=> {
        const markersX = [];
        const markersY = [];
        multiPoligon.geometry.coordinates.map((poliPoligon, j) => {
          poliPoligon.map((poligon, z) => {
            poligon.map(item => {
              markersX.push(item[0]);
              markersY.push(item[1]);
              x.push(item[0]);
              y.push(item[1]);
              return item
            })
            return poligon
          });
          return poliPoligon
        });
        let minX = Math.min(...markersX);
        let minY = Math.min(...markersY);
        let maxX = Math.max(...markersX);
        let maxY = Math.max(...markersY);
        let lat = (maxY + minY)/2;
        let lng = (maxX + minX)/2;
        markers.push({position: [lat, lng], amount: contactsInZip[i]});
        return multiPoligon;
      });
      let minX = Math.min(...x);
      let minY = Math.min(...y);
      let maxX = Math.max(...x);
      let maxY = Math.max(...y);
      let width = Math.abs(maxX - minX);
      let height = Math.abs(maxY - minY);
      let lat = (maxY + minY)/2;
      let lng = (maxX + minX)/2;
      let zoomX = (224153340 * width)/clientWidth;
      let zoomY = (248214343 * height)/clientHeight;
      let zoomM = Math.max(zoomX,zoomY);
      let zoom = 7;
      if (zoomM >= 78206000 && zoomM < 156412000)
        zoom = 1;
      if (zoomM >= 39103000 && zoomM < 78206000)
        zoom = 2;
      if (zoomM >= 19551000 && zoomM < 39103000)
        zoom = 3;
      if (zoomM >= 9776000 && zoomM < 19551000)
        zoom = 4;
      if (zoomM >= 4888000 && zoomM < 9776000)
        zoom = 5;
      if (zoomM >= 2444000 && zoomM < 4888000)
        zoom = 6;
      if (zoomM >= 1222000 && zoomM < 2444000)
        zoom = 7;
      if (zoomM >= 610984 && zoomM < 1222000)
        if (zoomM >= 610984)
          zoom = 8;
      if (zoomM >= 305492 && zoomM < 610984)
        zoom = 9;
      if (zoomM >= 152746 && zoomM < 305492)
        zoom = 10;
      if (zoomM >= 76373 && zoomM < 152746)
        zoom = 11;
      if (zoomM >= 38187 && zoomM < 76373)
        zoom = 12;
      if (zoomM >= 19093 && zoomM < 38187)
        zoom = 13;
      if (zoomM >= 9547 && zoomM < 19093)
        zoom = 14;
      if (zoomM >= 4773 && zoomM < 9547)
        zoom = 15;
      if (zoomM >= 2387 && zoomM < 4773)
        zoom = 16;
      if (zoomM >= 1193 && zoomM < 2387)
        zoom = 17;
      if (zoomM >= 596 && zoomM < 1193)
        zoom = 18;
      if (zoomM >= 298 && zoomM < 596)
        zoom = 19;
      if (zoomM < 298)
        zoom = 20;

      yield put({type: listsActions.GET_ZIP_BORDERS_REQUEST_SUCCESS,
        data: {
          zipBorders: response.data,
          mapPosition: {lat, lng},
          zoom: zoom,
          markers: markers
        }});
    }else {
      yield put({ type: listsActions.GET_ZIP_BORDERS_REQUEST_FAILED });
    }
  } catch (e) {
    yield put({ type: listsActions.GET_ZIP_BORDERS_REQUEST_FAILED, e });
  }
}

export function* getUniqueZips(action) {
  try {
    const response = yield call(getUniqueZipsApi, action.data);
    yield put({type: listsActions.GET_UNIQUE_ZIPS_REQUEST_SUCCESS, data: response.data});
    yield put({ type: listsActions.GET_ZIP_BORDERS_REQUEST, data: response.data.uniqZip})
  } catch (e) {
    yield put({ type: listsActions.GET_UNIQUE_ZIPS_REQUEST_FAILED, e });
  }
}

export default all([
  takeEvery(listsActions.GET_LISTS_REQUEST, getListsRequest),
  takeEvery(listsActions.CREATE_LIST_REQUEST, createListRequest),
  takeEvery(listsActions.UPLOAD_LIST_REQUEST, uploadListRequest),
  takeEvery(listsActions.DOWNLOAD_LIST_REQUEST, downloadListRequest),
  takeEvery(listsActions.GET_LISTS_UPLOAD_CSV_REQUEST, getListsUploadCsv),
  takeEvery(listsActions.EDIT_LIST_REQUEST, editList),
  takeEvery(listsActions.GET_ZIP_BORDERS_REQUEST, getZipBorders),
  takeEvery(listsActions.GET_UNIQUE_ZIPS_REQUEST, getUniqueZips),
])