import {
    SET_FORMATTER_REF,
    SET_INLINE_STYLES,
    SET_SELECTED_BLOCK,
    TOGGLE_INLINE_STYLE
} from '../actions/builder';

export default function reducer(state = [], action) {
    switch (action.type) {
        case SET_FORMATTER_REF:
            return {
                ...state,
                formatterRef: action.formatterRef
            }
        case SET_INLINE_STYLES:
            return {
                ...state,
                inlineStyles: action.inlineStyles
            }
        case TOGGLE_INLINE_STYLE:
            return {
                ...state,
                inlineStyles: action.inlineStyles
            }
        case SET_SELECTED_BLOCK:
            return {
                ...state,
                selectedBlock: action.selectedBlock
            }
        default:
            return state;
    }
}