import React, {useState} from 'react';
import {
  DopeModal,
  DopeModalHeader,
  DopeModalBody,
  DopeModalFooter,
} from 'components/DopeModal';
import { FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeButton } from 'components/DopeForms';


import './EddmConfirmationPoPup.scss';



export default function EddmConfirmationPoPup({isOpen, toggle, onConfirm, data, totalPrice, templateStatus, templateName, setTemplateName}) {
  const [error, setError] = useState('');
  const validation = () => {
    if(templateStatus==='saved'&&!templateName.length) {
      setError('Template name is required!');
      return
    }
    onConfirm();
  }
  return (
    <DopeModal
      className='eddmPostcard-approval-modal'
      // onOpened={onOpened}
      isOpen={isOpen}
      centered
    >
      <DopeModalHeader toggle={toggle}>{`${(templateStatus!=='saved')?'Confirm EDDM':'Confirm your Template'}`}</DopeModalHeader>
      <DopeModalBody>
        {
          (templateStatus!=='saved')&&(
            <React.Fragment>
              <p className="approval-text">By confirming the sending, you will make it ready for sending on the specified day.</p>
              <div className="approval-row">
                <div className="approval-row-item">
                  <h4 className="item-title">
                    Selected routes
                  </h4>
                  <div className="modal-routes-container">
                    {
                      data.routes.map((el)=>{
                        return <span key={el} style={{marginRight:"8px"}}>{el}</span>
                      })
                    }
                  </div>
                </div> 
                <div className="approval-row-item">
                  <h4 className="item-title">
                    Total price
                  </h4>
                  <div>
                    {
                      `$${totalPrice}`
                    }
                  </div>
                </div>           
              </div>
            </React.Fragment>
          )
        }        
        <div className="approval-grid">
          <div className="approval-grid-item">
            <h4 className="item-title">
              Front Side
            </h4>
            <div className="img-prev-container">
              {
                data.front ? (
                  <img src={data.front} alt="img-prev"/>
                ):null
              }
            </div>
          </div>
          <div className="approval-grid-item">
            <h4 className="item-title">
              Back Side
            </h4>
            <div className="img-prev-container">
              {
                data.back ? (
                  <img src={data.back} alt="img-prev"/>
                ):null
              }
            </div>
          </div>
        </div> 
        {templateStatus==='saved'&&(
          <FormGroup className="dope-form-group templateName-container">
            <DopeInput
              value={templateName}
              title='Template Name'
              supText={!templateName.length?'*':''}
              maxLength={30}
              error={error}
              onChange={e => {
                setError('');
                setTemplateName(e.target.value)}
              }
            />
          </FormGroup>
        )}     
      </DopeModalBody>
      <DopeModalFooter>        
        <DopeButton color='white' onClick={toggle}>
          Cancel
        </DopeButton>
        <DopeButton
          color='primary'
          onClick={validation}
        >
          {`${(templateStatus!=='saved')?'Confirm Mailing':'Save Template'}`}
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>
  );
}
