import { all, call, put, takeLatest } from 'redux-saga/effects';
import * as handwrittenActions from '../actions/handwritten';
import * as generalActions from 'actions/general';
import {
  saveHandwrittenApi,
  updateHandwrittenApi,
  buyHandwrittenApi,
  getHandwrittenListApi,
  getHandwrittenOrderApi,
  deleteHandwrittenOrderApi,
} from "requests/handwritten";
import {push} from "connected-react-router";
import {toast} from "react-toastify";
import {pathOr} from "ramda";


export function* saveHandwritten(action) {
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    const response = yield call(saveHandwrittenApi, action.data);
    yield put({type: handwrittenActions.SAVE_HANDWRITTEN_REQUEST_SUCCESS, data: response.data});
    yield put(push('/handwritten'));
    toast.success(`You have successfully saved Handwritten`);
  } catch (error) {
    yield put({type: handwrittenActions.SAVE_HANDWRITTEN_REQUEST_FAILED, error});
    let errorMessage = pathOr("Error. Cann't save Handwritten", ['response', 'data', 'error', 'dopeMessage'], error);
    toast.error(errorMessage);
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
}

