export function* getHandwrittenList(action) {
  try {
    const response = yield call(getHandwrittenListApi, action.data);
    yield put({type: handwrittenActions.GET_HANDWRITTEN_LIST_REQUEST_SUCCESS, data: {...response.data, ...action.data}});
  } catch (error) {
    yield put({type: handwrittenActions.GET_HANDWRITTEN_LIST_REQUEST_FAILED, error});
  }
}

export function* getHandwrittenOrder(action) {
  try {
    const response = yield call(getHandwrittenOrderApi, action.data);
    yield put({type: handwrittenActions.GET_HANDWRITTEN_ORDER_REQUEST_SUCCESS, data: response.data});
  } catch (error) {
    yield put({type: handwrittenActions.GET_HANDWRITTEN_ORDER_REQUEST_FAILED, error});
  }
}

export function* deleteHandwrittenOrder(action) {
  try {
    const response = yield call(deleteHandwrittenOrderApi, action.data);
    yield put({type: handwrittenActions.DELETE_HANDWRITTEN_ORDER_REQUEST_SUCCESS, data: response.data});
    toast.success(`You have successfully deleted Handwritten order`);
    yield put({type: handwrittenActions.GET_HANDWRITTEN_LIST_REQUEST, data: { query:{page:0, limit: 10}, ...action.data }})
  } catch (error) {
    let errorMessage = pathOr("Error. Cann't delete Handwritten order", ['response', 'data', 'error', 'dopeMessage'], error);
    toast.error(errorMessage);
    yield put({type: handwrittenActions.DELETE_HANDWRITTEN_ORDER_REQUEST_FAILED, error});
  }
}

export default all([
  takeLatest(handwrittenActions.SAVE_HANDWRITTEN_REQUEST, saveHandwritten),
  takeLatest(handwrittenActions.UPDATE_HANDWRITTEN_REQUEST, updateHandwritten),
  takeLatest(handwrittenActions.BUY_HANDWRITTEN_REQUEST, buyHandwritten),
  takeLatest(handwrittenActions.GET_HANDWRITTEN_LIST_REQUEST, getHandwrittenList),
  takeLatest(handwrittenActions.GET_HANDWRITTEN_ORDER_REQUEST, getHandwrittenOrder),
  takeLatest(handwrittenActions.DELETE_HANDWRITTEN_ORDER_REQUEST, deleteHandwrittenOrder),
]);