import { all, call, put, takeLatest } from 'redux-saga/effects';
import { toast } from 'react-toastify';
import LogRocket from 'logrocket';
import * as generalActions from 'actions/general';
import * as adminRequestsActions from 'actions/adminRequests';
import {
  getAdminRequestsApi,
  deleteRequestApi,
  changeRequestStatusApi,
  getAdminRequestApi,
  getAdminListsUploadCsvApi,
  uploadAccountRequestsListApi,
} from 'requests/adminRequests';
import {pathOr} from 'ramda';

export function* getAdminRequests(action) {
  try {
    const response = yield call(getAdminRequestsApi, action.data);    
    let data = {
      ...action.data.query,
      ...response.data,
    };
    yield put({
      type: adminRequestsActions.GET_ADMIN_REQUESTS_SUCCESS,
      data: data,
    });
  } catch (e) {
    LogRocket.captureException(e);
    yield put({ type: adminRequestsActions.GET_ADMIN_REQUESTS_FAILED });
    toast.error(`Error. Can't get requests list.`);
  }
}

export function* deleteRequest(action) {
  try {
    const response = yield call(deleteRequestApi, action.data);
    toast.success(response.data.message);
    action.callbackSuccess();
  } catch (e) {
    LogRocket.captureException(e);    
    toast.error(`Error. Can't delete request.`);
  }
}

export function* changeRequestStatus(action) {
  try {
    const response = yield call(changeRequestStatusApi, action.data);
    toast.success(response.data.message);
    action.callbackSuccess();
  } catch (e) {
    LogRocket.captureException(e);    
    // toast.error(`Error. Can't change request status.`);
    console.log(`Error. Can't change request status.`);   

    const errorMessage = pathOr("Error. Can't change request status.", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage)
  }
}

export function* getAdminRequest(action) {
  try {
    const response = yield call(getAdminRequestApi, action.data);  
    let data = {      
      ...response.data,
    };
    yield put({
      type: adminRequestsActions.GET_ADMIN_REQUEST_SUCCESS,
      data: data,
    });
  } catch (e) {
    LogRocket.captureException(e);
    yield put({ type: adminRequestsActions.GET_ADMIN_REQUEST_FAILED });
    toast.error(`Error. Can't get request.`);
  }
}

export function* getAdminListsUploadCsv(action) {
  try {
    const response = yield call(getAdminListsUploadCsvApi, action.data)
    yield put({ type: adminRequestsActions.GET_ADMIN_LISTS_UPLOAD_CSV_SUCCESS, data: response.data.data })
  } catch(error) {
    console.log(error);
  }
}

export function* uploadAccountRequestsList(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    yield call(uploadAccountRequestsListApi, action.data)
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    yield put({ type: adminRequestsActions.UPLOAD_ACCOUNT_REQUESTS_LIST_SUCCESS }) 
    action.callbackSuccess();    
    toast.success(`The list successfully sent`)
  } catch(e) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    yield put({ type: adminRequestsActions.UPLOAD_ACCOUNT_REQUESTS_LIST_FAILED, error: e.response.data.message })
    toast.error(`Error list wasn't add.`)
  }
}

export default all([
  takeLatest(adminRequestsActions.GET_ADMIN_REQUESTS_REQUEST, getAdminRequests),
  takeLatest(adminRequestsActions.DELETE_REQUEST_REQUEST, deleteRequest),
  takeLatest(adminRequestsActions.CHANGE_REQUEST_STATUS_REQUEST, changeRequestStatus),
  takeLatest(adminRequestsActions.GET_ADMIN_REQUEST_REQUEST, getAdminRequest),
  takeLatest(adminRequestsActions.GET_ADMIN_LISTS_UPLOAD_CSV_REQUEST, getAdminListsUploadCsv),
  takeLatest(adminRequestsActions.UPLOAD_ACCOUNT_REQUESTS_LIST_REQUEST, uploadAccountRequestsList),
]);


