import React from 'react';
import { compose, withProps, withHandlers } from 'recompose';
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
} from 'react-google-maps';
import MarkerClusterer from 'react-google-maps/lib/components/addons/MarkerClusterer';
import { GOOGLE_API_KEY } from '../../../../../config/config';

export  const Map = compose(
  withProps({
    googleMapURL:
      `https://maps.googleapis.com/maps/api/js?key=${GOOGLE_API_KEY}&v=3.exp&libraries=geometry,drawing,places`,
    loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `400px` }} />,
    mapElement: <div style={{ height: `100%` }} />,
  }),
  withHandlers({
    onMarkerClustererClick: () => markerClusterer => {
      const clickedMarkers = markerClusterer.getMarkers();
      // console.log(clickedMarkers);
      console.log(`Current clicked markers length: ${clickedMarkers.length}`);
    },
    componentDidMount() {
      this.setState({ img: '' });
    },
  }),
  withScriptjs,
  withGoogleMap,
)(props => (
  <GoogleMap
    center={props.center || { lat: 44, lng: -93 }}
    zoom={props.zoom || 7}
    mapTypeId={props.mapTypeId}
    defaultOptions={{
      styles: require('components/map-styles.json'),
    }}
    onDragEnd={props.onCenterChanged}
    onZoomChanged={props.onZoomChanged}
    ref={props.setRef}
  >
    <MarkerClusterer
      onClick={props.onMarkerClustererClick}
      averageCenter
      enableRetinaIcons
      gridSize={0}
    >
      {props.children}
    </MarkerClusterer>
  </GoogleMap>
));