import React, {useState} from 'react';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import DeleteAccountModal from './DeleteAccountModal';
import CancelDeleteAccountModal from './CancelDeleteAccountModal';
import { friendlyFormatPhoneNumber} from 'utils/phones';
import {	
	Alert,
} from 'reactstrap';
// import { toast } from 'react-toastify'
import { ReactComponent as Logo } from 'assets/img/icons/image.svg';
import { ReactComponent as AttentionIcon } from 'assets/img/icons/attention.svg';
import './MoreTab.scss';

export default function MoreTab({currentAccount, onSubmitDelete, onSubmitCancelDelete}) {   //:{logo,person:{firstName, lastName},email,phone}
  const [deleteModalShow, setDeleteModalShow] = useState(false);
  const [cancelDeleteModalShow, setCancelDeleteModalShow] = useState(false); 
  const toggleDeleteModal = () => {       
    setDeleteModalShow(!deleteModalShow);
  };
  const toggleCancelDeleteModal = () => {       
    setCancelDeleteModalShow(!cancelDeleteModalShow);
  };
  return (
    <div className='MoreTab'>
      <h2 className="more-title">Account Details</h2>      
      <div className="comp-info-container">
        <div className="img-container">
          { currentAccount.logo ?             
            <img className="comp-logo" src={ currentAccount.logo } alt="logo"/>
            :
            <Logo className="comp-logo" />
          }
        </div>
        <div className="comp-info">
          <h3 className="comp-name">{currentAccount.name ? currentAccount.name : null}</h3>                   
          <span className="comp-mail">email:&nbsp;{currentAccount.email}</span>
          {currentAccount.accountPhone&&<span className="comp-phone">account phone:&nbsp;{currentAccount.accountPhone}</span>}
          <span className="comp-phone">company phone:&nbsp;{friendlyFormatPhoneNumber(currentAccount.phone)}</span>
        </div>
      </div>

      <h2 className="more-title">Delete Account</h2>      
      <p className="more-text">You can delete your account from the system if you want.</p>
      
      {
        !currentAccount.requestToDelete ? (
          <DopeButton onClick={toggleDeleteModal} color='white'>
            Delete my account
          </DopeButton>
        ) : (
          <div className="cancelDel-container">
            <Alert color="warning" className="dope-attention">
              <AttentionIcon className="icon"/>            
              <span>You sent a delete request, it may take up to 24 hours until Admin will delete your account.</span>
            </Alert>
            <DopeButton onClick={toggleCancelDeleteModal} color="primary">
              CANCEL DELETE REQUEST
            </DopeButton>
          </div>
        )
      }
      
      <DeleteAccountModal
        open={deleteModalShow}
        toggle={toggleDeleteModal} 
        onSubmit={onSubmitDelete}                          
      />
      <CancelDeleteAccountModal
        open={cancelDeleteModalShow}
        toggle={toggleCancelDeleteModal} 
        onSubmit={onSubmitCancelDelete}                          
      />
    </div>
  );
  
}
