import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import CompanyLogo from '../CompanyLogo';
import Address from '../Address';
import InputMask from 'react-input-mask';
import {friendlyFormatPhoneNumber} from 'utils/phones'
import './CompanyInfo.scss';

export default function CompanyInfo(props) {
  const {
    companyAddress,
    shippingAddress,
    createdAt,
    phone,
    name,
    logo,
  } = props.data;
  const {
    companyAddress: newCompanyAddress = companyAddress,
    shippingAddress: newShippingAddress = shippingAddress,
    phone: newPhone = phone,
    name: newName = name,
    logo: newLogo = logo,
  } = props.updatedAccount;

  const {errors} = props;

  const onChange = e => {
    props.onChange(e.target.name, e.target.value);
    props.onErrorChange(e.target.name, '')
  };
  return (
    <div className='contact-info-tab account-form-container'>
      <Container fluid>
        <Row>
          <Col md={5}>
            <CompanyLogo
              createdAt={createdAt}
              name={name}
              img={newLogo}
              onChange={props.onFileUpload}
              edit={true}
              timeZone={props.timeZone}
            />
          </Col>
          <Col md={7} className='account-form'>
            
            <div className='company-info-block'>
              <h6 className="block-title">Company Information</h6>
              <div className='account-input-container company-name-container'>
                <label htmlFor='company-name'>Company Name*</label>
                <DopeInput
                  id='company-name'
                  name='name'
                  value={newName}
                  onChange={onChange}
                  error={errors.name}
                />
              </div>          
              <div className='account-input-container company-phone-container'>
                <label htmlFor='company-phone-number'>Company Phone Number*</label>
                <InputMask
                  className={errors.phone? 'dope-input form-control form-control error': 'dope-input form-control form-control'}
                  mask='(999)999-9999'
                  name='phone'
                  value={friendlyFormatPhoneNumber(newPhone)}
                  onChange={onChange}
                  placeholder='Add Forwarding Number'
                  error={errors.phone}
                />
                { errors.phone && <div className='validation-message'>{errors.phone}</div> }
              </div>
            </div>

            <Address
              data={newCompanyAddress}
              errors={errors}
              onErrorChange={props.onErrorChange}
              type='companyAddress'
              onPlaceLoaded={props.onPlaceLoaded}
              title='Company address'
              onChange={props.onChange}
            />

            <Address
              data={newShippingAddress}
              errors={errors}
              onErrorChange={props.onErrorChange}
              type='shippingAddress'
              onPlaceLoaded={props.onPlaceLoaded}
              title='Company Shipping Address (if different than company address)'
              onChange={props.onChange}
            />

            <div className='account-button-container'>
              <DopeButton onClick={props.onSubmit} color='primary'>
                Save changes
              </DopeButton>
            </div>

          </Col>
        </Row>
      </Container>
    </div>
  );
}
