export function* getCampaignsList(action) {
  try {
    const response = yield call(getCampaignsListApi, action.data);
    const data = { ...action.data };

    delete data.account;

    yield put({
      type: campaignsActions.GET_CAMPAIGNS_DATA_REQUEST_SUCCESS,
      data: { ...response.data, ...data },
    });
    window.scrollTo({ top: 0, behavior: 'smooth' });
  } catch (error) {
    yield put({
      type: campaignsActions.GET_CAMPAIGNS_DATA_REQUEST_FAILED,
      error,
    });
  }
}

export function* getCampaign(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
    const response = yield call(getCampaignApi, action.data);
    const verifiedContacts = pathOr(
      0,
      ['list', 'listProperties', 'verifiedContacts'],
      response.data,
    );
    yield put({
      type: campaignsActions.GET_CAMPAIGN_REQUEST_SUCCESS,
      data: {
        verifiedContacts,
        list: response.data.list._id,
        name: response.data.name,
        postage: response.data.postage || 'usps_standard',
        campaign: response.data._id,
      },
    });
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
  } catch (error) {
    yield put(push(CAMPAIGNS_PATH));

    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    yield put({ type: campaignsActions.GET_CAMPAIGN_REQUEST_FAILED, error });
    toast.error(
      `Can't find campaign with id ${action.data.params.campaignId}.`,
    );
  }
}

