export function* createNewCampaign(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
    const response = yield call(createNewCampaignApi, action.data);
    yield put({
      type: campaignsActions.CREATE_NEW_CAMPAIGN_REQUEST_SUCCESS,
      data: response.data._id,
    });
    yield put(push(`${CAMPAIGNS_PATH}/${response.data._id}`));
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });

    toast.success(` New campaign has been created.`);
  } catch (error) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.error(`Error. Can't create new campaign.`);
    yield put({
      type: campaignsActions.CREATE_NEW_CAMPAIGN_REQUEST_FAILED,
      error,
    });
  }
}

