export function* getPostcardByIdCampaign(action) {  
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
    const response = yield call(getPostcardByIdCampaignApi, action.data);    
    yield put({
      type: campaignsActions.GET_POSTCARD_BY_ID_CAMPAIGN_REQUEST_SUCCESS,
      data: response.data,
    });    
    yield put({
      type: campaignsActions.TOGGLE_POSTCARD_MODAL,
      data: false,
    });    
    if(action.sendingStatus&&action.sendingStatus === 'new')
      yield put(push(`${CAMPAIGNS_PATH}/${action.campaignId}/create`));
      // if(action.sendingStatus&&action.sendingStatus === 'edit') 
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
  } catch (error) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    yield put({
      type: campaignsActions.GET_POSTCARD_BY_ID_CAMPAIGN_REQUEST_FAILED,
      error,
    });
    toast.error(`Error. Can't download sending.`);
  }
}

