export function* deleteSending(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
    yield call(deleteSendingApi, action.data);
    yield put({
      type: campaignsActions.TOGGLE_DELETE_SENDING_MODAL,
    });

    const data = {
      query: {
        ...action.data.startEndDate,
        account: action.data.query.account,
        campaign: action.data.campaignId,
      },
    };
    yield put({
      type: campaignsActions.GET_SENDING_DATA_REQUEST,
      data,
    });

    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.success(`Sending has been deleted.`);
  } catch (error) {
    toast.error(`Error. Can't delete sending.`);
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    yield put({ type: campaignsActions.DELETE_SENDING_REQUEST_FAILED, error });
  }
}

