export function* getPostcardCampaignPreviewImg(action) { 
  try {    
    const [response1,response2] = yield all([
      call(getPostcardCampaignPreviewImgApi, {template: action.data.body.template.front, bucket:action.data.body.bucket, side: 'front'}),      
      call(getPostcardCampaignPreviewImgApi, {template: action.data.body.template.back, bucket:action.data.body.bucket, side: 'back'})
    ]);     
    const images = {
      front: {
        mini: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcardImages/${response1.data.images.mini}`,    
        full: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcardImages/${response1.data.images.full}`,
      },
      back: {
        mini: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcardImages/${response2.data.images.mini}`,
        full: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcardImages/${response2.data.images.full}`
      }
    };
    yield put({
      type: campaignsActions.GET_POSTCARD_CAMPAIGN_PREVIEW_IMG_SUCCESS,
      data: images
    });
    if(action.successCallback){      
      action.successCallback(images);
    }
    // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
  } catch (e) { 
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't get previewImg", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: campaignsActions.GET_POSTCARD_CAMPAIGN_PREVIEW_IMG_FAILED, e }); 
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });   
  }
}


export default all([
  takeLatest(campaignsActions.GET_CAMPAIGNS_DATA_REQUEST, getCampaignsList),
  takeLatest(campaignsActions.GET_CAMPAIGN_REQUEST, getCampaign),
  takeLatest(
    campaignsActions.GET_CAMPAIGN_LISTS_DATA_REQUEST,
    getCampaignListsDataRequest,
  ),
  takeLatest(campaignsActions.CREATE_NEW_CAMPAIGN_REQUEST, createNewCampaign),
  takeLatest(campaignsActions.EDIT_CAMPAIGN_REQUEST, editCampaign),
  takeLatest(campaignsActions.CREATE_NEW_SENDING_REQUEST, createNewSending),
  takeLatest(campaignsActions.UPDATE_SENDING_REQUEST, updateSending),
  takeLatest(campaignsActions.CLONE_NEW_SENDING_REQUEST, cloneNewSending),
  takeLatest(campaignsActions.DELETE_SENDING_REQUEST, deleteSending),
  takeLatest(
    campaignsActions.GET_ALL_POST_CARD_TEMPLATE_FOR_CAMPAIGN_REQUEST,
    getAllPostcardTemplatesForCampaign,
  ),
  takeLatest(
    campaignsActions.GET_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST,
    getMyTemplatesForCampaign,
  ),
  takeLatest(
    campaignsActions.GET_UNI_MY_TEMPLATES_FOR_CAMPAIGN_REQUEST,
    getUniMyTemplatesForCampaign,
  ),
  takeLatest(
    campaignsActions.GET_POSTCARD_BY_ID_CAMPAIGN_REQUEST,
    getPostcardByIdCampaign,
  ),
  // takeLatest(
  //   campaignsActions.GET_MY_POSTCARD_BY_ID_CAMPAIGN_REQUEST,
  //   getMyPostcardByIdCampaign,
  // ),
  takeLatest(campaignsActions.GET_SENDING_DATA_REQUEST, getSendingData),
  takeLatest(campaignsActions.SELECT_EDITED_SENDING, selectEditedSending),
  takeLatest(campaignsActions.SELECT_VIEW_SENDING, selectViewSending),
  takeLatest(campaignsActions.SENDING_RECHARGE_REQUEST, sendingRecharge),
  takeLatest(campaignsActions.GET_AVAILABLE_SENDING_DATA_REQUEST, getAvailableSendingDataRequest),
  takeLatest(campaignsActions.GET_REPORT_CAMPAIGN_STATISTIC_REQUEST, getReportCampaignNames), 
  takeLatest(campaignsActions.GET_REPORT_CAMPAIGN_STATISTIC_REQUEST, getReportCampaignStatistic), 
  takeLatest(campaignsActions.GET_REPORT_CAMPAIGN_CONVERSION_REQUEST, getReportCampaignConversion), 
  takeLatest(campaignsActions.GET_POSTCARD_CAMPAIGN_PREVIEW_IMG_REQUEST, getPostcardCampaignPreviewImg),  
]);
