import {GET_ADMIN_HANDWRITTEN_LIST_REQUEST, GET_ADMIN_HANDWRITTEN_LIST_REQUEST_SUCCESS} from "actions/adminHandwritten";

let initialState = {
  loading: false,
  handwrittenList: {},
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case GET_ADMIN_HANDWRITTEN_LIST_REQUEST:
      return {
        ...state,
        loading: true
      };

    case GET_ADMIN_HANDWRITTEN_LIST_REQUEST_SUCCESS:
      return {
        ...state,
        handwrittenList: action.data,
        loading: false
      };

    default:
      return state;
  }
}