import React from 'react';
import { Card, CardHeader, CardBody, FormGroup } from 'reactstrap';
import Cards from '../../components/Cards';
import './CreditCard.scss';

export default function CreditCard(props) {
    const header = (
        <div className="wizard-block-header-container">
            <h4 className='wizard-block-header'>
                Payment Information
            </h4>
            {props.errors && props.errors.cardId ? (
                    <span className='wizard-error'>
                        {props.errors.cardId}
                    </span>
                ) : (
                    ''
            )}
        </div>
    );
    return (
      <React.Fragment>
        {props.data && props.data.amount ? header : ''}
        <Card className='credit-card-container'>
          {props.children&&(
            <CardHeader>
              <h6>
                {props.children}
              </h6>
            </CardHeader>
          )}     
          <CardBody>                
            {props.data && !props.data.amount && props.errors.cardId ? (
              <FormGroup>
                <span className='wizard-error'>
                  {props.errors.cardId}
                </span>
              </FormGroup> 
            ) : (
              ''
            )}                  
            <FormGroup>
              <Cards
                selected={props.data && props.data.cardId}
                onSelect={card => {
                    props.onChange(card);
                    props.addErrors('cardId', '');
                }}
                currentAccount={props.currentAccount}
                order={true}   
                view={props.view}
              />
            </FormGroup>
          </CardBody>
        </Card>
      </React.Fragment>
    );
}
