import service from './service';
import { API_URL } from '../config/config';

export const getNeighborsApi = (data) => {
  return service.get(
    `${API_URL}/neighborhoods`,
    data
  );
};

export const convertAddressToLatLonApi = (data) => {
  return service.extGet(`https://emapdata.adc4gis.com/geocodes?address=${data}`)
  // return service.extGet(`https://maps.googleapis.com/maps/api/geocode/json?address=${data}&key=${GOOGLE_API_KEY}`)
};

export const sendNeighborsApi = (data) => {
  return service.post(
    `${API_URL}/neighborhoods?account=${data.queries}`,
    data.body
  );
};

export const updateNeighborsApi = (data) => {
  return service.patch(
    `${API_URL}/neighborhoods/${data.neighborhoodBlitzId}?account=${data.queries}`,
    data.body,
  )
};

export const getNeighborsPreviewImgApi = (data) => {
  return service.post(
    `${API_URL}/convert-image`,
    data,
  )
};

export const getNeighborsListApi = data => {
  return service.get(
    `${API_URL}/accounts/${data.accountId}/neighborhoods`, data.query
  )
};

export const getNeighborsItemApi = data => {
  return service.get(
    `${API_URL}/accounts/${data.accountId}/neighborhoods/${data.sendingId}`, data.query
  )
};

export const deleteNeighborsItemApi = data => {
  return service.delete(
    `${API_URL}/neighborhoods/${data.sendingId}?account=${data.accountId}`
  )
};

export const saveAsMyNeighborsTemplateApi = (data) => {    
  return service.post(
    `${API_URL}/accounts/${data.accountId}/templates`,
    data.body,
  )
};

export const getRecentNeighborhoodBlitzPostcardsApi = (data) => {
  return service.get(
    `${API_URL}/accounts/${data.accountId}/neighborhoods`, data.query
  );
};

export const getMyNeighborhoodBlitzPostcardsApi = (data) => {  
  return service.get(
    `${API_URL}/accounts/${data.accountId}/templates`, data.query
  );
};

export const deleteMyNeighborhoodBlitzPostcardApi = (data) => {  
  return service.delete(
    `${API_URL}/accounts/${data.accountId}/templates/${data.templateId}`
  );
};
  
export const buyNeighborsListOfContactsApi = (data) => {
  return service.post(
    `${API_URL}/neighborhoods/list-charge?account=${data.account}`, data.body
  )
};

export const createNbhCampaignsApi = (data) => {
  return service.post(
    `${API_URL}/neighborhoods/${data.nbhId}/campaigns`, data.body
  )
};