import React from 'react';
import Pagination from 'rc-pagination';
import localeInfo from 'rc-pagination/lib/locale/en_US';
import { ReactComponent as PrevIco } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as NextIco } from 'assets/img/icons/chevron-right.svg';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import { FadeLoader } from 'react-spinners';
import { pathOr } from 'ramda';

export default function MyPostcardTemplates({
  templatesData,
  myPostcardIsLoading,
  deleteProp,
  indexSelectedPostcard,
  onSelectTemplate,
  ...props
}) {
  const onPageChange = page => {
    props.getPostcards({ page: page - 1, limit: 8, types: 'postcard,eddm,neighborhood' }, 'saved');
  };
  const postcards = pathOr([], ['data'], templatesData);

  return (
    <React.Fragment>
      <div
        className={`${myPostcardIsLoading
          ? 'disabled postcards-preview-container'
          : 'postcards-preview-container'}`}
      >
        {postcards.length ? (
          postcards.map((template, i) => (
            <div key={template._id} className='postcards-preview-item'>
              <div
                className='postcards-preview-content'
                onClick={e => {
                  // props.resetPostCardTemplateBackKey();
                  // props.addErrors('template', '');                  
                  onSelectTemplate(i, template);
                }}
              >
                <div>
                  <img                    
                    src={
                      template.images&&template.images.front.mini 
                    }
                    alt='template'
                  />
                </div>

                {indexSelectedPostcard !== null &&
                postcards[indexSelectedPostcard]._id === template._id ? (
                  <div className='template-checked' />
                ) : (
                  ''
                )}
              </div>
              <div className='postcards-preview-name-block'>
                <span className="name-text">{template.name}</span>
                {deleteProp && (
                  <DeleteIcon
                    className="del-icon"
                    onClick={() => {                      
                      props.toggleDeleteTemplateModal();
                    }}
                  />
                )}
              </div>
            </div>
          ))
        ) : (
          ''
        )}

        {!myPostcardIsLoading && !postcards.length ? (
          <div className='no-templates-container'>No Templates</div>
        ) : (
          ''
        )}
      </div>
      {postcards.length ? (
        <Pagination
          className='dope-pagination'
          current={templatesData.page + 1}
          defaultPageSize={templatesData.limit}
          total={templatesData.totalSize}
          onChange={onPageChange}
          locale={localeInfo}
          prevIcon={<PrevIco />}
          nextIcon={<NextIco />}
          jumpPrevIcon={<span>...</span>}
          jumpNextIcon={<span>...</span>}
          showTitle={false}
        />
      ) : (
        ''
      )}
      {myPostcardIsLoading && (
        <div className='loader-main'>
          <FadeLoader size={100} color={'#403d39'} />
        </div>
      )}
    </React.Fragment>
  );
}
