import React from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import JobsTable from 'components/JobsTable';
import * as jobsActions from '../../actions/jobs';
import JobsModal from 'components/JobsModal';
// import FilterPanel from 'components/FilterPanel';
import {setOrderSubmissionStatus} from 'actions';
import OrderBlock from './OrderBlock';

class Jobs extends React.Component {
  state = {
    isModalOpened: false,
    modalData: {},
    generalSearch: {
      startDate: null,
      endDate: null,
      jobName: '',
      minPrice: 0,
      maxPrice: 0
    },
    sort: null
  };

  componentDidMount() {
    this.updateData();
  }
  componentDidUpdate(prevProps) {   
    if (this.props.currentAccount._id !== prevProps.currentAccount._id) {			
      this.updateData();
    }
  }

  componentWillUnmount() {
    this.props.resetJobs();
  }

  getInitialState = () => {
    return {
      startDate: null,
      endDate: null,
      jobName: '',
      minPrice: 0,
      maxPrice: 0
    };
  };

  // handleFilterChange = (name, value) => {
  //     let generalSearch = this.state.generalSearch;

  //     this.setState({
  //         generalSearch: Object.assign(generalSearch, { [name]: value })
  //     });
  // };

  // performFilter = () => {
  //     this.updateData();
  // };

  // clearFilter = () => {
  //     this.setState({
  //         generalSearch: this.getInitialState()
  //     }, () => this.updateData());
  // };

  updateData = (pagination = {
    limit: 10,
    page: 1
  }) => {
    const {generalSearch} = this.state;

    let filter = {};
    Object.keys(generalSearch).forEach(key => generalSearch[key] && Object.assign(filter, {[key]: generalSearch[key]}));
    let data = {
      account: this.props.currentAccount._id,
      ...pagination,
      page: (pagination.page - 1)
    };
    if (!pagination.sort) {
      delete data.sort
    }
    this.props.createJobsRequest(data);
  };

  onPageChange = (pagination) => {
    if (pagination.sort) 
      this.setState({sort: pagination.sort})
     else if (this.state.sort) 
      pagination.sort = this.state.sort;
    
    this.updateData(pagination)
  }

  

  toggleModal = () => {
    this.setState({modalData: {}, isModalOpened: false});
  };

  onDownloadPdf = (job) => {
    const data = {
      id: job.id,
      params: {
        account: this.props.currentAccount._id
      }
    }
    this.props.createPreviewPdfRequest(data)
	}
	
	onDetailsClicked = (job) => {		
    this.setState({modalData: job, isModalOpened: true})
  };

  render() {
    const {
      page,
      pages,
      totalSize,
      isLoading,
      data
		} = this.props.jobs;
    let paginationOptions = {
      onPageChange: this.onPageChange,
      page: page + 1,
      paginationSize: pages,
      totalSize: totalSize
    }

    return (
      <div className='content order-container'>
        <div>
          <OrderBlock/>
          <JobsTable isLoading={isLoading}
            jobs={data}
            paginationOptions={paginationOptions}
            onDetailsClicked={
              this.onDetailsClicked
            }
            currentAccount={
              this.props.currentAccount
            }
            onDownloadPdf={
              this.onDownloadPdf
            }/>
          <JobsModal toggle={
              this.toggleModal
            }
            open={
              this.state.isModalOpened
            }
            data={
              this.state.modalData
            }/>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({currentAccount: state.account.currentAccount, configs: state.dbConfigs.configs, orderStatus: state.job.orderStatus, jobs: state.jobs});

const mapDispatchToProps = {
  setOrderSubmissionStatus,
  ...jobsActions
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Jobs));

