import React from 'react';
import { ModalFooter } from 'reactstrap';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModal } from 'components/DopeModal/DopeModal';

import './QuarterlyPaymentModal.scss';

export default function QuarterlyPaymentModal(props) {

  const toggle = () => {
    props.toggle();
  };

  const onClose = () => {
    // console.log("Quarterly payment recharge canceled"); 
    props.toggle();       
  };  

  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <DopeModal
        className='QuarterlyPaymentModal'
        isOpen={props.open}
        toggle={toggle}
        centered               
      >
        <DopeModalHeader toggle={onClose}>
          Payment Failed
        </DopeModalHeader>
        <DopeModalBody>          
          Quarterly payment for the selected package failed. Please update your payment method.
        </DopeModalBody>
        <ModalFooter>
          <DopeButton color='white' onClick={toggle}>
            I'll do it later
          </DopeButton>
          <DopeButton
            color='primary'            
            onClick={props.onQuarterlyRecharge}
          >
            Yes, recharge
          </DopeButton>
        </ModalFooter>
      </DopeModal>
    </div>
  );
}
