import { address1, city, state, zip, name, firstName, lastName, phone} from 'utils/ajvProperties';

export const listSchema1 = { 
  type: 'object',
  required: ['name', 'phone'],
  properties: {
    ...name,
    ...phone,
    companyAddress: {
      type: 'object',
      properties: {
        ...address1, ...city, ...state, ...zip,
      },
      required: ['address1', 'city', 'state', 'zip',],
    },
    shippingAddress: {
      type: 'object',
      required: ['address1', 'city', 'state', 'zip',],
      properties: {
        ...address1, ...city, ...state, ...zip,
      },
    },
  },
};

