import React from 'react';
import { 
  // CardElement,
  injectStripe,
  CardNumberElement,
  CardExpiryElement,
  CardCVCElement,
} from 'react-stripe-elements';
import LogRocket from 'logrocket';
import {setAccount, setDefaultSource, getAccountRequest} from 'actions/account';
import request from 'utils/request';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { API_URL } from '../config/config';
import { SyncLoader } from 'react-spinners';
import { toast } from 'react-toastify';
import {pathOr} from 'ramda';
// import { submitNewCard} from 'actions/general';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { ReactComponent as SelectIcon } from 'assets/img/icons/select-grey.svg';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import './Cards.scss';

const errorText = 'Error. Please check your credit card amount and other credentials or contact with the admin.'

const CardIcon = (props) => {
  let cardIcon = '';  
  
  if (props.type === 'Visa') {
    cardIcon = 'visa-ico';
  } else if (props.type === 'MasterCard') {
    cardIcon = 'master-card-ico';
  } else if (props.type === 'American Express') {
    cardIcon = 'american-express-ico';
  } else if (props.type === 'Discover'){
    cardIcon = 'discover-ico';
  } else if (props.type === 'Diners Club'){
    cardIcon = 'diners-club-ico';
  } else if (props.type === 'JCB'){
    cardIcon = 'jcb-ico';
  } else if (props.type === 'UnionPay'){
    cardIcon = 'unionpay-ico';
  } else cardIcon = 'unknown-ico';
  return <div className={cardIcon}></div>;  
};

const CardInfo = (props) => {
  return <div style={{ marginLeft: 10 }}>{props.data}</div>;
};

const createOptions = () => {
  return {
    style: {     
      base: {
        fontSize: '14px',
        color: '#354052',
        // fontFamily: 'IBMPlexSans', 
        // fontFamily: 'sans-serif',
        lineHeight: '1.1',
        letterSpacing: '0.025em',
        '::placeholder': {
          color: '#9A9A9A',
          lineHeight: '1.1',
        },
      },
      invalid: {
        color: '#d52330',
      },
    }
  }
};


const Cards = injectStripe(
  class extends React.Component {
    state = {
      addNewCard: false,
      data: null,
      loadingNewCard: false,
    };

    componentDidMount() {
      if(this.props.currentAccount) this.getCards();
    }

    componentDidUpdate(prevProps) {
      if(prevProps.currentAccount && prevProps.currentAccount._id !== this.props.currentAccount._id) this.getCards();
      if(prevProps.currentAccount && prevProps.currentAccount.cards && prevProps.currentAccount.cards.length !== ( this.props.currentAccount.cards && this.props.currentAccount.cards.length ) ) this.getCards();        
    }

    getCards = async () => {
      try {
        let response = await request(`${API_URL}/get-cards`, {
          method: 'get',
          queries: {
            account: this.props.currentAccount._id
          }
        });        
        let data = response.data.map((elm) => {
          return {
            customer: elm.customer,
            id: elm.id,
            info: `**${elm.last4}`,
            type: elm.brand || 'Unrecognized',
            expirationDate: `${elm.expMonth} / ${elm.expYear}`
          };
        });
        
        // if(!this.props.currentAccount.default_source && this.props.order && data && data.length > 0){ 
        //   //   this.props.onSelect(data[0]);           
        //   this.props.setDefaultSource(data[0].id);         
        // }
        
        // for onboarding
        if(!this.props.currentAccount.default_source && this.props.order && data && data.length === 1){ 
          //   this.props.onSelect(data[0]);           
          this.props.setDefaultSource(data[0].id);         
        }        

        this.props.setAccount('cards', data)
        this.setState({ data });
      } catch (err) { 
        LogRocket.captureException(err);       
        console.log(err);
      }
    };    

    onSubmitNewCard = async () => {      
      this.setState({ loadingNewCard: true });       
      let { token } = await this.props.stripe.createToken();       
      try {
        await request(`${API_URL}/save-new-card`, {
          method: 'post',
          body: {
            tokenId: token.id
          },
          queries: {
            account: this.props.currentAccount._id
          }
        }); 
        //?? adding new cards automatically make this card default_source + on backend too  
        this.getCards();
        this.props.setDefaultSource(token.card.id); //!!!!not token.id
      } catch (err) { 
        LogRocket.captureException(err);        
        const errorMessage = pathOr(errorText, ['response', 'data', 'error', 'dopeMessage'], err);
        toast.error(errorMessage)        
        // toast.error(err.message);
        // console.log(err);       
      }                 
      this.setState({ addNewCard: false });  
      this.setState({ loadingNewCard: false });     
    };

    render() {            
      let { selected } = this.props;           
      if(!selected) selected = this.props.currentAccount.default_source;  //!!!for postcard & yardsign
      return (
        <React.Fragment>
          <table>
            <tbody>
              {this.state.data &&
                this.state.data.map((card) => (
                  <tr
                    key={card.id}
                    className={
                      selected === card.id ? 'selected-card card-row' : 'card-row'
                    }
                    style={{ minHeight: '50px'}}
                    onClick={() => {this.props.onSelect && this.props.onSelect(card)}}
                  >
                    { !this.props.canDeleteCard ? (
                      <td style={{
                        flex: "0 0 24px",
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        margin: "5px 0 5px 0",
                        padding: "0" }}
                      >
                        { selected === card.id ?(
                          <div>                        
                            <SelectIcon 
                              className="select-icon"
                            />
                          </div>
                        ):
                          null
                        }
                      </td>
                      ):
                      null
                    }
                    <td style={{flex: "1 1 auto"}}>
                      <div
                        style={{
                          display: 'flex',                          
                          alignItems: 'center',
                          margin: 5
                        }}
                      >
                        <CardIcon type={card.type} />
                        <CardInfo data={card.info} />
                      </div>
                    </td>
                    <td style={{flex: "0 0 auto"}}>
                      <div style={{ margin: 5 }}>
                        {card.expirationDate}
                      </div>
                    </td>
                    { this.props.canDeleteCard ? (
                      <td style={{
                        flex: "0 0 26px",
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        margin: "5px 10px 5px 5px"}}
                      >
                        { selected === card.id ?
                          <div>                        
                            <SelectIcon 
                              className="select-icon"
                            />
                          </div>
                        :(
                          <div className="del-icon-box">
                            <DeleteIcon
                              onClick={(e) => {                              
                                e.stopPropagation();                              
                                this.props.onDeleteCard(card);                                                                                           
                              }}
                              className="delete-icon"
                            />                                             
                          </div>
                        )
                        }                        
                      </td>
                    ):
                      null
                    }
                  </tr>
                ))}

              {this.state.addNewCard ? (
                <tr>
                  <td>
                    <div className={this.props.view ? `CardElement-container ${this.props.view}` : `CardElement-container`}>
                      {/* <CardElement 
                        hidePostalCode={ true }
                        {...createOptions()}
                      /> */}
                      <label className="CardElement-CN">
                        <span className="CardElTitle">
                          Card number&nbsp;<sup className="star">*</sup>
                        </span>
                        <div className="border-filed">
                          <CardNumberElement
                            {...createOptions()}                        
                          />
                        </div>
                      </label>
                      <div>
                        <label className="CardElement-ED">
                          <span className="CardElTitle">
                            Expiration date&nbsp;<sup className="star">*</sup>
                          </span>
                          <div className="border-filed">
                            <CardExpiryElement
                              {...createOptions()}                        
                            />
                          </div>
                        </label>
                        <label className="CardElement-CVC">
                          <span className="CardElTitle">
                            CVC&nbsp;<sup className="star">*</sup>
                          </span>
                          <div className="border-filed">
                            <CardCVCElement
                              {...createOptions()}
                            />
                          </div>
                        </label>
                      </div>
                    </div>                  
                  </td>
                  <td>                                        
                    <DopeButton color="primary" onClick={this.onSubmitNewCard} disabled={this.state.loadingNewCard} style={{position:"relative"}}>
                      Save card 
                      {this.state.loadingNewCard && (
                        <div className='loader-container' style={{ position:"absolute",top:"50%",left:"0","WebkitTransform":"translate(-150%, -50%)",transform:"translate(-150%,-50%)"}}>
                          <SyncLoader size={5}  color={'#403d39'}/>
                        </div>
                      )}                     
                    </DopeButton>                    
                  </td>
                </tr>
              ) : (
                <tr>
                  <td></td>                  
                  <td>                                  
                    <DopeButton
                      color="primary"
                      onClick={() =>
                        this.setState({
                          addNewCard: true
                        })
                      }
                    >
                      Add New Card
                    </DopeButton>                    
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </React.Fragment>
      );
    }
  }
);

const mapStateToProps = (state) => ({
  currentAccount: state.account.currentAccount
});

const mapDispatchToProps = {
  setAccount,
  setDefaultSource,
  getAccountRequest,
  // submitNewCard
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Cards));
