import React from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
// import './DeleteCardModal.scss';

export default function DeleteCardModal(props) {
  const onConfirm = () => {    
    props.onChange();
    props.onConfirm();
  }
  const onClose = () => {    
    props.onChange();
  }
  return (
    <React.Fragment>
      <DopeModal className='delete-card-modal' isOpen={props.modalShow} toggle={props.onChange} centered>      
        <DopeModalHeader className='dope-modal-header' toggle={onClose}>
          Delete Card
        </DopeModalHeader>  
        <DopeModalBody>
          <p>Are you sure you want to delete this card?</p>
        </DopeModalBody>
        <DopeModalFooter>
          <DopeButton color='white' onClick={onClose}>
            Cancel
          </DopeButton>
          <DopeButton color='primary' onClick={onConfirm}>
            Delete
          </DopeButton>
        </DopeModalFooter>
      </DopeModal>
    </React.Fragment>
  );
}
