import React, {useState, useEffect} from 'react';
import { useSelector } from 'react-redux';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModal } from 'components/DopeModal/DopeModal';
import Toggler from 'components/Toggler';

import ListsFilter from './ListsFilter';
import StatesFilter from './StatesFilter';
import CitiesFilter from './CitiesFilter';
import ZipFilter from './ZipFilter';

import ValidationFilter from './ValidationFilter';

import './FiltersModal.scss';

export default function FiltersModal(props) {  
  const filters = useSelector(state => state.contacts.filters); 
  const [modalFilters, setModalFilters] = useState(props.filters);
  const [inProcess, setInProcess] = useState(false);    

  useEffect(() => {    
    setModalFilters(filters);     
  },[filters]);

  const toggle = () => {    
    setModalFilters(filters); //for unsave changes without Apply button pressed       
    props.toggle();
  };

  const onClose = () => {  
    setInProcess(true);  
    setModalFilters(filters); //for unsave changes without Apply button pressed
    setTimeout(()=>setInProcess(false), 1000);
    props.toggle();
  };  
  
  const onClear = () => {    
    setInProcess(true);  
    setModalFilters({
      lists: [],
      states: [],
      cities: [],
      zipCodes: [],
      startDate: '',
      endData: '',
      validation: 'allContacts',
      isFiltered: false,
    });
    // dispatch(changeFilters({
    //   lists: [],
    //   states: [],
    //   cities: [],
    //   zipCodes: [],
    //   startDate: '',
    //   endData: '',
    //   validation: 'allContacts',
    //   isFiltered: false,
    // }));
    props.onClearFilters();
    setTimeout(()=>setInProcess(false), 500);
    props.toggle();
  }

  const onApplyFilters = () => {  
    setInProcess(true);          
    // let isFiltered=false;
    // if(
    //   modalFilters.lists.length>0||
    //   modalFilters.states.length>0||
    //   modalFilters.cities.length>0||
    //   modalFilters.zipCodes.length>0||
    //   modalFilters.startDate.length>0||
    //   modalFilters.endData.length>0||
    //   modalFilters.validation!=="allContacts"){
    //     isFiltered=true;
    //   }          
    // dispatch(changeFilters({
    //   ...modalFilters,
    //   isFiltered
    // }));
    props.onChangeFilters({
      ...modalFilters,
      // isFiltered
    });       
    setTimeout(()=>setInProcess(false), 500);
    props.toggle();
  }

  // const listsChangeFilter = (filter) => {
  //   props.onChangeFilters({...props.filters, lists: filter});
  // }
  // const statesChangeFilter = (filter) => {
  //   props.onChangeFilters({...props.filters, states: filter});
  // }
  // const citiesChangeFilter = (filter) => {
  //   props.onChangeFilters({...props.filters, cities: filter});
  // }
  // const zipCodesChangeFilter = (filter) => {
  //   props.onChangeFilters({...props.filters, zipCodes: filter});
  // }
  // const dateChangeFilter = () => {
    
  // }
  // const validationChangeFilter = () => {
    
  // }
  const listsChangeFilter = (filter) => {
    setModalFilters({...modalFilters, lists: filter});
  }
  const statesChangeFilter = (filter) => {
    setModalFilters({...modalFilters, states: filter});
  }
  const citiesChangeFilter = (filter) => {
    setModalFilters({...modalFilters, cities: filter});
  }
  const zipCodesChangeFilter = (filter) => {
    setModalFilters({...modalFilters, zipCodes: filter});
  }
  // const dateChangeFilter = () => {
  //   setModalFilters({...modalFilters});
  // }
  const validationChangeFilter = (value) => {
    setModalFilters({...modalFilters, validation: value});
  }
  
  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <DopeModal
        className='FiltersModal'
        isOpen={props.open}
        toggle={toggle}
        centered                
      >        
        <DopeModalHeader toggle={inProcess ? null : onClose}>
          Filters
        </DopeModalHeader>
        <DopeModalBody>
          <ul className="filters-list">
            <li className="filters-list-item">
              <Toggler label="Lists" isModified={modalFilters.lists.length !== 0}>
                <ListsFilter
                  filterLists={modalFilters.lists}
                  onChangeFilter={listsChangeFilter}
                />                 
              </Toggler>
            </li>
            <li className="filters-list-item">
              <Toggler label="States" isModified={modalFilters.states.length !== 0}>                    
                <StatesFilter
                  filterStates={modalFilters.states}
                  onChangeFilter={statesChangeFilter}
                />             
              </Toggler>
            </li>
            <li className="filters-list-item">
              <Toggler label="Cities" isModified={modalFilters.cities.length !== 0}>                
                <CitiesFilter
                  filterCities={modalFilters.cities}
                  onChangeFilter={citiesChangeFilter}
                />
              </Toggler>
            </li>
            <li className="filters-list-item">
              <Toggler label="ZIP" isModified={modalFilters.zipCodes.length !== 0}>
                <ZipFilter
                  filterZipCodes={modalFilters.zipCodes}
                  onChangeFilter={zipCodesChangeFilter}
                />               
              </Toggler>
            </li>
            {/* <li className="filters-list-item">
              <Toggler label="Created Date">                
                <UploadDateFilter
                  filterDate={modalFilters.startDate, filters.endData }
                  onChangeFilter={dateChangeFilter}
                />                
              </Toggler>
            </li> */}
            <li className="filters-list-item">
              <Toggler label="Validation" isModified={modalFilters.validation !== "allContacts"} >
                <ValidationFilter
                  filterValidation={modalFilters.validation}
                  onChangeFilter={validationChangeFilter}
                />
              </Toggler>
            </li>                    
          </ul>
        </DopeModalBody>   
        <DopeModalFooter>
          <DopeButton color='white' onClick={onClear} disabled={inProcess}>
            Clear All
          </DopeButton>
          <DopeButton color='primary' onClick={onApplyFilters} disabled={inProcess}>
            Apply filters
          </DopeButton>
        </DopeModalFooter>     
      </DopeModal>
    </div>
  );
}
