import React, { useState, useEffect } from 'react';
import { useParams, useHistory, Redirect } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { Card, CardHeader, CardBody, FormGroup } from 'reactstrap';
import { toast } from 'react-toastify';
import Builder from '../../../components/Builder/HtmlBuilder';
import { DopeInput, DopeButton } from 'components/DopeForms';
import { DopeDatePicker } from 'components/DopeForms/DopeDatePicker/DopeDatePicker';
import Select from 'react-select';
import ButtonsBlock from './ButtonsBlock';
import CreditCardsAfter from 'components/CreditCardsAfter';
import { ReactComponent as HelpIcon } from 'assets/img/icons/help.svg';
import ReactTooltip from 'react-tooltip';
import {
  setSendingTrackingNumber,
  setNewSendingData,
  createNewSendingRequest,
  updateSendingRequest,
  resetPostcardTemplatesDataCampaign,
  setSelectedBackKey,
  togglePostcardModal,
  togglePostcardApprovalModal,
  resetNewSendingPage,
  getSendingDataRequest,
  sendingRechargeRequest,
  setTabPostcardModal,
  resetCampaignsPage,
  getPostcardCampaignPreviewImgRequest,
} from 'actions/campaigns';
import {
  createTrackingNumbersRequest
} from 'actions/newTrackingNumber';
import { showMainLoader } from 'actions/general';

import { toggleTrackingNumberModal } from 'actions/newTrackingNumber';
import AddNumberModal from 'components/AddNumberModal';
import PostcardApprovalModal from './PostcardApprovalModal';
import { createExistingNumbersRequest } from 'actions/newTrackingNumber';
import { friendlyFormatPhoneNumber, fullNumberFormat } from 'utils/phones';
import { replacePhoneNumber } from 'utils/helper';
import {
  CreateListSchema,
  postcard_front_id,
  postcard_back_id,  
} from './config';
import { S3_BUCKET_PUBLIC_NAME, 
  REACT_APP_S3_ACCESS_KEY_ID, 
  REACT_APP_S3_SECRET_ACCESS_KEY,
} from 'config/config';
import { CAMPAIGNS_PATH } from 'utils/constants';
import BackSelector from '../../OrderPostcards/Editor/BackSelector';
import { ReactComponent as AttentionIcon } from 'assets/img/icons/attention.svg';
import Ajv from 'ajv';
import moment from 'moment';
import S3 from 'aws-sdk/clients/s3';
import './SendingPage.scss';

const ajv = new Ajv({ allErrors: true });

const s3Config = {
  bucketName: S3_BUCKET_PUBLIC_NAME,
  accessKeyId: REACT_APP_S3_ACCESS_KEY_ID,
  secretAccessKey: REACT_APP_S3_SECRET_ACCESS_KEY,
  region: 'us-east-1'
};
const s3Client = new S3(s3Config);

export default function SendingPage({savedAccountId, showAddingCard, setShowAddingCard}) {
  const [errors, setErrors] = useState({ phone: '' });
  const [renderedTemplate, setRenderedTemplate] = useState({});
  const [renderedStatus, setRenderedStatus] = useState("");

  const currentAccount = useSelector(state => state.account.currentAccount);
  const sendingData = useSelector(state => state.campaigns.sendingData);
  const sendingDataIsLoading = useSelector(
    state => state.campaigns.sendingDataIsLoading,
  );
  const newSendingData = useSelector(state => state.campaigns.newSendingData);
  const phoneNumberType = useSelector(state => state.campaigns.phoneNumberType);
  const editedTemplate = useSelector(state => state.campaigns.editedTemplate);
  const backMarkPlace = useSelector(state => state.campaigns.backMarkPlace);
  const postcardSize = useSelector(state => state.campaigns.postcardSize);
  const existingNumbers = useSelector(
    state => state.newTrackingNumber.existingNumbers,
  ); 
  const images = useSelector(state => state.campaigns.images);
  const account = useSelector(state => state.account);
  const currentAccountId = useSelector(state => state.account.currentAccount._id);

  const { campaignId, sendingId, typePreview } = useParams();

  const accountNumber = useSelector(state => state.account.currentAccount.phone);  

  const defaultSource = useSelector(state => state.account.currentAccount.default_source);  
  const showPostcardApproval = useSelector(state => state.campaigns.showPostcardApprovalModal);
  const [campaignSendData, setCampaignSendData] = useState({}); 
  const [afterAddingCardDataTrackingNumber, setAfterAddingCardDataTrackingNumber] = useState({});
  const [campaignAddingCardType, setCampaignAddingCardType] = useState(''); 
  
  const dispatch = useDispatch();
  const history = useHistory();    

  const [candidatesForDeleteFromAws, setCandidatesForDeleteFromAws] = useState([]);
  const [arrayImagesToDeleteFromAws, setArrayImagesToDeleteFromAws] = useState([]); 

  useEffect(() => {    
    dispatch(createExistingNumbersRequest(account.currentAccount._id));
    const phoneNumber = newSendingData.trackingNumber || newSendingData.ownNumber || ''; 
    replacePhoneNumber(phoneNumber);    
    return () => {
      dispatch(resetNewSendingPage());
    };
  }, []);  

  useEffect(() => {      
    if(phoneNumberType === 'ownNumber') {       
      let autoOwnNumber = newSendingData.ownNumber || accountNumber; 
      dispatch(setNewSendingData('ownNumber', autoOwnNumber ));
      replacePhoneNumber(autoOwnNumber);      
    }      
  }, [phoneNumberType]);  
  
  useEffect(() => {    
    if(phoneNumberType === 'trackingNumber') { 
      replacePhoneNumber(fullNumberFormat(newSendingData.trackingNumber));
    }    
  }, [existingNumbers]);  

  useEffect(() => {      
    if((savedAccountId !== currentAccountId) && (savedAccountId !== '')){
      dispatch(resetCampaignsPage());
      history.push('/campaigns');      
    }    
  }, [currentAccountId]);
  

  let viewPageType = null;
  if (sendingId === 'create') viewPageType = 'create';
  else if (sendingId && typePreview === 'edit') viewPageType = 'edit';
  else if (sendingId && typePreview === 'view') viewPageType = 'view';

  const formatedExistingNumbers = existingNumbers
    .filter(
      phone => {           
        return phone.orderStatus === 'reserved' ||
        phone.orderStatus === undefined
        || phone._id === ( sendingData.find( elem => (elem._id === sendingId) ) && sendingData.find( elem => (elem._id === sendingId) ).trackingNumber )
      }        
    )
    .map(item => ({
      value: item.trackingNumber,
      label: item,
    }));

  const formatOptionLabel = ({ label }) => (
    <div className='option-container'>
      <div>{label && label.name}</div>
      <div>{label && friendlyFormatPhoneNumber(label.trackingNumber)}</div>
    </div>
  );

  const restorePrevTrackingNumber = () => {
    let idTrackingNumber = sendingData.find( elem => (elem._id === sendingId) ) && sendingData.find( elem => (elem._id === sendingId) ).trackingNumber;    
    if (idTrackingNumber){
      let number = existingNumbers.find((elem)=>(elem._id === idTrackingNumber)) ? fullNumberFormat(existingNumbers.find((elem)=>(elem._id === idTrackingNumber)).trackingNumber) : '';           
      return number;
    }
    return '';
  }

  const getChangedTemplate = (templateStatus,successCallbeck) => {    
    if((templateStatus==='confirmed')&&checkDefaultDomImages()) {
      return;
    }
    dispatch(showMainLoader(true));
    let domFront = document.getElementById(postcard_front_id);
    let domBack = document.getElementById(postcard_back_id);    
    if(domFront&&domBack){
      let frontArray = [...domFront.querySelectorAll('img[data-image-changeable][src*="postcard-image"]')].map((el)=>el.src);
      let backArray = [...domBack.querySelectorAll('img[data-image-changeable][src*="postcard-image"]')].map((el)=>el.src);      
      setCandidatesForDeleteFromAws([...candidatesForDeleteFromAws,...frontArray,...backArray]);      
    }
    saveImagesToAws().then(()=>{      
      const newFront = `<html><body style="${calcTemplateBodyStyle(postcardSize)} -webkit-box-sizing: border-box; box-sizing: border-box;">${document.getElementById(postcard_front_id).parentElement.innerHTML}</body></html>`;
      const newBack = `<html><body style="${calcTemplateBodyStyle(postcardSize)} -webkit-box-sizing: border-box; box-sizing: border-box;">${document.getElementById(postcard_back_id).parentElement.innerHTML}</body></html>`;
      const data = {
        body: {
          template:{
            front: newFront,
            back: newBack,
          },
          templateStatus,
          bucket: 'postcardImages'
        },        
      };      
      setArrayImagesToDeleteFromAws(prepareArrayImagesToDeleteFromAws());
      dispatch(getPostcardCampaignPreviewImgRequest(data,successCallbeck));
    });
  };

  const createValidation = () => {
    const phoneNumber =
      phoneNumberType === 'trackingNumber'
        ? newSendingData.trackingNumber
        : newSendingData.ownNumber;
    const formattedForBE = {
      phone: phoneNumber,
    };
    const result = ajv.validate(CreateListSchema, formattedForBE);
    if (!result) {
      ajv.errors.map(error => {
        const errorKey = error.dataPath.split('.').pop();
        setErrors({ [errorKey]: 'Tracking number cant be blank' });
        return error;
      });
      window.scrollTo({ top: 0, behavior: 'smooth' });
      return false;
    }
    return true;
  };

  
  const onDraftCreate = (status) => {
    if (!createValidation()) return;    
    getChangedTemplate(status,onDraftCreatePrevSuccessCallbeck);
  }
  const onDraftCreatePrevSuccessCallbeck = (images) => {
    const front = document.getElementById(postcard_front_id).querySelector('.morpheus-card-body').outerHTML;
    const back = document.getElementById(postcard_back_id).querySelector('.morpheus-card-body').outerHTML;
    setRenderedTemplate( {front, back} );
    const data = {
      body: {
        ...newSendingData,
        template: {
          front,
          back,
        },
        status: 'draft',
        images,
      },
      params: {
        campaign: campaignId,
      },
      query: {
        account: account.currentAccount._id,
        campaign: campaignId,
      },
    };    
    dispatch(createNewSendingRequest(data));    
  };

  const onCreate = (status) => {
    if (!createValidation()) return;
    setRenderedStatus( status );
    getChangedTemplate(status,onCreatePrevSuccessCallbeck);    
  };
  const onCreatePrevSuccessCallbeck = () => {
    const front = document.getElementById(postcard_front_id).querySelector('.morpheus-card-body').outerHTML;
    const back = document.getElementById(postcard_back_id).querySelector('.morpheus-card-body').outerHTML;
    setRenderedTemplate( {front, back} );    
    dispatch(showMainLoader(false));  
    openPostcardApprovalModal();    
  };
  const onApprovalCreate = () => {     
    // callbackForDeleteImagesFromAws();     
    const data = {
      body: {
        ...newSendingData,        
        template: renderedTemplate,
        status: renderedStatus,
        images,
      },
      params: {
        campaign: campaignId,
      },
      query: {
        account: account.currentAccount._id,
        campaign: campaignId,
      },
    };
    if(!defaultSource) {     
      setCampaignSendData(data); 
      setCampaignAddingCardType('sending');     
      setShowAddingCard(true);
    }
    else {       
      dispatch(togglePostcardApprovalModal(false));
      dispatch(showMainLoader(true));
      dispatch(createNewSendingRequest(data));    
    }    
  };  

  const afterAddCreditCardSendCampaign = () => {   
    dispatch(togglePostcardApprovalModal(false));
    dispatch(showMainLoader(true));
    dispatch(createNewSendingRequest(campaignSendData)); 
  };

  const setShowAddingCardTrackingNumber = (valueBoolean, afterAddingCardDataTrackingNumber={}) => {
    setCampaignAddingCardType('trackingNumber');
    setShowAddingCard(true);
    setAfterAddingCardDataTrackingNumber(afterAddingCardDataTrackingNumber);   
  }  

  const afterAddCreditCardTrackingNumberCampaign = () => {
    setShowAddingCard(false);
    dispatch(createTrackingNumbersRequest(afterAddingCardDataTrackingNumber));
  };


  
  const onDraftEdit = (status) => {
    if (!createValidation()) return;    
    getChangedTemplate(status,onDraftEditPrevSuccessCallbeck); 
  }

  const onDraftEditPrevSuccessCallbeck = (images) => {
    const front = document.getElementById(postcard_front_id).querySelector('.morpheus-card-body').outerHTML;
    const back = document.getElementById(postcard_back_id).querySelector('.morpheus-card-body').outerHTML;      
    // callbackForDeleteImagesFromAws();   
    const data = {
      body: {
        ...newSendingData,
        template: {
          front,
          back,
        },       
        status: 'draft',
        images,
      },
      params: {
        sending: sendingId,
      },
      query: {
        account: account.currentAccount._id,
        campaign: campaignId,
      },
      campaignId,
    };
    if (newSendingData.ownNumber ) delete data.body.trackingNumber;
    dispatch(updateSendingRequest(data));    
  };

  const onEdit = (status) => {
    if (!createValidation()) return;    
    setRenderedStatus( status );    
    getChangedTemplate(status,onEditPrevSuccessCallbeck);
  }

  const onEditPrevSuccessCallbeck = () => {
    const front = document.getElementById(postcard_front_id).querySelector('.morpheus-card-body').outerHTML;
    const back = document.getElementById(postcard_back_id).querySelector('.morpheus-card-body').outerHTML; 
    setRenderedTemplate( {front, back} );   
    dispatch(showMainLoader(false));  
    openPostcardApprovalModal();
  };

  const onApprovalEdit = () => {    
    dispatch(togglePostcardApprovalModal(false));    
    dispatch(showMainLoader(true));    
    // callbackForDeleteImagesFromAws();
    const data = {
      body: {
        ...newSendingData,
        template: renderedTemplate,
        status: renderedStatus,
        images,
      },
      params: {
        sending: sendingId,
      },
      query: {
        account: account.currentAccount._id,
        campaign: campaignId,
      },
      campaignId,
    };
    if (newSendingData.ownNumber) delete data.body.trackingNumber;     
    dispatch(updateSendingRequest(data));    
  };


  const setTrackingNumber = number => {
    dispatch(setNewSendingData('trackingNumber', fullNumberFormat(number)));

    setErrors({ phone: '' });
  };

  const onClose = () => {
    dispatch(resetPostcardTemplatesDataCampaign());
    dispatch(setTabPostcardModal(1));
    history.push(`${CAMPAIGNS_PATH}/${campaignId}`);
  };

  const backsRender = template => {
    let back = (template && template.back) || '';

    if (!back) {
      if (template && template.backs && template.backs.length > 0) {
        if (template.backs[template.selectedBackKey]) {
          back = template.backs[template.selectedBackKey].back;
        } else {
          back = template.backs[0].back;
        }
      }
    }
    return back;
  };

  const onSelectTemplateBack = back => {
    dispatch(setSelectedBackKey(back));
  };

  const getSendingsData = ({ startDate, endDate }) => {
    const data = {
      query: {
        startSendingDate: startDate,
        endSendingDate: endDate,
        account: currentAccount._id,
        campaign: campaignId,
      },
    };

    dispatch(getSendingDataRequest(data));
  };

  const selectedDates = [
    {
      'dope-date-picker-disabled-day': sendingData.map(item =>
        Date.parse(item.sendingDate),
      ),
    },
  ];

  const calendarDate = newSendingData
    ? Date.parse(newSendingData.sendingDate)
    : new Date();

  const onRecharge = () => {
    const data = {
      query: {
        account: currentAccount._id,
      },
      params: {
        sending: sendingId,
      },
      campaign: campaignId,
    };
    dispatch(sendingRechargeRequest(data));
  };

  const openPostcardApprovalModal = () => {    
    dispatch(togglePostcardApprovalModal(true));    
  };

  const togglePostcardApproval = () => {       
    dispatch(togglePostcardApprovalModal(false));
  };

  

  const dataURLtoFile = (dataurl, filename) => {
    let arr = dataurl.split(','),
      mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);
    while(n--){
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], filename, {type:mime});
  };

  const uploadImage = (Key, Body) => {
    const upload = s3Client.putObject({
      Bucket: `${s3Config.bucketName}/postcardImages/${currentAccountId}`,
      Key,
      Body,
      ACL: "public-read"
    });
    return upload.promise();
  };
  // const deleteImage = (Key) => {
  //   const deleted = s3Client.deleteObject({
  //     Bucket: `${s3Config.bucketName}/postcardImages/${currentAccountId}`,
  //     Key
  //   });
  //   return deleted.promise();
  // };

  // const deleteConvertImage = (Key) => {
  //   const deleted = s3Client.deleteObject({
  //     Bucket: `${s3Config.bucketName}/postcardImages`,
  //     Key
  //   });
  //   return deleted.promise();
  // };

  const saveImagesToAws = async () => {    
    let frontDomImages = document.getElementById(postcard_front_id).querySelectorAll('img[data-image-changeable][src^="data:image"]');
    await Promise.all([...frontDomImages].map(async(el)=>{
      const filename = `postcard-image-${currentAccountId}-${new Date().getTime()}.${el.src.split(',')[0].match(/:(.*?);/)[1].split('/')[1]}`;
      await uploadImage(filename, dataURLtoFile(el.src, filename));
      el.src = `https://${s3Config.bucketName}.s3.amazonaws.com/postcardImages/${currentAccountId}/${filename}`;
    } ) );
    // save to aws back images
    let backDomImages = document.getElementById(postcard_back_id).querySelectorAll('img[data-image-changeable][src^="data:image"]');
    await Promise.all([...backDomImages].map(async(el)=>{
      const filename = `postcard-image-${currentAccountId}-${new Date().getTime()}.${el.src.split(',')[0].match(/:(.*?);/)[1].split('/')[1]}`;
      await uploadImage(filename, dataURLtoFile(el.src, filename));
      el.src = `https://${s3Config.bucketName}.s3.amazonaws.com/postcardImages/${currentAccountId}/${filename}`;
    } ) );
  }
  const prepareArrayImagesToDeleteFromAws = () => {    
    let allFinishImagesSRCFront = [...document.getElementById(postcard_front_id).querySelectorAll('img[data-image-changeable]')].map((el)=>el.src);
    let allFinishImagesSRCBack = [...document.getElementById(postcard_back_id).querySelectorAll('img[data-image-changeable]')].map((el)=>el.src);
    let allFinishImages = [...allFinishImagesSRCFront,...allFinishImagesSRCBack];
    let toDeleteArray = candidatesForDeleteFromAws.filter((el)=>!allFinishImages.includes(el));
    // console.log("Will be deleted after successfully sent to back:", toDeleteArray);
    return toDeleteArray;
  }

  const checkDefaultDomImages = ()=>{
    let checkDefDomImagesFront = document.getElementById(postcard_front_id).querySelectorAll('.morpheus-card-body img[data-image-changeable][src*="/templateimages/"]');
    let checkDefDomImagesBack = document.getElementById(postcard_back_id).querySelectorAll('.morpheus-card-body img[data-image-changeable][src*="/templateimages/"]');
    if(checkDefDomImagesFront.length||checkDefDomImagesBack.length){
      toast.error("You can't send the default image to print. Please, replace the example image.")
      return true;
    } 
    return false;  
  }

  const calcTemplateBodyStyle = (postcardSize) => {
    switch(postcardSize){
      case '6x9':        
        return 'width: 2775px; height: 1875px;'    //LOB bleed
        // return 'width: 2700px; height: 1800px;'            
      default:
        return 'width: 2775px; height: 1875px;'    //LOB bleed
        // return 'width: 2700px; height: 1800px;'
    }    
  }

  // const callbackForDeleteImagesFromAws = async () => {
  //   // console.log("Array images to delete:",arrayImagesToDeleteFromAws);
  //   let toDeleteArray = arrayImagesToDeleteFromAws;
  //   if(toDeleteArray.length){
  //     // console.log("Will be deleted after successfully sent approving to back:", toDeleteArray);
  //     await Promise.all([...toDeleteArray].map(async(el)=>{        
  //       await deleteImage(el.split('/').pop());
  //     } ) );
  //   }
  // }



  if (!editedTemplate)
    return <Redirect to={`${CAMPAIGNS_PATH}/${campaignId}`} />;
  else
    return (
      !showAddingCard ? (
        <div className='sending-page-container'>          
          <div className='dope-page-title'>
            <h2>
              {viewPageType === 'create' ? (
                'New Sending'
              ) : viewPageType === 'edit' ? (
                'Edit sending'
              ) : (
                'Sending'
              )}
            </h2>
            {viewPageType === 'edit' || viewPageType === 'view' ? (
              <div className='status-block'>
                <span className='status-text'>Status</span>
                <span
                  className={`sendings-status ${newSendingData.status}-block `}
                >
                  {newSendingData.status}
                </span>
              </div>
            ) : (
              <p>
                Choose the phone number to use on the postcard and customize the
                postcard to add a new sending to the campaign.
              </p>
            )}
          </div>
          <div className='sending-page-content'>
            {newSendingData.status === 'payment-failed' && (
              <Card className='dope-card sending-recharge-card'>
                <CardBody>
                  <div className='sending-recharge-block'>
                    <AttentionIcon />
                    <p>
                      Your payment did not go through. Please recharge your credit
                      card or update your payment details, and click Recharge.
                    </p>
                    <DopeButton color='primary' onClick={onRecharge}>
                      Recharge
                    </DopeButton>
                  </div>
                </CardBody>
              </Card>
            )}

            <Card className='dope-card sending-details-card'>
              <CardHeader className='dope-card-header'>General</CardHeader>
              <CardBody>
                <div className='sending-details-left'>
                  <div className='sending-details-radio-btn'>
                    <label className='dope-radio-container'>
                      <input
                        disabled={typePreview === 'view'}
                        type='checkbox'
                        onChange={() => {     
                          replacePhoneNumber( restorePrevTrackingNumber() );                   
                          dispatch(setSendingTrackingNumber({type: 'trackingNumber', trackingNumber: restorePrevTrackingNumber() }));
                          setErrors({ phone: '' });                        
                        }}
                        checked={phoneNumberType === 'trackingNumber'}
                      />
                      <span className='checkmark' />
                      Tracking phone number
                    </label>
                    <label className='dope-radio-container'>
                      <input
                        type='checkbox'
                        disabled={typePreview === 'view'}
                        onChange={() => {
                          dispatch(setSendingTrackingNumber({type: 'ownNumber', trackingNumber: ''}));
                          setErrors({ phone: '' });
                        }}
                        checked={phoneNumberType === 'ownNumber'}
                      />
                      <span className='checkmark' />
                      Use my phone number
                    </label>
                  </div>

                  {phoneNumberType === 'trackingNumber' ? (
                    <FormGroup
                      className={`sending-tracking-number-container ${errors.phone
                        ? 'select-error'
                        : ''}`}
                    >
                      <Select
                        className='dope-select'
                        classNamePrefix='dope'
                        isDisabled={typePreview === 'view'}
                        placeholder='Select an existing tracking'                        
                        formatOptionLabel={formatOptionLabel}
                        options={formatedExistingNumbers}
                        value={formatedExistingNumbers.filter(
                          item =>
                            fullNumberFormat(item.value) ===
                            fullNumberFormat(newSendingData.trackingNumber),
                        )}
                        onChange={e => {
                          replacePhoneNumber(e.value);                        
                          dispatch(
                            setNewSendingData(
                              'trackingNumber',
                              fullNumberFormat(e.value),
                            ),
                          );
                          setErrors({ phone: '' });
                        }}
                      />

                      <div className='validation-message'>{errors.phone}</div>
                    </FormGroup>
                  ) : (
                    <FormGroup className='postcard-other-number-container dope-form-group'>
                      <DopeInput
                        name='ownNumber'
                        id='ownNumber'
                        value={newSendingData.ownNumber.slice(2)}
                        mask='(999)999-9999'
                        placeholder='(___)___-____'
                        disabled={typePreview === 'view'}
                        onChange={e => {
                          replacePhoneNumber(e.target.value);
                          dispatch(
                            setNewSendingData(
                              'ownNumber',
                              fullNumberFormat(e.target.value),
                            ),
                          );
                          setErrors({ phone: '' });
                        }}
                        error={errors.phone}
                      />
                      {viewPageType !== 'view' ? (
                        <React.Fragment>
                          <HelpIcon className="help-own-number-icon" data-tip data-for="helpOwnNumberTooltip" data-place="right" />	
                          <ReactTooltip          
                            className="help-own-number-tooltip"  												       
                            html={true} 
                            id="helpOwnNumberTooltip"
                          >                              
                            {
                              `<span>You cannot track any numbers except for tracking numbers.</span>
                              <span>We added your profile number automatically,</span>
                              <span>you can change this number if you want.</span>`
                            }
                          </ReactTooltip>	                        
                        </React.Fragment>
                        ) : null
                      }
                    </FormGroup>
                  )}

                  <div
                    className={`sending-details-add-tracking-number ${viewPageType ===
                    'view'
                      ? 'disabled-block'
                      : ''}`}
                  >
                    <span className='add-tracking-number-text'>
                      Need more tracking numbers?
                    </span>
                    <span
                      className='add-tracking-number-link'
                      onClick={() => {
                        dispatch(setSendingTrackingNumber({type: 'trackingNumber', trackingNumber: ''}));
                        dispatch(toggleTrackingNumberModal(true))
                      }}
                    >
                      Add Tracking Number
                    </span>
                  </div>

                  <FormGroup className='postcard-other-number-container'>
                    <DopeDatePicker
                      selected={calendarDate}
                      dateFormat='MMM d, yyyy'
                      minDate={new Date()}
                      highlightDates={selectedDates}
                      onMonthChange={getSendingsData}
                      sendingDataIsLoading={sendingDataIsLoading}
                      disabled={typePreview === 'view'}
                      useWeekdaysShort={true}
                      onChange={date => {
                        dispatch(setNewSendingData('sendingDate', moment(date).format('L')));
                      }}
                    />
                  </FormGroup>
                </div>
                <div className='sending-details-right'>
                  <h6 className='sending-template-preview-header'>
                    Postcard Template <b>{viewPageType}</b>
                  </h6>
                  <div className='sending-details-content'>
                    <div className='sending-template-preview-img'>                      
                      <img
                        src={ 
                          viewPageType === 'create' ? (
                            editedTemplate.front_thumbnail ? 
                              editedTemplate.front_thumbnail 
                              :
                              editedTemplate.images&&editedTemplate.images.front ? 
                                editedTemplate.images.front.mini 
                                :
                                editedTemplate.previewUrls ?
                                  editedTemplate.previewUrls.frontMini 
                                  :
                                  `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcard-mini-front-preview-${editedTemplate._id}`                                  
                          ) : (
                            ( viewPageType === 'edit' || viewPageType === 'view') ? (
                              editedTemplate.images&&editedTemplate.images.front ? 
                                editedTemplate.images.front.mini 
                                :
                                editedTemplate.previewUrls ? 
                                  editedTemplate.previewUrls.frontMini 
                                  :
                                  editedTemplate.front_thumbnail ? 
                                    editedTemplate.front_thumbnail 
                                    : 
                                    editedTemplate.thumbnailMiniFront ? 
                                      editedTemplate.thumbnailMiniFront 
                                    : 
                                    `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcard-sending-mini-front-preview-${sendingId}`
                                
                            ): null
                          )                        
                        }
                        alt='template preview'
                      />                                      
                    </div>
                    <div>
                      <DopeButton
                        color='white'
                        onClick={
                          () => {
                            dispatch(resetPostcardTemplatesDataCampaign());
                            dispatch(setTabPostcardModal(1));
                            dispatch(togglePostcardModal(true))
                          }                                                
                        }
                        disabled={typePreview === 'view'}
                      >
                        Change Template
                      </DopeButton>
                    </div>
                  </div>
                </div>
              </CardBody>
            </Card>
            <Card
              className={`dope-card campaign-name-card ${viewPageType === 'view'
                ? 'disabled-dope-card'
                : ''}`}
            >
              <CardHeader className='dope-card-header'>
                Front Side of Postcard
              </CardHeader>
              <CardBody>
                <Builder
                  disabled={viewPageType === 'view'}
                  template={editedTemplate.front || ''}
                  changePostCardStatus={() => {}}
                  awsPath={'postcard'}
                  currentAccountId={currentAccountId}
                  templateSample={editedTemplate.front || ''}
                  phoneNumber={''}
                  phoneNumberForReset={
                    newSendingData.trackingNumber || newSendingData.ownNumber
                  }
                  side="front"
                  postcardSize={postcardSize}   //'6x9' One size only string incoming from ReduxState
                  postcardDpi={300}                  
                  postcardBleed={0.25}                                   
                />
              </CardBody>
            </Card>
            <Card
              className={`dope-card campaign-name-card ${viewPageType === 'view'
                ? 'disabled-dope-card'
                : ''}`}
            >
              <CardHeader className='dope-card-header'>
                Back Side of Postcard
              </CardHeader>
              {editedTemplate.backs && editedTemplate.backs.length > 1 ? (
                <div className='back-selector-container'>
                  <BackSelector
                    backs={editedTemplate.backs}
                    selectedBackKey={editedTemplate.selectedBackKey}
                    onSelectTemplateBack={onSelectTemplateBack}
                  />
                </div>
              ) : null}
              <CardBody>
                <Builder
                  disabled={viewPageType === 'view'}
                  template={backsRender(editedTemplate) || ''}
                  changePostCardStatus={() => {}}
                  awsPath={'postcard'}
                  currentAccountId={currentAccountId}
                  templateSample={backsRender(editedTemplate) || ''}
                  phoneNumber={''}
                  phoneNumberForReset={
                    newSendingData.trackingNumber || newSendingData.ownNumber
                  }
                  side="back"
                  postcardSize={postcardSize}   //'6x9' One size only string incoming from ReduxState
                  postcardDpi={300}                  
                  postcardBleed={0.25}                    
                  backMarkPlace={backMarkPlace}
                />
              </CardBody>
            </Card>
          </div>
          <ButtonsBlock
            viewPageType={viewPageType}
            onClose={onClose}
            onEdit={onEdit}
            onDraftEdit={onDraftEdit}
            onCreate={onCreate}  
            onDraftCreate={onDraftCreate}        
          />
          <AddNumberModal 
            newNumberHandler={setTrackingNumber} 
            setShowAddingCard={setShowAddingCardTrackingNumber}
          />
          <PostcardApprovalModal 
            isOpen={showPostcardApproval}
            toggle={togglePostcardApproval}
            onConfirm={viewPageType === 'create' ? onApprovalCreate : onApprovalEdit}
            front = {images.front.mini}
            back = {images.back.mini}
            data = {
              {
                front: images.front.mini,
                back: images.back.mini,
                ownNumber: newSendingData.ownNumber,
                trackingNumber: newSendingData.trackingNumber,
                sendingDate: newSendingData.sendingDate
              }
            }       
          />
        </div>
      ):(
        <CreditCardsAfter
          onAfterAddCreditCard={ (campaignAddingCardType==="sending") ? afterAddCreditCardSendCampaign : afterAddCreditCardTrackingNumberCampaign}
          onBack={()=>setShowAddingCard(false)}
        />      
      )
    );
}
