import { all, call, put, takeLatest } from 'redux-saga/effects';
import LogRocket from 'logrocket';
import * as accountActions from '../actions/account'
import * as actions from '../actions/index'
import * as signUpActions from '../actions/signUp'
import { toast } from 'react-toastify'
import { 
  updateAccountApi,
  createAccountApi,
  getAccountsApi,
  updateAccountSubscriptionApi,
  getAccountApi, 
  getAccountHistoriesApi,
  sendVerificationEmailApi,
  getOwnPostcardPreferencesApi,
  sendRechargeApi,  
  deleteAccountRequestApi,
  cancelDeleteAccountRequestApi,
  getSmsHistoryApi,
} from '../requests/account';
import { getAccessGroup } from 'utils/helper';
import { JOB_DASHBOARD_PATH, DASHBOARD_PATH  } from 'utils/constants';
import {pathOr} from 'ramda';

const errorText = "The package hasn't been updated.";


export function* getAccountRequest(action) {  
  try {       
    yield put({ type: accountActions.SET_ACCOUNT_LOADING, loading: true });
    const response = yield call(getAccountApi, action.data);
    yield put({
      type: accountActions.GET_ACCOUNT_REQUEST_SUCCESS,
      data: response.data,
    });
    localStorage.setItem('current_account', JSON.stringify(response.data));
    yield put({ type: accountActions.SET_ACCOUNT_LOADING, loading: false });
  } catch (e) {
    LogRocket.captureException(e); 
    yield put({ type: accountActions.GET_ACCOUNT_REQUEST_FAILED, error: e });
    yield put({ type: accountActions.SET_ACCOUNT_LOADING, loading: false });
  }
}

export function* getAccounts(action) {
  console.log("export function* getAccounts(action) {");
  try {
    const response = yield call(getAccountsApi)
    let accountList = response.data.data;

    if (accountList.length === 0) return false
    yield put({ type: actions.SET_ACCOUNT_LIST, accountList })

    let currentAccount = null
    try {
      currentAccount = JSON.parse(localStorage.getItem('current_account'));
    } catch (err) {LogRocket.captureException(err); }

    if (!currentAccount || !accountList.find((x) => x.id === currentAccount._id)) {
      currentAccount = accountList[0];
      localStorage.setItem('current_account', JSON.stringify(currentAccount));
    }
    yield put({ type: actions.SET_CURRENT_ACCOUNT, currentAccount })
    yield put({ type: accountActions.GET_ACCOUNTS_REQUEST_SUCCESS, data: { ...response.data } })
  } catch(error) {
    LogRocket.captureException(error); 
    yield put({ type: accountActions.GET_ACCOUNTS_REQUEST_FAILED, error })
  }
}

export function* createAccount(action) {
  try {
    const response = yield call(createAccountApi, action.data)
    yield put({ type: accountActions.CREATE_ACCOUNT_REQUEST_SUCCESS, data: { ...action.data.body, id: response.data.accountId } })
    yield put({ type: accountActions.GET_ACCOUNTS_REQUEST })
  } catch(error) {
    LogRocket.captureException(error); 
    yield put({ type: accountActions.CREATE_ACCOUNT_REQUEST_FAILED, error })
  }
}

