export function* updateAccount(action) {
  try {
    yield put({ type: accountActions.SET_ACCOUNT_UPDATING, loading: true });
    const response = yield call(updateAccountApi, action.data)    
    yield put({ type: accountActions.UPDATE_ACCOUNT_REQUEST_SUCCESS, data: { ...action.data.body, ...response.data } })
    const account = yield call(getAccountApi, action.data.account);
    yield put({type: accountActions.GET_ACCOUNT_REQUEST_SUCCESS,data: account.data,});
    localStorage.setItem('current_account', JSON.stringify(account.data))
    yield put({ type: accountActions.SET_ACCOUNT_UPDATING, loading: false });
  } catch(error) {
    LogRocket.captureException(error); 
    yield put({ type: accountActions.UPDATE_ACCOUNT_REQUEST_FAILED, error })
  }
}

export function* updateAccountSubscription(action) {
  try {
    const response = yield call(updateAccountSubscriptionApi, action.data)
    yield put({ type: accountActions.UPDATE_ACCOUNT_SUBSCRIPTION_REQUEST_SUCCESS, data: { ...action.data, ...response.data } })
    yield put({ type: accountActions.TOGGLE_SIGN_UP_SUCCESS_MODAL })
  } catch(error) {
    LogRocket.captureException(error);
    yield put({ type: accountActions.UPDATE_ACCOUNT_SUBSCRIPTION_REQUEST_FAILED, error })
  }
}

export function* getAccountsHistories(action) {
  try {
    yield put({ type: accountActions.SET_HISTORY_LOADING, loading: true })
    const response = yield call(getAccountHistoriesApi, action.data)
    yield put({ type: accountActions.GET_ACCOUNT_HISTORIES_REQUEST_SUCCESS, data: response.data })
    yield put({ type: accountActions.SET_HISTORY_LOADING, loading: false })
  } catch(error) {
    LogRocket.captureException(error);
    yield put({ type: accountActions.GET_ACCOUNT_HISTORIES_REQUEST_FAILED, error })
    yield put({ type: accountActions.SET_HISTORY_LOADING, loading: false })
  }
}

export function* updateMainPackage(action) {
  try {
    yield put({ type: accountActions.SET_ACCOUNT_LOADING, loading: true });
    yield call(updateAccountSubscriptionApi, action.data)
    yield put({ type: accountActions.GET_ACCOUNT_REQUEST, data: action.data.account })
    yield put({ type: accountActions.UPDATE_MAIN_PACKAGE_REQUEST_SUCCESS})
    yield put({ type: accountActions.GET_ACCOUNT_HISTORIES_REQUEST, data: {account: action.data.account, params: {page: 0, limit:10}}})
  } catch(error) {
    LogRocket.captureException(error);
    const errorMessage = pathOr(errorText, ['response', 'data', 'error', 'dopeMessage'], error);
    toast.error(errorMessage)
    yield put({ type: accountActions.UPDATE_MAIN_PACKAGE_REQUEST_FAILED, error })
    yield put({ type: accountActions.SET_ACCOUNT_LOADING, loading: false });
  }
}

export function* sendVerificationEmail(action) {
  try {
    yield call(sendVerificationEmailApi, {})
    toast.success(`Confirmation email has been resent to ${action.data}.`)
  } catch(error) {
    LogRocket.captureException(error);
    toast.error(`Confirmation email has been not sent to ${action.data}.`)
  }
}

export function* onboardingAccount(action) {  
  try {
    yield put({ type: signUpActions.ON_CHANGE_ONBOARDING, key: 'loading', value: true });
    const response = yield call(updateAccountApi, action.data);
    yield put({type: accountActions.GET_ACCOUNT_REQUEST_SUCCESS,data: response.data,});
    localStorage.setItem('current_account', JSON.stringify(response.data ));
    let authdata = JSON.parse(localStorage.getItem('authdata'));
    // authdata = {...authdata, ...response.data};   
    // temporary
    authdata = {...authdata, accountFilled: true}; 
    localStorage.setItem('authdata', JSON.stringify(authdata));
    yield put({ type: signUpActions.ON_CHANGE_ONBOARDING, key: 'accountUpdated', value: true });
    yield put({ type: signUpActions.ON_CHANGE_ONBOARDING, key: 'loading', value: false });  
    let accessGroup = getAccessGroup(window.location.host);    
    switch(accessGroup){
      case 1:
        action.data.history.push(JOB_DASHBOARD_PATH);
        break;
      case 2:
        action.data.history.push(DASHBOARD_PATH);
        break;
      case 3:
        action.data.history.push(JOB_DASHBOARD_PATH);
        break;
      default :
        action.data.history.push(JOB_DASHBOARD_PATH);
        break;        
    } 
    // action.data.history.push(JOB_DASHBOARD_PATH);    
  } catch(error) {
    LogRocket.captureException(error);
    yield put({ type: signUpActions.ON_CHANGE_ONBOARDING, key: 'loading', value: false });
    yield put({ type: signUpActions.ONBOARDING_ACCOUNT_REQUEST_FAILED, error });    
    toast.error(
      error.response.data.error.dopeMessage ? error.response.data.error.dopeMessage : "UpdateAccountApi Error."
    );    
  }
}

