export function* getSmsHistory(action) {
  try {
    const response = yield call(getSmsHistoryApi, action.data);    
    if (response&&response.data){      
      yield put({
        type: accountActions.GET_SMS_HISTORY_SUCCESS,
        data: response.data,
      });
    }        
  } catch (e) {
    console.log(e);
    yield put({ type: accountActions.GET_SMS_HISTORY_FAILED });
    toast.error(`Error. Can't get SMS history.`);
  }
}

export default all([
  takeLatest(accountActions.GET_ACCOUNT_REQUEST, getAccountRequest),
  takeLatest(accountActions.UPDATE_ACCOUNT_REQUEST, updateAccount),
  takeLatest(accountActions.CREATE_ACCOUNT_REQUEST, createAccount),
  takeLatest(accountActions.GET_ACCOUNTS_REQUEST, getAccounts),
  takeLatest(accountActions.UPDATE_ACCOUNT_SUBSCRIPTION_REQUEST, updateAccountSubscription),
  takeLatest(accountActions.GET_ACCOUNT_HISTORIES_REQUEST, getAccountsHistories),
  takeLatest(accountActions.UPDATE_MAIN_PACKAGE_REQUEST, updateMainPackage),
  takeLatest(accountActions.SEND_VERIFICATION_EMAIL_REQUEST, sendVerificationEmail),
  takeLatest(signUpActions.ONBOARDING_ACCOUNT_REQUEST, onboardingAccount),
  takeLatest(accountActions.GET_USER_OWN_POSTCARD_PRICE_REQUEST, getUserOwnPostcardPrice),  //dont used
  takeLatest(accountActions.SEND_RECHARGE_REQUEST, sendRechargeRequest),
  takeLatest(accountActions.DELETE_ACCOUNT_REQUEST, deleteAccountRequest),
  takeLatest(accountActions.CANCEL_DELETE_ACCOUNT_REQUEST, cancelDeleteAccountRequest),
  takeLatest(accountActions.GET_SMS_HISTORY_REQUEST, getSmsHistory),   
])
