import React from 'react';
import { 
  // CardElement,
  injectStripe,
  CardNumberElement,
  CardExpiryElement,
  CardCVCElement,
} from 'react-stripe-elements';
import { connect } from 'react-redux';
import LogRocket from 'logrocket';
import { withRouter } from 'react-router-dom';
import request from 'utils/request';
import { API_URL } from 'config/config';
import { SyncLoader } from 'react-spinners';
import { toast } from 'react-toastify';
import {pathOr} from 'ramda';
import { ReactComponent as LeftIcon } from 'assets/img/icons/arrow-left.svg';
import {setAccount, setDefaultSource, getAccountRequest} from 'actions/account';
import { DopeButton } from 'components/DopeForms';

import './CreditCardsAfter.scss';

const createOptionsAfter = () => {
  return {
    style: {     
      base: {
        fontSize: '14px',
        color: '#354052',
        // fontFamily: 'IBMPlexSans', 
        // fontFamily: 'sans-serif',
        lineHeight: '1.1',
        letterSpacing: '0.025em',
        '::placeholder': {
          color: '#9A9A9A',
          lineHeight: '1.1',
        },
      },
      invalid: {
        color: '#d52330',
      },
    }
  }
};

const errorTextAfter = 'Error. Please check your credit card amount and other credentials or contact with the admin.'

const CreditCardsAfter = injectStripe(
  class extends React.Component {
    state = {
      loadingNewCard: false,
    };

    onSubmitNewCard = async (onAfterAddCreditCard) => {      
      this.setState({ loadingNewCard: true });       
      let { token } = await this.props.stripe.createToken();       
      try {
        await request(`${API_URL}/save-new-card`, {
          method: 'post',
          body: {
            tokenId: token.id
          },
          queries: {
            account: this.props.currentAccount._id
          }
        });
        this.props.setDefaultSource(token.card.id);        
        this.setState({loadingNewCard: false});  
        onAfterAddCreditCard();
      } catch (err) { 
        this.setState({loadingNewCard: false});  
        LogRocket.captureException(err);        
        const errorMessage = pathOr(errorTextAfter, ['response', 'data', 'error', 'dopeMessage'], err);
        toast.error(errorMessage);
        console.log(err);       
      }      
    };

    render() { 
      return (
        <div className="CreditCardsAfter">        
          <h2 className="credit-cards-title">Add card to continue</h2>
          <p className="credit-cards-text">Card will be saved as primary payment method</p>           
                     
          <div className="credit-card-after-container">                        
            <label className="CardElement-CN">
              <span className="CardElTitle">
                Card number&nbsp;<sup className="star">*</sup>
              </span>
              <div className="border-filed">
                <CardNumberElement
                  {...createOptionsAfter()}                        
                />
              </div>
            </label>
            <div>
              <label className="CardElement-ED">
                <span className="CardElTitle">
                  Expiration date&nbsp;<sup className="star">*</sup>
                </span>
                <div className="border-filed">
                  <CardExpiryElement
                    {...createOptionsAfter()}                        
                  />
                </div>
              </label>
              <label className="CardElement-CVC">
                <span className="CardElTitle">
                  CVC&nbsp;<sup className="star">*</sup>
                </span>
                <div className="border-filed">
                  <CardCVCElement
                    {...createOptionsAfter()}
                  />
                </div>
              </label>
            </div>
          </div>

          <div className="credit-card-actions">
            <DopeButton
              color="white"
              onClick={this.props.onBack}
            >
              <LeftIcon
                className="btn-icon"
              />
              Back
            </DopeButton>
            <DopeButton
              color="primary"
              onClick={()=>this.onSubmitNewCard(this.props.onAfterAddCreditCard)}
            >
              {this.state.loadingNewCard && (
                <div className='loader-container' style={{ position:"absolute",top:"50%",left:"0","WebkitTransform":"translate(-150%, -50%)",transform:"translate(-150%,-50%)"}}>
                  <SyncLoader size={5}  color={'#403d39'}/>
                </div>
              )} 
              Add card and continue
            </DopeButton>  
          </div>
        </div>
      );
    }
  }
)

const mapStateToProps = (state) => ({
  currentAccount: state.account.currentAccount
});

const mapDispatchToProps = {
  setAccount,
  setDefaultSource,
  getAccountRequest,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(CreditCardsAfter));
