import React, { useEffect, useState } from 'react';
import { Label, FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInputGroup } from 'components/DopeForms/DopeInputGroup/DopeInputGroup';
import { InputGroupAddon } from 'reactstrap';
import { Link } from 'react-router-dom';
import { LOG_IN } from 'utils/constants';
import AuthContainer from 'components/AuthContainer';
import VisibilityIcon from 'assets/img/visibility.svg';
import './NewPasswordForm.scss';

export default function ForgotPasswordForm(props) {
  const [delayLoading, setDelayLoading] = useState(false);
  useEffect(() => {   
    let delayLoadingTimer;  
    if (!props.loading && delayLoading){           
      delayLoadingTimer = window.setTimeout(()=>setDelayLoading(false),60000);
    }
    if (props.loading){          
      setDelayLoading(props.loading);
    }      
    return () => {
      clearTimeout(delayLoadingTimer);
    };
  }, [props.loading]);
  return (
    <AuthContainer>
      <form className='new-password-form' onSubmit={props.submit}>
        <div className='auth-form-title'>Set a New Password</div>
        <div>
          <FormGroup className="dope-form-group">
            <Label className='input-title' htmlFor='password'>
              New Password
            </Label>
            <DopeInputGroup>
              <DopeInput
                id='password'
                name='password'
                type={props.passwordVisibility ? 'text' : 'password'}
                onChange={props.onChange}
                value={props.data.password}
                error={props.errors.password}
              />
              <InputGroupAddon addonType='append'>
                <img
                  className={
                    props.passwordVisibility ? (
                      'input-icon full-width active'
                    ) : (
                      'input-icon full-width'
                    )
                  }
                  src={VisibilityIcon}
                  alt='visibility'
                  name='passwordVisibility'
                  onClick={props.togglePasswordVisibility}
                />
              </InputGroupAddon>
            </DopeInputGroup>
            <small
              className={props.errors.password ? 'validation-message' : ''}
            >
              Must contain at least 8 characters, including uppercase,
              lowercase, and numbers.
            </small>
          </FormGroup>
          <FormGroup className="dope-form-group">
            <Label className='input-title' htmlFor='password'>
              Confirm New Password
            </Label>
            <DopeInputGroup>
              <DopeInput
                id='confirmPassword'
                name='confirmPassword'
                type={props.confirmPasswordVisibility ? 'text' : 'password'}
                onChange={props.onChange}
                value={props.data.confirmPassword}
                error={props.errors.confirmPassword}
              />
              <InputGroupAddon addonType='append'>
                <img
                  className={
                    props.confirmPasswordVisibility ? (
                      'input-icon full-width active'
                    ) : (
                      'input-icon full-width'
                    )
                  }
                  name='confirmPasswordVisibility'
                  src={VisibilityIcon}
                  alt='visibility'
                  onClick={props.togglePasswordVisibility}
                />
              </InputGroupAddon>
            </DopeInputGroup>
          </FormGroup>
        </div>
        <div className='new-password-form-bottom'>
          <div className='link-block'>
            <Link to={LOG_IN}>Back to Sign In</Link>
          </div>
          <DopeButton color='primary' type='submit' disabled={delayLoading}>
            Change password
          </DopeButton>
        </div>
      </form>
    </AuthContainer>
  );
}
