import React from 'react';

import { S3_BUCKET_PUBLIC_NAME } from 'config/config';
import './SendingCell.scss';

export default function SendingCell({ event }) {
  // console.log("SendingCell event: ", event);
  return (
    <div className='sending-cell'>
      <div className='sendings-preview-block'>
        <div className='sendings-preview-item'>
          <img            
            src={
              event.previewUrls ? 
                event.previewUrls.frontMini 
                : 
                `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcard-sending-mini-front-preview-${event._id}`
            }
            alt='postcard preview'
          />
        </div>
        <div className='sendings-preview-item'>
          <img            
            src={
              event.previewUrls ? 
                event.previewUrls.backMini 
                : 
                `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcard-sending-mini-back-preview-${event._id}`
            }
            alt='postcard preview'
          />
        </div>
      </div>
      <div className='status-sending-block'>
        <span className={`sendings-status ${event.status}-block`}>
          {event.status}
        </span>
      </div>
      <div className={`bg-status ${event.status}-block`}></div>
    </div>
  );
}
