import React from 'react';
import { FormGroup } from 'reactstrap';
import { DopeModalHeader } from '../../DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from '../../DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from '../../DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeModal } from '../../DopeModal/DopeModal';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import Select from 'react-select';
import Ajv from 'ajv';
import { STATES } from 'utils/constants';
import { AccountSchema } from './config';
import './EditContact.scss';

const ajv = new Ajv({ allErrors: true });

export default function EditContact(props) {
  const onChange = e => {
    let errors = props.errors;
    errors[e.target.name] = '';

    props.addErrorsEditContact(errors);

    props.onChangeEditContact(e.target.name, e.target.value);
  };

  const getOptions = () => {
    const states = STATES;

    return states.map(item => ({ value: item.name, label: item.name, abbreviation: item.abbreviation }));
  };

  const getSelectedItem = value => {
    const filtered = getOptions().filter(item => item.value === value || item.abbreviation === value)

    if (filtered.length) return filtered[0];
  };

  const onClose = () => {
    props.resetEditContactForm();
    props.toggleEditContactModal(false);
  };

  const editContactValidation = () => {
    const formattedForBE = {
      ...props.data,
    };

    let result = ajv.validate(AccountSchema, formattedForBE);
    const errors = {};

    if (!result) {
      ajv.errors.map(error => {
        const errorKey = error.dataPath.split('.').pop();
        errors[errorKey] = 'This field is required!';
      });

      props.addErrorsEditContact(errors);
    }

    return result;
  };

  const onSubmit = () => {
    if (!editContactValidation()) return;
    let req = {
      query: {
        account: props.currentAccount._id,
      },
      params: {
        contactId: props.data._id
      },
      body: { json: props.data },
    };

    props.editContactRequest(req);
  };
  return (
    <div>
      <DopeModal className='edit-contact' isOpen={props.open} centered>
        <DopeModalHeader>Edit Contact</DopeModalHeader>
        <DopeModalBody>
          <div className='one-line'>            
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.firstName || ''}
                title='First Name'
                supText='*'
                name='firstName'
                error={props.errors.firstName || ''}
                onChange={onChange}
              />
            </FormGroup>          
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.lastName || ''}
                title='Last Name'
                supText='*'
                name='lastName'
                data-group='companyAddress'
                error={props.errors.lastName || ''}
                onChange={onChange}
              />
            </FormGroup>            
          </div>
          <div className='one-line'>            
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.address1 || ''}
                title='Address'
                supText='*'
                name='address1'
                error={props.errors.address1 || ''}
                onChange={onChange}
              />
            </FormGroup>          
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.address2 || ''}
                title='Address 2'
                name='address2'
                onChange={onChange}
              />
            </FormGroup>            
          </div>
          <div className='one-line'>
            
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.city || ''}
                title='City'
                supText='*'
                name='city'
                error={props.errors.city || ''}
                onChange={onChange}
              />
            </FormGroup>
            
            <FormGroup className={`dope-form-group ${props.errors.state
                ? 'select-error'
                : ''}`}
            >              
              <span className='input-title'>
                State <sup>*</sup>
              </span>              
              <Select
                className="dope-select"
                classNamePrefix="dope"
                value={getSelectedItem(props.data.state || '')}
                options={getOptions()}
                placeholder=''
                onChange={item => {
                  let errors = props.errors;
                  errors.state = '';
                  props.addErrorsEditContact(errors);
                  props.onChangeEditContact('state', item.value);
                }}
              />
              {props.errors.state && (
                <div className='validation-message'>{props.errors.state || ''}</div>
              )}              
            </FormGroup>
          </div>
          <div className='one-line'>            
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.zip || ''}
                title='ZIP'
                supText='*'
                name='zip'
                data-group='companyAddress'
                mask='99999'
                error={props.errors.zip || ''}
                onChange={onChange}
              />
            </FormGroup>  
            <FormGroup className="dope-form-group">
            </FormGroup>          
          </div>
        </DopeModalBody>
        <DopeModalFooter className='dope-footer'>
          <DopeButton color='white' onClick={onClose}>
            Cancel
          </DopeButton>
          <DopeButton color='primary' onClick={() => onSubmit()}>
            Save
          </DopeButton>
        </DopeModalFooter>
      </DopeModal>
    </div>
  );
}