const loadingImage = API_URL + '/images/loading.gif';

const defaultHouse = API_URL + '/images/default-house.jpg';

const getIcon = (type) => {
  if (type === 'yard') return yardIcon;
  if (type === 'target') return targetIcon;
  if (type === 'office') return officeIco;
  return phoneIcon;
};

class YardSignTrackerCard extends React.Component {
  state = {
    img: '',
    placeSignDisabled: true,
    placeSignContent: 'Place Sign Here',
    emitChange: '',
    target: null,
    noYardSigns: true,
    loading: true,
    center: { lat: 44, lng: -93 }
  };

  map = React.createRef();

  componentDidMount() {
    this.setState({ img: '' });
    this.setMapCenter();
  }

  setMapCenter =  async() => {
    const latitude = pathOr(null, ['currentAccount', 'companyAddress', 'lat'], this.props);
    const longitude = pathOr(null, ['currentAccount', 'companyAddress', 'lng'], this.props);

    if (latitude && longitude) {
      this.setState({
        center: {
          lat: latitude,
          lng: longitude
        },
        loading: false
      });
    } else this.setState({ loading: false });
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    const lat = pathOr(null, ['currentAccount', 'companyAddress', 'lat'], this.props);
    const lng = pathOr(null, ['currentAccount', 'companyAddress', 'lng'], this.props);

    if (!this.state) this.setState({ img: '' });
    // You don't have to do this check first, but it can help prevent an unneeded render
    // console.log('component will receive props', nextProps);
    if (
      nextProps.openedInfo &&
      nextProps.openedInfo !== this.state.openedInfo
    ) {
      this.setState({ openedInfo: nextProps.openedInfo });
      for (let marker of this.props.markers) {
        if (marker._id === nextProps.openedInfo && marker.icon === 'phone')
          this.getImage({ address: marker.address, zip: marker.zip });
        else if (marker._id === nextProps.openedInfo && marker.icon === 'yard')
          this.getImage({
            latitude: marker.latitude,
            longitude: marker.longitude
          });
      }
    }

    if (!nextProps.addPinMode && this.state.target) {
      this.setState({ ...this.state, target: null });
    }

    if (nextProps.jobs) {
      const yardSigns = nextProps.jobs.filter((job) => {
        return job.type === 'Yard Sign';
      });

      if (yardSigns.length && this.state.noYardSigns) {
        this.setState({ ...this.state, noYardSigns: false });
      }
    }

    if ( !nextProps.center && lat && lng ) {
      this.props.setCenter( lat, lng );
    }
  }

  getImage(data) {
    this.setState({ img: loadingImage });
    request(`${API_URL}/get-image`, {
      method: 'post',
      body: data
    })
      .then((response) => {
        this.setState({ img: response.data.images[0] });
        return true;
        // return response.data.count;
      })
      .catch((err) => {
        console.log('Error', err);
        this.setState({ img: defaultHouse });
      });
  }

  filterYardSignsByDate = (marker, date) => {
    if (marker.icon !== 'yard') return true;
    if (marker.createdAt) return moment(marker.createdAt).isAfter(date);
    return false;
  };

  componentDidUpdate(prevProps) {
    if (
      this.props.markers &&
      (!prevProps.markers ||
        this.props.markers.length !== prevProps.markers.length)
    )
      if (prevProps.currentAccount._id !== this.props.currentAccount._id) {
        // console.log('MARKERS', this.props.markers);
        this.setMapCenter();
      }
  }
  noDuplicateMarkers = (arr) => {
    if (Array.isArray(arr) && arr.length) {
      return arr.reduce((acc, current) => {
        const x = acc.find((item) => {
          return (
            item.latitude === current.latitude &&
            item.longitude === current.longitude
          );
        });
        if (!x) {
          return acc.concat([current]);
        } else {
          return acc;
        }
      }, []);
    } else return [];
  };

  addPins = (params) => {
    if (params) {
      this.setState({ ...this.state, target: null });
    } else {
      navigator.geolocation.getCurrentPosition((position) => {
        this.props.toggleMapElementInfoStatus(
          null,
          position.coords.latitude,
          position.coords.longitude
        );

        let target = {
          latitude: position.coords.latitude,
          longitude: position.coords.longitude
        };
        this.setState({ placeSignDisabled: false, target });
      });
    }
  };

  onPlace = () => {
    const total = pathOr(0, ['amount'], this.props.currentJob);
    const placed = pathOr(0, ['pins', 'length'], this.props.currentJob);
    const balance = total - placed;

    if (!balance || balance <= 0) return;
    let markerNew = { ...this.state.target, icon: 'yard' };

    let markers = this.props.markers;
    let alreadyExists = markers.find(
      (marker) =>
        marker.latitude === markerNew.latitude &&
        marker.longitude === markerNew.longitude
    );

    if (alreadyExists) {
      console.log('ELEMENT ON THIS POSITION IS ALREADY EXISTS');
      return;
    }

    this.props.createAddPinRequest({
      pin: markerNew,
      jobID: this.props.currentJob.id,
      account: this.props.currentAccount._id
    });
  };

  onRemovePin = (idMarker) => {
    let currentJob = this.props.currentJob;
    if (currentJob && currentJob.pins) {
      currentJob.pins = currentJob.pins.filter((pin) => pin._id !== idMarker);
    }
    let markers = this.props.markers.filter(
      (marker) => marker._id !== idMarker
    );
    this.props.createRemovePinRequest({
      id: idMarker,
      account: this.props.currentAccount._id,
      markers: markers,
      currentJob: currentJob
    });
  };

  render() {
    return (
      <div className="map-info-container">
        <Card className="map-container">
          <CardBody>
            {this.state.loading ? (
              <div className="loader">
                <FadeLoader size={100} color={'#403d39'} />
              </div>
            ) : (
              <div id="map" className="map">
                {this.state.target && (
                  <div
                    style={{
                      position: 'absolute',
                      zIndex: 10,
                      marginTop: this.map.getDiv
                        ? this.map.getDiv().offsetHeight / 2 - 35
                        : '120px',
                      marginLeft: this.map.getDiv
                        ? this.map.getDiv().offsetWidth / 2 - 35
                        : '20vw'
                    }}
                  >
                    <img src={targetIcon} height={70} width={70} alt="target" />
                  </div>
                )}
                <Map
                  {...this.props}
                  setRef={(ref) => (this.map = ref)}
                  center={this.props.center}
                  onCenterChanged={() => {
                    if (this.map.getCenter) {
                      if (this.state.target) {
                        let target = this.state.target;
                        target.latitude = this.map.getCenter().lat();
                        target.longitude = this.map.getCenter().lng();
                        this.setState({ target });
                      }
                      // this.props.setCenter(this.props.currentAccount.latitude, this.props.currentAccount.longitude)
                    }
                  }}
                  onZoomChanged={() => this.props.setZoom(this.map.getZoom())}
                >
                  {this.noDuplicateMarkers(this.props.markers).map(
                    (marker, index) => (
                      <Marker
                        key={index}
                        position={{
                          lat: marker.latitude,
                          lng: marker.longitude
                        }}
                        icon={{
                          url: getIcon(marker.icon),
                          size: { width: 60 * 0.765, height: 60 },
                          scaledSize: { width: 50 * 0.765, height: 50 }
                        }}
                        onClick={() => {
                          this.props.setCenter(
                            marker.latitude,
                            marker.longitude
                          );
                          this.props.toggleMapElementInfoStatus(marker._id);
                        }}
                      />
                    )
                  )}
                </Map>
                <div>
                  <MapButtons
                    jobs={this.props.jobs}
                    currentJob={this.props.currentJob}
                    setCurrentJob={this.props.setCurrentJob}
                    switchMapView={this.props.switchMapView}
                    addPins={this.addPins}
                    markers={this.props.markers}
                    onPlace={this.onPlace}
                    addPinMode={this.props.addPinMode}
                    showAddPinBtn={this.props.showAddPinBtn}
                    noYardSigns={this.state.noYardSigns}
                  />
                </div>
              </div>
            )}
          </CardBody>
        </Card>
        <MapInfo
          openedInfo={this.props.openedInfo}
          markers={this.props.markers}
          img={this.state.img}
          onRemovePin={this.onRemovePin}
          timeZone={this.props.timeZone}
        />
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  markers: state.map.markers,
  openedInfo: state.map.openedInfo,
  zoom: state.map.zoom,
  center: state.map.center,
  mapTypeId: state.map.mapTypeId,
  currentAccount: state.account.currentAccount,
  currentJob: state.job.currentJob,
  timeZone: state.account.timeZone,
});

const mapDispatchToProps = {
  ...mainActions,
  ...jobsActions,
  ...dashboardActions
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(YardSignTrackerCard);
