import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Switch, Route, useRouteMatch, } from 'react-router-dom';
import AdminManageHandwrittenList from './AdminManageHandwrittenList/AdminManageHandwrittenList';
import './AdminManageHandwritten.scss';
import {resetCreateAdminHandwritten} from "actions/adminHandwritten";

export default function AdminManageHandwritten() {
	const dispatch = useDispatch();
  const { url } = useRouteMatch();  

  useEffect(() => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
    return () => {
      dispatch(resetCreateAdminHandwritten());
    };
  }, []);
  
  return (        
    <Switch>
      <Route exact path={`${url}`}>
        <AdminManageHandwrittenList/>
      </Route>
    </Switch>    
  );
}
