import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useParams, useHistory } from 'react-router-dom';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import MatchingContacts from 'views/ListsView/Lists/MatchingContacts';
import UploadModal from 'views/ListsView/Lists/UploadModal/Upload';
import {ReactComponent as LeftArrowIcon} from 'assets/img/icons/chevron-left.svg';
import {
  resetAdminRequest,
  getAdminRequestRequest,
  uploadAccountRequestsListRequest,
  setAdminUploadCsvData,
  getAdminListsUploadCsvRequest,
  } from 'actions/adminRequests';

import './AdminRequest.scss';

export default function AdminRequest() {

  const { requestId } = useParams();
  // console.log("requestId:", requestId);
  const dispatch = useDispatch();
  const history = useHistory();
  
  useEffect(() => {        
    // console.log("AdminRequest Mount");
    const data = {
      requestId,
    };
    dispatch(getAdminRequestRequest(data));     
    return () => {
      dispatch(resetAdminRequest());
    };
  },[]); 
  
  // const loading = useSelector(state => state.adminRequests.adminRequest.loading); 
  const requestData = useSelector(state => state.adminRequests.adminRequest.data)||{};
  const {account={},productType='',status='',createdAt=''} = requestData;  

  const configs = useSelector(state => state.dbConfigs.configs);
  const adminUploadCsvData = useSelector(state => state.adminRequests.adminRequest.adminUploadCsvData)||{};
  const allLists = useSelector(state => state.adminRequests.adminRequest.allLists)||[];
  
  useEffect(() => {        
    if(Object.keys(requestData).length){
      const data = {
        account: account._id,
      };
      dispatch(getAdminListsUploadCsvRequest(data));
    }    
  },[requestData]);
 
  const [showUploadModal, setShowUploadModal] = useState(false);  
  const [showMatchingPage, setShowMatchingPage] = useState(false);
  const [uploadCsvErrors, setUploadCsvErrors] = useState({});    

  const headers = configs.find(item=>{
    return item.name ==='csvFields'
  })
  

  const requiredHeaders = headers.value.filter(item => item.required === true).map(item=>{
    return item.name
  });
  
  const allHeaders = headers.value.map(item=>{
    return item
  });  

  // allHeaders.unshift('')  //Dima??? 

  const fixStateName = (string) => {
    let newStr = string.trim();
    if (newStr.length === 2)
      return newStr.toUpperCase();
    if (newStr.length > 0){
      let correctString = newStr.toLowerCase().split(/\s+/).map(word => word[0].toUpperCase() + word.substring(1)).join(' ');    
      return correctString;
    }
    return newStr;
  };

  const onUploadSubmit = () => {
    let stateColumnIndex = adminUploadCsvData.headers.indexOf('state');
    let newAdminUploadCsvData = {...adminUploadCsvData};
    if(!!~stateColumnIndex){
      let uploadCsvJson = [...adminUploadCsvData.csvJson];
      let fixedUploadCsvJson = uploadCsvJson.map( (el) => {            
        let elKeys = Object.keys(el);
        let fixedEl = {...el};
        fixedEl[elKeys[stateColumnIndex]] = fixStateName(fixedEl[elKeys[stateColumnIndex]]);
        return fixedEl 
      });      
      newAdminUploadCsvData.csvJson = fixedUploadCsvJson;
    }    
    let data = {
      requestId,
      body:{
        ...newAdminUploadCsvData,
        account: account._id, 
        listName: newAdminUploadCsvData.name,
        headers: newAdminUploadCsvData.headers.join(','),
        // listId: newAdminUploadCsvData.listId,
        // csv: adminUploadCsvData.csv,
        // isFirstRowHeader: newAdminUploadCsvData.isFirstRowHeader,
        // need listType?
        listType: 'account-request',        
      }    
    }    
    if(data.body.listName) delete data.body.listId;    
    dispatch( uploadAccountRequestsListRequest(data,()=>setShowMatchingPage(false)));    
  }
  const back = () => {
    history.push('/manage-requests');
  }

  return (    
    <div className="AdminRequest"> 
      <div className='back-btn-container'>
        <button className='back-btn'
          onClick={back}>
          <LeftArrowIcon className="back-icon"/>
          Back to accounts
        </button>
      </div>  
      <div style={{marginBottom:"24px"}}>     
        <p>Account name: {account.name||''}</p>
        <p>Account email: {account.email||''}</p>
        <p>Account accountPhone: {account.accountPhone||''}</p>
        <p>Account phone: {account.phone||''}</p>
        <p>Account Request productType: {productType}</p>
        <p>Account Request status: {status}</p>
        <p>Account Request createdAt: {createdAt}</p>   
      </div>  
      <div className="ListUpload">
        {
          showMatchingPage ? (
          <MatchingContacts 
            uploadCsvData={adminUploadCsvData}
            allLists={allLists} 
            onChangeUploadCsvData={(key,value)=>dispatch(setAdminUploadCsvData(key,value))}
            onSubmit={onUploadSubmit}
            toggleMatchingPage={()=>setShowMatchingPage(false)}
            addUploadCsvErrors={setUploadCsvErrors}
            requiredHeaders={requiredHeaders}
            allHeaders={['',...allHeaders]}
            errors={uploadCsvErrors}
          />
          ):(
            <DopeButton color="white" onClick={()=>setShowUploadModal(true)}>Upload My List</DopeButton>
          )
        }
      </div>
      <UploadModal
        open={showUploadModal}
        toggle={()=>setShowUploadModal(false)}        
        uploadCsvData={adminUploadCsvData}
        onChange={(key,value)=>dispatch(setAdminUploadCsvData(key,value))}
        toggleMatchingPage={()=>{setShowMatchingPage(true);setShowUploadModal(false);}}
      />
    </div>
  );
}
