import React, { Component } from 'react';
import {CardHeader, Table } from 'reactstrap';
import {pathOr} from 'ramda';
// import './OrderNeighborhoodPreview.scss';

class Order extends Component {
  // builderRef = React.createRef();

  render() {
    const amount = pathOr([], ['neighborhoodSending', 'contacts'], this.props.data);
    const totalPrice = pathOr(0, ['neighborhoodSending', 'price'], this.props.data)/100;
    // const routes = pathOr(0, ['neighborhoodSending', 'eddmRoutes'], this.props.data); 
    const distance = pathOr(0, ['neighborhoodSending', 'distance'], this.props.data);
    const searchAddress = pathOr(0, ['neighborhoodSending', 'searchAddress'], this.props.data);
    const size = pathOr(0, ['neighborhoodSending', 'size'], this.props.data);
    const postage = pathOr(0, ['neighborhoodSending', 'postage'], this.props.data);
    
    return (
      <React.Fragment>
        {this.props.showHeader !== false && (
          <CardHeader className='header'>Postcard Order</CardHeader>
        )}
        <div className="order-preview-container"
        >
          {/* <div className="order-preview-img">
            <img src={img} />
          </div> */}
          <Table striped>
            <tbody>
              <tr>
                <td>Neighbors to Address</td>
                <td>{searchAddress}</td>
              </tr> 
              <tr>
                <td>Postcards Amount</td>
                <td>{amount.length}</td>
              </tr> 
              <tr>
                <td>Postcard size</td>
                <td>{size}</td>
              </tr>
              <tr>
                <td>Search distance</td>
                <td>{`${(Math.floor( (distance*1000)/1609.344 ) / 1000 )}ml`}</td>
              </tr>  
              <tr>
                <td>Postage</td>
                <td>{postage}</td>
              </tr>
              <tr>
                <td>
                  <b>Total Amounts</b>
                </td>
                <td>
                  <b>${totalPrice}</b>
                </td>
              </tr>
            </tbody>
          </Table>
        </div>
      </React.Fragment>
    );
  }
}

export default Order;
