import React, { useState } from 'react';
import {
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap';
import { ReactComponent as DotsImg } from 'assets/img/icons/dots-vertical.svg';
import { fullNumberFormat } from 'utils/phones';
import './DropdownMenu.scss';

export default function Dropdawn(props) {
  const [dropdownOpen, setOpen] = useState(false);
  const job = props.data.job;
  const toggle = () => setOpen(!dropdownOpen);
  return (
    <ButtonDropdown
      className='dope-dropdown'
      direction='left'
      isOpen={dropdownOpen}
      toggle={toggle}
    >
      <DropdownToggle className='dots-btn'>
        <DotsImg />
      </DropdownToggle>
      <DropdownMenu className='tracking-numbers-dots-menu' positionFixed>
        <DropdownItem
          onClick={() => {
            props.editTrackingNumberModal(true);
            props.setEditTrackingNumberData({
              name: props.data.name,
              forwardingNumber: props.data.forwardingNumber,
              trackingNumberId: props.data._id,
              trackingNumber: fullNumberFormat(props.data.trackingNumber),
            });
          }}
        >
          Edit
        </DropdownItem>
        {props.data.orderStatus === 'reserved' ? (
          <DropdownItem
            onClick={() => {
              props.activateTrackingNumberModal(true);
              props.setActivateTrackingNumberData({
                name: '',
                trackingNumber: fullNumberFormat(props.data.trackingNumber),
              });
            }}
          >
            Activate
          </DropdownItem>
        ) : (
          <DropdownItem
            onClick={() => {
              props.deactivateTrackingNumberModal(true);
              props.setDeactivateTrackingNumberData({
                trackingNumber: fullNumberFormat(props.data.trackingNumber),
              });
            }}
          >
            Deactivate
          </DropdownItem>
        )}
        <DropdownItem disabled={!job || !job._id} onClick={props.goToContact}>
          Who Called
        </DropdownItem>
        <DropdownItem
          onClick={() => {
            props.removeTrackingNumberModal(true);
            props.setRemoveTrackingNumberData({
              trackingNumberId: props.data._id,
              trackingNumber: props.data.trackingNumber,
            });
          }}
        >
          Delete
        </DropdownItem>
      </DropdownMenu>
    </ButtonDropdown>
  );
}
