import React from 'react';
import StepTitle from '../../../components/StepTitle';
import Select from 'react-select';
import { FormGroup, Card, CardHeader, CardBody } from 'reactstrap';
import './SelectList.scss';

export default function SelectList(props) {
    let lists = [];
    props.contactList.forEach(list => {
        if (list.contactsQty)
            lists.push({ value: list, label: list.name, properties: list.listProperties || '' });
    });
    return (
        <Card className='wizard-section contact-list-container'>
            <CardHeader
                className='wizard-section-header'
                children={<StepTitle {...props.steps[1]} />}
            />
            <CardBody>
                <FormGroup>
                    <Select
                        className="dope-select"
                        classNamePrefix="dope"
                        options={lists}
                        onChange={e => {
                            props.onChange(e.value);
                            props.addErrors('contacts', '');
                        }}
                        value={lists.filter(option => option.value._id === props.selectedContactListId)}
                        isOptionDisabled={(option) => !option.properties || ( option.properties && !option.properties.verifiedContacts)}
                        placeholder='Select ...'
                    />
                    <div className='validation-message'>
                        {props.error.contacts}
                    </div>
                    <p>
                        *IF YOU HAVEN’T CREATED YOUR LISTS, CLICK “LISTS” IN THE
                        MENU ON THE LEFT
                    </p>
                </FormGroup>
            </CardBody>
        </Card>
    );
}
