import {
  GET_ADMIN_REQUESTS_REQUEST,
  GET_ADMIN_REQUESTS_SUCCESS,
  GET_ADMIN_REQUESTS_FAILED,
  RESET_ADMIN_REQUESTS_PAGE,
  GET_ADMIN_REQUEST_SUCCESS,
  RESET_ADMIN_REQUEST,
  SET_ADMIN_UPLOAD_CSV_DATA,
  GET_ADMIN_LISTS_UPLOAD_CSV_SUCCESS,
} from 'actions/adminRequests';

let initialState = {
  loading: false,
  requestsData: {},
  adminRequest: {
    // loading: false,
    adminUploadCsvData: {
      listId: "",
      name: "",
      isFirstRowHeader: false,
    },
    allLists: [],
  },
};

export default function reducer(state = initialState, action) {
  switch (action.type) { 
    case GET_ADMIN_REQUESTS_REQUEST:
      return {
        ...state,
        loading: true,
      }; 
    case GET_ADMIN_REQUESTS_SUCCESS:
      return {
        ...state,
        loading: false,
        requestsData: action.data,
      };
    case GET_ADMIN_REQUESTS_FAILED:
      return {
        ...state,
        loading: false,        
      }; 
    case RESET_ADMIN_REQUESTS_PAGE:
      return {
        ...initialState,
      }; 
    case GET_ADMIN_REQUEST_SUCCESS:
      return {
        ...state,        
        adminRequest:{
          // loading: false,
          ...state.adminRequest,
          data: action.data,
        }
      }
    case RESET_ADMIN_REQUEST:
      return {
        ...state,
        adminRequest:{},
      }
    case SET_ADMIN_UPLOAD_CSV_DATA:
      return {
        ...state,
        adminRequest:{
          ...state.adminRequest,
          adminUploadCsvData:{
            ...state.adminRequest.adminUploadCsvData,
            [action.key]: action.value
          },          
        },
      }
    case GET_ADMIN_LISTS_UPLOAD_CSV_SUCCESS:
      return {
        ...state,
        adminRequest:{
          ...state.adminRequest,
          allLists: action.data,         
        },
      }
    default:
      return state;
  }
}