import React, { useState } from 'react';
import { FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import Ajv from 'ajv';
import { AccountSchema } from './config';
import { fullNumberFormat } from 'utils/phones';
import './EditTrackingNumberModal.scss';

const ajv = new Ajv({ allErrors: true });

export default function EditTrackingNumberModal(props) {
  const initialErrors = { name: '', forwardingNumber: '' };
  const { forwardingNumber, name } = props.editTrackingNumberData;

  const [errors, setErrors] = useState(initialErrors);
  
  const onClose = () => {
    props.toggleEditTrackingNumberModal(false);
  };

  const resetModal = () => {
    props.setEditTrackingNumberData({
      name: '',
      trackingNumberId: '',
      trackingNumber: '',
      forwardingNumber: '',
    });
    setErrors(initialErrors);
  };

  const modalValidation = () => {
    const formattedForBE = {
      ...props.editTrackingNumberData,
      forwardingNumber: fullNumberFormat(forwardingNumber),
    };

    let result = ajv.validate(AccountSchema, formattedForBE);
    const errors = {};

    if (!result) {
      ajv.errors.map(error => {
        const errorKey = error.dataPath.split('.').pop();
        errors[errorKey] = 'This field is required!';
      });

      setErrors(errors);
    }

    return result;
  };

  const onSubmit = () => {
    if (!modalValidation()) return;
    props.editTrackingNumber();
  };

  return (
    <div>
      <DopeModal
        isOpen={props.open}
        onClosed={resetModal}
        className='remove-tracking-number-modal'
        centered
      >
        <DopeModalHeader>Edit Tracking Number</DopeModalHeader>
        <DopeModalBody>
          <FormGroup className='dope-form-group'>
            <DopeInput
              title='Name'
              supText='*'
              value={name}
              error={errors.name}
              onChange={e => {
                props.setEditTrackingNumberData({
                  ...props.editTrackingNumberData,
                  name: e.target.value,
                });
                setErrors({ ...errors, name: '' });
              }}
            />
          </FormGroup>
          <FormGroup className='dope-form-group'>
            <DopeInput
              title='Forwarding Number'
              supText='*'
              mask='(999)999-9999'
              value={forwardingNumber}
              error={errors.forwardingNumber}
              onChange={e => {
                props.setEditTrackingNumberData({
                  ...props.editTrackingNumberData,
                  forwardingNumber: e.target.value,
                });
                setErrors({ ...errors, forwardingNumber: '' });
              }}
            />
          </FormGroup>
        </DopeModalBody>
        <DopeModalFooter>
          <DopeButton color='white' onClick={onClose}>
            Cancel
          </DopeButton>
          <DopeButton
            color='primary'
            disabled={!forwardingNumber || !name}
            onClick={onSubmit}
          >
            Save
          </DopeButton>
        </DopeModalFooter>
      </DopeModal>
    </div>
  );
}
