import React, {useState} from 'react'
import {Col, Container, FormGroup, Row} from "reactstrap";
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeTextarea } from 'components/DopeForms/DopeTextarea/DopeTextarea'
import CreditCards from "views/AccountDetails/CreditCardInfo/CreditCards";
import DeleteCardModal from "views/AccountDetails/CreditCardInfo/DeleteCardModal";
import {useDispatch} from "react-redux";
import {getCustomPaymentRequest} from "actions/adminAccounts";

const CustomPayment = (props) => {

  const [deleteCardState, onSetDeleteCard] = useState({modalShow: false, id: ''});
  const [amount, setAmount] = useState('');
  const [description, setDescription] = useState('');
  const [amountError, setAmountError] = useState('');
  const [descriptionError, setDescriptionError] = useState('');
  const [descriptionFieldBlur, setDescriptionFieldBlur] = useState(false);
  const [isSending, setIsSending] = useState(false);
  const dispatch = useDispatch();

  const deleteCard = () => {
    onSetDeleteCard({ ...deleteCardState, modalShow: !deleteCardState.modalShow });
    props.onDeleteCard(deleteCardState.id);
  };

  const setAmountValue = (e) => {
    e.target.value > 5000
      ? setAmountError('Max value 5000')
      : setAmountError('');

    setAmount(e.target.value);
  };

  const setDescriptionValue = (e) => {
    e.target.value.length < 15 && descriptionFieldBlur
      ? setDescriptionError('Please enter at least 15 letters')
      : setDescriptionError('');

    setDescription(e.target.value);
  };

  const onBlur = (e) => {
    setDescriptionFieldBlur(true);
    if(e.target.value.length < 15){
      setDescriptionError('Please enter at least 15 letters')
    }
  };

  const callbackSuccess = () => {
    setIsSending(false);
    setDescription('');
    setAmount('');
  };
  const callbackFailure = () => {
    setIsSending(false);
  };

  const onSubmit = () => {
    if(amount && description){
      const data = {
        accountId: props.currentAccount && props.currentAccount._id,
        body: {
          'amount': amount * 100,
          'description': description,
          'default_source': props.currentAccount && props.currentAccount.default_source
        }
      };
      setIsSending(true);
      dispatch(getCustomPaymentRequest(data, callbackSuccess, callbackFailure));
    }
  };

  return(
    <div className='contact-info-tab account-form-container'>
      <Container fluid>
        <Row>
          <Col md={6}>
            <h6 className="block-title">Transaction Information</h6>
            <FormGroup className="dope-form-group">
              <DopeInput
                value={amount}
                title='Amount ($)'
                name='amount'
                type='number'
                min='0'
                error={amountError}
                placeholder='Enter value'
                onChange={setAmountValue}
              />
            </FormGroup>
            <FormGroup className="dope-form-group">
              <DopeTextarea
                value={description}
                title='Description'
                name='description'
                error={descriptionError}
                onBlur={onBlur}
                onChange={setDescriptionValue}
              />
            </FormGroup>
          </Col>
          <Col md={6}>
            <CreditCards
              onDefaultCardChange = {props.onDefaultCardChange}
              onDeleteCard = {(cardId) => {onSetDeleteCard({ ...deleteCardState, modalShow: !deleteCardState.modalShow, id: cardId })}}
              currentAccount={props.currentAccount} />
          </Col>
        </Row>
        <div className='account-button-container'>
          <DopeButton
            disabled={!!amountError || !!descriptionError || !(amount && description.length >= 15) || isSending}
            color="primary"
            onClick={onSubmit}
          >
            Proceed
          </DopeButton>
        </div>
      </Container>
      <DeleteCardModal
        modalShow={deleteCardState.modalShow}
        onChange={() => {onSetDeleteCard({ ...deleteCardState, modalShow: !deleteCardState.modalShow})}}
        onConfirm={deleteCard}
      />
    </div>
  )
};

export default CustomPayment;