import React, { Component } from 'react';
import {CardHeader, Table } from 'reactstrap';
import {pathOr} from 'ramda';
// import './OrderAutomationPreview.scss';

class Order extends Component {
  // builderRef = React.createRef();

  render() {
    const name = pathOr([], ['automation', 'name'], this.props.data);
    const totalPrice = pathOr(0, ['automation_price'], this.props.data)/100;    
    
    return (
      <React.Fragment>
        {this.props.showHeader !== false && (
          <CardHeader className='header'>Postcard Order</CardHeader>
        )}
        <div className="order-preview-container"
        >
          {/* <div className="order-preview-img">
            <img src={img} />
          </div> */}
          <Table striped>
            <tbody>
              <tr>
                <td>Name</td>
                <td>{name}</td>
              </tr>
              <tr>
                <td>
                  <b>Total Amounts</b>
                </td>
                <td>
                  <b>${totalPrice}</b>
                </td>
              </tr>
            </tbody>
          </Table>
        </div>
      </React.Fragment>
    );
  }
}

export default Order;
