import {
	SET_YARD_SIGN_DATA,
	TEMPLATE_LOADING_YS,
	SET_SHIPPING_ADDRESS,
	SAVE_ALL_YARD_SIGN_TEMPLATES,
	SELECT_YARD_SIGN_TEMPLATE,
	YARD_SIGN_WIZARD_NEXT_STEP,
	EDIT_YARD_SIGN_TEMPLATE,
	ON_CHANGE_YARD_SIGN_TEMPLATE,
	SET_SELECTED_NUMBER,	
	SET_TRACKING_NUMBER,
	SET_OWN_NUMBER_YARD_SIGN,
	SET_YARD_SIGN_AMOUNT,
	SET_YARD_SIGN_SIZE,	
	SET_YARD_SIGN_TYPE,
  SET_YARD_SIGN_STAKES,
	UPDATE_SHIPPING_ADDRESS,
	UPDATE_YARD_SIGN_ORDER_NAME,
	SELECT_CREDIT_CARD_YARDSIGN,
	UPDATE_PHONE_NUMBERS,
	ADD_ERRORS_YARD_SIGNS,
	FINAL_MODAL_SHOW_YARD_SIGNS,
	RESET_WIZARD_YARD_SIGNS,
	DELETE_ERRORS_NUMBER_YARD_SIGNS,
	YARD_SIGN_DISABLE_BTN,
  TOGGLE_YS_SAVED_TEMPLATE_MODAL,
  GET_YARD_SIGN_PREVIEW_IMG_SUCCESS,
  MODIFICATE_YARD_SIGN_TEMPLATE,
  YARD_SIGN_NEXT_STEP,
  CHANGE_YARD_SIGN_TEMPLATE_TAB_INDEX,
  SHOW_MY_YARD_SIGN_TEMPLATES_LOADER,
  SHOW_RECENT_YARD_SIGNS_LOADER,
  RESET_SELECT_YARD_SIGN_TEMPLATE,
  CHOICE_SAMPLE_SIZE_TEMPLATE,
  CHANGE_YARD_SIGN_STATUS,
  GET_MY_YARD_SIGN_TEMPLATES_SUCCESS,
  GET_RECENT_YARD_SIGNS_SUCCESS,
  
} from '../actions/createYardSign';


let initialState = {
	step: 1,
  templateTabIndex: '1',	   
	previewData: {},
	loading: false,
	newTemplateIsLoading: false,
  changeYardSignStatus: false,
	updateAddresses: false,
	finalModalShow: false,
	disableSubmitBtn: false,
  showSaveTemplateModal: false,
	errors: {},
  templateSample:{
    _id: '',
    type: 'simple',
    template: {
      '18x24':{
        front:           
          `
          <div class="morpheus-card-body" style="-ms-flex: 1 1 auto; -ms-flex-direction: column; -webkit-box-direction: normal; -webkit-box-flex: 1; -webkit-box-orient: vertical; -webkit-box-sizing: inherit; background-color: #ffffff; box-sizing: inherit; display: flex; flex: 1 1 auto; flex-direction: column; height: 100%; min-height: 0; overflow: hidden; pointer-events: none; position: relative;">

          </div>
          `
      }
    }
    // front:           
    //       `
    //       <div class="morpheus-card-body" style="-ms-flex: 1 1 auto; -ms-flex-direction: column; -webkit-box-direction: normal; -webkit-box-flex: 1; -webkit-box-orient: vertical; -webkit-box-sizing: inherit; background-color: #ffffff; box-sizing: inherit; display: flex; flex: 1 1 auto; flex-direction: column; height: 100%; min-height: 0; overflow: hidden; pointer-events: none; position: relative;">

    //       </div>
    //       `
  },  
	data: {
		selectedNumber: 'trackingPhoneNumber',		
		trackingNumber: '',
		ownNumber: '',
		forwardingNumber: '',
		amount: 10,
		name: '',
    stakes: false,
		cardId: '',
		billingAddress: {},
		shippingAddress: {
			address1: '',
			address2: '',
			city: '',
			state: '',
			zip: '',
		},
		size: '18x24',
		side: 'one-sided',
		template: {},
	},
  templates: {
    page: 0,
    limit: 15,
    data:[]
  },
  yardSignRecentTemplates:{   
    loading: false,     
    // page: 0,
    // pages: 0,
    // limit: 10,
    // totalSize: 0,
  },
  yardSignMyTemplates:{
    loading: false,
    // page: 0,
    // pages: 0,
    // limit: 10,
    // totalSize: 0,
  } 
};

export default function reducer(state = initialState, action) {
	switch (action.type) {
		case SET_YARD_SIGN_DATA:
			return {
				...state,
				data: {
					...state.data,
					[action.key]: action.value
				},
			};
    case CHANGE_YARD_SIGN_TEMPLATE_TAB_INDEX:
      return {
				...state,
				templateTabIndex: action.data,        
			};
    case GET_MY_YARD_SIGN_TEMPLATES_SUCCESS:
      return {
        ...state,
        yardSignMyTemplates: {
          ...state.yardSignMyTemplates,
          ...action.data,
        }
      };
    case GET_RECENT_YARD_SIGNS_SUCCESS:
      return {
        ...state,
        yardSignRecentTemplates: {
          ...state.yardSignRecentTemplates,
          ...action.data,
        }
      };
		case SET_SHIPPING_ADDRESS:
			return {
				...state,
				data: {
					...state.data,
					shippingAddress: {
						...state.data.shippingAddress,
						...action.address
					}
				},
			};
    case CHANGE_YARD_SIGN_STATUS:
      return {
        ...state,
        changeYardSignStatus: action.payload,
      };
		case SET_SELECTED_NUMBER:
			return {
				...state,
				data: {
					...state.data,
					selectedNumber: action.value
				}
			};				
		case SET_TRACKING_NUMBER:            
			return {
				...state,
				data: {
					...state.data,
					trackingNumber: action.data,
				},
			};
		case SET_OWN_NUMBER_YARD_SIGN:              
			return {
				...state,
				data: {
					...state.data,
					ownNumber: action.value,
				},
			};
		case SAVE_ALL_YARD_SIGN_TEMPLATES:
			return {
				...state,
				templates: action.templates,
			};
		case SELECT_YARD_SIGN_TEMPLATE:
			return {
				...state,	        			
        templateSample: action.sampleTemplate,
        data:{
          ...state.data,
          template: action.editTemplate,
        }        			
			};
    case CHOICE_SAMPLE_SIZE_TEMPLATE:
      return {
        ...state,	
        data:{
          ...state.data,
          template: action.template,
        } 
      }
    case RESET_SELECT_YARD_SIGN_TEMPLATE:
      return {
        ...state,
        templateSample: initialState.templateSample,
        data:{
          ...state.data,
          template: {}
        }
      };
		case EDIT_YARD_SIGN_TEMPLATE:
			return {
				...state,
				data: {
					...state.data,
					template: {
						...state.data.template,
						...action.template
					} 
				},
			};

		case ON_CHANGE_YARD_SIGN_TEMPLATE:
			return {
				...state,
				data: {
					...state.data,
					[action.key]: action.value
				}
			};
		case SET_YARD_SIGN_AMOUNT:
			return {
				...state,
				data: {
					...state.data,
					amount: action.amount
				}
			};
		case SET_YARD_SIGN_SIZE:
			return {
				...state,
				data: {
					...state.data,
					size: action.size
				}
			};
		case SET_YARD_SIGN_TYPE:			
			return {
				...state,
				data: {
					...state.data,
					side: action.side
				}
			};		
    case SET_YARD_SIGN_STAKES:      
      return {
				...state,
				data: {
					...state.data,
					stakes: action.value
				}
			};
		case UPDATE_SHIPPING_ADDRESS:
			return {
				...state,
				data: {
					...state.data,
					shippingAddress: {
						...state.data.shippingAddress,
						[action.key]: action.value
					}
				}
			};
		case UPDATE_YARD_SIGN_ORDER_NAME:
			return {
				...state,
				data: {
					...state.data,
					name: action.name
				}
			};
		case SELECT_CREDIT_CARD_YARDSIGN:
			return {
				...state,
				data: {
					...state.data,
					cardId: action.id
				}
			};
		case UPDATE_PHONE_NUMBERS:
			return {
				...state,
				data: {
					...state.data,
					[action.key]: action.value
				}
			};
		case ADD_ERRORS_YARD_SIGNS:
			return {
				...state,
				errors: {
					...state.errors,
					[action.key]: action.value
				}
			};
		case DELETE_ERRORS_NUMBER_YARD_SIGNS:			
			const filtered = Object.keys(state.errors)
				.filter(item=> item!==action.key)
				.reduce((obj, key) => {
					obj[key] = state.errors[key];
					return obj;
				}, {});			
			return {
				...state,
				errors: {					
					...filtered
				}
			};
		case YARD_SIGN_WIZARD_NEXT_STEP:
			return {
				...state,
				step: action.step
			};
		case FINAL_MODAL_SHOW_YARD_SIGNS:
			return {
				...state,
				finalModalShow: action.payload
			};
		case RESET_WIZARD_YARD_SIGNS:
			return {				
				...initialState
			};
    case TEMPLATE_LOADING_YS:
      return {
        ...state,
        templateLoadingYS: action.data
      };
    case YARD_SIGN_DISABLE_BTN:
      return {
        ...state,
        disableSubmitBtn: action.data
      };
    case TOGGLE_YS_SAVED_TEMPLATE_MODAL:
      return {
        ...state,
        showSaveTemplateModal: !state.showSaveTemplateModal,
      };
    case GET_YARD_SIGN_PREVIEW_IMG_SUCCESS:
      return {
        ...state,
        data: {
          ...state.data,          
          images: action.data,
        },        
      };
    case MODIFICATE_YARD_SIGN_TEMPLATE:      
      return {
        ...state,
        data: {
          ...state.data,
          template: { 
            ...state.data.template,           
            ...action.data,
          },
        },
      };
    case SHOW_MY_YARD_SIGN_TEMPLATES_LOADER:
      return{
        ...state,
        yardSignMyTemplates:{
          ...state.yardSignMyTemplates,
          loading: action.data,          
        }
      }
    case SHOW_RECENT_YARD_SIGNS_LOADER:
      return{
        ...state,
        yardSignRecentTemplates:{
          ...state.yardSignRecentTemplates,
          loading: action.data,          
        }
      }
    case YARD_SIGN_NEXT_STEP:
      return {
        ...state,
        step: action.step,
      };
		default:
			return state;
	}
}
