import React, {createRef, useEffect} from 'react';
import {Map, TileLayer, ZoomControl} from 'react-leaflet';
import { useSelector } from 'react-redux';
import './LeafletMap.scss';
import MarkerCluster from "./MarkerCluster";

export default function LeafletPinsMap({listContacts, activeTab, tabId}) {

  const mapPosition = useSelector(state => state.lists.mapPosition) || {};
  const zoom = useSelector(state => state.lists.zoom);
  const isLoading = useSelector(state => state.lists.isLoading);
  const ref = createRef();

  useEffect(() => {
    if(activeTab === tabId) ref.current.leafletElement.invalidateSize(false)
  }, [activeTab]);

  return (     
    <Map
      ref={ref}
      className={`${isLoading ? 'loading' : ''}`}
      center={[mapPosition.lat, mapPosition.lng]}
      zoomSnap={0.25}
      zoom={zoom}
      maxZoom={50}
      attributionControl={true}
      zoomControl={false}
      doubleClickZoom={true}
      scrollWheelZoom={true}
      dragging={true}      
      easeLinearity={0.35}
    >
      <div className="loading-indicator">Processing...</div>
      <ZoomControl position="bottomleft"/>
      <TileLayer
        url={`http://{s}.google.com/vt/lyrs=m&hl=en&x={x}&y={y}&z={z}`}
        subdomains={['mt0','mt1','mt2','mt3']}            
      />
      <MarkerCluster markers={listContacts.pins} />
    </Map>
  );
}
