import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
// import DeletePostcardModal from './DeletePostcardModal';
import Pagination from 'rc-pagination';
import { FadeLoader } from 'react-spinners';
import { ReactComponent as PrevIco } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as NextIco } from 'assets/img/icons/chevron-right.svg';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import DeletePostcardModal from './DeletePostcardModal';
import { 
  getMyEddmPostcardsRequest,
  deleteMyEddmPostcardRequest,
} from 'actions/eddm';

export default function MyPostcardTemplates({currentAccountId,onSelectTemplate,selectedTemplate}) {
  // console.log("MyPostcardTemplates props: ",props);  
  const dispatch = useDispatch();
  useEffect(() => {
    // dispatch(getMyEddmPostcardsRequest({accountId: currentAccountId, query:{ type: 'eddm', page:0, limit: 10 }}));
    dispatch(getMyEddmPostcardsRequest({accountId: currentAccountId, query:{ page:0, limit: 10, types: 'postcard,eddm,neighborhood' }}));
    // , sort: 'createdAt:-1'
  },[]);

  const templates = useSelector(state => state.eddm.eddmMyTemplates.data);
  const loading = useSelector(state => state.eddm.eddmMyTemplates.loading);
  const page = useSelector(state => state.eddm.eddmMyTemplates.page);
  const total = useSelector(state => state.eddm.eddmMyTemplates.totalSize);
  const limit = useSelector(state => state.eddm.eddmMyTemplates.limit);

  const [deleteTemplateId,setDeleteTemplateId] = useState('');
  const [showDeleteTemplateModal,setShowDeleteTemplateModal] = useState(false);
  const deleteMyPostcard = (id)=>{    
    dispatch(deleteMyEddmPostcardRequest({accountId: currentAccountId, templateId: id}, ()=>onPageChange(page + 1)));    
    setShowDeleteTemplateModal(false);
  }
  const onPageChange = page => {
    // dispatch(getMyEddmPostcardsRequest({accountId: currentAccountId, query:{ type: 'eddm', page: page - 1, limit}}));
    dispatch(getMyEddmPostcardsRequest({accountId: currentAccountId, query:{ page: page - 1, limit, types: 'postcard,eddm,neighborhood'}}));
  };
  return (
    <React.Fragment>
      <div className={`templates-preview-container${(templates&&templates.length<=5)?' oneRow':''}`}>
        {
          templates && templates.length ? (
            templates.map((template) => (             
              <div className='template-preview-item' key={template._id}>
                <div
                  className='template-item'
                  onClick={() => {                        
                    onSelectTemplate(template);                    
                  }}
                >
                  <div className={`template-preview-content${selectedTemplate === template._id ? ' checked':''}`}>
                    <img
                      className="template-preview-img"
                      src={template.front_thumbnail}
                      alt='postcard template'
                    />               
                  </div>
                  <div className='postcards-preview-name-block'>
                    <span className="name-text">{template.name}</span>                    
                  </div>
                  <div className='postcards-action-block'>                      
                    <DeleteIcon 
                      onClick={ (e)=>{
                        e.stopPropagation();
                        setDeleteTemplateId(template._id);
                        setShowDeleteTemplateModal(true);
                        // setImages(template.images);
                      }}
                    />
                  </div> 
                </div>
              </div>
            ))
          ) : (            
            <div className="no-templates-container">
              No Templates
            </div>            
          )          
        }
        {
          loading && (
            <div className='loader-main'>
              <FadeLoader size={100} color={'#403d39'} />
            </div>
          )
        }
      </div> 
      {templates && templates.length ? (
        <Pagination
          className='dope-pagination'
          current={page + 1}
          defaultPageSize={+limit}
          total={total}
          hideOnSinglePage={true}
          onChange={onPageChange}          
          prevIcon={<PrevIco />}
          nextIcon={<NextIco />}
          jumpPrevIcon={<span>...</span>}
          jumpNextIcon={<span>...</span>}
          showTitle={false}
        />
      ) : null}  
      <DeletePostcardModal 
        open={showDeleteTemplateModal}
        toggle={()=>setShowDeleteTemplateModal(!showDeleteTemplateModal)}
        deleteMyPostcard={()=>deleteMyPostcard(deleteTemplateId)}        
        // images={images}
      />   
    </React.Fragment>
  );
}
