import React from 'react';
import DopeRadioGroup from 'components/DopeForms/DopeRadioGroup';
import Select from 'react-select';
import { getFontWeightName } from 'utils/helper';

import './FontFamilyChoicer.scss';

class FontFamilyChoicer extends React.Component { 
  
  componentDidMount() {
    if(this.props.selectedFontFamily){
      let bdFont = this.props.yardSignFonts.find(fontitem=>fontitem.fontName===this.props.selectedFontFamily);
      if(bdFont){
        console.log("FIND FONT:",bdFont);
        this.props.onFontFamilyChange(bdFont);
      }
    }
  }
  componentDidUpdate(prevProps){   
   

    if(this.props.selectedFontFamily && prevProps.selectedFontFamily!==this.props.selectedFontFamily){
      let bdFont = this.props.yardSignFonts.find(fontitem=>fontitem.fontName===this.props.selectedFontFamily);
      if(bdFont){        
        this.props.onFontFamilyChange(bdFont);
      }
    }

    if(this.props.selectedFontFamilyItem && prevProps.selectedFontFamilyItem!==this.props.selectedFontFamilyItem){
      if(Object.keys(this.props.selectedFontFamilyItem).length){
        if(!Object.keys(this.props.selectedFontFamilyItem.objWeight).includes(this.props.selectedFontWeight.toString())){
          this.props.onFontWeightChange(Object.keys(this.props.selectedFontFamilyItem.objWeight)[0])
        }
      }
    }
  }
  handleFamilyChange = e => {
    this.props.onFontFamilyChange(e)
  }
  handleWeightChange = e => {        
    this.props.onFontWeightChange(e)
  }
  
  // formatOptionLabel = item => (item.fontName);
  formatFontWeight = (obj)=>{    
    return Object.keys(obj).map(el=>({"label":getFontWeightName(el),"value":+el}))
  }  
  render() {      
    let {objWeight={}} = this.props.selectedFontFamilyItem;
    let fontOptions = this.props.yardSignFonts.map(font=>({value: font, label: font.fontName }));  
    return (
      <div className="FFW-container">
        
        <h3 className="FFW-title">Font Family</h3>
        <div className="select-box">
          <Select
            className='dope-select'
            // menuIsOpen={true}
            classNamePrefix='dope'            
            placeholder='Select an new font'
            // formatOptionLabel={this.formatOptionLabel}
            options={fontOptions}
            value={fontOptions.filter(font => font.label===this.props.selectedFontFamilyItem.fontName)}
            onChange={(e)=>this.handleFamilyChange(e.value)}
          />
        </div>
        
        {Object.keys(objWeight).length?(
          <React.Fragment>
            <h3 className="FFW-title">Font Bold</h3>
            <DopeRadioGroup        
              data={this.formatFontWeight(objWeight)}            
              name="fontWeight"
              selectedValue={+this.props.selectedFontWeight}
              onChange={(e)=>this.handleWeightChange(e)}				             
            />
          </React.Fragment>
        ):null}       
      </div>
    );
  }
}

export default FontFamilyChoicer;