import React, { useEffect, useState } from 'react';
import {useSelector, useDispatch} from 'react-redux';
import orderBy from 'lodash/orderBy';
// import ColeDataTable from './ColeDataTable';
import AxleDataTable from './AxleDataTable';
import { TabPane, NavLink , Nav, NavItem } from 'reactstrap';
import { DopeTabContent } from 'components/DopeNav/DopeTabContent';
import InputRange from "react-input-range";
import {Collapse} from "reactstrap";
import DatePicker from "react-datepicker";
import moment from 'moment';
import { ReactComponent as ArrowUpIcon } from 'assets/img/icons/chevron-up.svg';
import { ReactComponent as ArrowDownIcon } from 'assets/img/icons/chevron-down.svg';
import "react-datepicker/dist/react-datepicker.css";
import classnames from 'classnames';
import {
  resetAdminUsingDataPage,
  getAdminUsingDataRequest,
  // getAdminUsingDataColeRequest,
  // getAdminUsingDataAxleRequest,
} from 'actions/adminUsingData';
import './UsingData.scss';

export default function UsingData() {
  const dispatch = useDispatch(); 
  useEffect(() => {
    dispatch(getAdminUsingDataRequest({page: 0, limit: 10, requestType:'data-axle'}));
    window.scrollTo({ top: 0, behavior: 'smooth' });
    return () => {dispatch(resetAdminUsingDataPage())};
  }, []);
  const usingData = useSelector(state => state.adminUsingData.usingData);
  const loading = useSelector(state => state.adminUsingData.loading);
  const [pagPage, setPagPage] = useState(usingData.page);
  const [sorting, setSorting] = useState(null);
  const [activeTableTab, setActiveTableTab] = useState('data-axle');
  const [showFilters, setShowFilters] = useState(false);  
  const [dateRange, setDateRange] = useState({
    min:new Date(),
    max:new Date(),
  });
  const [countRange, setCountRange] = useState({
    min:0,
    max:0
  });
  const [filters, setFilters] = useState({
    timestamp:{
      from: new Date(),
      to: new Date(),
    },
    count:{
      min:0,
      max:0
    },
  });  
  useEffect(() => {
    if(usingData.data.length){
      let min = Math.min(...usingData.data.map(e=>e.data.count));   
      let max = Math.max(...usingData.data.map(e=>e.data.count));
      let minDate = moment(Math.min(...usingData.data.map(e=>moment(e.data.lastItem).format("yyyyMMDD"))).toString()).format();   
      let maxDate = moment(Math.max(...usingData.data.map(e=>moment(e.data.lastItem).format("yyyyMMDD"))).toString()).format();
      if(isFinite(min)&&isFinite(max)){
        setFilters({
          ...filters,
          timestamp:{
            from: new Date(minDate),
            to: new Date(maxDate), 
          },
          count:{
            min,
            max,
          }
        });
        setDateRange({          
          min: new Date(minDate),
          max: new Date(maxDate),          
        })
        setCountRange({
          min,
          max,
        }); 
      }
    }
    setPagPage(0);
  }, [usingData.data]);
 
  
  const toggleTableTab = (tab) => {
    if (activeTableTab !== tab) {      
      dispatch(getAdminUsingDataRequest({page: 0, limit: 10, requestType:tab}));      
      setActiveTableTab(tab);      
    }
  };
  const getPagination = ({page,limit})=>{
    setPagPage(page-1);    
  }
  const getSorting = (sort)=>{
    setSorting(sort);
  }
  const sortData = (inData)=>{
    if (!sorting) return inData;    
    return {
      ...inData,
      data: orderBy(
        inData.data,      
        item=>{
          switch (sorting.field){
            case 'user':
              return  item.data.name            
            case 'timestamp':
              return  item.data.lastItem
            case 'count':
              return  item.data.count                       
            case 'purchased':
              return  item.data.priceTotal
            default:
              return  item.data.name 
          }              
        },
        sorting.order
      )
    }
  }
  const filterData = (inData)=>{
    let fdfrom = moment(filters.timestamp.from).format("yyyyMMDD");
    let fdto = moment(filters.timestamp.to).format("yyyyMMDD");
    return {
      ...inData,
      data: inData.data
      .filter(el=>((el.data.count>=filters.count.min)&&(el.data.count<=filters.count.max))) 
      .filter(el=>{
        let udtitem = moment(el.data.lastItem).format("yyyyMMDD");        
        return (udtitem>=fdfrom)&&(udtitem<=fdto)
      })      
    }
  }
  const FEPagination = (data) =>{
    let page
    if(pagPage<(data.data.length/data.limit)){
      page=pagPage;
    }else {
      if(data.data.length/data.limit>1){
        page=Math.ceil(data.data.length/data.limit)-1;  
      } else {
        page=0;
      } 
    }
    return {
      ...data,      
      page: page,  
      totalSize: data.data.length,
      data: data.data.filter((item,i)=>(i>=(page)*data.limit)&&(i<(page+1)*data.limit)),     
    }
  }
  const toggleUDFilters = ()=>{    
    setShowFilters((prevState)=>(!prevState))
  }
  const resetUDFilters = ()=>{
    setFilters({
      ...filters,
      timestamp:{
        from: dateRange.min,
        to: dateRange.max, 
      },
      count:{
        min: countRange.min,
        max: countRange.max,
      }
    });
  }
  const resetUDFilterBtn = ()=>{
    return (moment(filters.timestamp.from).format("yyyyMMDD")===moment(dateRange.min).format("yyyyMMDD"))&&
      (moment(filters.timestamp.to).format("yyyyMMDD")===moment(dateRange.max).format("yyyyMMDD"))&&
      (filters.count.min===countRange.min)&&
      (filters.count.max===countRange.max)    
  }
  return (
    <div className='UsingData'>  
      <div className='page-title-block'>
        <h2 className='page-title'>Using Data</h2>
      </div>
      <div className="filters-container">
        <div className="udbtn-container">
          <button className='dope-button btn btn-primary' onClick={() => toggleUDFilters()}>
            Filters
            {showFilters?<ArrowUpIcon/>:<ArrowDownIcon/>}          
          </button>
          {!resetUDFilterBtn()&&
            <button className='dope-button btn btn-white' onClick={() => resetUDFilters()}>
              Reset                   
            </button>
          }
        </div>
        <Collapse isOpen={showFilters}>
          <div className="filters-wrapper">
            <div className="filter-wrapper">
              <h4 className="filter-title">Timestamp</h4>
              <div className="date-filters-wrapper">
                <div className="date-filter">
                  <label htmlFor="UD-from">From</label>
                  <DatePicker
                    id="UD-from"
                    className='dope-input form-control'
                    selected={filters.timestamp.from}
                    minDate={dateRange.min}
                    maxDate={filters.timestamp.to||dateRange.max}
                    onChange={date => setFilters({...filters, timestamp:{...filters.timestamp,from:date}})}
                    // onChange={date => {}}
                    dateFormat="MMM dd,yyyy"                    
                  />
                </div>
                <div className="date-filter">
                  <label htmlFor="UD-to">To</label>
                  <DatePicker
                    id="UD-to"
                    className='dope-input form-control'
                    selected={filters.timestamp.to}
                    minDate={filters.timestamp.from||dateRange.min}
                    maxDate={dateRange.max}
                    onChange={date => setFilters({...filters, timestamp:{...filters.timestamp,to:date}})}
                    // onChange={date => {}}
                    dateFormat="MMM dd,yyyy"                    
                  />
                </div>
              </div>
            </div>
            <div className="filter-wrapper">
              <h4 className="filter-title">Count</h4>
              <div className="count-range">
                <InputRange                  
                  className='filter-range'                  
                  maxValue={countRange.max}
                  minValue={countRange.min}
                  value={filters.count}
                  onChange={value => setFilters({...filters, count: value})}              
                />
              </div>
            </div>
          </div>
        </Collapse>
      </div>
      <div className='usdata-container'> 
        <Nav tabs className="usdata-nav-tabs">
          {/* <NavItem className="usdata-nav-item">
            <NavLink
              className={classnames({
                active: activeTableTab === 'cole'
              })}
              onClick={() => {toggleTableTab('cole')}}
            >
              Cole Data API
            </NavLink>
          </NavItem> */}
          <NavItem className="usdata-nav-item">
            <NavLink
              className={classnames({
                active: activeTableTab === 'data-axle'
              })}
              onClick={() => {toggleTableTab('data-axle')}}
            >
              Axle Data API
            </NavLink>
          </NavItem>                     
        </Nav> 
        <div className='usdata-table'>          
          <DopeTabContent activeTab={activeTableTab}>
            {/* <TabPane tabId="cole">
              <div className='usdata-table'>   
                <ColeDataTable
                  usingData={FEPagination(sortData(filterData(usingData)))}                   
                  getData={getPagination} 
                  sortData={getSorting}                 
                  loading={loading}
                />                 
              </div>
            </TabPane>                 */}
            <TabPane tabId="data-axle">
              <div className='usdata-table'>                
                <AxleDataTable
                  usingData={FEPagination(sortData(filterData(usingData)))}
                  getData={getPagination} 
                  sortData={getSorting}                 
                  loading={loading}         
                />
              </div>
            </TabPane>            
          </DopeTabContent>          
        </div>        
      </div>  
    </div>
  );  
}