export default function reducer(state = initialState, action) {
  switch (action.type) { 
    case RESET_CREATE_EDDM:
      return {
        ...initialState,
      };   
    case SET_SELECTED_ROUTES:
      return {
        ...state,
        selectedRoutes: action.data, 
      };
    case RESET_SELECTED_ROUTES:
      return {
        ...state,
        selectedRoutes: [],
        totalSelectedRouteAddr: 0
      };      
    case RESET_FEATURES:
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,
          featureCollection:{
            ...state.featureCollection,
            features: [],
          } 
        },        
      }; 
    case SET_MAP_POSITION:
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,
          mapPosition: action.data,
        }
      }; 
    case SET_POLIGON_PROPERTIES:      
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,
          // poligonProperties: action.data,
          poligonProperties: action.data.properties,
          // poligonPropertiesBack: action.data.featureCollectionBack ? action.data.featureCollectionBack.routes.find(el=>el.zipcrid===action.data.properties.zipcrid) : {},
          poligonPropertiesBack: action.data.featureCollectionBack ? action.data.featureCollectionBack.routes.find(el=>el.zipcrid===action.data.properties.ZIP_CRID) : {},
        }
      }; 
    case RESET_POLIGON_PROPERTIES:
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,
          poligonProperties: {},
        }
      };
    case RESET_POLIGON_PROPERTIES_BACK:
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,
          poligonPropertiesBack: {
            routes: [],
          },
        }
      };
    case SET_MAP_POSITION_LOADING:
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,          
          mapPositionIsLoading: action.data,
        } 
      }; 
    // case GET_MAP_POSITION_BY_ZIP_REQUEST:
    //   return {
    //     ...state,
    //     eddmRouteMap:{
    //       ...state.eddmRouteMap,          
    //       mapPositionIsLoading: true,
    //     } 
    //   };
    // case GET_MAP_POSITION_BY_ZIP_REQUEST_SUCCESS:
    //   return {
    //     ...state,        
    //     eddmRouteMap:{
    //       ...state.eddmRouteMap,          
    //       mapPosition: action.data,
    //       mapPositionIsLoading: false,
    //       zoom: 15
    //     }        
    //   };
    // case GET_MAP_POSITION_BY_ZIP_REQUEST_FAILED:
    //   return {
    //     ...state,
    //     eddmRouteMap:{
    //       ...state.eddmRouteMap,          
    //       mapPositionIsLoading: false,
    //     } 
    //   };
    case GET_MAP_ROUTES_BY_ZIP_REQUEST_SUCCESS:
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,
          featureCollection: action.data.featureCollection,
          mapPosition: action.data.mapPosition,
          zoom: action.data.zoom
        }
      };
    case GET_ROUTES_STAT_BY_ZIP_REQUEST_SUCCESS:      
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,
          featureCollectionBack: action.data.featureCollection[0],          
        }
      };
    case SET_EDDMROUTEMAP_ERRORS:      
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,          
          errors:{
            ...state.eddmRouteMap.errors,
            ...action.errors,
          }
        } 
      };   
    case SET_ROUTE_VIEW:      
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,          
          routeView: action.data,
        } 
      }; 
    case SET_ZIP_FIND:      
      return {
        ...state,
        eddmRouteMap:{
          ...state.eddmRouteMap,          
          zipFind: action.data,
        } 
      }; 
    case SET_NEXT_STEP:      
      return {
        ...state,
        step: action.data,
      }; 
    case SET_EDDM_SIZE:      
      return {
        ...state,
        eddmSize: action.data,
      };
    case SET_EDDM_DESIGN:      
      return {
        ...state,
        eddmDesign: action.data,
      }; 
    case SET_EDDM_SERVICE:      
      return {
        ...state,
        eddmService: action.data,
      };
    case SET_EDDM_VENDOR:
      return {
        ...state,
        eddmVendor: action.data,
      };
    case SET_TOTAL_SELECTED_ROUTE_ADDR:      
      return {
        ...state,                 
        totalSelectedRouteAddr: action.data,
      }; 
    case SET_EDDM_CONTACT_INFO:      
      return {
        ...state,
        eddmEditor: {
          ...state.eddmEditor,
          contactInfo:{
            ...state.eddmEditor.contactInfo,
            [action.key]: action.value,
          }          
        },
      };   
    case SET_DELIVERY_INFO:    
      return {
        ...state,        
        eddmSummary:{
          ...state.eddmSummary,
          [action.key]: action.value,
        } 
      }; 
    case SET_SHIPPING_SERVICE:    
      return {
        ...state,        
        eddmSummary:{
          ...state.eddmSummary,
          shipingService: action.data,
        } 
      };
    case GET_EDDM_LIST_REQUEST:
      return {
        ...state,
        isLoading: true
      };
    case GET_EDDM_LIST_REQUEST_SUCCESS:
      return {
        ...state,        
        eddmList:{
          ...state.eddmList,
          ...action.data,
        }, 
        isLoading: false
      };
    case GET_EDDM_ITEM_REQUEST_SUCCESS:
      return {
        ...state,
        selectedRoutes: action.data.eddmRoutes.map(item => item.route),
        addressesType: action.data.eddmRoutes[0].addressesType,
        eddmRouteMap:{
          ...state.eddmRouteMap,
          zipFind: action.data.zip,
        },
        eddmSummary:{
          ...state.eddmSummary,
          deliveryAddress: action.data.deliveryAddress,
        },        
        previewImg: {
          ...state.previewImg,
          ...action.data.images,
        },
        // eddmDesign: action.data.eddmDesign,
        eddmDesign: action.data.status === 'design-request' ? 'askDesign' : 'ownDesign',
        eddmSize: action.data.size ? action.data.size : '6,5x9',
        eddmService: action.data.service,
        totalSelectedRouteAddr: action.data.totalDeliveryAddresses,
        eddmEditor:{
          ...state.eddmEditor,
          templateEdit:{
            ...state.eddmEditor.templateEdit,
            ...action.data.templates,
          },
          contactInfo:{
            ...state.eddmEditor.contactInfo,
            // ...action.data.designRequest.name,
            ...action.data.designRequest,
          }
        },
        status: action.data.status,
      };
    case GET_EDDM_PREVIEW_IMG_REQUEST_SUCCESS:
      return {
        ...state,
       previewImg: action.data,
      };
    case SET_READY_POSTCARD_EDDM:
      return {
        ...state,        
        readyPostcardEddm: {
          ...state.readyPostcardEddm, 
          [action.data.test]: action.data.template,         
        },        
      };
    // TODO change name to SET_EDDM_TEMPLATE_EDIT_BY_SIZE
    case SET_EDDM_TEMPLATE_EDIT:      
      return {
        ...state,
        eddmEditor: {
          ...state.eddmEditor,
          templateEdit: {
            ...state.eddmEditor.templateEdit,
            front: state.eddmEditor.templateSample[action.data].front,
            back: state.eddmEditor.templateSample[action.data].back,
          }, 
        },
      };
    case SET_MODIFICATED_EDDM_TEMPLATE_EDIT:      
      return {
        ...state,
        eddmEditor: {
          ...state.eddmEditor,
          templateEdit: {
            ...state.eddmEditor.templateEdit,
            ...action.data
          }, 
        },
      };
    // case SET_EDDM_SELECTED_IMAGES:
    //   return {
    //     ...state,
    //     eddmEditor: {
    //       ...state.eddmEditor,
    //       selectedImages: {
    //         ...state.eddmEditor.selectedImages,
    //         ...action.data
    //       }, 
    //     },
    //   };
    case SET_SHOW_CONFIRM_POPUP:      
      return {
        ...state,
        showConfirmPopup: action.data,
      };
    case SET_NEW_DELIVERY_ADDRESS:
      return {
        ...state,
        eddmSummary: {
          ...state.eddmSummary,
          deliveryAddress: action.value
        },
      };
    case SET_ADDRESSES_TYPE:
      return {
        ...state,
        addressesType: action.data
      };
    case ADD_ERRORS_EDDM:
      return {
        ...state,
        eddmEditor:{
          ...state.eddmEditor,
          errors: {
            ...state.errors,
            [action.key]: action.value,
          },
        }        
      };
    case CHANGE_EDDM_POSTCARD_TEMPLATE_TAB_INDEX:
      return {
        ...state,
        eddmEditor:{
          ...state.eddmEditor,
          templateTabIndex: action.data,
        }        
      };
    case SELECT_EDDM_MORPHEUS_TEMPLATE:     
      return{
        ...state,
        eddmEditor:{
          ...state.eddmEditor,
          selectedTemplate: action.template._id,
          templateEdit: {
            ...state.eddmEditor.templateEdit,
            front: action.template.front,
            back: action.template.back,
          }, 
          templateSample: {
            ...state.eddmEditor.templateSample,
            ...action.template.templateSample
          },
        },         
      };
    case SELECT_EDDM_ONE_PICTURE_TEMPLATE:
      return{
        ...state,
        eddmEditor:{
          ...state.eddmEditor,
          selectedTemplate: "",
          templateEdit: {
            ...state.eddmEditor.templateEdit,
            front: null,
            back: null,
          },          
        }  
      };
    case SET_CHANGE_EDDM_POSTCARD_STATUS:
      return{
        ...state,
        changeEddmPostcardStatus: {
          ...state.changeEddmPostcardStatus,
          ...action.data,
        }
      };
    case GET_RECENT_EDDM_POSTCARD_REQUEST_SUCCESS:          
      return{
        ...state,
        eddmRecentTemplates:{
          ...state.eddmRecentTemplates,
          ...action.data,          
        }
      };
    case SHOW_RECENT_EDDM_POSTCARDS_LOADER:
      return{
        ...state,
        eddmRecentTemplates:{
          ...state.eddmRecentTemplates,
          loading: action.data,          
        }
      }
    case GET_MY_EDDM_POSTCARD_REQUEST_SUCCESS:          
      return{
        ...state,
        eddmMyTemplates:{
          ...state.eddmMyTemplates,
          ...action.data,          
        }
      };
    case SHOW_MY_EDDM_POSTCARDS_LOADER:
      return{
        ...state,
        eddmMyTemplates:{
          ...state.eddmMyTemplates,
          loading: action.data,          
        }
      }
    default:
      return state;
  }
}
