import React from 'react';
import { ModalFooter, FormGroup } from 'reactstrap';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeTextarea } from 'components/DopeForms/DopeTextarea/DopeTextarea';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import Ajv from 'ajv';
import { AccountSchema } from './config';
import './EditList.scss';

const ajv = new Ajv({ allErrors: true });

export default function EditList(props) {
  // console.log("EditList   ",props);

  const onChange = e => {
    let errors = props.errors;
    errors[e.target.name] = '';

    props.addErrorsEditList(errors);

    props.onChangeEditList(e.target.name, e.target.value);
  };

  const onClose = () => {
    props.resetEditListForm();
    props.toggleEditListModal(false);
  };

  const editListValidation = () => {
    const formattedForBE = {
      ...props.data,
    };

    let result = ajv.validate(AccountSchema, formattedForBE);
    const errors = {};

    if (!result) {
      ajv.errors.map(error => {
        const errorKey = error.dataPath.split('.').pop();
        errors[errorKey] = 'This field is required!';
      });

      props.addErrorsEditList(errors);
    }

    return result;
  };

  const onSubmit = () => {
    if (!editListValidation()) return;    
    let req = {      
      account: props.currentAccount._id,    
      listId: props.data._id,      
      body: {list: props.data}
    };

    props.editListRequest(req);
  };
  
  return (
    <div>
      <DopeModal className='edit-list' isOpen={props.open} centered>
        <DopeModalHeader>Edit List</DopeModalHeader>
        <DopeModalBody> 
          <FormGroup className="dope-form-group">
            <DopeInput
              value={props.data.name || ''}
              title='Name'
              supText='*'
              name='name'
              error={props.errors.name || ''}
              onChange={onChange}
            />
          </FormGroup>
          <FormGroup className="dope-form-group">
            <DopeTextarea              
              value={props.data.description || ''}
              title='Description'              
              name='description'
              error={props.errors.description || ''}
              onChange={onChange}
            />   
          </FormGroup>
        </DopeModalBody>
        <ModalFooter className='dope-footer'>
          <DopeButton color='white' onClick={onClose}>
            Cancel
          </DopeButton>
          <DopeButton color='primary' onClick={() => onSubmit()}>
            Save
          </DopeButton>
        </ModalFooter>
      </DopeModal>
    </div>
  );
}