import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import CreditCard from '../../../components/CreditCard/CreditCard';
import ShippingAddress from './ShippingAddress';
import OrderSummary from './OrderSummary';
import { connect } from 'react-redux';
import {pathOr} from 'ramda';
import './Payment.scss';



class Payment extends React.Component {  

  checkOwnPricesForYardSign = (prices) => {
    return (
      Object.keys(prices).reduce((acc,cur)=>([
        ...acc,
        ...Object.keys(prices[cur]).reduce((accInt,curInt)=>([
          ...accInt,prices[cur][curInt].map(el=>el.price)])
          ,[])])
        ,[]).some(arr => arr.length)
    )
  }

  cost = () => {
    let priceObj = '';
    let selSize = this.props.data.size;
    let selType = this.props.data.side;

    if (this.props.userOwnPrice && this.checkOwnPricesForYardSign(this.props.userOwnPrice.yardSignPrice)) {
      priceObj = this.props.userOwnPrice.yardSignPrice;        
    }
    else{      
      priceObj = this.props.price.find(item => item.name === "yardSignPrice").value;      
    }  

    const quantity = pathOr(0, ['amount'],this.props.data)
    
    let price = '';

    priceObj[`${selType}`][`${selSize}`].forEach(item => {
      if(item.total === quantity)  price = item.price/100
    })    
    // return ( Math.ceil(quantity * price * 100) / 100).toFixed(2)
    let priceYard = Math.ceil(quantity * price * 100) / 100;
    if(this.props.data.stakes){
      priceYard += this.snakesCost();
    }

    return priceYard;       
  } 
  yardSignShippingService = () => {
    let priceArr = this.props.price.find(item => item.name === "yardSignShippingPrice").value;    
    let shipping = (priceArr.find(item => item.total === this.props.data.amount).price / 100);
    let stakesPriceArr = this.props.price.find(item => item.name === "stakeShippingPrice").value;    
    let stakesShipping = (stakesPriceArr.find(item => item.total === this.props.data.amount).price / 100);    
    if(this.props.data.stakes){
      shipping += stakesShipping;
    }
    return shipping;    
  } 
  snakesCost = () => {
    let snakesPriceObj = this.props.price.find(item => item.name === "stakePrice").value||1; 
    let snakesPrice = this.props.data.amount * snakesPriceObj.price / 100;    
    return snakesPrice;
  }
  total = () => {
    return this.cost() + this.yardSignShippingService();
  }

  render() {
    const props = this.props;
    // console.log("payments props:", props);
    return (
      <div className='order-step3'>    
        <Container fluid={true}>           
          <Row>
              <Col xl={7} lg={12} className='d-flex order-summary'>
                  <OrderSummary
                      onChange={this.props.onChange}                      
                      data={props.data}
                      price={this.cost()}
                      shippingService={this.yardSignShippingService()}
                      total={this.total()}
                      onChangeOrderName={props.onChange.updateYardSignOrderName}
                      errors={this.props.errors}
                      addErrors = {this.props.onChange.addErrors}
                  >Select a number of Yard Signs
                  </OrderSummary>
              </Col>
              <Col xl={5} lg={12} className='order-step3-img'>
                  <div className="order-step3-img-wrapper">
                      <img
                          src={this.props.data.images.front.mini}
                          alt='yard sign preview'
                      />
                  </div>
              </Col>
          </Row>
          <Row>
              <Col xl={7} lg={12} className='address-block'>
                  <ShippingAddress
                      data={props.data}
                      onChange={this.props.onChange.updateShippingAddress}
                      addErrors = {this.props.onChange.addErrors}
                      errors={props.errors}
                  />                  
              </Col>
              <Col xl={5} lg={12} className='credit-card-block'>
                  <CreditCard
                      currentAccount={props.currentAccount}
                      onChange={props.onChange.selectCreditCard}
                      data={props.data}
                      errors={this.props.errors}
                      addErrors = {this.props.onChange.addErrors}
                  >       
                  </CreditCard>
              </Col>
          </Row>         
        </Container>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  currentAccount: state.account.currentAccount,
  price: state.dbConfigs.configs
});

export default connect(mapStateToProps)(Payment);
