import React from 'react';
import BootstrapTable from 'react-bootstrap-table-next';
import Pagination from 'rc-pagination';
import localeInfo from 'rc-pagination/lib/locale/en_US';
import { FadeLoader } from 'react-spinners';
import { ReactComponent as PrevIco } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as NextIco } from 'assets/img/icons/chevron-right.svg';
import './DopeTable.scss';

export class DopeTable extends React.Component {
  onPageChange = page => {
    const {limit = 10} = this.props.paginationOptions;
    const pagination = { limit, page };
    this.props.paginationOptions &&
      this.props.paginationOptions.onPageChange(pagination);
  };

  render() {
    // const { limit, totalSize, page } = this.props.paginationOptions;    
    return (
      <div className='dope-table-container'>
        {this.props.loading && (
          <div className='loader-container'>
            <FadeLoader size={100} color={'#403d39'} />
          </div>
        )}
        <BootstrapTable className='dope-table' {...this.props} noDataIndication="No results found"/>
        {!this.props.woPagination&&
          <Pagination
            className='dope-pagination'
            current={this.props.paginationOptions.page}
            defaultPageSize={this.props.paginationOptions.limit}
            total={this.props.paginationOptions.totalSize}
            hideOnSinglePage={true}
            onChange={this.onPageChange}
            locale={localeInfo}
            prevIcon={<PrevIco/>}
            nextIcon={<NextIco/>}
            jumpPrevIcon={<span>...</span>}
            jumpNextIcon={<span>...</span>}
            showTitle={false}
          />
        }
      </div>
    );
  }
}
