import React from 'react'
import {
  Input,
  Label
} from 'reactstrap'
import classnames from 'classnames'

import './DopeTextarea.scss'

export const DopeTextarea = ({ id, title, error, supText, ...rest}) => {
  const classNames = classnames({ 'dope-textarea': true, 'form-control': true, 'error': error})
  const TextareaComponent = <Input className={classNames} id={id} {...rest} type="textarea" />

  return (
  <React.Fragment>
    { title && <Label className='textarea-title' htmlFor={id}>{title} {supText && <sup>{supText}</sup>}</Label> }
    { TextareaComponent }
    { error && <div className='validation-message'>{error}</div> }
  </React.Fragment>)
}