import React from 'react';
import './DopeRadio.scss';

const DopeRadio = ({label, checked, className, ...rest}) => {       
  return (
    <label className={`DopeRadio${className?` ${className}`:''}`}>
      <input
        type='radio'
        checked={checked}       
        {...rest}        
      />
      <span className="custom-radio"></span>        
      {
        label&&<span className="radio-label">{label}</span> 
      }           
    </label>
  );
}

export default DopeRadio;
