import React from 'react';
import {
  DopeModal,
  DopeModalHeader,
  DopeModalBody,
} from 'components/DopeModal';
import './PricingModal.scss';

export default function PricingModal({ open, toggle, data }) {
  return (
    <DopeModal isOpen={open} className='postcard-pricing-modal' centered>
      <DopeModalHeader toggle={toggle}>Pricing</DopeModalHeader>
      <DopeModalBody>
        <div className='dope-table-container postcard-pricing-table'>
          <div className='react-bootstrap-table'>
            <table className='table table-bordered'>
              <thead>
                <tr>
                  <th>Quantity</th>
                  <th>Standard Postage</th>
                  <th>First Class Postage</th>
                </tr>
              </thead>
              <tbody>
                {data.map((item, i) => (
                  <tr key={i}>
                    <td>{item.label}</td>
                    <td>${(item.price / 100).toFixed(2)}</td>
                    <td>${((item['first-class-price']? item['first-class-price'] : item.price )/ 100).toFixed(2)}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </DopeModalBody>
    </DopeModal>
  );
}
