import { all, call, put, takeLatest } from 'redux-saga/effects'
import { toast } from 'react-toastify';
import * as generalActions from 'actions/general';
import * as newTrackingNumbersActions from 'actions/newTrackingNumber';
import * as trackingNumbersActions from 'actions/trackingNumbers';
import { 
  getTrackingNumbersApi, 
  createExistingNumbersApi, 
  createSearchNumbersApi,
  deleteTrackingNumberApi,
  createTrackingNumberApi,
  editTrackingNumberApi,
  activateTrackingNumberApi,
  deactivateTrackingNumberApi,
} from 'requests/trackingNumbersRequests';
import {friendlyFormatPhoneNumber} from 'utils/phones';
import {pathOr} from 'ramda';

const errorText = "Tracking number hasn't been created.";

export function* getTrackingNumbers(action) {
  try {
    const response = yield call(getTrackingNumbersApi, action.data)
    yield put({ type: trackingNumbersActions.GET_TRACKING_NUMBERS_SUCCESS, data: response.data })
  } catch(error) {
    yield put({ type: trackingNumbersActions.GET_TRACKING_NUMBERS_FAILED, error })
  }
}

export function* createExistingNumbersRequest(action) {
  try {
    const response = yield call(createExistingNumbersApi, action.data)
    yield put({ type: newTrackingNumbersActions.CREATE_EXISTING_NUMBER_REQUEST_SUCCESS, data: response.data.data })
  } catch(e) {
    yield put({ type: newTrackingNumbersActions.CREATE_EXISTING_NUMBER_REQUEST_FAILED, error: e })
  }
}

export function* createTrackingNumberRequest(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
    const response = yield call(createTrackingNumberApi, action.data)
    yield put({ type: newTrackingNumbersActions.CREATE_TRACKING_NUMBER_REQUEST_SUCCESS, data: response.data })

    if(action.data.pagination) {
      yield put({ type: trackingNumbersActions.GET_TRACKING_NUMBERS, data: {...action.data.query,...action.data.pagination, } })
    }

    yield put({ type: newTrackingNumbersActions.SHOW_ADD_TRACKING_NUMBER_MODAL, data: false })
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.success(`Tracking number ${friendlyFormatPhoneNumber(action.data.body.trackingNumber)} has been created.`)
  } catch(e) {
    const errorMessage = pathOr(errorText, ['response', 'data', 'error', 'dopeMessage'], e);
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.error(`${errorMessage} Tracking number ${friendlyFormatPhoneNumber(action.data.body.trackingNumber)} hasn't been created.`)
    // toast.error(`Error. Tracking number ${friendlyFormatPhoneNumber(action.data.body.trackingNumber)} hasn't been created.`)    
    yield put({ type: newTrackingNumbersActions.CREATE_TRACKING_NUMBER_REQUEST_FAILED, error: e })
  }
}

export function* createSearchNumberRequest(action) {
  try {
    const response = yield call(createSearchNumbersApi, action.data)
    yield put({ type: newTrackingNumbersActions.SEARCH_NUMBERS_REQUEST_SUCCESS, data: response.data })
  } catch(e) {
    yield put({ type: newTrackingNumbersActions.SEARCH_NUMBERS_REQUEST_FAILED, error: e })
  }
}

export function* deleteTrackingNumber(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
    yield call(deleteTrackingNumberApi, action.data)
    yield put({ type: trackingNumbersActions.DELETE_TRACKING_NUMBER_SUCCESS, data: action.data })
    yield put({ type: trackingNumbersActions.GET_TRACKING_NUMBERS, data: {...action.data.query,...action.data.pagination} })
    yield put({ type: trackingNumbersActions.TOGGLE_REMOVE_TRACKING_NUMBER_MODAL, data: false })
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.success(`Tracking number ${action.data.params.trackingNumber} has been removed.`)
  } catch(error) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.error(`Error. Tracking number ${action.data.params.trackingNumber} hasn't been deactivated.`)
  }
}

export function* activateTrackingNumber(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
    const response = yield call(activateTrackingNumberApi, action.data)
    yield put({ type: trackingNumbersActions.ACTIVATE_TRACKING_NUMBER_REQUEST_SUCCESS, data: response.data.trackingNumber })
    yield put({ type: trackingNumbersActions.TOGGLE_ACTIVATE_TRACKING_NUMBER_MODAL, data: false })
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.success(`Tracking number ${friendlyFormatPhoneNumber(action.data.body.trackingNumber)} has been activated.`)
  } catch(e) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.error(`Error. Tracking number ${friendlyFormatPhoneNumber(action.data.body.trackingNumber)} hasn't been activated.`)
  }
}

export function* deactivateTrackingNumber(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
    const response = yield call(deactivateTrackingNumberApi, action.data)
    yield put({ type: trackingNumbersActions.DEACTIVATE_TRACKING_NUMBER_REQUEST_SUCCESS, data: response.data.trackingNumber })
    yield put({ type: trackingNumbersActions.TOGGLE_DEACTIVATE_TRACKING_NUMBER_MODAL, data: false })
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.success(`Tracking number ${friendlyFormatPhoneNumber(action.data.body.trackingNumber)} has been deactivated.`)
  } catch(e) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.error(`Error. Tracking number ${friendlyFormatPhoneNumber(action.data.body.trackingNumber)} hasn't been deactivated.`)
  }
}

export function* editTrackingNumber(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
    const response = yield call(editTrackingNumberApi, action.data)
    yield put({ type: trackingNumbersActions.EDIT_TRACKING_NUMBER_REQUEST_SUCCESS, data: response.data._id })
    yield put({ type: trackingNumbersActions.TOGGLE_EDIT_TRACKING_NUMBER_MODAL, data: false })
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.success(`Tracking number ${friendlyFormatPhoneNumber(action.data.body.trackingNumber)} has been updated.`)
  } catch(e) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    toast.error(`Error. Tracking number ${friendlyFormatPhoneNumber(action.data.body.trackingNumber)} hasn't been updated.`)
  }
}

export default all([
  takeLatest(trackingNumbersActions.GET_TRACKING_NUMBERS, getTrackingNumbers),
  takeLatest(newTrackingNumbersActions.CREATE_EXISTING_NUMBER_REQUEST, createExistingNumbersRequest),
  takeLatest(newTrackingNumbersActions.CREATE_TRACKING_NUMBER_REQUEST, createTrackingNumberRequest),
  takeLatest(newTrackingNumbersActions.SEARCH_NUMBERS_REQUEST, createSearchNumberRequest),
  takeLatest(trackingNumbersActions.DELETE_TRACKING_NUMBER, deleteTrackingNumber),
  takeLatest(trackingNumbersActions.ACTIVATE_TRACKING_NUMBER_REQUEST, activateTrackingNumber),
  takeLatest(trackingNumbersActions.DEACTIVATE_TRACKING_NUMBER_REQUEST, deactivateTrackingNumber),
  takeLatest(trackingNumbersActions.EDIT_TRACKING_NUMBER_REQUEST, editTrackingNumber),
])