import React, { useEffect, useState } from 'react';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import {FormGroup, Form, Label} from 'reactstrap';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { ReactComponent as SearchIco } from 'assets/img/icons/search.svg';
import './NeighborhoodBlitzMap.scss'
import { useSelector, useDispatch } from 'react-redux';
import LeafletMap from "./LeafletMap/LeafletMap";
import {
  getNeighborsRequest,
  convertAddressToLatLonRequest,
  setAmountAddresses,
  setCustomAmountAddresses,
  changeStep,
  setSearchAddress,
  resetSelectedNeighbors,
} from "actions/neighborhoodBlitz";

import AddressAutocomplete from "./AddressAutocomplete";
import DopeRadioGroup from "components/DopeForms/DopeRadioGroup";
import {DopeCheckbox} from "components/DopeForms";
import SaveListPopup from "./SaveListPopup/SaveListPopup";
import NeighborhoodsFilter from "./NeighborhoodsFilter/NeighborhoodsFilter";
import {DopeTable} from "components/DopeTable";
import {columnsFunc} from "./config";

export default function NeighborhoodBlitzMap({typePreview}) {
  // const [googleMapType, setGoogleMapType] = useState('m');
  const googleMapType = 'm';
  const [amountAddressesFlag, setAmountAddressesFlag] = useState(false);
  const [saveListPopupOpen, setSaveListPopupOpen] = useState(false);
  const [filterPopupOpen, setFilterPopupOpen] = useState(false);
  const [tablePageNumber, setTablePageNumber] = useState(1);
  const tablePageLimit = 10;
  const dispatch = useDispatch();

  const neighborhoods = useSelector(state => state.neighborhoodBlitz.neighborhoods);
  const areaAddress = useSelector(state => state.neighborhoodBlitz.areaAddress);
  const searchAddress = useSelector(state => state.neighborhoodBlitz.searchAddress);
  const searchError = useSelector(state => state.neighborhoodBlitz.searchError);
  const amountAddresses = useSelector(state => state.neighborhoodBlitz.amountAddresses);
  const customAmountAddresses = useSelector(state => state.neighborhoodBlitz.customAmountAddresses);
  const foundRealAddress = useSelector(state => state.neighborhoodBlitz.foundRealAddress);
  const filters = useSelector(state => state.neighborhoodBlitz.filters);
  const isLoading = useSelector(state => state.neighborhoodBlitz.isLoading);

  useEffect(() => {    
    if(amountAddresses&&![10,25,50,100,200].includes(amountAddresses)){
      setAmountAddressesFlag(true);
      dispatch(setCustomAmountAddresses(amountAddresses));    
    }
  }, [amountAddresses]);

  const maxlimit = 200;

  const getNeighbors = (maxCount) => {
    const data = {
      "address": searchAddress,
      "lat": areaAddress.lat,
      "lon": areaAddress.lon,
      "count": maxCount,
    };    

    if(filters.homeKeeper) data.owner = filters.homeKeeper === 'Owner';
    if(filters.homeValue) data.homeValue = filters.homeValue.value;
    if(filters.rangeValue) data.rangeValue = filters.rangeValue;

    setTablePageNumber(1);
    dispatch(setAmountAddresses(maxCount));
    dispatch(getNeighborsRequest(data));
  };

  const getFilteredNeighbors = (query) => {
    const data = {
      "address": searchAddress,
      "lat": areaAddress.lat,
      "lon": areaAddress.lon,
      "count": amountAddresses,
      ...query
    };
    setTablePageNumber(1);
    dispatch(getNeighborsRequest(data));
  };

  const convertAddress = (value) => {
    const address = value ? value : searchAddress;
    dispatch(setAmountAddresses(null));
    dispatch(resetSelectedNeighbors());
    dispatch(convertAddressToLatLonRequest(address));
  };

  const keyPress = (e) => {
    if(e.keyCode === 13){
      dispatch(setSearchAddress(e.target.value));
      convertAddress(e.target.value);
    }
  };

  const paginationOptions = {
    page: tablePageNumber,
    onPageChange: (value) => setTablePageNumber(value.page),
    limit: tablePageLimit,
    totalSize: neighborhoods.length
  };

  // const filterClasses = () => {
  //   if(filters.isFiltered && disableActions) return 'active disabled';
  //   if(disableActions || ( !neighborhoods.length && !filters.isFiltered) ) return 'disabled';
  //   if(filters.isFiltered) return 'active';
  //   return '';
  // };

  const disableActions = isLoading || (typePreview==='view');

  return (
    <div className="neighborhoodBlitzMap">
      <h1 className="main-title">Fill in an address</h1>
      <div className="mapCard">
        <div className="topMapCard">
          <div className="zipByAddress">
            <Form onSubmit={e => e.preventDefault()}>
              <FormGroup className='dope-form-group searchZip'>
                <SearchIco />
                <AddressAutocomplete
                  id='address'
                  placeholder='address'
                  name='address'
                  data-validate={true}
                  onChange={(e) => dispatch(setSearchAddress(e.target.value))}
                  value={searchAddress || ''}
                  onKeyDown={keyPress}
                  error={searchError}
                  onPlaceLoaded={(place) => {
                    if(place.formatted_address){
                      dispatch(setSearchAddress(place.formatted_address));
                      convertAddress(place.formatted_address);
                    }
                  }}
                  disabled={amountAddressesFlag || disableActions}
                />
              </FormGroup>
            </Form>
            <DopeButton
              disabled={amountAddressesFlag || disableActions}
              color="primary"
              onClick={() => convertAddress()}
            >Search</DopeButton>
          </div>
          {
            searchAddress&&<span className="searchAddress-info">{searchAddress}</span>
          }
        </div>

        <div className="neighborhoodBlitz-container">
          <div className='neighborhoodBlitzMapContainer'>
           <div className="neighborhoodBlitz-amount">
             <div className="title">
               Choose an amount of Neighbors
               {/*<FilterIcon*/}
               {/*  className={filterClasses()}*/}
               {/*  onClick={() => {                    */}
               {/*     if((filters.isFiltered || neighborhoods.length) && (!disableActions)) setFilterPopupOpen(true);*/}
               {/*  }}*/}
               {/*/>*/}
             </div>
             <div className="d-flex">
               <DopeRadioGroup
                 data={[
                   {value: 10, label: "10"},
                   {value: 25, label: "25"},
                   {value: 50, label: "50"},
                   {value: 100, label: "100"},
                   {value: 200, label: "200"},
                 ]}
                 name="neighborhoodsMaxCount"
                 selectedValue={amountAddresses}
                 onChange={value => getNeighbors(+value)}
                 disabled={!foundRealAddress || amountAddressesFlag || disableActions}
               />
               <div className="neighborhoodBlitz-wrap">
                 <Label className='header-label'>
                   <DopeCheckbox
                     checked={amountAddressesFlag}
                     disabled={!foundRealAddress || disableActions}
                     onChange={() => {
                       setAmountAddressesFlag(!amountAddressesFlag);                       
                       dispatch(setCustomAmountAddresses(''));
                     }}
                   />
                   Custom
                 </Label>
                 <DopeInput
                   type="number"
                   onKeyDown={(e) => {
                     if(e.keyCode === 13){
                       // dispatch(setCustomAmountAddresses(e.target.value));
                       let val = +e.target.value;
                       if(val>maxlimit) val = maxlimit;
                       if(val<0) val = 0;
                       getNeighbors(val);
                     }
                   }}
                   value={customAmountAddresses}
                   min={0}
                   max={200}                   
                   disabled={!amountAddressesFlag}
                   onChange={e => {
                    let val = +e.target.value;
                    if(val>maxlimit) val = maxlimit;
                    if(val<0) val = 0;
                    dispatch(setCustomAmountAddresses(val))
                  }}
                 />
               </div>
             </div>
           </div>
            <LeafletMap
              center={{lat: areaAddress.lat, lng: areaAddress.lon}}
              googleMapType={googleMapType}
              neighborhoods={neighborhoods}
              searchAddress={searchAddress}
            />
          </div>
          <div className='neighborhoodBlitzSummary'>
            <div className="neighborhoodBlitzSummaryTitle">
              Order Summary ({neighborhoods.length} Addresses)
            </div>
            <div className="neighborhoodBlitzSummaryItem">
              <DopeTable
                keyField="_id"
                columns={[...columnsFunc()]}
                data={neighborhoods.reduce((prev, current, index) => {
                  if((index >= tablePageNumber * tablePageLimit - tablePageLimit) && (index < tablePageNumber * tablePageLimit)){
                    return [...prev, {
                      _id: `${current.street}-${index}`,
                      index: index + 1,
                      address: `${current.street} ${current.suite ? ', ' + current.suite : ''}`,
                    }]
                  }
                  return prev;
                }, [])}
                paginationOptions={paginationOptions}
                />
            </div>
            <div className="btnContainer">
              {
                (typePreview==='view') ? (
                  <DopeButton
                    color="primary"                  
                    onClick={() => dispatch(changeStep(2))}
                    disabled={!neighborhoods.length || isLoading}
                  >
                    Continue
                  </DopeButton>
                ):(
                  <DopeButton
                    color="primary"
                    // onClick={() => setSaveListPopupOpen(true)}
                    onClick={() => dispatch(changeStep(2))}
                    disabled={!neighborhoods.length || isLoading}
                  >
                    Continue
                  </DopeButton>
                )
              }
            </div>
          </div>
        </div>
      </div>
      <SaveListPopup
        isOpen={saveListPopupOpen}
        toggle={() => setSaveListPopupOpen(!saveListPopupOpen)}
        changeStep={changeStep}
        neighborhoods={neighborhoods}
      />
      <NeighborhoodsFilter
        isOpen={filterPopupOpen}
        toggle={() => setFilterPopupOpen(!filterPopupOpen)}
        getFilteredNeighbors={getFilteredNeighbors}
      />
    </div>
  );
}