import { all, call, put, takeLatest } from 'redux-saga/effects';
import {push} from "connected-react-router";
import { toast } from 'react-toastify';
import LogRocket from 'logrocket';
import * as generalActions from 'actions/general';
import * as adminAccountsActions from 'actions/adminAccounts';
import {
  getAdminAccountsApi,
  getOwnPostcardPreferencesApi,
  setOwnPostcardPreferencesApi,
  deleteAccountAdmRequestApi,
  getCustomPaymentApi,
  getAdminAccountApi,
  sendSmsApi,
  changeAccountDataApi,
} from 'requests/adminAccounts';
import {pathOr} from 'ramda';

export function* getAdminAccounts(action) {
  try {
    const response = yield call(getAdminAccountsApi, action.data);
    let data = {
      ...action.data.query,
      ...response.data,
    };
    delete data.account;
    yield put({
      type: adminAccountsActions.GET_ADMIN_ACCOUNTS_REQUEST_SUCCESS,
      data: data,
    });
  } catch (e) {
    LogRocket.captureException(e);
    yield put({ type: adminAccountsActions.GET_ADMIN_ACCOUNTS_REQUEST_FAILED });
    toast.error(`Error. Can't get accounts list.`);
  }
}

export function* getAdminAccountRequest(action) {  
  try {
    yield put({ type: adminAccountsActions.SET_ADMIN_ACCOUNT_LOADING, loading: true });
    const response = yield call(getAdminAccountApi, action.data);
    yield put({
      type: adminAccountsActions.GET_ADMIN_ACCOUNT_REQUEST_SUCCESS,
      data: response.data,
    });
    yield put({ type: adminAccountsActions.SET_ADMIN_ACCOUNT_LOADING, loading: false });     
  } catch (e) {
    LogRocket.captureException(e);
    yield put({ type: adminAccountsActions.GET_ADMIN_ACCOUNT_REQUEST_FAILED, error: e });
    yield put({ type: adminAccountsActions.SET_ADMIN_ACCOUNT_LOADING, loading: false });
  }
}

export function* setOwnPostcardPrice(action) {  
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    yield call(setOwnPostcardPreferencesApi, action.data);    
    let ownPrice = action.data.body.enabled || action.data.body.postcardPriceEnabled || action.data.body.eddmPriceEnabled || action.data.body.neighborhoodPriceEnabled || action.data.body.yardSignPriceEnabled;
    yield put({
      type: adminAccountsActions.SET_OWN_POSTCARD_PRICE_REQUEST_SUCCESS,
      data: action.data.body,
      ownPrice,
    });
    if(action.successCallback){
      action.successCallback();
    }
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })    
    toast.success(`The Postcard Preferences has been changed.`);
  } catch (e) {
    LogRocket.captureException(e);
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    // yield put({ type: adminAccountsActions.GET_ADMIN_ACCOUNTS_REQUEST_FAILED });
    toast.error(`Error. Can't set custom price.`);
  }
}

export function* getOwnPostcardPrice(action) {
  try {
    // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    const response = yield call(getOwnPostcardPreferencesApi, action.data);    
    yield put({
      type: adminAccountsActions.GET_ADMIN_OWN_POSTCARD_PRICE_REQUEST_SUCCESS,
      data: response.data.price,
    });
    // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
  } catch (e) {
    LogRocket.captureException(e);
    // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    // yield put({ type: adminAccountsActions.GET_ADMIN_ACCOUNTS_REQUEST_FAILED });
    toast.error(`Error. Can't get custom price.`);
  }
}

export function* resetOwnPostcardPrice(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    yield call(setOwnPostcardPreferencesApi, action.data);
    let ownPrice = action.data.body.enabled || action.data.body.postcardPriceEnabled || action.data.body.eddmPriceEnabled || action.data.body.neighborhoodPriceEnabled || action.data.body.yardSignPriceEnabled;
    yield put({
      type: adminAccountsActions.RESET_OWN_POSTCARD_PRICE_REQUEST_SUCCESS,
      data: action.data.body,
      ownPrice
    });
    if(action.successCallback){
      action.successCallback();
    }
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.success(`The Postcard Preferences has been changed.`);    
  } catch (e) {
    LogRocket.captureException(e);
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    // yield put({ type: adminAccountsActions.GET_ADMIN_ACCOUNTS_REQUEST_FAILED });
    toast.error(`Error. Can't reset custom price.`);
  }
}

export function* deleteAccountAdmRequest(action) {
  try { 
    yield call(deleteAccountAdmRequestApi, action.data);    
    yield put({ type: adminAccountsActions.DELETE_ACCOUNT_ADM_REQUEST_SUCCESS, data: action.data.accountId })
    yield put(push('/accounts'));
  } catch(error) {
    yield put({ type: adminAccountsActions.DELETE_ACCOUNT_ADM_REQUEST_FAILED, error })    
    const errorMessage = pathOr("Cann't delete account", ['response', 'data', 'message'], error);
    toast.error(errorMessage)
  }
}

export function* getCustomPaymentRequest(action) {
  try {
    const response = yield call(getCustomPaymentApi, action.data);
    yield put({
      type: adminAccountsActions.GET_CUSTOM_PAYMENT_REQUEST_SUCCESS,
    });
    toast.success(response.data.message);
    action.callbackSuccess();
  } catch (e) {
    LogRocket.captureException(e);
    yield put({ type: adminAccountsActions.GET_CUSTOM_PAYMENT_REQUEST_FAILED });
    action.callbackFailure();
    toast.error(`Error. Can't charge.`);
  }
}

export function* sendSms(action) {
  try {
    const response = yield call(sendSmsApi, action.data);    
    yield put({
      type: adminAccountsActions.SEND_SMS_SUCCESS,
    });    
    toast.success(response.data.message);
    action.callbackSuccess();
  } catch (e) {
    console.log(e);
    yield put({ type: adminAccountsActions.SEND_SMS_FAILED });
    toast.error(`Error. Can't send SMS.`);
  }
}

export function* changeAccountData(action) {
  console.log("action:",action);
  try {
    const response = yield call(changeAccountDataApi, action.data);
    console.log("changeAccountData response", response);   
    if (response.status === 200) {
      yield put({
        type: adminAccountsActions.CHANGE_ACCOUNT_DATA_SUCCESS,
        data: {
          email: response.data.email,
          accountPhone: response.data.accountPhone
        }
      }); 
      toast.success('Account data was successfully changed');
    }    
  } catch (e) {    
    const errorMessage = pathOr("Error. Can't change account data.", ['response', 'data', 'error', 'dopeMessage'], e);
    yield put({ type: adminAccountsActions.CHANGE_ACCOUNT_DATA_FAILED });
    toast.error(errorMessage);
  }
}

export default all([
  takeLatest(adminAccountsActions.GET_ADMIN_ACCOUNTS_REQUEST, getAdminAccounts),
  takeLatest(adminAccountsActions.GET_ADMIN_ACCOUNT_REQUEST, getAdminAccountRequest),  
  takeLatest(adminAccountsActions.SET_OWN_POSTCARD_PRICE_REQUEST,setOwnPostcardPrice,),
  takeLatest(adminAccountsActions.GET_ADMIN_OWN_POSTCARD_PRICE_REQUEST,getOwnPostcardPrice,),
  takeLatest(adminAccountsActions.RESET_OWN_POSTCARD_PRICE_REQUEST, resetOwnPostcardPrice),
  takeLatest(adminAccountsActions.DELETE_ACCOUNT_ADM_REQUEST, deleteAccountAdmRequest),
  takeLatest(adminAccountsActions.GET_CUSTOM_PAYMENT_REQUEST, getCustomPaymentRequest),
  takeLatest(adminAccountsActions.SEND_SMS_REQUEST, sendSms),
  takeLatest(adminAccountsActions.CHANGE_ACCOUNT_DATA_REQUEST, changeAccountData),   
]);


