import React from 'react';
import {connect} from 'react-redux';

import {getCoupons, deleteCoupon, storeCoupon, updateCoupon} from '../../../actions/coupons';
import {Button, Table} from "reactstrap";
import Modal from './modal';


class Coupons extends React.Component {
    state = {
        modalOpened: false,
        selectedCoupon: {}
    };

    componentDidMount() {
        this.props.getCoupons();
    }

    openEditModal = (coupon) => {
        this.setState({ modalOpened: !this.state.modalOpened });
        this.setState({ selectedCoupon: coupon });
    };

    toggleModal = () => {
        this.setState({ modalOpened: !this.state.modalOpened });
        this.setState({ selectedCoupon: {} });
    };

    onSaveCoupon = (coupon) => {
        console.log(coupon);
        if (coupon._id) {
            this.props.updateCoupon({ ...coupon });
        } else {
            this.props.storeCoupon({ ...coupon });
        }
        this.toggleModal();
    };

    render() {

        return (
            <div>
                {this.props.coupons &&
                <div>
                    <h4 style={{ marginTop: 0 }}>Manage Coupons</h4>
                    <Table responsive hover>
                        <thead className="text-primary">
                        <tr>
                            <th>ID</th>
                            <th>Name</th>
                            <th>Discount</th>
                            <th>Quantity Available</th>
                            <th>Actions</th>
                        </tr>
                        </thead>
                        <tbody>
                        {this.props.coupons.map((coupon, key) =>
                            <tr
                                key={coupon._id}
                                className='simple-table'
                            >
                                <td>{coupon.shortID}</td>
                                <td>{coupon.name}</td>
                                <td>{coupon.percentageDiscount ? coupon.percentageDiscount + '%' : '$' + coupon.accountDiscount}</td>
                                <td>{coupon.quantityAvailable}</td>
                                <td>
                                    <Button onClick={() => this.openEditModal(coupon)}>Edit</Button>
                                    <Button color="danger" className='ml-1' onClick={() => this.props.deleteCoupon({_id: coupon._id})}>Delete</Button>
                                </td>
                            </tr>
                        )}
                        </tbody>
                    </Table>
                </div>
                }
                <div style={{textAlign: 'center'}}>
                    <Button onClick={() => this.openEditModal()}>Add Coupon</Button>
                </div>


                {this.state.modalOpened && <Modal toggle={this.toggleModal} open={this.state.modalOpened} data={this.state.selectedCoupon} onSave={this.onSaveCoupon} />}
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    coupons: state.coupons,
});

const mapDispatchToProps = {
    getCoupons,
    storeCoupon,
    updateCoupon,
    deleteCoupon
};

export default connect(mapStateToProps, mapDispatchToProps)(Coupons);
