import React from 'react';
import { ReactComponent as DownIcon } from 'assets/img/icons/arrow-down.svg'
import { ReactComponent as UpIcon } from 'assets/img/icons/arrow-up.svg'
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import { ReactComponent as CopyIcon } from 'assets/img/icons/copy1.svg';

import './FontSizeResizer.scss';

class FontSizeResizer extends React.Component {   
  handleChange = e => {
    this.props.onFontSizePick(e)
  }
  render() {
    return (
      <React.Fragment>
        <div className="text-ed-wrapper">
          <div>
            <h3 className="fs-OPB-title">Font Size</h3>
            <div className="fs-OPB-editor">
              <button className="dope-button btn btn-primary" onClick={this.handleChange} value={1}>
                <UpIcon className="btn-icon"/>
              </button>
              <button className="dope-button btn btn-primary" onClick={this.handleChange} value={-1}>
                <DownIcon className="btn-icon"/>
              </button>
              {/* <button className="fs-btn" onClick={this.handleChange} value={-1}>
                
              </button>
              <button className="fs-btn" onClick={this.handleChange} value={-1}>
                
              </button>
              <button className="fs-btn" onClick={this.handleChange} value={-1}>
                
              </button> */}
            </div>
          </div>
          <div>
            <h3 className="fs-OPB-title">Clone/Delete</h3>
            <div className="fs-OPB-editor">
              <button className="dope-button btn btn-primary" onClick={this.props.cloneTextBox}>
                <CopyIcon className="btn-icon"/>
              </button>
              <button className="dope-button btn btn-primary" onClick={this.props.deleteTextBox}>
                <DeleteIcon className="btn-icon"/>
              </button>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}


export default FontSizeResizer;
