import {
    SET_MARKERS,
    TOGGLE_MAP_ELEMENT_INFO_STATUS,
    SET_MAP_REF,
    SET_ZOOM,
    RESET_MAP_DATA,
    SET_CENTER,
    SWITCH_MAP_VIEW
} from '../actions';

export default function reducer(state = [], action) {
    switch (action.type) {
        case SET_MARKERS:
            return { ...state, markers: action.markers }
        case SET_MAP_REF:
            return { ...state, mapRef: action.mapRef }
        case SET_ZOOM:
            return { ...state, zoom: action.zoom }
        case SET_CENTER:
            return { ...state, center: { lat: action.latitude, lng: action.longitude } }
        case SWITCH_MAP_VIEW:
            if (state.mapTypeId === 'satellite')
                return { ...state, mapTypeId: 'roadmap' }
            return { ...state, mapTypeId: 'satellite' }
        case TOGGLE_MAP_ELEMENT_INFO_STATUS:
            if (action.latitude) {
                return {
                    ...state,
                    openedInfo: action.id,
                    center: { lat: action.latitude, lng: action.longitude },
                    currentPage: action.currentPage,
                    mapTypeId: 'satellite',
                    zoom: 21
                }
            }
            else
                return {
                    ...state,
                    openedInfo: action.id,
                    mapTypeId: 'satellite',
                    zoom: 21
                }
        case RESET_MAP_DATA:
            return {
                ...state,
                markers: undefined,
                mapRef: undefined,
                zoom: undefined,
                openedInfo: undefined,
                center: undefined,
                currentPage: undefined,
                mapTypeId: undefined
            }
        default:
            return state;
    }
}