import React, {useState} from 'react';
import { useSelector } from 'react-redux';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import {DopeCheckbox} from 'components/DopeForms/DopeCheckbox/DopeCheckbox';
import SearchListInput from '../SearchListInput';
// import {searchList} from 'utils/helper';
import { ReactComponent as CloseIcon } from 'assets/img/icons/close.svg';

import './ListsFilter.scss';

const searchList = (items, search) => {
  if(search.length === 0)
    return items;

  return items.filter((el)=>{
    return el.name.toLowerCase().indexOf(search.toLowerCase()) > -1;
  })
}

export default function ListsFilter(props) {   
  const [searchedList, setSearchedList] = useState(''); 
  const lists = useSelector(state => state.contacts.filtersData.lists)||[];
  
  
  const searchedCities = searchList(lists,searchedList);

  const onChangeFilterItem = (list) => {       
    const filterLists = [...props.filterLists];
    const resultFilter = filterLists.map(el=>el._id).indexOf(list._id) > -1 ? filterLists.filter(el => el._id !== list._id) : [ ...filterLists, list ];       
    props.onChangeFilter(resultFilter)
  }

  const removeFilterItem = (list) => {
    const filterLists = [...props.filterLists];
    const resultFilter = filterLists.map(el=>el._id).indexOf(list._id) > -1 ? filterLists.filter(el => el._id !== list._id) : [ ...filterLists];    
    props.onChangeFilter(resultFilter)
  }

  const selectAllFilterItem = () => {     
    const resultFilter = [...lists];    
    props.onChangeFilter(resultFilter)
  }

  const removeAllFilterItem = () => {
    props.onChangeFilter([])
  }  

  const Item = (list) => {
    return (
      <li className="fs-leftcol-item" key={list._id}>
        <DopeCheckbox
          label={list.name}
          checked={props.filterLists.map(el=>el._id).indexOf(list._id) > -1}
          onChange={()=>{onChangeFilterItem(list)}}
        />
      </li>
    )
  }  

  const Badge = (list) => {
    return (
      <li className="fs-rightcol-item" key={list._id}>
        {list.name}
        <CloseIcon className="close-icon" onClick={ ()=>{removeFilterItem(list)} }/>
      </li>
    )
  };
  
  return (
    <div className="ListsFilter">
      <div className="filter-col">
        <div className="filter-col-header">
          <DopeButton color='white'
            onClick={selectAllFilterItem}
            disabled = {lists.length === props.filterLists.length}
          >
            Select All lists
          </DopeButton>        
        </div>
        <div className="search-container">
          <SearchListInput placeholder="Search lists" value={searchedList} onChange={setSearchedList}/>
        </div>
        <ul className="fs-leftcol-list">
          { searchedCities.length>0 ? (
            searchedCities.map((list)=>(                            
              Item(list)
            ))
          ):(
            <span className="no-fs-leftcol-item">
              "No lists"
            </span>
          )
          }      
        </ul>
      </div>
      <div className="filter-col">
        <div className="filter-col-header">
          <span className="col-header-title">Selected</span>
          <DopeButton 
            color='white'
            onClick={removeAllFilterItem}
            disabled = {props.filterLists.length === 0}
          >
            Clear All
          </DopeButton>        
        </div>
        { props.filterLists.length>0 ? (
            <ul className="fs-rightcol-list">
              {
                props.filterLists.map((list)=>(                                          
                  Badge(list)
                ))
              }
            </ul> 
          ):(
            <ul className="fs-rightcol-list empty">
              <span className="no-fs-leftcol-item">
                "No lists selected"
              </span>
            </ul>
          )
        } 
      </div>
    </div>
  );
}
