import React, {useState} from 'react';
import { useSelector } from 'react-redux';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import {DopeCheckbox} from 'components/DopeForms/DopeCheckbox/DopeCheckbox';
import SearchListInput from '../SearchListInput';
import {searchList} from 'utils/helper';
import { ReactComponent as CloseIcon } from 'assets/img/icons/close.svg';

import './ZipFilter.scss';

export default function ZipFilter(props) {
  const [searchedList, setSearchedList] = useState('');
  const zipCodes = useSelector(state => state.contacts.filtersData.zipCodes)||[];
  const searchedCities = searchList(zipCodes,searchedList);

  const onChangeFilterItem = (zipCode) => {     
    const filterZipCodes = [...props.filterZipCodes];
    const resultFilter = filterZipCodes.indexOf(zipCode) > -1 ? filterZipCodes.filter(el => el !== zipCode) : [ ...filterZipCodes, zipCode ];
    props.onChangeFilter(resultFilter)
  }

  const removeFilterItem = (zipCode) => {     
    const filterZipCodes = [...props.filterZipCodes];
    const resultFilter = filterZipCodes.indexOf(zipCode) > -1 ? filterZipCodes.filter(el => el !== zipCode) : [ ...filterZipCodes];
    props.onChangeFilter(resultFilter)
  }

  const selectAllFilterItem = () => {     
    const resultFilter = [...zipCodes];    
    props.onChangeFilter(resultFilter)
  }

  const removeAllFilterItem = () => {
    props.onChangeFilter([])
  }  

  const Item = (zipCode) => {
    return (
      <li className="fs-leftcol-item" key={zipCode}>
        <DopeCheckbox
          label={zipCode}
          checked={props.filterZipCodes.indexOf(zipCode) > -1}
          onChange={()=>{onChangeFilterItem(zipCode)}}
        />
      </li>
    )
  }

  const Badge = (zipCode) => {
    return (
      <li className="fs-rightcol-item" key={zipCode}>            
        {zipCode}
        <CloseIcon className="close-icon" onClick={ ()=>{removeFilterItem(zipCode)} }/>
      </li>
    )
  };
  
  return (
    <div className="ZipFilter">
      <div className="filter-col">
        <div className="filter-col-header">
          <DopeButton
            color='white'
            onClick={selectAllFilterItem}
            disabled = {zipCodes.length === props.filterZipCodes.length}
          >
            Select All ZIP
          </DopeButton>
        </div>
        <div className="search-container">
          <SearchListInput placeholder="Search ZIP" value={searchedList} onChange={setSearchedList}/>
        </div>
        <ul className="fs-leftcol-list">
          { searchedCities.length>0 ? (
            searchedCities.map((zipCode)=>(                            
              Item(zipCode)
            ))
          ):(
            <span className="no-fs-leftcol-item">
              "No ZIP"
            </span>
          )
          }
        </ul>
      </div>
      <div className="filter-col">
        <div className="filter-col-header">
          <span className="col-header-title">Selected</span>
          <DopeButton
            color='white'
            onClick={removeAllFilterItem}
            disabled = {props.filterZipCodes.length === 0}
          >
            Clear All
          </DopeButton>        
        </div>
        { props.filterZipCodes.length>0 ? (
            <ul className="fs-rightcol-list">
              {
                props.filterZipCodes.map((zipCode)=>(                                          
                  Badge(zipCode)
                ))
              }
            </ul> 
          ):(
            <ul className="fs-rightcol-list empty">
              <span className="no-fs-leftcol-item">
                "No ZIP selected"
              </span>
            </ul>
          )
        } 
      </div>
    </div>
  );
}
