import React from 'react';
import './BackSelector.scss';

export default function BackSelector(props) {
  const { backs, selectedBackKey=0, onSelectTemplateBack } = props;    
  return (
    <div className="back-selector">
      { backs.map((back, key) => ( 
          <div className='back-item' key={back.back_thumbnail.split('/').slice(-1)}                      
            onClick={() => {                        
              onSelectTemplateBack({back: back.back,key: key});                        
            }} 
          >                   
            <div className='back-preview-box'>
              <img
                src={back.back_thumbnail}
                alt={back.back_thumbnail.split('/').slice(-1)}
              />
              { selectedBackKey === key ? (
                <div className='back-checked' />
                ):null
              }
            </div>                      
          </div>          
        ))
      }
    </div>
  );
}
