import React from 'react';

const listNameLayout = (cell, row) => {  
  const unverified =   row.listProperties && row.listProperties.unverifiedContacts > 0;
  return (
    <div className={`verified-cell ${unverified ? 'small-cell': ''}`}>
      <div>
        {`${cell}`}
      </div>
      {
        unverified && 
        <div className='invalidated-contact'>
          {`${row.listProperties.unverifiedContacts} invalidated Contact`}
      </div>
      }
    </div>
  );
};

export const  columnsFunc = (onSort) => {
  return [{
    dataField: 'name',
    text: 'Name',
    sort: true,
    sortFunc: function(){
      return false
    },
    onSort: onSort,
    formatter: listNameLayout,
  }, 
  {
    dataField: 'contactsQty',
    text: 'Contacts',
  },
  {
    dataField: 'date',
    text: 'Date Created',
    sort: true,
    sortFunc: () => {
      return false
    },
    onSort: onSort
  }, {
    dataField: 'listType',
    text: 'List Type',
    sort: true,
    sortFunc: () => {
      return false
    },
    onSort: onSort
  }, {
    dataField: 'description',
    text: 'Description',
    sort: true,
    sortFunc: function(){
      return false
    },
    onSort: onSort
  }, {
    dataField: 'requestStatus',
    text: 'Status',
    sort: true,
    sortFunc: function(){
      return false
    },
    onSort: onSort
  }]}