import React, {useState, useRef, useEffect} from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { Card, CardBody, FormGroup } from 'reactstrap';
import { FadeLoader } from 'react-spinners';
import moment from 'moment';
import { DopeTable } from 'components/DopeTable/DopeTable';
import { AUTOMATION_PATH } from 'utils/constants';
import { useSelector, useDispatch } from 'react-redux';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import Skeleton from 'react-loading-skeleton';
import {
  columnsFunc,
  nbhColumnsFunc,
  contactsColumnsFunc,
} from './config';
import { ReactComponent as LeftArrowIcon } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as CopyUrlIcon } from 'assets/img/icons/copyUrl.svg';
import { ReactComponent as AttentionIcon } from 'assets/img/icons/attention.svg';

import {
  resetAutomationPreview,
  getAutomationPreviewRequest,  
  rechargeAutomationRequest,
  changeStatusAutomationRequest,
  getAutomationPreviewContactsRequest,
  getAutomationPreviewNBHContactsRequest,
} from "actions/automation";
import './AutomationPreview.scss';

export default function AutomationPreview({savedAccountId}) {  
  const dispatch = useDispatch();
  const history = useHistory();   
  const { automationId } = useParams();  
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const automationPreview = useSelector(state => state.automation.automationPreview);
  const status = useSelector(state => state.automation.automationPreview.status);
  const listId = useSelector(state => state.automation.automationPreview.listId);
  const contactData = useSelector(state => state.automation.automationPreview.contactData);
  const listIdArr = useSelector(state => state.automation.automationPreview.listIdArr);
  const listArrData = useSelector(state => state.automation.automationPreview.listArrData);
  const typeAutomation = useSelector(state => state.automation.automationPreview.type);
  const domain = useSelector(state => state.general.domain);
  const windowWidth = useSelector(state => state.general.windowWidth);  
  
  // const errors = useSelector(state => state.automation.automationSummary.errors);        
  const [urlInputCopied, setUrlInputCopied] = useState(false);
  const [contactsReady, setContactsReady] = useState(false);
  const [nbhContactsReady, setNbhContactsReady] = useState({});
  
  
  useEffect(() => {    
    if((savedAccountId !== currentAccountId) && (savedAccountId !== '')){      
      history.push('/automation');            
    }    
  }, [currentAccountId]);
  
  useEffect(() => { 
    const data = {
      sendingId: automationId,
    };
    dispatch(getAutomationPreviewRequest(data));
    return () => {
      dispatch(resetAutomationPreview());
    };
  },[]);

  useEffect(() => {    
    // if(listId.length&&typeAutomation!=='neighborhood'){
    if(listId.length){
      const data = {        
        queries:{
          account: currentAccountId,
          lists: listId,
          page:0,
          limit:10,
        },
      }
      dispatch(getAutomationPreviewContactsRequest(data));
      setContactsReady(true);
    }    
  },[listId]); 
  
  useEffect(() => {    
    if(listIdArr.length&&typeAutomation==='neighborhood'){
      // listIdArr.map((item)=>{
      //   console.log(item);
      //   let data = {
      //     queries:{
      //       account: currentAccountId,
      //       lists: item.id,
      //     },
      //   }
      //   // dispatch(getAutomationPreviewNBHContactsRequest(data));
      // }) 
      setContactsReady(true);     
    }    
  },[listIdArr]); 
  
  useEffect(()=>{       
    setNbhContactsReady(listArrData);
  },[listArrData]);
  
  const myRef = useRef();  
  const copyToClipboard = () => {
    myRef.current.select();
    document.execCommand("copy");
    setUrlInputCopied(true);
    setTimeout(()=>{setUrlInputCopied(false);},2000);
  };
  
  const changeStatusAutomation = (newStatus) =>{
    const data = {
      body: {            
        status: newStatus,
      },
      automationId: automationId,
      queries: currentAccountId,
    };  
    dispatch(changeStatusAutomationRequest(data));
  } 
  
  const onRechargeAutomation = () => {
    const data = {
      body: {
        account: currentAccountId,               
        // status: 'active',
      },
      automationId: automationId,
      queries: currentAccountId,
    };  
    dispatch(rechargeAutomationRequest(data));
    // dispatch(rechargeAutomationRequest(data,rechargeAutomationSuccessCallback));
  }   
  
  const onPageChange = (pagination = {page: contactData.page, limit: 10}) => {
    const data = {
      queries:{
        account: currentAccountId,
        lists: listId,
        ...pagination,
        page: pagination.page - 1,        
      },
    }
    dispatch(getAutomationPreviewContactsRequest(data));  
  };  
  const paginationOptions = {
    page: contactData.page + 1,
    onPageChange: onPageChange,
    totalSize: contactData.totalSize,
  };  
  // columns = columnsFunc(this.onSort)
  let columns = columnsFunc(()=>{});

  let nbhColumns = nbhColumnsFunc(()=>{});

  let contactsColumns = contactsColumnsFunc(()=>{});

  const fetchAsyncContacts=(id)=>{
    if(!nbhContactsReady[id]){
      let data = {
        queries:{
          account: currentAccountId,
          lists: id,
        },
      }
      setTimeout(()=>dispatch(getAutomationPreviewNBHContactsRequest(data)),100);
    }
  }

  const expandRow = {
    onlyOneExpanding: true,
    showExpandColumn: true,
    onExpand: (row, isExpand, rowIndex, e) => { 
      if(isExpand){
        fetchAsyncContacts(row._id);        
      }
    },  
    parentClassName: (isExpanded) => (isExpanded?'expandedParent':''),
    className: (isExpanded) => (isExpanded?'expanded':''),    
    renderer: row => (
      <div className="nbh-table-wrapper">        
        {nbhContactsReady[row._id]?(          
          <DopeTable
            keyField="_id"
            columns={contactsColumns}
            data={listArrData[row._id]&&listArrData[row._id].data.map((item,ind) => (
              {
                _id: item._id,
                dateSending: item.automations.filter(el=>(el.id===automationId)).length ? moment(item.automations.find(el=>(el.id===automationId)).date).format('MMM DD, YYYY') : '--',                
                firstName: item.firstName,
                lastName: item.lastName,                    
                address1: item.address1,
                address2: item.address2,
                city: item.city,
                state: item.state,
                zip: item.zip,
                zip4: item.zip4,
                phone: item.phone,
              }
            ))}                
            rowClasses="table-row"
            woPagination          
            loading={automationPreview.nbhListLoading}                    
          />
        ):(
          <div className='loader-nbh'>
            <FadeLoader size={100} color={'#403d39'} />
          </div>
        )}
      </div>
    ),    
  };

  const findParentName = (id,parentContactsData) =>{    
    let {firstName='',lastName=''} = parentContactsData.find(el=>el._id===id)||{};   
    return `${firstName} ${lastName}`;       
  }
  const findParentAddress = (id,parentContactsData) =>{    
    let {address1=''} = parentContactsData.find(el=>el._id===id)||{};   
    return address1
  }

  return (
    <div className="AutomationPreview">
      <React.Fragment>
        <div className='back-btn-container'>
          <button className='back-btn' onClick={()=>history.push(`${AUTOMATION_PATH}`)}>
            <LeftArrowIcon className="back-icon"/>
            Back to automation
          </button>
        </div>
        <div className="preview-header">
          <h2 className="status-title">View Automation</h2>
          {
            status==="active" && (
              <DopeButton
                color="primary"
                onClick={()=>changeStatusAutomation("paused")} 
              >
                Pause Automation
              </DopeButton>
            )
          }
          {
            status==="paused" && (
              <DopeButton
                color="primary"             
                onClick={()=>changeStatusAutomation("active")} 
              >
                Activate Automation
              </DopeButton>
            )
          }
        </div>
        {automationPreview.status === 'payment-failed' && (
          <Card className='dope-card automation-recharge-card'>
            <CardBody>
              <div className='automation-recharge-block'>
                <div className="i-t-box">
                  <AttentionIcon className="icon"/>
                  <p className="text">
                    Your payment did not go through. Please recharge your credit
                    card or update your payment details, and click Recharge.
                  </p>
                </div>
                <DopeButton color='primary' onClick={onRechargeAutomation}>
                  Recharge
                </DopeButton>
              </div>
            </CardBody>
          </Card>
        )}
        <div className="automationEditorCard-v2">          
          <div className="section">
            {/* <div className="preview-header"> */}
              <div className="sub-title">Your Postcard</div>
            {/* </div>           */}
            <div className="approval-grid">
              <div className="approval-grid-item">
                <h4 className="item-title">
                  Front Side
                </h4>
                <div className="img-prev-container">
                  { automationPreview.previewImg.front.mini&&
                    <img src={automationPreview.previewImg.front.mini} alt="img-prev"/> || <Skeleton height={(windowWidth>=1200)?216:384}/>
                  }
                </div>
              </div>
              <div className="approval-grid-item">
                <h4 className="item-title">
                  Back Side
                </h4>
                <div className="img-prev-container">
                  { automationPreview.previewImg.back.mini&&               
                    <img src={automationPreview.previewImg.back.mini} alt="img-prev"/> || <Skeleton height={(windowWidth>=1200)?216:384}/>               
                  }
                </div>
              </div>
            </div>
          </div>
          <div className="section">
            <h3 className="sub-title">
              List Name                            
            </h3>
            <div className="list-info">
              <FormGroup className='dope-form-group'>
                <DopeInput
                  title='Name'
                  name='name'                  
                  placeholder='List name'                  
                  value={automationPreview.name}                  
                  disabled={true}
                />
              </FormGroup>
              <FormGroup className='dope-form-group'>
                <DopeInput
                  title='Description' 
                  name='description'
                  placeholder='List description'                  
                  value={automationPreview.description}                  
                  disabled={true}
                />
              </FormGroup>
            </div>
          </div>
          <div className="section">
            <h3 className="sub-title">The Automation connected to Zapier</h3>
            <h4 className="instr-title">Webhook link</h4>
            <div className="urlInput-container">
              <CopyUrlIcon className="CopyUrlIcon" onClick={copyToClipboard}/>
              <input ref={myRef} className="urlInput" value={`https://${domain}/api/webhooks/list/${automationPreview.webhookIdentifier}`} onClick={copyToClipboard} readOnly/>
              {urlInputCopied ? <span className="urlcopy-text">Copied.</span> : null}
            </div>
          </div>
          { contactsReady&&(
            <div className="section">
              <h3 className="sub-title">The Automation List</h3>            
                {typeAutomation==='neighborhood'?(                    
                  !automationPreview.listLoading&&!listIdArr.length&&!contactData.data.length?(
                    <p>List hasn't contacts for NBH(Empty Zapier List)</p>
                  ):(
                    <div className="aut-nbh-table-wrapper">
                      <DopeTable
                        keyField="_id"
                        columns={nbhColumns}
                        data={listIdArr.map((item,ind) => (                    
                          {
                            _id: item.id,
                            name: findParentName(item.contact_id,contactData.data),
                            address1: findParentAddress(item.contact_id,contactData.data),
                            info: item.id, 
                            empty: ''                           
                          }
                        ))}                
                        rowClasses="table-row"
                        woPagination
                        // paginationOptions={paginationNBHOptions}
                        loading={automationPreview.listLoading}                        
                        expandRow={expandRow}                                          
                      />
                    </div>
                  ) 
                ):(
                  !automationPreview.listLoading&&!contactData.data.length?(
                    <p>List hasn't contacts (Empty List)</p>
                  ):(
                    <div className="table-wrapper">
                      <DopeTable
                        keyField="_id"
                        columns={columns}
                        data={contactData.data.map((item) => (                    
                          {
                            _id: item._id,
                            dateSending: item.automations.filter(el=>(el.id===automationId)).length ? moment(item.automations.find(el=>(el.id===automationId)).date).format('MMM DD, YYYY') : '--',
                            firstName: item.firstName,
                            lastName: item.lastName,                    
                            address1: item.address1,
                            address2: item.address2,
                            city: item.city,
                            state: item.state,
                            zip: item.zip,
                            zip4: item.zip4,
                            phone: item.phone,
                          }
                        ))}                
                        rowClasses="table-row"
                        // woPagination
                        paginationOptions={paginationOptions}
                        loading={automationPreview.listLoading}                    
                      />
                    </div>
                  ) 
                )              
              }                        
            </div>
          )
          }
        </div>        
      </React.Fragment>        
    </div>
  );
}
