import React from 'react';
import * as Datetime from 'react-datetime';
import * as moment from 'moment';
import {
    Button,
    Modal,
    ModalHeader,
    ModalFooter, Form, FormGroup, Label, Input,
} from 'reactstrap';

export default class extends React.Component {

    state = {
        coupon: {

            accountDiscount: 0,
            createdAt: '',
            expirationDate: '',
            name: '',
            value: '',
            percentageDiscount: '',
            quantityAvailable: '',
            startDate: '',
        }
    };

    componentDidMount(nextProps, nextContext) {
        if(this.props.data) {
            this.setState({
                coupon: {
                    ...this.props.data,
                    expirationDate: moment(this.props.data.expirationDate),
                    startDate: moment(this.props.data.startDate),
                }
            }, () => {
                console.log(this.state)
            })
        }
    }

    handleChange = (event) => {
        console.log(event);
        this.setState({
            coupon: {
                ...this.state.coupon,
                [event.target.name]: event.target.value
            }
        });
    };

    handleDate = (field, date) => {

        this.setState({
            coupon: {
                ...this.state.coupon,
                [field]: date
            }
        });
    };

    render() {
        // let coupon = {state};
        return (
            <Modal isOpen={this.props.open} toggle={this.props.toggle}>
                <ModalHeader>{this.state.coupon._id ? 'Edit' : 'Create'} Coupon</ModalHeader>
                <div style={{'margin': '1em'}} >
                    <Form>
                        <FormGroup>
                            <Label for="name">Name</Label>
                            <Input type="text" name="name" id="name" value={this.state.coupon.name} onChange={this.handleChange} />
                        </FormGroup>
                        <FormGroup>
                            <Label for="percentageDiscount">% Discount</Label>
                            <Input type="text" name="percentageDiscount" id="percentageDiscount" value={this.state.coupon.percentageDiscount} onChange={this.handleChange} />
                        </FormGroup>
                        <FormGroup>
                            <Label for="accountDiscount">$ Discount</Label>
                            <Input type="text" name="accountDiscount" id="accountDiscount" value={this.state.coupon.accountDiscount} onChange={this.handleChange} />
                        </FormGroup>
                        <FormGroup>
                            <Label for="quantityAvailable">Quantity Available</Label>
                            <Input type="text" name="quantityAvailable" id="quantityAvailable" value={this.state.coupon.quantityAvailable} onChange={this.handleChange} />
                        </FormGroup>
                        <FormGroup>
                            <Label for="value">Coupon code</Label>
                            <Input type="text" name="value" id="value" value={this.state.coupon.value} onChange={this.handleChange} />
                        </FormGroup>
                        <FormGroup>
                            <Label for="expirationDate">Start Date</Label>
                            <Datetime locale="us" name="startDate" id="startDate" value={this.state.coupon.startDate} onChange={(date) => this.handleDate('startDate', date)} />
                        </FormGroup>
                        <FormGroup>
                            <Label for="expirationDate">Expiration Date</Label>
                            <Datetime locale="us" name="expirationDate" id="expirationDate" value={this.state.coupon.expirationDate} onChange={(date) => this.handleDate('expirationDate', date)} />
                        </FormGroup>
                    </Form>
                </div>
                <ModalFooter>
                    <Button className='secondary' onClick={this.props.toggle}>Close</Button>
                    <Button className='primary' onClick={() => this.props.onSave(this.state.coupon)}>{this.props.data && this.state.coupon._id ? 'Save' : 'Create'}</Button>
                </ModalFooter>
            </Modal>
        );
    }
}
