import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Switch, Route, useRouteMatch } from 'react-router-dom';
import EddmList from './EddmList';
import EddmCreate from './EddmCreate';
import { resetCreateEddm } from 'actions/eddm';
import './Eddm.scss';

export default function Eddm(props) {  
  // const configs = useSelector(state => state.dbConfigs.configs);
  // const ownPriceData = useSelector(state => state.general.ownPrice);  
  // const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const dispatch = useDispatch();
  const { url } = useRouteMatch();
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const [savedAccountId, setSavedAccountId] = useState('');  

  useEffect(() => {    
    // console.log("Eddm mount");
    return () => {      
      dispatch(resetCreateEddm());
    };
  }, []);

  useEffect(() => {         
    setSavedAccountId(currentAccountId);
  }, [currentAccountId]);
  
  return (        
    <Switch>
      <Route exact path={`${url}`}>
        <EddmList/>
      </Route>
      <Route exact path={`${url}/:eddmId/:typePreview?`}>
        <EddmCreate savedAccountId={savedAccountId}/>
      </Route>
    </Switch>    
  );
}
