import React, {useState} from 'react';
import {
  DopeModal,
  DopeModalHeader,
  DopeModalBody,
  DopeModalFooter,
} from 'components/DopeModal';
import { FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeButton } from 'components/DopeForms';
import './NeighborhoodBlitzConfirmationPoPup.scss';
import {DopeTable} from "components/DopeTable";
import {columnsFunc} from "./config";



export default function NeighborhoodBlitzConfirmationPoPup({isOpen, toggle, onConfirm, data, templateStatus, templateName, setTemplateName}) {
  const [error, setError] = useState('');
  const validation = () => {
    if(templateStatus==='saved'&&!templateName.length) {
      setError('Template name is required!');
      return
    }
    onConfirm();
  }
  const [tablePageNumber, setTablePageNumber] = useState(1);
  const tablePageLimit = 10;

  const paginationOptions = {
    page: tablePageNumber,
    onPageChange: (value) => setTablePageNumber(value.page),
    limit: tablePageLimit,
    totalSize: data.neighborhoods.length
  };

  return (
    <DopeModal
      className='postcard-approval-modal'
      // onOpened={onOpened}
      isOpen={isOpen}
      centered
    >
      <DopeModalHeader toggle={toggle}>{`${(templateStatus!=='saved')?'Confirm Neighborhood Blitz':'Confirm your Template'}`}</DopeModalHeader>
      <DopeModalBody>
        {
          (templateStatus!=='saved')&&(
            <React.Fragment>
              <p className="approval-text">By confirming the sending, you will make it ready for sending on the specified day. </p>
              <div className="approval-row">
                <div className="approval-row-item">
                  <h4 className="item-title">Addresses ({data.neighborhoods.length})</h4>
                  <DopeTable
                    keyField="_id"
                    columns={[...columnsFunc()]}
                    data={data.neighborhoods.reduce((prev, current, index) => {
                      if((index >= tablePageNumber * tablePageLimit - tablePageLimit) && (index < tablePageNumber * tablePageLimit)){
                        return [...prev, {
                          _id: `${current.street}-${index}`,
                          index: index + 1,
                          address: `${current.street} ${current.suite ? ', ' + current.suite : ''}`,
                        }]
                      }
                      return prev;
                    }, [])}
                    paginationOptions={paginationOptions}
                  />
                </div> 
              </div>
            </React.Fragment>
          )
        }     
        <div className="approval-grid">
          <div className="approval-grid-item">
            <h4 className="item-title">
              Front Side
            </h4>
            <div className="img-prev-container">
              {
                data.front ? (
                  <img src={data.front} alt="img-prev"/>
                ):null
              }
            </div>
          </div>
          <div className="approval-grid-item">
            <h4 className="item-title">
              Back Side
            </h4>
            <div className="img-prev-container">
              {
                data.back ? (
                  <img src={data.back} alt="img-prev"/>
                ):null
              }
            </div>
          </div>
        </div>
        {templateStatus==='saved'&&(
          <FormGroup className="dope-form-group templateName-container">
            <DopeInput
              value={templateName}
              title='Template Name'
              supText={!templateName.length?'*':''}
              maxLength={30}
              error={error}
              onChange={e => {
                setError('');
                setTemplateName(e.target.value)}
              }
            />
          </FormGroup>
        )} 
      </DopeModalBody>
      <DopeModalFooter>
        <DopeButton color='white' onClick={toggle}>
          Cancel
        </DopeButton>
        <DopeButton
          color='primary'
          onClick={validation}
        >          
          {`${(templateStatus!=='saved')?'Confirm Mailing':'Save Template'}`}
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>
  );
}
