import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import * as signUpActions from '../../../actions/signUp';
import { toast } from 'react-toastify';
import { setResetPassAlertMessage } from 'actions/index';
import NewPasswordForm from './NewPasswordForm';

const passwordsText = "Must contain at least 8 characters, including uppercase, lowercase, and numbers.";
const passwordsMatchText = "Those passwords doesn't match.";

class NewPassword extends Component {
  state = {
    data: {
      password: '',
      confirmPassword: '',
    },
    errors: {
      password: '',
      confirmPassword: '',
    },
    loading: false,
    passwordVisibility: false,
    confirmPasswordVisibility: false,
    forgotPasswordToken: '',
  };

  componentDidMount() {    
    const url = window.location.href;       
    const params = new URL(url);        
    const forgotPasswordToken = params.searchParams.get('forgotPasswordToken'); 
    if (forgotPasswordToken) {      
      this.setState({ forgotPasswordToken });       
    }    
  }

  onChange = e => {
    const data = {
      data: {
        ...this.state.data,
        [e.target.name]: e.target.value,
      },
      errors: {
        ...this.state.errors,
        [e.target.name]: '',
      },
    };
    this.setState({ ...data });
  };

  togglePasswordVisibility = e => {
    this.setState({ [e.target.name]: !this.state[e.target.name] });
  };

  validation = () => {    
    const { password, confirmPassword } = this.state.data;
    let errors = this.state.errors;
    const passRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)\S{8,}$/;   
    if( !passRegex.test(password) || (password !== confirmPassword) ){            
      if( !passRegex.test(password) ){        
        errors.password = passwordsText;
      }
      if (password !== confirmPassword) {  
        errors.confirmPassword = passwordsMatchText;
      }
      this.setState({ errors });      
      return false;
    }
    this.setState({
      errors: {  
        ...this.state.errors,      
        password: '',
        confirmPassword: '',
      },
    });
    return true;
  };

  submit = async e => {    
    e.preventDefault();
    if (!this.validation()) return;
    this.setState({ loading: true });
    try {      
      let response = await this.props.auth.sendNewPassword( this.state.forgotPasswordToken, this.state.data.password );
      console.log(response);
      if(response.status === 200){
        toast.success(response.data.message);
        setTimeout(() => {
          this.props.history.replace('/');
        }, 3000); 
        this.setState({ loading: false });  
      }                  
    } catch (error) {      
      if(error.response.status === 400){        
        this.props.history.replace(`/verification?error=${error.response.data.message||'Forgot password token is invalid or expired'}`);
      }else{
        this.setState({ loading: false });
      }      
    }    
  };

  render() {
    const {
      data,
      errors,
      loading,
      passwordVisibility,
      confirmPasswordVisibility,
    } = this.state;
    return (
      <NewPasswordForm
        onChange={this.onChange}
        data={data}
        errors={errors}
        loading={loading}
        passwordVisibility={passwordVisibility}
        confirmPasswordVisibility={confirmPasswordVisibility}
        togglePasswordVisibility={this.togglePasswordVisibility}
        submit={this.submit}
      />
    );
  }
}

const mapDispatchToProps = {
  ...signUpActions,
  setResetPassAlertMessage,
};

export default connect(null, mapDispatchToProps)(withRouter(NewPassword));
