import React, {useEffect, useState} from 'react';
import { useHistory, useRouteMatch } from 'react-router-dom';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { HANDWRITTEN_PATH } from 'utils/constants';
import {useDispatch, useSelector} from "react-redux";
import {FadeLoader} from "react-spinners";
import {getHandwrittenListRequest, deleteHandwrittenOrderRequest} from "actions/handwritten";
import './HandwrittentList.scss';
import {DopeTable} from "components/DopeTable";
import moment from "moment";
import {columnsFunc} from "./config";
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import DeleteHandwrittenItemModal from "./DeleteHandwrittenItemModal";

export default function HandwrittenList() {
  let { url } = useRouteMatch();
  let history = useHistory();
  const dispatch = useDispatch();
  const currentAccountId = useSelector(state => state.account.currentAccount._id);

  useEffect(() => {
    const data = {
      accountId: currentAccountId,
      query: {
        page: 0,
        limit: 10,
      },
    };
    dispatch(getHandwrittenListRequest(data));
  }, [currentAccountId]);

  const handwrittenList = useSelector(state => state.handwritten.handwrittenList);
  const isLoading = useSelector(state => state.handwritten.loading);
  const [deletePopupOpen, setDeletePopupOpen] = useState(false);
  const [rowInfo, setRowInfo] = useState({});

  const deleteHandwrittenOrder = () => {
    dispatch(deleteHandwrittenOrderRequest(rowInfo))
  };

  const onSort = (field, order)=>{
    if(field === 'name') field = 'firstName';
    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder };
    updateData(pagination);
  };

  const columns = columnsFunc(onSort);

  const customColumn = {
    dataField: "remove",
    text: "",
    sort: false,
    formatter: () => <DeleteIcon className="del-icon"/>,
    attrs: { className: "icon-cell" }
  };

  const rowClasses = (route) => {
    if(route.status === 'sent') return 'table-row sent';
    if(route.status === 'paid') return 'table-row in-progress';
    return 'table-row';
  };

  const updateData = (pagination = { page: handwrittenList.query.page, limit: 10, sort: handwrittenList.query.sort }) => {
    if(!pagination.sort && handwrittenList.query.sort) pagination.sort = handwrittenList.query.sort;
    if(pagination.address===undefined && handwrittenList.query.address) pagination.address = handwrittenList.query.address;
    let data = {
      accountId: currentAccountId,
      query: {
        ...pagination,
        page: pagination.page - 1,
      }
    };
    dispatch(getHandwrittenListRequest(data))
  };

  const paginationOptions = {
    page: handwrittenList.query ? handwrittenList.query.page + 1 : 1,
    limit: handwrittenList.query ? handwrittenList.query.limit : 10,
    onPageChange: updateData,
    totalSize: handwrittenList.total
  };

  return (
    <div className='HandwrittenList'>
      <div className="title-container">
        <h1 className="title">Handwritten</h1>
        <DopeButton
          color="primary"
          onClick={()=>{history.push(`${HANDWRITTEN_PATH}/create`);}}
        >
          create New Handwritten
        </DopeButton>
      </div>
      <div className="handwrittenListTable">
        <div className="handwrittenList-header">
          <div>
            {handwrittenList.total > 0
              ? <span>{handwrittenList.total} Handwritten orders</span>
              : <span>No Handwritten Found</span>
            }
          </div>
        </div>
        { (handwrittenList.total > 0) ? (
          <DopeTable
            keyField="_id"
            columns={[...columns, customColumn]}
            data={handwrittenList.data.map((item, i) => (
              {
                _id: item._id,
                index: i + 1 + (handwrittenList.query ? handwrittenList.query.page * handwrittenList.query.limit : 0),
                name: item.name,
                size: item.size,
                contacts: item.list.listProperties.verifiedContacts,
                status: item.status,
                price: `$ ${item.price ? (item.price/100).toFixed(2) : 0}`,
                createdAt: moment(item.createdAt).format('MMM DD, YYYY'),
              }
            ))}
            rowClasses={rowClasses}
            loading={isLoading}
            paginationOptions={paginationOptions}
            rowEvents={{
              onClick: (e, row) => {
                if(e.target.tagName === 'svg' || e.target.tagName === 'path'){
                  setRowInfo({
                    handwrittenId: row._id,
                    accountId: currentAccountId
                  });
                  setDeletePopupOpen(true);
                  return;
                }
                const {_id, status='new'} = row;
                switch(status) {
                  case 'new':
                    history.push(`${url}/create`);
                    break;
                  case 'draft':
                    history.push(`${url}/${_id}/edit`);
                    break;
                  case 'sent':
                  case 'paid':
                    history.push(`${url}/${_id}/view`);
                    break;
                  default:
                    history.push(`${url}/create`);
                }
              }
            }}
          />
        ) : (
          <div className="nohandwrittenList">
            {isLoading && (
              <div className='loader-container'>
                <FadeLoader size={100} color={'#403d39'} />
              </div>
            )}
            <span>No Handwritten Found</span>
          </div>
        )
        }
      </div>
      <DeleteHandwrittenItemModal
        isOpen={deletePopupOpen}
        toggle={() => setDeletePopupOpen(!deletePopupOpen)}
        deleteHandwrittenOrder={deleteHandwrittenOrder}
      />
    </div>
  );
}