import React from 'react';
import { withRouter } from 'react-router-dom';
import * as incomingCallsActions from 'actions/dashboard';
import * as mainActions from 'actions';
import { connect } from 'react-redux';
import { toggleMapElementInfoStatus } from 'actions';
import { DopeTable } from 'components/DopeTable/DopeTable';
import { pathOr } from 'ramda';
// import { FadeLoader } from 'react-spinners';
import * as moment from 'moment';
import 'moment-timezone';

import {columnsFunc} from './config';


const rowStyle = () => {
  const style = {};

  style.cursor = 'pointer';

  return style;
};

class YardSignTrackerList extends React.Component {
  state = {
    sort: null,
  };
  onPageChange = pagination => {
    if(!pagination.sort && this.state.sort) pagination.sort = this.state.sort
    this.props.updateTable(pagination);
  };

  formatCalls = calls => {
    let formatedCalls = [];

    calls.forEach(item => {      
      const fullName =
        item.firstName && item.lastName
          ? `${item.firstName} ${item.lastName}`
          : '';
      const address1 = item.address1 || '';
      const address2 = item.address2 || '';
      const city = item.city || '';
      const state = item.state || '';
      const zip = item.zip || '';
      const timeStamp = pathOr('', ['callTime'], item);
      const phone = item.phoneNumber || '';
      const lat = pathOr('', ['contact', 'latitude'], item);
      const lng = pathOr('', ['contact', 'longitude'], item);
      const call = {
        _id: item._id,
        fullName: fullName,
        phone,
        address1,
        address2,
        timeStamp: moment(timeStamp).tz(this.props.timeZone).format('MMM DD YYYY - hh:mm a'),
        city,
        state,
        zip,
        lat: lat,
        lng: lng,
      };
      formatedCalls.push(call);
    });

    return formatedCalls;
  };

  onSort = (field, order) => {
    if(field === 'timeStamp') field = 'createdAt';
    else if(field === 'fullName') field = 'callerDetails.firstName';
    else if(field === 'phone') field = 'phoneNumber';
    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder};
    this.setState({sort: newOrder})
    this.onPageChange( pagination );
  }

  columns = columnsFunc(this.onSort)
  

  render() {
    const {
      page,
      pages,
      totalSize,
      calls,
      isPaginate,
    } = this.props.dashboard.incomingCalls;

    let paginationOptions = {
      onPageChange: this.onPageChange,
      page: page + 1,
      paginationSize: pages,
      totalSize: totalSize,
    };

    return (
      <React.Fragment>
        {calls.length && (
          <DopeTable
            keyField='_id'
            columns={this.columns}
            data={this.formatCalls(calls)}
            rowEvents={{
              onClick: (e, row, rowIndex) => {
                let marker = row;

                if (!marker.lat || !marker.lng) return;

                const map = document.getElementById('map');

                map &&
                  map.scrollIntoView({ block: 'center', behavior: 'smooth' });
                this.props.toggleMapElementInfoStatus(
                  marker._id,
                  marker.lat,
                  marker.lng,
                );
                this.props.setCurrentJob();
                this.props.showAddPinBtn('hide');
              },
            }}
            rowStyle={rowStyle}
            rowClasses='table-row'
            paginationOptions={paginationOptions}
            loading={isPaginate}
          />
        )}
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => ({
  markers: state.map.markers,
  openedInfo: state.map.openedInfo,
  dashboard: state.dashboard,
  currentAccount: state.account.currentAccount,
  timeZone:  state.account.timeZone,
});

const mapDispatchToProps = {
  toggleMapElementInfoStatus,
  ...incomingCallsActions,
  ...mainActions,
};

export default connect(mapStateToProps, mapDispatchToProps)(
  withRouter(YardSignTrackerList),
);
