import {
    SET_CURRENT_JOB,
    SET_ORDER_SUBMISSION_STATUS
} from '../actions';

export default function reducer(state = [], action) {
    switch (action.type) {
        case SET_CURRENT_JOB:
           if(!action.currentJob) return {
            ...state,
            currentJob: null
            }
            return {
                ...state,
                currentJob: {
                    id: action.currentJob.id,
                    name: action.currentJob.name,
                    type: action.currentJob.type,
                    pins: action.currentJob.pins,
                    amount: action.currentJob.yardSign.amount,
                }
            }
        case SET_ORDER_SUBMISSION_STATUS:
            return {
                ...state,
                orderStatus: action.status.status
            }
        default:
            // console.log('Reducer Called', action);
            return state;
    }
}