import React, { useState, useEffect } from "react";
import { TabContent, TabPane, Nav, NavItem, NavLink } from "reactstrap";
import { useSelector, useDispatch } from "react-redux";
import { useParams, useHistory } from 'react-router-dom';
import { friendlyFormatPhoneNumber} from 'utils/phones';
import moment from 'moment';
import Pagination from 'rc-pagination';
import localeInfo from 'rc-pagination/lib/locale/en_US';
import { FadeLoader } from 'react-spinners';
import {	
	Alert,
} from 'reactstrap';
import { ReactComponent as PrevIco } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as NextIco } from 'assets/img/icons/chevron-right.svg';
import { ReactComponent as AttentionIcon } from 'assets/img/icons/attention.svg';
import { getReportCampaignNamesRequest,
  getReportCampaignConversionRequest,
  getReportCampaignStatisticRequest,
  resetReportCampaignPage,  
  resetCampaignsPage,
} from 'actions/campaigns';

import "./ReportCampaign.scss";

export default function ReportCampaign({savedAccountId}) {  
  const [paginationPage, setPaginationPage] = useState(0);  
  const paginationLimit = 5;
  const currentAccountId = useSelector(state => state.account.currentAccount._id);

  let { campaignId } = useParams();
  const dispatch = useDispatch();
  let history = useHistory();

  const data = useSelector(state => state.campaigns.reportCampaign); 
  useEffect(() => {      
    window.scrollTo({ top: 0, left: 0, behavior: 'auto' });
    const statisticData = {
      params: {
        campaignId: campaignId,
      },
    },
    conversionData = {
      params: {
        campaignId: campaignId,
      },      
      query: {
        page: paginationPage,
        limit: paginationLimit,
      },
    },
    reportNamesData = {
      params: {
        campaignId: campaignId,
      }
    };    
    dispatch(getReportCampaignStatisticRequest(statisticData));
    dispatch(getReportCampaignConversionRequest(conversionData));    
    if(!data.campaignName||!data.listName){      
      dispatch(getReportCampaignNamesRequest(reportNamesData));
    }
    // 
    return () => {
      dispatch(resetReportCampaignPage());
    };
  }, []); 
  useEffect(() => {      
    if((savedAccountId !== currentAccountId) && (savedAccountId !== '')){
      dispatch(resetCampaignsPage());
      history.push('/campaigns');
    }    
  }, [currentAccountId]); 
  
  const [activeTab, setActiveTab] = useState("3");
  const toggle = tab => {
    if (activeTab !== tab) setActiveTab(tab);
  }; 

  const totalSendings = useSelector(state => state.campaigns.reportCampaign.totalSendings);

  const onPageChange = page => {    
    setPaginationPage( page - 1);        
    const paginationConversionData = {
      params: {
        campaignId: campaignId,
      },      
      query: {
        page: page - 1,
        limit: paginationLimit,
      },
    };
    dispatch(getReportCampaignConversionRequest(paginationConversionData));
  };    

  let   
    calcMaxPSend = 0,
    calcMaxCals = 0,
    calcMaxConvRate = 0;

  if (data.sentSendings){       
    calcMaxPSend = data.sentSendings.reduce((pr,cr) => cr.postcardsSent>pr?cr.postcardsSent:pr,-Infinity);
    calcMaxCals = data.sentSendings.reduce((pr,cr) => cr.callsReceived>pr?cr.callsReceived:pr,-Infinity);       
    calcMaxConvRate = 1;
  }
  
  return (
    <div className="report-campaign-container">      
      <h2 className="report-title"> {data.campaignName} – Campaign Report</h2>
      <div className="stat-container">
        <div className="stat-th">
          <h4 className="stat-title">List</h4>
          <span className="stat-info">{data.listName}</span>
        </div>
        <div className="stat-td">
          <h4 className="stat-title">Sendings Sent</h4>
          <span className="stat-info">{data.totalSendingsSent}</span>
        </div>
        <div className="stat-td">
          <h4 className="stat-title">Postcards Sent</h4>
          <span className="stat-info">{data.totalPostcardsSent}</span>
        </div>
        <div className="stat-td">
          <h4 className="stat-title">Calls Received</h4>
          <span className="stat-info">{data.callsReceived}</span>
        </div>
        <div className="stat-td">
          <h4 className="stat-title">Conversion Rate</h4>
          <span className="stat-info">{data.conversionRate}&nbsp;%</span>
        </div>
      </div>

      <div className="performance-container">
        { data.conversionIsLoading && (          
          <div className='loader-container'>
            <FadeLoader size={100} color={'#403d39'} />
          </div>
        )}
        <div className="perf-th">
          <h4 className="perf-title">Campaign Performance</h4>
          <Nav tabs className="perf-nav-tabs">
            <NavItem className="perf-nav-item">
              <NavLink
                className={activeTab === "1" ? "active" : ""}
                onClick={() => {
                  toggle("1");
                }}
              >
                Postcards Sent
              </NavLink>
            </NavItem>
            <NavItem className="perf-nav-item">
              <NavLink
                className={activeTab === "2" ? "active" : ""}
                onClick={() => {
                  toggle("2");
                }}
              >
                Calls Received
              </NavLink>
            </NavItem>
            <NavItem className="perf-nav-item">
              <NavLink
                className={activeTab === "3" ? "active" : ""}
                onClick={() => {
                  toggle("3");
                }}
              >
                Conversion Rate
              </NavLink>
            </NavItem>
          </Nav>
        </div>
        <TabContent activeTab={activeTab} className="perf-tab-content">
          <TabPane tabId="1">
            {data.sentSendings &&
              data.sentSendings.map((sending, i) => (
                <div className="perf-item" key={sending._id}>
                  <div className="p-item-info">
                    <span className="p-item-date">{moment(sending.sendingDate).format('MMM DD, YYYY')}</span>
                    <span className="p-item-phone">{friendlyFormatPhoneNumber(sending.trackingNumber)}</span>
                  </div>
                  <div className="p-item-preview">
                    <div className="preview-img-box">
                    {sending.previewUrls&&sending.previewUrls.frontMini&&
                      <img
                        className="preview-img"
                        src={sending.previewUrls.frontMini}
                        alt="front"
                      />
                    }
                    </div>
                    <div className="preview-img-box">
                    {sending.previewUrls&&sending.previewUrls.backMini&&
                      <img
                        className="preview-img"
                        src={sending.previewUrls.backMini}
                        alt="front"
                      />
                    }
                    </div>
                  </div>
                  <div className="chart-container">
                    <span
                      className="perf-chart"
                      style={{ width: `${(sending.postcardsSent / calcMaxPSend) * 100}%` }}
                    >
                      <span className="perf-chart-value">{sending.postcardsSent}</span>
                    </span>
                  </div>
                </div>
              ))}
          </TabPane>
          <TabPane tabId="2">
            {data.sentSendings &&
              data.sentSendings.map((sending, i) => (
                <div className="perf-item" key={sending._id}>
                  <div className="p-item-info">
                    <span className="p-item-date">{moment(sending.sendingDate).format('MMM DD, YYYY')}</span>
                    <span className="p-item-phone">{friendlyFormatPhoneNumber(sending.trackingNumber)}</span>
                  </div>
                  <div className="p-item-preview">
                    <div className="preview-img-box">
                      {sending.previewUrls&&sending.previewUrls.frontMini&&
                        <img
                          className="preview-img"
                          src={sending.previewUrls.frontMini}
                          alt="front"
                        />
                      }
                    </div>
                    <div className="preview-img-box">
                      {sending.previewUrls&&sending.previewUrls.backMini&&
                        <img
                          className="preview-img"
                          src={sending.previewUrls.backMini}
                          alt="front"
                        />
                      }
                    </div>
                  </div>
                  <div className="chart-container">
                    <span
                      className="perf-chart"
                      style={{ width: `${(sending.callsReceived / calcMaxCals) * 100}%` }}
                    >
                      <span className="perf-chart-value">{sending.callsReceived}</span>
                    </span>
                  </div>
                </div>
              ))}
          </TabPane>
          <TabPane tabId="3">
          {data.sentSendings &&
            data.sentSendings.map((sending, i) => (
              <div className="perf-item" key={sending._id}>
                <div className="p-item-info">
                  <span className="p-item-date">{moment(sending.sendingDate).format('MMM DD, YYYY')}</span>
                  <span className="p-item-phone">{friendlyFormatPhoneNumber(sending.trackingNumber)}</span>
                </div>
                <div className="p-item-preview">
                  <div className="preview-img-box">
                  {sending.previewUrls&&sending.previewUrls.frontMini&&
                    <img
                      className="preview-img"
                      src={sending.previewUrls.frontMini}
                      alt="front"
                    />
                  }
                  </div>
                  <div className="preview-img-box">
                  {sending.previewUrls&&sending.previewUrls.backMini&&
                    <img
                      className="preview-img"
                      src={sending.previewUrls.backMini}
                      alt="front"
                    />
                  }
                  </div>
                </div>
                <div className="chart-container">
                  {
                    sending.isOwn ? (                      
                      <Alert color="warning" className="dope-attention">
											  <AttentionIcon className="icon"/>
                        <span>
                          "Conversion rate is only calculated when you use tracking number"
                        </span>
                      </Alert>	
                    ):(
                      <span className="perf-chart" style={{ width: `${( (sending.callsReceived / sending.postcardsSent) / calcMaxConvRate) * 100}%` }}>
                        <span className="perf-chart-value">{sending.postcardsSent>0 ? (sending.callsReceived / sending.postcardsSent * 100).toFixed(2) : 0 }&nbsp;%</span>
                      </span>
                    )
                  }                  
                </div>
              </div>
            ))}
          </TabPane>
        </TabContent> 
        <Pagination
          className='dope-pagination'         
          current={paginationPage + 1}          
          defaultPageSize={paginationLimit}
          total={totalSendings}          
          hideOnSinglePage={true}
          onChange={onPageChange}          
          locale={localeInfo}
          prevIcon={<PrevIco/>}
          nextIcon={<NextIco/>}
          jumpPrevIcon={<span>...</span>}
          jumpNextIcon={<span>...</span>}
          showTitle={false}
        />       
      </div>
    </div>
  );
}
