import service from './service';
import { API_URL } from '../config/config';

export const createBillingAddressApi = (path) => {
  return service.get(
    `${API_URL}/accounts/${path}`
  );
};

export const createSubmitPostcardApi = (data) => {
  return service.post(
    `${API_URL}/postcards/send?account=${data.currentId}`, data
  );
};

export const savePostcardTemplateApi = (data) => {
  return service.post(
    `${API_URL}/postcards/save?account=${data.queries}`, data.body
  );
};

export const getAllTemplatesApi = (data) => {
  return service.get(
    `${API_URL}/postcard-templates`,
    data
  );
};

export const getPostcardsApi = (data) => {
  return service.get(
    `${API_URL}/postcards`,
    data
  );
};

export const getPostcardByIdApi = (data) => {
  return service.get(
    `${API_URL}/postcards/${data.id}`
  );
};

export const createGetContactsApi = (data) => {  
  return service.get(
    `${API_URL}/contacts`,
    data
  );
};

export const createGetListRequestApi = (data) => {
  return service.get(
    `${API_URL}/list-request?account=${data}`
  );
};

export const deleteMyPostcardApi = (data) => {
  // OLD
  // return service.delete(
  //   `${API_URL}/postcards?account=${data.query.account}&postcardId=${data.query.postcardId}`,
  //   data.body
  // );
  return service.delete(
    `${API_URL}/accounts/${data.accountId}/templates/${data.templateId}`
  );
};

export const getTemplatesWithCategoryApi = (data) => {
  return service.get(
    `${API_URL}/postcard-templates`,
    data
  );
};

export const getPostcardPreviewImgApi = (data) => {
  // console.log("getPostcardPreviewImgApi data:", data);
  return service.post(
    `${API_URL}/convert-image`,
    data,
  )
};

export const saveUniPostcardTemplateApi = (data) => {    
  return service.post(
    `${API_URL}/accounts/${data.accountId}/templates`,
    data.body,
  )
};


export const getUniMyPostcardTemplatesApi = (data) => {  
  return service.get(
    `${API_URL}/accounts/${data.accountId}/templates`, data.query
  );
};