import React from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';

export default function DeleteRequestPopup({isOpen,toggle,deleteRequest}) {
  const onSubmit = () => {   
    deleteRequest();
    toggle();
  };

  return (    
    <DopeModal className='DeleteAccountModal' isOpen={isOpen} toggle={toggle} centered>
      <DopeModalHeader className='dope-modal-header' toggle={toggle}>
        Delete Request?        
      </DopeModalHeader>
      <DopeModalFooter>
        <DopeButton color='white' onClick={toggle}>
          Cancel
        </DopeButton>
        <DopeButton color='primary' onClick={onSubmit}>
          Delete
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>    
  );
}
