import { all, call, put, takeLatest } from 'redux-saga/effects';
import { toast } from 'react-toastify';
import * as generalActions from 'actions/general';
import * as listDataActions from '../actions/listData';
import {pathOr} from 'ramda';

import {
  sendListDataRequestApi,  
} from 'requests/listData';

export function* sendListDataRequest(action) {  
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true }); 
  try {
    const response = yield call(sendListDataRequestApi, action.data);    
    if(response && response.status === 200) {
      console.log("response:",response);                
    } 
    yield put({
      type: listDataActions.TOGGLE_LIST_DATA_FINAL_MODAL,
      data: true
    });   
    yield put({
      type: listDataActions.SEND_LIST_DATA_REQUEST_SUCCESS
    });
    // no need because modal dublicate info
    // toast.success(`You have successfully sent Data request`);
  } catch (e) {    
    yield put({ type: listDataActions.SEND_LIST_DATA_REQUEST_FAILED });
    let errorMessage = pathOr("Error. Cann't sent Data request", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);    
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false }); 
}

export default all([  
  takeLatest(listDataActions.SEND_LIST_DATA_REQUEST, sendListDataRequest),  
]);
