import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Switch, Route, useRouteMatch } from 'react-router-dom';
import NeighborhoodBlitzCreate from './NeighborhoodBlitzCreate/NeighborhoodBlitzCreate';
import NeighborhoodBlitzList from "./NeighborhoodBlitzList/NeighborhoodBlitzList";
import {resetCreateNeighbors} from "actions/neighborhoodBlitz";

export default function NeighborhoodBlitz(props) {
  const dispatch = useDispatch();
  const { url } = useRouteMatch();
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const [savedAccountId, setSavedAccountId] = useState('');

  useEffect(() => {
    return () => {
      dispatch(resetCreateNeighbors());
    };
  }, []);

  useEffect(() => {         
    setSavedAccountId(currentAccountId);
  }, [currentAccountId]);

  return (
    <Switch>
      <Route exact path={`${url}`}>
        <NeighborhoodBlitzList/>
      </Route>
      <Route exact path={`${url}/:neighborhoodBlitzId/:typePreview?`}>
        <NeighborhoodBlitzCreate savedAccountId={savedAccountId}/>
      </Route>
    </Switch>
  );
}