import React, { useState, useEffect } from 'react';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { ReactComponent as ArrowUpIcon } from 'assets/img/icons/chevron-up.svg';
import { ReactComponent as ArrowDownIcon } from 'assets/img/icons/chevron-down.svg';
import { ReactComponent as CloseIcon } from 'assets/img/icons/close.svg';

import './FiltersBadges.scss';

export default function FiltersBadges({filters, onChangeFilters, isContactPage}) {

  const [filtersBadgesView, setFiltersBadgesView] = useState(false);  
  const [badgesOverflow, setBadgesOverflow] = useState(false);

  const toggleFiltersBadgesView = () => {
    setFiltersBadgesView(!filtersBadgesView); 
  }

  useEffect(() => {
    let listOverflow = {overflow: false, overflowItems: 0}    
    let badgeList = document.querySelector('.filters-badge-list');
    let badgeListWidth = badgeList.offsetWidth;
    let badgeListItems = badgeList.querySelectorAll('.filters-badge-item'); 
    listOverflow = checkBadgeListItemsWidth([...badgeListItems], badgeListWidth);    
    setBadgesOverflow(listOverflow); 
    if(!listOverflow.overflow) setFiltersBadgesView(false);   
  },[filters]);

  const checkBadgeListItemsWidth = (items, listWidth) => {
    let overflow = false, itemsWidth = 0, overflowItems = 0;        
    for (let i = 0; i < items.length; i++) {          
      itemsWidth+= items[i].offsetWidth+8;         
      if (itemsWidth > (listWidth)){
        overflow = true; 
        overflowItems = items.length - i;      
        break;
      }
      else {
        overflow = false;
        overflowItems = 0;                  
      }
    };    
    return {overflow, overflowItems};
  }

  const getFiltersBadge = (filters) => {
    const filtersBadge = [...filters.states,...filters.cities,...filters.zipCodes];      
    if ( (filtersBadge.length > 0) || (filters.lists.length > 0) ){
      return ( 
        <ul className="filters-badge-list">
          {            
            filters.lists.map((item)=>(                                          
              <li className="filters-badge-item" key={item._id}>
                <span>List:&nbsp;{item.name}</span>
                {isContactPage && (
                  <CloseIcon className="close-icon" onClick={ ()=>{removeListsFilterItem(filters,item)} }/>
                )}
              </li>
            ))            
          }
          {            
            filters.states.map((item)=>(                            
              <li className="filters-badge-item" key={item}>
                <span>State:&nbsp;{item}</span>
                <CloseIcon className="close-icon" onClick={ ()=>{removeStatesFilterItem(filters,item)} }/>
              </li>             
            ))             
          }
          {            
            filters.cities.map((item)=>(                            
              <li className="filters-badge-item" key={item}>
                <span>City:&nbsp;{item}</span>
                <CloseIcon className="close-icon" onClick={ ()=>{removeCitiesFilterItem(filters,item)} }/>
              </li>             
            ))            
          }
          {            
            filters.zipCodes.map((item)=>(                            
              <li className="filters-badge-item" key={item}>
                <span>ZIP:&nbsp;{item}</span>
                <CloseIcon className="close-icon" onClick={ ()=>{removeZipCodesFilterItem(filters,item)} }/>
              </li>             
            ))                          
          }
        </ul>
      )
    }
    else return ( 
      <ul className="filters-badge-list empty">
        <li className="filters-badge-item">
          "No filters selected"
        </li>
      </ul>
    )    
  }

  const removeListsFilterItem = (filters,item) => {      
    const resultFilter = filters.lists.map(el=>el._id).indexOf(item._id) > -1 ? filters.lists.filter(el => el._id !== item._id) : [ ...filters.lists];         
    onChangeFilters({
      ...filters,
      lists : resultFilter 
    });   
  }
  const removeStatesFilterItem = (filters,item) => {     
    const resultFilter = filters.states.indexOf(item) > -1 ? filters.states.filter(el => el !== item) : [ ...filters.states];
    onChangeFilters({
      ...filters,
      states : resultFilter 
    });   
  }
  const removeCitiesFilterItem = (filters,item) => {    
    const resultFilter = filters.cities.indexOf(item) > -1 ? filters.cities.filter(el => el !== item) : [ ...filters.cities];
    onChangeFilters({
      ...filters,
      cities : resultFilter 
    });   
  }
  const removeZipCodesFilterItem = (filters,item) => {     
    const resultFilter = filters.zipCodes.indexOf(item) > -1 ? filters.zipCodes.filter(el => el !== item) : [ ...filters.zipCodes];
    onChangeFilters({
      ...filters,
      zipCodes : resultFilter 
    });   
  }
  
  return (
    <div className={`${filtersBadgesView ? "FiltersBadges opened" : "FiltersBadges"}`}>            
      {
        getFiltersBadge(filters)
      }       
      <div className="seeAll-btn-container">  
        {
          badgesOverflow.overflow ?(         
            <DopeButton
              color="white"
              onClick={toggleFiltersBadgesView}
            >
              <span className="seeAll-text">
                +&nbsp;{badgesOverflow.overflowItems}&nbsp;more
              </span>
              {filtersBadgesView ? (
                  <ArrowUpIcon className="arrow-icon"/>
                ):(
                  <ArrowDownIcon className="arrow-icon"/>
                )
              }                
            </DopeButton>
          ):null
        }
      </div>        
    </div>
  );
}
