import { all, call, put, takeLatest } from 'redux-saga/effects';
import * as adminHandwrittenActions from '../actions/adminHandwritten';
import {
  getAdminHandwrittenListApi,
  updateAdminHandwrittenApi,
  deleteAdminHandwrittenOrderApi,
} from "requests/adminHandwritten";
import {toast} from "react-toastify";
import {pathOr} from "ramda";


export function* getAdminHandwrittenList(action) {
  try {
    const response = yield call(getAdminHandwrittenListApi, action.data);
    yield put({type: adminHandwrittenActions.GET_ADMIN_HANDWRITTEN_LIST_REQUEST_SUCCESS, data: {...response.data, ...action.data}});
  } catch (error) {
    yield put({type: adminHandwrittenActions.GET_ADMIN_HANDWRITTEN_LIST_REQUEST_FAILED, error});
  }
}


export function* updateAdminHandwritten(action) {
  try {
    const response = yield call(updateAdminHandwrittenApi, action.data);
    yield put({type: adminHandwrittenActions.UPDATE_ADMIN_HANDWRITTEN_REQUEST_SUCCESS, data: response.data});
    yield put({type: adminHandwrittenActions.GET_ADMIN_HANDWRITTEN_LIST_REQUEST, data: { query:{page:0, limit: 10} }})
  } catch (error) {
    yield put({type: adminHandwrittenActions.UPDATE_ADMIN_HANDWRITTEN_REQUEST_FAILED, error});
    let errorMessage = pathOr("Error. Cann't update Handwritten", ['response', 'data', 'error', 'dopeMessage'], error);
    toast.error(errorMessage);
  }
}

export function* deleteAdminHandwrittenOrder(action) {
  try {
    const response = yield call(deleteAdminHandwrittenOrderApi, action.data);
    yield put({type: adminHandwrittenActions.DELETE_ADMIN_HANDWRITTEN_ORDER_REQUEST_SUCCESS, data: response.data});
    toast.success(`You have successfully deleted Handwritten order`);
    yield put({type: adminHandwrittenActions.GET_ADMIN_HANDWRITTEN_LIST_REQUEST, data: { query:{page:0, limit: 10} }})
  } catch (error) {
    let errorMessage = pathOr("Error. Cann't delete Handwritten order", ['response', 'data', 'error', 'dopeMessage'], error);
    toast.error(errorMessage);
    yield put({type: adminHandwrittenActions.DELETE_ADMIN_HANDWRITTEN_ORDER_REQUEST_FAILED, error});
  }
}

export default all([
  takeLatest(adminHandwrittenActions.GET_ADMIN_HANDWRITTEN_LIST_REQUEST, getAdminHandwrittenList),
  takeLatest(adminHandwrittenActions.UPDATE_ADMIN_HANDWRITTEN_REQUEST, updateAdminHandwritten),
  takeLatest(adminHandwrittenActions.DELETE_ADMIN_HANDWRITTEN_ORDER_REQUEST, deleteAdminHandwrittenOrder),
]);