import React from 'react';
import { Card, CardBody, CardHeader } from 'reactstrap';
import request from 'utils/request';
import ToggleButton from 'react-toggle-button';
import { API_URL } from '../../../config/config';

export default class extends React.Component {
  state = {
    selected: null
  };

  onToggleDisabled = async (value) => {
    try {
      await request(
        `${API_URL}/yard-sign-templates/${this.state.selected._id}`,
        {
          method: 'post',
          body: {
            data: {
              disabled: value
            }
          }
        }
      );
      await this.props.onUpdated();
      this.setState({
        selected: this.props.templates.find(
          (x) => x._id === this.state.selected._id
        )
      });
    } catch (err) {
      console.log(err);
    }
  };

  render() {
    return (
      <Card>
        <CardHeader
          style={{ textAlign: 'center', fontWeight: 'bold', fontSize: 25 }}
        >
          Existing Templates
        </CardHeader>
        <CardBody>
          <div className="templates">
            <div className="container row">
              {this.props.templates.map((template, key) => (
                <div className="col-4 col-md-3" key={key}>
                  <img
                    className="template"
                    onClick={() => this.setState({ selected: template })}
                    src={template.thumbnail}
                    alt="new"
                  />
                </div>
              ))}
            </div>
          </div>
          {this.state.selected && (
            <div
              style={{
                marginTop: 20,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center'
              }}
            >
              <img src={this.state.selected.thumbnail} alt="new"/>
              <div
                style={{ marginTop: 20, display: 'flex', alignItems: 'center' }}
              >
                <ToggleButton
                  inactiveLabel={<span>False</span>}
                  activeLabel={<span>True</span>}
                  value={!this.state.selected.disabled}
                  onToggle={this.onToggleDisabled}
                />
                <h3 style={{ margin: 0, marginLeft: 10 }}>Enabled</h3>
              </div>
              {this.state.selected.colors &&
                this.state.selected.colors.length > 0 && (
                  <div
                    style={{
                      display: 'flex',
                      alignItems: 'center',
                      marginTop: 20
                    }}
                  >
                    <h3 style={{ margin: 0 }}>Colors:</h3>
                    {this.state.selected.colors.map(({ id, hex }) => (
                      <div
                        style={{
                          borderRadius: 5,
                          width: 36,
                          height: 36,
                          background: hex,
                          margin: 5,
                          border: '1px solid black'
                        }}
                        key={id}
                      />
                    ))}
                  </div>
                )}
            </div>
          )}
        </CardBody>
      </Card>
    );
  }
}
