import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
// import MetisMenu from 'react-metismenu';
import { MainNav, AdminNav } from './NavItems';
import { setCurrentAccount } from '../../actions';
import { resetAccountUserOwnPrice, getAccountRequest } from 'actions/account';

import Select from 'react-select';
import './VerticalNavWrapper.scss';

const Link = require('react-router-dom').Link;
class Nav extends Component {  
  
  getAccountOptions = () => {
    return this.props.accountList&&this.props.accountList.map(item => ({
      value: item,
      label: item.name||'no name',
    }));
  };

  getSelectedAccount = value => {
    const filtered = this.getAccountOptions().filter(item => {
      return item.value._id === value._id;
    });
    if (filtered.length) return filtered[0];
  };
  onlyActiveAccounts = () => {
    return this.getAccountOptions().filter(item => item.value.status==="active")
  }

  onChange = e => {    
    localStorage.setItem('current_account', JSON.stringify(e.value));
    // TODO CHECK THIS PLACE
    // this.props.setCurrentAccount(e.value);    
    // this.props.resetAccountUserOwnPrice(); //reset ownprice for postcard
    // TO HERE
    this.props.getAccountRequest(e.value._id);
  };
  render() {    
    return (
      <Fragment>
        <div className='side-menu'>
          <ul className='menu-container'>
            {MainNav.filter(item => !item.hideInSidebar&&
                                    item.group.includes(this.props.accessGroup)&&
                                    (this.props.domain!=='dope360.com'||!item.accessAccounts||item.accessAccounts.includes(this.props.currentAccount.email))
              ).map((item, i) => (                
                <li className='menu-item' key={i}>
                  <Link
                    className={this.isPathActive(item.to) ? 'active' : ''}
                    to={item.to}
                  >
                    <item.iconComponent />
                    {item.label}
                  </Link>
                </li>     
              )
            )}
          </ul>
        </div>

        {this.props.isAdmin && (
          <div className='side-menu'>            
            <h5 className='app-sidebar__heading'>Admin!</h5>
            <div className="account-select-container">
              {/* TODO */}
              <Select
                className="dope-select"
                classNamePrefix="dope"              
                value={this.getSelectedAccount(this.props.currentAccount)}
                options={this.onlyActiveAccounts()}
                onChange={this.onChange}
              />
            </div>
            <h5 className="status-text">{`Status: ${this.props.currentAccount.status||''}`}</h5>
            <ul className='menu-container'>
              {AdminNav.filter(item => !item.hideInSidebar&&
                                    item.group.includes(this.props.accessGroup)&&
                                    (this.props.domain!=='dope360.com'||!item.accessAccounts||item.accessAccounts.includes(this.props.currentAccount.email))
                ).map((item, i) => {
                return (
                  <li className='menu-item' key={i}>
                    <Link
                      className={this.isPathActive(item.to) ? 'active' : ''}
                      to={item.to}
                    >
                      <item.iconComponent />
                      {item.label}
                    </Link>
                  </li>
                );
              })}
            </ul>
          </div>
        )}
      </Fragment>
    );
  }

  isPathActive(path) {
    return this.props.location.pathname.startsWith(path);
  }
}

const mapStateToProps = state => ({
  accountList: state.account.accountList,
  currentAccount: state.account.currentAccount,
  isAdmin: state.account.isAdmin,
  accessGroup: state.general.accessGroup,
  domain: state.general.domain,
});

const mapDispatchToProps = {
  setCurrentAccount,
  getAccountRequest,
  resetAccountUserOwnPrice,  
};
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Nav));
