import { all, call, put, takeLatest } from 'redux-saga/effects';
import * as yardSignAction from 'actions/createYardSign';
import * as generalActions from '../actions/general';
import { toast } from 'react-toastify';
import { 
  getYardSignsTemplateRequestApi,
  submitYardSignsApi,
  getYardSignPreviewImgApi,
  saveUniYardSignTemplateApi,
  deleteMyYardSignApi,
  getMyYardSignTemplatesApi,
  getRecentYardSignsApi,
} from 'requests/createYardSigns';
import { S3_BUCKET_PUBLIC_NAME} from 'config/config';
import {pathOr} from 'ramda';

const errorText = 'Error please check your credit card amount and other credentials or contact with the admin.';

export function* getAllYardSignTemplates(action) {
  try {
    yield put({
      type: yardSignAction.TEMPLATE_LOADING_YS,
      data: true,
    });
    const response = yield call(getYardSignsTemplateRequestApi, action.data);    

    yield put({
      type: yardSignAction.SAVE_ALL_YARD_SIGN_TEMPLATES,
      templates: {        
        data: response.data.yardSigns,
        total: response.data.total,
        page: action.data.query.page + 1,
        limit: action.data.query.limit,
      },
    });
    yield put({
      type: yardSignAction.TEMPLATE_LOADING_YS,
      data: false,
    });
  } catch (e) {
    yield put({
      type: yardSignAction.TEMPLATE_LOADING_YS,
      data: false,
    });
  }
}

export function* submitYardSigns(action) {
  try {
    yield call(submitYardSignsApi, action.data)
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    yield put({ type: yardSignAction.FINAL_MODAL_SHOW_YARD_SIGNS, payload: true })
  } catch(e) {
    const errorMessage = pathOr(errorText, ['response', 'data', 'error', 'dopeMessage'], e);
    yield put({ type: yardSignAction.YARD_SIGN_DISABLE_BTN, data: false })
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })    
    toast.error(errorMessage)
    yield put({ type: yardSignAction.SUBMIT_YARD_SIGNS_REQUEST_FAILED, error: e })
  }
}

export function* getYardSignPreviewImg(action) { 
  try {    
    // const [response1,response2] = yield all([
    //   call(getYardSignPreviewImgApi, {template: action.data.body.template.front, bucket:action.data.body.bucket, side: 'front'}),      
    //   call(getYardSignPreviewImgApi, {template: action.data.body.template.back, bucket:action.data.body.bucket, side: 'back'})
    // ]); 
    const response1 = yield call(getYardSignPreviewImgApi, {template: action.data.body.template.front, bucket:action.data.body.bucket, side: 'front'});    
    const images = {
      front: {
        mini: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/yardsignImages/${response1.data.images.mini}`,    
        full: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/yardsignImages/${response1.data.images.full}`,
      },
      // back: {
      //   mini: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/yardsignImages/${response2.data.images.mini}`,
      //   full: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/yardsignImages/${response2.data.images.full}`
      // }
    };
    yield put({
      type: yardSignAction.GET_YARD_SIGN_PREVIEW_IMG_SUCCESS,
      data: images
    });    
    if(action.successCallback){      
      action.successCallback();
    }
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
  } catch (e) { 
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't get previewImg", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: yardSignAction.GET_YARD_SIGN_PREVIEW_IMG_FAILED, e }); 
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });   
  }
}

export function* saveUniYardSignTemplate(action) {
  try {    
    yield call(saveUniYardSignTemplateApi, action.data)    
    yield put({ type: yardSignAction.TOGGLE_YS_SAVED_TEMPLATE_MODAL })
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.success(`YardSign template has been saved to your templates.`)
  } catch(e) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.error(`YardSign template hasn't been saved to your templates.`)
    yield put({ type: yardSignAction.TOGGLE_YS_SAVED_TEMPLATE_MODAL })
    yield put({ type: yardSignAction.CREATE_SUBMIT_YARD_SIGN_REQUEST_FAILED, error: e })
  }
}

export function* deleteMyYardSign(action) {   
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    const response = yield call(deleteMyYardSignApi, action.data);    
    toast.success(response.data.message);  
    action.callbackSuccess(); 
  } catch (e) {
    let errorMessage = pathOr("Error.", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: yardSignAction.DELETE_MY_YARD_SIGN_FAILED });
  }  
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false }); 
}

export function* getMyYardSignTemplates(action) {  
  yield put({ type: yardSignAction.SHOW_MY_YARD_SIGN_TEMPLATES_LOADER, data: true });
  try {
    const response = yield call(getMyYardSignTemplatesApi, action.data);     
    let data = {};
    data = {
      ...response.data,      
      data: response.data.data.map( (el)=>(
        {          
          _id: el._id,
          front: el.templates ? el.templates.front : '',           
          front_thumbnail: el.images ? el.images.front.mini : '',
          name: el.name||'',
          size: el.size,
          createdAt: el.createdAt,
        }        
      ))
    };             
    yield put({
      type: yardSignAction.GET_MY_YARD_SIGN_TEMPLATES_SUCCESS,
      data: {...data, ...action.data.query}
    });    
  } catch (e) {
    let errorMessage = pathOr("Error.", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: yardSignAction.GET_MY_YARD_SIGN_TEMPLATES_FAILED });
  }
  // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false }); 
  yield put({ type: yardSignAction.SHOW_MY_YARD_SIGN_TEMPLATES_LOADER, data: false });  
}

export function* getRecentYardSigns(action) {
  // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true }); 
  yield put({ type: yardSignAction.SHOW_RECENT_YARD_SIGNS_LOADER, data: true });
  try {
    const response = yield call(getRecentYardSignsApi, action.data);
    // console.log("getRecentYardSigns response:",response);    
    let data = {};
    data = {
      ...response.data,
      data: response.data.data.map( (el)=>(
        {          
          _id: el._id,
          front: el.template ? el.template.front : '', 
          front_thumbnail: el.images ? el.images.front.mini : '',
          size: el.size,
          createdAt: el.createdAt,
        }        
      ))
    };             
    yield put({
      type: yardSignAction.GET_RECENT_YARD_SIGNS_SUCCESS,
      data: {...data, ...action.data.query}
    });    
  } catch (e) {    
    let errorMessage = pathOr("Error.", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: yardSignAction.GET_RECENT_YARD_SIGNS_FAILED });
  } 
  yield put({ type: yardSignAction.SHOW_RECENT_YARD_SIGNS_LOADER, data: false });  
}

export default all([
  takeLatest(yardSignAction.GET_ALL_YARD_SIGN_TEMPLATES_REQUEST,getAllYardSignTemplates),
  takeLatest(yardSignAction.SUBMIT_YARD_SIGNS_REQUEST,submitYardSigns),
  takeLatest(yardSignAction.GET_YARD_SIGN_PREVIEW_IMG_REQUEST, getYardSignPreviewImg),
  takeLatest(yardSignAction.SAVE_UNI_YARD_SIGN_TEMPLATE_REQUEST, saveUniYardSignTemplate),
  takeLatest(yardSignAction.DELETE_MY_YARD_SIGN_REQUEST, deleteMyYardSign),
  takeLatest(yardSignAction.GET_MY_YARD_SIGN_TEMPLATES_REQUEST, getMyYardSignTemplates), 
  takeLatest(yardSignAction.GET_RECENT_YARD_SIGNS_REQUEST, getRecentYardSigns),
]);

