import React, { Component } from 'react';
import {CardHeader, Table } from 'reactstrap';
import {pathOr} from 'ramda';
// import './OrderPostcardsSendingPreview.scss';

class Order extends Component {
  // builderRef = React.createRef();

  render() { 
    const amount = pathOr(0, ['postcard', 'amount'], this.props.data);
    const totalPrice = pathOr(0, ['postcard', 'price'], this.props.data)/100;
    return (
      <React.Fragment>
        {this.props.showHeader !== false && (
          <CardHeader className='header'>Postcard Order</CardHeader>
        )}
        <div className="order-preview-container"
        >         
          <Table striped>
            <tbody>
              <tr>
                <td>Postcards Sending Amount </td>
                <td>{amount}</td>
              </tr>
              <tr>
                <td>
                  <b>Total Amounts</b>
                </td>
                <td>
                  <b>${totalPrice}</b>
                </td>
              </tr>
            </tbody>
          </Table>
        </div>
      </React.Fragment>
    );
  }
}

export default Order;
