import React, {useEffect, useState} from 'react';
import { useHistory, useRouteMatch } from 'react-router-dom';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { NEIGHBORHOOD_BLITZ_PATH } from 'utils/constants';
import {useDispatch, useSelector} from "react-redux";
import {Form, FormGroup} from "reactstrap";
import { ReactComponent as SearchIco } from 'assets/img/icons/search.svg';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import {FadeLoader} from "react-spinners";
// import { debounce } from 'utils/helper';
import {getNeighborsListRequest, deleteNeighborsItemRequest} from "actions/neighborhoodBlitz";
import './NeighborhoodBlitzList.scss';
import {DopeTable} from "components/DopeTable";
import moment from "moment";
import {columnsFunc} from "./config";
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import DeleteNeighborhoodBlitzItemPopup from "./DeleteNeighborhoodBlitzItemPopup";

export default function NeighborhoodBlitzList() {
  let { url } = useRouteMatch();
  let history = useHistory();
  const dispatch = useDispatch();
  const currentAccountId = useSelector(state => state.account.currentAccount._id);

  useEffect(() => {
    const data = {
      accountId: currentAccountId,
      query: {
        page: 0,
        limit: 10,
        // test
        // templateStatus: "auto-saved",
      },
    };
    setSearchValue('');
    dispatch(getNeighborsListRequest(data));
  }, [currentAccountId]);

  const neighborhoodBlitzList = useSelector(state => state.neighborhoodBlitz.neighborhoodBlitzList);  
  const isLoading = useSelector(state => state.neighborhoodBlitz.isLoading);
  const [searchValue, setSearchValue] = useState('');
  const [deletePopupOpen, setDeletePopupOpen] = useState(false);
  const [rowInfo, setRowInfo] = useState({});
  const [searchTriggered, setSearchTriggered] = useState(false);

  const deleteNeighborsItem = () => {
    dispatch(deleteNeighborsItemRequest(rowInfo))
  };

  const onSort = (field, order)=>{
    if(field === 'name') field = 'firstName';
    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder };
    updateData(pagination);
  };

  const columns = columnsFunc(onSort);

  const customColumn = {
    dataField: "remove",
    text: "",
    sort: false,
    formatter: () => <DeleteIcon className="del-icon"/>,
    attrs: { className: "icon-cell" }
  };

  const rowClasses = (route) => {
    if(route.status === 'sent') return 'table-row sent';
    if(route.status === 'in-progress') return 'table-row in-progress';
    return 'table-row';
  };

  const updateData = (pagination = { page: neighborhoodBlitzList.query.page, limit: 10, sort: neighborhoodBlitzList.query.sort }) => {
    if(!pagination.sort && neighborhoodBlitzList.query.sort) pagination.sort = neighborhoodBlitzList.query.sort;
    if(pagination.address===undefined && neighborhoodBlitzList.query.address) pagination.address = neighborhoodBlitzList.query.address; 
    let data = {
      accountId: currentAccountId,
      query: {
        ...pagination,
        page: pagination.page - 1,
      }
    };
    dispatch(getNeighborsListRequest(data))
  };

  const paginationOptions = {
    page: neighborhoodBlitzList.query ? neighborhoodBlitzList.query.page + 1 : 1,
    limit: neighborhoodBlitzList.query ? neighborhoodBlitzList.query.limit : 10,
    onPageChange: updateData,
    totalSize: neighborhoodBlitzList.totalSize
  };

  // const debounceOnChange = React.useCallback(debounce(updateData, 700), []);

  const onChangeSearchValue = (e) => {
    !searchTriggered && setSearchTriggered(true);
    setSearchValue(e.target.value);
    //  donn't work with debounce (used prev first account in updateData)
    // debounceOnChange({ page: 1, limit: 10, sort: neighborhoodBlitzList.query.sort, address: e.target.value });
    updateData({ page: 1, limit: 10, sort: neighborhoodBlitzList.query.sort, address: e.target.value });
  };

  return (
    <div className='NeighborhoodBlitzList'>
      <div className="title-container">
        <h1 className="title">Neighborhood Blitz</h1>
        <DopeButton
          color="primary"
          onClick={()=>{history.push(`${NEIGHBORHOOD_BLITZ_PATH}/create`);}}
        >
          create New Blitz
        </DopeButton>
      </div>
      <div className="neighborhoodBlitzListTable">
        <div className="neighborhoodBlitzList-header">
          <div>
            {neighborhoodBlitzList.totalSize > 0
              ? <span>{neighborhoodBlitzList.totalSize} Neighborhood Blitz orders</span>
              : <span>No Neighborhood Blitz Found</span>
            }
          </div>
          {neighborhoodBlitzList.totalSize > 0 || searchTriggered ? (
            <div className="zipSearch">
              <Form onSubmit={e => e.preventDefault()}>
                <FormGroup className='dope-form-group searchZip'>
                  <SearchIco />
                  <DopeInput
                    type="text"
                    value={searchValue}
                    min='0'
                    placeholder={'Search by Address'}
                    onChange={onChangeSearchValue}
                  />
                </FormGroup>
              </Form>
            </div>
          ):null
          }
        </div>
        { (neighborhoodBlitzList.totalSize > 0) ? (
          <DopeTable
            keyField="_id"
            columns={[...columns, customColumn]}
            data={neighborhoodBlitzList.data.map((item, i) => (
              {
                _id: item._id,
                index: i + 1 + neighborhoodBlitzList.page * neighborhoodBlitzList.limit,
                address: item.searchAddress,
                amount: item.dataToFetch ? item.dataToFetch.length : item.searchCount,
                status: item.status,
                price: `$ ${item.price ? (item.price/100).toFixed(2) : 0}`,
                createdAt: moment(item.createdAt).format('MMM DD, YYYY'),
              }
            ))}
            rowClasses={rowClasses}
            loading={isLoading}
            paginationOptions={paginationOptions}
            rowEvents={{
              onClick: (e, row, rowIndex) => {
                if(e.target.tagName === 'svg' || e.target.tagName === 'path'){
                  setRowInfo({
                    sendingId: row._id,
                    accountId: currentAccountId
                  });
                  setDeletePopupOpen(true);
                  return;
                }
                const {_id, status='new'} = row;
                switch(status) {
                  case 'new':
                    history.push(`${url}/create`);
                    break;
                  case 'draft-route':
                    history.push(`${url}/${_id}/edit-route`);
                    break;
                  case 'draft':   //(draft-postcard)
                  case 'design-request':
                    history.push(`${url}/${_id}/edit`);
                    break;
                  case 'sent':
                  case 'in-progress':
                    history.push(`${url}/${_id}/view`);
                    break;
                  default:
                    history.push(`${url}/create`);
                }
              }
            }}
          />
        ) : (
          <div className="noNeighborhoodBlitzList">
            {isLoading && (
              <div className='loader-container'>
                <FadeLoader size={100} color={'#403d39'} />
              </div>
            )}
            <span>No Neighborhood Blitz Found</span>
          </div>
        )
        }
      </div>
      <DeleteNeighborhoodBlitzItemPopup
        isOpen={deletePopupOpen}
        toggle={() => setDeletePopupOpen(!deletePopupOpen)}
        deleteNeighborsItem={deleteNeighborsItem}
      />
    </div>
  );
}