import React from 'react';
import AddressAutocomplete from 'components/AddressAutocomplete';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { Row, Col } from 'reactstrap';
import { pathOr } from 'ramda';
import {useDispatch} from "react-redux";

export default function ShippingAddress(props) {
  const state = pathOr('', ['state'], props.data);
  const city = pathOr('', ['city'], props.data);
  const zip = pathOr('', ['zip'], props.data);
  const zip4 = pathOr('', ['zip4'], props.data);
  const address1 = pathOr('', ['address1'], props.data);
  const address2 = pathOr('', ['address2'], props.data);
  const dispatch = useDispatch();

  let address = {
    state,
    city,
    zip,
    zip4,
    address1,
    address2,
  }

  const onChange = e => {
    dispatch(props.onChange(props.type, {...address, [e.target.name]: e.target.value}));

    if(e.target.hasAttribute('autocomplete')){
      !e.target.value.length
        ? props.onErrorChange({...props.errors, [e.target.name]: 'This field is required'})
        : props.onErrorChange({...props.errors, [e.target.name]: ''})
    }
  };

  return (
    <div className='company-info-block'>
      <AddressAutocomplete
        id='address1'
        label='Address1*'
        placeholder='address1'
        name='address1'
        data-validate={true}
        onChange={onChange}
        value={address1}
        error={props.errors.address1}
        disabled={props.disabled}
        onPlaceLoaded={place =>
          props.onPlaceLoaded('address1', place, props.type)}
      />
      <div className='account-input-container company-name-container'>
        <label htmlFor='address2'>Address2</label>
        <DopeInput
          id='address2'
          name='address2'
          value={address2}
          placeholder="address2"
          onChange={onChange}
          disabled={props.disabled}
        />
      </div>

      <AddressAutocomplete
        id='city'
        label='City*'
        placeholder='city'
        name='city'
        onChange={onChange}
        value={city}
        error={props.errors.city}
        disabled={props.disabled}
        onPlaceLoaded={place => props.onPlaceLoaded('city', place, props.type)}
      />

      <AddressAutocomplete
        id='state'
        label='Select State*'
        placeholder='state'
        name='state'
        onChange={onChange}
        value={state}
        disabled={props.disabled}
        onPlaceLoaded={place => props.onPlaceLoaded('state', place, props.type)}
        error={props.errors.state}
      />
      <Row>
        <Col md={6}>
          <AddressAutocomplete
            id='zip'
            label='Zip Code*'
            placeholder='zip code'
            name='zip'
            onChange={onChange}
            value={zip}
            error={props.errors.zip}
            disabled={props.disabled}
            onPlaceLoaded={place => props.onPlaceLoaded('zip', place, props.type)}
          />
        </Col>
        <Col md={6}>
          <div className='account-input-container company-name-container'>
            <label htmlFor='zip4'>Zip4</label>
            <DopeInput
              id='zip4'
              name='zip4'
              value={zip4}
              placeholder="zip4"
              onChange={onChange}
              disabled={props.disabled}
            />
          </div>
        </Col>
      </Row>
    </div>
  );
}
