import service from './service';
import { API_URL } from '../config/config';

export const saveHandwrittenApi = (data) => {
  return service.post(
    `${API_URL}/handwritten-orders?account=${data.accountId}`, data.body
  )
};

export const updateHandwrittenApi = (data) => {
  return service.patch(
    `${API_URL}/handwritten-orders/${data.handwrittenId}?account=${data.accountId}`, data.body,
  )
};

export const buyHandwrittenApi = (data) => {
  return service.post(
    `${API_URL}/handwritten-orders?account=${data.accountId}`, data.body
  )
};

export const getHandwrittenListApi = (data) => {
  return service.get(
    `${API_URL}/handwritten-orders?account=${data.accountId}`, data.query
  )
};

export const getHandwrittenOrderApi = (data) => {
  return service.get(
    `${API_URL}/handwritten-orders/${data.handwrittenId}`, data.query
  )
};

export const deleteHandwrittenOrderApi = (data) => {
  return service.delete(
    `${API_URL}/handwritten-orders/${data.handwrittenId}?account=${data.accountId}`
  )
};
