import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory, useRouteMatch } from 'react-router-dom';
import {DopeTable} from "components/DopeTable";
import {FadeLoader} from "react-spinners";
import Select from 'react-select';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import DeleteRequestPopup from './DeleteRequestPopup';
import {columnsFunc} from "./config";
import moment from "moment";
import {
	getAdminRequestsRequest,
	resetAdminRequestsPage,
	deleteRequestRequest,
	changeRequestStatusRequest,
} from 'actions/adminRequests';
import './AdminManageRequestsList.scss';




export default function AdminManageRequestsList() { 
	const dispatch = useDispatch(); 	
	let history = useHistory();
	let { url } = useRouteMatch();
	
	useEffect(() => {
		dispatch(getAdminRequestsRequest({ query:{page:0, limit: 10} }));
    return () => {      
      dispatch(resetAdminRequestsPage());
    };
	}, []);  
	
	const loading = useSelector(state => state.adminRequests.loading);
	const requestsData = useSelector(state => state.adminRequests.requestsData);
	const default_source = useSelector(state => state.account.currentAccount.default_source);
	const [deletePopup, setDeletePopup] = useState({popupOpen: false, id: ''});

	const onSort = (field, order)=>{
		// console.log("onSort:",field,order);
		if (field==="account") field= "name";
    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder };
    updateData(pagination);
	};
	
	const columns = columnsFunc(onSort);

	const rowClasses = (route, index) => {    
    return 'table-row';
	};
	
	const updateData = (pagination = { page: requestsData.page, limit: 10, sort: requestsData.sort }) => {      
    let data = {      
      query: {
        ...pagination,
        page: pagination.page - 1,
      }
    };    
    dispatch(getAdminRequestsRequest(data))
	};

	const paginationOptions = {
    page: requestsData.page + 1,
    limit: requestsData.limit ? +requestsData.limit : 10,
    onPageChange: updateData,    
    totalSize: requestsData.total
	};
	
	const delColumn = {		
		dataField: "remove",
		text: "",
		sort: false,
		formatter: (cell, row) =>{
			return (
				<React.Fragment>									
					{row.status==='charged'&&(
						<DeleteIcon 
							className="del-icon"						
							onClick={ ()=>setDeletePopup({popupOpen:true,id:row._id }) }
						/>
					)}
				</React.Fragment>
			)
		},		
		attrs: { className: "del-cell" }
	};


	
	
	const statusFormatter = (cell, row, rowIndex, formatExtraData) => {
    if (cell==="rejected"||cell==="payment-failed"||cell==="new") {
      const statusArr = [
        { value: 'rejected', label: 'Rejected' },
        { value: 'charged', label: 'Charged' },
				{ value: 'payment-failed', label: 'Payment failed' },
				{ value: 'new', label: 'New' },
      ];
      return (
				<span onClick={e=>e.stopPropagation()}>
					<Select
						className="dope-select"
						classNamePrefix="dope"
						value={statusArr.filter(option => option.value === cell)}
						options={statusArr}
						onChange={e => changeReauestStatus(e, row._id)}          
						menuPortalTarget={document.body}
						styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }}
					/>
				</span>
      );
    } else {
      switch (cell) {
        case 'charged':
          return (
            <span className='charged status-cell'>              
              Charged
            </span>
          );        
        default:
          return '';
      }
    }
	};
	
	const statusColumn = {
    dataField: 'status',
    text: 'Status',
		sort: true,
		sortFunc: function(){
      return false
    },
    onSort: onSort,
		formatter: statusFormatter,
    attrs: { className: 'status-cell-td' },
	};

	const accountColumn = {		
		dataField: "account",
		text: "Account",
		sort: false,		
		formatter: (cell, row) =>{			
			return (
				<React.Fragment>
					<div className="acc-cell-box"> 
						<span>{row.account.name}</span>
						<span>{row.account.email}</span>
						<span>{row.account.phone}</span>
					</div>
				</React.Fragment>
			)
		},		
		attrs: { className: "acc-cell" }
	};
	
	const deleteRequestItem=()=>{		
		dispatch(deleteRequestRequest(deletePopup.id,updateData));	
	}

	const changeReauestStatus=(e,id)=>{							
		let body;
		if (e.value==='charged'){
			body={
				status: e.value,
				amount: 50, //test
				default_source: default_source, //test only
				description: 'test test test',
			}
		}else{
			body={
				status: e.value,				
			}
		}
		let data = { 
			id, 
			body
		}; 
		
		dispatch(changeRequestStatusRequest(data,updateData));
	}	
	return (
		<div className='AdminManageRequestsList'> 
			<div className='page-title-block'>
        <h2 className='page-title'>Manage Requests</h2>
      </div>
			<div className="requestsTable">
				{ (requestsData.total > 0) ? (
					<DopeTable
						keyField="_id"
						columns={[accountColumn,...columns, statusColumn, delColumn]}
						data={requestsData.data.map((item) => (
							{
								_id: item._id,
								productType: item.productType,
								status: item.status,
								account: item.account,
								metadata: item.metadata,								
								createdAt: moment(item.createdAt).format('MMM DD, YYYY'),
							}
						))}
						rowClasses={rowClasses}
						loading={loading}
						paginationOptions={paginationOptions}
						rowEvents={{
							onClick: (e, row, rowIndex) => {
								const {_id} = row;
								history.push(`${url}/${_id}`);
							}
						}}
					/>
				) : (
					<div className="norequestsData">
						{loading && (
							<div className='loader-container'>
								<FadeLoader size={100} color={'#403d39'} />
							</div>
						)}
						<span>No Rewuests Found</span>
					</div>
				)}
			</div>
			<DeleteRequestPopup
        isOpen={deletePopup.popupOpen}
        toggle={ () => setDeletePopup({popupOpen:false,id:''}) }
				deleteRequest={deleteRequestItem}				
      />
		</div>
	);
}
