import React from 'react';
import AddressAutocomplete from 'components/AddressAutocomplete';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { Row, Col } from 'reactstrap';
import { pathOr } from 'ramda';

export default function Address(props) {
  const state = pathOr('', ['state'], props.data);
  const city = pathOr('', ['city'], props.data);
  const zip = pathOr('', ['zip'], props.data);
  const zip4 = pathOr('', ['zip4'], props.data);
  const address1 = pathOr('', ['address1'], props.data);
  const address2 = pathOr('', ['address2'], props.data);
  const stateError = pathOr('', ['errors', [props.type], 'state'], props);
  const cityError = pathOr('', ['errors', [props.type], 'city'], props);
  const zipError = pathOr('', ['errors', [props.type], 'zip'], props);
  const address1Error = pathOr('', ['errors', [props.type], 'address1'], props);
  
  const onChange = e => {
    let address = {
      state,
      city,
      zip,
      zip4,
      address1,
      address2,
    }
    let errors = {
      state: stateError,
      city: cityError,
      zip: zipError,
      address1: address1Error,
    };

    props.onChange(props.type, {...address, [e.target.name]: e.target.value});
    props.onErrorChange(props.type, {...errors, [e.target.name]: ''})
  };
  return (
    <div className='company-info-block'>
      <h6 className="block-title">{props.title}</h6>

      <AddressAutocomplete
        id='address1'
        label='Address1*'
        placeholder='address1'
        name='address1'
        onChange={onChange}
        value={address1}
        error={address1Error}
        onPlaceLoaded={place =>
          props.onPlaceLoaded('address1', place, props.type)}
      />
      <div className='account-input-container company-name-container'>
        <label htmlFor='address2'>Address2</label>
        <DopeInput
          id='address2'
          name='address2'
          value={address2}
          placeholder="address2"
          onChange={onChange}
        />
      </div>

      <AddressAutocomplete
        id='city'
        label='City*'
        placeholder='city'
        name='city'
        onChange={onChange}
        value={city}
        error={cityError}
        onPlaceLoaded={place => props.onPlaceLoaded('city', place, props.type)}
      />

      <AddressAutocomplete
        id='state'
        label='Select State*'
        placeholder='state'
        name='state'
        onChange={onChange}
        value={state}
        onPlaceLoaded={place => props.onPlaceLoaded('state', place, props.type)}
        error={stateError}
      />       
      <Row>
        <Col md={6}> 
          <AddressAutocomplete
            id='zip'
            label='Zip Code*'
            placeholder='zip code'
            name='zip'
            onChange={onChange}
            value={zip}
            error={zipError}
            onPlaceLoaded={place => props.onPlaceLoaded('zip', place, props.type)}
          />
        </Col>
        <Col md={6}> 
          <div className='account-input-container company-name-container'>
            <label htmlFor='zip4'>Zip4</label>
            <DopeInput
              id='zip4'
              name='zip4'
              value={zip4}
              placeholder="zip4"
              onChange={onChange}
            />
          </div>
        </Col>
      </Row>
    </div>
  );
}
