  signup = (email, password, accountPhone, errorHandle) => {
    axios.post(
      `${API_URL}/signup`,
      {
        "email": email,
        "password": password,
        "accountPhone": accountPhone,
        // user_metadata: { step1_passed: 'false', email_verified: 'false' }
      }
    )
    .then(res => { 
      // console.log("res: ", res);
      if(res.status === 200){
        this.setAuthSession(res.data);
        history.replace(SIGN_UP_VERIFY_EMAIL_MESSAGE);               
      }
    }) 
    .catch(err => { 
      if (err.response) {
        // console.log("err.response: ", err.response);
        if( err.response.data.error.extra.param === "email" || 
            err.response.data.error.extra.param === "password" || 
            err.response.data.error.extra.param === "confirmPassword" ) {
          errorHandle({
            [err.response.data.error.extra.param] : err.response.data.error.dopeMessage
          }); 
        } else {
          toast.error(err.response.data.error.dopeMessage);
        }        
      } else if (err.request) {
        console.log("err.request: ", err.request.response);        
        toast.error(err.request.response.data.error.dopeMessage);
      } else {
        console.log("signup ERROR");
        toast.error("signup ERROR");
      } 
    })
  };  

