          const accountPhoneRegex = /^\+?[0-9]{9,15}$/; 
          let authdata;
          if( emailRegex.test(email) ){
            authdata = {              
              email,
              emailVerified: false,
              phoneVerified: false,
            }; 
          }else if( accountPhoneRegex.test(email) ){
            authdata = {              
              accountPhone: email,
              emailVerified: false,
              phoneVerified: false,
            }; 
          }
          // БУЛО ТАК
          // let authdata = {          
          //   email,
          //   emailVerified: false
