  getUserMetadata = () => {
    let authdata = JSON.parse(localStorage.getItem('authdata'));     
    return authdata;
  };
  
  setAuthSession = (data) => {
    // console.log("setAuthSession data: ", data);
    // data.expiresAt = data.expiresIn * 1000 + new Date().getTime();
    localStorage.setItem('authdata', JSON.stringify(data));
  };

  updateAuthSession = (data) => {    
    let authdata = JSON.parse(localStorage.getItem('authdata'));
    // console.log("updateAuthSession: ", data);
    authdata = {...authdata, ...data};           
    localStorage.setItem('authdata', JSON.stringify(authdata));
  };

  getUserEmail = () => {
    const authdata = JSON.parse(localStorage.getItem('authdata'))
    if(authdata && authdata.email) return authdata.email;
  };

  getUserAccountPhone = () => {
    const authdata = JSON.parse(localStorage.getItem('authdata'))
    if(authdata && authdata.accountPhone) return authdata.accountPhone;
  };

  getUserStatus = () => {
    const authdata = JSON.parse(localStorage.getItem('authdata'))
    if(authdata && authdata.status) return authdata.status;
  }

  logout = () => {
    localStorage.removeItem('authdata');
    localStorage.removeItem('current_account');   
    history.replace('/'); 
  };

