  sendVerificationEmailRequest = (email) => {
    axios.post(
      `${API_URL}/send-verification-email`,            
      {
        "email": email
      }
    )
    .then(res => { 
      // console.log("sendVerificationEmailRequest res: ", res);      
      if(res.status === 200){
        toast.success(res.data.message);
      }
    }) 
    .catch(err => { 
      if (err.response) { 
        console.log("sendVerificationEmailRequest err.response: ", err.response);
      } else if (err.request) { 
        console.log("sendVerificationEmailRequest err.request: ", err.response);
      } else { 
        console.log("sendVerificationEmailRequest ERROR");
      } 
    })
  }

