import React from 'react';
import Ajv from 'ajv';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import {
  createAccountRequest,  
  getAccountRequest,
  updateAccountRequest,
  setDefaultSource,
} from 'actions/account';
import * as signUpAction from 'actions/signUp';
import CompanyInfoForm from './CompanyInfoForm';
import { SIGN_UP_PACKAGE } from 'utils/constants';
import { firstName, lastName, name, phone, address1, city, state, zip } from 'utils/ajvProperties';
import Loader from 'components/Loader/Loader';
import './CompanyInformation.scss';
import { fullNumberFormat } from 'utils/phones';
import { toast } from 'react-toastify';

const ajv = new Ajv({ allErrors: true });

const AccountSchema = {
  type: 'object',  
  required: ['firstName', 'lastName', 'name', 'phone', 'address1', 'city', 'state', 'zip'],
  properties: {
    ...firstName,
    ...lastName,
    ...name,
    ...phone,
    ...address1,
    ...city,
    ...state,
    ...zip,
  },
};

class AccountDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      logo: null,
    };
  }

  componentDidMount() {
    this.props.getAccountRequest();
    this.props.getPackageListForSignUpRequest();
  }

  async componentDidUpdate(prevProps) {
    if ( this.props.signUp.scrollFlag !== prevProps.signUp.scrollFlag) {
      const errorNode = document.querySelector('.validation-message');
      if(errorNode){        
        let parentBlock = errorNode.closest('.error-scroll-parent');
        parentBlock&&parentBlock.scrollIntoView({
          block: 'start',
          behavior: 'smooth',
        });
      }
    }
  }  

  validate = () => {
    const { person, name, phone, companyAddress } = this.props.signUp;       
    const formattedForBE = {
      firstName: person.firstName,
      lastName: person.lastName,
      name: name,
      phone: fullNumberFormat(phone),
      address1: companyAddress.address1,
      city: companyAddress.city,
      state: companyAddress.state,
      zip: companyAddress.zip,      
    };

    let result = ajv.validate(AccountSchema, formattedForBE);
    const errors = {};

    if (!result) {      
      ajv.errors.map(error => {
        const errorKey = error.dataPath.split('.').pop();
        errors[errorKey] = 'This field is required!';                     
      });      
      this.props.onChangeOnboarding('errors', errors);
      this.props.onChangeOnboarding( 'scrollFlag', !this.props.signUp.scrollFlag);
    }    
    return result;
  };

  onSubmit = async () => {
    if (!this.validate()) return;
    const {
      person,
      name,
      companyAddress,
      phone,      
    } = this.props.signUp;

    try {
      const data = {
        account: this.props.currentAccount && this.props.currentAccount._id,
        body: {
          person,
          name,
          companyAddress,
          shippingAddress: { ...companyAddress },
          billingAddress: { ...companyAddress },
          phone: fullNumberFormat(phone),          
          logo: this.state.logo || null,
          accountFilled: true,
        },
        history: this.props.history        
      };
      this.props.onboardingAccountRequest(data)
    } catch (e) {
      console.log(e);
    }
  };

  onImageChange = async (e, key) => {
    const reader = new FileReader();
    let photo = e.target.files[0];
    e.target.value = '';
    if(photo && photo.size && photo.size > 8388608) {
      toast.warn('The image size should be less than 8 megabyte.')
      return
    }
    reader.onloadend = async () => {
      this.setState({ [key]: reader.result });
    };
    try {
      reader.readAsDataURL(photo);
    } catch (e) {
      console.log(e);
    }
  };

  removeImage = () => {
    this.setState({ logo: null });
  };

  onSignIn = () => {
    this.props.auth.logout();
  };

  onBack = () => {
    this.props.history.push(SIGN_UP_PACKAGE);
  };

  onChange = e => {    
    this.props.onChangeOnboarding(
      e.target.name,
      e.target.value,
      e.target.dataset.group,
    );
    this.props.onChangeOnboarding(e.target.name, '', 'errors');
  };
  render() {    
    return (
      <React.Fragment>        
        <div className='content'>
          <Loader show={this.props.signUp.loading} />
        </div>  
        { this.props.currentAccount ?               
          <CompanyInfoForm
            data={this.props.signUp}
            currentAccount={this.props.currentAccount}
            onSubmit={this.onSubmit}
            onFileUpload={this.onFileUpload}
            loading={this.state.loading}
            errors={this.props.signUp.errors}
            logo={this.state.logo}
            onImageChange={this.onImageChange}
            removeImage={this.removeImage}
            onSignIn={this.onSignIn}
            onBack={this.onBack}
            onChange={this.onChange}
            onChangeOnboarding={this.props.onChangeOnboarding}
            onDefaultCardChange={this.onDefaultCardChange}
          />
        :
          null           
        }
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => ({
  currentAccount: state.account.currentAccount,
  accountData: state.account,
  signUp: state.signUp,
});

const mapDispatchToProps = {
  ...signUpAction,
  createAccountRequest,
  getAccountRequest,
  updateAccountRequest,
  setDefaultSource,
};

export default connect(mapStateToProps, mapDispatchToProps)(
  withRouter(AccountDetails),
);
