const LogoSrc = (domainUrl) => {  
  switch(domainUrl) {
    case 'dope360.com':
      return 'logo-dope360.png';
    case 'staging.dope360.com':
      return 'logo-dope360.png';
    case 'qa.dope360.com':
      return 'logo-dope360.png';
    case 'localhost:3000':
      return 'logo-dope360.png';    
    case 'dmvendingmachine.com':
      return 'savon_logo.png';      
    default:
      return 'logo-dope360.png'
  }  
};

export default LogoSrc;
