import { all, call, put, takeEvery } from 'redux-saga/effects'

import * as signUpActions from '../actions/signUp'
import { getPackageListApi } from 'requests/account'

export function* getPackageListRequest() {
  try {
    yield put({type: signUpActions.ON_CHANGE_ONBOARDING, key: 'loading', value: true});
    const packageList = yield call(getPackageListApi);
    const packages = packageList.data.filter(p => p.type === 'mainPackage');
    yield put({type: signUpActions.GET_PACKAGE_LIST_FOR_SIGN_UP_REQUEST_SUCCESS, data: packages});
    yield put({type: signUpActions.ON_CHANGE_ONBOARDING, key: 'loading', value: false});
  } catch (e) {
    yield put({ type: signUpActions.GET_PACKAGE_LIST_FOR_SIGN_UP_REQUEST_FAILED, error: e });
    yield put({type: signUpActions.ON_CHANGE_ONBOARDING, key: 'loading', value: false});
  }
}

export default all([
  takeEvery(signUpActions.GET_PACKAGE_LIST_FOR_SIGN_UP_REQUEST, getPackageListRequest),
])