import React from 'react';
import {
  Container,
  Row,
  Col,
  Label,
} from 'reactstrap';
import CompanyLogo from '../CompanyLogo';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { pathOr } from 'ramda';
import './ContactInfo.scss';

export default function ContactInformation(props) {
  const {name, logo} = props.data;
  const {logo: newLogo = logo} = props.updatedAccount
  const firstName = pathOr('', ['person', 'firstName'], props.data);
  const lastName = pathOr('', ['person', 'lastName'], props.data);
  const createdAt = pathOr('', ['createdAt'], props.data);
  const newFirstName = pathOr(firstName, ['person', 'firstName'], props.updatedAccount);
  const newLastName = pathOr(lastName, ['person', 'lastName'], props.updatedAccount);
  const firstNameError = pathOr('', ['person', 'firstName'], props.errors);
  const lastNameError = pathOr('', ['person', 'lastName'], props.errors);

  const onChange = (e) => {
    const person = {
      firstName: newFirstName,
      lastName: newLastName,
    }
    const personErrors = {
      firstName: firstNameError,
      lastName: lastNameError,
    }

    props.onChange('person', {...person, [e.target.name]: e.target.value });
    props.onErrorChange('person', {...personErrors,  [e.target.name]:''});
  }
  return (
    <div className='contact-info-tab account-form-container'>
      <Container fluid>
        <Row>
          <Col md={5}>
          <CompanyLogo timeZone={props.timeZone} createdAt={createdAt} name={name} img={newLogo} onChange={props.onFileUpload} />
          </Col>
          <Col md={7} className='account-form'>
            <h6 className="profile-title">Contact Information</h6>
            <div className="account-input-container">
              <Label>Contact First Name*</Label>
              <DopeInput
                name="firstName"
                onChange={onChange}
                value={newFirstName}
                error={firstNameError}
              />
            </div>
            <div className="account-input-container">
              <Label>Contact Last Name*</Label>
              <DopeInput
                name="lastName"
                onChange={onChange}
                value={newLastName}
                error={lastNameError}/>
            </div>
            <div className='account-button-container'>
              <DopeButton onClick={props.onSubmit} color="primary"> Save changes</DopeButton>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}
