import React, { Component } from 'react';
import {TabPane, NavLink} from 'reactstrap';
import { withRouter } from 'react-router-dom';  
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { DopeNav } from 'components/DopeNav/DopeNav';
import { DopeNavItem } from 'components/DopeNav/DopeNavItem';
import { DopeTabContent } from 'components/DopeNav/DopeTabContent';
import classnames from 'classnames';
import { setResetPassAlertMessage } from 'actions/index';
import {pathOr} from 'ramda';
import ForgotPasswordForm from './ForgotPasswordForm';
import ForgotPasswordFormAccountPhone from './ForgotPasswordFormAccountPhone';
import { toast } from 'react-toastify';
import AuthContainer from 'components/AuthContainer';

import './ForgotPassword.scss';

const successAlertMessage = `Open the email that we sent to your inbox and verify your email address.`;
const forgotPasswordErrorText = "Email for reset password wasn't sent.";
const emailErrorText = "Error in email - email format validation failed:";
const accountPhoneErrorText = "Error in phone - phone format validation failed:";



class ForgotPassword extends Component {
  state = {
    activeTableTab: '1',
    data: {
      email: '',
      accountPhone: '',
      code: '',
    },
    errors: {
      email: '',
      accountPhone: '',
      code: '',
    },
    checkCodeVisible: false,
    loading: false,
  };

  validation = () => {
    const { email } = this.state.data;
    let errors = this.state.errors;    
    const emailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
    if( !emailRegex.test(email) ){
      errors.email = `${emailErrorText} ${email}`;      
      this.setState({ errors });      
      return false;
    }
    this.setState({
      errors: {  
        ...this.state.errors,      
        email: '',        
      },
    });
    return true;
  };
  validationAccountPhone = () => {
    const { accountPhone } = this.state.data;
    let errors = this.state.errors;
    const accountPhoneRegex = /^\+?[0-9]{9,15}$/;
    if( !accountPhoneRegex.test(accountPhone) ){
      errors.accountPhone = `${accountPhoneErrorText} ${accountPhone}`;      
      this.setState({ errors });      
      return false;
    }
    this.setState({
      errors: {  
        ...this.state.errors,      
        accountPhone: '',        
      },
    });
    return true;
  };  

  onChange = (key, value) => {
    const data = {
      data: {
        ...this.state.data,
        [key]: value,
      },
      errors: {
        ...this.state.errors,
        [key]: '',
      },
    };
    this.setState({ ...data });
  };

  submit = async e => {
    e.preventDefault();
    if (!this.validation()) return;
    this.setState({ loading: true });
    try {
      // eslint-disable-next-line
      let response = await this.props.auth.sendChangePasswordEmail(this.state.data.email.toLowerCase()); 
      // toast.success(response.data.message);     
      toast.success(successAlertMessage)
    } catch (error) {
      console.log(error);
      const errorMessage = pathOr(forgotPasswordErrorText, ['response', 'data', 'error', 'dopeMessage'], error);
      toast.error(errorMessage)
    }
    this.setState({ loading: false });
  };
  
  successSubmitAccountPhone = ()=>{
    this.setState({
      checkCodeVisible: true,
    }) 
  }
  submitAccountPhone = async e => {
    e.preventDefault();    
    if (!this.validationAccountPhone()) return;    
    this.setState({ loading: true });
    try {      
      this.props.auth.sendSMSChangePasswordAccountPhone(this.state.data.accountPhone, this.successSubmitAccountPhone, this.errorHandle);        
    } catch (error) {
      console.log(error);
      const errorMessage = pathOr(forgotPasswordErrorText, ['response', 'data', 'error', 'dopeMessage'], error);
      toast.error(errorMessage)
    }
    this.setState({ loading: false });
  };
  onSendCode = async()=>{
    let data={
      accountPhone: this.state.data.accountPhone,
      code: this.state.data.code,
    }    
    try {
      this.setState({loading: true})
        this.props.auth.sendCodeForgotPassword(data, this.errorHandle);        
      this.setState({loading: false})
    } catch (err) {
      this.setState({loading: false})
      console.log(err);
    }
  }
  toggleTableMode = (tab) => {
    if (this.state.activeTableTab !== tab) {
      this.setState({
        activeTableTab: tab
      });
    }
  };
  errorHandle = (error) => {    
    this.setState({
      errors: {
        ...this.state.errors,
        ...error
      }
    });
  }

  render() {
    const { data, errors, loading } = this.state;
    if (this.props.auth.isAuthenticated()) return <Redirect to='/' />; //?
    return (
      <AuthContainer>
        <div className="ForgotPassword">
          <DopeNav tabs>
            <DopeNavItem>
              <NavLink
                className={classnames({active: this.state.activeTableTab === '1'})}
                onClick={() => {this.toggleTableMode('1');}}
              >
                Phone
              </NavLink>
            </DopeNavItem>
            <DopeNavItem>
              <NavLink
                className={classnames({active: this.state.activeTableTab === '2'})}
                onClick={() => {this.toggleTableMode('2');}}
              >
                Email
              </NavLink>
            </DopeNavItem>
          </DopeNav>
          <DopeTabContent activeTab={this.state.activeTableTab}> 

            <TabPane tabId="1">  
              <ForgotPasswordFormAccountPhone
                onChange={this.onChange}
                data={data}
                errors={errors}
                loading={loading}
                submit={this.submitAccountPhone}
                checkCodeVisible={this.state.checkCodeVisible}
                sendCode={this.onSendCode}
              />
            </TabPane>  
                        
            <TabPane tabId="2">          
              <ForgotPasswordForm
                onChange={this.onChange}
                data={data}
                errors={errors}
                loading={loading}
                submit={this.submit}
              />
            </TabPane>
            
          </DopeTabContent>
        </div>        
      </AuthContainer>
    );
  }
}

const mapDispatchToProps = {
  setResetPassAlertMessage,
};

export default connect(null, mapDispatchToProps)(withRouter(ForgotPassword));
