import React from 'react';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';

export default function ButtonBlock(props) {

  if(props.step === 1) return (
    <DopeButton
      color="primary"
      onClick={props.onNext}
      disabled={props.disableSubmitBtn}
    >
      Next
    </DopeButton>
  )
  else if(props.step === 2) {
    return (<>
    <DopeButton
      color='white'
      onClick={props.onBack}
      disabled={props.disableSubmitBtn}
    >
      Back
    </DopeButton>
    <DopeButton 
      color='white'
      onClick={props.toggleSaveTemplateModal}>
      Save as Template
    </DopeButton>
    <DopeButton
      color="primary"
      onClick={props.onNext}
      disabled={props.disableSubmitBtn}
    >
      Next
    </DopeButton>
    </>)
  } else if(props.step === 3) {
      return (<>
        <DopeButton
          color='white'
          onClick={props.onBack}
          disabled={props.disableSubmitBtn}
        >
          Back
        </DopeButton>
        <DopeButton
          color="primary"
          onClick={props.validationSubmit}
          disabled={props.disableSubmitBtn}
        >
          Confirm Mailing
        </DopeButton>
        </>)
  }
}
