import React, {Component} from "react";
import Loader from "components/Loader/Loader";

class VerificationConfirm extends Component {

  async componentDidMount() {
    await this.props.auth.verificationConfirm(this.props.match.params.id);
  }

  render() {
    return(
      <div>
        <Loader show={true}/>
      </div>
    )
  }
}

export default VerificationConfirm;
