import React from 'react';

import './PopularOrders.scss';

export default function PopularOrders() {
  
  const data = {    
    popularOrders: [
      {                
        name: "Christmas 2019",
        type: "Postcard",
        calls: 25,        
      },
      {                
        name: "Christmas 2020",
        type: "Postcard",
        calls: 99,        
      },
      {  
        name: "Cleaning Dec 2019",
        type: "Postcard",
        calls: 12,        
      },
      {  
        name: "Christmas Light 2019",
        type: "Postcard",
        calls: 41,        
      },
      { 
        name: "Coupons 2019-2",
        type: "Postcard",
        calls: 59,        
      }
    ]
  };
  let maxCalls = 0;
  maxCalls = data.popularOrders.reduce((pr,cr) => cr.calls>pr ? cr.calls : pr,-Infinity);  
  return (
    <div className='PopularOrders'>      
      <div className="dash-card">
        <div className="dash-card-header">
        <h2 className="dash-title">Popular Orders</h2>
        </div>
        <div className="dash-card-body">
          {data.popularOrders && data.popularOrders.map((popularOrder, i) => (
            <div className="perf-item" key={popularOrder.name}>
              <div className="p-item-info">
                <span className="p-item-name">{popularOrder.name}</span>
                <span className="p-item-type">{popularOrder.type}</span>
              </div>              
              <div className="chart-container">
                <span
                  className="perf-chart"
                  style={{ width: `${ popularOrder.calls ? ((popularOrder.calls / maxCalls) * 100) : 0 }%` }}
                >
                  <span className="perf-chart-value">{ popularOrder.calls ? popularOrder.calls : 0 }&nbsp;calls</span>
                </span>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
