import React, { useEffect } from "react";
import L from "leaflet";
import "leaflet.markercluster/dist/leaflet.markercluster";
import "leaflet.markercluster/dist/MarkerCluster.css";
import "leaflet.markercluster/dist/MarkerCluster.Default.css";
import { useLeaflet } from "react-leaflet";
import { ReactComponent as LocationIcon } from 'assets/img/icons/location.svg';
import ReactDOMServer from 'react-dom/server';

const marker = ReactDOMServer.renderToString(<LocationIcon/>);
const mcg = L.markerClusterGroup();

const MarkerCluster = ({ markers }) => {
  const { map } = useLeaflet();

  useEffect(() => {
    mcg.clearLayers();
    markers && markers.forEach(({ latitude, longitude, address1, zip }) => {
      const icon = L.divIcon({
        className: `custom-icon`,
        html: `${marker.slice(0, marker.indexOf('fill='))}fill="#${zip}0" ${marker.slice(marker.indexOf('fill-rule'))}`,
        iconAnchor: [25, 43],
        iconSize: [50, 50],
        popupAnchor: [2, -40],
      });
      if(latitude && longitude){
        L.marker(new L.LatLng(latitude, longitude), {
          icon: icon
        }).addTo(mcg).bindPopup(`<div>
          <p>address: ${address1}</p>
          <p>latitude: ${latitude}</p>
          <p>longitude: ${longitude}</p>
          <p>zip: ${zip}</p>
          </div>`)
        }
      }
    );

    // optionally center the map around the markers
    // map.fitBounds(mcg.getBounds());
    // // add the marker cluster group to the map
    map.addLayer(mcg);
  }, [markers, map]);

  return null;
};

export default MarkerCluster;
