import React, {useState} from 'react';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import {DopeCheckbox} from 'components/DopeForms/DopeCheckbox/DopeCheckbox';
import SearchListInput from '../SearchListInput';
import {searchList} from 'utils/helper';
import { STATES } from 'utils/constants';
import { ReactComponent as CloseIcon } from 'assets/img/icons/close.svg';

import './StatesFilter.scss';

let getStates = () => {
  return STATES.map(el=>el.name);
}


export default function StatesFilter(props) {  
  const [searchedList, setSearchedList] = useState('');

  const states = getStates()||[]; 
  const searchedStates = searchList(states,searchedList);  

  const onChangeFilterItem = (onestate) => {     
    const filterStates = [...props.filterStates];
    const resultFilter = filterStates.indexOf(onestate) > -1 ? filterStates.filter(el => el !== onestate) : [ ...filterStates, onestate ];
    props.onChangeFilter(resultFilter)
  }

  const removeFilterItem = (onestate) => {     
    const filterStates = [...props.filterStates];
    const resultFilter = filterStates.indexOf(onestate) > -1 ? filterStates.filter(el => el !== onestate) : [ ...filterStates];
    props.onChangeFilter(resultFilter)
  }

  const selectAllFilterItem = () => {     
    const resultFilter = [...states];    
    props.onChangeFilter(resultFilter)
  }

  const removeAllFilterItem = () => {
    props.onChangeFilter([])
  }  

  const Item = (onestate) => {    
    return (
      <li className="fs-leftcol-item" key={onestate}>
        <DopeCheckbox
          label={onestate}
          checked={props.filterStates.indexOf(onestate) > -1}
          onChange={()=>{onChangeFilterItem(onestate)}}
        />
      </li>
    )
  };
  
  const Badge = (onestate) => {
    return (
      <li className="fs-rightcol-item" key={onestate}>            
        {onestate}
        <CloseIcon className="close-icon" onClick={ ()=>{removeFilterItem(onestate)} }/>
      </li>
    )
  };
  
  return (
    <div className="StatesFilter">
      <div className="filter-col">
        <div className="filter-col-header">
          <DopeButton 
            color='white'
            onClick={selectAllFilterItem}
            disabled = {states.length === props.filterStates.length}
          >
            Select All states
          </DopeButton>        
        </div>
        <div className="search-container">
          <SearchListInput placeholder="Search states" value={searchedList} onChange={setSearchedList}/>
        </div>
        <ul className="fs-leftcol-list">
          { searchedStates.length>0 ? (
            searchedStates.map((onestate)=>(                            
              Item(onestate)              
            ))
          ):(
            <span className="no-fs-leftcol-item">
              "No states"
            </span>
          )
          }      
        </ul>
      </div>
      <div className="filter-col">
        <div className="filter-col-header">
          <span className="col-header-title">Selected</span>
          <DopeButton 
            color='white'
            onClick={removeAllFilterItem}
            disabled = {props.filterStates.length === 0}
          >
            Clear All
          </DopeButton>        
        </div>        
        { props.filterStates.length>0 ? (
            <ul className="fs-rightcol-list">
              {
                props.filterStates.map((onestate)=>(                                          
                  Badge(onestate)
                ))
              }
            </ul> 
          ):(
            <ul className="fs-rightcol-list empty">
              <span className="no-fs-leftcol-item">
                "No states selected"
              </span>
            </ul>
          )
        }        
      </div>
    </div>
  );
}
