import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Switch, Route, useRouteMatch } from 'react-router-dom';
import AdminManageRequestsList from './AdminManageRequestsList';
import AdminRequest from './AdminRequest';
import {
 	resetAdminRequestsPage,
} from 'actions/adminRequests';

import './AdminManageRequests.scss';

export default function AdminManageRequests() { 
	const dispatch = useDispatch();
  const { url } = useRouteMatch();  

  useEffect(() => {        
    return () => {      
      dispatch(resetAdminRequestsPage());
    };
  }, []);  
  
  return (        
    <Switch>
      <Route exact path={`${url}`}>
        <AdminManageRequestsList/>
      </Route>
      <Route exact path={`${url}/:requestId`}>
        <AdminRequest/>
      </Route>
    </Switch>    
  );
}
