import React, { useState, useEffect } from 'react';
import { Card, CardHeader, CardBody, FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms';
import SearchInput from 'components/SearchInput';
import { DopeTable } from 'components/DopeTable/DopeTable';
import { columnsFunc } from './config';
import { pathOr } from 'ramda';
import moment from 'moment';
import { CreateListSchema } from './config';
import {
  resetSendingData,
  resetNewCampaignsPage,
  getSendingDataRequest,
  getCampaignListsDataRequest,
  getCampaignRequest,
  createNewCampaignRequest,
  setNewCampaignData,
  editCampaignRequest,
  deleteSendingRequest,
  togglePostcardModal, 
  setNewSendingData,
  selectEditedSending,
  toggleDeleteSendingModal,
  selectViewSending,
  toggleCloneSendingModal,
  getAvailableSendingDataRequest,
  cloneNewSendingRequest,
  resetCloneSendingModal,
  resetCampaignsPage,
} from 'actions/campaigns';
import { getPriceByCost } from 'utils/helper';
import { useSelector, useDispatch } from 'react-redux';
import Ajv from 'ajv';
import { toast } from 'react-toastify';
import PricingModal from './PricingModal/PricingModal';
import DeleteSendingModal from './DeleteSendingModal';
import CloneSendingModal from './CloneSendingModal';
import { useParams, useHistory } from 'react-router-dom';
import Calendar from './Calendar/Calendar';
import ButtonsBlock from './ButtonsBlock';

// import {isIE, isSafari, isEdge, isMobileSafari} from 'react-device-detect';
// import ReactDeviceDetect from 'components/ReactDeviceDetect';

import './CampaignPage.scss';

const ajv = new Ajv({ allErrors: true });

export default function CampaignPage({ postcardPrice, savedAccountId }) {
  const [errors, setErrors] = useState({ name: '', list: '' });
  const [showPricingModal, setShowPricingModal] = useState(false);
  const [deleteSendingId, setDeleteSendingId] = useState(null);
  const [startEndDate, setStartEndDate] = useState(null);
  const [clonedTemplateId, setClonedTemplateId] = useState(null);

  const currentAccount = useSelector(state => state.account.currentAccount);

  const listsDataIsLoading = useSelector(
    state => state.campaigns.listsDataIsLoading,
  );
  const sendingDataIsLoading = useSelector(
    state => state.campaigns.sendingDataIsLoading,
  );
  const sendingData = useSelector(state => state.campaigns.sendingData);  
  const listsData = useSelector(state => state.campaigns.listsData);
  const newCampaignData = useSelector(state => state.campaigns.newCampaignData);
  const showDeleteSendingModal = useSelector(
    state => state.campaigns.showDeleteSendingModal,
  );
  const showCloneSendingModal = useSelector(
    state => state.campaigns.showCloneSendingModal,
  );

  const availableSendingDataIsLoading = useSelector(
    state => state.campaigns.availableSendingDataIsLoading,
  );
  const availableSendingData = useSelector(
    state => state.campaigns.availableSendingData,
  );
  const newSendingData = useSelector(state => state.campaigns.newSendingData);
  const currentAccountId = useSelector(state => state.account.currentAccount._id);

  let { campaignId } = useParams();
  const dispatch = useDispatch();

  let history = useHistory();

  let pageViewType = null;
  if (campaignId === 'create') pageViewType = 'create';
  else pageViewType = 'edit';

  useEffect(() => {
    dispatch(resetSendingData());    
    getCampaignLists();

    if (!newCampaignData.campaign && campaignId && campaignId !== 'create') {
      getCampaign(campaignId);
    }

    return () => {
      dispatch(resetNewCampaignsPage());      
      window.scrollTo(0, 0);
    };
  }, []);

  useEffect(() => {      
    if((savedAccountId !== currentAccountId) && (savedAccountId !== '')){
      dispatch(resetCampaignsPage());
      history.push('/campaigns');      
    }    
  }, [currentAccountId]); 

  const data = listsData.data || [];
  

  const getCampaignLists = (
    pagination = { page: 1, limit: 10 },
    searchValue,
  ) => {
    let data = {
      account: currentAccount && currentAccount._id,
      ...pagination,
      page: pagination.page - 1,
    };

    const campaignsDataSort = listsData.hasOwnProperty('sort')
      ? listsData.sort
      : null;
    const paginationSort = pagination.hasOwnProperty('sort')
      ? pagination.sort
      : null;

    if (campaignsDataSort && !paginationSort) data.sort = listsData.sort;

    if (searchValue) data.name = searchValue;

    data.limit = 10;
    dispatch(getCampaignListsDataRequest(data));
  };

  const getCampaign = id => {
    const data = {
      query: {
        account: currentAccount._id,
      },
      params: {
        campaignId: id,
      },
    };
    dispatch(getCampaignRequest(data));
  };

  const createNewCampaign = () => {
    const data = {
      body: {
        name: newCampaignData.name,
        list: newCampaignData.list,
        postage: newCampaignData.postage,
      },
      query: {
        account: currentAccount._id,
      },
    };
    dispatch(createNewCampaignRequest(data));
  };

  const editCampaign = () => {    
    const data = {
      body: {
        name: newCampaignData.name,
        list: newCampaignData.list,
        postage: newCampaignData.postage,
      },
      query: {
        account: currentAccount._id,
      },
      params: {
        campaign: newCampaignData.campaign,
      },
    };
    dispatch(editCampaignRequest(data));
  };

  const togglePricingModal = () => {
    setShowPricingModal(prev => !prev);
  };

  const setList = (listId, qty) => {
    if (!qty || !listId) return;
    let verifiedContacts = listsData.data.find(item=>item._id===listId).listProperties.verifiedContacts||0;
    dispatch(
      setNewCampaignData({
        ...newCampaignData,
        list: listId,
        verifiedContacts: verifiedContacts,
      }),
    );
  };

  const calculatePrice = (qty, postageType) => {    
    if (!qty) return "0.00";

    let pricePerOne = getPriceByCost(postcardPrice, qty);

    if (postageType && postageType === 'usps_first_class')
      pricePerOne = pricePerOne['first-class-price'];
    else pricePerOne = pricePerOne.price;

    return (pricePerOne * qty / 100).toFixed(2);
  };

  let pricePerOne = getPriceByCost(
    postcardPrice,
    newCampaignData.verifiedContacts,
  );

  const onSort = (field, order) => {
    if (field === 'contacts') field = 'listProperties.verifiedContacts';
    const boolOrder = order === 'desc' ? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder };
    getCampaignLists(pagination);
  };

  const validation = () => {
    const formattedForBE = {
      ...newCampaignData,
    };
    let errors = {};

    const result = ajv.validate(CreateListSchema, formattedForBE);
    if (result) {
      return true;
    } else if (ajv.errors != null) {
      ajv.errors.map(error => {
        const errorKey = error.dataPath.split('.').pop();
        errors[errorKey] = 'This field is required';

        if (errorKey === 'list')
          toast.error('Select the contact list to schedule a sending.');
      });
      setErrors(errors);
      window.scrollTo({ top: 0, behavior: 'smooth' });
    }
  };

  const onCreate = () => {
    if (!validation()) return;
    createNewCampaign();
  };

  const onEdit = () => {
    if (!validation()) return;
    editCampaign();
  };

  const openPostcardTemplatesModal = day => {

    // if ( isIE || isSafari || isEdge || isMobileSafari) {
    //   window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
    //   // console.log("browser not supported");
    //   return false;
    // } 

    dispatch(togglePostcardModal(true));
    dispatch(setNewSendingData('sendingDate', moment(day).format('L')));
  };

  const getSendingsData = (startSendingDate, endSendingDate) => {
    const data = {
      query: {
        startSendingDate,
        endSendingDate,
        account: currentAccount._id,
        campaign: campaignId,
      },
    };

    dispatch(getSendingDataRequest(data));
  };

  const editSending = sending => {

    // if ( isIE || isSafari || isEdge || isMobileSafari) {
    //   window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
    //   // console.log("browser not supported");
    //   return false;
    // } 
    // console.log("editSending: ", sending);
    
    const data = {
      params: {
        sendingId: sending._id,
      },
      query: {
        account: currentAccount._id,
        campaignId: campaignId,
      },
      campaignId: campaignId,
    };
    dispatch(selectEditedSending(data));
  };

  const viewSending = sending => {
    const data = {
      params: {
        sendingId: sending._id,
      },
      query: {
        account: currentAccount._id,
      },
      campaignId: campaignId,
    };
    dispatch(selectViewSending(data));
  };

  const toggleDeleteSending = (sendingId, startEndDate) => {
    setDeleteSendingId(sendingId);
    setStartEndDate(startEndDate);
    dispatch(toggleDeleteSendingModal());
  };

  const deleteSending = () => {
    const data = {
      query: {
        account: currentAccount._id,
      },
      params: {
        sending: deleteSendingId,
      },
      startEndDate,
      campaignId,
    };

    dispatch(deleteSendingRequest(data));
  };

  const resetCloneSending = () => dispatch(resetCloneSendingModal());

  const toggleCloneSending = (event) => {    
    setClonedTemplateId(event._id);
    dispatch(toggleCloneSendingModal());
  };

  const calendarDate =
    newSendingData && newSendingData.sendingDate
      ? Date.parse(newSendingData.sendingDate)
      : null;

  const selectedDates = [
    {
      'dope-date-picker-disabled-day': availableSendingData.map(item =>
        Date.parse(item.sendingDate),
      ),
    },
  ];

  const getAvailableSendingsData = ({ startDate, endDate }) => {
    const data = {
      query: {
        startSendingDate: startDate,
        endSendingDate: endDate,
        account: currentAccount._id,
        campaign: campaignId,
      },
    };

    dispatch(getAvailableSendingDataRequest(data));
  };

  const onDataSelect = date => {
    dispatch(setNewSendingData('sendingDate', moment(date).format('L')));
  }; 

  const cloneSending = () => {        
    const data = {
      body: {
        sendingDate: newSendingData.sendingDate,
      },
      params: {
        sendingId: clonedTemplateId,
      },
      query: {
        account: currentAccount._id,
        campaign: campaignId,
      },
    };    
    dispatch(cloneNewSendingRequest(data));
  };

  return (
    <div className='new-campaign-container'>      
      <div className='dope-page-title'>
        <h2>{pageViewType === 'edit' ? 'Edit campaign' : 'New Campaign'}</h2>
      </div>
      {/* {campaignId !== 'create' && 
        <ReactDeviceDetect 
          // titleText = "don't support functionality that using in our Postcard Builder in Campaigns"
        />
      } */}
      <div className='new-campaign-content'>
        <Card className='dope-card campaign-name-card'>
          <CardHeader className='dope-card-header'>Campaign</CardHeader>
          <CardBody>
            <FormGroup className='dope-form-group campaign-name'>
              <DopeInput
                title='Name'
                supText='*'
                error={errors.name}
                value={newCampaignData.name}
                onChange={e => {
                  dispatch(
                    setNewCampaignData({
                      ...newCampaignData,
                      name: e.target.value,
                    }),
                  );
                  setErrors({ ...errors, name: '' });
                }}
              />
            </FormGroup>
          </CardBody>
        </Card>
        <Card className='dope-card campaign-lists-card'>
          <CardHeader className='dope-card-header'>Contact Lists</CardHeader>
          <CardBody>
            <p className='contact-list-text'>
              Select the contact list to which you want to send postcards in the
              campaign.
            </p>
            <SearchInput getData={getCampaignLists} caseSensitivityOff placeholder="Search by company name"/>
            <DopeTable
              classes='campaigns-list-requests'
              keyField='_id'
              rowClasses={({ contactsQty, ...row }, rowIndex) => {
                if (!contactsQty) return 'disabled-row';
              }}
              columns={columnsFunc(onSort, setList, newCampaignData)}
              paginationOptions={{
                onPageChange: getCampaignLists,
                page: listsData.page + 1,
                paginationSize: 10,
                totalSize: listsData.totalSize,
              }}
              data={data.map((item, i) => {
                const contactsQty = pathOr(
                  0,
                  ['listProperties', 'verifiedContacts'],
                  item,
                );                
                return {
                  ...item,
                  contacts: contactsQty,
                  createdAt: moment(item.createdAt).format('MMM DD, YYYY'),
                };
              })}
              rowEvents={{
                onClick: (e, { contactsQty, _id, ...row }, rowIndex) => {
                  setList(_id, contactsQty);
                },
              }}
              loading={listsDataIsLoading}
            />
          </CardBody>
        </Card>

        {campaignId !== 'create' && (
          <React.Fragment>
            <Card className='dope-card campaign-calendar-card'>
              <CardBody>
                <Calendar
                  getSendingsData={getSendingsData}
                  eventIsLoading={sendingDataIsLoading}
                  sendingData={sendingData}
                  openPostcardTemplatesModal={openPostcardTemplatesModal}
                  toggleDeleteSending={toggleDeleteSending}
                  toggleCloneSending={toggleCloneSending}
                  editSending={editSending}
                  viewSending={viewSending}
                  sendingDataIsLoading={sendingDataIsLoading}
                />
              </CardBody>
            </Card>
            <Card className='dope-card campaign-pricing-card'>
              <CardHeader className='dope-card-header'>Pricing</CardHeader>
              <CardBody>
                <div className='postage-type-item'>
                  <label className='dope-radio-container'>
                    <input
                      type='radio'
                      checked={newCampaignData.postage === 'usps_standard'}
                      value='usps_standard'
                      onChange={e => {
                        dispatch(
                          setNewCampaignData({
                            ...newCampaignData,
                            postage: e.target.value,
                          }),
                        );
                      }}
                    />
                    <span className='checkmark' />
                    Standard Postage
                  </label>
                  <span className='postcard-price-per-one'>{`$${(pricePerOne[
                    'price'
                  ] / 100).toFixed(2)} / postcard`}</span>
                </div>
                <div className='postage-type-item'>
                  <label className='dope-radio-container'>
                    <input
                      type='radio'
                      checked={newCampaignData.postage === 'usps_first_class'}
                      value='usps_first_class'
                      onChange={e => {
                        dispatch(
                          setNewCampaignData({
                            ...newCampaignData,
                            postage: e.target.value,
                          }),
                        );
                      }}
                    />
                    <span className='checkmark' />
                    First Class Postage
                  </label>
                  <span className='postcard-price-per-one'>{`$${(pricePerOne[
                    'first-class-price'
                  ] / 100).toFixed(2)} / postcard`}</span>
                </div>
                <div className="pricing-link-container">
                  <span className='view-pricing-link' onClick={togglePricingModal}>
                    View Pricing
                  </span>
                </div>
                <div className='postage-total-price'>
                  <span className='postage-total-label'>Total:</span>
                  <span className='postage-total-amount'>
                    ${calculatePrice(
                      newCampaignData.verifiedContacts,
                      newCampaignData.postage,
                    )}
                  </span>
                  <span className='postage-total-per-sending'>per sending</span>
                </div>
              </CardBody>
            </Card>
          </React.Fragment>
        )}
      </div>
      <ButtonsBlock
        pageViewType={pageViewType}
        onEdit={onEdit}
        onCreate={onCreate}
      />
      <PricingModal
        open={showPricingModal}
        toggle={togglePricingModal}
        data={postcardPrice}
      />
      <DeleteSendingModal
        isOpen={showDeleteSendingModal}
        toggle={toggleDeleteSending}
        deleteSending={deleteSending}
      />
      <CloneSendingModal
        isOpen={showCloneSendingModal}
        toggle={toggleCloneSending}
        cloneSending={cloneSending}
        selectedDates={selectedDates}
        availableSendingDataIsLoading={availableSendingDataIsLoading}
        getAvailableSendingsData={getAvailableSendingsData}
        calendarDate={calendarDate}
        onDataSelect={onDataSelect}
        resetCloneSending={resetCloneSending}
      />
    </div>
  );
}
