import React from 'react';
import { Card, CardBody, TabPane, Alert } from 'reactstrap';
import AccountBar from './AccountBar';
import AccountTab from './AccountTab';
import CompanyInfo from './CompanyInfo';
import ContactInfo from './ContactInfo';
import CreditCardInfo from './CreditCardInfo';
import CustomPayment from './CustomPayment/CustomPayment';
import Sms from './Sms';
// import Package from './Package/PackageView';
import PaymentHistory from './PaymentHistory';
import MoreTab from './MoreTab';
import { connect } from 'react-redux';
import { toast } from 'react-toastify'
import Loader from 'components/Loader/Loader'; 
import { DopeTabContent } from 'components/DopeNav/DopeTabContent';
import { ReactComponent as InfoIco } from 'assets/img/icons/attention.svg';
import * as accountActions from 'actions/account';
import { pathOr } from 'ramda';
import Ajv from 'ajv';
import {listSchema1, listSchema2, listSchema3} from './config';
import { API_URL } from '../../config/config';
import request from '../../utils/request';


import {fullNumberFormat} from 'utils/phones';
import './AccountDetailsPage.scss';


const ajv = new Ajv({ allErrors: true, verbose: true });

const errors = {
  companyAddress:{},
  shippingAddress: {},
  billingAddress:{},
  person: {}
}
class AccountDetails extends React.Component {  
  constructor(props) {
    super(props);        
    this.state = {
      activeTab: '1',
      subscriptionUpdating: false,
      errors: errors,      
    }
  }  

  componentDidMount() {       
    this.getData();    
  }

  componentDidUpdate(prevProps) {    
    const currentPackage = pathOr('', ['ballance', 'mainSubscription', 'package', '_id'], this.props.currentAccount);
    const prevPackage = pathOr('', ['ballance', 'mainSubscription', 'package', '_id'], prevProps.currentAccount);

    if(currentPackage && prevPackage && currentPackage !== prevPackage) this.setState({...this.state, subscriptionUpdating: true})
    if (this.props.currentAccount._id !== prevProps.currentAccount._id) {
      this.props.resetNewAccountData();
      this.getData();
    }
  }

  componentWillUnmount() {    
    this.props.resetNewAccountData();
  }

  getData = () => {    
    this.props.currentAccount._id && this.props.getAccountRequest(this.props.currentAccount._id);
    this.props.getAccountsHistoriesRequest({account: this.props.currentAccount._id, params: {page: 0, limit:10}});    
  };

  onPlaceLoaded = (key, place, group) => {
    let data = this.props.currentAccount;

    let zip = place.address_components.find(x => x.types[0] === 'postal_code');
    zip = zip ? zip.long_name : '';

    let city = place.address_components.find(x => x.types[0] === 'locality');
    city = city ? city.long_name : '';

    let state = place.address_components.find(
      x => x.types[0] === 'administrative_area_level_1',
    );
    state = state ? state.long_name : '';

    let address1_street_number = place.address_components.find(
      x => x.types[0] === 'street_number',
    );
    let address1_route = place.address_components.find(
      x => x.types[0] === 'route',
    );
    let address1 = address1_street_number
      ? address1_street_number.long_name
      : '';
    address1 = address1_route ? address1 + address1_route.long_name : '';

    let address = {
      zip,
      city,
      state,
      address1,
    };

    data[group] = address;
    this.props.setNewAccountData([group], address);
  };

  onFileUpload = (evt, group) => {
    const reader = new FileReader();
    let key = evt.target.name;
    let photo = evt.target.files[0];   
    evt.target.value = '';
    if(photo && photo.size && photo.size > 8388608) {
        toast.warn('The image size should be less than 8 megabyte.')
        return
      }
    reader.onloadend = () => {
      if (group) {
        this.props.setNewAccountData(key, reader.result, group);
      } else {
        this.props.setNewAccountData(key, reader.result);
      }
    };

    try {
      reader.readAsDataURL(photo);
    } catch (e) {
     console.error('error download logo')
    }
  };

  onErrorChange = (key, value) => {
    let errors = this.state.errors;

     errors[key]= value;
   
    this.setState({errors})
  }

  upgradePackage = (packageId) => {
    this.props.updateMainPackageRequest({
      account: this.props.currentAccount._id,
      mainPackageId: packageId
    })
  }

  onValidate = () => {
    let result;
    let { companyAddress: prevCompanyAddress, 
      shippingAddress: prevShippingAddress,
      name: prevName, 
      person: prevPerson, 
      phone: prevPhone,
      billingAddress: prevBillingAddress} = this.props.currentAccount;

    let { companyAddress = prevCompanyAddress, 
      shippingAddress = prevShippingAddress, 
      name = prevName, 
      person = prevPerson,
      phone = prevPhone,
      billingAddress = prevBillingAddress} = this.props.updatedAccount;

    const formattedForBE = {
      name: name,
      phone: fullNumberFormat(phone),
      companyAddress,
      shippingAddress,
      billingAddress,
      person,
    };
    
    if(this.state.activeTab === '1') {
      result = ajv.validate(listSchema1, formattedForBE);
    } else if(this.state.activeTab === '2') {
      result = ajv.validate(listSchema2, formattedForBE);
    } else if(this.state.activeTab === '3') {
      result = ajv.validate(listSchema3, formattedForBE);
    }
    let obj = {};

    if (result) {
      this.onSubmit();
    } else if (ajv.errors != null) {
      ajv.errors.map((error) => {
        const errorKey = error.dataPath.split('.');
        const key1 = errorKey[1];
        const key2 = errorKey[2];
        if(errorKey.length === 2)
        obj[key1] = 'This field is required'
      
        else if(errorKey.length === 3) { 
          if(!obj[key1]) obj[key1] = {};
          obj[key1][key2] = 'This field is required';
        }
        return error;
      });
      const personalAccountNode = document.getElementsByClassName('tab-content');
      personalAccountNode[0] &&
      personalAccountNode[0].scrollIntoView({ block: 'start', behavior: 'smooth' });
    }
    this.setState({errors : obj})
  }

  onSubmit = () => {
    if(!Object.keys(this.props.updatedAccount).length) return;
    
    const data = {
      body:  this.props.updatedAccount ,
      account: this.props.currentAccount._id,
    };
    
    if(this.props.updatedAccount.phone) {
      data.body.phone = fullNumberFormat(this.props.updatedAccount.phone)
    };

    this.props.updateAccountRequest(data);
  };

  onTabChange = tab => {
    if (this.state.activeTab !== tab) {
      this.props.resetNewAccountData();
      this.setState({
        activeTab: tab,
        subscriptionUpdating: false,
        errors: errors
      });
    }
  };

  onDefaultCardChange = async (el) => {    
    let body = { "default_source": el };
    let accountId = this.props.currentAccount._id;       
    try {      
      let response = await request(`${API_URL}/accounts/${accountId}/set-card-default`, {                                              
        method: 'patch',        
        body
      });   
      if ( (response.status === 200) && (response.data) && (response.data.cardId) ) {
        this.props.setDefaultSource(response.data.cardId);
      }              
    } catch (err) {
      console.log(err);      
    }
  };  
  
  onDeleteCard = async (el) => {     
    let accountId = this.props.currentAccount._id,
        deleteCard = el;        
    try {            
      let response = await request(`${API_URL}/accounts/${accountId}/remove-card/${deleteCard}`, {                                              
        method: 'delete'
      });          
      if ( (response.status === 200) && (response.data) && (response.data.cardId) ) {                
        this.props.removeCard(response.data.cardId);                 
      }                     
    } catch (err) {
      console.log(err);      
    }
  };
  
  deleteAccount = () => {
    let data = {
      accountId: this.props.currentAccount._id,      
    }
    this.props.deleteAccountRequest(data);    
  }

  
  cancelDeleteAccount = () => {
    if(this.props.currentAccount.requestToDelete) {
      const data = {   
        body: {requestToDelete: false},
        accountId: this.props.currentAccount._id,      
      }
      this.props.cancelDeleteAccountRequest(data);
    };        
  }

  render() {
    const balance = pathOr({}, ['ballance'], this.props.currentAccount);
    const activated = pathOr({}, ['createdAt'], this.props.currentAccount);
    const minutes = pathOr({}, ['ballance', 'minutes'], this.props.currentAccount);
    // const default_source = pathOr({}, ['default_source'], this.props.currentAccount);    
    const dataProps = {
      timeZone: this.props.timeZone,
      data:this.props.currentAccount,
      onFileUpload: this.onFileUpload,
      onPlaceLoaded: this.onPlaceLoaded,
      currentAccount: this.props.currentAccount._id,
      updatedAccount: this.props.updatedAccount || {},
      onSubmit: this.onValidate,
      onChange: this.props.setNewAccountData,
      errors: this.state.errors,
      onErrorChange: this.onErrorChange,
      onDefaultCardChange: this.onDefaultCardChange,
      onDeleteCard: this.onDeleteCard
    }

    return this.props.isLoading ? (
      <div className='content'>
        <Loader show={this.props.isLoading}/>
      </div>
    ) : (     
      <div className='content account-container'>
        <Loader show={this.props.isUpdating}/>
        <AccountBar 
          balance={balance}
          activated={activated}
          timeZone={this.props.timeZone}
        />
        <Alert color='warning' isOpen={minutes < 20 && minutes > 0}>
          <InfoIco /> When you have 15 minutes left, system gives 100 minutes
          automatically for $5.
        </Alert>
        <Alert color='success' isOpen={false} toggle={() => {}}>
          <InfoIco /> 100 Minutes added to account for recharging minutes!
        </Alert>
        {/* <Alert color='danger' isOpen={!minutes&&default_source} >
          <InfoIco /> Your card has been declined. Incoming calls have been
          temporarily stopped. Please update your card to keep your account
          active.
        </Alert> */}
        <Card>
          <CardBody>
            <AccountTab
              activeTab={this.state.activeTab}
              onChange={this.onTabChange}
              isAdmin={this.props.isAdmin}
            />
            <DopeTabContent activeTab={this.state.activeTab}>
              <TabPane tabId='1'>
                <CompanyInfo {...dataProps}/>
              </TabPane>
              <TabPane tabId='2'>
                <ContactInfo {...dataProps}/>
              </TabPane>
              <TabPane tabId='3'>
                <CreditCardInfo
                  {...dataProps}
                  currentAccount = {this.props.currentAccount}                  
                />
              </TabPane>              
              <TabPane tabId='4'>
                <PaymentHistory
                  currentAccount={this.props.currentAccount}
                  histories={this.props.histories}
                  historiesReq={this.props.getAccountsHistoriesRequest}
                  isHistoryLoading={this.props.isHistoryLoading}
                  timeZone={this.props.timeZone} />
              </TabPane>
              {this.props.isAdmin && (
                <TabPane tabId='5'>
                  <CustomPayment
                    errors={dataProps.errors}
                    {...dataProps}
                    currentAccount = {this.props.currentAccount}
                  />
                </TabPane>
              )}
              {this.props.isAdmin && (
                <TabPane tabId='6'>
                  <Sms
                    errors={dataProps.errors}
                    {...dataProps}
                    currentAccount = {this.props.currentAccount}
                  />
                </TabPane>
              )}
              <TabPane tabId='7'>
                <MoreTab
                  currentAccount={this.props.currentAccount}
                  onSubmitDelete={this.deleteAccount}
                  onSubmitCancelDelete={this.cancelDeleteAccount}
                />
              </TabPane>
            </DopeTabContent>
          </CardBody>
        </Card>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  currentAccount: state.account.currentAccount,
  updatedAccount: state.account.updatedAccount,
  isLoading: state.account.isLoading,
  isUpdating: state.account.isUpdating,
  isHistoryLoading: state.account.isHistoryLoading,
  packageList: state.general.packageList,
  histories:  state.account.histories,
  timeZone:  state.account.timeZone
});

const mapDispatchToProps = {
  ...accountActions,
};

export default connect(mapStateToProps, mapDispatchToProps)(AccountDetails);
