import { all, call, put, takeLatest } from 'redux-saga/effects'
import * as morpheusTemplates from '../actions/morpheusTemplates'
import { 
  getAllEddmTemplatesApi,
  getAllNeighborhoodBlitzTemplatesApi,
  } from '../requests/morpheusTemplates';

export function* getAllEddmTemplates(action) {
  try {
    yield put({ type: morpheusTemplates.SET_LOADING_ALL_TEMPLATES, data: true })
    const response = yield call(getAllEddmTemplatesApi, action.data);
    yield put({ type: morpheusTemplates.GET_ALL_EDDM_TEMPLATES_SUCCESS, data: {...response.data, ...action.data.query}})
  } catch(e) {    
    yield put({ type: morpheusTemplates.GET_ALL_EDDM_TEMPLATES_FAILED, error: e })
  }
  yield put({ type: morpheusTemplates.SET_LOADING_ALL_TEMPLATES, data: false })
}
export function* getAllNeighborhoodBlitzTemplates(action) {
  try {    
    yield put({ type: morpheusTemplates.SET_LOADING_ALL_TEMPLATES, data: true })
    const response = yield call(getAllNeighborhoodBlitzTemplatesApi, action.data);
    yield put({ type: morpheusTemplates.GET_ALL_NEIGHBORHOODBLITZ_TEMPLATES_SUCCESS, data: {...response.data, ...action.data.query}})
  } catch(e) {    
    yield put({ type: morpheusTemplates.GET_ALL_NEIGHBORHOODBLITZ_TEMPLATES_FAILED, error: e })
  }
  yield put({ type: morpheusTemplates.SET_LOADING_ALL_TEMPLATES, data: false })
}
export function* getAllAutomationTemplates(action) {
  try {
    yield put({ type: morpheusTemplates.SET_LOADING_ALL_TEMPLATES, data: true })    
    const response = yield call(getAllNeighborhoodBlitzTemplatesApi, action.data);
    yield put({ type: morpheusTemplates.GET_ALL_AUTOMATION_TEMPLATES_SUCCESS, data: {...response.data, ...action.data.query}})
  } catch(e) {    
    yield put({ type: morpheusTemplates.GET_ALL_AUTOMATION_TEMPLATES_FAILED, error: e })
  }
  yield put({ type: morpheusTemplates.SET_LOADING_ALL_TEMPLATES, data: false })
}

export default all([    
  takeLatest(morpheusTemplates.GET_ALL_EDDM_TEMPLATES_REQUEST, getAllEddmTemplates),
  takeLatest(morpheusTemplates.GET_ALL_NEIGHBORHOODBLITZ_TEMPLATES_REQUEST, getAllNeighborhoodBlitzTemplates),
  takeLatest(morpheusTemplates.GET_ALL_AUTOMATION_TEMPLATES_REQUEST, getAllAutomationTemplates),
])

