import React, { useState } from 'react';
import { FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms';
import { debounce } from 'utils/helper';
import { ReactComponent as SearchIco } from 'assets/img/icons/search.svg';
import './SearchInput.scss';

export default function SearchInput(props) {
  const [value, setValue] = useState('');  
  const debounceOnChange = React.useCallback(debounce(props.getData, 700), []);
  const onChange = e => {    
    const val = e.target.value;      
    setValue(val);
    if (val.length >= 3 || val.length === 0){      
      debounceOnChange({ page: 1, limit: 20 }, `${props.caseSensitivityOff ? val.toLowerCase() : val }`);
    }
  };
  return (
    <div className='dope-search-container'>
      <FormGroup>
        <SearchIco />
        <DopeInput
          placeholder= {props.placeholder||''}
          value={value}
          onChange={onChange}
        />
      </FormGroup>
    </div>
  );
}
