import { name } from 'utils/ajvProperties';

export const CreateListSchema = {
  type: 'object',
  required: ['forwardingNumber', 'name', 'trackingNumber'],
  properties: {
    trackingNumber: {
      type: 'string',
      pattern: '^[+]{0,1}[0-9]{10,}$',
    },
    forwardingNumber: {
      type: 'string',
      pattern: '^[+]{1}[0-9]{10,}$',
    },
    ...name,
    selectedFromDropDown: {
      type: 'boolean',
      const: true,
    },
  },
};
