import React, {useState} from 'react';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import DopeRadio from "components/DopeForms/DopeRadio";
import { useSelector, useDispatch } from 'react-redux';
import {setEddmService, setNextStep, setShippingService} from 'actions/eddm';
import {capitalFirstLetter} from 'utils/helper';
import {columnsFunc, columnsFuncOnlyBusiness, columnsFuncOnlyRes} from "./config";
import {DopeTable} from "components/DopeTable";
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import { getPriceByCost } from 'utils/helper';
import {
  setSelectedRoutes,
  setTotalSelectedRouteAddr,
  setNewDeliveryAddress,  
  updateEddmSummaryRequest,
  saveEddmAsDraftSummaryRequest,
} from "actions/eddm";
import ShippingAddress from "./ShippingAddress";
import './EddmSummary.scss';


export default function EddmSummary({calcTotalSelectedRouteAddr, totalRouteAddr, typePreview, eddmId, eddmPostcardPrice}) {
  
  const dispatch = useDispatch();
  const selectedRoutes = useSelector(state => state.eddm.selectedRoutes);
  const eddmService = useSelector(state => state.eddm.eddmService);
  // const eddmVendor = useSelector(state => state.eddm.eddmVendor);
  const featureCollection = useSelector(state => state.eddm.eddmRouteMap.featureCollection);  
  const featureCollectionSelected = featureCollection.features.filter(item => selectedRoutes.includes(item.attributes.ZIP_CRID));
  const totalSelectedRouteAddr = useSelector(state => state.eddm.totalSelectedRouteAddr); 
  const eddmSummary = useSelector(state => state.eddm.eddmSummary);  
  const currentAccount = useSelector(state => state.account.currentAccount);
  const shippingAddress = eddmSummary.deliveryAddress
    ? eddmSummary.deliveryAddress
    : currentAccount.shippingAddress;
  const addressesType = useSelector(state => state.eddm.addressesType);
  const zipFind = useSelector(state => state.eddm.eddmRouteMap.zipFind);
  const eddmSize = useSelector(state => state.eddm.eddmSize);
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const [tablePageNumber, setTablePageNumber] = useState(1);
  const tablePageLimit = 10;
  const status = useSelector(state => state.eddm.status);
 

  const customColumn = {
    dataField: "remove",
    text: "",
    sort: false,
    formatter: () => <DeleteIcon className="del-icon"/>,
    attrs: { className: "icon-cell" }
  };

  let columns;
  if(addressesType === 'residential'){
    columns = columnsFuncOnlyRes();
  }else if(addressesType === 'business'){
    columns = columnsFuncOnlyBusiness()
  }else {
    columns = columnsFunc()
  }

  const saveUploadSummary = () => {  
    const eddmRoutes = selectedRoutes.map(item => {
      return {
        addressesType: addressesType,
        route: item,
        zip: zipFind,
        deliveryAddressesCount: totalRouteAddr(item)
      }
    });
    const data = {
      body: {
        deliveryAddress: eddmSummary.deliveryAddress || currentAccount.shippingAddress,
        shipping: 'dope360', //TODO
        size: eddmSize,
        eddmRoutes:eddmRoutes,
        zip: zipFind,
        service: eddmService,
        totalDeliveryAddresses: totalSelectedRouteAddr,
        status: status,
      },
      queries: currentAccountId,
    };    
    dispatch(saveEddmAsDraftSummaryRequest(data));
    // dispatch(setNextStep(3));
  }

  const editUploadSummary = () => {
    const eddmRoutes = selectedRoutes.map(item => {
      return {
        addressesType: addressesType,
        route: item,
        zip: zipFind,
        deliveryAddressesCount: totalRouteAddr(item)
      }
    });
    const data = {
      body: {
        deliveryAddress: eddmSummary.deliveryAddress || currentAccount.shippingAddress,
        shipping: 'dope360', //TODO
        size: eddmSize,
        eddmRoutes:eddmRoutes,
        zip: zipFind,
        service: eddmService,
        totalDeliveryAddresses: totalSelectedRouteAddr,
        status: status,
      },
      queries: currentAccountId,
    };
    
    data.eddmId = eddmId;
    dispatch(updateEddmSummaryRequest(data)); 
  }

  const calcTotalPrice=(eddmSize)=>{
    let totalPrice = calculatePrice(
      totalSelectedRouteAddr,
      eddmSummary.shipingService,
      eddmSize
    );    
    // if (eddmService==='full'){
    //   totalPrice += 50;
    // }
    return totalPrice;
  }

  const onPlaceLoaded = (key, place, group) => {

    let data = currentAccount;

    let zip = place.address_components.find(x => x.types[0] === 'postal_code');
    zip = zip ? zip.long_name : '';

    let city = place.address_components.find(x => x.types[0] === 'locality');
    city = city ? city.long_name : '';

    let state = place.address_components.find(
      x => x.types[0] === 'administrative_area_level_1',
    );
    state = state ? state.long_name : '';

    let address1_street_number = place.address_components.find(
      x => x.types[0] === 'street_number',
    );
    let address1_route = place.address_components.find(
      x => x.types[0] === 'route',
    );
    let address1 = address1_street_number
      ? address1_street_number.long_name
      : '';
    address1 = address1_route ? address1 + address1_route.long_name : '';

    let address = {
      zip,
      city,
      state,
      address1,
    };

    data[group] = address;
    dispatch(setNewDeliveryAddress([group], address));
  };

  const [deliveryAddressError, setDeliveryAddressError] = useState({
    state: "", city: "", zip: "", address1: ""
  });

  const onErrorChange = (value) => {
    setDeliveryAddressError(value);
  };

  const calculatePrice = (qty, shipingService, eddmSize) => {
    if (!qty) return "0.00";
    let pricePerOneClasses = getPriceByCost(eddmPostcardPrice.filter(item => item.name === `${eddmSummary.shipingService}`)[0].price[eddmSize], qty);
    // TODO IF first-class/price
    // if (postageType && postageType === 'usps_first_class')
    //   pricePerOne = pricePerOne['first-class-price'];
    // else pricePerOne = pricePerOne.price;
    // let pricePerOne = pricePerOneClasses.price;    
    let pricePerOne = pricePerOneClasses['first-class-price'];
    return (pricePerOne * qty / 100).toFixed(2);
  };

  const paginationOptions = {
    page: tablePageNumber,
    onPageChange: (value) => setTablePageNumber(value.page),
    limit: tablePageLimit,
    totalSize: featureCollectionSelected.length
  };

  const checkDeliveryAddress = Object.values(deliveryAddressError).join('');
  const checkEmptyDeliveryAddress = shippingAddress.zip && shippingAddress.state && shippingAddress.city && shippingAddress.address1;

  return (
    <div className="EddmSummary">
      <h1 className="main-title">Order Summary</h1>
      <div className="EddmSummaryCard">
        <div className="title-container t-shadow">
          <div className="title">Selected Routes ({selectedRoutes.length})</div>
          <DopeButton
            color="white"
            onClick={()=>{dispatch(setNextStep(1));}}  
            disabled={typePreview==='view'}          
          >
            Edit
          </DopeButton>
        </div>
        <DopeTable
          keyField="_id"
          columns={[...columns, customColumn]}
          data={featureCollectionSelected.reduce((prev, item, index) => {
            if((index >= ( tablePageNumber - 1 ) * tablePageLimit) && (index < tablePageNumber * tablePageLimit)){
              return [...prev, {
                _id: item.attributes.ZIP_CRID,
                route: `${item.attributes.ZIP_CODE}-${item.attributes.CRID_ID}`,
                residential: item.attributes.RES_CNT,
                business: item.attributes.BUS_CNT,
                total: addressesType === 'residential'
                  ? item.attributes.RES_CNT
                  : addressesType === 'business'
                    ? item.attributes.BUS_CNT
                    : item.attributes.RES_CNT + item.attributes.BUS_CNT,
                // size: '???',
                income: `$${item.attributes.MED_INCOME/1000}k`,
              }]
            }
            return prev;
          }, [])}
          paginationOptions={paginationOptions}
          rowClasses='table-row'
          rowEvents={{
            onClick: (e, row) => {
              if(e.target.tagName === 'svg' || e.target.tagName === 'path'){
                if(typePreview==='view') return;
                const newSelectedRoutes = [...selectedRoutes].filter(e=>(e!==row._id));
                const totalSRAddr = calcTotalSelectedRouteAddr(newSelectedRoutes);

                dispatch(setSelectedRoutes(newSelectedRoutes));
                dispatch(setTotalSelectedRouteAddr(totalSRAddr));
              }
            }
          }}
        />
        {totalSelectedRouteAddr < 200 && (
          <div className='EddmSummary-less'>
            <h5>Click
              <span onClick={() => dispatch(setNextStep(1))}> HERE </span>
              to select a route</h5>
            <p className="info-text">Select enough routes to meet the 200 piece minimum required by the USPS</p>
          </div>
        )}
      </div>
      <div className="EddmSummaryCard">
        <div className="title-container">
          <div className="title">Service</div>
        </div>
        <div className="cardBody">
          <div className="summary-radio-row">
            <DopeRadio
              name="service"
              checked={eddmService === "full"}
              value="full"
              label="Full service"
              disabled={typePreview==='view'}
              onChange={(e) => {
                dispatch(setEddmService(e.target.value));
              }}
            />            
            <span className="tooltips-text">
              <span data-tip data-for={`tooltips-full-service`} >What is Full Service?</span>              
            </span>
          </div>
          <div className="summary-radio-row">
            <DopeRadio
              className="comingSoon"
              name="service"
              checked={eddmService === "simple"}
              value="simple"
              label="Simple service"              
              disabled={true} //TODO TEMPORARY
              onChange={(e) => {
                dispatch(setEddmService(e.target.value));
              }}
            />                       
            <span className="tooltips-text">
              <span data-tip data-for={`tooltips-simple-service`}>What is Simple Service?</span>              
            </span>
          </div>
        </div>
      </div>

      <div className="summaryGrid">
        {eddmService==='simple'&&(
          <div className="delivGridCard">
            <div className="title-container t-shadow">
              <div className="title">
                Delivery                
              </div>
            </div>
            <div className="cardBodySection">
              <div className='deliveryInfo-grid'>
                <ShippingAddress
                  data={shippingAddress}
                  errors={deliveryAddressError}
                  onErrorChange={onErrorChange}
                  type='shippingAddress'
                  onPlaceLoaded={onPlaceLoaded}
                  onChange={setNewDeliveryAddress}
                  disabled={typePreview==='view'}
                />
              </div>
              <div className="shipping-container">
                <div className="sub-title">Shiping</div>
                {eddmSummary.shipingServices.length&&eddmSummary.shipingServices.map((el)=>(
                    <div className="shipping-radio-row" key={el.name}>
                      <DopeRadio
                        name={el.name}
                        checked={el.name === eddmSummary.shipingService }
                        value={el.name}
                        label={el.name}
                        disabled={typePreview==='view'}
                        onChange={(e) => {
                          dispatch(setShippingService(e.target.value));
                        }}
                      />                       
                      <span className="shipingService-price">
                        {`$${el.price}`}
                      </span>
                    </div>
                ))}
              </div>                         
            </div>
          </div>
        )}
        { (eddmService==='full')&&(
          <div className="delivGridCard coldirection100">
            <div className="title-container t-shadow noShrink">
              <div className="title">
                Delivery                
              </div>
            </div>
            <div className="delivGridCardBody fullServ">
              <p className="fullInfo">We’ll do all delivery proces for you</p>
              <p className="fullInfo2">It’s include in Full Service</p>
            </div>
          </div>
        )}
        <div className="summaryGridCard">
          <div className="title-container t-shadow">
            <div className="title">Order Summary</div>
          </div>          
          <div className="cardBodySection">
            <div className="sectionOrderItem">
              <span className="item-text">Individual Routes Selected</span>
              <span className="item-value">{selectedRoutes.length}</span>
            </div>            
            <div className="sectionOrderItem">
              <span className="item-text">Total Delivery Addresses (Min. 200)</span>
              <span className="item-value">{totalSelectedRouteAddr}</span>
            </div>
          </div>
          <div className="cardBodySection">
            <div className="sectionOrderItem">
              <span className="item-text">Type of Service</span>
              <span className="item-value">{capitalFirstLetter(eddmService)} Service</span>
            </div>                          
          </div>
          <div className="totalSection">
            <span className="item-total-text">Total Price</span>
            <div className="item-total-grid">
              <span className="item-total-value">${calcTotalPrice('6,5x9')}</span>
              &nbsp;&ndash;&nbsp;              
              <span className="item-total-value">${calcTotalPrice('8,5x11')}</span>
            </div>
          </div> 
        </div>
      </div>

      <div className="btnContainer"> 
        {
        typePreview ? (
          typePreview==='edit' ? (
            <DopeButton
              onClick={e=>editUploadSummary(e)}
              color='primary'
              disabled={totalSelectedRouteAddr<200 || (eddmService==='simple' && (!!checkDeliveryAddress || !checkEmptyDeliveryAddress))}
            >
              Save and upload postcard
            </DopeButton>
          ):(
            <DopeButton
              onClick={()=>{dispatch(setNextStep(3));}}              
              color='primary'              
            >
              Continue
            </DopeButton>
          )
        ):(     
          <DopeButton
            onClick={e=>saveUploadSummary(e)}
            color='primary'
            disabled={totalSelectedRouteAddr<200 || (eddmService==='simple' && (!!checkDeliveryAddress || !checkEmptyDeliveryAddress))}
          >
            Save and upload postcard
          </DopeButton>
        )
        }
      </div>
    </div>
  );
}
