import React, {useState} from 'react';
import { Collapse} from 'reactstrap';
import { ReactComponent as ArrowUpIcon } from 'assets/img/icons/chevron-up.svg';
import { ReactComponent as ArrowDownIcon } from 'assets/img/icons/chevron-down.svg';
import './Toggler.scss';

export default function Toggler({label='Click here...', isModified, children}) {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);   
  return (
    <div className="Toggler">    
      <div onClick={toggle} className={'toggle-btn'}>
        {/* <ArrowIcon className={`arrow-icon ${isOpen ? "up" : "down"}`}/> */}
        {isOpen ? (
            <ArrowUpIcon className="arrow-icon"/>
          ):(
            <ArrowDownIcon className="arrow-icon"/>
          )
        }
        <span>{label}</span>
        {
          isModified ? (
            <span className="toggler-dot"></span>
          ): null
        }
      </div>
      <Collapse isOpen={isOpen}>
        {children}
      </Collapse>
    </div> 
  );
}
