import React from 'react';
import {
  DopeModal,
  DopeModalHeader,
  DopeModalBody,
  DopeModalFooter,
} from 'components/DopeModal';
import { DopeButton } from 'components/DopeForms';
import { DopeNav } from 'components/DopeNav/DopeNav';
import { DopeNavItem } from 'components/DopeNav/DopeNavItem';
import { DopeTabContent } from 'components/DopeNav/DopeTabContent';
import { NavLink, TabPane } from 'reactstrap';
import AllTemplates from './AllTemplates';
import MyRecentPostcards from './MyRecentPostcards';
import MyPostcardTemplates from './MyPostcardTemplates';
import CategoryTemplates from './CategoryTemplates';
import {
  getAllPostcardTemplatesForCampaignRequest,
  getMyTemplatesForCampaignRequest,
  getUniMyTemplatesForCampaignRequest,
  setIndexSelectedPostcardTemplateCampaign,
  getPostcardByIdCampaignRequest,
  resetPostcardTemplatesDataCampaign,
  togglePostcardModal,
  setSelectedPostcardTemplate,
  setTabPostcardModal,
  setMyPostcardByIndexCampaign,  
} from 'actions/campaigns';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { useRouteMatch } from 'react-router-dom';

import './PostcardTemplatesModal.scss';

export default function PostcardTemplatesModal(props) {  
  const account = useSelector(state => state.account);
  const category = useSelector(state => state.general.category);
  const allTemplatesIsLoading = useSelector(
    state => state.campaigns.allTemplatesIsLoading,
  );
  const myPostcardIsLoading = useSelector(
    state => state.campaigns.myPostcardIsLoading,
  );
  const indexSelectedPostcard = useSelector(
    state => state.campaigns.indexSelectedPostcard,
  );
  const templatesData = useSelector(state => state.campaigns.templatesData);  
  const newCampaignData = useSelector(state => state.campaigns.newCampaignData);
  const showPostcardModal = useSelector(
    state => state.campaigns.showPostcardModal,
  );
  const editedTemplate = useSelector(state => state.campaigns.editedTemplate);
  const postcardTab = useSelector(state => state.campaigns.postcardTab);  

  const dispatch = useDispatch();
  let history = useHistory();
  let { url } = useRouteMatch();

  const onOpened = () => {
    // if (!templatesData) getAllTemplates();
    getAllTemplates();
  };

  const getAllTemplates = (pagination = { page: 0, limit: 8 }) => {
    dispatch(getAllPostcardTemplatesForCampaignRequest(pagination));
    dispatch(setIndexSelectedPostcardTemplateCampaign(null));
  };

  const getPostcards = (
    pagination = { page: 0, limit: 8 },
    status = 'auto-saved',
  ) => {   

    dispatch(setIndexSelectedPostcardTemplateCampaign(null));

    if(status==='saved'){
      // UNIFICATE MYPOSTCARDS FOR POSTCARDS/EDDM/BLITZ
      const data = {        
        accountId: account.currentAccount._id,
        query: {
          types: 'postcard,eddm,neighborhood',
          ...pagination,          
        },        
      };      
      dispatch(getUniMyTemplatesForCampaignRequest(data));
    }else{
      const data = {
        status: status,
        account: account.currentAccount._id,
        ...pagination,
      };
      dispatch(getMyTemplatesForCampaignRequest(data));
    }
  };

  const getCategoryTemplates = (
    pagination = { page: 0, limit: 8 },
    categoryId,
  ) => {
    const data = {
      category: categoryId,
      account: account.currentAccount._id,
      types: 'postcard,eddm,neighborhood',
      ...pagination,
    };

    dispatch(setIndexSelectedPostcardTemplateCampaign(null));
    dispatch(getAllPostcardTemplatesForCampaignRequest(data));
  };

  const onSelectTemplate = (index, template) => {
    dispatch(setIndexSelectedPostcardTemplateCampaign(index));
  };

  const onClose = () => {
    dispatch(togglePostcardModal(false));
    dispatch(resetPostcardTemplatesDataCampaign());
  };

  const onCreate = () => {
    dispatch(togglePostcardModal(false));

    const id = templatesData.data[indexSelectedPostcard]._id;    
    if (postcardTab === 2 || postcardTab === 3) {
      let sendingStatus = 'new';
      if (editedTemplate){        
        sendingStatus = 'edit';
      } 
      if(postcardTab === 2){ 
        dispatch(
          getPostcardByIdCampaignRequest({ id }, newCampaignData.campaign, sendingStatus),
        );
      }
      if(postcardTab === 3){
        if(sendingStatus === 'new'){            
          dispatch( setMyPostcardByIndexCampaign({
              front: templatesData.data[indexSelectedPostcard].templates.front,
              back: templatesData.data[indexSelectedPostcard].templates.back,
              images: templatesData.data[indexSelectedPostcard].images,
            })
          );          
          history.push(`${url}/${newCampaignData.campaign}/create`);
        }
        if(sendingStatus === 'edit'){
          dispatch( setMyPostcardByIndexCampaign({
            front: templatesData.data[indexSelectedPostcard].templates.front,
            back: templatesData.data[indexSelectedPostcard].templates.back,
            images: templatesData.data[indexSelectedPostcard].images,
            })
          );       
        }        
      }
    } else {
      dispatch(
        setSelectedPostcardTemplate(templatesData.data[indexSelectedPostcard]),
      );
      if (!editedTemplate){        
        history.push(`${url}/${newCampaignData.campaign}/create`);
      }
    }
  };

  return (
    <DopeModal
      className='postcard-template-modal'
      onOpened={onOpened}
      isOpen={showPostcardModal}
      centered
    >
      <DopeModalHeader toggle={onClose}>New Sending</DopeModalHeader>
      <DopeModalBody>
        <p>Select a base postcard template for a new sending.</p>
        <div className='postcard-template-content'>
          <DopeNav tabs>
            <DopeNavItem>
              <NavLink
                className={`${postcardTab === 1 ? 'active' : ''}`}
                onClick={() => {
                  dispatch(setTabPostcardModal(1));
                  dispatch(resetPostcardTemplatesDataCampaign());
                  getAllTemplates();
                }}
              >
                All
              </NavLink>
            </DopeNavItem>
            <DopeNavItem>
              <NavLink
                className={`${postcardTab === 2 ? 'active' : ''}`}
                onClick={() => {
                  dispatch(setTabPostcardModal(2));
                  dispatch(resetPostcardTemplatesDataCampaign());
                  getPostcards({ page: 0, limit: 8 }, 'auto-saved');
                }}
              >
                My Recent Postcards
              </NavLink>
            </DopeNavItem>
            <DopeNavItem>
              <NavLink
                className={`${postcardTab === 3 ? 'active' : ''}`}
                onClick={() => {
                  dispatch(setTabPostcardModal(3));
                  getPostcards({ page: 0, limit: 8 }, 'saved');
                  dispatch(resetPostcardTemplatesDataCampaign());
                }}
              >
                My Templates
              </NavLink>
            </DopeNavItem>
            {category.map((item, i) => {
              if (item.type === 'forPostcards')
                return (
                  <DopeNavItem key={i}>
                    <NavLink
                      className={`${postcardTab === 3 + i + 1 ? 'active' : ''}`}
                      onClick={() => {
                        dispatch(setTabPostcardModal(3 + i + 1));
                        dispatch(resetPostcardTemplatesDataCampaign());
                        getCategoryTemplates({ page: 0, limit: 8 }, item._id);
                      }}
                    >
                      {item.name}
                    </NavLink>
                  </DopeNavItem>
                );
            })}
          </DopeNav>
          <DopeTabContent activeTab={postcardTab}>
            <TabPane tabId={1}>
              {postcardTab === 1 && (
                <AllTemplates
                  templatesData={templatesData}
                  indexSelectedPostcard={indexSelectedPostcard}
                  allTemplatesIsLoading={allTemplatesIsLoading}
                  onSelectTemplate={onSelectTemplate}
                  getAllTemplates={getAllTemplates}
                />
              )}
            </TabPane>
            <TabPane tabId={2}>
              {postcardTab === 2 && (
                <MyRecentPostcards
                  templatesData={templatesData}
                  myPostcardIsLoading={myPostcardIsLoading}
                  indexSelectedPostcard={indexSelectedPostcard}
                  onSelectTemplate={onSelectTemplate}
                  getPostcards={getPostcards}
                />
              )}
            </TabPane>
            <TabPane tabId={3}>
              {postcardTab === 3 && (
                <MyPostcardTemplates
                  templatesData={templatesData}
                  myPostcardIsLoading={myPostcardIsLoading}
                  onSelectTemplate={onSelectTemplate}                  
                  getPostcards={getPostcards}
                  indexSelectedPostcard={indexSelectedPostcard}
                  deleteProp={false}
                />
              )}
            </TabPane>
            {category.map((item, i) => {
              return (
                <TabPane tabId={3 + i + 1} key={i}>
                  {postcardTab === 3 + i + 1 && (
                    <CategoryTemplates
                      templatesData={templatesData}
                      categoryId={item._id}
                      allTemplatesIsLoading={allTemplatesIsLoading}
                      getCategoryTemplates={getCategoryTemplates}
                      onSelectTemplate={onSelectTemplate}
                      indexSelectedPostcard={indexSelectedPostcard}
                    />
                  )}
                </TabPane>
              );
            })}
          </DopeTabContent>
        </div>
      </DopeModalBody>
      <DopeModalFooter>
        <DopeButton color='white' onClick={onClose}>
          Cancel
        </DopeButton>
        <DopeButton
          color='primary'
          disabled={indexSelectedPostcard !== 0 && !indexSelectedPostcard}
          onClick={onCreate}
        >
          Create
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>
  );
}
