import React, { useEffect, useState } from 'react';
import { useHistory, useRouteMatch } from 'react-router-dom';
import { DopeTable } from 'components/DopeTable';
import { Button } from 'reactstrap';
import SearchInput from '../SearchInput';
import moment from 'moment';
import 'moment-timezone';
import { TabPane, NavLink , Nav, NavItem } from 'reactstrap';
import {friendlyFormatPhoneNumber} from 'utils/phones';
// import { DopeNav } from 'components/DopeNav/DopeNav';
// import { DopeNavItem } from 'components/DopeNav/DopeNavItem';
import { DopeTabContent } from 'components/DopeNav/DopeTabContent';
import classnames from 'classnames';
import { ReactComponent as SettingIco } from 'assets/img/icons/settings.svg';
import './AccountsTable.scss';

export default function AccountsTable(props) {

  let { url } = useRouteMatch();
  let history = useHistory();
  const { data = [], page = 1, limit = 20, totalSize = 0, sort, } = props.accountsData;
  const [activeTableTab, setActiveTableTab] = useState('1'); 
  const [tableSearch, setTableSearch] = useState(''); 

  useEffect(() => {  
    const data = { page: 1, limit: 20};
    if(sort) data.sort = sort;
    if (activeTableTab === '2'){       
      data.status = 'active';    
    }
    if (activeTableTab === '3'){
      // data.status = 'pending';
      data.status = 'pending,incomplete,past_due';
    } 
    if (activeTableTab === '4'){
      data.requestToDelete=true;      
    }              
    props.getData(data);
    
    // return () => {};
  }, [activeTableTab]); 
  
  const onPageChange = pagination => {  
    let data = {...pagination}
    if (activeTableTab === '2'){       
      data.status = 'active';    
    }
    if (activeTableTab === '3'){
      // data.status = 'pending';
      data.status = 'pending,incomplete,past_due';
    } 
    if (activeTableTab === '4'){
      data.requestToDelete=true;      
    }    
    if(sort) {      
      data.sort = sort;
    }
    //search by name active onPageChange
    data.name= tableSearch;    
    props.getData(data);
  };

  const onSort = (field, order) => {
    if (field === 'date') field = 'createdAt';
    const boolOrder = order === 'desc' ? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 20, sort: newOrder };
    if (activeTableTab === '2'){       
      pagination.status = 'active';    
    }
    if (activeTableTab === '3'){
      // pagination.status = 'pending';
      pagination.status = 'pending,incomplete,past_due';
    }
    if (activeTableTab === '4'){
      pagination.requestToDelete=true;      
    } 
    //search by name active onSort
    pagination.name= tableSearch;        
    props.getData(pagination);
  };

  const searchPagination = () => {
    let pagination = {page: 1, limit: 20};         
    if (activeTableTab === '2'){       
      pagination.status = 'active';    
    }
    if (activeTableTab === '3'){
      // pagination.status = 'pending';
      pagination.status = 'pending,incomplete,past_due';
    }  
    if (activeTableTab === '4'){
      pagination.requestToDelete=true;      
    }   
    if(sort) pagination.sort = sort; 
    return pagination;
  }  

  const customActions = function(cell, row, rowIndex, formatExtraData) {
    return (
      <Button
        className='setting-ico'
        onClick={() => {
          // TODO
          // props.setSelectedAccount(row.name);
          // TODO
          // props.setViewAdminAccountsPage('postcardPricing');           
          // props.selectAccount(row.name);
          history.push(`${url}/${row._id}/preferences`);
        }}
      >
        <SettingIco />
      </Button>
    );
  };

  const toggleTableMode = (tab) => {
    if (activeTableTab !== tab) {
      setActiveTableTab(tab);           
    }
  };

  const columnsFunc = () => [
    {
      dataField: 'name',
      text: 'Company Name',
      sort: true,
      sortFunc: () => {
        return false;
      },
      onSort: onSort,
    },
    {
      dataField: 'phone',
      text: 'Phone',
    },
    {
      dataField: 'email',
      text: 'Email',
    },

    {
      dataField: 'createdAt',
      text: 'Sign Up Date',
      sort: true,
      sortFunc: () => {
        return false;
      },
      onSort: onSort,
    },
    {
      dataField: 'lastSendingDate',
      text: 'Date of last sending',            
    },
    {
      dataField: 'totalMoneySpent',
      text: 'Money Spended',      
    },
    {
      dataField: 'setting',
      formatter: customActions,
    },
  ];  

  const getLastSendingDate = (item)=>{
    if(!item.lastSendingDate){
      return '';
    }
    return  moment(item.lastSendingDate).tz(props.timeZone).format('MMM DD, YYYY')
  }

  const totalMoneySpent = (item)=>{
    if(!item.totalMoneySpent){
      return '0';
    }
    return  `$ ${(item.totalMoneySpent/100).toFixed(2)}`;
  }
  return (
    <React.Fragment>
      <div className='page-title-block'>
        <h2 className='page-title'>Accounts</h2>
      </div>
      <div className='admin-accounts-container'>
        <div className='accounts-table-header'>
          <div className='accounts-count'>{`${totalSize} accounts`}</div>
          <SearchInput 
            getData={props.getData}
            caseSensitivityOff
            pagination={searchPagination()}
            activeTableTab={activeTableTab}
            setTableSearch={setTableSearch}/>
        </div>
        <Nav tabs className="acc-nav-tabs">
          <NavItem className="acc-nav-item">
            <NavLink
              className={classnames({
                active: activeTableTab === '1'
              })}
              onClick={() => {toggleTableMode('1')}}
            >
              All Accounts
            </NavLink>
          </NavItem>
          <NavItem className="acc-nav-item">
            <NavLink
              className={classnames({
                active: activeTableTab === '2'
              })}
              onClick={() => {toggleTableMode('2')}}
            >
              Active Accounts
            </NavLink>
          </NavItem>
          <NavItem className="acc-nav-item">
            <NavLink
              className={classnames({
                active: activeTableTab === '3'
              })}
              onClick={() => {toggleTableMode('3')}}
            >
              Inactive Accounts
            </NavLink>
          </NavItem> 
          <NavItem className="acc-nav-item">
            <NavLink
              className={classnames({
                active: activeTableTab === '4'
              })}
              onClick={() => {toggleTableMode('4')}}
            >
              Delete Requests
            </NavLink>
          </NavItem>              
        </Nav>
        <div className='accounts-admin-table'>          
          <DopeTabContent activeTab={activeTableTab}>
            <TabPane tabId="1" className="general-tab">
              <DopeTable
                keyField='id'
                data={
                  data.map(item => ({
                    ...item,
                    phone: friendlyFormatPhoneNumber(item.phone),
                    createdAt: moment(item.createdAt)
                      .tz(props.timeZone)
                      .format('MMM DD, YYYY'),
                    lastSendingDate: getLastSendingDate(item),
                    totalMoneySpent: totalMoneySpent(item),
                  })) || []
                }
                paginationOptions={{
                  page: page + 1,
                  limit: limit,
                  totalSize: totalSize,
                  onPageChange,
                }}
                columns={columnsFunc()}
                loading={props.accountsDataIsLoaded}                
              />
            </TabPane>                
            <TabPane tabId="2">
              <DopeTable
                keyField='id'
                data={
                  data.map(item => ({
                    ...item,
                    phone: friendlyFormatPhoneNumber(item.phone),
                    createdAt: moment(item.createdAt)
                      .tz(props.timeZone)
                      .format('MMM DD, YYYY'),
                    lastSendingDate: getLastSendingDate(item),
                    totalMoneySpent: totalMoneySpent(item),
                  })) || []
                }
                paginationOptions={{
                  page: page + 1,
                  limit: limit,
                  totalSize: totalSize,
                  onPageChange,
                }}
                columns={columnsFunc()}
                loading={props.accountsDataIsLoaded}
              />
            </TabPane>
            <TabPane tabId="3">
              <DopeTable
                keyField='id'
                data={
                  data.map(item => ({
                    ...item,
                    phone: friendlyFormatPhoneNumber(item.phone),
                    createdAt: moment(item.createdAt)
                      .tz(props.timeZone)
                      .format('MMM DD, YYYY'),
                    lastSendingDate: getLastSendingDate(item),
                    totalMoneySpent: totalMoneySpent(item),
                  })) || []
                }
                paginationOptions={{
                  page: page + 1,
                  limit: limit,
                  totalSize: totalSize,
                  onPageChange,
                }}
                columns={columnsFunc()}
                loading={props.accountsDataIsLoaded}
              />
            </TabPane>
            <TabPane tabId="4">
              <DopeTable
                keyField='id'
                data={
                  data.map(item => ({
                    ...item,
                    phone: friendlyFormatPhoneNumber(item.phone),
                    createdAt: moment(item.createdAt)
                      .tz(props.timeZone)
                      .format('MMM DD, YYYY'),
                    lastSendingDate: getLastSendingDate(item),
                    totalMoneySpent: totalMoneySpent(item),
                  })) || []
                }
                paginationOptions={{
                  page: page + 1,
                  limit: limit,
                  totalSize: totalSize,
                  onPageChange,
                }}
                columns={columnsFunc()}
                loading={props.accountsDataIsLoaded}
              />
            </TabPane>
          </DopeTabContent>          
        </div>
      </div>
    </React.Fragment>
  );
}
