import { all, call, put, takeLatest } from 'redux-saga/effects'

import * as dashboardCallsActions from '../actions/dashboard';
import * as jobsActions from '../actions/jobs';
import * as mainActions from '../actions';
import { createJobsApi, } from '../requests/jobs';
import { createIncomingСallsApi, createAllIncomingСallsApi, createDashboardStatisticApi, createAddPinApi, createRemovePinApi } from '../requests/dashboard'

export function* createIncomingСallsRequest(action) {
  yield put({ type: dashboardCallsActions.SET_DASHBOARD_PROPERTY, key: 'isPaginate', value: true, group: 'incomingCalls' })

  try {
    const response = yield call(createIncomingСallsApi, action.data);
    yield put({ type: dashboardCallsActions.CREATE_INCOMING_CALLS_REQUEST_SUCCESS, data: response.data })
    yield put({ type: dashboardCallsActions.SET_DASHBOARD_PROPERTY, key: 'isPaginate', value: false, group: 'incomingCalls' })
  } catch(e) {
    yield put({ type: dashboardCallsActions.CREATE_INCOMING_CALLS_REQUEST_FAILED, error: e })
    yield put({ type: dashboardCallsActions.SET_DASHBOARD_PROPERTY, key: 'isPaginate', value: false, group: 'incomingCalls' })
  }
}

export function* createAllIncomingСallsRequest(action) {
  yield put({ type: dashboardCallsActions.SET_DASHBOARD_PROPERTY, key: 'isLoading', value: true, group: 'statistic' })

  try {
    const response = yield call(createAllIncomingСallsApi, action.data);
    yield put({ type: dashboardCallsActions.CREATE_ALL_INCOMING_CALLS_REQUEST_SUCCESS, data: response.data })
    yield put({ type: dashboardCallsActions.SET_DASHBOARD_PROPERTY, key: 'isLoading', value: false, group: 'allIncomingCalls' })
  } catch(e) {
    yield put({ type: dashboardCallsActions.CREATE_INCOMING_CALLS_REQUEST_FAILED, error: e })
    yield put({ type: dashboardCallsActions.SET_DASHBOARD_PROPERTY, key: 'isLoading', value: false, group: 'allIncomingCalls' })
  }
}

export function* createDashboardStatisticRequest(action) {
  yield put({ type: dashboardCallsActions.SET_DASHBOARD_PROPERTY, key: 'isLoading', value: true, group: 'allIncomingCalls' })
  try {
    const response = yield call(createDashboardStatisticApi, action.data)
    yield put({ type: dashboardCallsActions.CREATE_DASHBOARD_STATISTIC_REQUEST_SUCCESS, data: response.data })
    yield put({ type: dashboardCallsActions.SET_DASHBOARD_PROPERTY, key: 'isLoading', value: false, group: 'allIncomingCalls' })
  } catch(e) {
    yield put({ type: dashboardCallsActions.CREATE_DASHBOARD_STATISTICS_REQUEST_FAILED, error: e })
    yield put({ type: dashboardCallsActions.SET_DASHBOARD_PROPERTY, key: 'isLoading', value: false, group: 'allIncomingCalls' })
  }
}

export function* createAddPinRequest(action) {
  try {
    const response = yield call(createAddPinApi, action.data)
    yield put({ type: dashboardCallsActions.ADD_PIN_REQUEST_SUCCESS, data: response.data })

    const res = yield call(createJobsApi, {account: action.data.account})
    yield put({ type: jobsActions.CREATE_JOBS_REQUEST_SUCCESS, data: res.data })
    let currentJob = res.data.data.filter(job => job.id === action.data.jobID)

    let markers =[];
    currentJob[0].pins.forEach(pin => {
      markers.push({
        _id: pin._id,
        latitude: pin.lat,
        longitude: pin.lng,
        createdAt: pin.createdAt,
        icon: 'yard',
      });
    });

    yield put({ type: mainActions.SET_MARKERS, markers: markers })
    yield put({ type: mainActions.SET_CURRENT_JOB, currentJob: currentJob[0] })
  } catch(e) {
    yield put({ type: dashboardCallsActions.ADD_PIN_REQUEST_FAILED, error: e })
  }
}

export function* createRemovePinRequest(action) {
  try {
    yield call(createRemovePinApi, action.data)
    const res = yield call(createJobsApi, {account: action.data.account})
    yield put({ type: jobsActions.CREATE_JOBS_REQUEST_SUCCESS, data: res.data })
    yield put({ type: mainActions.SET_MARKERS, markers: action.data.markers })
    if(action.data.currentJob) 
      yield put({ type: mainActions.SET_CURRENT_JOB, currentJob: action.data.currentJob })
  } catch(e) {
    yield put({ type: dashboardCallsActions.REMOVE_PIN_REQUEST_FAILED, error: e })
  }
}

export default all([
    takeLatest(dashboardCallsActions.CREATE_INCOMING_CALLS_REQUEST, createIncomingСallsRequest),
    takeLatest(dashboardCallsActions.CREATE_ALL_INCOMING_CALLS_REQUEST, createAllIncomingСallsRequest),
    takeLatest(dashboardCallsActions.CREATE_DASHBOARD_STATISTIC_REQUEST, createDashboardStatisticRequest),
    takeLatest(dashboardCallsActions.ADD_PIN_REQUEST, createAddPinRequest),
    takeLatest(dashboardCallsActions.REMOVE_PIN_REQUEST, createRemovePinRequest),
])