import React, { useState, useEffect } from 'react';
import dateFns from 'date-fns';
import { FadeLoader } from 'react-spinners';
import { useParams } from 'react-router-dom';

import AddSending from './AddSending';
import { DopeButton } from 'components/DopeForms/DopeButton';
import { ReactComponent as LeftArrowIcon } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as RightArrowIcon } from 'assets/img/icons/chevron-right.svg';

import './Calendar.scss';

export default function Calendar({
  getSendingsData,
  sendingData,
  eventIsLoading,
  openPostcardTemplatesModal,
  editSending,
  viewSending,
  toggleDeleteSending,
  toggleCloneSending,
  sendingDataIsLoading
}) {
  // console.log("Calendar: ", sendingData);
  const [currentMonth, setCurrentMonth] = useState(new Date());

  let { campaignId } = useParams();

  useEffect(
    () => {
      if (campaignId && campaignId !== 'create') getEvents();
    },
    [currentMonth],
  );

  const getStartEndDate = () => {
    const monthStart = dateFns.startOfMonth(currentMonth);
    const monthEnd = dateFns.endOfMonth(monthStart);
    const startSendingDate = dateFns.startOfWeek(monthStart);
    const endSendingDate = dateFns.endOfWeek(monthEnd);
    return {startSendingDate, endSendingDate}
  }

  const getEvents = () => {
    const {startSendingDate, endSendingDate} = getStartEndDate()

    getSendingsData(startSendingDate, endSendingDate);
  };

  const renderHeader = () => {
    const dateFormat = 'MMMM YYYY';

    return (
      <div className='calendar-header'>
        <div className='dope-card-header calendar-header-title'> Schedule</div>
        <div className='calendar-header-nav'>
          <DopeButton
            className='calendar-header-nav-btn'
            color='white'
            onClick={prevMonth}
          >
            <LeftArrowIcon />
          </DopeButton>

          <div className='calendar-header-month'>
            {dateFns.format(currentMonth, dateFormat)}
          </div>
          <DopeButton
            className='calendar-header-nav-btn'
            color='white'
            onClick={nextMonth}
          >
            <RightArrowIcon />
          </DopeButton>
        </div>
      </div>
    );
  };

  const renderDays = () => {
    const dateFormat = 'ddd';
    const days = [];

    let startDate = dateFns.startOfWeek(currentMonth);

    for (let i = 0; i < 7; i++) {
      days.push(
        <div className='col col-center' key={i}>
          {dateFns.format(dateFns.addDays(startDate, i), dateFormat)}
        </div>,
      );
    }

    return <div className='days row'>{days}</div>;
  };

  const x = day => {
    return sendingData.find((item, i) => {
      const dayCheck = dateFns.isSameDay(day, item.sendingDate);
      if (dayCheck) return item;
    });
  };

  const renderCells = () => {
    const monthStart = dateFns.startOfMonth(currentMonth);
    const monthEnd = dateFns.endOfMonth(monthStart);
    const startDate = dateFns.startOfWeek(monthStart);
    const endDate = dateFns.endOfWeek(monthEnd);

    const dateFormat = 'D';
    const rows = [];

    let days = [];
    let day = startDate;
    let formattedDate = '';

    while (day <= endDate) {
      for (let i = 0; i < 7; i++) {
        formattedDate = dateFns.format(day, dateFormat);
        const cloneDay = day;
        days.push(
          <div
            className={`col cell ${dateFns.isBefore(day, Date.now())
              ? 'disabled'
              : ''} ${dateFns.isSameDay(day, Date.now()) ? 'today' : ''}`}
            key={day}
            onClick={() => onDateClick(dateFns.parse(cloneDay))}
          >
            <span className='number'>{formattedDate}</span>
            <AddSending
              day={day}
              event={x(day)}
              openPostcardTemplatesModal={openPostcardTemplatesModal}
              toggleDeleteSending={toggleDeleteSending}
              toggleCloneSending={toggleCloneSending}
              editSending={editSending}
              viewSending={viewSending}
              getStartEndDate={getStartEndDate}
            />
          </div>,
        );
        day = dateFns.addDays(day, 1);
      }
      rows.push(
        <div className='row' key={day}>
          {days}
        </div>,
      );
      days = [];
    }

    return <div className='body'>{rows}</div>;
  };

  const onDateClick = day => {
    // setCurrentMonth(
    //   {
    //     // selectedDate: day
    //   },
    // );
  };

  const nextMonth = () => {
    setCurrentMonth(dateFns.addMonths(currentMonth, 1));
  };

  const prevMonth = () => {
    setCurrentMonth(dateFns.subMonths(currentMonth, 1));
  };

  return (
    <div
      className={`Calendar ${(campaignId && campaignId === 'create') ||
      eventIsLoading
        ? 'calendar-disabled'
        : ''}`}
    >
      {eventIsLoading && (
        <div className='loader-container'>
          <FadeLoader size={100} color={'#403d39'} />
        </div>
      )}
      {renderHeader()}
      {renderDays()}
      {renderCells()}
      {
        sendingDataIsLoading ? (
          <div className="calendar-shadow"></div>
        ):null
      }
    </div>
  );
}
