import React, {useState, useRef} from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { FormGroup, Label } from 'reactstrap';
import DopeRadio from "components/DopeForms/DopeRadio";
import DopeRadioGroup from "components/DopeForms/DopeRadioGroup";
import { AUTOMATION_PATH } from 'utils/constants';
import Skeleton from 'react-loading-skeleton';
import { useSelector, useDispatch } from 'react-redux';
import CreditCardsAfter from 'components/CreditCardsAfter';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { ReactComponent as LeftArrowIcon } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as CopyUrlIcon } from 'assets/img/icons/copyUrl.svg';
import { ReactComponent as ErrorIcon } from 'assets/img/icons/error.svg';
import { ReactComponent as SuccessIcon } from 'assets/img/icons/success.svg';
import {
  updateAutomationRequest,
  setAutomationSummary,
  setSummaryANBHCount,
} from "actions/automation";
import './AutomationSummary.scss';

export default function AutomationSummary({backStep,showAddingCard,setShowAddingCard}) {  
  const dispatch = useDispatch();
  const history = useHistory();   
  const { automationId } = useParams();
  const previewImg = useSelector(state => state.automation.previewImg);
  const currentAccountId = useSelector(state => state.account.currentAccount._id); 
  const templateEdit = useSelector(state => state.automation.automationEditor.templateEdit); 
  const webhookIdentifier = useSelector(state => state.automation.webhookIdentifier); 
  const domain = useSelector(state => state.general.domain);
  const windowWidth = useSelector(state => state.general.windowWidth);  
  const automationSummary = useSelector(state => state.automation.automationSummary);
  const defaultSource = useSelector(state => state.account.currentAccount.default_source);  
  const neighborhoodCount = useSelector(state => state.automation.automationSummary.neighborhoodCount); 
  const customCount = useSelector(state => state.automation.automationSummary.customCount); 
  const type = useSelector(state => state.automation.automationSummary.type);
  const [automationSendData, setAutomationSendData] = useState({});
  // const errors = useSelector(state => state.automation.automationSummary.errors);      
  const [urlInputCopied, setUrlInputCopied] = useState(false);  
  const [instrInputCopied, setInstrInputCopied] = useState(new Set());
  // const countsValues = [10,25,50,100,500]; 
  const countsValues = [10,25,50,100]; 
  const maxlimit = 100; 
  
  const onSummaryAutomationChange = (name, value) => {    
    dispatch(setAutomationSummary(name, value));
  };
  const onSummaryANBHCountChange = (e) =>{
    dispatch(setSummaryANBHCount(e));
  }
  const saveDraft = ()=>{    
    const data = {
      body: {
        account: currentAccountId,
        name: automationSummary.name,
        description: automationSummary.description,        
        status: 'draft',          
        templates: templateEdit,
        images: previewImg,
        type: type,
        neighborhoodCount: neighborhoodCount,
      },      
      automationId: automationId,
      queries: currentAccountId,
    };  
    if(type==='simple') data.body.neighborhoodCount=0;
    // if(type==='simple') delete data.body.neighborhoodCount;
    dispatch(updateAutomationRequest(data));
  }
  const myRef = useRef();
  const myInstrRef = useRef();
  const copyToClipboard = () => {
    myRef.current.select();
    document.execCommand("copy");
    setUrlInputCopied(true);
    setTimeout(()=>{setUrlInputCopied(false);},2000);
  };
  const copyInstrToClipboard = (number) => {    
    myInstrRef.current.querySelector(`.instrInput.cp[value="${number}"]`).select();
    document.execCommand("copy");       
    setInstrInputCopied(prev => new Set(prev.add(number)));    
    setTimeout(()=>{      
      setInstrInputCopied(prev => {
        prev.delete(number);
        return new Set(prev);
      });  
    },2000);
  }  
  const activateAutomation = () =>{
    const data = {
      body: {
        account: currentAccountId,
        name: automationSummary.name,
        description: automationSummary.description,        
        status: 'active',          
        templates: templateEdit,
        images: previewImg,
        type: type,
        neighborhoodCount: neighborhoodCount,
      },
      automationId: automationId,
      queries: currentAccountId,
    }; 
    if(type==='simple') data.body.neighborhoodCount=0;
    // if(type==='simple') delete data.body.neighborhoodCount;
    if(!defaultSource) {
      // save to internall state "data"
      setAutomationSendData(data);   
      setShowAddingCard(true);
    }else{ 
      dispatch(updateAutomationRequest(data,activateAutomationCalback));
    }
  }
  const activateAutomationCalback = ()=>{
    history.push(`${AUTOMATION_PATH}`);
  }
  const afterAddCreditCardSendAutomation = () => {
    dispatch(updateAutomationRequest(automationSendData,activateAutomationCalback)); 
  }; 

  return (    
    <div className="AutomationSummary">  
      {!showAddingCard ? ( 
        <React.Fragment>
          <div className='back-btn-container'>
            <button className='back-btn' onClick={()=>history.push(`${AUTOMATION_PATH}`)}>
              <LeftArrowIcon className="back-icon"/>
              Back to automation
            </button>
          </div>
          <h2 className="status-title">New Automation</h2>
          <div className="automationEditorCard-v2">
            <div className="section">
              <div className="title"><span className="badge-step">STEP 3</span>ADD LIST NAME AND CONNECT ZAPIER</div>
            </div>
            <div className="section">
              <div className="preview-header">
                <div className="sub-title">Your Postcard</div>
                <DopeButton
                  color="white"
                  onClick={backStep}                
                >
                  edit postcard
                </DopeButton>
              </div>          
              <div className="approval-grid">
                <div className="approval-grid-item">
                  <h4 className="item-title">
                    Front Side
                  </h4>
                  <div className="img-prev-container">
                    { previewImg.front.mini&&
                      <img src={previewImg.front.mini} alt="img-prev"/> || <Skeleton height={(windowWidth>=1200)?216:384}/>
                    }
                  </div>
                </div>
                <div className="approval-grid-item">
                  <h4 className="item-title">
                    Back Side
                  </h4>
                  <div className="img-prev-container">
                    { previewImg.back.mini&&               
                      <img src={previewImg.back.mini} alt="img-prev"/> || <Skeleton height={(windowWidth>=1200)?216:384}/>               
                    }
                  </div>
                </div>
              </div>
            </div>
            <div className="section">
              <h3 className="sub-title">
                Select type of automation              
              </h3>
              <div className="cardBody">
                <div className="summary-radio-row">
                  <DopeRadio
                    name="type"
                    checked={type === "simple" || type === ''}
                    value="simple"
                    label="Simple Automation"
                    onChange={(e) => {
                      onSummaryAutomationChange(e.target.name,e.target.value);
                    }}
                  />            
                  {/* <span className="tooltips-text">
                    <span data-tip data-for={`tooltips-simple`} >What is Simple Automation?</span>
                    <ReactTooltip          
                      className="summary-tooltip"          
                      html={true} 
                      id={`tooltips-simple`}
                    >                       
                      {
                        `<span>Lorem, ipsum dolor sit amet consectetur adipisicing elit.</span>`
                      }
                    </ReactTooltip>
                  </span> */}
                </div>
                <div className="summary-radio-row">
                  <DopeRadio                    
                    name="type"
                    checked={type === "neighborhood"}
                    value="neighborhood"
                    label="Neighborhood Automation"
                    onChange={(e) => {
                      onSummaryAutomationChange(e.target.name,e.target.value);
                    }}
                  />                       
                  {/* <span className="tooltips-text">
                    <span data-tip data-for={`tooltips-neighborhood`}>What is Neighborhood Automation?</span>
                    <ReactTooltip          
                      className="summary-tooltip"          
                      html={true}                       
                      id={`tooltips-neighborhood`}
                    >  
                      {
                        `<span>Lorem ipsum dolor sit amet consectetur adipisicing elit.</span>`
                      }
                    </ReactTooltip>
                  </span> */}
                </div>
              </div>
            </div>
            { type === "neighborhood" ? (
              <div className="section">
                <h3 className="sub-title">
                  Choose an amount of Neighbors 
                  {neighborhoodCount?<SuccessIcon className="att-icon success"/>:<ErrorIcon className="att-icon error"/>}            
                </h3>
                <div className="cardBody">
                  <div className="summary-radio-row">
                    <DopeRadioGroup
                      data={countsValues.map(el=>{
                        return {"value":el,"label":el}
                      })}
                      name="neighborhoodCount"
                      selectedValue={!customCount?neighborhoodCount:null}
                      onChange={(value)=>onSummaryANBHCountChange({'neighborhoodCount':+value,'customCount':false})}
                    />
                    <div className="custom-wrap">
                      <Label className='custom-label'>                        
                        Custom
                      </Label>
                      <DopeInput
                        type="number"
                        min={0}
                        max={100}
                        value={(customCount&&neighborhoodCount)?neighborhoodCount:''}                        
                        onChange={(e)=>{
                          let val = +e.target.value;
                          if(val>maxlimit) val = maxlimit;
                          if(val<0) val = 0;
                          onSummaryANBHCountChange({'neighborhoodCount':Math.trunc(Math.abs(val)),'customCount':true})
                        }}
                      />
                    </div>
                  </div>
                </div>
              </div>
            ):null
            }
            <div className="section">
              <h3 className="sub-title">
                Add List Name
                {automationSummary.name.trim().length?<SuccessIcon className="att-icon success"/>:<ErrorIcon className="att-icon error"/>}              
              </h3>
              <div className="list-info">
                <FormGroup className='dope-form-group'>
                  <DopeInput
                    title='Name'
                    name='name'
                    supText='*'
                    placeholder='List name'
                    // error={this.props.newTrackingNumber.errors.name}
                    value={automationSummary.name}
                    onChange={e=>onSummaryAutomationChange(e.target.name, e.target.value)}
                  />
                </FormGroup>
                <FormGroup className='dope-form-group'>
                  <DopeInput
                    title='Description' 
                    name='description'
                    maxLength='auto'
                    placeholder='List description'
                    // error={this.props.newTrackingNumber.errors.name}
                    value={automationSummary.description}
                    onChange={e=>onSummaryAutomationChange(e.target.name, e.target.value)}
                  />
                </FormGroup>
              </div>
            </div>
            <div className="section">
              <h3 className="sub-title">How to Connect List to Zapier (example)</h3>
              <h4 className="instr-title">1. Use Webhook link</h4>
              <div className="urlInput-container">              
                <input ref={myRef} className="urlInput" value={`https://${domain}/api/webhooks/list/${webhookIdentifier}`} onClick={copyToClipboard} readOnly/>
                <CopyUrlIcon className="CopyUrlIcon" onClick={copyToClipboard}/>
                {urlInputCopied ? <span className="urlcopy-text">Copied.</span> : null}
              </div>
              <h4 className="instr-title">2. Fill all data fields like in example below</h4>
              <div className="instr-ext-grid" ref={myInstrRef}>
                  <div className="instr-part-grid">
                    {
                      [
                        {a:'firstName',b:'First Name'},
                        {a:'lastName',b:'Last Name'},
                        {a:'address1',b:'Address1'},
                        {a:'address2',b:'Address2'},
                        {a:'city',b:'City'},
                      ].map(el=>(
                        <React.Fragment key={el.a}>                        
                          <div className="inp-wrap">
                            {
                              instrInputCopied.has(el.a)?<span className="copy-text">Copied.</span>:null
                            }
                            <input className="instrInput cp" value={el.a} onClick={()=>copyInstrToClipboard(el.a)} readOnly/>
                            <CopyUrlIcon className="instrIcon"/>
                          </div>
                          <span>=</span>
                          <input className="instrInput" value={el.b} readOnly/>
                        </React.Fragment>                      
                      ))
                    }                  
                  </div>
                  <div className="instr-part-grid">
                    {
                      [
                        {a:'state',b:'State'},
                        {a:'zip',b:'ZIP'},
                        {a:'zip4',b:'ZIP4'},
                        {a:'phone',b:'Phone number'},
                        {a:'country',b:'Country'},
                      ].map(el=>(
                        <React.Fragment key={el.a}>                        
                          <div className="inp-wrap">
                            {
                              instrInputCopied.has(el.a)?<span className="copy-text">Copied.</span>:null
                            }
                            <input className="instrInput cp" value={el.a} onClick={()=>copyInstrToClipboard(el.a)} readOnly/>
                            <CopyUrlIcon className="instrIcon"/>
                          </div>
                          <span>=</span>
                          <input className="instrInput" value={el.b} readOnly/>
                        </React.Fragment>                      
                      ))
                    }                  
                  </div>
              </div>
              
            </div>
          </div>
          <div className="bnts-container">            
            <DopeButton
              color="white"
              onClick={saveDraft}                
            >
              SAVE AS DRAFT
            </DopeButton>
            <DopeButton
              color="primary"
              onClick={activateAutomation} 
              disabled={
                !automationSummary.name.trim().length ||
                (type==="neighborhood")&&!neighborhoodCount
              }               
            >
              Activate
            </DopeButton>
          </div> 
        </React.Fragment>
      ):(
        <CreditCardsAfter
          onAfterAddCreditCard={afterAddCreditCardSendAutomation}
          onBack={()=>setShowAddingCard(false)}
        /> 
      )}       
    </div>    
  );
}
