import React from 'react';
import { FadeLoader } from 'react-spinners';
import Pagination from 'rc-pagination';
import localeInfo from 'rc-pagination/lib/locale/en_US';
import { ReactComponent as PrevIco } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as NextIco } from 'assets/img/icons/chevron-right.svg';
import { pathOr } from 'ramda';

export default function AllTemplates(props) {
  const postcards = pathOr(
    '',
    ['createPostCard', 'allPostcardsTemplate', 'data'],
    props,
  );  

  const onPageChange = page => {
    props.getAllTemplates({ page: page - 1, limit: 8 });
  };
  
  return (
    <>
    <div
      className={`${props.newTemplateIsLoaded
        ? 'disabled postcards-preview-container'
        : 'postcards-preview-container'}`}
    >
      {!props.createPostCard.selectFromTemplates ? (
        <React.Fragment>
          {postcards && postcards.length ? (
            postcards.map((template, key) => (
              <div className='postcards-preview-item' key={template._id}>
                <div
                  className='template-item'
                  onClick={() => {
                    props.resetPostCardTemplateBackKey();
                    props.onSelectTemplate(template);
                    props.addErrors('template', '');
                  }}
                >
                  <div className='postcards-preview-content'>
                    <img
                      src={template.front_thumbnail}
                      alt='postcard template'
                    />
                    {props.createPostCard.data.template._id === template._id ? (
                      <div className='template-checked' />
                    ) : null}
                  </div>
                </div>
              </div>
            ))
          ) : null
          }
          {props.newTemplateIsLoaded && (
            <div className='loader-main'>
              <FadeLoader size={100} color={'#403d39'} />
            </div>
          )}
        </React.Fragment>
      ) : null
      }
    </div>
    {postcards && postcards.length ? (
        <Pagination
          className='dope-pagination'
          current={props.createPostCard.allPostcardsTemplate.page + 1}
          defaultPageSize={props.createPostCard.allPostcardsTemplate.limit}
          total={props.createPostCard.allPostcardsTemplate.total}
          hideOnSinglePage={true}
          onChange={onPageChange}
          locale={localeInfo}
          prevIcon={<PrevIco />}
          nextIcon={<NextIco />}
          jumpPrevIcon={<span>...</span>}
          jumpNextIcon={<span>...</span>}
          showTitle={false}
        />
      ) : null}
    </>
  );
}
