import React from 'react';
import { FormGroup, Alert, Label } from 'reactstrap';
import { Link } from 'react-router-dom';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeInputGroup } from 'components/DopeForms/DopeInputGroup/DopeInputGroup';
import { InputGroupAddon } from 'reactstrap';
import { ReactComponent as Ico } from 'assets/img/visibility.svg';
import AuthContainer from 'components/AuthContainer';
import { SIGN_UP, FORGOT_PASSWORD } from 'utils/constants';

export default function LoginForm(props) {
  return (
    <AuthContainer>
      <div className='login-form'>
        
        {props.resetPassAlertMessage && (
          <div>
            <Alert color='success'>{props.resetPassAlertMessage}</Alert>
          </div>
        )}

        <div className='auth-form-title'>Sign In</div>

        <div>          
          <FormGroup className="dope-form-group">                      
            <DopeInput
              title="Email / Phone"
              id='email'
              type='text'
              onChange={e => props.onChange('email', e.target.value.trim())}
              value={props.data.email}
              error={props.errors.email}
            />
          </FormGroup>          
          <FormGroup className='dope-form-group'>
            <Label className='input-title' htmlFor='password'>
              Password
            </Label>
            <DopeInputGroup>
              <DopeInput                
                id='password'
                type={props.passwordVisibility ? 'text' : 'password'}
                onChange={e => props.onChange('password', e.target.value)}
                value={props.data.password}
                error={props.errors.password}
              />
              <InputGroupAddon addonType='append' onClick={props.togglePasswordVisibility}>
                <Ico
                  className={
                    props.passwordVisibility ? (
                      'input-icon full-width active'
                    ) : (
                      'input-icon full-width'
                    )
                  }
                  alt='visibility'
                />
              </InputGroupAddon>
            </DopeInputGroup>
          </FormGroup>
        </div>

        <div className='login-form-bottom'>
          <div className='link-block'>
            <Link to={SIGN_UP}>Register</Link>
            <Link to={FORGOT_PASSWORD}>Forgot Password</Link>
          </div>
          <DopeButton
            color='primary'
            onClick={() => props.login()}
            disabled={props.loading}
          >
            Sign In
          </DopeButton>
        </div>

      </div>
    </AuthContainer>
  );
}
