import React from 'react';
import ReactDOM from 'react-dom';
import { ToastContainer, toast } from 'react-toastify';
import { ReactComponent as CloseIco } from 'assets/img/icons/close.svg';
import './ToastifyContainer.scss';

const CloseButton = ({ AnyProps, closeToast }) => (
  <div className='close-btn-container' onClick={closeToast}>
    <CloseIco />
  </div>
);

export default function ToastifyContainer(props) {
  return ReactDOM.createPortal(
    <ToastContainer
      closeButton={<CloseButton />}
      className='dope-toast-container'
      hideProgressBar={true}
      position={toast.POSITION.BOTTOM_CENTER}
      autoClose={10000}
      {...props}
    />,
    document.body
  )
}
