import {
    CREATE_JOBS_REQUEST_SUCCESS,
    JOBS_CHANGE_PAGINATION,
    RESET_JOBS,
    CHANGE_STATUS_JOB,
} from '../actions/jobs';

let initialState = {
    data: [],
    page: null,
    pages: null,
    totalSize: null,
    isLoading: false,
};

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case CREATE_JOBS_REQUEST_SUCCESS:
            return {
                ...state,
                ...action.data
            };
        case JOBS_CHANGE_PAGINATION:
            return {
                ...state,
                [action.key]: action.value
            };
        case CHANGE_STATUS_JOB:
            return {
                ...state,
                data: action.payload
            };
        case RESET_JOBS:
            return {
                ...state,
                ...initialState
            };
        default:
            return state;
    }
}
