  getFilters = ()=>{
    const filters={};
    if (this.props.filters.lists.length>0)
      filters.lists = this.props.filters.lists.map(el=>el._id).join(",");
    if (this.props.filters.states.length>0){
      let arrStatesAbr = [];
      this.props.filters.states.forEach(statesName => {
        let item = STATES.find(el => el.name === statesName);
        arrStatesAbr.push(item.name.toLowerCase());
        arrStatesAbr.push(item.abbreviation.toLowerCase());
      });      
      filters.states = arrStatesAbr.join(",");
    }
    if (this.props.filters.cities.length>0)
      filters.cities = this.props.filters.cities.join(",");
    if (this.props.filters.zipCodes.length>0)
      filters.zipCodes = this.props.filters.zipCodes.join(",");
    if (this.props.filters.startDate)
      filters.startDate = this.props.filters.startDate;
    if (this.props.filters.endData)
      filters.endData = this.props.filters.endData;
    switch(this.props.filters.validation){
      case 'allContacts': break;
      case 'validatedOnly': filters.validation = true;
      break;
      case 'invalidatedOnly': filters.validation = false;
      break;     
      default: break;
    }    
    return filters
  }  

  updateData = (pagination = { page: this.state.pagination.page, limit: 10 }) => {
    let data = {
      account: this.props.currentAccount._id, //'5c1c00f48a113040bf07c5be',
      ...this.getFilters(),
      ...pagination,
      page: pagination.page - 1,      
    };
    if (this.state.searchName.length > 0){
      data.name = this.state.searchName.toLowerCase();
    }    
    // if(this.state.showInvalid) data.verified = false;

    
    if(!pagination.sort) {      
      data.sort = this.state.pagination.sort
    };
    
    this.props.getContacts(data); //for get/save contacts.data into redux
    
    this.setState({
      scroll: window.pageYOffset
    });
  };

