  onDeleteSubmit = (listId) => {    
    if (this.state.selectedContacts.length > 1) {
      let data = {
        ids: this.state.selectedContacts,
        account: this.props.currentAccount._id,
      }
      if(listId){
        data.list = listId;
      }      
      this.props.deleteContacts(data);
    } else {
      let data = {
        id: this.state.selectedContacts[0],
        account: this.props.currentAccount._id,
      }
      if(listId){
        data.list = listId;
      } 
      this.props.deleteOneContact(data);
    }
    this.clearContactsSelection();
    this.toggleDeleteModal();
  };

