  onCreateListToggle = () => {    
    this.setState({
      createListModal: !this.state.createListModal
    });
  };

  onCreateListChange = (e, name) => {
    const { value } = e.target;
    this.setState({
      ...this.state,
      createListModalErrors: {},
      createListModalData: {
        ...this.state.createListModalData,
        [name]: value
      }
    });
  };

  onCreateListSubmit = () => {
    const data = {
      ...this.state.createListModalData,
      contacts: this.state.selectedContacts,
      account: this.props.currentAccount._id
    };
    if (this.validateCreateList()) {
      this.props.createList(data);
      this.setState({
        ...this.state,
        createListModal: false,
        createListModalData: {
          name: '',
          description: ''
        }
      });
    }
  };

  validateCreateList = () => {
    const result = ajv.validate(
      CreateListSchema,
      this.state.createListModalData
    );

    if (!result) {
      const createListModalErrors = {};
      ajv.errors.map((error) => {
        const errorKey = error.dataPath.split('.').pop();
        createListModalErrors[errorKey] = 'This field is required';
      });
      this.setState({
        ...this.state,
        createListModalErrors
      });
    }

    return result;
  };

  onSort = (field, order)=>{
    if(field === 'name') field = 'firstName';   

    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: this.state.pagination.page, limit: 10, sort: newOrder};
    this.onPageChange( pagination );
  }

  onPageChange = (pagination) => {    
    if(!pagination.sort && this.state.pagination.sort) pagination.sort = this.state.pagination.sort
    this.setState({ pagination });
    this.updateData(pagination);
  };

