  fixStateName = (string) => {
    let newStr = string.trim();
    if (newStr.length === 2)
      return newStr.toUpperCase();
    if (newStr.length > 0){
      let correctString = newStr.toLowerCase().split(/\s+/).map(word => word[0].toUpperCase() + word.substring(1)).join(' ');    
      return correctString;
    }
    return newStr;
  };

