import React from 'react';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import Select from 'react-select';
import './MapButtons.scss';
import { Row, Col } from 'reactstrap';
import { pathOr } from 'ramda';

export default function MapButtons(props) {
  const total = pathOr(0, ['amount'], props.currentJob);
  const placed = pathOr(0, ['pins', 'length'], props.currentJob);
  const balance = total - placed;

  let yardSigns = props.jobs.filter(job => {
    return job.type === 'Yard Sign';
  });

  yardSigns = yardSigns.map(yard => {
    return { value: yard.id, label: yard.name, ...yard };
  });

  const selectCurrentJob = (e, b) => {
    props.setCurrentJob(e);
  };

  const addPins = () => {
    props.setCurrentJob(yardSigns[0]);
    props.showAddPinBtn();
    props.addPins();
  };

  const onPlacePinDone = () => {
    props.showAddPinBtn();
    props.setCurrentJob();
    props.addPins('hide');
  };

  return (
    <div className='map-btn-container'>
      {props.addPinMode ? (
        <div className='map-add-pin-block'>
          <Row className='top-block'>
            <Col md={4}>
              <Select
                className="dope-select"
                classNamePrefix="dope"
                options={yardSigns}
                onChange={selectCurrentJob}
                value={yardSigns.filter(yard => {
                  return props.currentJob && yard.id === props.currentJob.id;
                })}
              />
            </Col>
            <Col md={8} className='pins-quantity-block'>
              <div className='pins-quantity-item'>
                <span>Total:</span>
                <span>{total}</span>
              </div>
              <div className='pins-quantity-item'>
                <span>Placed:</span>
                <span>{placed}</span>
              </div>
              <div className='pins-quantity-item'>
                <span>Balance:</span>
                <span>{balance}</span>
              </div>
            </Col>
          </Row>
          <div className='map-add-pin-btn'>
            <DopeButton color='white' onClick={onPlacePinDone}>
              Done
            </DopeButton>
            <DopeButton color='primary' onClick={props.onPlace}>
              Place
            </DopeButton>
          </div>
        </div>
      ) : (
        <div className='start-placing-btn'>
          <DopeButton color='primary' onClick={addPins} disabled={props.noYardSigns}>
            Start placing
          </DopeButton>
        </div>
      )}
    </div>
  );
}
