import React from 'react'
import { SketchPicker } from 'react-color'

class ColorPicker extends React.Component {
    state = {
        visible: false,
        color: ''
    };
    
    handleClick = () => {
        this.setState({ visible: !this.state.visible })
    };

    handleClose = () => {
        this.setState({ visible: false })
    };

    handleChange = (color) => {
        const { onChange, name } = this.props;
        onChange && onChange({
            target:{
                name,
                value: color.hex,
            }
        });
    };

    getColor = (color) => ({
        width: '36px',
        height: '36px',
        borderRadius: '5px',
        background: color,
    });

    onClose = () => {
        const { onClose, name, value } = this.props;
        onClose && onClose({
            target:{
                name,
                value
            }
        })
    };

    render() {
        const { visible } = this.state;

        const { value } = this.props;
        return (
            <div className="colorPicker">
                <div className="colorPicker__swatch" onClick={this.handleClick}>
                    <div style={this.getColor(value)}/>
                </div>
                <div className="colorPicker__close" onClick={this.onClose}>
                    <i className="fa fa-times-circle"/>
                </div>
                {visible ? (
                    <div className="colorPicker__popover">
                        <div className="colorPicker__cover" onClick={this.handleClose}/>
                        <SketchPicker color={value} onChange={this.handleChange}/>
                    </div>) : null
                }
            </div>
        )
    }
}

export default ColorPicker