import {
  DOWNLOAD_LIST_REQUEST_SUCCESS,
  GET_LISTS_REQUEST_SUCCESS,
  GET_LISTS_REQUEST_FAILED,
  GET_LISTS_REQUEST,
  UPLOAD_LIST_REQUEST_FAILED,
  CLEAR_ERRORS,
  TOGGLE_UPLOAD,
  GET_LISTS_UPLOAD_CSV_REQUEST_SUCCESS,
  ON_CHANGE_UPLOAD_CSV_DATA,
  RESET_LISTS_PAGE,
  UPLOAD_LIST_REQUEST_SUCCESS,
  TOGGLE_MATCHING_PAGE,
  ADD_UPLOAD_CSV_ERRORS,  
  SET_EDIT_LIST_DATA,
  ADD_ERRORS_EDIT_LIST,
  TOGGLE_EDIT_LIST_MODAL,
  ON_CHANGE_EDIT_LIST,
  EDIT_LIST_REQUEST_SUCCESS,
  RESET_EDIT_LIST_FORM,
  GET_ZIP_BORDERS_REQUEST,
  GET_ZIP_BORDERS_REQUEST_SUCCESS,
  GET_ZIP_BORDERS_REQUEST_FAILED,
  RESET_LIST_DETAIL,
  GET_UNIQUE_ZIPS_REQUEST,
  GET_UNIQUE_ZIPS_REQUEST_SUCCESS,
  GET_UNIQUE_ZIPS_REQUEST_FAILED,
} from 'actions/listRequests'

const initialState = {
  allLists:null,
  isLoading: false,
  data: {},
  listToDownload: null,
  errors: [],
  uploadModal: false,
  showMatchingPage: false,
  uploadCsvData: {
    listId: '',
    name: '',
    isFirstRowHeader: true,
  },
  uploadCsvErrors: {},
  showEditListModal: false,
  editListError: {},
  editListData: {},
  zipBorders: null,
  listContacts: {},
  mapPosition: { lat: 39.50, lng: -98.35 },
  zoom: 4,
  markers: []
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case TOGGLE_UPLOAD:
      return {
        ...state,
        uploadModal: !state.uploadModal
      }
    case GET_LISTS_REQUEST:
      return {
        ...state,
        isLoading: true
      }
    case GET_LISTS_REQUEST_SUCCESS:
      let data = action.data && action.data.data
      if (data && data.length) {
          for (let elm of data) {
              elm.date = new Date(elm.requestDate).toLocaleDateString()
              if (elm.csv) elm.csv = 'data:text/csvbase64,' + elm.csv
              if (elm.location && elm.location.city && elm.location.state) {
                  let location = ''
                  if (elm.location.city) {
                      location = elm.location.city
                      if (elm.location.state) location += ', ' + elm.location.state
                  }
                  elm.location = location
              } else delete elm.location
          }
      }
      return {
        ...state,
        data: action.data,
        isLoading: false
      }
    case GET_LISTS_REQUEST_FAILED:
      return {
        ...state,
        isLoading: false
      }
    case DOWNLOAD_LIST_REQUEST_SUCCESS:
      return {
        ...state,
        listToDownload: action.data
      }
    case UPLOAD_LIST_REQUEST_FAILED:
      return {
        ...state,
        errors: [action.error]
      }
    case CLEAR_ERRORS:
      return {
        ...state,
        errors: []
      }
    case GET_LISTS_UPLOAD_CSV_REQUEST_SUCCESS:
      return {
        ...state,
        allLists: action.data
      }
    case ON_CHANGE_UPLOAD_CSV_DATA:
      return {
        ...state,
        uploadCsvData: {
          ...state.uploadCsvData,
          [action.key]: action.value
        }
      }
    case UPLOAD_LIST_REQUEST_SUCCESS:
      return {
        ...state,
        uploadCsvData: {},
        showMatchingPage: false,
      }
    case TOGGLE_MATCHING_PAGE:
      return {
        ...state,
        showMatchingPage: !state.showMatchingPage
      }
    case RESET_LISTS_PAGE:
      return {
        ...initialState
      }
    case ADD_UPLOAD_CSV_ERRORS:
      return {
        ...state,
        uploadCsvErrors: action.data,
      };    
    case SET_EDIT_LIST_DATA:      
      return {
        ...state,
        editListData: action.data,
      };
    case ADD_ERRORS_EDIT_LIST:
      return {
        ...state,
        editListError: action.data,
      };
    case TOGGLE_EDIT_LIST_MODAL:
      return {
        ...state,
        showEditListModal: action.data,
      };
    case ON_CHANGE_EDIT_LIST:
      return {
        ...state,
        editListData: {
          ...state.editListData,
          [action.key]: action.value,
        },
      };
    case RESET_EDIT_LIST_FORM:
      return {
        ...state,
        editListData: initialState.editListData,
        editContactError: initialState.editContactError,
      };
    case EDIT_LIST_REQUEST_SUCCESS:      
      let updatedList = action.data.body.list;      
      let editData = state.data.data;
      editData = editData.map(item => {
        if (item._id === updatedList._id) {
          item = { ...item, ...updatedList };
        }
        return item;
      });
      return {
        ...state,
        data: {
          ...state.data,
          data: editData,
        },
      };
    case GET_ZIP_BORDERS_REQUEST:
    case GET_UNIQUE_ZIPS_REQUEST:
      return{
        ...state,
        isLoading: true
      };
    case GET_ZIP_BORDERS_REQUEST_SUCCESS:
      return{
        ...state,
        zipBorders: action.data.zipBorders,
        mapPosition: action.data.mapPosition,
        zoom: action.data.zoom,
        markers: action.data.markers,
        isLoading: false
      };
    case GET_ZIP_BORDERS_REQUEST_FAILED:
    case GET_UNIQUE_ZIPS_REQUEST_FAILED:
      return{
        ...state,
        isLoading: false
      };
    case RESET_LIST_DETAIL:
      return{
        ...state,
        listContacts: {},
        zipBorders: null,
        mapPosition: { lat: 39.50, lng: -98.35 },
        zoom: 4,
      };
    case GET_UNIQUE_ZIPS_REQUEST_SUCCESS:
      return{
        ...state,
        listContacts: action.data
      };

    default:
      return state;
  }
}