import React, {createRef, useEffect} from 'react';
import {Map, TileLayer, ZoomControl, withLeaflet} from 'react-leaflet';
import { useSelector } from 'react-redux';
import './LeafletMap.scss';
import DeflateDefault from 'react-leaflet-deflate';
import L from "leaflet";
import ReactDOMServer from "react-dom/server";
import { ReactComponent as LocationIcon } from 'assets/img/icons/location.svg';
const Deflate = withLeaflet(DeflateDefault);

export default function LeafletBordersMap({listContacts, activeTab, tabId}) {

  const zipBorders = useSelector(state => state.lists.zipBorders);
  const mapPosition = useSelector(state => state.lists.mapPosition) || {};
  const zoom = useSelector(state => state.lists.zoom);
  const isLoading = useSelector(state => state.lists.isLoading);
  const ref = createRef();

  useEffect(() => {
    if(activeTab === tabId) ref.current.leafletElement.invalidateSize(false)
  }, [activeTab]);

  const onEachFeature = (feature, featureLayer) => {
    featureLayer.bindTooltip(`${listContacts.uniqZip[feature.properties.zip]}`);
  };

  const icon = L.divIcon({
    className: 'custom-icon custom-marker',
    html: ReactDOMServer.renderToString(<LocationIcon/>),
    iconAnchor: [25, 43],
    iconSize: [50, 50],
    tooltipAnchor: [10, -20],
  });

  return (     
    <Map
      ref={ref}
      className={`${isLoading ? 'loading' : ''}`}
      center={[mapPosition.lat, mapPosition.lng]}
      zoomSnap={0.25}
      zoom={zoom}
      maxZoom={50}
      attributionControl={true}
      zoomControl={false}
      doubleClickZoom={true}
      scrollWheelZoom={true}
      dragging={true}      
      easeLinearity={0.35}
    >
      <div className="loading-indicator">Processing...</div>
      <ZoomControl position="bottomleft"/>
      <TileLayer
        url={`http://{s}.google.com/vt/lyrs=m&hl=en&x={x}&y={y}&z={z}`}
        subdomains={['mt0','mt1','mt2','mt3']}            
      />
      {zipBorders && (
        <Deflate
          data={zipBorders}
          minSize={10}
          markerCluster={true}
          markerOptions={{icon}}
          onEachFeature={onEachFeature}
        />
      )}
    </Map>
  );
}
