import React, {useState} from 'react';
import { ModalFooter, FormGroup } from 'reactstrap';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import Ajv from 'ajv';
import {name} from 'utils/ajvProperties';
import './SaveTemplateModal.scss';

const ajv = new Ajv({ allErrors: true });

const AccountSchema = {
  type: 'object',
  required: [ 'name'],
  properties: {
    ...name ,
  }
};

export default function SaveTemplateModal(props) {
  const [postcardName, setPostcardName] = useState('');
  const [error, setError] = useState('');

  const validation = () => {
    const formattedForBE = { name: postcardName }

    let result = ajv.validate(AccountSchema, formattedForBE);

    if (!result) {
      ajv.errors.map(error => {
        // const errorKey = error.dataPath.split('.').pop();
        setError('This field is required!');
      });
      return false
    }

    return true;
  };

  const close = () => {
    setPostcardName('');
    setError('');
  };

  const submit = (postcardName) => {
    if(!validation()) return
    props.savePostcard(postcardName)
  };

  return (
    <div>
      <DopeModal
        className='save-template-modal'
        isOpen={props.open}
        onClosed={close}
      >
        <DopeModalHeader>Create Template</DopeModalHeader>
        <DopeModalBody>
        <FormGroup className="dope-form-group">
          <DopeInput
            value={postcardName}
            title='Template Name'
            supText='*'
            maxLength={30}
            error={error}
            onChange={e => {
              setError('');
              setPostcardName(e.target.value)}
            }
          />
        </FormGroup>
        </DopeModalBody>
        <ModalFooter>
          <DopeButton color='white' onClick={props.toggle}>
            Cancel
          </DopeButton>
          <DopeButton
            color='primary'
            disabled={!postcardName}
            onClick={() => submit(postcardName)
            }
          >
            Save
          </DopeButton>
        </ModalFooter>
      </DopeModal>
    </div>
  );
}
