import React, { Component } from 'react';
// import { Redirect } from 'react-router-dom';
// import Ajv from 'ajv';
import SignUpForm from './SignUpForm';
import Loader from 'components/Loader/Loader';
// import {pathOr} from 'ramda';
import './SignUp.scss';

const passwordsText = "Must contain at least 8 characters, including uppercase, lowercase, and numbers.";
const passwordsMatchText = "Those passwords doesn't match.";
const emailErrorText = "Error in email - email format validation failed:";
const accountPhoneErrorText = "Error in accountPhone - accountPhone validation failed:";

class SignUp extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: {
        email: '',
        password: '',
        confirmPassword: '',
        accountPhone: '',
      },
      errors: {
        email: '',
        password: '',
        confirmPassword: '',
        accountPhone: '',
      },
      passwordVisibility: false,
      confirmPasswordVisibility: false,
      loading: false,
    };
  }

  componentDidMount() {
    const url = window.location.href;
    const params = new URL(url);
    const email = params.searchParams.get('email');
    if(email){
      this.setState(
        {
          data:{
            email: email,
          }
        }
      )
    }
  }

  togglePasswordVisibility = () => {
    this.setState({ passwordVisibility: !this.state.passwordVisibility });
  };

  toggleConfirmPasswordVisibility = () => {
    this.setState({ confirmPasswordVisibility: !this.state.confirmPasswordVisibility });
  };

  onChange = (key, value) => {    
    const data = {
      data: {
        ...this.state.data,        
        [key]: value,
      },
      errors: {
        ...this.state.errors,        
        [key]: '',
      },
    };
    this.setState({ ...data });
  };

  validation = () => {    
    const { email, password, confirmPassword, accountPhone } = this.state.data;
    let errors = this.state.errors;
    // const emailRegex = /^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,5})$/; //MAX
    // const emailRegex = /^[\w-\.\']+@([\w-]+\.)+[\w-]{2,4}$/;
    const emailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
    const passRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)\S{8,}$/; 
    const accountPhoneRegex = /^\+?[0-9]{9,15}$/;  
    if( !emailRegex.test(email) || !passRegex.test(password) || (password !== confirmPassword) || !accountPhoneRegex.test(accountPhone) ){ 
      if( !emailRegex.test(email) ){
        errors.email = `${emailErrorText} ${email}`;
      }
      if( !passRegex.test(password) ){        
        errors.password = passwordsText;
        // if( password.trim() !== password ){             
        //   errors.password = `${errors.password} Please, dont use "space" before or after password`;        
        // }
      }
      if (password !== confirmPassword) {  
        errors.confirmPassword = passwordsMatchText;        
      }
      if( !accountPhoneRegex.test(accountPhone) ){
        errors.accountPhone = `${accountPhoneErrorText} ${accountPhone}`;
      }
      this.setState({ errors });      
      return false;
    }
    this.setState({
      errors: {  
        ...this.state.errors, 
        email: '',      
        password: '',
        confirmPassword: '',
        accountPhone: '',
      },
    });
    return true;          
  };

  errorHandle = (error) => {    
    this.setState({
      errors: {
        ...this.state.errors,
        ...error
      }
    });
  }

  signUp = async () => {    
    if (!this.validation()) return;
    try {
      this.setState({loading: true})
      // eslint-disable-next-line
      let responce = await this.props.auth.signup(this.state.data.email.toLowerCase(), this.state.data.password, this.state.data.accountPhone, this.errorHandle);      
      this.setState({loading: false})
    } catch (err) {
      this.setState({
        loading: false
      })
    }
  };

  render() {    
    return (
      <React.Fragment>
        <div className='content'>
          <Loader show={this.state.loading} />
        </div>
        <SignUpForm
          loading = {this.state.loading}
          data={this.state.data}
          errors={this.state.errors}
          passwordVisibility={this.state.passwordVisibility}
          confirmPasswordVisibility={this.state.confirmPasswordVisibility}
          togglePasswordVisibility={this.togglePasswordVisibility}
          toggleConfirmPasswordVisibility={this.toggleConfirmPasswordVisibility}
          onSubmit={this.signUp}
          onChange={this.onChange}
        />
      </React.Fragment>
    );
  }
}

export default SignUp;
