import {
  SHOW_MAIN_LOADER,
  GET_PACKAGE_LIST_REQUEST_SUCCESS,
  GET_CATEGORY_REQUEST_SUCCESS,
  GET_OWN_PRICE_REQUEST_SUCCESS,
  SET_DOMAIN,
  SET_THEME_COLOR,
  SET_WINDOW_WIDTH,
} from 'actions/general';

const initialState = {
  isLoading: false,
  loadingText: '',
  packageList: [],
  category: [],
  ownPrice: null,
  ownPriceChecked: false,
  domain: '',
  themeColor: '',
  windowWidth: '1200',
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case SHOW_MAIN_LOADER:
      if (action.data.text) {
        return {
          ...state,
          isLoading: action.data.isLoading,
          loadingText: action.data.text,
        };
      } else
        return {
          ...state,
          isLoading: action.data,
          loadingText: '',
        };
    case GET_PACKAGE_LIST_REQUEST_SUCCESS:
      return {
        ...state,
        packageList: action.data,
      };
    case GET_CATEGORY_REQUEST_SUCCESS:
      return {
        ...state,
        category: action.data,
      };
    case GET_OWN_PRICE_REQUEST_SUCCESS:
      // let checkOwnPostcardPrice = action.data.price.postcardPrice&&action.data.price.postcardPrice.length>0;
      // not only postcardprice now is reason ownPrice
      // console.log("checkOwnPostcardPrice:", checkOwnPostcardPrice);
      return {
        ...state,
        ownPriceChecked: true,
        ownPrice: action.data.price,
      };
    case SET_DOMAIN:
      return {
        ...state,
        domain: action.data.domain,
        accessGroup: action.data.accessGroup
      };
    case SET_THEME_COLOR:
      return{
        ...state,
        themeColor: action.data,        
      }
    case SET_WINDOW_WIDTH:
      return{
        ...state,
        windowWidth: action.data,        
      }
    default:
      return state;
  }
}
