import React from 'react';
import StepTitle from 'components/StepTitle';
import { Card, NavLink, TabPane } from 'reactstrap';
import { DopeNav } from 'components/DopeNav/DopeNav';
import { DopeNavItem } from 'components/DopeNav/DopeNavItem';
import { DopeTabContent } from 'components/DopeNav/DopeTabContent';
import AllTemplates from './AllTemplates';
import MyRecentPostcards from './MyRecentPostcards';
import MyPostcardTemplates from './MyPostcardTemplates';
import CategoryTemplates from './CategoryTemplates';
import {pathOr} from 'ramda';
import './Templates.scss';

export default function Templates(props) {
  // console.log("Templates: ", props);
  const tab = props.createPostCard.templateTabIndex;
  const postcardTemplatesData = pathOr([], ['createPostCard','postcardTemplatesData'], props);
  const postcardTemplatesIsLoaded = pathOr([], ['createPostCard','postcardTemplatesIsLoaded'], props);
  const selectedPostcardId = pathOr([], ['createPostCard', 'data', 'template', '_id'], props);
  const { createPostCard } = props;
  const categoryTemplates = pathOr([], ['createPostCard','categoryTemplates'], props);
  const categoryTemplatesIsLoaded = pathOr([], ['createPostCard','categoryTemplatesIsLoaded'], props);
  return (
    <Card className='wizard-section template-container'>
      <div className='template-preview-container'>
        <div className="wizard-section-header">
          <StepTitle {...props.steps[2]}>
            {props.errors.template && (
              <span className='wizard-error'>{props.errors.template}</span>
            )}
          </StepTitle>
        </div>
        <DopeNav tabs>
          <DopeNavItem>
            <NavLink
              className={`${tab === '1' ? 'active' : ''}`}
              onClick={() => {
                props.resetPostCardTemplate();
                props.resetAllPostcardTemplates();
                props.resetCategoryTemplates();
                props.changePostcardTemplateTabIndex('1');
                props.getAllTemplates();
              }}
            >
              All
            </NavLink>
          </DopeNavItem>
          <DopeNavItem>
            <NavLink
              className={`${tab === '2' ? 'active' : ''}`}
              onClick={() => {
                props.resetPostCardTemplate();
                props.clearPostcardTemplatesData();
                props.resetCategoryTemplates();
                props.changePostcardTemplateTabIndex('2');
                props.getPostcards({page:0, limit:8}, 'auto-saved')
              }}
            >
              My Recent Postcards
            </NavLink>
          </DopeNavItem>
          <DopeNavItem>
            <NavLink
              className={`${tab === '3' ? 'active' : ''}`}
              onClick={() => {
                props.resetPostCardTemplate();
                props.clearPostcardTemplatesData();
                props.resetCategoryTemplates();
                props.changePostcardTemplateTabIndex('3');
                props.getPostcards({page:0, limit:8, types: 'postcard,eddm,neighborhood'}, 'saved')
              }}
            >
              My Templates
            </NavLink>
          </DopeNavItem>
              {
                props.category.map((item, i) => {
                  if(item.type === "forPostcards") return (
                    <DopeNavItem key={i}>
                      <NavLink
                        className={`${tab === '3' + i + 1 ? 'active' : ''}`}
                        onClick={() => {
                          props.resetPostCardTemplate();
                          props.clearPostcardTemplatesData();
                          props.resetCategoryTemplates();
                          props.changePostcardTemplateTabIndex('3'+ i + 1);
                          props.getCategoryTemplates({page:0, limit:8, types: 'postcard,eddm,neighborhood'}, item._id)
                        }}
                      >
                        {item.name}
                      </NavLink>
                    </DopeNavItem>
                  )
                })
              }


        </DopeNav>

        <DopeTabContent activeTab={tab}>
          <TabPane tabId='1'>
            <AllTemplates
              createPostCard={createPostCard}
              newTemplateIsLoaded={props.newTemplateIsLoaded}
              onSelectTemplate={props.onSelectTemplate}
              addErrors={props.addErrors}
              getAllTemplates={props.getAllTemplates}
              resetPostCardTemplateBackKey={props.resetPostCardTemplateBackKey}
            />
          </TabPane>
          <TabPane tabId='2'>
            <MyRecentPostcards
              postcardTemplatesData={postcardTemplatesData}
              postcardTemplatesIsLoaded={postcardTemplatesIsLoaded}
              selectedPostcardId={selectedPostcardId}
              onSelectTemplate={props.onSelectTemplate}
              getPostcards={ props.getPostcards}
              addErrors={props.addErrors}
              resetPostCardTemplateBackKey={props.resetPostCardTemplateBackKey}
            />
          </TabPane>
          <TabPane tabId='3'>
            <MyPostcardTemplates
              postcardTemplatesData={postcardTemplatesData}
              postcardTemplatesIsLoaded={postcardTemplatesIsLoaded}
              selectedPostcardId={selectedPostcardId}
              onSelectMyTemplate={props.onSelectMyTemplate}
              getPostcards={ props.getPostcards}
              addErrors={props.addErrors}
              deleteMyPostcard={props.deleteMyPostcard}
              showDeleteTemplateModal={props.showDeleteTemplateModal}
              toggleDeleteTemplateModal={props.toggleDeleteTemplateModal}
              resetPostCardTemplateBackKey={props.resetPostCardTemplateBackKey}
            />
          </TabPane>
          {
            props.category.map((item, i) => {
              return (
                <TabPane tabId={'3' + i +1} key={i}>
                  <CategoryTemplates
                    categoryId={item._id}
                    categoryTemplates={categoryTemplates}
                    categoryTemplatesIsLoaded={categoryTemplatesIsLoaded}
                    createPostCard={createPostCard}
                    addErrors={props.addErrors}
                    onSelectTemplate={props.onSelectTemplate}
                    getCategoryTemplates={props.getCategoryTemplates}
                  />
                </TabPane>
              )
            })
          }
        </DopeTabContent>
      </div>
    </Card>
  );
}
