import React from 'react';
import { NavLink, TabPane } from 'reactstrap';
import { DopeNav } from 'components/DopeNav/DopeNav';
import { DopeNavItem } from 'components/DopeNav/DopeNavItem';
import { DopeTabContent } from 'components/DopeNav/DopeTabContent';
import AllTemplates from './AllTemplates';
import MyRecentPostcards from './MyRecentPostcards';
import MyPostcardTemplates from './MyPostcardTemplates';
import './Templates.scss';

export default function Templates(props) {
  // console.log("Templates: ", props);  
  const {
    tab,
    ...rest
  } = props;
  return (
    <div className='morpheus-templates-container'>      
      <DopeNav tabs>
        <DopeNavItem>
          <NavLink
            className={`${tab === '1' ? 'active' : 'w1'}`}
            onClick={() => {
              props.changePostcardTemplateTabIndex('1');
              // props.getAllTemplates();
            }}
          >
            All
          </NavLink>
        </DopeNavItem>        
        <DopeNavItem>
          <NavLink
            className={`${tab === '2' ? 'active' : 'w2'}`}
            onClick={() => {            
              props.changePostcardTemplateTabIndex('2');
              // props.getPostcards({page:0, limit:10}, 'auto-saved')
            }}
          >
            My Recent Postcards
          </NavLink>
        </DopeNavItem>       
        <DopeNavItem>
          <NavLink
            className={`${tab === '3' ? 'active' : 'w3'}`}
            onClick={() => {
              props.changePostcardTemplateTabIndex('3');
              // props.getPostcards({page:0, limit:10}, 'saved')
            }}
          >
            My Templates
          </NavLink>
        </DopeNavItem>
      </DopeNav>
      <DopeTabContent activeTab={tab}>
        <TabPane tabId='1'>
          <AllTemplates            
            {...rest}
          />
        </TabPane>        
        <TabPane tabId='2'>
          <MyRecentPostcards            
            {...rest}            
          />
        </TabPane>
        <TabPane tabId='3'>
          <MyPostcardTemplates
            {...rest}
          />
        </TabPane>          
      </DopeTabContent>
    </div>
  );
}
