import {
    GET_DB_CONFIGS_SUCCESS,
    SET_DB_CONFIGS,
    SET_DB_CONFIGS_SUCCESS,
    SET_DB_CONFIGS_FAIL
} from '../actions';

export default function reducer(state = {}, action) {
    switch (action.type) {
        case GET_DB_CONFIGS_SUCCESS:
            return {
                ...state,
                configs: action.configs,
                loadingFieldName: ''
            };

        case SET_DB_CONFIGS:
            return {
                ...state,
                loadingFieldName: action.payload && action.payload.key
            };

        case SET_DB_CONFIGS_SUCCESS:
        case SET_DB_CONFIGS_FAIL:
            return {
                ...state,
                loadingFieldName: ''
            };

        default:
            return state;
    }
}
