export default function ListsView(props) {
  const { url } = useRouteMatch();

  return (
    <div className="content">
      <Switch>
        <Route exact path={`${url}`}>
          <Lists isAdmin={props.isAdmin} />
        </Route>
        <Route exact path={`${url}/:listId`}>
          <ListDetail/>
        </Route>
      </Switch>
    </div>
  );
}
