import { select, put, takeEvery, all, call } from 'redux-saga/effects';
import request from "utils/request";
import { API_URL } from 'config/config';

import * as actions from '../actions'

const PATH = `${API_URL}/config`;

export const getConfigs = (state) => state.dbConfigs.configs;

export function* setSetting(action) {
    const { key, value, withApiCall } = action.payload;
    try {
        if (withApiCall) {
            yield call(request, PATH, {
                method: 'post',
                body: {
                    key,
                    value
                }
            });
        }

        yield put(actions.setDbConfigsSuccess({newItem: {
                name: key,
                value
            }}))

    } catch (e) {
        console.log("setSetting setDbConfigsFail");
        yield put(actions.setDbConfigsFail())
    }
};

export function* getSettings(action) {
    // console.log("getSettings(action): ", action);
    let newItem = null;
    if(action.payload && action.payload.newItem) {
        newItem = action.payload.newItem;
    }
    try {
        let data = [];
        let configs = yield select(getConfigs);
        if (configs) {
            data = [...configs];
        }
        if (data.length && newItem) {
            let configToChange = data.find(config => config.name === newItem.name);

            if (configToChange) {
                data = data.map(config => {
                    if (config.name === newItem.name) {
                        return newItem;
                    }
                    return config;
                })
            } else {
                data.push(newItem);
            }
        }

        if (!data.length) {
            const res = yield call(request, PATH, {
                method: 'get'
            });
            data = res.data;
        }
        yield put(actions.getDbConfigsSuccess(data))

    } catch (e) {
        console.log("getSettings setDbConfigsFail");
        yield put(actions.getDbConfigsFail(e))

    }
}

export default all([
    takeEvery(actions.SET_DB_CONFIGS, setSetting),
    takeEvery(actions.SET_DB_CONFIGS_SUCCESS, getSettings),
    takeEvery(actions.GET_DB_CONFIGS, getSettings)
])
