import React from 'react';
import { FormGroup } from 'reactstrap';
import { DopeModalHeader } from '../../DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from '../../DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from '../../DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeModal } from '../../DopeModal/DopeModal';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import Select from 'react-select';
import Ajv from 'ajv';
import { STATES } from 'utils/constants';
import { AccountSchema } from './config';
import './AddContact.scss';

const ajv = new Ajv({ allErrors: true });

export default function AddContact(props) {
  
  const allLists = [...props.allLists];

  if ((allLists && allLists[0] && allLists[0]._id !== 'createNewList') || allLists.length === 0)
    allLists.unshift({ name: 'Create a New List', _id: 'createNewList' });

  const onChange = e => {
    let errors = props.errors;
    errors[e.target.name] = '';

    props.addErrorsNewContact(errors);

    props.onChangeNewContact(e.target.name, e.target.value);
  };

  const getListsOptions = () => {
    return allLists.map(item => ({ value: item._id, label: item.name }));
  };

  const getSelectedList = value => {
    const filtered = getListsOptions().filter(item => item.value === value);

    if (filtered.length) return filtered[0];
  };

  const getOptions = () => {
    const states = STATES;

    return states.map(item => ({ value: item.name, label: item.name }));
  };

  const getSelectedItem = value => {
    const filtered = getOptions().filter(item => item.value === value);

    if (filtered.length) return filtered[0];
  };

  const onClose = () => {
    props.resetNewContactForm();
    props.toggleAddContactModal(false);
  };

  const newContactValidation = () => {        
    const formattedForBE = {
      ...props.data,
    };

    if (props.data.listRequestID !== 'createNewList') {
      delete AccountSchema.properties.name;
      AccountSchema.required.filter(item => {
        return item !== 'name';
      });
    }

    let result = ajv.validate(AccountSchema, formattedForBE);
    const errors = {};

    if (!result) {
      ajv.errors.map(error => {
        const errorKey = error.dataPath.split('.').pop();
        errors[errorKey] = 'This field is required!';
      });      
      props.addErrorsNewContact(errors);
    }
    return result;
  };

  const onSubmit = () => {
    if (!newContactValidation()) return;    
    let req = {
      query: {
        account: props.currentAccount._id,
        listRequestID: props.data.listRequestID,
      },
      body: { json: props.data },
    };
    if (props.data.listRequestID === 'createNewList')
      req.query.listRequestID = '';
    props.addNewContactRequest(req);
  };
  return (
    <div>
      <DopeModal className='add-contact' isOpen={props.open} centered>
        <DopeModalHeader>New Contact</DopeModalHeader>
        <DopeModalBody>
          <div className='one-line'>
            <FormGroup className={`dope-form-group create-new-list ${props.errors.listRequestID
                ? 'select-error'
                : ''}`}
            >
              <span className='input-title'>
                Add to List <sup>*</sup>
              </span>              
              <Select
                className="dope-select"
                classNamePrefix="dope"
                value={getSelectedList(props.data.listRequestID || '')}
                options={getListsOptions()}
                placeholder=''
                name='listRequestID'
                onChange={item => {                                    
                  props.addErrorsNewContact( {...props.errors, listRequestID : '' } );
                  props.onChangeNewContact('listRequestID', item.value);
                }}
                error={true}
              />              
              {props.errors.listRequestID && (
                <div className='validation-message'>
                  {props.errors.listRequestID || ''}
                </div>
              )}
            </FormGroup>

            <FormGroup className="dope-form-group">
              {props.data.listRequestID === 'createNewList' && (
                <DopeInput
                  value={props.data.name || ''}
                  title='New List Name'
                  supText='*'
                  name='name'
                  error={props.errors.name || ''}
                  onChange={onChange}
                />
              )}
            </FormGroup>
          </div>
          <div className='one-line'>
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.firstName || ''}
                title='First Name'
                supText='*'
                name='firstName'
                error={props.errors.firstName || ''}
                onChange={onChange}
              />
            </FormGroup>
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.lastName || ''}
                title='Last Name'
                supText='*'
                name='lastName'
                data-group='companyAddress'
                error={props.errors.lastName || ''}
                onChange={onChange}
              />
            </FormGroup>
          </div>
          <div className='one-line'>
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.address1 || ''}
                title='Address'
                supText='*'
                name='address1'
                error={props.errors.address1 || ''}
                onChange={onChange}
              />
            </FormGroup>
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.address2 || ''}
                title='Address 2'
                name='address2'
                error={props.errors.address2 || ''}
                onChange={onChange}
              />
            </FormGroup>
          </div>
          <div className='one-line'>
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.city || ''}
                title='City'
                supText='*'
                name='city'
                error={props.errors.city || ''}
                onChange={onChange}
              />
            </FormGroup>
            <FormGroup className={`dope-form-group ${props.errors.state
                ? 'select-error'
                : ''}`}
            >
              <span className='input-title'>
                State <sup>*</sup>
              </span>
              <div className='dope-select'>
                <Select
                  className="dope-select"
                  classNamePrefix="dope"
                  value={getSelectedItem(props.data.state || '')}
                  options={getOptions()}
                  placeholder=''
                  onChange={item => {                    
                    props.addErrorsNewContact( {...props.errors, state : '' } );
                    props.onChangeNewContact('state', item.value);
                  }}
                />
              </div>

              {props.errors.state && (
                <div className='validation-message'>{props.errors.state || ''}</div>
              )}
            </FormGroup>
          </div>
          <div className='one-line'>
            <FormGroup className="dope-form-group">
              <DopeInput
                value={props.data.zip || ''}
                title='ZIP'
                supText='*'
                name='zip'
                data-group='companyAddress'
                mask='99999'
                error={props.errors.zip || ''}
                onChange={onChange}
              />
            </FormGroup>
            <FormGroup className="dope-form-group"></FormGroup>
          </div>
        </DopeModalBody>
        <DopeModalFooter className='dope-footer'>
          <DopeButton color='secondary' onClick={onClose}>
            Cancel
          </DopeButton>
          <DopeButton color='primary' onClick={() => onSubmit()}>
            Create
          </DopeButton>
        </DopeModalFooter>
      </DopeModal>
    </div>
  );
}
