import React from 'react';

export default class AddressAutocomplete extends React.Component {
    constructor(props) {
        super(props);
        this.autocompleteInput = React.createRef();
        this.autocomplete = null;
        this.handlePlaceChanged = this.handlePlaceChanged.bind(this);
    }

    componentDidMount() {
        this.autocomplete = new window.google.maps.places.Autocomplete(this.autocompleteInput.current,
            { "types": ["geocode"] });

        this.autocomplete.addListener('place_changed', this.handlePlaceChanged);
    }

    handlePlaceChanged() {
        const place = this.autocomplete.getPlace();
        this.props.onPlaceLoaded(place);
    }

    render() {
        const {name, id, placeholder, value, onChange, label, error, onKeyDown, disabled} = this.props;
        return (
            <div className="input-container">
        { label && <label htmlFor={id}>{label}</label>}
            <input
                ref={this.autocompleteInput}
                id="autocomplete"
                type="text"
                name={name}
                disabled={disabled}
                placeholder={placeholder}
                onKeyDown={onKeyDown}
                value={value}
                onChange={onChange}
                className={error? "dope-input form-control error":  "dope-input form-control"}
            />
            { error && <div className='validation-message'>{error}</div> }
            </div>
        );
    }
}