import React from 'react';
import {
  DopeModal,
  DopeModalHeader,
  DopeModalBody,
  DopeModalFooter,
} from 'components/DopeModal';
import { DopeButton } from 'components/DopeForms';
import './DeleteSendingModal.scss';

export default function DeleteSendingModal({ isOpen, toggle, deleteSending }) {
  return (
    <DopeModal className='delete-sending-modal' isOpen={isOpen} centered>
      <DopeModalHeader toggle={toggle}>Remove Sending</DopeModalHeader>
      <DopeModalBody>
        <p>
          Are you sure you want to remove the sending? You cannot undo this
          action.
        </p>
      </DopeModalBody>
      <DopeModalFooter>
        <DopeButton color='white' onClick={toggle}>
          Cancel
        </DopeButton>
        <DopeButton color='primary' onClick={deleteSending}>
          Remove
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>
  );
}
