import React from 'react';
import Cards from 'components/Cards';

export default function CreditCards(props) {
  return (
    <div className='credit-card-container'>
      <h6 className="profile-title">Credit Card information</h6>
      <Cards
        currentAccount={props.currentAccount}
        selected={props.currentAccount.default_source}
        onSelect={card => {             
          props.onDefaultCardChange(card.id);   
        }} 
        onDeleteCard = {card => {             
            props.onDeleteCard(card.id);   
        }}
        canDeleteCard
      />
    </div>
  );
}
