import React from 'react';
import AddNumberModal from 'components/AddNumberModal';
import * as newTrackingNumberAction from '../../actions/newTrackingNumber';
import {
	FormGroup,
	Card,
	CardHeader,
	CardBody,
	Alert,
} from 'reactstrap';
import { friendlyFormatPhoneNumber, fullNumberFormat} from '../../utils/phones';
import StepTitle from '../../components/StepTitle';
import Select from 'react-select';
import { connect } from 'react-redux';
import DopeRadioGroup from '../DopeForms/DopeRadioGroup';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
// import ReactTooltip from 'react-tooltip';
import { ReactComponent as AttentionIcon } from 'assets/img/icons/attention.svg';
// import { ReactComponent as HelpIcon } from 'assets/img/icons/help.svg';

import './PhoneNumberForm.scss';


class PhoneNumberForm extends React.Component {
	constructor(props) {
		super(props)
	
		this.state = {
		addNumber: false,
		}
	}  
	// accountNumber

	componentWillMount() {
		this.getExistingTrackingNumbers();		
	}

	componentDidUpdate(prevProps) {		
    if (this.props.currentAccount._id !== prevProps.currentAccount._id) {
      this.props.resetExistingTrackingNumber();
      this.getExistingTrackingNumbers();
		}
		if (this.props.newTrackingNumber.existingNumbers.length !== prevProps.newTrackingNumber.existingNumbers.length) {			     
      this.getExistingTrackingNumbers();
    }
  }

	componentWillUnmount() {
		this.props.resetExistingTrackingNumber();
	}

	getExistingTrackingNumbers = () => {
		this.setState({ loading: true });
		this.props.createExistingNumbersRequest(this.props.currentAccount._id)
		this.setState({ loading: false });
	};

	render() {
		const props = this.props;				
		const {existingNumbers} = this.props.newTrackingNumberData;
		const {trackingNumber, ownNumber} = this.props.submitData;		
		const formatOptionLabel = ({ label }) => (
			<div className="option-container">
				<div>{label && label.name}</div>
				<div>{label && friendlyFormatPhoneNumber(label.trackingNumber)}</div>
			</div>
		);

		let formatedExistingNumbers = existingNumbers.map(number =>{
			return { value: number.trackingNumber, label: number }
		})

		return (
			<Card className="wizard-section">
				<CardHeader className="wizard-section-header" children={<StepTitle {...this.props.steps[0]}/>}/>
				{
					props.typeOfForm === 'postcards' && (
						<React.Fragment>
							<div className="radio-container">
								<DopeRadioGroup
									data={[
										{
										value: "trackingPhoneNumber",
										label: "Tracking phone number"
										},
										{
										value: "ownPhoneNumber",
										label: "Use my phone number"
										}
									]}
									name="phoneNumbers"
									selectedValue={props.submitData.selectedNumber}
									onChange={e => {
										this.props.onChangeSetSelectedNumber(e);											
										// this.props.onChangeOwnNumber("");
										this.props.onChangeTrackingNumber("");
										this.props.deleteErrorsNumbers('trackingNumber');
										this.props.deleteErrorsNumbers('ownNumber');
									}}				             
								/>
							</div>	
							{
								props.submitData.selectedNumber === "trackingPhoneNumber" ? (
									<CardBody className='add-tracking-number'>
										<FormGroup className="postcard-tracking-number-container">
											<Select
												className="dope-select"
												classNamePrefix="dope"
												placeholder="Select an existing tracking"
												isLoading={props.existingNumbersIsLoading}
												formatOptionLabel={formatOptionLabel}
												options={formatedExistingNumbers}
												value={formatedExistingNumbers.filter(item => fullNumberFormat(item.value) === fullNumberFormat (trackingNumber)
												)}
												onChange={e =>{
													const number = fullNumberFormat(e.value);
													this.props.onChangeTrackingNumber(number);
													this.props.addErrors('trackingNumber', '')}
												}
											/>
											<div className='validation-message'>{props.errorsData.trackingNumber}</div>
										</FormGroup>
										<div className="add-btn-container">	
											<span>Need more tracking numbers?</span>
											<span className='add-number-btn' onClick={()=>this.props.toggleTrackingNumberModal(true)}>
												Add tracking numbers.
											</span>
										</div>
										<AddNumberModal 
												newNumberHandler={this.props.onChangeTrackingNumber}
												setShowAddingCard={this.props.setShowAddingCardPhoneNumberForm}
										/>										
									</CardBody>
								):(									
									<CardBody className='add-tracking-number'>
										<FormGroup className="postcard-other-number-container">
											<DopeInput
												name="ownNumber"								
												id="ownNumber"			
												value={ownNumber.slice(2)}	
												mask="(999)999-9999"
												alwaysShowMask								
												onChange={e => {
													let number = fullNumberFormat(e.target.value);													
													this.props.onChangeOwnNumber(number);
													this.props.deleteErrorsNumbers('ownNumber');													
												}}
												error={props.errorsData.ownNumber}
											/>
											{/* <HelpIcon className="help-own-number-icon" data-tip data-for="helpOwnNumberTooltip" data-place="right" />	
											<ReactTooltip          
												className="help-own-number-tooltip"  												       
												html={true} 
												id="helpOwnNumberTooltip"
											>  
												{`<span>We added your profile number automatically,</span> <span>you can change this number if you want</span>`}
											</ReactTooltip>																				 */}
										</FormGroup>
										<Alert color="warning" className="dope-attention">
											<AttentionIcon className="icon"/>
											<span>
												<span>You cannot track any numbers except for tracking numbers.</span><br/>
												<span>We added your profile number automatically, you can change this number if you want.</span>
											</span>
										</Alert>	
									</CardBody>	
								)
							}
						</React.Fragment>
					)
				}
				{	
					props.typeOfForm === 'yardSigns' && (						
						<React.Fragment>
							<div className="radio-container">
								<DopeRadioGroup
									data={[
										{
										value: "trackingPhoneNumber",
										label: "Tracking phone number"
										},
										{
										value: "ownPhoneNumber",
										label: "Use my phone number"
										}
									]}
									name="phoneNumbers"
									selectedValue={props.submitData.selectedNumber}						
									onChange={e => {
										this.props.onChangeSetSelectedNumber(e);
										// this.props.onChangeOwnNumber("");
										this.props.onChangeTrackingNumber("");
										this.props.deleteErrorsNumbers('trackingNumber');
										this.props.deleteErrorsNumbers('ownNumber');
									}}				             
								/>
							</div>	
							{
								props.submitData.selectedNumber === "trackingPhoneNumber" ? (									
									<CardBody className='add-tracking-number'>
										<FormGroup className="yard-tracking-number-container">
											<Select
												className="dope-select"
												classNamePrefix="dope"
												placeholder="Select an existing tracking"
												isLoading={props.existingNumbersIsLoading}
												formatOptionLabel={formatOptionLabel}
												options={formatedExistingNumbers}
												value={formatedExistingNumbers.filter(item => fullNumberFormat(item.value) === fullNumberFormat (trackingNumber)
												)}
												onChange={e =>{
													const number = fullNumberFormat(e.value);													
													this.props.onChangeTrackingNumber(number);
													this.props.addErrors('trackingNumber', '')}
												}
											/>
											<div className='validation-message'>{props.errorsData.trackingNumber}</div>
										</FormGroup>
										<div className="add-btn-container">	
											<span>Need more tracking numbers?</span>
											<span className='add-number-btn' onClick={()=>this.props.toggleTrackingNumberModal(true)}>
												Add tracking numbers.
											</span>
										</div>
										<AddNumberModal 
											newNumberHandler={this.props.onChangeTrackingNumber}
										/>					
									</CardBody>
								):(
									<CardBody className='add-tracking-number'>
										<FormGroup className="yard-other-number-container">
											<DopeInput
												name="ownNumber"								
												id="ownNumber"		
												value={ownNumber.slice(2)}	
												mask="(999)999-9999"
												alwaysShowMask								
												onChange={e => {
													let number = fullNumberFormat(e.target.value);													
													this.props.onChangeOwnNumber(number);
													this.props.deleteErrorsNumbers('ownNumber');													
												}}
												error={props.errorsData.ownNumber}
											/>
											{/* <HelpIcon className="help-own-number-icon" data-tip data-for="helpOwnNumberTooltip" data-place="right" />	
											<ReactTooltip          
												className="help-own-number-tooltip"  												       
												html={true} 
												id="helpOwnNumberTooltip"
											>  
												{`<span>We added your profile number automatically,</span> <span>you can change this number if you want</span>`}
											</ReactTooltip>												 */}
										</FormGroup>
										<Alert color="warning" className="dope-attention">
											<AttentionIcon className="icon"/>
											<span>
												<span>You cannot track any numbers except for tracking numbers.</span><br/>
												<span>We added your profile number automatically, you can change this number if you want.</span>
											</span>
										</Alert>	
									</CardBody>					
								)
							}
						</React.Fragment>
					)
				}				
			</Card>
		);
	}
}

const mapStateToProps = state => ({
	currentAccount: state.account.currentAccount,
	newTrackingNumber: state.newTrackingNumber,
	existingNumbersIsLoading: state.newTrackingNumber.existingNumbersIsLoading
});

const mapDispatchToProps = {
	...newTrackingNumberAction
}

export default connect(mapStateToProps, mapDispatchToProps)(PhoneNumberForm);
