import React, {useState, useEffect } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import AutomationEditor from './AutomationEditor';
import AutomationSummary from './AutomationSummary';
import { AUTOMATION_PATH } from 'utils/constants';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import Templates from './Templates';
import { ReactComponent as LeftArrowIcon } from 'assets/img/icons/chevron-left.svg';
import {   
  setNextStepAutomation,
  changeAutomationPostcardTemplateTabIndex,
  selectAutomationMorpheusTemplate,  
  getAutomationItemRequest,
  resetAutomation,
} from 'actions/automation';
import { useSelector, useDispatch } from 'react-redux';
import './AutomationCreate.scss';

export default function AutomationCreate({savedAccountId}) {

  const { automationId } = useParams();
  const dispatch = useDispatch();
  const history = useHistory();
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const templateTabIndex = useSelector(state => state.automation.automationEditor.templateTabIndex);
  const selectedTemplate = useSelector(state => state.automation.automationEditor.selectedTemplate);  
  const step = useSelector(state => state.automation.step);  
  const [showAddingCard, setShowAddingCard] = useState(false);
  
  useEffect(() => {    
    if(automationId==='create'){
      dispatch(resetAutomation());
    }
    if(automationId!=='create'){      
      const data = {
        sendingId: automationId,
      };
      dispatch(getAutomationItemRequest(data));
    }    
    return () => {
      dispatch(resetAutomation());
    };
  },[]);
  useEffect(() => {
    if((savedAccountId !== currentAccountId) && (savedAccountId !== '')){      
      history.push('/automation');            
    }    
  }, [currentAccountId]);  
  
  const changePostcardTemplateTabIndex = (e)=>{
    dispatch(changeAutomationPostcardTemplateTabIndex(e));
  }  
  const selectMorpheusTemplate = (template)=>{
    dispatch(selectAutomationMorpheusTemplate(template));
  }  
  const backStep = () => {
    dispatch(setNextStepAutomation(step-1));
  }

  return (    
    <div className={`AutomationCreate${showAddingCard?" addCard":""}`}>       
      {
        step===1 && (
          <React.Fragment>
            <div className='back-btn-container'>
              <button className='back-btn' onClick={()=>history.push(`${AUTOMATION_PATH}`)}>
                <LeftArrowIcon className="back-icon"/>
                Back to automation
              </button>
            </div>
            <h2 className="status-title">New Automation</h2>
            <div className="automationEditorCard-v2">              
              <div className="section-v2">
                <div className="title"><span className="badge-step">STEP 1</span>SELECT A BASE POSTCARD TEMPLATE TO CUSTOMIZE</div>              
              </div>
              <Templates
                // getAllTemplates={getAllMorpheusTemplates} 
                // getPostcards={getMyMorpheusPostcards}
                tab={templateTabIndex}
                changePostcardTemplateTabIndex={changePostcardTemplateTabIndex}
                onSelectTemplate={selectMorpheusTemplate}
                selectedTemplate={selectedTemplate}
                currentAccountId={currentAccountId}                
              />              
            </div>
            <div className="bnts-container">
              <DopeButton
                color="primary"
                onClick={()=>dispatch(setNextStepAutomation(step+1))}
                disabled={!selectedTemplate}
              >
                Next
              </DopeButton>
            </div>
          </React.Fragment>
        )
      }      
      {
        step === 2 && (          
          <AutomationEditor
            backStep={backStep}
          />            
        )
      }
      {
        step === 3 && (    
          <AutomationSummary
            backStep={backStep}
            showAddingCard={showAddingCard}
            setShowAddingCard={setShowAddingCard}
          />
        )
      }      
    </div>
  );
}
