import React, {useState} from 'react';
import { Collapse} from 'reactstrap';
import { ReactComponent as DropIcon } from 'assets/img/icons/land-drop-icon.svg';
import './LandToggler.scss';

export default function LandToggler({label='Click here...', children}) {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);   
  return (
    <div className="LandToggler">    
      <div onClick={toggle} className={`toggle-btn ${isOpen?"active":""}`}>
        <DropIcon className="arrow-icon"/>
        <span>{label}</span>        
      </div>
      <Collapse isOpen={isOpen}>
        {children}
      </Collapse>
    </div> 
  );
}
