import React from 'react';
import { Card, CardBody, CardHeader } from 'reactstrap';
import Builder from 'components/Builder/HtmlBuilder';
import BackSelector from './BackSelector'
import { friendlyFormatPhoneNumber } from 'utils/phones';
import './Editor.scss';

export default function Editor(props) {
  const { data, backMarkPlace } = props;  
  return (
    <React.Fragment>
      <Card>
        <CardHeader className='template-header'>Front</CardHeader>
        <CardBody>
          <Builder
            template={data.template.front || ''}
            changePostCardStatus={props.changePostCardStatus}
            templateSample={props.templateSample.front || ''}
            phoneNumber={friendlyFormatPhoneNumber(
              data.trackingNumber || data.ownNumber,
            )}
            awsPath={'postcard'}
            currentAccountId={props.currentAccountId}
            setUserPostcardImages={props.setUserPostcardImages}
            side="front"
            postcardSize={data.template.postcardSize}    //'6x9' One size only string incoming from ReduxState
            postcardDpi={300}
            postcardBleed={0.25}
            // postcardBleed={0}     //without bleed for LOB
          />
        </CardBody>
      </Card>
      <Card>
        <CardHeader className='template-header'>Back</CardHeader>        
        { data.template.backs&&data.template.backs.length>1 ? (                    
            <div className="back-selector-container">
              <BackSelector
                backs={data.template.backs}                
                selectedBackKey={data.template.selectedBackKey}
                onSelectTemplateBack = {props.onSelectTemplateBack}
              /> 
            </div>
          ):null 
        }         
        <CardBody>
          <Builder  
            selectedBackKey={data.template.selectedBackKey}
            template={ data.template.back || ''}            
            templateSample={ props.templateSample.back || ''} 
            changePostCardStatus={props.changePostCardStatus}
            phoneNumber={friendlyFormatPhoneNumber(
              data.trackingNumber || data.ownNumber,
            )}
            awsPath={'postcard'}
            currentAccountId={props.currentAccountId}
            setUserPostcardImages={props.setUserPostcardImages}
            backMarkPlace={backMarkPlace}
            side="back"
            postcardSize={data.template.postcardSize}    //'6x9' One size only string incoming from ReduxState
            postcardDpi={300}
            postcardBleed={0.25}
            // postcardBleed={0}     //without bleed for LOB
          />
        </CardBody>
      </Card>
    </React.Fragment>
  );
}
