import {
  CREATE_EXISTING_NUMBER_REQUEST,
  CREATE_EXISTING_NUMBER_REQUEST_SUCCESS,
  CREATE_EXISTING_NUMBER_REQUEST_FAILED,
  SEARCH_NUMBERS_REQUEST_SUCCESS,
  SET_NEW_TRACKING_NUMBER,
  RESET_TRACKING_NUMBER_MODAL,
  ADD_ERRORS_NEW_TRACKING_NUMBER,
  SELECTED_DROP_DOWN_NEW_TRACKING_NUMBER,
  SEARCH_NUMBERS_REQUEST,
  CREATE_TRACKING_NUMBER_REQUEST_SUCCESS,
  RESET_EXISTING_TRACING_NUMBERS,
  SHOW_ADD_TRACKING_NUMBER_MODAL,
  SEARCH_NUMBERS_REQUEST_FAILED,
  TOGGLE_SHOW_ADD_NEW_NUMBER_DROP_DOWN,
  SET_NEW_SEARCH_NUMBERS,
} from 'actions/newTrackingNumber';

let initialState = {
    showAddNumberModal: false,
    areaCode: '',
    trackingNumber: '',
    forwardingNumber: '',
    name: '',
    newSearchNumbersIsLoading: false,
    newSearchNumbers: [],
    existingNumbers: [],
    existingNumbersIsLoading: false,
    selectedFromDropDown: false,
    showAddNewNumberDropDown: false,
    errors: { },
}

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case CREATE_EXISTING_NUMBER_REQUEST:
      return {
          ...state,
          existingNumbersIsLoading: true,
      }
    case CREATE_EXISTING_NUMBER_REQUEST_SUCCESS:
      return {
          ...state,
          existingNumbers: [
            ...action.data
          ],
          existingNumbersIsLoading: false,
      }
    case CREATE_EXISTING_NUMBER_REQUEST_FAILED:
      return {
          ...state,
          existingNumbersIsLoading: false,
      }
    case SEARCH_NUMBERS_REQUEST:
      return {
        ...state,
        newSearchNumbersIsLoading: true,
      }
    case CREATE_TRACKING_NUMBER_REQUEST_SUCCESS:
      return {
        ...state,
        existingNumbers: [
          ...state.existingNumbers,
          action.data
        ]
      }
    case SEARCH_NUMBERS_REQUEST_FAILED:
      return {
        ...state,
        newSearchNumbersIsLoading: false
      }
    case SEARCH_NUMBERS_REQUEST_SUCCESS:
      return {
        ...state,
        newSearchNumbers: action.data,
        newSearchNumbersIsLoading: false,
        showAddNewNumberDropDown: true,
      }
    case SET_NEW_TRACKING_NUMBER:
      return {
        ...state,
        [action.key]: action.value
      }
    case RESET_TRACKING_NUMBER_MODAL:{
      const newState = {...initialState};
      delete newState.existingNumbers
      return {
        ...state,
        ...newState
      }
    }
    case ADD_ERRORS_NEW_TRACKING_NUMBER:
      return {
        ...state,
        errors: {
          ...state.errors,
          [action.key]: action.value,
        }
      }
    case SELECTED_DROP_DOWN_NEW_TRACKING_NUMBER:
      return {
        ...state,
        selectedFromDropDown: action.payload
      }
    case RESET_EXISTING_TRACING_NUMBERS:
      return {
        ...state,
        ...initialState
      }
    case SHOW_ADD_TRACKING_NUMBER_MODAL:
      return {
        ...state,
        showAddNumberModal: action.data
      }
    case TOGGLE_SHOW_ADD_NEW_NUMBER_DROP_DOWN:
      return {
        ...state,
        showAddNewNumberDropDown: action.data
      }
    case SET_NEW_SEARCH_NUMBERS:
      return {
        ...state,
        newSearchNumbers: action.data
      }
    default:
      return state
    }
}
