import React, { useEffect, useState } from 'react';
import { FormGroup, Alert } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { Link } from 'react-router-dom';
import { LOG_IN } from 'utils/constants';

import './ForgotPasswordForm.scss';

export default function ForgotPasswordForm(props) {
  const [delayLoading, setDelayLoading] = useState(false);
  useEffect(() => {   
    let delayLoadingTimer;  
    if (!props.loading && delayLoading){           
      delayLoadingTimer = window.setTimeout(()=>setDelayLoading(false), 30000);
    }
    if (props.loading){          
      setDelayLoading(props.loading);
    }      
    return () => {
      clearTimeout(delayLoadingTimer);
    };
  }, [props.loading]);
  
  return (    
    <form className='forgot-password-form-email' noValidate onSubmit={props.submit}>
      <div className='auth-form-title'>Password Recovery</div>
      <div className='forgot-password-text'>
        Please enter your email, and we will send you a link to reset your
        password.
      </div>
      <div>
        {props.alert && <Alert color='danger'>{props.alert}</Alert>}          
        <FormGroup className="dope-form-group email-group">           
          <DopeInput
            title="Email"
            type='text'
            onChange={e => props.onChange('email', e.target.value.trim())}
            value={props.data.email}
            error={props.errors.email}
          />
        </FormGroup>          
      </div>
      <div className='forgot-password-form-bottom'>
        <div className='link-block'>
          <Link to={LOG_IN}>Back to Sign In</Link>
        </div>
        <DopeButton color='primary' type='submit' disabled={delayLoading}>
          Send
        </DopeButton>
      </div>
    </form>    
  );
}
