import React from 'react';
import { FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms';
// import { debounce } from 'utils/helper';
import { ReactComponent as SearchIco } from 'assets/img/icons/search.svg';
import { ReactComponent as SearchCloseIco } from 'assets/img/icons/close-circle.svg';

import './SearchName.scss';

export default function SearchName(props) {
  //  search state ejected to upper component
  // const [value, setValue] = useState('');  
  // const debounceOnChange = React.useCallback(debounce(props.updateData, 500), []);
  const onChange = e => {    
    const val = e.target.value;      
    // setValue(val);
    // if (val.length >= 3 || val.length === 0){            
    //   debounceOnChange({ page: 1, limit: 10 }, `${props.caseSensitivityOff ? val.toLowerCase() : val }`);
    // }
    // debounceOnChange({ page: 1, limit: 10 }, `${props.caseSensitivityOff ? val.toLowerCase() : val }`);    
    props.setSearchName(val);    
  };
  return (
    <div className='dope-search-container SearchName'>
      <FormGroup>
        {props.searchName ? 
          <SearchCloseIco className="SearchCloseIco" onClick={props.clearSearchName}/>
          :
          <SearchIco />
        }
        <DopeInput
          placeholder= {props.placeholder||''}
          value={props.searchName}
          onChange={onChange}
        />
      </FormGroup>
    </div>
  );
}
