import React, { Component } from 'react';
import {CardHeader, Table } from 'reactstrap';
import {pathOr} from 'ramda';
import './OrderEDDMPreview.scss';

class Order extends Component {
  // builderRef = React.createRef();

  render() {  
    const amount = pathOr(0, ['eddmSending', 'totalDeliveryAddresses'], this.props.data);
    const totalPrice = pathOr(0, ['eddmSending', 'price'], this.props.data)/100;
    const routes = pathOr([], ['eddmSending', 'eddmRoutes'], this.props.data); 
    const service = pathOr(0, ['eddmSending', 'service'], this.props.data);
    const shipping = pathOr(0, ['eddmSending', 'shipping'], this.props.data);
    const size = pathOr(0, ['eddmSending', 'size'], this.props.data);
    const zip = pathOr(0, ['eddmSending', 'zip'], this.props.data);
       
    return (
      <React.Fragment>
        {this.props.showHeader !== false && (
          <CardHeader className='header'>Postcard Order</CardHeader>
        )}
        <div className="order-preview-container"
        >
          {/* <div className="order-preview-img">
            <img src={img} />
          </div> */}
          <Table striped>
            <tbody>
              <tr>
                <td>Postcards Amount</td>
                <td>{amount}</td>
              </tr> 
              <tr>
                <td>ZIP</td>
                <td>{zip}</td>
              </tr> 
              <tr>
                <td>Routes</td>
                <td>{routes.map(el=>el.route).join(', ')}</td>
              </tr>
              <tr>
                <td>Postcard size</td>
                <td>{size}</td>
              </tr>               
              <tr>
                <td>service</td>
                <td>{service}</td>
              </tr> 
              <tr>
                <td>shipping</td>
                <td>{shipping}</td>
              </tr>              
              <tr>
                <td>
                  <b>Total Amounts</b>
                </td>
                <td>
                  <b>${totalPrice}</b>
                </td>
              </tr>
            </tbody>
          </Table>
        </div>
      </React.Fragment>
    );
  }
}

export default Order;
