import uuid from 'short-uuid';
import axios from 'axios';
import LogRocket from 'logrocket';
import {GOOGLE_API_KEY} from '../../config/config';

const id = uuid('abc123456789');
const getId = () => id.generate().substr(0, 4);

const random = (min, max) => {
  let rand = min - 0.5 + Math.random() * (max - min + 1);
  rand = Math.round(rand);
  return rand;
};

const rgbToHex = (rgb) => {
  let hex = Number(rgb).toString(16);
  if (hex.length < 2) {
    hex = '0' + hex;
  }
  return hex;
};

const fullColorHex = (r, g, b) => {
  const red = rgbToHex(r);
  const green = rgbToHex(g);
  const blue = rgbToHex(b);
  return red + green + blue;
};

const getRandomHexColor = () => {
  return '#' + fullColorHex(random(0, 254), random(0, 254), random(0, 254));
};

const makeColor = () => ({
  value: getRandomHexColor(),
  id: getId()
});

const makePalette = () => ({
  id: getId(),
  label: 'Palette ' + getId(),
  colors: [makeColor()]
});

const getGeoCoords = async (address1, city) => {
  let center;
  const defaultCoords = { lat: 44, lng: -93 };
  try {
    let coordsRequestURL = `https://maps.googleapis.com/maps/api/geocode/json?address=${address1},${city},
            &key=${GOOGLE_API_KEY}`;
    let coordsResponse = await axios.get(coordsRequestURL);
    if (coordsResponse.data.status === 'ZERO_RESULTS') {
      // Couldn't retrieve coordinates from Google
      center = defaultCoords;
    }
    const { lat, lng } = coordsResponse.data.results[0].geometry.location;
    center = { lat, lng };
  } catch (e) {
    LogRocket.captureException(e); 
    console.log(e);
    center = defaultCoords;
  }
  return center;
};

export {
  getRandomHexColor,
  getId,
  random,
  makePalette,
  makeColor,
  getGeoCoords
};
