import { put, takeEvery, all, call, fork } from 'redux-saga/effects';
import request from "../utils/request";

import * as actions from '../actions/coupons'

const PATH = '/api/coupon';

export function* getCoupons(action) {
    try {
        const res = yield call(request, PATH, {
            method: 'get'
        });
        yield put(actions.getCouponsSuccess(res.data))

    } catch (e) {
        yield put(actions.getCouponsFail(e))
    }
}

export function* getCoupon(action) {
    try {
        const res = yield call(request, `${PATH}/${action.payload._id}`, {
            method: 'get'
        });
        yield put(actions.getCouponSuccess(res.data))

    } catch (e) {
        yield put(actions.getCouponFail(e))
    }
}

export function* storeCoupon(action) {
    try {
        const res = yield call(request, PATH, {
            method: 'post',
            body: action.payload
        });
        yield put(actions.storeCouponSuccess(res.data));
        yield fork(getCoupons);

    } catch (e) {
        yield put(actions.storeCouponFail(e));
    }
}

export function* updateCoupon(action) {
    try {
        const res = yield call(request, `${PATH}/${action.payload._id}`, {
            method: 'put',
            body: action.payload
        });
        yield put(actions.updateCouponSuccess(res.data));
        yield fork(getCoupons);

    } catch (e) {
        yield put(actions.updateCouponFail(e))
    }
}

export function* deleteCoupon(action) {
    try {
        const res = yield call(request, `${PATH}/${action.payload._id}`, {
            method: 'delete',
        });
        yield put(actions.deleteCouponSuccess(res.data))
        yield fork(getCoupons);

    } catch (e) {
        yield put(actions.deleteCouponFail(e))
    }
}

export default all([
    takeEvery(actions.GET_COUPON, getCoupon),
    takeEvery(actions.GET_COUPONS, getCoupons),
    takeEvery(actions.STORE_COUPON, storeCoupon),
    takeEvery(actions.UPDATE_COUPON, updateCoupon),
    takeEvery(actions.DELETE_COUPON, deleteCoupon),

])
