import React, {useEffect, useState} from 'react';
import LeafletBordersMap from './LeafletMaps/LeafletBordersMap';
import LeafletPinsMap from './LeafletMaps/LeafletPinsMap';
import {resetListDetail, getUniqueZipsRequest} from "actions/listRequests";
import {useDispatch, useSelector} from "react-redux";
import {useParams} from "react-router-dom";
import './ListDetail.scss';
import Contacts from "components/Contacts";
import {DopeTable} from "components/DopeTable";
import {NavLink, TabPane} from "reactstrap";
import {DopeTabContent} from "components/DopeNav/DopeTabContent";
import {DopeNav} from "components/DopeNav/DopeNav";
import {DopeNavItem} from "components/DopeNav/DopeNavItem";
import {columnsFunc} from "./config";

const ListDetail = () => {

  const {listId} = useParams();
  const dispatch = useDispatch();
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const listContacts = useSelector(state => state.lists.listContacts);
  const contactFilters = useSelector(state => state.contacts.filters);
  const [activeTab, setActiveTab] = useState(1);

  const tableData = listContacts.uniqZip
    ? Object.entries(listContacts.uniqZip).map(([key, value], index) => {
      return {
        _id: key,
        index: index + 1,
        zip: key,
        amount: value,
      }
    })
    : [];

  useEffect(() => {
    let data = {
      account: currentAccountId,
      lists: listId,
      page: 0,
      limit: 10,
    };
    dispatch(getUniqueZipsRequest(data));

    return () => {
      dispatch(resetListDetail());
    };
  }, []);

  const onSort = (field, order) => {
    tableData.sort((a, b) => order === 'asc' ? a[field] - b[field] : b[field] - a[field]);
  };

  return (
    <div className="ListDetail">
      <h1 className="main-title">List: {contactFilters.lists[0] && contactFilters.lists[0].name}</h1>
      <DopeNav tabs>
        <DopeNavItem>
          <NavLink className={activeTab === 1 ? 'active' : ''} onClick={() => setActiveTab(1)}>Borders</NavLink>
        </DopeNavItem>
        <DopeNavItem>
          <NavLink className={activeTab === 2 ? 'active' : ''} onClick={() => setActiveTab(2)}>Pins</NavLink>
        </DopeNavItem>
      </DopeNav>
      <DopeTabContent activeTab={activeTab}>
        <TabPane tabId={1}>
          <div className="listMap">
            <div className="listMapContainer">
              <LeafletBordersMap listContacts={listContacts} activeTab={activeTab} tabId={1}/>
            </div>
            <div className='listMapSummary'>
              <div className="listMapSummaryTitle">
                Summary ({listContacts.total} Contacts)
              </div>
              <div className="listMapSummaryItem">
                <DopeTable
                  woPagination
                  keyField="_id"
                  columns={[...columnsFunc(onSort)]}
                  data={tableData}
                />
              </div>
            </div>
          </div>
        </TabPane>
        <TabPane tabId={2}>
          <div className="listMap">
            <div className="listMapContainer">
              <LeafletPinsMap listContacts={listContacts} activeTab={activeTab} tabId={2}/>
            </div>
            <div className='listMapSummary'>
              <div className="listMapSummaryTitle">
                Summary ({listContacts.total} Contacts)
              </div>
              <div className="listMapSummaryItem">
                <DopeTable
                  woPagination
                  keyField="_id"
                  columns={[...columnsFunc(onSort)]}
                  data={tableData}
                />
              </div>
            </div>
          </div>
        </TabPane>
      </DopeTabContent>
      <Contacts/>
    </div>
  );
};

export default ListDetail;
  