import React from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import './DeactivateTrackingNumberModal.scss';

export default function RemoveTrackingNumberModal(props) {
  const onClose = () => {
    props.toggleDeactivateTrackingNumberModal(false);
  };

  const resetModal = () => {
    props.setDeactivateTrackingNumberData({trackingNumber: ''})
  }
  return (
    <div>
      <DopeModal
        isOpen={props.open}
        onClosed={resetModal}
        className='remove-tracking-number-modal'
        centered
      >
        <DopeModalHeader>Deactivate Tracking Number</DopeModalHeader>
        <DopeModalBody>
          <p>
            Are you sure you want to deactivate the number? The forwarding of
            calls will stop, and Dope360 will stop tracking all number related
            activities.
          </p>
        </DopeModalBody>
        <DopeModalFooter>
          <DopeButton color='white' onClick={onClose}>
            Cancel
          </DopeButton>
          <DopeButton color='primary' onClick={props.deactivateTrackingNumber}>
            Deactivate
          </DopeButton>
        </DopeModalFooter>
      </DopeModal>
    </div>
  );
}
