import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import AuthContainer from 'components/AuthContainer';
import { ReactComponent as WarningIco } from 'assets/img/icons/warning.svg';
import history from 'history.js';
import {   
  LOG_IN
} from 'utils/constants';
import './Verification.scss';

class Verification extends Component {
  state = {    
    errorText: '',
  };

  componentDidMount() {    
    const url = window.location.href;       
    const params = new URL(url);        
    const errorText = params.searchParams.get('error'); 
    if (errorText) {      
      this.setState({ errorText });       
    }    
  }

  onSignIn = () => {    
    history.replace(LOG_IN);
  };
  
  render() {
    const {
      errorText,
    } = this.state;
    return (      
      <AuthContainer>
        <div className='verification-form'>
          <div className='auth-link-container'>
            <span className='auth-link' onClick={this.onSignIn}>
              Back to Sign In
            </span>
          </div>
          <div className='circle-container'>
            <div className='circle'>
              <WarningIco/>
            </div>
          </div>
          <div className='auth-form-title'>                  
            {errorText||'Verification hash is invalid or expired'}          
          </div>
        </div>
      </AuthContainer>
    );
  }
}

export default withRouter(Verification);
