import React from 'react';

import './MapSetting.scss';

export default function MapSetting({googleMapType, setGoogleMapType}) { 
    
  return ( 
    <div className="MapSetting">    
      <button className={`map-setBtn${googleMapType==="m" ? ' active':''}`} name="m" onClick={()=>(setGoogleMapType('m'))}>Streets</button>
      <button className={`map-setBtn${googleMapType==="s,h" ? ' active':''}`} name="s,h" onClick={()=>(setGoogleMapType('s,h'))}>Hybrid</button>
      <button className={`map-setBtn${googleMapType==="s" ? ' active':''}`} name="s" onClick={()=>(setGoogleMapType('s'))}>Satellite</button>
      <button className={`map-setBtn${googleMapType==="p" ? ' active':''}`} name="p" onClick={()=>(setGoogleMapType('p'))}>Terrain</button>
    </div>
  );
}
