import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import DopeRadioGroup from 'components/DopeForms/DopeRadioGroup';
import {DopeCheckbox} from 'components/DopeForms/DopeCheckbox/DopeCheckbox';
import {          
  Form,
  FormGroup,
} from 'reactstrap';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeTextarea } from 'components/DopeForms/DopeTextarea/DopeTextarea';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import ModalImg from '../../assets/img/dope-360-logo.svg';
import Ajv from 'ajv';
import { 
  resetListData,
  sendListDataRequest,
  setListDataType,
  toggleListDataFinalModal,
} from 'actions/listData';
import './ListData.scss';

export default function ListData(props) {    
  const dispatch = useDispatch();
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const listDataType = useSelector(state => state.listData.listDataType);
  const finalModalOpen = useSelector(state => state.listData.finalModalOpen);
  const ajv = new Ajv({ allErrors: true });

  const CreateListSchemaBusiness =  {
    "type": "object",
    "required": ["country", "state", "zip","typeBusiness","jobTitles","roles","count"],
    "properties": {
      "country": {
        "type": "string",
        "minLength": 0,        
      },
      "state": {
        "type": "string",
        "minLength": 1
      },
      "zip": {
        "type": "string",
        "minLength": 1
      },
      "typeBusiness": {
        "type": "string",
        "minLength": 1
      },
      "jobTitles": {
        "type": "string",
        "minLength": 1
      },
      "roles": {
        "type": "string",
        "minLength": 1
      },
      "count": {
        "type": "string",
        "minLength": 1
      },
      "comments": {
        "type": "string",
        "minLength": 1
      },
    }    
  }
  const CreateListSchemaResidential =  {
    "type": "object",
    "required": ["country", "state", "zip","homeOwner","income","homeValue","age","count"],
    "properties": {
      "country": {
        "type": "string",
        "minLength": 0,        
      },
      "state": {
        "type": "string",
        "minLength": 1
      },
      "zip": {
        "type": "string",
        "minLength": 1
      },
      "homeOwner": {
        "type": "string",
        "minLength": 1
      },
      "income": {
        "type": "array",
        "items": { "type": "string" },
        "minItems": 1
      },
      "homeValue": {
        "type": "array",
        "items": { "type": "string" },
        "minItems": 1
      },
      "age": {
        "type": "array",
        "items": { "type": "string" },
        "minItems": 1
      },
      "count": {
        "type": "string",
        "minLength": 1
      },      
      "comments": {
        "type": "string",
        "minLength": 1
      },
    }    
  }



// Homeowners/Renters/Both 




  useEffect(() => {    
    // console.log("ListData mount");
    return () => {      
      dispatch(resetListData());
    };
  }, []);

  useEffect(() => {         
    dispatch(resetListData());
  }, [currentAccountId]);

  useEffect(() => {         
    resetInternalState();     
  }, [listDataType]);

 
  const [validation, setValidation] = useState({});
  const [data, setData] = useState({}); 

  const onSubmit = (e)=>{  
    e.preventDefault();
    if(listDataType==="business"){
      let formattedForBE = {
        ...data,        
      }    
      let result = ajv.validate(CreateListSchemaBusiness, formattedForBE);
      if(result) {                
        let requestData = {
          body:{
            ...formattedForBE,
            account: currentAccountId,
            type: listDataType, 
          }          
        };
        dispatch(sendListDataRequest(requestData));
      }    
    }else{  
      let income=[],homeValue=[], age=[];
      if (data.income) income = [...data.income];
      if (data.homeValue) homeValue = [...data.homeValue];
      if (data.age) age = [...data.age];
      
      let formattedForBE = {
        ...data,
        income: income,
        homeValue: homeValue,
        age: age,        
      }        
      console.log("formattedForBE", formattedForBE);
      let result = ajv.validate(CreateListSchemaResidential, formattedForBE);
      if(result) {       
        let requestData = {
          body:{
            ...formattedForBE,
            account: currentAccountId,
            type: listDataType,
          }
        };
        dispatch(sendListDataRequest(requestData));
      }
    }
    if(ajv.errors != null) {
      const newValidation = {        
      ...ajv.errors.map(error => {
          const errorKey = error.params.missingProperty || error.dataPath.split('.').pop();
          validation[errorKey] = 'This field is required';
        })
      };        
      setValidation({
        ...validation,
        ...newValidation
      });
    }
  }

  const onChange = (e) => {      
    setValidation({ 
      ...validation,
      [e.target.name]: null
    });        
    setData({
      ...data,
      [e.target.name]: e.target.value
    })
    // dispatch(setListDataData(e.target.name,e.target.value));    
  }

  const onChangeCheckbox = (name, value) => {
    setValidation({ 
      ...validation,
      [name]: null
    });    
    const updatedSet = new Set(data[name]);
    if (updatedSet.has(value)) {
        updatedSet.delete(value);
    } else {
      updatedSet.add(value);
    }
    setData({
        ...data,
        [name]: updatedSet
    })
  }

  const onRadioGroupChange = (name, value) => {
    setValidation({ 
      ...validation,
      [name]: null
    });     
    setData({
      ...data,
      [name]: value
    })
  }

  const getValidationMessage = (name) => {    
    if (validation&&validation[name]) {
        return (<div className='validation-message'>
          {validation[name]}
        </div>)
    }
  } 

  const resetInternalState = ()=>{
    if(listDataType==="residential"){      
      setData({        
        homeOwner: 'owner',
      });
    }
    if(listDataType==="business"){
      setData({});
    }
    setValidation({}); 
  }
  
  const submitListDataFinalModal = () => {
    resetInternalState();
    dispatch(resetListData());    
  }
  
  return (        
    <div className="ListData"> 
      <div className="title-container">
        <h1 className="title">Get List Data</h1>        
      </div>           
      <div className="listDataCard">
        <div className="cardTitleContainer t-shadow">
          <div className="cardTitle">What kind of list do you want?</div>
        </div>
        <div className="cardBody">
          <DopeRadioGroup
            data={[                
              {
                value: "business",
                label: "Business",                  
              },
              {
                value: "residential",
                label: "Residential",                  
              }
            ]}
            name="listDataType"               
            selectedValue={listDataType}
            onChange={e=>dispatch(setListDataType(e))}              
          />
        </div>
      </div>
      {listDataType==='business'&&(
        <div className="listDataRequestCard">  
          <Form onSubmit={e=>onSubmit(e)}>                                           
            <div className='form-section'>
              <div className="list-grid">
                <FormGroup className="dope-form-group">
                  <DopeInput                        
                      supText='*'
                      title="How many contacts are you looking for?"
                      value={data.count||''}
                      name="count"
                      placeholder={'Count of contacts'}
                      onChange={onChange}
                      error={validation.count || ''}
                  />                                    
                </FormGroup>
              </div>
              <div className="list-grid">              
                <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="Country"
                        value={data.country||''}
                        name="country"
                        placeholder={'Country'}
                        onChange={onChange}
                        error={validation.country || ''}
                    />                                    
                </FormGroup>
                <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="State"
                        value={data.state||''}
                        name="state"
                        placeholder={'State'}
                        onChange={onChange}
                        error={validation.state || ''}
                    />                                    
                </FormGroup>
                <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="ZIP"
                        value={data.zip||''}
                        name="zip"
                        placeholder={'Zip'}
                        onChange={onChange}
                        error={validation.zip || ''}
                    />                                    
                </FormGroup>
              </div> 
              <div className="list-grid">
                <FormGroup className="dope-form-group">
                    <DopeInput
                        supText='*'                      
                        title="Type of business"
                        value={data.typeBusiness||''}
                        name="typeBusiness"
                        placeholder={'Type of Business'}
                        onChange={onChange}
                        error={validation.typeBusiness || ''}
                    />                                    
                </FormGroup>
                <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="Job Titles"
                        value={data.jobTitles||''}
                        name="jobTitles"
                        placeholder={'Job Titles'}
                        onChange={onChange}
                        error={validation.jobTitles || ''}
                    />                                    
                </FormGroup>
                <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="Roles"
                        value={data.roles||''}
                        name="roles"
                        placeholder={'Roles'}
                        onChange={onChange}
                        error={validation.roles || ''}
                    />                                    
                </FormGroup>
              </div> 
              <div className="list-grid"> 
                <FormGroup className="dope-form-group">
                    <DopeTextarea                        
                        title="Any notes or comments to add to your order?"
                        value={data.comments||''}
                        name="comments"
                        placeholder={'Comments'}
                        onChange={onChange}
                        error={validation.comments || ''}
                    />                          
                </FormGroup> 
              </div>
            </div>            
          </Form>
        </div>
      )}
      {listDataType==='residential'&&(
        <div className="listDataRequestCard">  
          <Form onSubmit={e=>onSubmit(e)}>                                           
            <div className='form-section'>
              <div className="list-grid">                
                <FormGroup className="dope-form-group">
                  <DopeInput                        
                    supText='*'
                    title="How many contacts are you looking for?"
                    value={data.count||''}
                    name="count"
                    placeholder={'Count of contacts'}
                    onChange={onChange}
                    error={validation.count || ''}                          
                  />                                    
                </FormGroup>                 
              </div>
              <div className="list-grid">
                <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="Country"
                        value={data.country||''}
                        name="country"
                        placeholder={'Country'}
                        onChange={onChange}
                        error={validation.country || ''}
                    />                                    
                </FormGroup>
                <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="State"
                        value={data.state||''}
                        name="state"
                        placeholder={'State'}
                        onChange={onChange}
                        error={validation.state || ''}
                    />                                    
                </FormGroup>
                <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="ZIP"
                        value={data.zip||''}
                        name="zip"
                        placeholder={'Zip'}
                        onChange={onChange}
                        error={validation.zip || ''}
                    />                                    
                </FormGroup>
              </div> 
              <div className="list-grid">                        
                {/* <FormGroup className="dope-form-group">
                    <DopeInput
                        supText='*'                      
                        title="Home owner"
                        value={data.homeOwner||''}
                        name="homeOwner"
                        placeholder={'Home owner'}
                        onChange={onChange}
                        error={validation.homeOwner || ''}
                    />                                    
                </FormGroup> */}
                <FormGroup className="dope-form-group">
                  <h4 className="checkboxes-title">Type homeOwner<sup>*</sup></h4>
                  <DopeRadioGroup
                    data={[                
                      {
                        value: "owner",
                        label: "Owner",                  
                      },
                      {
                        value: "renter",
                        label: "Renter",                  
                      },
                      {
                        value: "both",
                        label: "Both",                  
                      }
                    ]}
                    name="homeOwner"                                                  
                    selectedValue={data.homeOwner||'owner'}
                    onChange={(e)=>onRadioGroupChange("homeOwner", e)}              
                  />
                  {getValidationMessage("homeOwner")}
                </FormGroup>
              </div> 
              <div className="list-grid">
                {/* <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="Income"
                        value={data.income||''}
                        name="income"
                        placeholder={'Income'}
                        onChange={onChange}
                        error={validation.income || ''}
                    />                                    
                </FormGroup> */}
                <FormGroup className="dope-form-group">
                  <h4 className="checkboxes-title">Income <sup>*</sup></h4>
                  <div className="checkboxes-group">
                    <label>                     
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.income&&data.income.has('1')||false}
                          value='1'
                          onChange={()=>onChangeCheckbox("income",'1')}
                      />
                      {`0 - $50k`}
                    </label>
                    <label>
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.income&&data.income.has('50000')||false}
                          value='50000'
                          onChange={()=>onChangeCheckbox("income",'50000')}
                      />
                      {`> $50k`}
                    </label>
                    <label>
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.income&&data.income.has('75000')||false}
                          value='75000'
                          onChange={()=>onChangeCheckbox("income",'75000')}
                      />
                      {`> $75k`}
                    </label>
                    <label>
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.income&&data.income.has('100000')||false}
                          value='100000'
                          onChange={()=>onChangeCheckbox("income",'100000')}
                      />
                      {`> $100k`}
                    </label>
                    <label>
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.income&&data.income.has('250000')||false}
                          value='250000'
                          onChange={()=>onChangeCheckbox("income",'250000')}
                      />
                      {`> $250k`}
                    </label>
                  </div>
                  {getValidationMessage("income")}
                </FormGroup>
                {/* <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="Home Value"
                        value={data.homeValue||''}
                        name="homeValue"
                        placeholder={'HomeValue'}
                        onChange={onChange}
                        error={validation.homeValue || ''}
                    />                                    
                </FormGroup> */}
                <FormGroup className="dope-form-group">
                  <h4 className="checkboxes-title">Home Value <sup>*</sup></h4>
                  <div className="checkboxes-group">
                    <label>                     
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.homeValue&&data.homeValue.has('100000')||false}
                          value='100000'
                          onChange={()=>onChangeCheckbox("homeValue",'100000')}
                      />
                      {`> $100k`}
                    </label>
                    <label>
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.homeValue&&data.homeValue.has('250000')||false}
                          value='250000'
                          onChange={()=>onChangeCheckbox("homeValue",'250000')}
                      />
                      {`> $250k`}
                    </label>
                    <label>
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.homeValue&&data.homeValue.has('500000')||false}
                          value='500000'
                          onChange={()=>onChangeCheckbox("homeValue",'500000')}
                      />
                      {`> $500k`}
                    </label>
                    <label>
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.homeValue&&data.homeValue.has('750000')||false}
                          value='750000'
                          onChange={()=>onChangeCheckbox("homeValue",'750000')}
                      />
                      {`> $750k`}
                    </label>
                    <label>
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.homeValue&&data.homeValue.has('1000000')||false}
                          value='1000000'
                          onChange={()=>onChangeCheckbox("homeValue",'1000000')}
                      />
                      {`> $1,000k`}
                    </label>
                  </div>
                  {getValidationMessage("homeValue")}
                </FormGroup>
                {/* <FormGroup className="dope-form-group">
                    <DopeInput                        
                        supText='*'
                        title="Age"
                        value={data.age||''}
                        name="age"
                        placeholder={'Age'}
                        onChange={onChange}
                        error={validation.age || ''}
                    />                                    
                </FormGroup>  */}
                <FormGroup className="dope-form-group">
                  <h4 className="checkboxes-title">Age <sup>*</sup></h4>
                  <div className="checkboxes-group">
                    <label>                     
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.age&&data.age.has('20')||false}
                          value='20'
                          onChange={()=>onChangeCheckbox("age",'20')}
                      />
                      {`20+`}
                    </label>
                    <label>                     
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.age&&data.age.has('30')||false}
                          value='30'
                          onChange={()=>onChangeCheckbox("age",'30')}
                      />
                      {`30+`}
                    </label>
                    <label>                     
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.age&&data.age.has('40')||false}
                          value='40'
                          onChange={()=>onChangeCheckbox("age",'40')}
                      />
                      {`40+`}
                    </label>
                    <label>                     
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.age&&data.age.has('50')||false}
                          value='50'
                          onChange={()=>onChangeCheckbox("age",'50')}
                      />
                      {`50+`}
                    </label>
                    <label>                     
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.age&&data.age.has('60')||false}
                          value='60'
                          onChange={()=>onChangeCheckbox("age",'60')}
                      />
                      {`60+`}
                    </label>
                    <label>                     
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.age&&data.age.has('70')||false}
                          value='70'
                          onChange={()=>onChangeCheckbox("age",'70')}
                      />
                      {`70+`}
                    </label>
                    <label>                     
                      <DopeCheckbox
                          className='dope-checkbox'
                          checked={data.age&&data.age.has('80')||false}
                          value='80'
                          onChange={()=>onChangeCheckbox("age",'80')}
                      />
                      {`80+`}
                    </label>
                  </div>
                  {getValidationMessage("age")}
                </FormGroup>
              </div>              
              <div className="list-grid">
                <FormGroup className="dope-form-group">
                    <DopeTextarea                        
                        title="Any notes or comments to add to your order?"
                        value={data.comments||''}
                        name="comments"
                        placeholder={'Comments'}
                        onChange={onChange}
                        error={validation.comments || ''}
                    />                                    
                </FormGroup> 
              </div>
            </div>            
          </Form>
        </div>
      )}
      <DopeButton type="submit" color="primary" onClick={e=>onSubmit(e)}>Submit</DopeButton>
      <DopeModal
        isOpen={finalModalOpen}
        toggle={e=>dispatch(toggleListDataFinalModal(!finalModalOpen))}
        className='list-data-final-modal'
        centered
      >
        <DopeModalBody>
          <div className='modal-img-container'>
            <img src={ModalImg} alt='logo' />
          </div>
          <p>Your request has been successfully sent!</p>
        </DopeModalBody>
        <DopeModalFooter>
          <DopeButton className='dope-button btn btn-primary' onClick={submitListDataFinalModal}>
            Ok
          </DopeButton>
        </DopeModalFooter>
      </DopeModal>
    </div>    
  );
}