import React, { Component } from 'react';
import { connect } from 'react-redux';
import withAuthCheck from 'components/utils/withAuthCheck';
import LoginForm from './LoginForm';
import Loader from 'components/Loader/Loader';
import LogRocket from 'logrocket';
import history from 'history.js';
import {   
  SIGN_UP_VERIFY_EMAIL,
} from 'utils/constants';
import './LogInPage.scss';

const loginErrorText = "Error in email or accountPhone, format validation failed:";

class Login extends Component {
  state = {
    data: {
      email: '',
      password: '',
    },
    errors: {
      email: '',
      password: '',
    },
    loading: false,
    alert: null,
    passwordVisibility: false,
  };

  componentDidMount() {        
    document.addEventListener('keydown', this.onKeyDown);
    this.checkEmailVerifiedStatus();    
  }

  getBool = (val) => {    
    if(val){
      return !!JSON.parse(String(val).toLowerCase());
    }
    else return false;  
  }

  resolveRedirectToVerifyEmailAccountPhone = () => {
    const user_metadata = this.props.auth.getUserMetadata();
    if(user_metadata){ 
      const status = user_metadata.status;   
      const emailVerified = this.getBool(user_metadata.emailVerified);
      const phoneVerified = this.getBool(user_metadata.phoneVerified);
      if( status === 'pending' && emailVerified === false && phoneVerified === false ) {          
        history.replace(SIGN_UP_VERIFY_EMAIL);
      } 
    }
  } 

  checkEmailVerifiedStatus = async () => {
    const user_metadata = this.props.auth.getUserMetadata();
    if(user_metadata){               
      const email = user_metadata.email;
      const accountPhone = user_metadata.accountPhone;                
      try {
        // autoredirect always to SIGN_UP_VERIFY_EMAIL if email not verified //put resolve function
        // this.props.auth.checkEmailPhoneVerifiedFromDb(email, this.resolveRedirectToVerifyEmailAccountPhone );
        if(email){
          this.props.auth.checkEmailPhoneVerifiedFromDb(email);
        } else if(accountPhone){
          this.props.auth.checkEmailPhoneVerifiedFromDb(accountPhone);
        }
      }
      catch (err) {
        console.log(err);
        LogRocket.captureException(err);
      }
    }
  }

  onKeyDown = e => {   // Why?? can check form submit
    if (
      e.keyCode === 13 &&
      this.state.data.email.length > 0 &&
      this.state.data.password.length > 0
    )
      this.login();
  };

  componentWillUnmount = () => {
    document.removeEventListener('keydown', this.onKeyDown);
  };

  validation = () => {
    // const { email, password } = this.state.data;
    const { email } = this.state.data;
    let errors = this.state.errors;    
    const emailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
    const accountPhoneRegex = /^\+?[0-9]{9,15}$/;     
    if( !emailRegex.test(email) && !accountPhoneRegex.test(email) ){ 
      
      errors.email = `${loginErrorText} ${email}`;
         
      this.setState({ errors });      
      return false;
    }
    this.setState({
      errors: {  
        ...this.state.errors, 
        email: '',      
        password: '',
      },
    });
    return true; 
  };

  errorHandle = (error) => {    
    this.setState({
      errors: {
        ...this.state.errors,
        ...error
      }
    });
  }

  login = async () => {
    if( (this.state.data.email === this.props.auth.getUserEmail()) || (this.state.data.email === this.props.auth.getUserAccountPhone()) ){  
      this.resolveRedirectToVerifyEmailAccountPhone();
    }
    if (this.validation()) {
      this.setState({ loading: true });
      try {        
        await this.props.auth.login(this.state.data.email.toLowerCase(), this.state.data.password, this.errorHandle);          
        this.setState({ loading: false});             
      } catch (err) {
        console.log("login err: ", err);
        // TODO CHECK THIS ERROR        
        this.setState({ loading: false});         
      }
    }
  };

  onChange = (key, value) => {    
    const data = {
      data: {
        ...this.state.data,
        [key]: value,
      },
      errors: {
        ...this.state.errors,
        [key]: '',
      },
    };
    this.setState({ ...data });
  };

  togglePasswordVisibility = () => {
    this.setState({ passwordVisibility: !this.state.passwordVisibility });
  };

  render() {        
    return (
      <React.Fragment>
        <div className='content'>
          <Loader show={this.state.loading} />
        </div>
        <LoginForm
          login={this.login}
          onChange={this.onChange}
          data={this.state.data}
          errors={this.state.errors}
          loading={this.state.loading}
          passwordVisibility={this.state.passwordVisibility}
          togglePasswordVisibility={this.togglePasswordVisibility}
          alert={this.state.alert}
          resetPassAlertMessage={this.props.resetPassAlertMessage}
        />
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => ({
  resetPassAlertMessage: state.auth.resetPassAlertMessage,  
});

export default connect(mapStateToProps)(withAuthCheck(Login));
