import React from 'react';
import { withRouter } from 'react-router-dom';
import LogRocket from 'logrocket';
import {
  Card,
  CardBody,
  Button,
  Input,
  Form,
  FormGroup,
  Label,
  CardHeader,
  CardTitle,
  Table
} from 'reactstrap';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import request from '../utils/request';
import { connect } from 'react-redux';
import { API_URL } from '../config/config';
// var config = require('../config/config.json');

// const accountID = config.testAccountID;

const header = [
  { title: 'Name', prop: 'name', sortable: true, filterable: true },
  { title: 'Type', prop: 'type', sortable: true, filterable: true }
];

const detailedHeader = ['name', 'type'];

class Contact extends React.Component {
  state = {
    data: {},
    addModal: false,
    addModalData: { jobs: [{}] },
    viewModal: false,
    viewModalData: {},
    callTrackingModalOpen: false,
    listRequests: [],
    includedListRequests: []
  };

  async componentDidMount() {
    try {
      let response = await request(
        `${API_URL}/contact/${this.props.match.params.id}`,
        { method: 'get', queries: { account: this.props.currentAccount._id } }
      );
      let data = response.data;
      this.setState({ data });
    } catch (err) {
      LogRocket.captureException(err);
      console.log(err);
    }
  }

  // onAddToggle() {
  //     // let addModalData = JSON.parse(JSON.stringify(this.state.data));
  //     // addModalData.jobs.push({});
  //     this.setState({ addModal: !this.state.addModal, addModalData: {} });
  // }

  onViewToggle(elm) {
    this.setState({
      viewModal: !this.state.viewModal,
      viewModalData: elm ? elm : {}
    });
  }

  render() {
    return (
      <div>
        <Card>
          <CardBody style={{ fontSize: '15px', textAlign: 'center' }}>
            <div
              style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center'
              }}
            >
              <p style={{ color: 'grey', marginRight: 10 }}>Name:</p>
              {this.state.data.name}
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center'
              }}
            >
              <p style={{ color: 'grey', marginRight: 10 }}>Description: </p>
              {this.state.data.description}
            </div>
          </CardBody>
        </Card>
        {this.state.data.jobs && (
          <Card>
            <CardHeader>
              <CardTitle tag="h4">Jobs</CardTitle>
            </CardHeader>
            <CardBody>
              <Table responsive hover>
                <thead className="text-primary">
                  <tr>
                    {header.map((prop, key) => (
                      <th key={key}>{prop.title}</th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {typeof this.state.data.jobs === 'object' &&
                    this.state.data.jobs.map((prop, key) => (
                      <tr key={key} onClick={() => this.onViewToggle(prop)}>
                        <td>{prop.name}</td>
                        <td>{prop.type}</td>
                      </tr>
                    ))}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        )}
        <div style={{ textAlign: 'center' }}>
          <Button
            onClick={(e) =>
              this.props.history.push(
                this.props.location.pathname + '/create-job'
              )
            }
          >
            Add Job
          </Button>
        </div>
        <DopeModal isOpen={this.state.viewModal} toggle={() => this.onViewToggle()}>
          <DopeModalHeader>Detailed Info</DopeModalHeader>
          <Form style={{ padding: 10 }}>
            {Object.keys(this.state.viewModalData).map(
              (key) =>
                detailedHeader.includes(key) && (
                  <FormGroup key={key}>
                    <Label>{key}</Label>
                    <Input disabled value={this.state.viewModalData[key]} />
                  </FormGroup>
                )
            )}
            {this.state.viewModalData.mailTracking &&
              this.state.viewModalData.mailTracking.enabled && (
                <div style={{ textAlign: 'center' }}>
                  <p
                    style={{ color: 'grey', marginBottom: 5, fontSize: '15px' }}
                  >
                    List requests
                  </p>
                  {this.state.viewModalData.mailTracking.lists.map(
                    (list, index) => (
                      <div key={index}>{list.name}</div>
                    )
                  )}
                </div>
              )}
            {this.state.viewModalData.callTracking &&
              this.state.viewModalData.callTracking.enabled && (
                <div style={{ textAlign: 'center' }}>
                  <p
                    style={{ color: 'grey', marginBottom: 5, fontSize: '15px' }}
                  >
                    Call tracking
                  </p>
                  <div>
                    Tracking number:{' '}
                    {this.state.viewModalData.callTracking.trackingNumber &&
                      this.state.viewModalData.callTracking.trackingNumber
                        .number}
                  </div>
                  <div>
                    Forwarding number:{' '}
                    {this.state.viewModalData.callTracking.forwardingNumber}
                  </div>
                </div>
              )}
            {this.state.viewModalData.onlineAdvertising &&
              this.state.viewModalData.onlineAdvertising.enabled && (
                <div style={{ textAlign: 'center' }}>
                  <p
                    style={{ color: 'grey', marginBottom: 5, fontSize: '15px' }}
                  >
                    Online Advertising
                  </p>
                  <div>
                    Website URL:{' '}
                    {this.state.viewModalData.onlineAdvertising.websiteUrl}
                  </div>
                  <div>
                    Facebook Business Page:{' '}
                    {
                      this.state.viewModalData.onlineAdvertising
                        .facebookBusinessPage
                    }
                  </div>
                  <div>
                    Notes: {this.state.viewModalData.onlineAdvertising.notes}
                  </div>
                </div>
              )}
          </Form>
        </DopeModal>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  currentAccount: state.account.currentAccount
});

export default connect(mapStateToProps)(withRouter(Contact));
