import React, { useEffect, useState } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import EddmRouteMap from './EddmRouteMap';
import EddmEditor from './EddmEditor';
import EddmSummary from './EddmSummary';
import { ReactComponent as LeftArrowIcon } from 'assets/img/icons/chevron-left.svg';
import {   
  setNextStep,
  resetCreateEddm,
  getEddmItemRequest,
  getRoutesStatByZipRequest,
  } from 'actions/eddm';
import { useSelector, useDispatch } from 'react-redux';

import './EddmCreate.scss';

export default function EddmCreate({savedAccountId}) {

  const { eddmId, typePreview } = useParams();
  // console.log("typePreview:", typePreview);
  const dispatch = useDispatch();
  const history = useHistory();
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const zipFind = useSelector(state => state.eddm.eddmRouteMap.zipFind); 
  const [showAddingCard, setShowAddingCard] = useState(false);
  const mapPositionIsLoading = useSelector(state => state.eddm.eddmRouteMap.mapPositionIsLoading);
  
  useEffect(() => {  
    dispatch(resetCreateEddm());//test
    // console.log("EddmRouteMap Mount");
    // console.log("typePreview:", typePreview, "eddmId:", eddmId);  
   
    if(eddmId!=='create'){      
      // (TODO send request (SAGA) here for data from BE about route/postcard/servicess etc...and synchronize Redux from these responce data)
      // TODO send there request with eddmId to BE for access templateDATA and fill redux state for templateEdit
      // templateEdit:{
      //   front: '<div id="postcard_front"> ... </div>',
      //   back: '<div id="postcard_back"> ... </div>',
      // },
      const data = {
        sendingId: eddmId,
      };
      dispatch(getEddmItemRequest(data));
      // dispatch(setNextStep(1));
    }
    
    return () => {
      dispatch(resetCreateEddm());
    };
  },[]);

  useEffect(() => {
    if((savedAccountId !== currentAccountId) && (savedAccountId !== '')){      
      history.push('/eddm');            
    }    
  }, [currentAccountId]);

  useEffect(() => {  
    if(eddmId!=='create'){ 
      // data from backend
      const data = {
        query: {
          zip: zipFind
        },
      };
      dispatch(getRoutesStatByZipRequest(data));
    } 
  },[zipFind]);  
  
  const configs = useSelector(state => state.dbConfigs.configs);
  const step = useSelector(state => state.eddm.step);  
  const featureCollection = useSelector(state => state.eddm.eddmRouteMap.featureCollection);
  const addressesType = useSelector(state => state.eddm.addressesType);
  const ownPrice = useSelector(state => state.general.ownPrice);
  
  const getEddmPostcardPrices = () => {
    const defaultPostcardPrice = configs.filter(item => item.name === 'shippingServices')[0].value;
    const customPostcardPrice = ownPrice?ownPrice.eddmPrice:null;
    // console.log("defaultPostcardPrice", defaultPostcardPrice);
    // console.log("customPostcardPrice", customPostcardPrice);
    
    if (
      customPostcardPrice&&customPostcardPrice.length&&
      Object.values(customPostcardPrice).some(arr => Object.values(arr.price).some(arr => arr.length))
    ){           
      return customPostcardPrice;
    }    
    return defaultPostcardPrice;
  };

  const back = () => {
    dispatch(setNextStep(step-1));
  }

  const totalRouteAddr = (routeId)=>{
    // let route=featureCollection.features.find((el)=>(el.properties.zipcrid===routeId));
    let route=featureCollection.features.find((el)=>(el.attributes.ZIP_CRID===routeId));
    if(route){
      if(addressesType === 'residential'){
        // return route.properties.residential||0
        return route.attributes.RES_CNT||0
      }else if(addressesType === 'business'){
        // return route.properties.business||0
        return route.attributes.BUS_CNT||0
      }else {
        // return ( (route.properties.business||0) + (route.properties.residential||0) )
        return ( (route.attributes.BUS_CNT||0) + (route.attributes.RES_CNT||0) )
      }
    }else return 0;
  }

  const calcTotalSelectedRouteAddr = (selRts)=>{
    let totalSum = selRts.reduce((acc, cur)=>{
      return acc + totalRouteAddr(cur)
    },0);
    return totalSum
  }

  return (    
    <div className={`EddmCreate${showAddingCard?" addCard":""}`}>
      {/* STEP 1 */}
      {
        step===1 && (
          <React.Fragment>
            <div className='back-btn-container'>
              <button className='back-btn' onClick={()=> history.push('/eddm')} disabled={mapPositionIsLoading}>
                <LeftArrowIcon className="back-icon"/>
                Back to EDDM'S
              </button>
            </div>
            <EddmRouteMap
              totalRouteAddr={totalRouteAddr}
              calcTotalSelectedRouteAddr={calcTotalSelectedRouteAddr}
              eddmPostcardPrice={getEddmPostcardPrices()}
            />
          </React.Fragment>
        )
      }
      {/* END STEP 1 */}

      {/* STEP 2 */}
      {step === 2 && (
        <React.Fragment>
          <div className='back-btn-container'>
            <button className='back-btn' onClick={e=>back(e)} >
              <LeftArrowIcon className="back-icon"/>
              Back to routes
            </button>
          </div>
          <EddmSummary            
            calcTotalSelectedRouteAddr={calcTotalSelectedRouteAddr}
            totalRouteAddr={totalRouteAddr}
            typePreview={typePreview}
            eddmId={eddmId}
            eddmPostcardPrice={getEddmPostcardPrices()}
          />          
        </React.Fragment>
      )}        
      {/* END STEP 2 */}

      {/* STEP 3 */}
      {step === 3 && (
        <React.Fragment>
          {!showAddingCard&&
            (
              <div className='back-btn-container'>
                <button className='back-btn' onClick={e=>back(e)} >
                  <LeftArrowIcon className="back-icon"/>
                  Back
                </button>
              </div>
            )
          }
          <EddmEditor
            eddmId={eddmId}
            typePreview={typePreview}
            totalRouteAddr={totalRouteAddr}
            eddmPostcardPrice={getEddmPostcardPrices()}
            showAddingCard={showAddingCard}
            setShowAddingCard={setShowAddingCard}
            // data={this.dataBackMod(data)}
            // front_id={postcard_front_id}
            // back_id={postcard_back_id}
            // errors={errors}
            // addErrors={this.props.addErrorsPostCard}
            // changePostCardStatus={this.props.changePostCardStatus}
            // allPostcardsTemplate={allPostcardsTemplate}
            // postcardTemplatesData={postcardTemplatesData}
            // templateSample = {{...this.dataBackMod(data).template}}            
            // onSelectTemplateBack={this.props.selectPostCardTemplateBack}  
            // setUserPostcardImages={this.props.setUserPostcardImages}         
          />
        </React.Fragment>
      )}
      {/* END STEP 3 */}

    </div>
  );
}
