import React from "react";
import { ReactComponent as HelpIcon } from 'assets/img/icons/help.svg';
import ReactTooltip from "react-tooltip";

const withTooltip = (title) => {
  return (
    <>
      {title}
      <span className="itemTitleTooltips" data-tip data-for={`tooltips-residential`}><HelpIcon/></span>
      <ReactTooltip
        className="res-bus-tooltip"
        html={true}
        id={`tooltips-residential`}
      >
        {`<span>Click here for Residential vs Business</span>`}
      </ReactTooltip>
    </>
  );
};


export const columnsFunc = (onFilterAddressType, openIncomePopup) => [
  {
    dataField: 'route',
    text: 'Route',
  },
  {
    dataField: 'residential',
    text: 'Residential',
    headerEvents: {
      onClick: onFilterAddressType
    },
    headerClasses: 'hasModal',
    headerFormatter: () => withTooltip('Residential')
  },
  {
    dataField: 'business',
    text: 'Business',
    headerEvents: {
      onClick: onFilterAddressType
    },
    headerClasses: 'hasModal',
    headerFormatter: () => withTooltip('Business')
  },
  {
    dataField: 'total',
    text: 'Total'
  },
  {
    dataField: 'income',
    text: 'Average income',
    headerClasses: 'hasModal',
    headerEvents: {
      onClick: openIncomePopup
    },
  },
  // {
  //   dataField: 'cost',
  //   text: 'Postage cost',
  // },
];

