import { ON_CHANGE_ONBOARDING, 
  GET_PACKAGE_LIST_FOR_SIGN_UP_REQUEST_SUCCESS,
  // ONBOARDING_ACCOUNT_REQUEST_FAILED,
} from '../actions/signUp';

let initialState = {
  step: 1,
  loading: false,
  person: {
    firstName: '',
    lastName: '',
  },
  name: '',
  logo: null,
  companyAddress: {
    state: '',
    city: '',
    zip: '',
    zip4: '',
    address1: '',
    address2: '',
  },
  phone: '',
  packageId: '',
  cardHolder: '',
  // tokenId: null,  
  // card: {},
  errors: {},
  scrollFlag: false,
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case ON_CHANGE_ONBOARDING: {
      if (action.group) {
        return {
          ...state,
          [action.group]: {
            ...state[action.group],
            [action.key]: action.value,
          },
        };
      } else {
        return {
          ...state,
          [action.key]: action.value,
        };
      }
    }
    case GET_PACKAGE_LIST_FOR_SIGN_UP_REQUEST_SUCCESS: {
      return {
        ...state,
        packageList: action.data
      }
    }
    // case ONBOARDING_ACCOUNT_REQUEST_FAILED: {
    //   return {
    //     ...state,
    //   }
    // }    
    default:
      return state;
  }
}
