import React, { useEffect } from 'react';
import LoadingOverlay from 'react-loading-overlay';
import disableScroll from 'disable-scroll';
import './Loader.scss';

export default function Loader(props) {  
  useEffect(() => {
    disableScroll.on();

    if (!props.show) {
      disableScroll.off();
    }

    return () => {
      disableScroll.off();
    };
  });

  if (props.show)
    return <LoadingOverlay active={true} spinner text={props.text||'Please, wait'}/>;
  return null;
}
