import { all, call, put, takeLatest } from 'redux-saga/effects'

import * as JobsActions from '../actions/jobs'
import { createJobsApi, createPreviewPdfApi, createChangeStatusJobApi } from '../requests/jobs';
import saveAs from 'save-as';
import { toast } from 'react-toastify'

export function* createJobsRequest(action) {
  try {
    yield put({ type: JobsActions.JOBS_CHANGE_PAGINATION, key: 'isLoading', value: true })

    const response = yield call(createJobsApi, action.data)

    yield put({ type: JobsActions.CREATE_JOBS_REQUEST_SUCCESS, data: response.data })
    yield put({ type: JobsActions.JOBS_CHANGE_PAGINATION, key: 'isLoading', value: false })
  } catch(e) {
    yield put({ type: JobsActions.CREATE_JOBS_REQUEST_FAILED, error: e })
    yield put({ type: JobsActions.JOBS_CHANGE_PAGINATION, key: 'isLoading', value: false })
  }
}

export function* createPreviewPdf(action) {
  try {
    const response = yield call(createPreviewPdfApi, action.data)
    yield put({ type: JobsActions.CREATE_PREVIEW_PDF_REQUEST_SUCCESS, data: response.data })

    saveAs(response.data)

  } catch(e) {
    yield put({ type: JobsActions.CREATE_PREVIEW_PDF_REQUEST_FAILED, error: e })
  }
}

export function* createChangeStatusJob(action) {
  try {
    yield put({ type: JobsActions.JOBS_CHANGE_PAGINATION, key: 'isLoading', value: true })
    yield call(createChangeStatusJobApi, action.data)
    yield put({ type: JobsActions.JOBS_CHANGE_PAGINATION, key: 'isLoading', value: false })
    yield put({ type: JobsActions.CHANGE_STATUS_JOB, payload: action.newJobs })

    toast.success('Order status changed')
  } catch(e) {
    yield put({ type: JobsActions.CREATE_PREVIEW_PDF_REQUEST_FAILED, error: e })
    toast.error('Order status not changed')
  }
}

export default all([
    takeLatest(JobsActions.CREATE_JOBS_REQUEST, createJobsRequest),
    takeLatest(JobsActions.CREATE_PREVIEW_PDF_REQUEST, createPreviewPdf),
    takeLatest(JobsActions.CREATE_CHANGE_STATUS_JOB_REQUEST, createChangeStatusJob),
])
