import React from 'react';
import Select from 'react-select';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import Ajv from 'ajv';
import { AccountSchema } from './config';
import { name } from 'utils/ajvProperties';
import {DopeCheckbox} from 'components/DopeForms/DopeCheckbox/DopeCheckbox';
import { Label, FormGroup } from 'reactstrap';
import PerfectScrollbar from 'react-perfect-scrollbar';
import 'react-perfect-scrollbar/dist/css/styles.css';
import './MatchingContacts.scss';

const ajv = new Ajv({ allErrors: true });

export default function MatchingContacts(props) {
  

  const headerFirstRowChange = () => {
    props.onChangeUploadCsvData('isFirstRowHeader', !props.uploadCsvData.isFirstRowHeader)}

  const allLists = [...props.allLists];
  const selectedHeaders = [...props.uploadCsvData.headers];

  if (
    (allLists[0] && allLists[0]._id !== 'createNewList') ||
    allLists.length === 0
  )
    allLists.unshift({ name: 'Create a New List', _id: 'createNewList' });

  const getColumnOptions = (value) => {
    return props.allHeaders.reduce((prev, current) => {
      if (current){
        if(selectedHeaders.includes(current.name) && current.name !== value) return prev;
        return [...prev, { value: current.name, label: current.label }]
      }
      return [...prev, { value: '', label: 'Skip Column' }];
    }, []);
  };

  const getSelectedColumn = value => {
    const filtered = getColumnOptions().filter(item => item.value === value);

    if (filtered.length) return filtered[0];
  };

  const getListsOptions = () => {
    return allLists.map(item => ({ value: item._id, label: item.name }));
  };

  const getSelectedList = value => {
    const filtered = getListsOptions().filter(item => item.value === value);

    if (filtered.length) return filtered[0];
  };

  const validation = () => {
    const formattedForBE = {
      ...props.uploadCsvData,
    };

    let schema = { ...AccountSchema };

    if (props.uploadCsvData.listId !== 'createNewList') {
      delete schema.properties.name;
      schema.required.forEach((item, i) => {
        if (item === 'name') schema.required.splice(i, 1);
      });
    } else if (!schema.properties.name) {
      schema.properties.name = { ...name.name };
      schema.required.push('name');
    }

    let result = ajv.validate(schema, formattedForBE);
    const errors = {};

    if (!result) {
      ajv.errors.map(error => {
        const errorKey = error.dataPath.split('.').pop();
        errors[errorKey] = 'This field is required!';
      });

      props.requiredHeaders.forEach((header, i) => {
        if (!~selectedHeaders.indexOf(header)) {
          errors.headers = 'This field is required!';
        }
      });

      props.addUploadCsvErrors(errors);
      return false;
    } else {
      let res = true;
      props.requiredHeaders.forEach(header => {
        if (!~selectedHeaders.indexOf(header)) {
          errors.headers = 'This field is required!';
          res = false;
        }
      });

      if (errors.headers) props.addUploadCsvErrors(errors);
      return res;
    }
  };

  const submit = () => {
    if (!validation()) return;
    props.onSubmit();
  };

  const csvJsonLength = Object.keys(props.uploadCsvData.csvJson[0]).length||7;  
  let emptyFields = [...Array(csvJsonLength).keys()].reduce((acc,cur,i)=>({...acc,[`field${i+1}`]:""}),{});
  // console.log("emptyFields:", emptyFields);
  return (
    <div className='matching-contact'>
      <h2>Upload my list</h2>
      <div className='matching-contact-container'>
        <div className='matching-contact-header'>
          <div className='one-line'>
            <FormGroup className={`dope-form-group ${props.errors.listId
                ? 'select-error'
                : ''}`}              
            >
              <span className='input-title'>
                Add Uploaded Contacts to List <sup>*</sup>
              </span>
              <Select
                className="dope-select"
                classNamePrefix="dope"
                value={getSelectedList(props.uploadCsvData.listId)}
                options={getListsOptions()}
                placeholder='No lists chosen'
                menuPortalTarget={document.body}
                styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }}                
                onChange={item => {
                  let errors = props.errors;
                  errors.listId = '';
                  errors.name = '';
                  props.addUploadCsvErrors(errors);
                  props.onChangeUploadCsvData('listId', item.value);
                  props.onChangeUploadCsvData('name', '');
                }}
              />
            </FormGroup>
            <FormGroup className="dope-form-group">
              {props.uploadCsvData.listId === 'createNewList' && (
                <DopeInput
                  value={props.uploadCsvData.name || ''}
                  title='New List Name'
                  supText='*'
                  name='name'
                  error={props.errors.name || ''}
                  onChange={e => {
                    let errors = props.errors;
                    errors.name = '';
                    props.addUploadCsvErrors(errors);
                    props.onChangeUploadCsvData('name', e.target.value);
                  }}
                />
              )}
            </FormGroup>
          </div>
          <p className={`${props.errors.headers ? 'error-text' : ''}`}>
            Pair the columns in the uploaded file with Dope information. You
            must select First Name, Last Name, Address 1.
          </p>
          <div className="header-label-container">
            <Label className="header-label">
              <DopeCheckbox
                checked={props.uploadCsvData.isFirstRowHeader}
                onChange={headerFirstRowChange}
              />
              The first row of the file contains column names
            </Label>
          </div>          
        </div>
        <PerfectScrollbar>
          <div className='matching-contact-body'>
            {props.uploadCsvData.csvJson && (
              <div>
                {props.uploadCsvData.csvJson.map((item, i) => {
                  return (
                    <React.Fragment key={`${i}row`}>
                      {i === 0 ? (
                        <div className='csv-row'>
                          {Object.keys({...emptyFields,...item}).map((key, i) => {
                            return (
                              <div key={`${i}cell`} className={`csv-cell ${!selectedHeaders[i]? 'csv-cell-skip' : '' }`}>
                                <Select
                                  className="dope-select select-match-column"
                                  classNamePrefix="matchingContacts"
                                  value={getSelectedColumn(selectedHeaders[i])}
                                  options={getColumnOptions(selectedHeaders[i])}
                                  menuPortalTarget={document.body}
                                  styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }}
                                  isClearable={true}
                                  onChange={item => {
                                    let headers = [...selectedHeaders];
                                    headers.forEach((x, y) => {
                                      if (item && x === item.value) headers[y] = '';
                                    });
                                    headers[i] = item ? item.value : '';
                                    props.onChangeUploadCsvData(
                                      'headers',
                                      headers,
                                    );
                                    let errors = { ...props.errors };
                                    errors.headers = '';
                                    props.addUploadCsvErrors(errors);
                                  }}
                                />
                              </div>
                            );
                          })}
                        </div>
                      ) : (
                        ''
                      )}
                      <div key={`${i}row2`} className={`csv-row ${i === 0 && props.uploadCsvData.isFirstRowHeader ? 'csv-row-header': ''}`}>
                        { 
                        Object.values({...emptyFields,...item}).map((value, i) => {
                          // console.log("item:", item);
                          return (
                            <div key={i} className={`csv-cell ${ (i >= csvJsonLength) ? " ignored" : ''}`}>                            
                              {value}
                            </div>
                          );
                        })}
                      </div>
                    </React.Fragment>
                  );
                })}
              </div>
            )}
          </div>
        </PerfectScrollbar>
        <div className='matching-contacts-footer'>
          <DopeButton color='white' onClick={props.toggleMatchingPage}>
            Back
          </DopeButton>
          <DopeButton color='primary' onClick={submit}>
            Upload
          </DopeButton>
        </div>
      </div>
    </div>
  );
}
