import React from 'react';
import { DopeButton } from 'components/DopeForms';

export default function ButtonsBlock({
  onClose,
  onEdit,
  onDraftEdit,
  onCreate,
  onDraftCreate,
  viewPageType,  
}) {
  return (
    <div className='sending-page-btn'>
      <DopeButton
        color={`${viewPageType === 'view' ? 'primary' : 'white'}`}
        onClick={onClose}
      >
        Cancel
      </DopeButton>

      {viewPageType === 'edit' && (
        <React.Fragment>
          <DopeButton color='white' onClick={() => onDraftEdit('draft')}>
            Save as Draft
          </DopeButton>
          <DopeButton color='primary' onClick={() => onEdit('confirmed')}>
            Save
          </DopeButton>
        </React.Fragment>
      )}

      {viewPageType === 'create' ? (
        <React.Fragment>
          <DopeButton color='white' onClick={() => onDraftCreate('draft')}>
            Save as Draft
          </DopeButton>
          <DopeButton color='primary' onClick={() => onCreate('confirmed')}>
          {/* <DopeButton color='primary' onClick={() => openPostcardApprovalModal()}> */}
            Confirm
          </DopeButton>
        </React.Fragment>
      ) : null}
    </div>
  );
}
