import React from 'react';
import {
    Card,
    CardBody,
    CardHeader,
    FormGroup,
    Label,
    FormFeedback,
    InputGroup,
    InputGroupAddon,
    // Button,
} from 'reactstrap';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import './OrderSummary.scss';
import {DopeInput, } from 'components/DopeForms/DopeInput/DopeInput'

export default function OrderSummary(props) {    
    const onChangeName = (e) => {
        props.setOrderName(e.target.value);
        props.addErrors('name', '')
    }
    return (
        <Card className='postcard-order-summary'>
            <CardHeader>{props.children}</CardHeader>
            <CardBody>
                <div className='order-summary-total'>
                    Number of Postcards: {props.amount || 0}
                </div>
                <div className='order-summary-total'>Cost: ${props.cost}</div>

                <FormGroup>
                    <Label className='order-summary-label' for='amount'>
                        Do you have coupon?
                    </Label>
                    <InputGroup>                        
                        <DopeInput
                            onChange={e => props.onCouponChange(e.target.value)}
                            invalid={props.isCouponValid === false}
                            valid={props.isCouponValid === true}
                            value={props.couponVal}
                        />                 
                        <InputGroupAddon addonType='append'>
                            <DopeButton color="primary"                                
                                onClick={() => {
                                    props.useCoupon();
                                }}
                            >
                                Use
                            </DopeButton>
                        </InputGroupAddon>  
                    </InputGroup>
                    <FormFeedback valid>
                        Your coupon has been applied
                    </FormFeedback>
                    <FormFeedback>Your coupon is invalid</FormFeedback> 
                </FormGroup>
                <div className='order-summary-total'>Total: ${props.total}</div>
                <div className="field">
                    <DopeInput
                        placeholder="order name"
                        value={props.orderName}
                        onChange={onChangeName}
                        error={props.errors.name}
                    />
                </div>
            </CardBody>
        </Card>
    );
}
