export default function reducer(state = initialState, action) {
  switch (action.type) { 
    case RESET_AUTOMATION:
      return {
        ...initialState,
      }; 
    case RESET_AUTOMATION_LIST:
      return {
        ...state,        
        automationList:{
          ...initialState.automationList,
        }
      };  
    case RESET_AUTOMATION_PREVIEW:
      return {
        ...state,
        automationPreview: initialState.automationPreview,        
      };
    case GET_AUTOMATION_LIST_REQUEST_SUCCESS:
      return {
        ...state,        
        automationList:{
          ...state.automationList,
          ...action.data,
        }, 
        isLoading: false
      };
    case GET_AUTOMATION_ITEM_SUCCESS:
      return {
        ...state,
        ...action.data,
        automationEditor:{
          ...state.automationEditor,
          templateEdit: action.templateEdit,
        },
        automationSummary:{
          ...state.automationSummary,
          ...action.automationSummary
        }
      };
    case SET_AUTOMATION_SUMMARY:
      return {
        ...state,       
        automationSummary:{
          ...state.automationSummary, 
          [action.key]: action.value,         
        }
      }
    case SET_SUMMARY_ANBH_COUNT:
      return {
        ...state,       
        automationSummary:{
          ...state.automationSummary, 
          neighborhoodCount: action.data.neighborhoodCount,
          customCount: action.data.customCount,
        }
      }
    case SET_PREVIEW_LOADING:
      return {
        ...state,       
        automationPreview:{
          ...state.automationPreview, 
          loading: action.loading,         
        }
      }
    case SET_LIST_NBH_CONTACTS_LOADING:
      return {
        ...state,       
        automationPreview:{
          ...state.automationPreview, 
          nbhListLoading: action.loading,         
        }
      }
    case SET_LIST_CONTACTS_LOADING:
      return {
        ...state,       
        automationPreview:{
          ...state.automationPreview, 
          listLoading: action.loading,         
        }
      }
    case GET_AUTOMATION_PREVIEW_SUCCESS:
      return {
        ...state,       
        automationPreview:{
          ...state.automationPreview, 
          ...action.data,           
        }
      }
    case GET_AUTOMATION_PREVIEW_FAILED:
      return {
        ...state,       
        automationPreview:{
          ...state.automationPreview,          
        }
      }
    case CHANGE_STATUS_AUTOMATION_SUCCESS:
      return {
        ...state,       
        automationPreview:{
          ...state.automationPreview, 
          status: action.data,
        }
      }
    case GET_AUTOMATION_PREVIEW_CONTACTS_SUCCESS:
      return {
        ...state,
        automationPreview:{
          ...state.automationPreview, 
          contactData: action.data,          
        }
      }
    case GET_AUTOMATION_PREVIEW_NBH_CONTACTS_SUCCESS:
      return {
        ...state,
        automationPreview:{
          ...state.automationPreview,
          listArrData:{
            ...state.automationPreview.listArrData,
            [action.key]: action.data
          }         
        }
      }
    case SET_WEB_HOOK_IDENTIFIER:
      return{
        ...state,
        webhookIdentifier: action.data, 
      }    
    case SET_NEXT_STEP_AUTOMATION:      
      return {
        ...state,
        step: action.data,
      };     
    case GET_AUTOMATION_LIST_REQUEST:
      return {
        ...state,
        isLoading: true
      };    
    case GET_AUTOMATION_PREVIEW_IMG_REQUEST_SUCCESS:
      return {
        ...state,
       previewImg: action.data,
      };
    case SET_MODIFICATED_AUTOMATION_TEMPLATE_EDIT:      
      return {
        ...state,
        automationEditor: {
          ...state.automationEditor,
          templateEdit: {
            ...state.automationEditor.templateEdit,
            ...action.data
          }, 
        },
      };
    case CHANGE_AUTOMATION_POSTCARD_TEMPLATE_TAB_INDEX:
      return {
        ...state,
        automationEditor:{
          ...state.automationEditor,
          templateTabIndex: action.data,
        }        
      };
    case SELECT_AUTOMATION_MORPHEUS_TEMPLATE:     
      return{
        ...state,
        automationEditor:{
          ...state.automationEditor,
          selectedTemplate: action.template._id,
          templateEdit: {
            ...state.automationEditor.templateEdit,
            front: action.template.front,
            back: action.template.back,
          },
          templateSample: {
            ...state.automationEditor.templateSample,
            ...action.template.templateSample
          },          
        },  
      };    
    case SET_CHANGE_AUTOMATION_POSTCARD_STATUS:
      return{
        ...state,
        changeAutomationPostcardStatus: {
          ...state.changeAutomationPostcardStatus,
          ...action.data,
        }
      };
    case GET_RECENT_AUTOMATION_POSTCARD_REQUEST_SUCCESS:          
      return{
        ...state,
        automationRecentTemplates:{
          ...state.automationRecentTemplates,
          ...action.data,          
        }
      };
    case SHOW_RECENT_AUTOMATION_POSTCARDS_LOADER:
      return{
        ...state,
        automationRecentTemplates:{
          ...state.automationRecentTemplates,
          loading: action.data,          
        }
      }
    case GET_MY_AUTOMATION_POSTCARD_REQUEST_SUCCESS:          
      return{
        ...state,
        automationMyTemplates:{
          ...state.automationMyTemplates,
          ...action.data,          
        }
      };
    case SHOW_MY_AUTOMATION_POSTCARDS_LOADER:
      return{
        ...state,
        automationMyTemplates:{
          ...state.automationMyTemplates,
          loading: action.data,          
        }
      }
    case RECHARGE_AUTOMATION_SUCCESS:      
      return{
        ...state,
        automationPreview:{
          ...state.automationPreview, 
          status: action.data.status,          
        }
      }
    default:
      return state;
  }
}
