
export const getPriceByCost = (ranges, quantity) => {
  return ranges.reduce(function(prev, curr) {
    return (Math.abs(curr.min - quantity) < Math.abs(prev.max - quantity) ? curr : prev);
  });
}

export const capitalFirstLetter = (str) => {  
  if(str.length)
    return str[0].toUpperCase()+ str.slice(1)          
}

export const debounce = function (func, wait) {
  let timeout;
  return function(...args) {
    const context = this;
    if (timeout) clearTimeout(timeout);
    timeout = setTimeout(() => {
      timeout = null;
      func.apply(context, args);
    }, wait);
  };
}

