//Convert base64 to Blob obj
export const b64toBlob = function(b64Data, contentType, sliceSize) {
  contentType = contentType || '';
  sliceSize = sliceSize || 512;

  var byteCharacters = atob(b64Data);
  var byteArrays = [];

  for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      var slice = byteCharacters.slice(offset, offset + sliceSize);

      var byteNumbers = new Array(slice.length);
      for (var i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i);
      }

      var byteArray = new Uint8Array(byteNumbers);

      byteArrays.push(byteArray);
  }

  var blob = new Blob(byteArrays, {type: contentType});
  return blob;
}

export const replacePhoneNumber = inputPhoneNumber => {    
  const phoneNumber = inputPhoneNumber.replace(/[^0-9]/g, '').slice(-10);
  const phone = phoneNumber? `(${phoneNumber.slice(0, 3)})${phoneNumber.slice(3, 6)}-${phoneNumber.slice(6)}` : '(000)000-0000';
  
  const phone3 = phoneNumber.slice(6);
  const phone2 = phoneNumber.slice(3, 6);
  const phone1 = phoneNumber.slice(0, 3);
  
  const allPhone = document.querySelectorAll('[data-phonenumber]');
  if (allPhone && allPhone.length){    
    allPhone.forEach(elem => (elem.innerHTML = phone));
  }


  const allPhone3 = document.querySelectorAll('[data-phone3]');
  if (allPhone3 && allPhone3.length)
    allPhone3.forEach(elem => (elem.innerHTML = phone3));

  const allPhone2 = document.querySelectorAll('[data-phone2]');
  if (allPhone2 && allPhone2.length)
    allPhone2.forEach(elem => (elem.innerHTML = phone2));

  const allPhone1 = document.querySelectorAll('[data-phone1]');
  if (allPhone1 && allPhone1.length)
    allPhone1.forEach(elem => (elem.innerHTML = phone1));
};

export function parsePostcardSizeToPX(size="6x9",dpi=300,bleed=0.25) {  
  let sizePX, sizeArr;
  sizeArr=size.split('x');  
  sizePX={
    height: `${ (parseFloat(sizeArr[0].replace(',', '.')) + bleed) * dpi}px`,
    width: `${ (parseFloat(sizeArr[1].replace(',', '.')) + bleed) * dpi}px`
  };
  return sizePX;
}

export function getFontType(type) {  
    switch(type){
      case 'woff2':
        return 'woff2';        
      case 'woff':
        return 'woff';        
      case 'ttf':
        return 'truetype';        
      case 'otf':
        return 'opentype';        
      case 'eot':
        return 'embedded-opentype';        
      default :
        return 'woff2';        
    }
} 
export function calcInternalWidth (el){    
  let lw = getComputedStyle(el).borderLeftWidth||0;
  let rw = getComputedStyle(el).borderLeftWidth||0;
  if(lw&&lw.includes('px')){
    lw = lw.split('px')[0];
  }
  if(rw&&rw.includes('px')){
    rw = rw.split('px')[0];      
  }    
  return el.offsetWidth - lw - rw
}
export function calcInternalHeight (el){
  let tw = getComputedStyle(el).borderTopWidth||0;
  let bw = getComputedStyle(el).borderBottomWidth||0;
  if(tw&&tw.includes('px')){
    tw = tw.split('px')[0];
  }
  if(bw&&bw.includes('px')){
    bw = bw.split('px')[0];
  }    
  return el.offsetHeight - tw - bw
}

export function getFontWeightName (weightNum) {
  switch(weightNum){
    case '100':
      return 'Thin';      
    case '200':
      return 'Extra Light';     
    case '300':
      return 'Light';      
    case '400':
      return 'Normal';      
    case '500':
      return 'Medium';     
    case '600':
      return 'Semi Bold';      
    case '700':
      return 'Bold';      
    case '800':
      return 'Extra Bold';      
    case '900':
      return 'Heavy';      
    default: 
      return 'Normal';
  }
}