import React from 'react';
import { useDispatch } from 'react-redux';
import { setThemeColor} from 'actions/general';
import './ThemeSwitch.scss';

export default function ThemeSwitch() {
  const dispatch = useDispatch();
  const handleTheme = (themeColor) => {
    document.querySelector('html').dataset.theme=themeColor;
    dispatch(setThemeColor(themeColor))
  };

  return (
    <div className="ThemeSwitch">
      <button className="theme-btn" style={{backgroundColor:"#d52330"}} onClick={() => {handleTheme("red")}}></button>
      <button className="theme-btn" style={{backgroundColor:"#0B609E"}} onClick={() => {handleTheme("blue")}}></button>
      <button className="theme-btn" style={{backgroundColor:"#6b5269"}} onClick={() => {handleTheme("brown")}}></button>      
      <button className="theme-btn" style={{backgroundColor:"#ffa500"}} onClick={() => {handleTheme("orange")}}></button>
      <button className="theme-btn" style={{backgroundColor:"#355c7D"}} onClick={() => {handleTheme("sunsetBlue")}}></button>
      <button className="theme-btn" style={{backgroundColor:"#f0f"}} onClick={() => {handleTheme("purple")}}></button>      
    </div>
  );
}
