import React from 'react';
import { FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms';
import { ReactComponent as SearchIco } from 'assets/img/icons/search.svg';
import './SearchListInput.scss';

export default function SearchListInput({value, onChange, placeholder }) { 
  return (
    <div className='dope-search-container'>
      <FormGroup>
        <SearchIco />
        <DopeInput
          placeholder= {placeholder||''}
          value={value}
          onChange={(e)=> onChange(e.target.value)}
        />
      </FormGroup>
    </div>
  );
}
