import React from 'react';
import {
  DopeModal,
  DopeModalHeader,
  DopeModalBody,
  DopeModalFooter,
} from 'components/DopeModal';
import { DopeButton } from 'components/DopeForms';
import { friendlyFormatPhoneNumber } from 'utils/phones';
import moment from 'moment';

import './PostcardApprovalModal.scss';



export default function PostcardApprovalModal({isOpen, toggle, onConfirm, data}) {  
  return (
    <DopeModal
      className='postcard-approval-modal'
      // onOpened={onOpened}
      isOpen={isOpen}      
      centered
    >
      <DopeModalHeader toggle={toggle}>Confirm Sending</DopeModalHeader>
      <DopeModalBody>        
        <p className="approval-text">By confirming the sending, you will make it ready for sending on the specified day. </p>
        <div className="approval-grid">
          <div className="approval-grid-item">
            <h4 className="item-title">
              Sending Date
            </h4> 
            {
              data.sendingDate ? (
                <p className="item-info">                
                  { moment(data.sendingDate).format('MMM DD, YYYY') }
                </p>
              ):null
            }
          </div>
          { data.trackingNumber||data.ownNumber ? (
            <div className="approval-grid-item">
              <h4 className="item-title">
                {
                  `${data.trackingNumber ? "Tracking" : "Own"} Number`
                }              
              </h4>
              <p className="item-info">
              {
                data.trackingNumber ? friendlyFormatPhoneNumber(data.trackingNumber) : null
              }
              {            
                data.ownNumber ? friendlyFormatPhoneNumber(data.ownNumber) : null
              }
              </p>
            </div>
            ):null
          }          
        </div>
        <div className="approval-grid">
          <div className="approval-grid-item">
            <h4 className="item-title">
              Front Side
            </h4>
            <div className="img-prev-container">              
            {
              data.front ? (
                <img src={data.front} alt="img-prev"/>
              ):null
            }              
            </div>              
          </div>
          <div className="approval-grid-item">
            <h4 className="item-title">
              Back Side
            </h4>
            <div className="img-prev-container">
            {
              data.back ? (
                <img src={data.back} alt="img-prev"/>
              ):null
            }   
            </div>            
          </div>
        </div>
      </DopeModalBody>
      <DopeModalFooter>
        <DopeButton color='white' onClick={toggle}>
          Cancel
        </DopeButton>
        <DopeButton
          color='primary'          
          onClick={onConfirm}
        >
          Confirm
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>
  );
}
