import React,{ useEffect, useState } from 'react';
import OnePictureBuilder from 'components/OnePictureBuilder/OnePictureBuilder';
import { FormGroup } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeTextarea } from 'components/DopeForms/DopeTextarea/DopeTextarea';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import DopeRadioGroup from 'components/DopeForms/DopeRadioGroup';
import CreditCardsAfter from 'components/CreditCardsAfter';
import Templates from './Templates';
import { fullNumberFormat} from 'utils/phones';
import { useSelector, useDispatch } from 'react-redux';
import { toast } from 'react-toastify';
import { 
  setEddmSize,  
  setEddmDesign,
  setEddmContactInfo,
  getEddmPreviewImgRequest,  
  setModificatedEddmTemplateEdit,  
  updateEddmRequest,
  setShowConfirmPopup,  
  addErrorsEddm,
  changeEddmPostcardTemplateTabIndex,  
  selectEddmMorpheusTemplate,
  selectEddmOnePictureTemplate,
  setChangeEddmPostcardStatus,
  saveAsMyEddmTemplateRequest,
} from 'actions/eddm';
import { getPriceByCost } from 'utils/helper';
import {postcard_front_id, postcard_back_id} from 'views/Eddm/config';

import S3 from 'aws-sdk/clients/s3';
import { S3_BUCKET_PUBLIC_NAME,
  REACT_APP_S3_ACCESS_KEY_ID,
  REACT_APP_S3_SECRET_ACCESS_KEY,
} from 'config/config';
import { showMainLoader } from 'actions/general';

import './EddmEditor.scss';
import EddmConfirmationPopup from "./EddmConfirmationPoPup/EddmConfirmationPoPup";

const s3Config = {
  bucketName: S3_BUCKET_PUBLIC_NAME,
  accessKeyId: REACT_APP_S3_ACCESS_KEY_ID,
  secretAccessKey: REACT_APP_S3_SECRET_ACCESS_KEY,
  region: 'us-east-1'
};
const s3Client = new S3(s3Config);

export default function EddmEditor(props) {  

  const dispatch = useDispatch();

  useEffect(() => {   
    let domFront = document.getElementById(postcard_front_id);
    let domBack = document.getElementById(postcard_back_id);

    // wait while dom with template rendered and then select all !!!eddm-image!!! images from aws
    if(domFront&&domBack&&(candidatesForDeleteFromAws===null)){
      let frontArray = [...domFront.querySelectorAll('img[data-image-changeable][src*="eddm-image"]')].map((el)=>el.src);
      let backArray = [...domBack.querySelectorAll('img[data-image-changeable][src*="eddm-image"]')].map((el)=>el.src);          
      // console.log("setCandidatesForDeleteFromAws----->>>>", [...frontArray,...backArray]);
      setCandidatesForDeleteFromAws([...frontArray,...backArray]);
      // console.log("useEffect setCandidatesForDeleteFromAws:", [...frontArray,...backArray]);
    } 
  });

  const [candidatesForDeleteFromAws, setCandidatesForDeleteFromAws] = useState(null);
  
  const eddmSize = useSelector(state => state.eddm.eddmSize);
  const eddmDesign = useSelector(state => state.eddm.eddmDesign);
  const contactInfo = useSelector(state => state.eddm.eddmEditor.contactInfo);
  const errors = useSelector(state => state.eddm.eddmEditor.errors);

  const front = useSelector(state => state.eddm.eddmEditor.templateEdit.front); //temporary set in reducer if '' then loaded templateSample
  const back = useSelector(state => state.eddm.eddmEditor.templateEdit.back); //temporary set in reducer if '' then loaded templateSample

  const templateSample = useSelector(state => state.eddm.eddmEditor.templateSample);
  const templateEdit = useSelector(state => state.eddm.eddmEditor.templateEdit);  
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const selectedRoutes = useSelector(state => state.eddm.selectedRoutes);
  const previewImg = useSelector(state => state.eddm.previewImg);
  const showConfirmPopup = useSelector(state => state.eddm.showConfirmPopup);
  const eddmSummary = useSelector(state => state.eddm.eddmSummary);
  const zipFind = useSelector(state => state.eddm.eddmRouteMap.zipFind);
  const eddmService = useSelector(state => state.eddm.eddmService);
  const currentAccount = useSelector(state => state.account.currentAccount);
  const totalSelectedRouteAddr = useSelector(state => state.eddm.totalSelectedRouteAddr);
  const addressesType = useSelector(state => state.eddm.addressesType);
  const defaultSource = useSelector(state => state.account.currentAccount.default_source);
  const templateTabIndex = useSelector(state => state.eddm.eddmEditor.templateTabIndex);
  const selectedTemplate = useSelector(state => state.eddm.eddmEditor.selectedTemplate); 
  const morpheusTemplates = useSelector(state => state.morpheusTemplates);
  const changeEddmPostcardStatusFront = useSelector(state => state.eddm.changeEddmPostcardStatus.front);
  const changeEddmPostcardStatusBack = useSelector(state => state.eddm.changeEddmPostcardStatus.back);
  const backMarkPlace = useSelector(state => state.eddm.eddmEditor.backMarkPlace); 
  const eddmRecentTemplates = useSelector(state => state.eddm.eddmRecentTemplates);
  const eddmMyTemplates = useSelector(state => state.eddm.eddmMyTemplates);

  
  
  
  const [eddmSendData, setEddmSendData] = useState({});
  const [arrayImagesToDeleteFromAws, setArrayImagesToDeleteFromAws] = useState([]);  
  const [disableConfirmButton, setDisableConfirmButton] = useState(false);
  const [templateStatus, setTemplateStatus] = useState('auto-saved'); 
  const [templateName, setTemplateName] = useState('');   

  const onChangeSize = (e)=>{
    // TODO TEST TEMPLATE CHANGES front and back
    if( changeEddmPostcardStatusFront || changeEddmPostcardStatusBack ) {        
      saveImagesToAws().then(()=>{
        setModificatedEddmTemplateToEdit(changeEddmPostcardStatusFront, changeEddmPostcardStatusBack);
        dispatch(setChangeEddmPostcardStatus({front: false, back: false}));
        dispatch(setEddmSize(e));        
      });
    }else{
      dispatch(setEddmSize(e));
    }
  }

  const onChangeDesign = (e)=>{     
    dispatch(setEddmDesign(e));
  }

  const onContactInfoChange = (name, value) => {
    errors[name] = '';
    // props.addErrorsContactInfo(errors); //TODO
    dispatch(setEddmContactInfo(name, value));
  };

  const dataURLtoFile = (dataurl, filename) => {
    let arr = dataurl.split(','),
      mime = arr[0].match(/:(.*?);/)[1],      
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);      
    while(n--){
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], filename, {type:mime});
  };

  const uploadImage = (Key, Body) => {
    const upload = s3Client.putObject({
      Bucket: `${s3Config.bucketName}/eddmImages/${currentAccountId}`,
      Key,
      Body,
      ACL: "public-read"
    });
    return upload.promise();
  };
  const deleteImage = (Key) => {
    const deleted = s3Client.deleteObject({
      Bucket: `${s3Config.bucketName}/eddmImages/${currentAccountId}`,
      Key
    });
    return deleted.promise();
  };
  const deleteConvertImage = (Key) => {
    const deleted = s3Client.deleteObject({
      Bucket: `${s3Config.bucketName}/eddmImages`,
      Key
    });
    return deleted.promise();
  };
  const saveImagesToAws = async () => {
    // save to aws front images    
    let frontDomImages = document.getElementById(postcard_front_id).querySelectorAll('img[data-image-changeable][src^="data:image"]');    
    await Promise.all([...frontDomImages].map(async(el)=>{
      const filename = `eddm-image-${currentAccountId}-${new Date().getTime()}.${el.src.split(',')[0].match(/:(.*?);/)[1].split('/')[1]}`;
      await uploadImage(filename, dataURLtoFile(el.src, filename));      
      el.src = `https://${s3Config.bucketName}.s3.amazonaws.com/eddmImages/${currentAccountId}/${filename}`;           
    } ) );
    // save to aws back images
    let backDomImages = document.getElementById(postcard_back_id).querySelectorAll('img[data-image-changeable][src^="data:image"]');    
    await Promise.all([...backDomImages].map(async(el)=>{
      const filename = `eddm-image-${currentAccountId}-${new Date().getTime()}.${el.src.split(',')[0].match(/:(.*?);/)[1].split('/')[1]}`;
      await uploadImage(filename, dataURLtoFile(el.src, filename));      
      el.src = `https://${s3Config.bucketName}.s3.amazonaws.com/eddmImages/${currentAccountId}/${filename}`; 
    } ) );    
  }
  const prepareArrayImagesToDeleteFromAws = () => {
    // CHECK images that should be deleted from aws as changed to new
    let allFinishImagesSRCFront = [...document.getElementById(postcard_front_id).querySelectorAll('img[data-image-changeable]')].map((el)=>el.src);      
    let allFinishImagesSRCBack = [...document.getElementById(postcard_back_id).querySelectorAll('img[data-image-changeable]')].map((el)=>el.src);  
    let allFinishImages = [...allFinishImagesSRCFront,...allFinishImagesSRCBack];
    let toDeleteArray = candidatesForDeleteFromAws.filter((el)=>!allFinishImages.includes(el));
    // console.log("Will be deleted after successfully sent to back:", toDeleteArray); 
    return toDeleteArray;
  }  
  
  // TODO sizes into template to BE
  // 6.25*9
  // height: 1950px;
  // width: 2775px;
  // 8.5*11
  // height: 2625px;
  // width: 3375px;

  const calcTemplateBodyStyle = (postcardSize)=>{
    switch(postcardSize){
      case '6,5x9':
        // return 'width: 2775px; height: 2025px;'    //LOB bleed
        return 'width: 2700px; height: 1950px;'
      case '6,5x11':
        // return 'width: 3375px; height: 2025px;'    //LOB bleed
        return 'width: 3300px; height: 1950px;'
      case '6x11':
        // return 'width: 3375px; height: 1875px;'    //LOB bleed
        return 'width: 3300px; height: 1800px;'
      case '8,5x11':
        // return 'width: 3375px; height: 2625px;'    //LOB bleed
        return 'width: 3300px; height: 2550px;'
      default:
        // return 'width: 2775px; height: 2025px;'    //LOB bleed
        return 'width: 2700px; height: 1950px;'
    }    
  }
  const checkDefaultDomImages = ()=>{
    let checkDefDomImagesFront = document.getElementById(postcard_front_id).querySelectorAll('.morpheus-card-body img[data-image-changeable][src*="/templateimages/"]');
    let checkDefDomImagesBack = document.getElementById(postcard_back_id).querySelectorAll('.morpheus-card-body img[data-image-changeable][src*="/templateimages/"]');
    if(checkDefDomImagesFront.length||checkDefDomImagesBack.length){
      toast.error("You can't send the default image to print. Please, replace the example image.")
      return true;
    } 
    return false;  
  }

  const confirmPostcard = async (templateStatus) => {
    
    if(templateStatus!=="saved"){
      if(checkDefaultDomImages()) return;
    }

    dispatch(showMainLoader(true));    
   
    setDisableConfirmButton(true); 
    setTemplateStatus(templateStatus);       

    saveImagesToAws().then(()=>{ 
      const newFront = `<html><body style="${calcTemplateBodyStyle(eddmSize)} -webkit-box-sizing: border-box; box-sizing: border-box;">${document.getElementById(postcard_front_id).parentElement.innerHTML}</body></html>`;
      const newBack = `<html><body style="${calcTemplateBodyStyle(eddmSize)} -webkit-box-sizing: border-box; box-sizing: border-box;">${document.getElementById(postcard_back_id).parentElement.innerHTML}</body></html>`;
      const data = {
        body: { 
          template:{
            front: newFront,
            back: newBack,
          },
          templateStatus,
          bucket: 'eddmImages'
        },        
      };
      setModificatedEddmTemplateToEdit(true, true);      
      dispatch(getEddmPreviewImgRequest(data));      
      setDisableConfirmButton(false)
    });      
  } 

  const callbackForDeleteImagesFromAws = async () => { 
    console.log("Array images to delete:",arrayImagesToDeleteFromAws);   
    let toDeleteArray = arrayImagesToDeleteFromAws;    
    if(toDeleteArray.length){      
      await Promise.all([...toDeleteArray].map(async(el)=>{          
        await deleteImage(el.split('/').pop()); 
      } ) ); 
    }
  }

  // SENDEDDM
  const sendEddm = () => {    
    const front = document.getElementById(postcard_front_id).querySelector('.morpheus-card-body').outerHTML;
    const back = document.getElementById(postcard_back_id).querySelector('.morpheus-card-body').outerHTML;
    const eddmRoutes = selectedRoutes.map(item => {
      return {
        addressesType: addressesType,
        route: item,
        zip: zipFind,
        deliveryAddressesCount: props.totalRouteAddr(item)
      }
    });
    let fullPreviewImg = {
      back: {
        full: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/eddmImages/${previewImg.back.full}`,
        mini: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/eddmImages/${previewImg.back.mini}`
      },
      front: {
        full: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/eddmImages/${previewImg.front.full}`,
        mini: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/eddmImages/${previewImg.front.mini}`
      }      
    };
    const data = {
      body: {
        deliveryAddress: eddmSummary.deliveryAddress || currentAccount.shippingAddress,
        shipping: eddmSummary.shipingService,
        eddmRoutes: eddmRoutes,
        images: fullPreviewImg,
        size: eddmSize,
        zip: zipFind,
        service: eddmService,
        totalDeliveryAddresses: totalSelectedRouteAddr,
        status: 'in-progress',
        eddmDesign: eddmDesign,
        templates:{
          front,
          back,
        },
        templateStatus,
      },
      queries: currentAccountId,
    };
    data.eddmId = props.eddmId;    
    if(templateStatus==='saved'){    
      let saveTemplateData = {
        body:{
          type: 'eddm',
          name: templateName,
          images: {...data.body.images},
          templates: {...data.body.templates}
        },                    
        accountId: currentAccountId,
      }
      setArrayImagesToDeleteFromAws(prepareArrayImagesToDeleteFromAws());
      dispatch(setShowConfirmPopup(!showConfirmPopup));
      dispatch(saveAsMyEddmTemplateRequest(saveTemplateData, callbackForDeleteImagesFromAws));
      setTemplateStatus('auto-saved');
      return;
    }    
    if(!defaultSource) {      
      setEddmSendData(data);            
      setArrayImagesToDeleteFromAws(prepareArrayImagesToDeleteFromAws());
      props.setShowAddingCard(true);
    }
    else {
      setArrayImagesToDeleteFromAws(prepareArrayImagesToDeleteFromAws());
      dispatch(setShowConfirmPopup(!showConfirmPopup));
      dispatch(updateEddmRequest(data, callbackForDeleteImagesFromAws));    
    }
  };
  const afterAddCreditCardSendEddm = () => {
    dispatch(setShowConfirmPopup(!showConfirmPopup));
    dispatch(updateEddmRequest(eddmSendData, callbackForDeleteImagesFromAws));
  };  
  const confirmAskDesign = () => {
    let re = /^\+1\d{10}$/;    
    let validNumber = re.test(contactInfo.phone);

    if( !validNumber ){
      dispatch(addErrorsEddm('phone','Enter correct number'));
      return;
    } 

    const eddmRoutes = selectedRoutes.map(item => {
      return {
        addressesType: addressesType,
        route: item,
        zip: zipFind,
        deliveryAddressesCount: props.totalRouteAddr(item)
      }
    });
    const data = {
      body: {
        deliveryAddress: eddmSummary.deliveryAddress || currentAccount.shippingAddress,
        shipping: eddmSummary.shipingService,
        eddmRoutes: eddmRoutes,
        zip: zipFind,
        service: eddmService,
        totalDeliveryAddresses: totalSelectedRouteAddr,
        status: 'design-request',
        eddmDesign: eddmDesign,
        designRequest: {
          name: contactInfo.name,
          phone: contactInfo.phone,
          description: contactInfo.description,
        }
      },
      queries: currentAccountId,
    };        
    data.eddmId = props.eddmId;
    dispatch(updateEddmRequest(data));    
  };

  // SENDEDDM DRAFT
  const saveAsDraft = () => {   
    saveImagesToAws().then(()=>{       
      const front = document.getElementById(postcard_front_id).querySelector('.morpheus-card-body').outerHTML;
      const back = document.getElementById(postcard_back_id).querySelector('.morpheus-card-body').outerHTML;            
      const eddmRoutes = selectedRoutes.map(item => {
        return {
          addressesType: addressesType,
          route: item,
          zip: zipFind,
          deliveryAddressesCount: props.totalRouteAddr(item)
        }
      });
      const data = {
        body: {
          deliveryAddress: eddmSummary.deliveryAddress || currentAccount.shippingAddress,
          shipping: eddmSummary.shipingService,
          eddmRoutes:eddmRoutes,          
          size: eddmSize,
          zip: zipFind,
          service: eddmService,
          totalDeliveryAddresses: totalSelectedRouteAddr,
          status: 'draft',
          eddmDesign: eddmDesign,
          templates:{
            front,
            back,
          }
        },
        queries: currentAccountId,
      };      
      data.eddmId = props.eddmId;      
      dispatch(updateEddmRequest(data, callbackForDeleteImagesFromAws));
    });
  };
  const closeConfirmPopup = async () => {    
    let toDeletePreviewArray = [
      previewImg.front.mini,
      previewImg.back.mini,
      previewImg.front.full,
      previewImg.back.full
    ];    
    if(toDeletePreviewArray.length){      
      await Promise.all(toDeletePreviewArray.map(async(el)=>{
        await deleteConvertImage(el); 
      } ) ); 
    }
    dispatch(setShowConfirmPopup(!showConfirmPopup));
  }

  const calculatePrice = (qty, shipingService, eddmSize) => {
    if (!qty) return "0.00";
    let pricePerOneClasses = getPriceByCost(props.eddmPostcardPrice.filter(item => item.name === `${eddmSummary.shipingService}`)[0].price[eddmSize], qty);    
    let pricePerOne = pricePerOneClasses['first-class-price'];
    return (pricePerOne * qty / 100).toFixed(2);
  };
  const calculatePricePerOne = (qty, shipingService, eddmSize) => {
    if (!qty) return "0.00";
    let pricePerOneClasses = getPriceByCost(props.eddmPostcardPrice.filter(item => item.name === `${eddmSummary.shipingService}`)[0].price[eddmSize], qty);
    return (pricePerOneClasses['first-class-price'] / 100).toFixed(2);
  };
  const calcTotalPrice=()=>{
    let totalPrice = calculatePrice(
      totalSelectedRouteAddr,
      eddmSummary.shipingService,
      eddmSize
    );
    return totalPrice;
  }

  const selectMorpheusTemplate = (template)=>{
    dispatch(selectEddmMorpheusTemplate(template));
  }

  const selectOnePictureTemplate = ()=>{
    dispatch(selectEddmOnePictureTemplate());
  }
  
  const changePostcardTemplateTabIndex = (e)=>{
    dispatch(changeEddmPostcardTemplateTabIndex(e));
  }  

  const prepareTemplateSample = (size, side)=>{      
    if(selectedTemplate!==''){
      switch(templateTabIndex){
        case 1:
          return morpheusTemplates.templates.find((el)=>(el._id===selectedTemplate))[side] || '';
        case 2:
          return eddmRecentTemplates.data.find((el)=>(el._id===selectedTemplate))[side] || '';
        case 3:
          return eddmMyTemplates.data.find((el)=>(el._id===selectedTemplate))[side] || '';
        default:
          return morpheusTemplates.templates.find((el)=>(el._id===selectedTemplate))[side] || '';
      }      
    } 
    if(templateEdit[side]){
      return templateEdit[side] || '';
    }
    return templateSample[size][side] || '';
  }

  const setModificatedEddmTemplateToEdit = (frontShouldChange, backShouldChange) =>{
    let front, back;
    if (frontShouldChange&&backShouldChange){
      front = document.getElementById(postcard_front_id).querySelector('.Morpheus-card-container').innerHTML;
      back = document.getElementById(postcard_back_id).querySelector('.Morpheus-card-container').innerHTML;
      dispatch(setModificatedEddmTemplateEdit({
        front,
        back,
      }));
    }
    if (frontShouldChange&&!backShouldChange){
      front = document.getElementById(postcard_front_id).querySelector('.Morpheus-card-container').innerHTML;      
      dispatch(setModificatedEddmTemplateEdit({
        front,        
      }));
    }
    if (!frontShouldChange&&backShouldChange){
      back = document.getElementById(postcard_back_id).querySelector('.Morpheus-card-container').innerHTML;      
      dispatch(setModificatedEddmTemplateEdit({
        back,        
      }));
    }
  }      
  return (   
    !props.showAddingCard ? (        
      <div className="EddmEditor">            
        <div className="eddmEditorCard">
          <div className="title-container t-shadow">
            <div className="title">Create Postcard</div>
          </div>
          <div className="cardBody">
            <DopeRadioGroup
              data={[                
                {
                  value: "ownDesign",
                  label: "Select your postcard template",                  
                },
                {
                  value: "askDesign",
                  label: "Ask for design using DOPE",                 
                }
              ]}
              name="eddmDesign"               
              selectedValue={eddmDesign}
              onChange={e => onChangeDesign(e)}
              disabled={props.typePreview==='view'}
            />
          </div>
        </div>
        {eddmDesign==='askDesign'&&(
          <div className="eddmEditorCard">
            <div className="title-container t-shadow">
              <div className="title">Contact Information</div>     
            </div> 
            <div className="cardBody contactInfo">
              <div className='contactInfo-grid'>
                <FormGroup className="dope-form-group">
                  <DopeInput
                    value={contactInfo.name||''}
                    title='Name'
                    supText='*'
                    name='name'
                    error={errors.name}
                    onChange={e=>onContactInfoChange(e.target.name, e.target.value)}
                    placeholder={'Name'}
                    disabled={props.typePreview==='view'}
                  />
                </FormGroup>
                <FormGroup className="dope-form-group">
                  <DopeInput
                    value={(contactInfo.phone||'').slice(2)}
                    title='Your Phone Number'
                    mask="(999)999-9999"
                    alwaysShowMask
                    supText='*'
                    name='phone'
                    error={errors.phone}
                    onChange={e=>onContactInfoChange(e.target.name, fullNumberFormat(e.target.value))}
                    placeholder={'Phone number'}
                    disabled={props.typePreview==='view'}
                  />
                </FormGroup>            
                <FormGroup className="dope-form-group two-grid">
                  <DopeTextarea
                    value={contactInfo.description||''}
                    title='Description'
                    name='description'
                    error={errors.description}
                    onChange={e=>onContactInfoChange(e.target.name, e.target.value)}
                    placeholder={'Add your description ...'}
                    rows='1'
                    disabled={props.typePreview==='view'}
                  />
                </FormGroup>
              </div>
            </div>    
          </div>
        )}
        {eddmDesign==='ownDesign'&&(
          <React.Fragment>            
            { 
              (props.typePreview!=='view') && (
                <div className="eddmEditorCard-v1">
                  <div className="title">Select a Template</div>
                  <Templates                    
                    tab={templateTabIndex}
                    changePostcardTemplateTabIndex={changePostcardTemplateTabIndex}
                    onSelectTemplate={selectMorpheusTemplate}
                    selectedTemplate={selectedTemplate}
                    currentAccountId={currentAccountId}
                    onSelectOnePictureTemplate={selectOnePictureTemplate}
                  />              
                </div>
              )
            }

            <div className="eddmEditorCard-v1">
              <div className="title">Select size of Postcard</div>
              <DopeRadioGroup
                data={[
                  {
                    value: "6,5x9",
                    label: `6,5x9 ($${calculatePricePerOne(totalSelectedRouteAddr,eddmSummary.shipingService,'6,5x9')})`,                    
                  },
                  {
                    value: "6,5x11",
                    label: `6,5x11 ($${calculatePricePerOne(totalSelectedRouteAddr,eddmSummary.shipingService,'6,5x11')})`,                    
                  },
                  {
                    value: "8,5x11",
                    label: `8,5x11 ($${calculatePricePerOne(totalSelectedRouteAddr,eddmSummary.shipingService,'8,5x11')})`,                    
                  }
                ]}
                name="eddmSize"               
                selectedValue={eddmSize}
                onChange={e => onChangeSize(e)} 
                disabled={props.typePreview==='view'}
              />
            </div>
            <div className="eddmEditorCard-v1">
              <div className="title">Front Side of Postcard</div>        
              <OnePictureBuilder
                template={front || templateSample[eddmSize].front}              
                templateSample={prepareTemplateSample(eddmSize,'front')}                
                classPref={templateSample[eddmSize].classPref || 'sm'}
                postcardSize={eddmSize}  //selected from three options in app  '6.5x9'  '6x11'  '8.5x11'
                postcardDpi={300}
                // postcardBleed={0.25}
                postcardBleed={0}     //without bleed for LOB
                awsPath={'eddm'}
                side="front"                
                disabled={props.typePreview==='view'} 
                currentAccountId={currentAccountId}
                changePostCardStatus={ (data)=>{dispatch(setChangeEddmPostcardStatus(data))} }
                
                phoneNumber="+18888888888" //temporary TODO remove                
              />
            </div>
            <div className="eddmEditorCard-v1">
              <div className="title">Back Side of Postcard</div>        
              <OnePictureBuilder                
                template={back || templateSample[eddmSize].back}
                templateSample={prepareTemplateSample(eddmSize,'back')} 
                classPref={templateSample[eddmSize].classPref || 'sm'}
                postcardSize={eddmSize}  //selected from three options in app  '6.5x9'  '6x11'  '8.5x11'
                postcardDpi={300}
                // postcardBleed={0.25}
                postcardBleed={0}     //without bleed for LOB
                awsPath={'eddm'}
                side="back"                
                disabled={props.typePreview==='view'}
                currentAccountId={currentAccountId}
                changePostCardStatus={ (data)=>{dispatch(setChangeEddmPostcardStatus(data))} }
                backMarkPlace={backMarkPlace}
                  
                phoneNumber="+18888888888" //temporary TODO remove            
              />
            </div>          
          </React.Fragment>
        )}
        <div className="btnContainer">          
          { 
            (props.typePreview!=='view' && eddmDesign==='ownDesign') && (
              <DopeButton
                color='white'
                // onClick={e=>saveAsMyTemplate(e)}
                onClick={()=>confirmPostcard("saved")}
                disabled={props.typePreview==='view'}
              >
                Save as My Template
              </DopeButton>
            )
          }
          {
            (eddmDesign==='ownDesign')&&
            <DopeButton
              color='white'
              onClick={e=>saveAsDraft(e)}
              disabled={props.typePreview==='view'}
            >
              Save as Draft
            </DopeButton>
          }        
          {
          (eddmDesign==='askDesign')&&
          <DopeButton
            onClick={()=>confirmAskDesign()}
            color='primary'
            disabled={props.typePreview==='view' || !contactInfo.name || !contactInfo.phone}
          >
            Confirm
          </DopeButton>
          }
          {
          (eddmDesign==='ownDesign')&&
          <DopeButton 
            onClick={()=>confirmPostcard("auto-saved")} 
            color='primary' 
            disabled={(props.typePreview==='view')||disableConfirmButton}>
            Confirm
          </DopeButton>
          }        
        </div>

        <EddmConfirmationPopup
          isOpen={showConfirmPopup}
          toggle={() => closeConfirmPopup()}        
          onConfirm={() => sendEddm()}    
          totalPrice={calcTotalPrice()}    
          templateStatus={templateStatus}
          templateName={templateName}
          setTemplateName={setTemplateName}
          data = {
            {            
              front: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/eddmImages/${previewImg.front.mini}`,
              back: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/eddmImages/${previewImg.back.mini}`,
              routes: selectedRoutes,
            }
          }
        />        
      </div> 
    ):(
      <CreditCardsAfter
        onAfterAddCreditCard={afterAddCreditCardSendEddm}
        onBack={()=>props.setShowAddingCard(false)}
      />      
    )
  );
}
