import React from 'react';
import { DopeButton } from 'components/DopeForms';

export default function ButtonsBlock({ pageViewType, onEdit, onCreate }) {
  return (
    <div className='new-campaign-btn'>
      {pageViewType === 'create' ? (
        <DopeButton color='primary' onClick={onCreate}>
          Continue
        </DopeButton>
      ) : (
        <DopeButton color='primary' onClick={onEdit}>
          Save
        </DopeButton>
      )}
    </div>
  );
}
