import React, {useEffect, useState} from 'react';
import { useHistory, useRouteMatch } from 'react-router-dom';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { AUTOMATION_PATH } from 'utils/constants';
import './AutomationList.scss';
import {useDispatch, useSelector} from "react-redux";
import {DopeTable} from "components/DopeTable";
import {columnsFunc} from "./config";
import {Form, FormGroup} from "reactstrap";
import { ReactComponent as SearchIco } from 'assets/img/icons/search.svg';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import {
  getAutomationListRequest,
  deleteAutomationItemRequest,
  setNextStepAutomation,
  resetAutomationList,
} from "actions/automation";
import moment from "moment";
import {FadeLoader} from "react-spinners";
import DeleteAutomationItemPopup from "./DeleteAutomationItemPopup";

export default function AutomationList({savedAccountId}) {  
  let { url } = useRouteMatch();
  let history = useHistory();
  const dispatch = useDispatch();
  const currentAccountId = useSelector(state => state.account.currentAccount._id); 

  useEffect(() => {    
    return () => {      
      dispatch(resetAutomationList());
    };
  }, []);

  useEffect(() => {
    const data = {
      // accountId: currentAccountId,
      query: {
        account: currentAccountId,
        page: 0,
        limit: 10,
      },
    };
    setSearchValue('');
    dispatch(getAutomationListRequest(data));
  }, [currentAccountId]);
  
  const automationList = useSelector(state => state.automation.automationList);  
  const isLoading = useSelector(state => state.automation.isLoading);
  const [searchValue, setSearchValue] = useState('');
  const [deletePopupOpen, setDeletePopupOpen] = useState(false);
  const [rowInfo, setRowInfo] = useState({});
  const [searchTriggered, setSearchTriggered] = useState(false);  

  const deleteAutomationItem = () => {
    dispatch(deleteAutomationItemRequest(rowInfo))
  };
  const onSort = (field, order)=>{
    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder };
    updateData(pagination);
  };
  const columns = columnsFunc(onSort);
  const customColumn = {
    dataField: "remove",
    text: "",
    sort: false,
    formatter: () => <DeleteIcon className="del-icon"/>,
    attrs: { className: "icon-cell" }
  };
  const rowClasses = (row, index) => {
    if(row.status === 'active') return 'table-row active';
    if(row.status === 'paused') return 'table-row paused';
    if(row.status === 'payment-failed') return 'table-row payment-failed';
    return 'table-row';
  };
  const updateData = (pagination = { page: automationList.query.page, limit: 10, sort: automationList.query.sort }) => {    
    if(!pagination.sort && automationList.query.sort) pagination.sort = automationList.query.sort;
    if(pagination.zip===undefined && automationList.query.zip) pagination.zip = automationList.query.zip;     
    let data = {
      accountId: currentAccountId,
      query: {
        ...pagination,
        page: pagination.page - 1,
        account: currentAccountId,
      }
    };    
    dispatch(getAutomationListRequest(data))
  };
  const paginationOptions = {
    page: automationList.query ? automationList.query.page + 1 : 1,
    limit: automationList.query ? automationList.query.limit : 10,
    onPageChange: updateData,    
    totalSize: automationList.total
  };
  const onChangeSearchValue = (e) => {    
    !searchTriggered&&setSearchTriggered(true);
    setSearchValue(e.target.value);    
    updateData({ page: 1, limit: 10, sort: automationList.query.sort, name: e.target.value });
  };

  return (
    <div className='AutomationList'>
      <div className="title-container">
        <h1 className="title">Automation</h1>
        <DopeButton
          color="primary"
          onClick={()=>{history.push(`${AUTOMATION_PATH}/create`);}}
        >
          New Automation
        </DopeButton>
      </div>
      <div className="automationListTable">
        <div className="automationList-header">
          <div>
            {automationList.total > 0
              ? <span>{automationList.total} Automation</span>
              : <span>No Automation Found</span>
            }
          </div>
          {((automationList.total > 0)||searchTriggered) ? (
            <div className="automationSearch">
              <Form onSubmit={e => e.preventDefault()}>
                <FormGroup className='dope-form-group search'>
                  <SearchIco />
                  <DopeInput
                    type="text"
                    value={searchValue}                    
                    placeholder={'Search by name'}
                    onChange={onChangeSearchValue}
                  />
                </FormGroup>
              </Form>
            </div>
          ):null
          }
        </div>
        { (automationList.total > 0) ? (
            <DopeTable
              keyField="_id"
              columns={[...columns, customColumn]}
              data={automationList.data.map((item) => {
                if(item.type&&item.type==="neighborhood"){
                  return(
                    {
                      _id: item._id,                  
                      listName: `${item.list&&item.list.name} (${item.lists.length} lists)`,
                      description: `${item.list&&item.list.description} (${item.neighborhoodCount} neighborhoods)`,
                      status: item.status,
                      createdAt: moment(item.createdAt).format('MMM DD, YYYY'),
                      type: 'neighborhood',
                    }
                  )
                }else{
                  return(
                    {
                      _id: item._id,                  
                      listName: item.list?item.list.name:'',
                      description: item.list?item.list.description:'',
                      status: item.status,
                      createdAt: moment(item.createdAt).format('MMM DD, YYYY'),
                      type: 'simple',
                    }
                  )
                }
              })}
              rowClasses={rowClasses}
              loading={isLoading}
              paginationOptions={paginationOptions}
              rowEvents={{
                onClick: (e, row, rowIndex) => {
                  if(e.target.tagName === 'svg' || e.target.tagName === 'path'){
                    setRowInfo({
                      sendingId: row._id,
                      accountId: currentAccountId,                      
                      query: { 
                        account: currentAccountId,
                        page: automationList.query.page, 
                        limit: 10, 
                        sort: automationList.query.sort }
                    });
                    setDeletePopupOpen(true);
                    return;
                  }
                  const {_id, status='new'} = row;                  
                  switch(status) {                    
                    case 'new':    
                      dispatch(setNextStepAutomation(1));
                      history.push(`${url}/create`);
                      break;                    
                    case 'draft':
                      dispatch(setNextStepAutomation(3));
                      history.push(`${url}/${_id}/edit`);
                      break;
                    case 'active': 
                    case 'in-progress': 
                    case 'paused':
                    case 'payment-failed':                       
                      // dispatch(setNextStepAutomation(3));    
                      history.push(`${url}/${_id}/view`);
                      break;
                    default:
                      dispatch(setNextStepAutomation(1));                      
                      history.push(`${url}/create`);
                  } 
                }
              }}
            />            
          ) : (
            <div className="noAutomationList">
              {isLoading && (
                <div className='loader-container'>
                  <FadeLoader size={100} color={'#403d39'} />
                </div>
              )}
              <span>No Automation Found</span>
            </div>
          )
        }
      </div>
      <DeleteAutomationItemPopup
        isOpen={deletePopupOpen}
        toggle={() => setDeletePopupOpen(!deletePopupOpen)}
        deleteAutomationItem={deleteAutomationItem}
      />
    </div>
  );
}
