export const getEddmListApi = data => {
  return service.get(
    `${API_URL}/accounts/${data.accountId}/eddm-sendings`, data.query
  )
};
export const getEddmItemApi = data => {
  return service.get(
    `${API_URL}/eddm-sending/${data.sendingId}`, data.query
  )
};
export const deleteEddmItemApi = data => {
  return service.delete(
    `${API_URL}/eddm-sending/${data.sendingId}`, data.query
  )
};
export const getMapRoutesByZipApi = zip => {
  // return service.extGet(`https://emapdata.adc4gis.com/routes?zips=${zip}`);
  return service.extGet(`https://gis.usps.com/arcgis/rest/services/EDDM/selectZIP/GPServer/routes/execute?f=json&env%3AoutSR=4326&ZIP=${zip}&Rte_Box=R&UserName=EDDM`);
};
export const getEddmPreviewImgApi = (data) => {
  // console.log("getEddmPreviewImgApi data:", data);
  return service.post(
    `${API_URL}/convert-image`,
    data,
  )
};

export const sendEddmApi = (data) => {
  // console.log("getEddmPreviewImgApi data:", data);
  return service.post(
    `${API_URL}/eddm-sending?account=${data.queries}`,
    data.body,
  )
};

export const updateEddmApi = (data) => {
  return service.patch(
    `${API_URL}/eddm-sending/${data.eddmId}?account=${data.queries}`,
    data.body,
  )
};

export const getRoutesStatByZipApi = (data) => {
  return service.get(
    `${API_URL}/delstat`, data.query
  );
};

export const saveAsMyEddmTemplateApi = (data) => { 
  return service.post(
    `${API_URL}/accounts/${data.accountId}/templates`,
    data.body,
  )  
};

export const getRecentEddmPostcardsApi = (data) => {  
  return service.get(
    `${API_URL}/accounts/${data.accountId}/eddm-sendings`, data.query
  );
};

export const getMyEddmPostcardsApi = (data) => {  
  return service.get(
    `${API_URL}/accounts/${data.accountId}/templates`, data.query
  );
};

export const deleteMyEddmPostcardApi = (data) => {  
  return service.delete(
    `${API_URL}/accounts/${data.accountId}/templates/${data.templateId}`
  );
};

