import { all, call, put, takeLatest } from 'redux-saga/effects';
import { toast } from 'react-toastify';
import * as generalActions from 'actions/general';
import * as automationActions from '../actions/automation';
import {pathOr} from 'ramda';
import {
  getAutomationListApi,
  deleteAutomationItemApi,
  createAutomationApi,
  updateAutomationApi,
  getAutomationItemApi,
  getAutomationPreviewImgApi,
  rechargeAutomationApi,
  changeStatusAutomationApi,
  getAutomationPreviewContactsApi,
  getRecentAutomationPostcardsApi,
  getMyAutomationPostcardsApi,
  deleteMyAutomationPostcardApi,
} from 'requests/automation';
import { S3_BUCKET_PUBLIC_NAME} from 'config/config';
import {push} from "connected-react-router";
import { AUTOMATION_PATH } from 'utils/constants';

export function* getAutomationList(action) {
  try {
    const response = yield call(getAutomationListApi, action.data);    
    const data = { ...action.data };
    yield put({
      type: automationActions.GET_AUTOMATION_LIST_REQUEST_SUCCESS,
      data: { ...response.data, ...data },
    });
  } catch (e) {
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't get automation list", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: automationActions.GET_AUTOMATION_LIST_REQUEST_FAILED, e });
  }
}
export function* getAutomationItem(action) {
  try {
    const response = yield call(getAutomationItemApi, action.data);  
    let data = {
      previewImg: response.data.images,
      webhookIdentifier: response.data.list.webhookIdentifier,
    };
    let templateEdit = {
      ...response.data.templates,
    };
    let automationSummary = {
      name: response.data.list.name,
      description: response.data.list.description,
    };   
    yield put({
      type: automationActions.GET_AUTOMATION_ITEM_SUCCESS,
      data: data,
      automationSummary: automationSummary,
      templateEdit: templateEdit,
    });     
  } catch (e) {
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't get automation info", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: automationActions.GET_AUTOMATION_ITEM_FAILED, e });
  }
}
export function* deleteAutomationItem(action) {
  try {
    const response = yield call(deleteAutomationItemApi, action.data);
    
    const data = { ...action.data };
    yield put({
      type: automationActions.DELETE_AUTOMATION_ITEM_REQUEST_SUCCESS,
      data: { ...response.data, ...data },
    });
    toast.success(`You have successfully deleted automation`);   
    yield put({
      type: automationActions.GET_AUTOMATION_LIST_REQUEST,
      data: action.data,
    });
  } catch (e) {
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't delete automation", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: automationActions.DELETE_AUTOMATION_ITEM_REQUEST_FAILED, e });
  }
}
export function* getAutomationPreviewImg(action) {   
  try { 
    const [response1,response2] = yield all([
      call(getAutomationPreviewImgApi, {template: action.data.body.template.front, bucket:action.data.body.bucket, side: 'front'}),      
      call(getAutomationPreviewImgApi, {template: action.data.body.template.back, bucket:action.data.body.bucket, side: 'back'})
    ]);
    const previewImg = {
      front: 
      {
        mini: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/automationImages/${response1.data.images.mini}`,
        full: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/automationImages/${response1.data.images.full}`, 
      },
      back: {
        mini: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/automationImages/${response2.data.images.mini}`,
        full: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/automationImages/${response2.data.images.full}`,
      }
    };
    yield put({
      type: automationActions.GET_AUTOMATION_PREVIEW_IMG_REQUEST_SUCCESS,
      data: previewImg
    });
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    if(action.callback) {
      action.callback(previewImg);
    }    
  } catch (e) { 
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't get previewImg", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: automationActions.GET_AUTOMATION_PREVIEW_IMG_REQUEST_FAILED, e });    
  }
}
export function* createAutomation(action) {   
  try {
    const response = yield call(createAutomationApi, action.data);    
    yield put({
      type: automationActions.CREATE_AUTOMATION_SUCCESS,
      data: response.data,
    });
    yield put({
      type: automationActions.SET_WEB_HOOK_IDENTIFIER,
      data: response.data.list.webhookIdentifier,      
    }); 
    yield put(push(`${AUTOMATION_PATH}/${response.data._id}`));
    yield put({
      type: automationActions.SET_NEXT_STEP_AUTOMATION,
      data: 3, 
    })  
    toast.success(` New draft automation has been created.`); 
  } catch (e) {
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't delete automation", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: automationActions.CREATE_AUTOMATION_FAILED, e });
  }
}
export function* updateAutomation(action) {  
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    yield call(updateAutomationApi, action.data);    
    yield put({
      type: automationActions.UPDATE_AUTOMATION_SUCCESS
    });    
    toast.success(` Draft automation has been updated.`); 
    if(action.callback) {
      action.callback();
    }   
  } catch (e) {    
    console.log("error", e);
    yield put({ type: automationActions.UPDATE_AUTOMATION_FAILED, e });         
    let errorMessage = pathOr("Error. Cann't send automation", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);    
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
}
export function* getAutomationPreview(action) {
  yield put({type: automationActions.SET_PREVIEW_LOADING,loading: true});
  try {    
    const response = yield call(getAutomationItemApi, action.data);
    let data = {
      previewImg: response.data.images,
      // webhookIdentifier: response.data.list.webhookIdentifier,           
      status: response.data.status,      
    };    
    if (response.data.type&&response.data.type==='neighborhood') {
      data.webhookIdentifier=response.data.list?response.data.list.webhookIdentifier:'in process';
      data.name=response.data.list?response.data.list.name:`${response.data.lists.length} lists`;
      data.description=response.data.list?response.data.list.description:`${response.data.neighborhoodCount} neighborhoods`;
      data.listId=response.data.list._id;//for get info about contacts from zapier
      data.listIdArr=response.data.lists;
      data.type=response.data.type;
    }else{
      data.webhookIdentifier=response.data.list.webhookIdentifier;
      data.name=response.data.list.name;
      data.description=response.data.list.description;      
      data.listId=response.data.list._id;
    }     
    yield put({
      type: automationActions.GET_AUTOMATION_PREVIEW_SUCCESS,
      data: data,
    });      
  } catch (e) {
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't get automation info", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: automationActions.GET_AUTOMATION_PREVIEW_FAILED, e });
  }  
  yield put({type: automationActions.SET_PREVIEW_LOADING,loading: false});
}
export function* rechargeAutomation(action) {  
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    const response = yield call(rechargeAutomationApi, action.data);
    if(response.data.status==='active'){
      yield put({
        type: automationActions.RECHARGE_AUTOMATION_SUCCESS,
        data: response.data,
      });    
      toast.success(` Automation has been recharged.`); 
      // if(action.successCallback){
      //   action.successCallback();
      // } 
    }else{
      toast.error(` Automation hasn't been recharged.`);
    }   
  } catch (e) {    
    console.log("error", e);
    yield put({ type: automationActions.RECHARGE_AUTOMATION_FAILED, e });         
    let errorMessage = pathOr("Error. Cann't recharge automation", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);    
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
}
export function* changeStatusAutomation(action) {   
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    const response = yield call(changeStatusAutomationApi, action.data);
    if(response && response.status === 200){
      if (action.data.body.status==='active'){
        toast.success(` Automation has been reactivated.`);
      }
      if (action.data.body.status==='paused'){
        toast.success(` Automation has been paused.`);
      }
    }      
    yield put({
      type: automationActions.CHANGE_STATUS_AUTOMATION_SUCCESS,
      data: action.data.body.status,
    });   
  } catch (e) {    
    console.log("error", e);
    yield put({ type: automationActions.CHANGE_STATUS_AUTOMATION_FAILED, e });            
    let errorMessage = pathOr("Error. Cann't change automation's status", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);    
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
}
