export function* getRecentAutomationPostcards(action) {
  // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true }); 
  yield put({ type: automationActions.SHOW_RECENT_AUTOMATION_POSTCARDS_LOADER, data: true });
  try {
    const response = yield call(getRecentAutomationPostcardsApi, action.data);  
    // console.log("getRecentAutomationPostcards response:",response);  
    let data = {};
    data = {
      ...response.data,
      // data: response.data.data.filter( (el)=>(el.templateStatus==='auto-saved') ).map( (el)=>(
      data: response.data.data.map( (el)=>(
        {          
          _id: el._id,
          front: el.templates ? el.templates.front : '', 
          back: el.templates ? el.templates.back : '',
          front_thumbnail: el.images ? el.images.front.mini : '',
          // size: el.size,
          status: el.status,
          createdAt: el.createdAt,
        }        
      )),
      limit: action.data.query.limit,
      page: action.data.query.page,
    };             
    yield put({
      type: automationActions.GET_RECENT_AUTOMATION_POSTCARD_REQUEST_SUCCESS,
      // data: {...data, ...action.data.query}
      data: data
    });    
  } catch (e) {
    let errorMessage = pathOr("Error.", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: automationActions.GET_RECENT_AUTOMATION_POSTCARD_REQUEST_FAILED });
  }
  // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false }); 
  yield put({ type: automationActions.SHOW_RECENT_AUTOMATION_POSTCARDS_LOADER, data: false });  
}
export function* getMyAutomationPostcards(action) {  
  // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true }); 
  yield put({ type: automationActions.SHOW_MY_AUTOMATION_POSTCARDS_LOADER, data: true });
  try {
    const response = yield call(getMyAutomationPostcardsApi, action.data);    
    let data = {};
    data = {
      ...response.data,
      data: response.data.data.map( (el)=>(
        {          
          _id: el._id,
          front: el.templates ? el.templates.front : '', 
          back: el.templates ? el.templates.back : '',
          front_thumbnail: el.images ? el.images.front.mini : '',
          // size: el.size,
          name: el.name||'',
          createdAt: el.createdAt,
        }        
      ))
    };             
    yield put({
      type: automationActions.GET_MY_AUTOMATION_POSTCARD_REQUEST_SUCCESS,
      data: {...data, ...action.data.query}
    });    
  } catch (e) {
    let errorMessage = pathOr("Error.", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: automationActions.GET_MY_AUTOMATION_POSTCARD_REQUEST_FAILED });
  }
  // yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false }); 
  yield put({ type: automationActions.SHOW_MY_AUTOMATION_POSTCARDS_LOADER, data: false });  
}
