import React from 'react';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { S3_BUCKET_PUBLIC_NAME,
  REACT_APP_S3_ACCESS_KEY_ID,
  REACT_APP_S3_SECRET_ACCESS_KEY,
} from 'config/config';
import './SpectsModal.scss';

const s3Config = {
  bucketName: S3_BUCKET_PUBLIC_NAME,
  accessKeyId: REACT_APP_S3_ACCESS_KEY_ID,
  secretAccessKey: REACT_APP_S3_SECRET_ACCESS_KEY,
  region: 'us-east-1'
};

export default function SpectsModal(props) {
  
  const toggle = () => {
    props.toggle();
  };

  const onClose = () => {
    props.toggle();    
  };

  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <DopeModal
        className='SpectsModal'
        isOpen={props.open}
        toggle={toggle}
        centered        
      >        
        <DopeModalHeader toggle={onClose}>
          Postcard spects details
        </DopeModalHeader>
        <DopeModalBody>
          <img 
            src={`https://${s3Config.bucketName}.s3.amazonaws.com/static/${props.awsPath}/spects/spects.${props.postcardSize}.jpg`}
            alt="Spects-img"
          />
        </DopeModalBody>        
      </DopeModal>
    </div>
  );
}
