import React from 'react';
import ReactTooltip from 'react-tooltip';

export const columnsFunc = (onSort) => [    
  {
    dataField: 'createdAt',
    text: 'Created',
    sort: true,
    sortFunc: function(){
      return false
    },
    onSort: onSort,
  },
  {
    dataField: 'metadata',
    text: 'Request Data',
    sort: false,
    sortFunc: function(){
      return false
    },
    onSort: onSort,
    formatter: metadataFormatter,
  },
  {    
    dataField: 'productType',
    text: 'Type',
    sort: true,
    sortFunc: function(){
      return false
    },
    onSort: onSort,
  }
];



const metadataFormatter = (cell, row, rowIndex, formatExtraData) => { 
  let listStr = Object.entries(cell).map(el=>`${el[0]}": "${el[1]}`).join(', ') || '';
  let vertlistStr='';
  if(listStr){
    vertlistStr =  Object.entries(cell).map( (el,i,arr)=>`<span>${el[0]}": "${el[1]}${i<arr.length-1 ? ',' : ''}</span>`).join('') || '';
  }
  
  return ( 
    listStr ?
      <div className='list-cell'>            
        <span className="list-text"  data-tip data-for={`tooltips-${row._id}`} > {listStr} </span>         
        <ReactTooltip          
          className="contact-list-tooltip"          
          html={true} 
          place='bottom'
          id={`tooltips-${row._id}`}
        >  
          {vertlistStr}
        </ReactTooltip>
      </div>
     : null    
  );
};