import service from './service';
import { API_URL } from '../config/config';

export const createJobsApi = (data) => {
  return service.get(
    `${API_URL}/jobs`,
    data
  );
};

export const createPreviewPdfApi = (data) => {
  return service.getBlob(
    `${API_URL}/job/${data.id}/preview`,
    data.params,
  );
};

export const createChangeStatusJobApi = (data) => {
  return service.patch(
    `${API_URL}/job/${data.id}`,
    data.payload
  );
};