import React, {useState} from 'react';
import { ModalFooter, InputGroupAddon, Label, FormGroup } from 'reactstrap';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInputGroup } from 'components/DopeForms/DopeInputGroup/DopeInputGroup';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import * as csv from 'csvtojson';
import './Upload.scss';

export default function Upload(props) {
  const [fileError, setFileError] = useState(false)
  const setCSV = csv => {
    // TODO: Implement streaming of file to backend
    return csv.replace(/application\/vnd.ms-excel/i, 'text/csv');
  };

  const onFileUpload = e => {
    
    const reader = new FileReader();
    let file = e.target.files[0];    

    reader.onloadend = () => {
      if (!/(\.csv)$/.test(file.name)) {
        setFileError(true)
        return;
      }
      
      setFileError(false);
      const result = setCSV(reader.result);
      let csvFileData = result.slice(result.indexOf('base64,') + 7);      
      csv({noheader: true})
        .fromString(Buffer.from(csvFileData, 'base64').toString())
        .then(jsonObj => {
          if(!jsonObj.length) {
            setFileError(true)
            return
          }
          let res = jsonObj.slice(0, 6);
          props.onChange('csvJson', res);

          let selectHeadersArray = Object.keys(res[0]);
          let selectHeaders = [];
          selectHeadersArray.forEach(item => {
            selectHeaders.push('');
          });
          props.onChange('headers', selectHeaders);
          props.onChange('csv', reader.result);
          props.onChange(
            'filename',
            file.name.length > 50 ? file.name.slice(0, 50) + '...' : file.name,
          );
        });
    };
    try {
      reader.readAsDataURL(file);
    } catch (e) {
      console.log(e);
    }
  };

  const onNext = () => {
    props.toggleMatchingPage();
  };

  const toggle = () => {
    props.toggle();
  };

  const onClose = () => {
    props.toggle();
    resetModal();
  };

  const resetModal = () => {
    props.onChange('filename', '');
    setFileError(false);
  }

  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <DopeModal
        className='upload-list-modal'
        isOpen={props.open}
        toggle={toggle}
        centered
        onClosed={resetModal}
      >
        <DopeModalHeader>Upload My List</DopeModalHeader>
        <DopeModalBody>
          <FormGroup className="dope-form-group">
            <span className='input-title'>Upload file</span>
            <DopeInputGroup>
              <DopeInput
                placeholder='No file chosen'
                value={props.uploadCsvData.filename || ''}
                onChange={() => {}}
              />
              <InputGroupAddon addonType='append'>
                <DopeInput
                  name='csv'
                  type='file'
                  id='file'
                  accept='.csv'
                  onChange={onFileUpload}
                />
                <Label className="dope-button btn btn-white" for='file'>Browse</Label>
              </InputGroupAddon>
            </DopeInputGroup>
            <div className={`small ${fileError? 'error-text': '' }`}>
              Acceptable file types: CSV or tab-delimited text files.
            </div>
          </FormGroup>
          {props.errors.length ? (
            <div className='upload-errors'>
              {props.errors.map(e => <div>{e}</div>)}
            </div>
          ) : null}
        </DopeModalBody>
        <ModalFooter>
          <DopeButton color='white' onClick={onClose}>
            Cancel
          </DopeButton>
          <DopeButton
            color='primary'
            disabled={!props.uploadCsvData.filename}
            onClick={onNext}
          >
            Continue
          </DopeButton>
        </ModalFooter>
      </DopeModal>
    </div>
  );
}
