import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Pagination from 'rc-pagination';
import { FadeLoader } from 'react-spinners';
import { ReactComponent as PrevIco } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as NextIco } from 'assets/img/icons/chevron-right.svg';
import { 
  getAllYardSignTemplatesRequest,
} from 'actions/createYardSign';

export default function AllTemplates(props) {  
  const dispatch = useDispatch();  

  const yardSignTemplatesLoading = useSelector(state => state.createYardSign.templateLoadingYS);
  const templates = useSelector(state => state.createYardSign.templates.data);
  const page = useSelector(state => state.createYardSign.templates.page);
  const total = useSelector(state => state.createYardSign.templates.total);
  const limit = useSelector(state => state.createYardSign.templates.limit);

  const onPageChange = page => {
    dispatch(getAllYardSignTemplatesRequest({query:{ page: page - 1, limit ,size:props.size}}));
  };
  const rowClasses = ()=>{
    if(templates&&templates.length<=5){
      return 'oneRow';
    }
    if(templates&&templates.length<=10&&templates&&templates.length>5){
      return 'twoRow';
    }
    return '';    
  }
  
  return (
    <React.Fragment>
      <div className={`templates-preview-container ${rowClasses()}`}>
        {          
          templates && templates.length ? (
            templates.map((template) => (             
              <div className='template-preview-item' key={template._id}>
                <div
                  className='template-item'
                  onClick={() => {                        
                    props.onSelectTemplate(template);                    
                  }}
                >
                  <div className={`template-preview-content${props.selectedTemplate === template._id ? ' checked':''}`}>
                    <img
                      className="template-preview-img"
                      src={template.front_thumbnail}
                      alt='yardSigns template'
                    />                    
                  </div>
                </div>
              </div>
            ))
          ) : ( 
            !yardSignTemplatesLoading && (           
              <div className="no-templates-container">
                No Templates
              </div>  
            )          
          )          
        }
        {
          yardSignTemplatesLoading && (
            <div className='loader-main'>
              <FadeLoader size={100} color={'#403d39'} />
            </div>
          )
        }
      </div> 
      {templates && templates.length ? (
        <Pagination
          className='dope-pagination'
          current={page}
          defaultPageSize={limit}
          total={total}
          hideOnSinglePage={true}
          onChange={onPageChange}          
          prevIcon={<PrevIco />}
          nextIcon={<NextIco />}
          jumpPrevIcon={<span>...</span>}
          jumpNextIcon={<span>...</span>}
          showTitle={false}
        />
      ) : null}     
    </React.Fragment>
  );
}
