export const getAllEddmTemplatesApi = (data) => {  
  return service.get(
    `${API_URL}/eddm-templates`,
    data.query
  );
};

export const getAllNeighborhoodBlitzTemplatesApi = (data) => {  
  return service.get(
    `${API_URL}/neighborhood-templates`,
    data.query
  );
};




