import React, {useEffect} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Pagination from 'rc-pagination';
import { FadeLoader } from 'react-spinners';
import { ReactComponent as PrevIco } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as NextIco } from 'assets/img/icons/chevron-right.svg';
import { 
  getAllNeighborhoodBlitzTemplatesRequest,
  resetAllTemplates,
} from 'actions/morpheusTemplates';

export default function AllTemplates(props) {
  // console.log("AllTemplates props: ",props); 
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getAllNeighborhoodBlitzTemplatesRequest({query:{ page:0, limit: 10 }}));
    // , sort: 'createdAt:-1'
    return () => {      
      dispatch(resetAllTemplates());
    };
  },[]);
  const morpheusTemplatesLoading = useSelector(state => state.morpheusTemplates.loading);
  const templates = useSelector(state => state.morpheusTemplates.templates.data);
  const page = useSelector(state => state.morpheusTemplates.templates.page);
  const total = useSelector(state => state.morpheusTemplates.templates.total);
  const limit = useSelector(state => state.morpheusTemplates.templates.limit);

  const onPageChange = page => {  
    dispatch(getAllNeighborhoodBlitzTemplatesRequest({query:{ page: page - 1, limit }}));
  };
  
  return (
    <React.Fragment>
      <div className={`templates-preview-container${(templates&&templates.length<=5)?' oneRow':''}`}>
        {
          templates && templates.length ? (
            templates.map((template) => (             
              <div className='template-preview-item' key={template._id}>
                <div
                  className='template-item'
                  onClick={() => {                        
                    props.onSelectTemplate(template);                    
                  }}
                >
                  <div className={`template-preview-content${props.selectedTemplate === template._id ? ' checked':''}`}>
                    <img
                      className="template-preview-img"
                      src={template.front_thumbnail}
                      alt='postcard template'
                    />                    
                  </div>
                </div>
              </div>
            ))
          ) : (
            !morpheusTemplatesLoading && (           
              <div className="no-templates-container">
                No Templates
              </div>
            )         
          )          
        }
        {
          morpheusTemplatesLoading && (
            <div className='loader-main'>
              <FadeLoader size={100} color={'#403d39'} />
            </div>
          )
        }
      </div> 
      {templates && templates.length ? (
        <Pagination
          className='dope-pagination'
          current={page + 1}
          defaultPageSize={limit}
          total={total}
          hideOnSinglePage={true}
          onChange={onPageChange}          
          prevIcon={<PrevIco />}
          nextIcon={<NextIco />}
          jumpPrevIcon={<span>...</span>}
          jumpNextIcon={<span>...</span>}
          showTitle={false}
        />
      ) : null}     
    </React.Fragment>
  );
}
