import {
    SET_RESET_PASS_ALERT_MESSAGE
} from '../actions';

export default function reducer(state = [], action) {
    switch (action.type) {
        case SET_RESET_PASS_ALERT_MESSAGE:
            return {
                ...state,
                resetPassAlertMessage: action.resetPassAlertMessage
            }
        default:
            return state;
    }
}