import React, { useEffect, useState } from 'react';
import { FormGroup, Alert, Label } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
// import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInputGroup } from 'components/DopeForms/DopeInputGroup/DopeInputGroup';
import { InputGroupAddon } from 'reactstrap';
import { Link } from 'react-router-dom';
import { LOG_IN } from 'utils/constants';

import './ForgotPasswordFormAccountPhone.scss';

export default function ForgotPasswordFormAccountPhone(props) {
  const [delayLoading, setDelayLoading] = useState(false);  
  
  useEffect(() => {   
    let delayLoadingTimerAccountPhone;  
    if (!props.loading && delayLoading){
      delayLoadingTimerAccountPhone = window.setTimeout(()=>setDelayLoading(false), 30000);
    }
    if (props.loading){          
      setDelayLoading(props.loading);
    }      
    return () => {
      clearTimeout(delayLoadingTimerAccountPhone);
    };
  }, [props.loading]);
  
  return (    
    <form className='forgot-password-form-phone' noValidate onSubmit={props.submit}>
      <div className='auth-form-title'>Password Recovery</div>
      <div className='forgot-password-text'>
        Please enter your phone, and we will send you a SMS with code to reset your password.
      </div>
      <div>
        {props.alert && <Alert color='danger'>{props.alert}</Alert>}          
        <FormGroup className="dope-form-group phone-sms-addon"> 
          <Label className='input-title' htmlFor='password'>
            Phone
          </Label>
          <DopeInputGroup>       
            <DopeInput
              type='text'
              onChange={e => props.onChange('accountPhone', e.target.value.trim())}
              value={props.data.accountPhone}
              error={props.errors.accountPhone}
            />
            <InputGroupAddon
              addonType='append'
              onClick={props.submit}
              className={`${delayLoading?'disabled':''}`}
            >              
              <span className="check-text">send SMS</span>              
            </InputGroupAddon>
          </DopeInputGroup>
        </FormGroup> 
        <div className={`check-code-form${props.checkCodeVisible?' active':''}`}>
          <DopeInputGroup>
            <DopeInput
              name='code'              
              type='text'
              onChange={e => props.onChange('code', e.target.value)}
              value={props.data.code}
              error={props.errors.code}                      
            />                
            <InputGroupAddon
              addonType='append'
              onClick={props.sendCode}
            >              
              <span className="check-text">send</span>
            </InputGroupAddon>
          </DopeInputGroup>
        </div>         
      </div>
      <div className='forgot-password-form-bottom'>
        <div className='link-block'>
          <Link to={LOG_IN}>Back to Sign In</Link>
        </div>
        {/* <DopeButton color='primary' type='submit' disabled={delayLoading}>
          Send
        </DopeButton> */}
      </div>
    </form>    
  );
}
