import React, { useEffect, useState } from 'react';
import NeighborhoodBlitzMap from "./NeighborhoodBlitzMap/NeighborhoodBlitzMap";
import {useDispatch, useSelector} from "react-redux";
import { useParams, useHistory } from 'react-router-dom';
import { ReactComponent as LeftArrowIcon } from 'assets/img/icons/chevron-left.svg';
import {changeStep, getNeighborsItemRequest} from "actions/neighborhoodBlitz";
import NeighborhoodBlitzSummary from "./NeighborhoodBlitzSummary/NeighborhoodBlitzSummary";
import './NeighborhoodBlitzCreate.scss';
import NeighborhoodBlitzEditor from "./NeighborhoodBlitzEditor/NeighborhoodBlitzEditor";
import {resetCreateNeighbors} from "actions/neighborhoodBlitz";

export default function NeighborhoodBlitzCreate({savedAccountId}) {
  const dispatch = useDispatch();
  const history = useHistory();
  const { neighborhoodBlitzId, typePreview } = useParams();
  const step = useSelector(state => state.neighborhoodBlitz.step);
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const [showAddingCard, setShowAddingCard] = useState(false);
  const isLoading = useSelector(state => state.neighborhoodBlitz.isLoading);

  useEffect(() => {
    dispatch(resetCreateNeighbors());//test
    if(neighborhoodBlitzId !== 'create'){
      const data = {
        accountId: currentAccountId,
        sendingId: neighborhoodBlitzId,
      };
      dispatch(getNeighborsItemRequest(data));
    }
    return () => {
      dispatch(resetCreateNeighbors());
    };
  },[]);

  useEffect(() => {
    if((savedAccountId !== currentAccountId) && (savedAccountId !== '')){      
      history.push('/neighborhood-blitz');            
    }    
  }, [currentAccountId]);

  return (
    <div className={`NeighborhoodBlitzCreate${showAddingCard?" addCard":""}`}>

      {/* STEP 1 */}
      {step === 1 && (
        <React.Fragment>
          <div className='back-btn-container'>
            <button className='back-btn' onClick={()=> history.push('/neighborhood-blitz')} disabled={isLoading} >
              <LeftArrowIcon className="back-icon"/>
              Back to Neighborhood Blitz
            </button>
          </div>
          <NeighborhoodBlitzMap
            typePreview={typePreview}
          />
        </React.Fragment>
        )
      }
      {/* END STEP 1 */}

      {/* STEP 2 */}
      {step === 2 && (
        <React.Fragment>
          <div className='back-btn-container'>
            <button className='back-btn' onClick={() => dispatch(changeStep(step - 1))}>
              <LeftArrowIcon className="back-icon"/>
              Back to Neighborhood Blitz
            </button>
          </div>
          <NeighborhoodBlitzSummary
            typePreview={typePreview}
            neighborhoodBlitzId={neighborhoodBlitzId}
          />
        </React.Fragment>
      )}
      {/* END STEP 2 */}

      {/* STEP 3 */}
      {step === 3 && (
        <React.Fragment>          
          {!showAddingCard&&
            (
              <div className='back-btn-container'>
                <button className='back-btn' onClick={() => dispatch(changeStep(step - 1))} >
                  <LeftArrowIcon className="back-icon"/>
                  Back
                </button>
              </div>
            )
          }
          <NeighborhoodBlitzEditor
            neighborhoodBlitzId={neighborhoodBlitzId}
            typePreview={typePreview}
            showAddingCard={showAddingCard}
            setShowAddingCard={setShowAddingCard}
          />
        </React.Fragment>
      )}
      {/* END STEP 3 */}

    </div>
  );
}