import service from './service';
import { API_URL } from '../config/config';

export const getCampaignsListApi = data => {
  return service.get(`${API_URL}/campaign`, data);
};

export const getCampaignApi = data => {
  return service.get(`${API_URL}/campaign/${data.params.campaignId}`, data.query);
};

export const getListsApi = data => {
  return service.get(`${API_URL}/list-request`, data);
};

export const createNewCampaignApi = data => {
  return service.post(
    `${API_URL}/campaign?account=${data.query.account}`,
    data.body,
  );
};

export const editCampaignApi = data => {
  return service.patch(
    `${API_URL}/campaign/${data.params.campaign}?account=${data.query.account}`,
    data.body,
  );
};

export const createNewSendingApi = data => {    
  return service.post(
    `${API_URL}/sending/${data.params.campaign}?account=${data.query.account}&campaign=${data.query.campaign}`,
    data.body,
  );
};

export const cloneNewSendingApi = data => {    
  return service.patch(
    `${API_URL}/sending/${data.params.sendingId}/duplicate?account=${data.query.account}&campaign=${data.query.campaign}`,
    data.body,
  );
};

export const updateSendingApi = data => {  
  return service.patch(
    `${API_URL}/sending/${data.params.sending}?account=${data.query.account}&campaign=${data.query.campaign}`,
    data.body,
  );
};

export const deleteSendingApi = data => {
  return service.delete(
    `${API_URL}/sending/${data.params.sending}?account=${data.query.account}`,
  );
};

