import service from './service';
import { API_URL } from 'config/config';

export const getYardSignsTemplateRequestApi = data => {  
  return service.get(`${API_URL}/yard-sign-templates`, data.query);
};

export const submitYardSignsApi = data => {
  return service.post(`${API_URL}/yard-sign?account=${data.query.account}`, data.body);
};

export const getYardSignPreviewImgApi = (data) => {
  // console.log("getYardSignPreviewImgApi data:", data);
  return service.post(
    `${API_URL}/convert-image`,
    data,
  )
};
// TODO FOR YARDSIGN
export const saveUniYardSignTemplateApi = (data) => {    
  return service.post(
    `${API_URL}/accounts/${data.accountId}/templates`,
    data.body,
  )
};
