import { all, call, put, takeLatest, takeLeading } from 'redux-saga/effects'
import * as createPostCard from '../actions/createPostCard'
import * as generalActions from '../actions/general'
import { createSubmitPostcardApi,    
  saveUniPostcardTemplateApi,
  getAllTemplatesApi, 
  getPostcardsApi, 
  createGetContactsApi, 
  createGetListRequestApi, 
  getPostcardByIdApi, 
  deleteMyPostcardApi, 
  getPostcardPreviewImgApi,
  getTemplatesWithCategoryApi,
  getUniMyPostcardTemplatesApi,
} from '../requests/createPostCard';
import {pathOr} from 'ramda';
import { toast } from 'react-toastify';
import { S3_BUCKET_PUBLIC_NAME} from 'config/config';

const errorText = 'Error please check your credit card amount and other credentials or contact with the admin.';

export function* createSubmitPostcardRequest(action) {
  try {
    yield call(createSubmitPostcardApi, action.data)
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    yield put({ type: createPostCard.FINAL_MODAL_SHOW_POST_CARD, payload: true })
  } catch(e) {
    const errorMessage = pathOr(errorText, ['response', 'data', 'error', 'dopeMessage'], e);
    yield put({ type: createPostCard.POSTCARD_DISABLE_BTN, data: false })
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })    
    toast.error(errorMessage)    
    yield put({ type: createPostCard.CREATE_SUBMIT_POST_CARD_REQUEST_FAILED, error: e })
  }
}

export function* saveUniPostcardTemplateRequest(action) {
  try {    
    yield call(saveUniPostcardTemplateApi, action.data)    
    yield put({ type: createPostCard.TOGGLE_SAVED_TEMPLATE_MODAL })
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.success(`Postcard template has been saved to your templates.`)
  } catch(e) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.error(`Postcard template hasn't been saved to your templates.`)
    yield put({ type: createPostCard.TOGGLE_SAVED_TEMPLATE_MODAL })
    yield put({ type: createPostCard.CREATE_SUBMIT_POST_CARD_REQUEST_FAILED, error: e })
  }
}

export function* getAllPostcardTemplatesRequest(action) {
  try {
    yield put({ type: createPostCard.SET_CREATE_POSTCARD_PROPERTY, key: 'newTemplateIsLoaded', value: true})
    const response = yield call(getAllTemplatesApi, action.data)
    yield put({ type: createPostCard.GET_ALL_POST_CARD_TEMPLATE_REQUEST_SUCCESS, data: {...response.data, ...action.data, page: action.data.page}})
    yield put({ type: createPostCard.SET_CREATE_POSTCARD_PROPERTY, key: 'newTemplateIsLoaded', value: false})    
  } catch(e) {
    yield put({ type: createPostCard.GET_ALL_POST_CARD_TEMPLATE_REQUEST_FAILED, error: e })
    yield put({ type: createPostCard.SET_CREATE_POSTCARD_PROPERTY, key: 'newTemplateIsLoaded', value: false})
  }
}

export function* getMyPostcardTemplatesRequest(action) {
  try {
    yield put({ type: createPostCard.CHANGE_POSTCARD_TEMPLATES_IS_LOADED, data: true})
    const response = yield call(getPostcardsApi, action.data)
    yield put({ type: createPostCard.GET_MY_POSTCARD_TEMPLATES_REQUEST_SUCCESS, data: response.data})
    yield put({ type: createPostCard.CHANGE_POSTCARD_TEMPLATES_IS_LOADED, data: false})
  } catch(e) {
    yield put({ type: createPostCard.CHANGE_POSTCARD_TEMPLATES_IS_LOADED, data: false})
    yield put({ type: createPostCard.GET_MY_POSTCARD_TEMPLATES_REQUEST_FAILED, error: e })
  }
}

export function* getUniMyPostcardTemplatesRequest(action) {
  try {
    yield put({ type: createPostCard.CHANGE_POSTCARD_TEMPLATES_IS_LOADED, data: true})
    const response = yield call(getUniMyPostcardTemplatesApi, action.data)
    yield put({ 
      type: createPostCard.GET_UNI_MY_POSTCARD_TEMPLATES_REQUEST_SUCCESS,
      data: response.data
    })
    yield put({ type: createPostCard.CHANGE_POSTCARD_TEMPLATES_IS_LOADED, data: false})
  } catch(e) {
    yield put({ type: createPostCard.CHANGE_POSTCARD_TEMPLATES_IS_LOADED, data: false})
    yield put({ type: createPostCard.GET_UNI_MY_POSTCARD_TEMPLATES_REQUEST_FAILED, error: e })
  }
}

export function* createGetContactsRequest(action) {
  try {
    const response = yield call(createGetContactsApi, action.data)
    yield put({ type: createPostCard.CREATE_GET_CONTACTS_REQUEST_SUCCESS, data: response.data})
  } catch(e) {
    yield put({ type: createPostCard.CREATE_GET_CONTACTS_REQUEST_FAILED, error: e })
  }
}

export function* createGetAllContactsRequest(action) {
  try {
    const response = yield call(createGetContactsApi, action.data)
    yield put({ type: createPostCard.GET_ALL_CONTACTS_REQUEST_SUCCESS, data: response.data})
  } catch(e) {
    yield put({ type: createPostCard.GET_ALL_CONTACTS_REQUEST_FAILED, error: e })
  }
}

export function* createGetListRequestRequest(action) {
  try {
    const response = yield call(createGetListRequestApi, action.data)
    yield put({ type: createPostCard.CREATE_GET_LIST_REQUEST_SUCCESS, data: response.data.data})
  } catch(e) {
    yield put({ type: createPostCard.CREATE_GET_LIST_REQUEST_FAILED, error: e })
  }
}

export function* getPostcardById(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    const response = yield call(getPostcardByIdApi, action.data)
    yield put({ type: createPostCard.GET_POSTCARD_BY_ID_REQUEST_SUCCESS, data: response.data})
    yield put({ type: createPostCard.POST_CARD_NEXT_STEP, step: action.step + 1})
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
  } catch(e) {
    yield put({ type: createPostCard.GET_POSTCARD_BY_ID_REQUEST_FAILED, error: e })
    toast.error(`Error. Cannot download template.`)
  }
}

export function* deleteMyPostcard(action) { 
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
  try {
    const response = yield call(deleteMyPostcardApi, action.data);    
    toast.success(response.data.message);    
    if(action.callbackSuccess){ 
      action.callbackSuccess();
    }
  } catch (e) {
    let errorMessage = pathOr("Error.", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: createPostCard.DELETE_MY_POSTCARD_FAILED, error: e })
  }   
  yield put({ type: createPostCard.TOGGLE_DELETE_TEMPLATE_MODAL })
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })  
}

export function* getTemplatesWithCategory(action) {
  try {
    yield put({ type: createPostCard.CHANGE_CATEGORY_TEMPLATE_IS_LOADED, data: true})
    const response = yield call(getTemplatesWithCategoryApi, action.data)
    yield put({ type: createPostCard.GET_TEMPLATES_WITH_CATEGORY_REQUEST_SUCCESS, data: {...response.data, limit: action.data.limit, page: action.data.page}})
    yield put({ type: createPostCard.CHANGE_CATEGORY_TEMPLATE_IS_LOADED, data: false})
  } catch(e) {
    yield put({ type: createPostCard.CHANGE_CATEGORY_TEMPLATE_IS_LOADED, data: false})
    toast.error(`Error. Cannot download template.`)
  }
}

export function* getPostcardPreviewImg(action) { 
  try {    
    const [response1,response2] = yield all([
      call(getPostcardPreviewImgApi, {template: action.data.body.template.front, bucket:action.data.body.bucket, side: 'front'}),      
      call(getPostcardPreviewImgApi, {template: action.data.body.template.back, bucket:action.data.body.bucket, side: 'back'})
    ]);     
    const images = {
      front: {
        mini: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcardImages/${response1.data.images.mini}`,    
        full: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcardImages/${response1.data.images.full}`,
      },
      back: {
        mini: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcardImages/${response2.data.images.mini}`,
        full: `https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcardImages/${response2.data.images.full}`
      }
    };
    yield put({
      type: createPostCard.GET_POSTCARD_PREVIEW_IMG_SUCCESS,
      data: images
    });    
    if(action.successCallback){      
      action.successCallback();
    }
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
  } catch (e) { 
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't get previewImg", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: createPostCard.GET_POSTCARD_PREVIEW_IMG_FAILED, e }); 
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });   
  }
}

export default all([
    takeLeading(createPostCard.CREATE_SUBMIT_POST_CARD_REQUEST, createSubmitPostcardRequest),    
    takeLatest(createPostCard.SAVE_UNI_POST_CARD_TEMPLATE_REQUEST, saveUniPostcardTemplateRequest),
    takeLatest(createPostCard.GET_ALL_POST_CARD_TEMPLATE_REQUEST, getAllPostcardTemplatesRequest),
    takeLatest(createPostCard.GET_MY_POSTCARD_TEMPLATES_REQUEST, getMyPostcardTemplatesRequest),
    takeLatest(createPostCard.GET_UNI_MY_POSTCARD_TEMPLATES_REQUEST, getUniMyPostcardTemplatesRequest),
    takeLatest(createPostCard.CREATE_GET_CONTACTS_REQUEST, createGetContactsRequest),
    takeLatest(createPostCard.CREATE_GET_LIST_REQUEST, createGetListRequestRequest), 
    takeLatest(createPostCard.GET_ALL_CONTACTS_REQUEST, createGetAllContactsRequest),
    takeLatest(createPostCard.GET_POSTCARD_BY_ID_REQUEST, getPostcardById),
    takeLatest(createPostCard.DELETE_MY_POSTCARD_REQUEST, deleteMyPostcard),
    takeLatest(createPostCard.GET_TEMPLATES_WITH_CATEGORY_REQUEST, getTemplatesWithCategory),  
    takeLatest(createPostCard.GET_POSTCARD_PREVIEW_IMG_REQUEST, getPostcardPreviewImg),    
])