import {
  RESET_CREATE_HANDWRITTEN,
  GET_HANDWRITTEN_LIST_REQUEST,
  GET_HANDWRITTEN_LIST_REQUEST_SUCCESS,
  GET_HANDWRITTEN_ORDER_REQUEST_SUCCESS,
  SAVE_HANDWRITTEN_REQUEST_SUCCESS,
} from "actions/handwritten";

let initialState = {
  loading: false,
  handwrittenOrder: null,
  handwrittenList: {
    query: {
      page: 0,
      limit: 10,
      sort: null,
    },
  },
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case RESET_CREATE_HANDWRITTEN:
      return {
        ...initialState,
      };

    case GET_HANDWRITTEN_LIST_REQUEST:
      return {
        ...state,
        loading: true
      };

    case GET_HANDWRITTEN_LIST_REQUEST_SUCCESS:
      return {
        ...state,
        handwrittenList: action.data,
        loading: false
      };

    case GET_HANDWRITTEN_ORDER_REQUEST_SUCCESS:
      return {
        ...state,
        handwrittenOrder: action.data,
      };

    case SAVE_HANDWRITTEN_REQUEST_SUCCESS:
      return {
        ...state,
        // filter_id: action.data._id
      };

    default:
      return state
  }
}