import React from 'react'
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { Button } from 'reactstrap';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import  EditList  from './EditList/EditList';
import config from 'config'
import request from 'utils/request'
import UploadModal from './UploadModal/Upload'
import ViewModal from './View';
import MatchingContacts from './MatchingContacts';
import { DopeTable } from '../../../components/DopeTable/DopeTable'
import * as listActions from "actions/listRequests"
import { ReactComponent as UserIcon } from 'assets/img/icons/user.svg';
import { ReactComponent as DownloadIcon } from 'assets/img/icons/download.svg';
import { ReactComponent as EditIco } from 'assets/img/icons/edit.svg';
import { API_URL } from '../../../config/config'
import {columnsFunc} from './config';
import './Lists.scss';


class Lists extends React.Component {
  state = {
    pagination: {
      page: 1,
      limit: 10,
      sort: null,
    },
    // createModal: false,
    // createModalData: { },
    viewModal: false,
    viewModalData: {},
    confirmModal: false,
    loading: true,
    elmLoading: false,
    generalSearch: {
      startDate: null,
      endDate: null,
      name: '',
      listType: [],
      description: '',
      targetLocation: [],
      status: []
    }
  }

  componentDidMount() {
    this.updateData();
    this.props.getListsUploadCsvRequest({account:this.props.currentAccount._id})
  }

  componentDidUpdate(prevProps) {
    if (this.props.currentAccount._id !== prevProps.currentAccount._id) this.updateData();
    if (this.props.listToDownload !== prevProps.listToDownload) this.saveFile();
  }

  componentWillUnmount() {
    this.props.resetListsPage()
  }

  getInitialState = () => {
    return {
      startDate: null,
      endDate: null,
      name: '',
      listType: 'admin request',
      description: '',
      targetLocation: [],
      status: []
    }
  }

  handleFilterChange = (name, value) => {
    let generalSearch = this.state.generalSearch
    
    this.setState({
      generalSearch: Object.assign(generalSearch, { [name]: value })
    })
  }

  performFilter = () => {
    this.updateData()
  }

  clearFilter = () => {
    this.setState({
      generalSearch: this.getInitialState()
    }, () => this.updateData())
  }

  updateData = (pagination = { limit: 10, page: 1 }) => {      
    let data = { account: this.props.currentAccount._id, ...pagination, page: pagination.page-1 };
    if(!pagination.sort) {
      delete data.sort
    }
    this.props.getListsRequest(data);
  }

  onUploadToggle = () => {
    this.props.toggleUpload()
  }

  onViewOpen = async (elm) => {
    this.setState({ elmLoading: true, viewModal: true })
    let data = null
    try {
      let response = await request(`${API_URL}/list-request/${elm._id}`, {
        method: 'get',
        queries: {
          account: this.props.currentAccount._id
        }
      })
      data = response.data
    } catch (err) {
      console.log(err.message)
    }
    this.setState({ elmLoading: false, viewModalData: data || {} })
  }

  onViewToggle = async () => {
    this.setState({ viewModal: !this.state.viewModal, viewModalData: {} })
  }


  fixStateName = (string) => {
    let newStr = string.trim();
    if (newStr.length === 2)
      return newStr.toUpperCase();
    if (newStr.length > 0){
      let correctString = newStr.toLowerCase().split(/\s+/).map(word => word[0].toUpperCase() + word.substring(1)).join(' ');    
      return correctString;
    }
    return newStr;
  };

  onUploadSubmit = () => {
    const { limit, page } = this.state.pagination;

    let stateColumnIndex = this.props.uploadCsvData.headers.indexOf('state');
    let uploadCsvData = {...this.props.uploadCsvData};
    if(!!~stateColumnIndex){
      let uploadCsvJson = [...this.props.uploadCsvData.csvJson];
      let fixedUploadCsvJson = uploadCsvJson.map( (el) => {            
        let elKeys = Object.keys(el);
        let fixedEl = {...el};
        fixedEl[elKeys[stateColumnIndex]] = this.fixStateName(fixedEl[elKeys[stateColumnIndex]]);
        return fixedEl 
      });      
      uploadCsvData.csvJson = fixedUploadCsvJson;
    }    
    let data = {
      ...uploadCsvData,      
      headers: this.props.uploadCsvData.headers.join(','),
      listType: 'contacts request',
      account: this.props.currentAccount._id, 
      addToCrmAutomatically: config.addToCrmAutomatically.toString(),
      limit,
      page: page-1
    }       
    if(data.name) delete data.listId    
    this.props.uploadListRequest(data)
  }

  saveFile = () => {
    const exportedFilename = 'export.csv'
    const blob = new Blob([this.props.listToDownload.csv], { type: 'text/csvcharset=utf-8' })
    if (navigator.msSaveBlob) {
      navigator.msSaveBlob(blob, exportedFilename)
    } else {
      var link = document.createElement("a")
      if (link.download !== undefined) {
        var url = URL.createObjectURL(blob)
        link.setAttribute("href", url)
        link.setAttribute("download", exportedFilename)
        link.style.visibility = 'hidden'
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
      }
    }
  }

  onListDownload = async (e, row) => {
    e.stopPropagation()

    const data = {
      account: this.props.currentAccount._id,
      listId: row._id
    }
    this.props.downloadListRequest(data)
  };

  onListEdit = async (e, row) => {
    e.stopPropagation();
    const {_id, name = '', description = ''} = row;
    const list = {
      name, description, _id
    };
    this.props.setEditListData(list);
    this.props.toggleEditListModal(true);
  };

  goToContactsList = (e, row) => {
    e.stopPropagation();
    this.props.history.push(`/contacts?listId=${row._id}`)
  };
    
  customActions = (cell, row, rowIndex, formatExtraData) => {
    return (
      <div className="flexbox">
        <Button className="icon-button" onClick={(e) => this.onListEdit(e, row)}>
          <EditIco/>
        </Button>
        <Button className="icon-button" onClick={(e) => this.onListDownload(e, row)}>
          <DownloadIcon/>
        </Button>
        <Button className="icon-button" onClick={(e) => this.goToContactsList(e, row)}>
          <UserIcon/>
        </Button>
      </div> 
    ) 
  }

  onSort = (field, order) => {
    if(field === 'date') field = 'createdAt';
    const boolOrder = order === 'desc'? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder};
    this.onPageChange( pagination );
  }

  onPageChange = (pagination) => {
    if(!pagination.sort && this.state.pagination.sort) pagination.sort = this.state.pagination.sort
    this.setState({ pagination})
    this.updateData( pagination )
  }

  columns = columnsFunc(this.onSort);

  render() {       
    const { page, pages, totalSize, data = [] } = this.props.data;
    const {configs, showMatchingPage,  uploadCsvData, uploadCsvErrors} = this.props;


    const customColumn = {
      dataField: "edit", 
      text: "",
      sort: false,
      formatter: this.customActions,
      headerAttr: { width: 80 },
      attrs: { className: "icon-cell" } 
    }

    const paginationOptions = {
      onPageChange: this.onPageChange,
      page: page +1,
      paginationSize: pages,
      totalSize: totalSize
    }

    const headers = configs.find(item=>{
      return item.name ==='csvFields'
    })
    

    const requiredHeaders = headers.value.filter(item => item.required === true).map(item=>{
      return item.name
    });
    
    const allHeaders = headers.value.map(item=>{
      return item
    });
    
    

    allHeaders.unshift('')
    
    return (
      <div className='lists-page'>
        { showMatchingPage ? <MatchingContacts 
          uploadCsvData={uploadCsvData}
          allLists={this.props.allLists || []} 
          onChangeUploadCsvData={this.props.onChangeUploadCsvData}
          onSubmit={this.onUploadSubmit}
          toggleMatchingPage={this.props.toggleMatchingPage}
          addUploadCsvErrors={this.props.addUploadCsvErrors}
          requiredHeaders={requiredHeaders}
          allHeaders={allHeaders}
          errors={uploadCsvErrors}
        />
        :(
          <React.Fragment>            
            <div className='list-table-title'>
              <h2 className="title">Lists</h2>
              {this.props.configs.find(config => config.name === 'createLists').value &&
                <div className='button-group'>
                  <DopeButton color="white" onClick={this.onUploadToggle}>Upload My List</DopeButton>
                  {/* TODO HIDE */}
                  {/* <DopeButton color="primary" onClick={this.onCreateToggle}>Create List Request</DopeButton> */}
                </div>
              }
            </div>          
              <DopeTable
                keyField="shortID"                
                data={data}
                columns={[ ...this.columns, customColumn ]}                
                rowEvents={{
                  onClick: (e, row, rowIndex) => {
                    const {_id} = row;
                    this.props.history.push(`lists/${_id}`);
                  }
                }}
                rowClasses="table-row"
                paginationOptions={paginationOptions}
                loading={this.props.loading}
              />

            <EditList
              currentAccount={this.props.currentAccount}
              data= {this.props.editListData}
              errors={this.props.editListError}
              addErrorsEditList={this.props.addErrorsEditList}          
              open={this.props.showEditListModal}
              toggleEditListModal={this.props.toggleEditListModal}
              onChangeEditList={this.props.onChangeEditList}
              editListRequest = {this.props.editListRequest}
              resetEditListForm={this.props.resetEditListForm}
            />
            
            <UploadModal
              open={this.props.uploadModal}
              toggle={this.onUploadToggle}
              errors={this.props.errors}
              uploadCsvData={this.props.uploadCsvData}
              onChange={this.props.onChangeUploadCsvData}
              toggleMatchingPage={this.props.toggleMatchingPage}
            />

            <ViewModal
              isAdmin={this.props.isAdmin}
              open={this.state.viewModal}
              toggle={this.onViewToggle}
              loading={this.state.elmLoading}
              viewModalData={this.state.viewModalData}
            />
          </React.Fragment>
        )}
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
    currentAccount: state.account.currentAccount,
    configs: state.dbConfigs.configs,
    listToDownload: state.lists.listToDownload,
    data: state.lists.data,
    loading: state.lists.isLoading,
    errors: state.lists.errors,
    uploadModal: state.lists.uploadModal,
    showMatchingPage: state.lists.showMatchingPage,
    allLists: state.lists.allLists,
    uploadCsvData: state.lists.uploadCsvData,
    uploadCsvErrors: state.lists.uploadCsvErrors,
    editListData: state.lists.editListData,
    editListError: state.lists.editListError,
    showEditListModal: state.lists.showEditListModal,
})

const mapDispatchToProps = {    
    ...listActions,
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Lists))
