import React from 'react'
import {    
    Form,
    FormGroup,
} from 'reactstrap';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeModalHeader } from '../../DopeModal/DopeModalHeader/DopeModalHeader'
import { DopeModalBody } from '../../DopeModal/DopeModalBody/DopeModalBody'
import { DopeModalFooter } from '../../DopeModal/DopeModalFooter/DopeModalFooter'
import { DopeModal } from '../../DopeModal/DopeModal'
import { DopeInput } from '../../DopeForms/DopeInput/DopeInput'
import { DopeTextarea } from '../../DopeForms/DopeTextarea/DopeTextarea'
import './CreateListModal.scss'

export const CreateListModal = (props) => {
  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <DopeModal className='create-list-modal' isOpen={props.open} toggle={props.toggle} centered>
        <DopeModalHeader toggle={props.toggle}>Create List</DopeModalHeader>
        <DopeModalBody>
          <Form onSubmit={props.onSubmit}>
            <FormGroup className="dope-form-group">
              <DopeInput
                required
                title='List Name'
                supText='*'
                error={props.errors.name}
                onChange={(e) => props.onChange(e, 'name')}
              />
            </FormGroup>
            <FormGroup className="dope-form-group">
              <div className='input-title'>Description</div>
              <DopeTextarea
                error={props.errors.description}                
                onChange={(e) => props.onChange(e, 'description')}
              />
            </FormGroup>
          </Form>
        </DopeModalBody>
        <DopeModalFooter>
          <DopeButton color="white" onClick={props.toggle}>Cancel</DopeButton>
          <DopeButton color="primary" onClick={props.onSubmit}>Create</DopeButton>
        </DopeModalFooter>
      </DopeModal>
    </div>
  )
}