import React from 'react';
import Editor from './Editor/Editor';
import Manager from './Manager/Manager';
import request from 'utils/request';
import html2canvas from 'html2canvas';
import { API_URL } from '../../config/config';

const new_template_id = 'new_yard_sign_template';

export default class extends React.Component {
    state = {
        category: 'yard-sign',
        loading: false,
        templates: []
    }

    componentDidMount() {
        this.getTemplates();
    }

    getTemplates = async () => {
        try {
            let response = await request(`${API_URL}/yard-sign-templates`, { method: 'get' });
            this.setState({ templates: response.data.yardSigns});
        } catch (err) {
            console.log(err);
        }
    }

    onSave = async (template) => {
        this.setState({ loading: true });
        try {
            template.thumbnail = (await html2canvas(document.getElementById(new_template_id))).toDataURL('image/png');
            await request(`${API_URL}/yard-sign-templates`, {
                method: 'post',
                body: { template }
            });
            this.getTemplates();
        } catch (err) {
            console.log(err);
        }
        this.setState({ loading: false });
    }

    render() {
        return (
            <div className='content'>
                <Manager templates={this.state.templates} onUpdated={this.getTemplates} />
                <Editor onSave={this.onSave} id={new_template_id} loading={this.state.loading} />
            </div>
        );
    }
}