import service from './service';
import { API_URL } from '../config/config';

export const getTrackingNumbersApi = (data) => {
  return service.get(`${API_URL}/tracking-number`,
  data)
}

export const createExistingNumbersApi = (data) => {
  return service.get(
    `${API_URL}/tracking-number?account=${data}`
  );
};

export const createTrackingNumberApi = (data) => {
  return service.post(
    `${API_URL}/tracking-number?account=${data.query.account}`,
    data.body
  );
};

export const createSearchNumbersApi = (data) => {
  return service.get(
    `${API_URL}/tracking-number/available-numbers?areaCode=${data.areaCode}&account=${data.account}`
  );
};

export const deleteTrackingNumberApi = (data) => {
  return service.delete(
    `${API_URL}/tracking-number/${data.params.trackingNumberId}?account=${data.query.account}`
  );
};

export const activateTrackingNumberApi = (data) => {
  return service.post(
    `${API_URL}/tracking-number/activate?account=${data.query.account}`,
    data.body
  );
};

export const deactivateTrackingNumberApi = (data) => {
  return service.post(
    `${API_URL}/tracking-number/deactivate?account=${data.query.account}`,
    data.body
  );
};

export const editTrackingNumberApi = (data) => {
  return service.patch(
    `${API_URL}/tracking-number/${data.params.trackingNumberId}?account=${data.query.account}`,
    data.body
  );
};
