export default function reducer(state = initialState, action) {
  switch (action.type) {
    case GET_NEIGHBORS_REQUEST:
      return {
        ...state,
        isLoading: true
      };
    case GET_NEIGHBORS_REQUEST_SUCCESS:
      return {
        ...state,
        neighborhoods: action.data.dataToFetch,
        searchDistance: action.data.distance,
        isLoading: false
      };
    case GET_NEIGHBORS_REQUEST_FAILED:
      return {
        ...state,
        isLoading: false
      };
    case CONVERT_ADDRESS_TO_LAT_LON_REQUEST:
      return {
        ...state,
        isLoading: true
      };
    case CONVERT_ADDRESS_TO_LAT_LON_REQUEST_SUCCESS:
      return {
        ...state,
        areaAddress: action.data,
        foundRealAddress: true,
        isLoading: false,
      };
    case CONVERT_ADDRESS_TO_LAT_LON_REQUEST_FAILED:
      return {
        ...state,
        searchError: 'Wrong address',
        isLoading: false,
      };
    case SET_AMOUNT_ADDRESSES:
      return {
        ...state,
        amountAddresses: action.data
      };
    case SET_CUSTOM_AMOUNT_ADDRESSES:      
      return {
        ...state,
        customAmountAddresses: action.data
      };
    case CHANGE_STEP:
      return {
        ...state,
        step: action.data,
      };
    case SET_POSTAGE_DATA:
      return {
        ...state,
        postage: action.data,
      };
    case SET_NEW_DELIVERY_ADDRESS:
      return {
        ...state,
        returnAddress: action.value,
      };
    case SET_SEARCH_ADDRESS:
      return {
        ...state,
        searchAddress: action.data,
        searchError: ''
      };
    case SET_NEIGHBORHOOD_SIZE:
      return {
        ...state,
        neighborhoodSize: action.data,
      };
    case SET_NEIGHBORHOOD_DESIGN:
      return {
        ...state,
        neighborhoodDesign: action.data,
      };
    case SET_NEIGHBORHOOD_CONTACT_INFO:
      return {
        ...state,
        neighborhoodEditor: {
          ...state.neighborhoodEditor,
          contactInfo:{
            ...state.neighborhoodEditor.contactInfo,
            [action.key]: action.value,
          }
        },
      };
    case SET_SHOW_CONFIRM_POPUP:
      return {
        ...state,
        showConfirmPopup: action.data,
      };
    // case SET_NEIGHBORS_SELECTED_IMAGES:
    //   return {
    //     ...state,
    //     neighborhoodEditor: {
    //       ...state.neighborhoodEditor,
    //       selectedImages: {
    //         ...state.neighborhoodEditor.selectedImages,
    //         ...action.data
    //       },
    //     },
    //   };
    case GET_NEIGHBORS_PREVIEW_IMG_REQUEST_SUCCESS:
      return {
        ...state,
        previewImg: action.data,
      };
    case GET_NEIGHBORS_LIST_REQUEST:
      return {
        ...state,
        isLoading: true
      };
    case GET_NEIGHBORS_LIST_REQUEST_SUCCESS:
      return {
        ...state,
        neighborhoodBlitzList: action.data,
        isLoading: false
      };
    case RESET_CREATE_NEIGHBORS:
      return {
        ...initialState,
      };
    case GET_NEIGHBORS_ITEM_REQUEST_SUCCESS:
      return {
        ...state,
        neighborhoods: action.data.dataToFetch,
        searchAddress: action.data.searchAddress,
        returnAddress: action.data.returnAddress,
        searchDistance: action.data.distance ? action.data.distance : 350,
        amountAddresses: action.data.searchCount,
        postage: action.data.postage,
        filters: {
          ...action.data.filters,
          homeValue: action.data.filters.homeValue ? JSON.parse(action.data.filters.homeValue) : '',
        },
        previewImg: {
          ...state.previewImg,
          ...action.data.images,
        },
        neighborhoodDesign: action.data.status === 'design-request' ? 'askDesign' : 'ownDesign',
        // neighborhoodSize: action.data.size ? action.data.size : '6x9',
        neighborhoodEditor:{
          ...state.neighborhoodEditor,
          templateEdit:{
            ...state.neighborhoodEditor.templateEdit,
            ...action.data.templates,
          },
          contactInfo:{
            ...state.neighborhoodEditor.contactInfo,
            ...action.data.designRequest,
          }
        },
        status: action.data.status,
      };
    case RESET_SELECTED_NEIGHBORS:
      return {
        ...state,
        neighborhoods: []
      };
    case CHANGE_NEIGHBORS_FILTERS:
      return {
        ...state,
        filters: action.data,
      };    
    case SET_NEIGHBORS_TEMPLATE_EDIT:
      return {
        ...state,
        neighborhoodEditor: {
          ...state.neighborhoodEditor,
          templateEdit: {
            ...state.neighborhoodEditor.templateEdit,
            front: state.neighborhoodEditor.templateSample[action.data].front,
            back: state.neighborhoodEditor.templateSample[action.data].back,
          },
        },
      };
    case SET_MODIFICATED_NEIGHBORS_TEMPLATE_EDIT:      
      return {
        ...state,
        neighborhoodEditor: {
          ...state.neighborhoodEditor,
          templateEdit: {
            ...state.neighborhoodEditor.templateEdit,
            ...action.data
          }, 
        },
      };
    case ADD_ERRORS_NEIGHBORS:
      return {
        ...state,
        neighborhoodEditor:{
          ...state.neighborhoodEditor,
          errors: {
            ...state.errors,
            [action.key]: action.value,
          },
        }        
      };
    case CHANGE_NEIGHBORS_POSTCARD_TEMPLATE_TAB_INDEX:
      return {
        ...state,
        neighborhoodEditor:{
          ...state.neighborhoodEditor,
          templateTabIndex: action.data,
        }        
      };
    case SELECT_NEIGHBORS_MORPHEUS_TEMPLATE:
      return{
        ...state,
        neighborhoodEditor:{
          ...state.neighborhoodEditor,
          selectedTemplate: action.template._id,
          templateEdit: {
            ...state.neighborhoodEditor.templateEdit,
            front: action.template.front,
            back: action.template.back,
          },  
          templateSample: {
            ...state.neighborhoodEditor.templateSample,
            ...action.template.templateSample
          },        
        }  
      };
    case SELECT_NEIGHBORS_ONE_PICTURE_TEMPLATE:
      return{
        ...state,
        neighborhoodEditor:{
          ...state.neighborhoodEditor,
          selectedTemplate: "",
          templateEdit: {
            ...state.neighborhoodEditor.templateEdit,
            front: null,
            back: null,
          },          
        }  
      };
    case SET_CHANGE_NEIGHBORS_POSTCARD_STATUS:
      return{
        ...state,
        changeNeighborsPostcardStatus: {
          ...state.changeNeighborsPostcardStatus,
          ...action.data,
        }
      };
    case GET_RECENT_NEIGHBORHOODBLITZ_POSTCARD_REQUEST_SUCCESS:         
      return{
        ...state,
        neighborhoodBlitzRecentTemplates:{
          ...state.neighborhoodBlitzRecentTemplates,
          ...action.data,          
        }
      };
    case SHOW_RECENT_NEIGHBORHOODBLITZ_POSTCARDS_LOADER:
      return{
        ...state,
        neighborhoodBlitzRecentTemplates:{
          ...state.neighborhoodBlitzRecentTemplates,
          loading: action.data,          
        }
      }
    case GET_MY_NEIGHBORHOODBLITZ_POSTCARD_REQUEST_SUCCESS:          
      return{
        ...state,
        neighborhoodBlitzMyTemplates:{
          ...state.neighborhoodBlitzMyTemplates,
          ...action.data,          
        }
      };
    case SHOW_MY_NEIGHBORHOODBLITZ_POSTCARDS_LOADER:
      return{
        ...state,
        neighborhoodBlitzMyTemplates:{
          ...state.neighborhoodBlitzMyTemplates,
          loading: action.data,          
        }
      }
    default:
      return state
  }
}