import React from 'react';
import { Card, CardBody } from 'reactstrap';
import {pathOr} from 'ramda';
import moment from 'moment';
import 'moment-timezone';

export default function AccountBar(props) {  
  // const packageName = pathOr('none', ['mainSubscription','package', 'meta', 'name'], props.balance);  
  const activated = moment(props.activated).tz(props.timeZone).format("D MMM YYYY");
  // const expirationDate = moment(pathOr(0, ['mainSubscription','expirationDate'], props.balance)).tz(props.timeZone).format("D MMM YYYY");
  const minutes = pathOr(0, ['minutes'], props.balance);
  const numbers = pathOr(0, ['trackingNumbers'], props.balance).length;
  // const discount = pathOr(0, ['mainSubscription','package', 'meta','dataPurchasesDiscount'], props.balance);  
  return (
    <Card>
      <CardBody>
        <div className='account-header'>          
          <div className='header-item'>
            <h5>Joined</h5>
            <p>{activated}</p>
          </div>          
          <div className='header-item'>
            <h5>Minutes Remaining</h5>
            <p>{minutes}</p>
          </div>
          <div className='header-item'>
            <h5>Active Phone Numbers</h5>
            <p>{numbers}</p>
          </div>          
        </div>
      </CardBody>
    </Card>
  );
}
