import {
  GET_ADMIN_USING_DATA_REQUEST,
  GET_ADMIN_USING_DATA_SUCCESS,
  GET_ADMIN_USING_DATA_FAILED,
  RESET_ADMIN_USING_DATA_PAGE,  
  SET_ADMIN_USING_DATA_LOADING,
  SET_USING_DATA_PAGE,
  CHANGE_USING_DATA,
} from 'actions/adminUsingData';

let initialState = {  
  loading: false,
  usingData: {
    data:[],
    page:0,
    limit:10,
    totalSize:0,
  },
  // searchValue: '',
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case GET_ADMIN_USING_DATA_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case GET_ADMIN_USING_DATA_SUCCESS:     
      return {
        ...state,
        usingData: action.data,
        loading: false,
      };
    case GET_ADMIN_USING_DATA_FAILED:
      return {
        ...state,
        loading: false,
      };
    case RESET_ADMIN_USING_DATA_PAGE:
      return {
        ...state,
        ...initialState,
      };    
    case SET_ADMIN_USING_DATA_LOADING:      
      return {
        ...state,
        loading: action.loading,
      };
    case SET_USING_DATA_PAGE:
      return {
        ...state,
        usingData: {
          ...state.usingData,
          page: action.data,
        },
      };
    case CHANGE_USING_DATA:
      return {
        ...state,
        usingData: {
          ...state.usingData,
          data: action.data,
        },
      };
    default:
      return state;
  }
}
