import React, { Component } from 'react';
import './DashboardCard.scss';

class DashboardCard extends Component {
    render() {
        const { countResult, name, callsCount, color, themeColorBg, Img, title = '' } = this.props;

        return (
            <div className='work-result__item' title={title}>
                <div
                    className={`work-result__item--top ${themeColorBg ? 'theme-bg' : ''}`}
                    style={{ backgroundColor: color }}
                >
                    <div className="work-result-img">
                        <Img/>
                    </div>
                    <div className="work-result-content">
                        <p className="work-result-count">{countResult}</p>
                        <p className="work-result-title">{name}</p>
                    </div>
                </div>
                <div className='work-result__item--bottom'>
                    <p>{callsCount}</p>
                    <p>calls received</p>
                </div>
            </div>
        );
    }
}

export default DashboardCard;