import React, {useState} from 'react'
import { DopeModalHeader } from '../../DopeModal/DopeModalHeader/DopeModalHeader'
import { DopeModalBody } from '../../DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeModal } from '../../DopeModal/DopeModal'
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import DopeRadioGroup from 'components/DopeForms/DopeRadioGroup';
import './Delete.scss'


export const DeleteModal = (props) => {
  let { lists=[] } = props.filters; 

  const [deleteContactFromLists, setDeleteContactFromLists] = useState('allLists');

  const listID = () => {    
    if (deleteContactFromLists === 'selectedLists')    
      return lists[0]._id
    else
      return null
  }

  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <DopeModal className='DeleteContactModal' isOpen={props.open} toggle={props.toggle} centered>
        <DopeModalHeader>{props.quantity>1 ? "Delete Contacts?" : "Delete Contact?"}</DopeModalHeader>
          { (lists.length === 1) ? (
              <DopeModalBody>
                <div className="radioBody">
                  <DopeRadioGroup        
                    data={[
                      {
                        value: "allLists",
                        label: `${props.quantity>1 ? "Delete contacts" : "Delete this contact"} from all lists`                       
                      },
                      {
                        value: "selectedLists",
                        label: "Delete only from selected list and keep in other lists"                      
                      }
                    ]}
                    vertical
                    name="deleteContactFromLists"
                    selectedValue={deleteContactFromLists}
                    onChange={(e) => setDeleteContactFromLists(e)}				             
                  />
                </div>
              </DopeModalBody>
            )
            :
            (
              <DopeModalBody>
                <div className="fromAllBody">                              
                  {`${props.quantity>1 ? "These contacts" : "This contact"} will be deleted from all lists.`}                    
                </div>
              </DopeModalBody>
            )
          }
        <DopeModalFooter className='dope-footer'>
          <DopeButton color="white" onClick={props.toggle}>Cancel</DopeButton>
          <DopeButton color="primary" onClick={() => props.onSubmit(listID())}>Delete</DopeButton>
        </DopeModalFooter>
      </DopeModal>
    </div>
  )
}