import { all, call, put, takeLatest } from 'redux-saga/effects';
import { toast } from 'react-toastify';
import * as generalActions from 'actions/general';
import * as eddmActions from '../actions/eddm';
import {pathOr} from 'ramda';

import {  
  getEddmListApi,
  getEddmItemApi,
  deleteEddmItemApi,
  getMapRoutesByZipApi,
  getEddmPreviewImgApi,
  sendEddmApi,
  updateEddmApi,
  getRoutesStatByZipApi,
  saveAsMyEddmTemplateApi,  
  getRecentEddmPostcardsApi,
  getMyEddmPostcardsApi,
  deleteMyEddmPostcardApi,
} from 'requests/eddm';
import {push} from "connected-react-router";

export function* getMapRoutesByZip(action) {
  try {    
    yield put({
      type: eddmActions.SET_MAP_POSITION_LOADING,
      data: true,
    });
    let clientWidth = document.querySelector('.leaflet-container') ? document.querySelector('.leaflet-container').clientWidth : document.querySelector('.eddmRouteTableContainer').clientWidth;
    let clientHeight = document.querySelector('.leaflet-container') ? document.querySelector('.leaflet-container').clientHeight : 462;//389;//changed positioning
    
    const response = yield call(getMapRoutesByZipApi, action.data);
    
    if(response.data.results[0].value.features.length){
     
      const x = [];
      const y = [];

      response.data.results[0].value.features.map((multiPoligon,i)=> {
        multiPoligon.geometry.paths.map((poliPoligon, j) => {
          poliPoligon.map(item => {
            x.push(item[0]);
            y.push(item[1]);
          })
        });
      });
      let minX = Math.min(...x);
      let minY = Math.min(...y);
      let maxX = Math.max(...x);
      let maxY = Math.max(...y);
      let width = Math.abs(maxX - minX);      
      let height = Math.abs(maxY - minY);
      let lat = (maxY + minY)/2;
      let lng = (maxX + minX)/2;      
      let zoomX = (224153340 * width)/clientWidth; 
      let zoomY = (248214343 * height)/clientHeight;     
      //Contain by X and Y  
      let zoomM = Math.max(zoomX,zoomY);
      //Cover by X
      // let zoomM = zoomX; 
      //Cover by Y
      // let zoomM = zoomY;       
      let zoom = 7;      
      if (zoomM >= 610984)
        zoom = 8;
      if (zoomM >= 305492 && zoomM < 610984)
        zoom = 9;
      if (zoomM >= 152746 && zoomM < 305492)
        zoom = 10;
      if (zoomM >= 76373 && zoomM < 152746)
        zoom = 11;
      if (zoomM >= 38187 && zoomM < 76373)
        zoom = 12;
      if (zoomM >= 19093 && zoomM < 38187)
        zoom = 13;
      if (zoomM >= 9547 && zoomM < 19093)
        zoom = 14;
      if (zoomM >= 4773 && zoomM < 9547)
        zoom = 15;
      if (zoomM >= 2387 && zoomM < 4773)
        zoom = 16;
      if (zoomM >= 1193 && zoomM < 2387)
        zoom = 17;
      if (zoomM >= 596 && zoomM < 1193)
        zoom = 18;
      if (zoomM >= 298 && zoomM < 596)
        zoom = 19;  
      if (zoomM < 298)
        zoom = 20;
      yield put({
        type: eddmActions.GET_MAP_ROUTES_BY_ZIP_REQUEST_SUCCESS,
        data: {          
          featureCollection: response.data.results[0].value,
          mapPosition: {
            lat,
            lng,
          },
          zoom: zoom,
        }
      });
    } else{      
      yield put({
        type: eddmActions.SET_EDDMROUTEMAP_ERRORS,
        errors: { zip: 'Wrong ZIP code' },
      });      
    }
    yield put({
      type: eddmActions.SET_MAP_POSITION_LOADING,
      data: false,
    });
  } catch (error) {
    console.log("error", error);
    yield put({
      type: eddmActions.SET_MAP_POSITION_LOADING,
      data: false,
    });
    yield put({
      type: eddmActions.GET_MAP_ROUTES_BY_ZIP_REQUEST_FAILED,
      error
    });
  }
}

export function* getRoutesStatByZip(action) {
  try { 
    const response = yield call(getRoutesStatByZipApi, action.data);
    if(response && response.status === 200){      
      yield put({
        type: eddmActions.GET_ROUTES_STAT_BY_ZIP_REQUEST_SUCCESS,           
        data: {
          featureCollection: response.data,
        }     
      });       
    }    
  } catch (error) {
    console.log("error", error);    
    yield put({
      type: eddmActions.GET_ROUTES_STAT_BY_ZIP_REQUEST_FAILED,
      error
    });
  }
}

export function* getEddmList(action) {
  try {
    const response = yield call(getEddmListApi, action.data);
    const data = { ...action.data };
    yield put({
      type: eddmActions.GET_EDDM_LIST_REQUEST_SUCCESS,
      data: { ...response.data, ...data },
    });
  } catch (e) {
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't get eddm list", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: eddmActions.GET_EDDM_LIST_REQUEST_FAILED, e });
  }
}
