export function* getEddmPreviewImg(action) {   
  try {    
    
    const [response1,response2] = yield all([
      call(getEddmPreviewImgApi, {template: action.data.body.template.front, bucket:action.data.body.bucket, side: 'front'}),      
      call(getEddmPreviewImgApi, {template: action.data.body.template.back, bucket:action.data.body.bucket, side: 'back'})
    ]); 
   

    const previewImg = {
      front: {
        mini: response1.data.images.mini,
        full: response1.data.images.full
      },
      back: {
        mini: response2.data.images.mini,
        full: response2.data.images.full
      }
    };

    yield put({
      type: eddmActions.GET_EDDM_PREVIEW_IMG_REQUEST_SUCCESS,
      data: previewImg
    });
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
    
    yield put(eddmActions.setShowConfirmPopup(true))
    
  } catch (e) { 
    console.log("error", e);
    let errorMessage = pathOr("Error. Cann't get previewImg", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: eddmActions.GET_EDDM_PREVIEW_IMG_REQUEST_FAILED, e });    
  }
}

export function* saveEddmAsDraft(action) {
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true }); 
  try {
    const response = yield call(sendEddmApi, action.data);
    if(response && response.status === 200 && action.callback) {
      action.callback();
    }
    yield put({
      type: eddmActions.SAVE_EDDM_AS_DRAFT_REQUEST_SUCCESS
    });
    yield put(push('/eddm'));
    toast.success(`You have successfully saved eddm as draft`);

  } catch (e) {
    if(true && action.callback) {  //todo check 400 answer or response
      action.callback();
    } 
    let errorMessage = pathOr("Error. Cann't save eddm", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
    yield put({ type: eddmActions.SAVE_EDDM_AS_DRAFT_REQUEST_FAILED });
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false }); 
}

export function* saveEddmAsDraftSummary(action) {
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true }); 
  try {
    const response = yield call(sendEddmApi, action.data);    
    if(response && response.status === 200) {
      yield put(push(`/eddm/${response.data._id}/edit`));
      yield put(eddmActions.setNextStep(3));
    }    
    yield put({
      type: eddmActions.SAVE_EDDM_AS_DRAFT_REQUEST_SUCCESS
    });
    toast.success(`You have successfully saved eddm settings`);
  } catch (e) {    
    yield put({ type: eddmActions.SAVE_EDDM_AS_DRAFT_REQUEST_FAILED });
    let errorMessage = pathOr("Error. Cann't save eddm", ['response', 'data', 'error', 'dopeMessage'], e);
    toast.error(errorMessage);
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false }); 
}

export function* updateEddm(action) {  
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true });
  try {
    const response = yield call(updateEddmApi, action.data);
    if(response && response.status === 200 && action.callback) {
      action.callback();
    }
    yield put({
      type: eddmActions.UPDATE_EDDM_REQUEST_SUCCESS
    });
    if(action.data.body.status==='in-progress'){
      toast.success(`You have successfully sent eddm`);
    }else{
      toast.success(`You have successfully updated eddm`);
    }    
    yield put(push('/eddm'));
  } catch (e) {
    if(true && action.callback) {  //todo check 400 answer or response
      action.callback();
    }
    console.log("error", e);
    yield put({ type: eddmActions.UPDATE_EDDM_REQUEST_FAILED, e });
    if(action.data.body.status==='in-progress'){      
      let errorMessage = pathOr("Error. Cann't send eddm", ['response', 'data', 'error', 'dopeMessage'], e);
      toast.error(errorMessage);
      yield put(push('/eddm'));
    }else{
      let errorMessage = pathOr("Error. Cann't update eddm", ['response', 'data', 'error', 'dopeMessage'], e);
      toast.error(errorMessage);
    }  
  }
  yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false });
}

