export default all([  
  takeLatest(eddmActions.GET_MAP_ROUTES_BY_ZIP_REQUEST, getMapRoutesByZip),
  takeLatest(eddmActions.GET_EDDM_LIST_REQUEST, getEddmList),
  takeLatest(eddmActions.GET_EDDM_ITEM_REQUEST, getEddmItem),
  takeLatest(eddmActions.DELETE_EDDM_ITEM_REQUEST, deleteEddmItem),
  takeLatest(eddmActions.GET_EDDM_PREVIEW_IMG_REQUEST, getEddmPreviewImg), 
  takeLatest(eddmActions.SAVE_EDDM_AS_DRAFT_REQUEST, saveEddmAsDraft),
  takeLatest(eddmActions.UPDATE_EDDM_REQUEST, updateEddm),
  takeLatest(eddmActions.SAVE_EDDM_AS_DRAFT_SUMMARY_REQUEST, saveEddmAsDraftSummary),
  takeLatest(eddmActions.UPDATE_EDDM_SUMMARY_REQUEST, updateEddmSummary),
  takeLatest(eddmActions.GET_ROUTES_STAT_BY_ZIP_REQUEST, getRoutesStatByZip),  
  takeLatest(eddmActions.SAVE_AS_MY_EDDM_TEMPLATE_REQUEST, saveAsMyEddmTemplate),
  takeLatest(eddmActions.GET_RECENT_EDDM_POSTCARD_REQUEST, getRecentEddmPostcards), 
  takeLatest(eddmActions.GET_MY_EDDM_POSTCARD_REQUEST, getMyEddmPostcards), 
  takeLatest(eddmActions.DELETE_MY_EDDM_POSTCARD_REQUEST, deleteMyEddmPostcard),  
]);


