import React from "react";
import { Container, Col, Row } from "reactstrap";
import moment from 'moment';
import ReactTooltip from 'react-tooltip';
import { Card, CardHeader, CardBody } from "reactstrap";
import DopeRadio from "components/DopeForms/DopeRadio";
// import { DopeCheckbox } from "components/DopeForms/DopeCheckbox/DopeCheckbox";
import OrderSummary from "./OrderSummary";
import ReturnAddress from "./ReturnAddress";
import PricingModal from "./PricingModal";
import CreditCard from "../../../components/CreditCard/CreditCard";
import { connect } from "react-redux";
import request from "../../../utils/request";
import { pathOr } from "ramda";
import { DopeTable } from "components/DopeTable/DopeTable";
import { API_URL } from "../../../config/config";
import { columnsFunc } from "./config";
import "./Payment.scss";

class Payment extends React.Component {
  state = {
    couponValue: "",
    isCouponValid: null,
    coupon: {},
    postcardPricePerContact: {},
    showPricingModal: false
  };

  onCouponChange = value => {
    this.setState({
      couponValue: value
    });
  };

  calculatePricePerSign = (pricePerSign, customCoupon) => {
    const coupon = customCoupon || this.coupon;

    if (coupon.percentageDiscount) {
      return Math.max(pricePerSign * (1 - coupon.percentageDiscount / 100), 0);
    }
    if (coupon.accountDiscount) {
      return Math.max(pricePerSign - coupon.accountDiscount, 0);
    }

    return pricePerSign;
  };

  getPricingData = () => {    
    let pricingData = [];
    if (this.props.userOwnPrice && this.props.userOwnPrice.postcardPrice && this.props.userOwnPrice.postcardPrice.length){
      pricingData = this.props.userOwnPrice.postcardPrice;
    }
    else {
      pricingData = this.props.price.find(item => item.name === "postcardPrice").value;
    }
    return pricingData;
  };

  cost = () => {    
    const quantity = pathOr(
      0,
      ["data", "allContacts", "data", "length"],
      this.props
    );
    let priceRange = [];
    let price = "";

    if (this.props.userOwnPrice && this.props.userOwnPrice.postcardPrice && this.props.userOwnPrice.postcardPrice.length){      
      priceRange = this.props.userOwnPrice.postcardPrice;
    }
    else{      
      priceRange = this.props.price.find(item => item.name === "postcardPrice").value;
    }

    let selectedPostage = this.props.data.postage;
    switch (selectedPostage) {
      case "first-class":
        priceRange.forEach(item => {
          if (item.min && item.max) {
            if (item.min <= quantity && quantity <= item.max)
              return (price = item["first-class-price"]);
          } else if (item.min) {
            if (item.min <= quantity) price = item["first-class-price"];
          }
        });
        break;
      case "standard":
      default:
        priceRange.forEach(item => {
          if (item.min && item.max) {
            if (item.min <= quantity && quantity <= item.max)
              return (price = item.price);
          } else if (item.min) {
            if (item.min <= quantity) price = item.price;
          }
        });
        break;
    }

    const cost = quantity * price;

    if (cost && Number(cost)) return (cost/100).toFixed(2);
    else return (price/100).toFixed(2);
  };

  costPerOne = (selectedPostage) => {    
    const quantity = pathOr(
      0,
      ["data", "allContacts", "data", "length"],
      this.props
    );
    let priceRange = [];
    let price = "";

    if (this.props.userOwnPrice && this.props.userOwnPrice.postcardPrice && this.props.userOwnPrice.postcardPrice.length){
      priceRange = this.props.userOwnPrice.postcardPrice;      
    }
    else{
      priceRange = this.props.price.find(item => item.name === "postcardPrice").value;      
    }    
    switch (selectedPostage) {
      case "first-class":
        priceRange.forEach(item => {
          if (item.min && item.max) {
            if (item.min <= quantity && quantity <= item.max)
              return (price = item["first-class-price"]);
          } else if (item.min) {
            if (item.min <= quantity) price = item["first-class-price"];
          }
        });
        break;
      case "standard":
      default:
        priceRange.forEach(item => {
          if (item.min && item.max) {
            if (item.min <= quantity && quantity <= item.max)
              return (price = item.price);
          } else if (item.min) {
            if (item.min <= quantity) price = item.price;
          }
        });
        break;
    }
    return (price / 100).toFixed(2);
  };

  // total = () => {
  //     return (
  //         this.calculatePricePerSign(
  //             this.state.postcardPricePerContact,
  //             this.state.coupon,
  //         ) * (this.props.data.contacts.length || 0)
  //     );
  // };

  useCoupon = () => {
    if (this.state.couponValue) {
      return request(`${API_URL}/coupon/${this.state.couponValue}/check`, {
        method: "get"
      })
        .then(data => data.data)
        .then(
          coupon => {
            this.props.onChange("coupon", this.state.couponValue);
            this.props.onChange(
              "pricePerSign",
              this.calculatePricePerSign(this.props.data.amount, coupon)
            );

            this.setState({ isCouponValid: true, coupon });
          },
          () => {
            this.setState({ isCouponValid: false });
          }
        );
    }
  };
  onSort = (field, order) => {
    const boolOrder = order === "desc" ? 1 : -1;
    const newOrder = `${field}:${boolOrder}`;
    const pagination = { page: 1, limit: 10, sort: newOrder };
    this.props.paginationOptions.onPageChange(pagination);
  };

  togglePricingModal = () => {
    this.setState(prevState => ({
      showPricingModal: !prevState.showPricingModal
    }));
  };

  columns = columnsFunc(this.onSort);

  render() {
    const props = this.props;      
    return (
      <React.Fragment>
        <Container fluid={true}>
          <Row className="template-preview">
            <Col md={6} className="front-postcard">
              <img
                // src={`https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcardImages/${this.props.data.images.front.mini}`}
                src={this.props.data.images.front.mini}
                alt="yard sign preview"
              />
            </Col>
            <Col md={6} className="back-postcard">
              <img
                // src={`https://${S3_BUCKET_PUBLIC_NAME}.s3.amazonaws.com/postcardImages/${this.props.data.images.back.mini}`}
                src={this.props.data.images.back.mini}
                alt="yard sign preview"
              />
            </Col>
          </Row>
          <div className="contacts-table">
            <DopeTable
              className="contacts-table striped"
              keyField="_id"
              data={props.data.contacts.data}
              columns={[...this.columns]}
              paginationOptions={props.paginationOptions}
              loading={props.data.contacts.loading}
            />
          </div>
          <Card className="pricing-card">
            <CardHeader>Pricing</CardHeader>
            <CardBody>
              <div className="pricing-radio-container">
                <div className="pricing-radio-row">
                  <DopeRadio
                    name="pricing"
                    checked={props.data.postage === "standard"}
                    value="standard"
                    label="Standard Postage"
                    onChange={() => {
                      this.props.setPostageData("standard");
                    }}
                  />
                  <span className="price-per-text">${this.costPerOne("standard")} / postcard</span>
                  <span className="delivery-days">Estimated Delivery: {moment(new Date()).add(7, 'days').format('MMM DD')} - {moment(new Date()).add(21, 'days').format('MMM DD')}</span>
                  <span className="tooltips-text">
                    <span data-tip data-for={`tooltips-standard`}>What is Standard type?</span>
                    <ReactTooltip          
                      className="pricing-tooltip"          
                      html={true}                       
                      id={`tooltips-standard`}
                    >  
                      {
                        `<span>Standard mail is not forwarded or returned if undeliverable unless a special endorsement is used. Standard mail will typically be delivered in under 5 business days locally, but nationwide it could take 2 to 3 weeks. It should not be used for time-sensitive mail. As with Presorted First-class, to receive the lower postage rate, the addresses must be NCOA updated, CASS certified, and put into presort order.</span>`
                      }
                    </ReactTooltip>
                  </span>
                </div>
                <div className="pricing-radio-row">
                  <DopeRadio
                    name="pricing"
                    checked={props.data.postage === "first-class"}
                    value="first-class"
                    label="First Class Postage"
                    onChange={() => {
                      this.props.setPostageData("first-class");
                    }}
                  />
                  <span className="price-per-text">${this.costPerOne("first-class")} / postcard</span>
                  <span className="delivery-days">Estimated Delivery: {moment(new Date()).add(4, 'days').format('MMM DD')} - {moment(new Date()).add(6, 'days').format('MMM DD')}</span>
                  <span className="tooltips-text">
                    <span data-tip data-for={`tooltips-first-class`} >What is First class type?</span>
                    <ReactTooltip          
                      className="pricing-tooltip"          
                      html={true} 
                      id={`tooltips-first-class`}
                    >                       
                      {
                        `<span>USPS first-class delivery is given priority over standard mail, and local mail will typically be delivered in 2-3 days. Nationally, first-class mail should be delivered in about 4 days. First-class mail postage includes forwarding and return services with no additional charge.</span>`
                      }
                    </ReactTooltip>
                  </span>
                </div>
              </div>
              <div className="pricing-link-container">
                <span
                  className="view-pricing-link"
                  onClick={this.togglePricingModal}
                >
                  View Pricing
                </span>
              </div>
              {/* Uncomment this for sending additional postcard to my address when BACK will be ready*/}
              {/* <div>
                <DopeCheckbox
                  label="Send an additional postcard to my address"
                  checked={props.data.sendingToMyAddress === true}
                  onChange={() => {
                    this.props.setSendingToMyAddress(
                      !props.data.sendingToMyAddress
                    );
                  }}
                />
              </div> */}
            </CardBody>
          </Card>
          <Row>
            <Col xl={4}>
              <OrderSummary
                amount={props.data.allContacts.data.length}
                cost={this.cost()}
                total={this.cost()}
                useCoupon={this.useCoupon}
                onCouponChange={this.onCouponChange}
                couponVal={this.state.couponValue}
                orderName={this.props.data.name}
                setOrderName={props.setOrderName}
                errors={this.props.errors}
                addErrors={props.addErrors}
              >
                Postcard Order Summary
              </OrderSummary>
            </Col>
            <Col xl={4}>
              <ReturnAddress
                data={props.data}
                onChange={props.updateAddresses}
                addErrors={props.addErrors}
                errors={props.errors}
              >
                Return address
              </ReturnAddress>
            </Col>
            <Col xl={4}>
              <CreditCard
                currentAccount={props.currentAccount}
                onChange={props.selectCreditCard}
                data={props.data}
                errors={props.errors}
                addErrors={this.props.addErrors}
                view={'twoRows'}
              >
                Payment Information
              </CreditCard>
            </Col>
          </Row>
        </Container>
        <PricingModal
          open={this.state.showPricingModal}
          toggle={this.togglePricingModal}
          // data={props.userOwnPrice ? props.userOwnPrice.postcardPrice : null}
          data={this.getPricingData()}
        />
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => ({
  currentAccount: state.account.currentAccount,
  price: state.dbConfigs.configs,  
});

export default connect(mapStateToProps)(Payment);
