import React,{ useState } from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalBody } from 'components/DopeModal/DopeModalBody/DopeModalBody';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { FormGroup } from 'reactstrap';
import { useSelector, useDispatch } from 'react-redux';
// import CreditCard from "components/CreditCard";
// import {setDefaultSource} from "actions/account";
// import {addErrorsPostCard} from "actions/createPostCard";

import {
  buyNeighborsListOfContactsRequest,
} from "actions/neighborhoodBlitz";

import './SaveListPopup.scss'

export default function SaveListPopup({isOpen, toggle, changeStep, neighborhoods}) {
  const dispatch = useDispatch();

  const [listName, setListName] = useState('');
  const currentAccount = useSelector(state => state.account.currentAccount);
  // const data = useSelector(state => state.createPostCard.data);
  // const price = useSelector(state => state.dbConfigs.configs).find(item => item.name === "colePrices").value;
  // const totalPrice = (neighborhoods.length * price).toFixed(2);
  const [listNameError, setListNameError] = useState('');
  const step = useSelector(state => state.neighborhoodBlitz.step);

  const saveAndContinue = () => {
    if(listName===''){
      setListNameError("List's name can't be empty");
      return;
    }
    const data = {
      body: {
        listName: listName,
        cardId: currentAccount.default_source,
        contacts: neighborhoods,
      },
      account: currentAccount._id
    };
    if(step === 1) dispatch(buyNeighborsListOfContactsRequest(data, () => dispatch(changeStep(2))));
    if(step === 2) dispatch(buyNeighborsListOfContactsRequest(data, toggle));
  };

  return (
    <DopeModal
      isOpen={isOpen}
      centered
      toggle={toggle}
      className="SaveListPopup"
    >
      <DopeModalHeader className='dope-modal-header' toggle={toggle}>
        Do you want to save this list to your account?
      </DopeModalHeader>
      <DopeModalBody>
        <FormGroup className="dope-form-group">
          <DopeInput
            value={listName}
            name='listName'
            title='List Name'
            error={listNameError}
            onChange={e => {
              setListNameError('');
              setListName(e.target.value);
            }}
          />
        </FormGroup>
        {/*<CreditCard*/}
        {/*  currentAccount={currentAccount}*/}
        {/*  selected={currentAccount.default_source}*/}
        {/*  onChange={(card) => dispatch(setDefaultSource(card.id))}*/}
        {/*  data={data}*/}
        {/*  errors={{}}*/}
        {/*  addErrors={() => dispatch(addErrorsPostCard())}*/}
        {/*>*/}
        {/*  Payment Information*/}
        {/*</CreditCard>*/}
        {/*<p>Contacts: {neighborhoods.length}</p>*/}
        {/*<p>Total: ${totalPrice}</p>*/}
        {/*{totalPrice < 5 && (*/}
        {/*  <div className='small error-text'>*/}
        {/*    Min price should be 5$. Please add more contacts*/}
        {/*  </div>*/}
        {/*)}*/}
      </DopeModalBody>
      <DopeModalFooter>
        {step === 1 && (
          <DopeButton
            color='white'
            onClick={() => dispatch(changeStep(2))}
          >
            Continue without saving
          </DopeButton>
        )}
        <DopeButton
          color='primary'
          onClick={saveAndContinue}
          // disabled={totalPrice < 5 || !currentAccount.default_source}
        >
          {/*{step === 1 ? 'Save And Continue' : 'Buy List'}*/}
          Save And Continue
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>
  );
}
