import React from 'react';
import { Label, Form, FormGroup } from 'reactstrap';
import { Link } from 'react-router-dom';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeInputGroup } from 'components/DopeForms/DopeInputGroup/DopeInputGroup';
import { InputGroupAddon } from 'reactstrap';
import { LOG_IN } from 'utils/constants';
import { ReactComponent as Ico } from 'assets/img/visibility.svg';
import AuthContainer from 'components/AuthContainer';
import './SignUpForm.scss';

export default function SignUpForm(props) {
  // console.log("SignUpForm:", props);
  return (
    <AuthContainer>
      <Form onSubmit={props.onSubmit} className='sign-up-form'>
        <div className='auth-form-title'>Sign Up</div>
        <FormGroup className="dope-form-group">
          <DopeInput
            id='email'
            type='text'
            name='email'
            title='Email'
            supText='*'
            error={props.errors.email}
            value={props.data.email}            
            onChange={e => props.onChange('email', e.target.value.trim())}
          />          
        </FormGroup>
        <FormGroup className="dope-form-group">
          <Label className='input-title' htmlFor='password'>
            Password <sup>*</sup>
          </Label>
          <DopeInputGroup>
            <DopeInput
              id='password'
              name='password'
              type={props.passwordVisibility ? 'text' : 'password'}              
              onChange={e => props.onChange('password', e.target.value)}
              value={props.data.password}
              error={props.errors.password}
            />
            <InputGroupAddon
              addonType='append'
              onClick={props.togglePasswordVisibility}
            >
              <Ico
                className={
                  props.passwordVisibility ? (
                    'input-icon full-width active'
                  ) : (
                    'input-icon full-width'
                  )
                }
                alt='visibility'
                name='passwordVisibility'
              />
            </InputGroupAddon>
          </DopeInputGroup>
          <small className={props.errors.password ? 'validation-message' : ''}>
            Must contain at least 8 characters, including uppercase, lowercase,
            and numbers.
          </small>
        </FormGroup>
        <FormGroup className="dope-form-group">
          <Label className='input-title' htmlFor='confirmPassword'>
            Confirm Password <sup>*</sup>
          </Label>
          <DopeInputGroup>
            <DopeInput
              id='confirmPassword'
              name='confirmPassword'
              type={props.confirmPasswordVisibility ? 'text' : 'password'}              
              onChange={e => props.onChange('confirmPassword', e.target.value)}
              value={props.data.confirmPassword}
              error={props.errors.confirmPassword}
            />
            <InputGroupAddon
              addonType='append'
              onClick={props.toggleConfirmPasswordVisibility}
            >
              <Ico
                className={
                  props.confirmPasswordVisibility ? (
                    'input-icon full-width active'
                  ) : (
                    'input-icon full-width'
                  )
                }
                alt='visibility'
              />
            </InputGroupAddon>
          </DopeInputGroup>
        </FormGroup>       
        <FormGroup className="dope-form-group">
          <DopeInput
            id='accountPhone'
            type='text'
            name='accountPhone'
            title='Account Phone Number'
            supText='*'
            error={props.errors.accountPhone}
            value={props.data.accountPhone}            
            onChange={e => props.onChange('accountPhone', e.target.value)}
            placeholder="+12029861805"
          />          
        </FormGroup>
        <div className='sign-up-form-bottom'>
          <div className='link-block'>
            <Link to={LOG_IN}>Back to Sign In</Link>
          </div>
          <DopeButton
            color='primary'
            disabled={props.loading}
            onClick={props.onSubmit}
          >
            Register
          </DopeButton>
        </div>
      </Form>
    </AuthContainer>
  );
}
