import React from 'react';
import {RadioGroup, Radio} from 'react-radio-group';
import './DopeRadioGroup.scss';

const DopeRadioGroup = ({data=[], vertical, disabled, ...rest}) => {       
  return (       
    <RadioGroup className={`DopeRadioGroup ${vertical?"vertical":"horizontal"}`} {...rest}>
      {
        data.map((item, i) => {          
          return(    
            <label className="radio" key={item.value}>                      
              <Radio value={item.value} disabled={item.disabled||disabled}/>
              <span className="custom-radio"></span>
              <span className="radio-label">{item.label}</span>              
            </label>
          );            
        })
      }
    </RadioGroup>      
  );
}

export default DopeRadioGroup;
