import React, {useState} from 'react';
import { useSelector } from 'react-redux';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import {DopeCheckbox} from 'components/DopeForms/DopeCheckbox/DopeCheckbox';
import SearchListInput from '../SearchListInput';
import {searchList} from 'utils/helper';
import { ReactComponent as CloseIcon } from 'assets/img/icons/close.svg';

import './CitiesFilter.scss';

export default function CitiesFilter(props) {  
  const [searchedList, setSearchedList] = useState('');
  
  const cities = useSelector(state => state.contacts.filtersData.cities)||[]; 
  const searchedCities = searchList(cities,searchedList); 

  const onChangeFilterItem = (city) => {     
    const filterCities = [...props.filterCities];
    const resultFilter = filterCities.indexOf(city) > -1 ? filterCities.filter(el => el !== city) : [ ...filterCities, city ];
    props.onChangeFilter(resultFilter)
  }

  const removeFilterItem = (city) => {     
    const filterCities = [...props.filterCities];
    const resultFilter = filterCities.indexOf(city) > -1 ? filterCities.filter(el => el !== city) : [ ...filterCities];
    props.onChangeFilter(resultFilter)
  }

  const selectAllFilterItem = () => {     
    const resultFilter = [...cities];    
    props.onChangeFilter(resultFilter)
  }

  const removeAllFilterItem = () => {
    props.onChangeFilter([])
  }  

  const Item = (city) => {
    return (
      <li className="fs-leftcol-item" key={city}>
        <DopeCheckbox
          label={city}
          checked={props.filterCities.indexOf(city) > -1}
          onChange={()=>{onChangeFilterItem(city)}}
        />
      </li>      
    )
  }

  const Badge = (city) => {
    return (
      <li className="fs-rightcol-item" key={city}>            
        {city}
        <CloseIcon className="close-icon" onClick={ ()=>{removeFilterItem(city)} }/>
      </li>
    )
  };
  
  return (  //Cities   city  
    <div className="CitiesFilter">
      <div className="filter-col">
        <div className="filter-col-header">
          <DopeButton 
            color='white'
            onClick={selectAllFilterItem}
            disabled = {cities.length === props.filterCities.length}
          >
            Select All Cities
          </DopeButton>        
        </div>
        <div className="search-container">
          <SearchListInput placeholder="Search cities" value={searchedList} onChange={setSearchedList}/>
        </div>
        <ul className="fs-leftcol-list">
          { searchedCities.length>0 ? (
            searchedCities.map((city)=>(                            
              Item(city)              
            ))
          ):(
            <span className="no-fs-leftcol-item">
              "No cities"
            </span>
          )
          }      
        </ul>
      </div>
      <div className="filter-col">
        <div className="filter-col-header">
          <span className="col-header-title">Selected</span>
          <DopeButton 
            color='white'
            onClick={removeAllFilterItem}
            disabled = {props.filterCities.length === 0}
          >
            Clear All
          </DopeButton>        
        </div>
        { props.filterCities.length>0 ? (
            <ul className="fs-rightcol-list">
              {
                props.filterCities.map((city)=>(                                          
                  Badge(city)
                ))
              }
            </ul> 
          ):(
            <ul className="fs-rightcol-list empty">
              <span className="no-fs-leftcol-item">
                "No cities selected"
              </span>
            </ul>
          )
        } 
      </div>
    </div>
  );
}
