import service from './service';
import { API_URL } from '../config/config';

export const getAdminHandwrittenListApi = (data) => {
  return service.get(
    `${API_URL}/handwritten-orders`, data.query
  )
};

export const updateAdminHandwrittenApi = (data) => {
  return service.patch(
    `${API_URL}/handwritten-orders/${data.handwrittenId}/status`, data.body,
  )
};

export const deleteAdminHandwrittenOrderApi = (handwrittenId) => {
  return service.delete(
    `${API_URL}/handwritten-orders/${handwrittenId}`
  )
};
