import React from 'react';
import { Card, CardBody } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import './ShippingAddress.scss';

export default function ShippingAddress(props) {
  const address = [
    {
      title: 'Address 1',
      prop: 'address1',
      required: true,      
    },
    {
      title: 'Address 2 - Optional',
      prop: 'address2',      
    },
    {
      title: 'City',
      prop: 'city',
      required: true,      
    },
    {
      title: 'State',
      prop: 'state',
      required: true,      
    },
    {
      title: 'Postal Code',
      prop: 'zip',
      required: true,
      type: 'number',      
      // mask: "99999",
    },
  ];

  return (
    <React.Fragment>
      <h4 className='wizard-block-header shipping-address-header'>
        Shipping Address - Where Do<br/>You Want Your Signs Sent?
      </h4>
      <Card className='shipping-address-container'>        
        <CardBody>
          {address.map((x, key) => (
            <div className='field' key={key}>
              <DopeInput
                type={x.type||'text'}               
                name={x.prop}
                id={x.prop}
                placeholder={x.title}
                value={props.data.shippingAddress[x.prop] || ''}
                error={props.errors[x.prop]}
                required={x.required}
                onChange={e => {
                  if(x.prop==='zip'){
                    let zip = e.target.value.slice(0, 5);
                    props.onChange(x.prop, zip);
                    props.addErrors(x.prop, '');
                  }else{
                    props.onChange(x.prop, e.target.value);
                    props.addErrors(x.prop, '');
                  }
                }}
              />
            </div>
          ))}
        </CardBody>
      </Card>
    </React.Fragment>
  );
}
