import React from 'react';
import { connect } from 'react-redux';
import { SketchPicker } from 'react-color';
import ReactCrop from 'react-image-crop';
import { pdfjs } from 'react-pdf';
import { Document, Page } from 'react-pdf';
import 'react-image-crop/dist/ReactCrop.css';
import SpectsModal from '../SpectsModal';
import { Label } from 'reactstrap';
import { toast } from 'react-toastify';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import { FadeLoader } from 'react-spinners';
import striptags from 'striptags';//be careful work only "striptags": "2.2.1",
import {parsePostcardSizeToPX} from 'utils/helper';
import FontSizeResizer from '../FontSizeResizer';
import Resizer from './Resizer';
import {replacePhoneNumber} from 'utils/helper';
import { ReactComponent as Reset } from 'assets/img/icons/reset.svg';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import { ReactComponent as FileIcon } from 'assets/img/icons/file.svg';
import { ReactComponent as LeftIcon } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as RightIcon } from 'assets/img/icons/chevron-right.svg';
import S3 from 'aws-sdk/clients/s3';
import { S3_BUCKET_PUBLIC_NAME,
  REACT_APP_S3_ACCESS_KEY_ID,
  REACT_APP_S3_SECRET_ACCESS_KEY,
} from 'config/config';
import './HtmlBuilder.scss';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css'; //v5.0.0

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const s3Config = {
  bucketName: S3_BUCKET_PUBLIC_NAME,
  accessKeyId: REACT_APP_S3_ACCESS_KEY_ID,
  secretAccessKey: REACT_APP_S3_SECRET_ACCESS_KEY,
  region: 'us-east-1'
};
const s3Client = new S3(s3Config);

class Builder extends React.Component {
  /*
    class "builder-template-container" is required
    data attrs tips:
    data-editable - content can be edited
    data-font-color-changeable - color can be changed
    data-font-size-changeable - font-size can be changed
    data-background-changeable - background color can be changed
    data-image-changeable - image can be changed
    data-border-color-changeable - border-color can be changed
    data-background-group - if you have 2+ items in 1 group, background color will be applied to all of them
    data-font-color-group - if you have 2+ items in 1 group, font color will be applied to all of them
    data-enabled - if you want to apply background finding functionality for a random element
    data-size-changeable - size can be changed
  */
  constructor(props) {
    super(props);
    this.state = {
      showFontColorPicker: false,
      showFontSizeResizer: false,
      showBackgroundColorPicker: false,
      showBorderColorPicker: false,
      showImageUploader: false,
      showResizerHeight: false,
      showResizerWidth: false,
      availableColors: [],
      selectedFontColor: '',
      selectedBorderColor: '',
      selectedBackgroundColor: '',
      selectedResize:{
        width: '',
        height: '',
      },
      spectsModalShow: false,
      pdfCanvasShow: false,
      pdfNumPages: null,
      pdfPageNumber: 1,
      pdfScale: 1,
      pdfSrc: null,
      // PDFLoading: false,
      PDFRendering: false, 
      editedPrevImage: null, 
      base64CroppedImage: null,     
      imageSrc: null,      
      crop: {
        unit: '%',
        width: 100,                      
        aspect: 1,    
      },      
      coverCrop: {
        imgContainerWidth: '2705px',
        imgContainerHeight: '1705px',
      }
    };    
    this.myRef = React.createRef();    
  }

  componentDidMount() {     
    const colorPalettes = this.props.configs.find(
      config => config.name === "colorPalette"
    );
    const postcardsPalette = this.props.configs.find(
      config => config.name === "postcardsPalette"
    );
    const selectedPalette =
      colorPalettes && postcardsPalette && Array.isArray(colorPalettes.value)
        ? colorPalettes.value.find(
          palette => palette.id === postcardsPalette.value
        )
        : null;
    this.setState({
      availableColors: selectedPalette
        ? selectedPalette.colors.map(color => color.value)
        : []
    });
  }

  componentDidUpdate(prevProps) {
    if (this.props.template!==prevProps.template) {

      // this.onReset();  
      this.setState({
        showFontColorPicker: false,
        showBackgroundColorPicker: false,
        showBorderColorPicker: false,      
        showFontSizeResizer: false, 
        showResizerHeight: false,
        showResizerWidth: false,
        showImageUploader: false,     
        imageSrc: null,
        croppedImageUrl: null,
        base64CroppedImage: null,
        croperType: ''
      });     
    }    
  }

  onReset = () => {
    if (this.props.changePostCardStatus) this.props.changePostCardStatus(true);  //if (this.props.changePostCardStatus) this.props.changePostCardStatus({[this.props.side]: false}) //don't used  //TODO
    const newTemplate = this.parsingTemplate(this.props.templateSample);
    this.myRef.current.innerHTML = `${newTemplate}${this.generateBackMarkPlace()}`;
    if(this.props.phoneNumberForReset) replacePhoneNumber(this.props.phoneNumberForReset)
    this.setState({
      showFontColorPicker: false,
      showBackgroundColorPicker: false,
      showBorderColorPicker: false,
      showFontSizeResizer: false,
      showResizerHeight: false,
      showResizerWidth: false,
      showImageUploader: false,      
      imageSrc: null,
      croppedImageUrl: null,
      base64CroppedImage: null,
      croperType: ''
    });    
  };

  currentTargets = [];
  prevTargets = [];

  onClick = event => {
    if(this.props.disabled) return
    
    this.prevTargets = [...this.currentTargets];
    this.currentTargets = [];
    if (this.canElementBeFocused(event.target)) {
      this.currentTargets = this.foundTargets(event.target);
    }
    this.prevTargets.forEach(target => {
      this.resetTargetOptions(target.el);
      if (target.type === "focused") {
        target.el.classList.remove("builder-template-focused-item");
      }
    });    
    this.currentTargets.forEach(target => {
      this.applyTargetOptions(target.el);
      if (target.type === "focused") {
        target.el.classList.add("builder-template-focused-item");
        target.el.focus();
      }
      if (target.type === "image") {
        this.setState(   
          {
            editedPrevImage: target.el.src,
          }
        );  
        if(target.el.dataset.imageContain !== undefined){
          this.setSizeImageContain(target.el); 
        }
        else if(target.el.dataset.imageCover !== undefined){
          this.setAspectRatioImageCover(target.el);
        } else{
          this.setAspectRatioImageCover(target.el);
        }            
      }      
    });
  };

  setSizeImageContain(el) {   
    // console.log("FUNCTION setSizeImageContain");     
    let imgContainerWidth = el.parentElement.clientWidth || 2775;
    let imgContainerHeight = el.parentElement.clientHeight || 1875;    
    this.setState(   
      {
        crop: {
          unit: "%",
          width: 100,
          height: 50          
        },
        croperType: "imageContain",
        coverCrop: {          
          imgContainerWidth,
          imgContainerHeight
        }
      }
    );    
  }

  setAspectRatioImageCover(el) {    
    // console.log("FUNCTION setAspectRatioImageCover");  
    let aspectRatio = el.parentElement.clientWidth / el.parentElement.clientHeight || 1;
    let imgContainerWidth = el.parentElement.clientWidth || 2775;
    let imgContainerHeight = el.parentElement.clientHeight || 1875;    
    this.setState(   
      {
        crop: {             
          aspect: aspectRatio,
          unit: "%",
          width: 100,          
        },
        croperType: "aspectRatioImageCover",
        coverCrop: {
          imgContainerWidth,
          imgContainerHeight
        }
      }
    );    
  }

  canElementBeFocused(el) {
    return Object.keys(el.dataset).length > 0;
  }

  foundTargets(el) {
    let targets = [];
    const fontColorElement = this.foundFontColorElement(el);
    const fontSizeElement = this.foundFontSizeElement(el);
    const backgroundElement = this.foundBackgroundElement(el);
    const borderColorElement = this.foundBorderColorElement(el);
    const resizeElement = this.foundResizeElement(el); 
    if (el.dataset.editable !== undefined) {
      targets = [
        {
          type: "focused",
          el: el
        }
      ];
    } else if (el.dataset.imageChangeable !== undefined) {
      targets = [
        {
          type: "focused",
          el: el
        },
        {
          type: "image",
          el: el
        }
      ];
    }
    if (fontColorElement) {
      this.setState( {"selectedFontColor": fontColorElement.style.color} );
      targets.push({ type: "text", el: fontColorElement });
      if (
        el.dataset.editable === undefined &&
        el.dataset.imageChangeable === undefined
      ) {
        targets.push({ type: "focused", el: fontColorElement });
      }
      if (fontColorElement.dataset.fontColorGroup) {
        document
          .querySelectorAll(
            `[data-font-color-group="${fontColorElement.dataset.fontColorGroup}"]`
          )
          .forEach(node => {
            targets.push({ type: "text", el: node });
          });
      }
    }
    if (fontSizeElement) {
      targets.push({ type: "font-size", el: fontSizeElement });
      if (
        el.dataset.editable === undefined &&
        el.dataset.imageChangeable === undefined
      ) {
        targets.push({ type: "focused", el: fontSizeElement });
      }
      if (fontSizeElement.dataset.fontSizeGroup) {
        document
          .querySelectorAll(
            `[data-font-size-group="${fontSizeElement.dataset.fontSizeGroup}"]`
          )
          .forEach(node => {
            targets.push({ type: "font-size", el: node });
          });
      }
    }
    if (backgroundElement) {      
      this.setState( {"selectedBackgroundColor": backgroundElement.style.backgroundColor} );
      targets.push({ type: "background", el: backgroundElement });
      if (
        el.dataset.editable === undefined &&
        el.dataset.imageChangeable === undefined
      ) {
        targets.push({ type: "focused", el: backgroundElement });
      }
      if (backgroundElement.dataset.backgroundGroup) {
        document
          .querySelectorAll(
            `[data-background-group="${backgroundElement.dataset.backgroundGroup}"]`
          )
          .forEach(node => {
            targets.push({ type: "background", el: node });
          });
      }
    }
    if (borderColorElement) {      
      this.setState( {"selectedBorderColor": borderColorElement.style.borderColor} );
      targets.push({ type: "border-color", el: borderColorElement });
      if (
        el.dataset.editable === undefined &&
        el.dataset.imageChangeable === undefined
      ) {
        targets.push({ type: "focused", el: borderColorElement });
      }
      if (borderColorElement.dataset.borderColorGroup) {
        document
          .querySelectorAll(
            `[data-border-color-group="${borderColorElement.dataset.borderColorGroup}"]`
          )
          .forEach(node => {
            targets.push({ type: "border-color", el: node });
          });
      }
    }
    if (resizeElement) {       
      this.setState({
        "selectedResize": {
          width: resizeElement.style.width,
          height: resizeElement.style.height,
        }
      });
      targets.push({ type: "resize", el: resizeElement });
      if (
        el.dataset.editable === undefined &&
        el.dataset.imageChangeable === undefined
      ) {
        targets.push({ type: "focused", el: resizeElement });
      }      
    } 
    return targets;
  }

  foundFontColorElement(el) {
    if (
      el.className.split(" ").includes("builder-template-container") ||
      el.tagName === "html"
    ) {
      return null;
    }
    if (el.dataset.fontColorChangeable !== undefined) {
      return el;
    }
    return this.foundFontColorElement(el.parentElement);
  }

  foundFontSizeElement(el) {
    if (
      el.className.split(" ").includes("builder-template-container") ||
      el.tagName === "html"
    ) {
      return null;
    }
    if (el.dataset.fontSizeChangeable !== undefined) {
      return el;
    }
    return this.foundFontSizeElement(el.parentElement);
  }

  foundBackgroundElement(el) {
    if (
      el.className.split(" ").includes("builder-template-container") ||
      el.tagName === "html"
    ) {
      return null;
    }
    if (el.dataset.backgroundChangeable !== undefined) {
      return el;
    }
    return this.foundBackgroundElement(el.parentElement);
  }

  foundBorderColorElement(el) {
    if (
      el.className.split(" ").includes("builder-template-container") ||
      el.tagName === "html"
    ) {
      return null;
    }
    if (el.dataset.borderColorChangeable !== undefined) {
      return el;
    }
    return this.foundBorderColorElement(el.parentElement);
  }

  foundResizeElement(el) {
    if (
      el.className.split(" ").includes("builder-template-container") ||
      el.tagName === "html"
    ) {
      return null;
    }
    if (el.dataset.resizeChangeable !== undefined) {
      return el;
    }
    return this.foundResizeElement(el.parentElement);
  }

  dataURLtoFile = (dataurl, filename) => {
    let arr = dataurl.split(','),
      mime = arr[0].match(/:(.*?);/)[1],      
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);      
    while(n--){
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], filename, {type:mime});
  };

  uploadImage = (Key, Body) => {
    const upload = s3Client.putObject({
      Bucket: `${s3Config.bucketName}/${this.props.awsPath}Images/${this.props.currentAccountId}`,
      Key,
      Body,
      ACL: "public-read"
    });
    return upload.promise();
  };

  saveImagesToAws = async () => {
    // save to aws image           
    // console.log("saveImagesToAws this.state.base64Crop before:", this.state.base64Crop);    
    const filename = `${this.props.awsPath}-image-${this.props.currentAccountId}-${new Date().getTime()}.${this.state.base64CroppedImage.split(',')[0].match(/:(.*?);/)[1].split('/')[1]}`;    
    await this.uploadImage(filename, this.dataURLtoFile(this.state.base64CroppedImage, filename));      
    let imageSrc = `https://${s3Config.bucketName}.s3.amazonaws.com/${this.props.awsPath}Images/${this.props.currentAccountId}/${filename}`;    
    return imageSrc;  
  }

  applyCroppedImage= e => { 
    // console.log("applyCroppedImage this.state.imageSrc:", this.state.imageSrc);
    this.saveImagesToAws().then((imageSrc) => {
      this.currentTargets.forEach(target => {
        if (target.type === "image") {
          target.el.src = imageSrc;          
        }
      });
      // this.props.setSelectedImages({...this.props.selectedImages, [this.props.side]: imageSrc})
      this.setState({   
        showImageUploader: false,   
        imageSrc: null,
        croppedImageUrl: null,
        base64CroppedImage: null,
        croperType: ''        
      });               
    });
  }

  cancelGetImage = e => {
    this.currentTargets.forEach(target => {
      if (target.type === "image") {
        target.el.src = this.state.editedPrevImage;
      }
    });
    this.setState({   
      showImageUploader: false,   
      imageSrc: null,
      croppedImageUrl: null,
      base64CroppedImage: null,
      croperType: ''        
    });
  }

  resetTargetOptions(el) {
    if (!(el instanceof HTMLElement)) {
      return;
    }
    if (el.dataset.editable !== undefined) {
      el.contentEditable = "false";
    }
    if (el.dataset.fontColorChangeable !== undefined) {
      this.setState({ showFontColorPicker: false });
    }
    if (el.dataset.fontSizeChangeable !== undefined) {
      this.setState({ showFontSizeResizer: false });
    }
    if (el.dataset.backgroundChangeable !== undefined) {
      this.setState({ showBackgroundColorPicker: false });
    }
    if (el.dataset.borderColorChangeable !== undefined) {
      this.setState({ showBorderColorPicker: false });
    }
    if (el.dataset.resizeChangeable !== undefined) {      
      this.setState( 
        {
          showResizerHeight: false,
          showResizerWidth: false,
        }
      );
    }
    if (el.dataset.imageChangeable !== undefined) {      
      this.setState({
        showImageUploader: false,
        imageSrc: null,
        croppedImageUrl: null,
        base64CroppedImage: null,  
      });
    }    
  }

  applyTargetOptions(el) {
    if (!(el instanceof HTMLElement)) {
      return;
    }
    if (el.dataset.editable !== undefined) {
      el.contentEditable = "true";
    }
    if (el.dataset.fontColorChangeable !== undefined) {
      this.setState({ showFontColorPicker: true });
    }
    if (el.dataset.fontSizeChangeable !== undefined) {
      this.setState({ showFontSizeResizer: true });
    }
    if (el.dataset.backgroundChangeable !== undefined) {
      this.setState({ showBackgroundColorPicker: true });
    }
    if (el.dataset.borderColorChangeable !== undefined) {
      this.setState({ showBorderColorPicker: true });
    }
    if (el.dataset.resizeChangeable !== undefined) {        
      this.setState( 
        {
          showResizerHeight: el.dataset.resizeHeight !== undefined,
          showResizerWidth: el.dataset.resizeWidth !== undefined,
        }
      );      
    }
    if (el.dataset.imageChangeable !== undefined) {
      this.setState({ showImageUploader: true });      
    }
  }

  onFontColorPick = color => {    
    if (this.props.changePostCardStatus) this.props.changePostCardStatus(true);
    this.currentTargets.forEach(target => {
      if (target.type === "text") {
        target.el.style.color = `rgba(${color.rgb.r},${color.rgb.g},${color.rgb.b},${color.rgb.a})`;
      }
    });
  };

  onBackgroundColorPick = color => {    
    if (this.props.changePostCardStatus) this.props.changePostCardStatus(true);
    this.currentTargets.forEach(target => {
      if (target.type === "background") {
        target.el.style.backgroundColor = `rgba(${color.rgb.r},${color.rgb.g},${color.rgb.b},${color.rgb.a})`;
      }
    });
  };

  onBorderColorPick = color => {    
    if (this.props.changePostCardStatus) this.props.changePostCardStatus(true);
    this.currentTargets.forEach(target => {
      if (target.type === "border-color") {
        target.el.style.borderColor = `rgba(${color.rgb.r},${color.rgb.g},${color.rgb.b},${color.rgb.a})`;
      }
    });
  };

  onFontSizePick = e => {
    if (this.props.changePostCardStatus) this.props.changePostCardStatus(true);
    this.currentTargets.forEach(target => {
      if (target.type === "font-size") { 
        let deltafontSize, newFontSize; 
        let fontSize = parseInt( target.el.style.fontSize.replace(/\D/g,'') ) || 50;   
        // if( fontSize > 100 ) {
          deltafontSize = Math.ceil( fontSize / 50 );
        // } else {
        //   deltafontSize = Math.ceil( fontSize / 100 );
        // }                 
        newFontSize = fontSize + deltafontSize * parseInt(e.target.value);        
        target.el.style.fontSize = `${newFontSize}px`;        
      }
    });
  };

  onResizePick = (e,h,w) => {
    if (this.props.changePostCardStatus) this.props.changePostCardStatus(true);
    this.currentTargets.forEach(target => {      
      if (target.type === "resize") { 
        let deltaSize=1, newWidth, newHeight; 
        let width = parseInt( target.el.style.width.replace(/\D/g,'') ) || '';
        let height = parseInt( target.el.style.height.replace(/\D/g,'') ) || '';   
        if(h){        
          newHeight = height + deltaSize * parseInt(e.target.value);
          if(newHeight<0) newHeight = 0;
          if(newHeight>100) newHeight = 100;         
          target.el.style.height = `${newHeight}%`;
        }
        if(w){
          newWidth = width + deltaSize * parseInt(e.target.value);
          if(newWidth<0) newWidth = 0;  
          if(newWidth>100) newWidth = 100;        
          target.el.style.width = `${newWidth}%`;
        }        
      }
    });
  };

  onImageDelete = e => {
    if (this.props.changePostCardStatus) this.props.changePostCardStatus(true);
    this.currentTargets.forEach(target => {
      if (target.type === "image") {
        target.el.src = "https://" + S3_BUCKET_PUBLIC_NAME + ".s3.amazonaws.com/static/postcard/img/empty.png";         
      }
    });
  };

  removeScripts = html => html && html.replace(/<script>.*<\/script>/g, "");
  
  parsingTemplate = template => {
    const phoneNumber = this.props.phoneNumber || "(000)000-0000";
    const phone3 = this.props.phoneNumber.slice(-4);
    const phone2 = this.props.phoneNumber.slice(-8, -5);
    const phone1 = this.props.phoneNumber.slice(-12, -9);
    let HTML = this.removeScripts(template);  
                        
    return HTML.replace(/<span data-phonenumber.*?<\/span>/g, "<span data-phonenumber data-editable style=\"display:inline-block;min-height:1em;min-width:250px;\">"+phoneNumber+"</span>")
      .replace(/<span data-phone1.*?<\/span>/g, "<span data-phone1 data-editable style=\"display:inline-block;min-height:1em;min-width:150px;\">"+phone1+"</span>")
      .replace(/<span data-phone2.*?<\/span>/g, "<span data-phone2 data-editable style=\"display:inline-block;min-height:1em;min-width:150px;\">"+phone2+"</span>")
      .replace(/<span data-phone3.*?<\/span>/g, "<span data-phone3 data-editable style=\"display:inline-block;min-height:1em;min-width:150px;\">"+phone3+"</span>")
      .replace(/\{S3_BUCKET_PUBLIC_NAME\}/g, S3_BUCKET_PUBLIC_NAME);
  };
  
  // TODO ?? WTF ???
  onPressKey = e => {    
    this.props.changePostCardStatus && this.props.changePostCardStatus(true);
    if (e.keyCode === 13) {
      e.preventDefault();
    }
  };

  insertAtCaret(txtblock,text) {		
		if (!txtblock) { return; }		
		let strPos = 0;
		let selection = document.getSelection();
    let range = new Range;
    range.setStart(txtblock, 0);
    range.setEnd(selection.anchorNode, selection.anchorOffset);
		strPos = range.toString().length;
		var front = (txtblock.textContent).substring(0, strPos);
		var back = (txtblock.textContent).substring(strPos, txtblock.textContent.length);
		txtblock.textContent = front + text + back;
		strPos = strPos + text.length;
    this.setCursorPosition(txtblock,strPos);
	}  
  setCursorPosition(parent, position) {    
    let child = parent.firstChild
    while(position > 0) {
      let length = child.textContent.length
      if(position > length) {
        position -= length
        child = child.nextSibling
      }
      else {
        if(child.nodeType == 3) return document.getSelection().collapse(child, position)
        child = child.firstChild
      }
    }
  }
  removeSelectedText (txtblock) {
    let startHighlitedPosition=0,finishHighlitedPosition=0;
    if (window.getSelection || document.getSelection) {
      let oSelection = (window.getSelection ? window : document).getSelection();
      startHighlitedPosition = Math.min(oSelection.anchorOffset,oSelection.focusOffset);
      finishHighlitedPosition = Math.max(oSelection.anchorOffset,oSelection.focusOffset);      
      txtblock.textContent = txtblock.textContent.substr(0, startHighlitedPosition) + txtblock.textContent.substr(finishHighlitedPosition, txtblock.textContent.length);
      this.setCursorPosition(txtblock,startHighlitedPosition);        
    } else {
      document.selection.clear();
      console.log("Browser?");      
    }
  }
  onPaste = e => {
    let editedElement = document.querySelector('.builder-template-focused-item[contenteditable="true"]');
    if(editedElement){
      let pasteText = (e.clipboardData || window.clipboardData).getData('text');
      e.preventDefault();
      let textNew = striptags(pasteText);
      this.removeSelectedText(editedElement);
      this.insertAtCaret(editedElement,textNew);      
    }
  }

  // Simple image uploading

  async onImageChange(evt) {
    const self = this;
    const img = new Image();
    let photo = evt.target.files[0];
    if(photo && photo.size && photo.size > 8388608) {
      evt.target.value = '';
      toast.warn('The image size should be less than 8 megabyte.')      
      return
    }    
    img.onload = function () {            
      URL.revokeObjectURL(this.src);
      var c = document.createElement("canvas"),
      ctx = c.getContext("2d");
      c.width = this.width;
      c.height = this.height;
      ctx.drawImage(this, 0, 0);  
      let jpeg = c.toDataURL("image/jpeg", 0.9);      
      self.currentTargets.forEach(target => {
        if (target.type === "image") {
          target.el.src = jpeg;
        }
      });
    };
    // TODO USE ONLY BLOB
    // self.currentTargets.forEach(target => {
    //   if (target.type === "image") {
    //     target.el.src = URL.createObjectURL(evt.target.files[0]);
    //   }
    // });
    img.src = URL.createObjectURL(evt.target.files[0]);    
    evt.target.value = '';    
  } 

  // react-image-crop

  onSelectFile = e => {
    let photo = e.target.files[0];
    if( photo.size && photo.size > 8388608 && photo.type!=='application/pdf' ) {
      e.target.value = '';
      toast.warn('The image size should be less than 8 megabyte.')      
      return
    }    
    if (e.target.files && e.target.files.length > 0) {
      const reader = new FileReader();      
      this.setState({
        imageType: e.target.files[0].type,
        pdfNumPages: null,
        pdfPageNumber: 1,
        pdfScale: 1,
        pdfSrc: null
      })
      switch (e.target.files[0].type) { 
        case 'application/pdf':
          reader.addEventListener('load', () =>{ 
            this.setState({
              pdfCanvasShow: true,
              pdfSrc: reader.result
            })            
          });
          reader.readAsDataURL(e.target.files[0]);
          this.setState({PDFLoading: true });
          break;        
        default:          
          reader.addEventListener('load', () =>{
            if (this.props.changePostCardStatus) this.props.changePostCardStatus(true);
            this.setState({
              imageSrc: reader.result
            })
          });                  
          reader.readAsDataURL(e.target.files[0]);
          break;
      }
      e.target.value = '';
    }
  };

  // If you setState the crop in here you should return false.
  onImageLoaded = image => {    
    this.imageRef = image;     
  }; 

  onCropComplete = crop => {    
    this.makeClientCrop(crop);    
  };

  onCropChange = (crop, percentCrop) => {
    // You could also use percentCrop:
    // this.setState({ crop: percentCrop });    
    // this.setState({ crop });
    this.setState({ crop: percentCrop });
  };

  async makeClientCrop(crop) {    
    if (this.imageRef && crop.width && crop.height) {      
      const croppedImageUrl = await this.getCroppedImg(
        this.imageRef,
        crop,
        'newFile.jpeg'
      );
      // TODO REMOVE DIMA LOGIC WITH IMAGES
      // Url convert to blob Object
      // let blob = await fetch(croppedImageUrl)
      //   .then(r => r.blob())
      //   .then(blobFile => blobFile);
      // let prevImg = null;      
      // this.currentTargets.forEach(target => {
      //   if (target.type === 'image' &&  target.el.dataset.name && this.props.setUserPostcardImages) {
      //     // Change image second time and add previous img for removing from array
      //     prevImg =  target.el.dataset.name;
      //     target.el.dataset.name =  `image-${Date.now()}`;
      //     this.props.setUserPostcardImages(blob, target.el.dataset.name, prevImg)
      //   } else if(target.type === 'image' && this.props.setUserPostcardImages) {
      //     // Change img first time
      //     target.el.dataset.name =  `image-${Date.now()}`;
      //     this.props.setUserPostcardImages(blob, target.el.dataset.name)
      //   } 
      // });
      this.setState({ croppedImageUrl });
    }
  }

  getCroppedImg(image, crop, fileName) {
    const base64 = image.src;
    const block = base64.split(";"); // Get the content type of the image
    const contentType = block[0].split(":")[1];
    const canvas = document.createElement('canvas');
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    let ownScaleX, ownScaleY;
    switch(this.state.croperType){
      case 'imageContain':
        let ownMaxScales=Math.max(this.state.coverCrop.imgContainerWidth / crop.width, this.state.coverCrop.imgContainerHeight / crop.height);
        ownScaleX = ownMaxScales;
        ownScaleY = ownMaxScales;
        // console.log(Math.max(this.state.coverCrop.imgContainerWidth / crop.width, this.state.coverCrop.imgContainerHeight / crop.height) );
        break;   
      case 'aspectRatioImageCover':
        ownScaleX = this.state.coverCrop.imgContainerWidth / crop.width;
        ownScaleY = this.state.coverCrop.imgContainerHeight / crop.height; 
        break;      
      default:
        break; 
    }         
    // console.log("croper type:  ", this.state.croperType);
    // console.log("croper ownScaleX:  ", ownScaleX);
    // console.log("croper ownScaleY:  ", ownScaleY);    
    canvas.width = crop.width * ownScaleX;
    canvas.height = crop.height * ownScaleY;
    const ctx = canvas.getContext('2d');
    // console.log("----------------------------------------");
    // console.log("DON'T TOUCH.....FOR FUTURE TESTING OTHER TYPES OF FUCKED CROPPER");
    // console.log("crop getCroppedImg :", crop);
    // console.log("image.naturalWidth :", image.naturalWidth);
    // console.log("image.naturalHeight :", image.naturalHeight);
    // console.log("image.width :", image.width);
    // console.log("image.height :", image.height);    
    // console.log("imgContainerWidth: ",this.state.coverCrop.imgContainerWidth);
    // console.log("imgContainerHeight: ",this.state.coverCrop.imgContainerHeight);
    // console.log("ownScaleX: ", ownScaleX);
    // console.log("ownScaleY: ", ownScaleY);
    // console.log("canvas.width: ", canvas.width);
    // console.log("canvas.height: ", canvas.height);
    // console.log(
    //   "\n","\n",
    //   "cropper:",
    //   "\n",
    //   "crop.width: ",crop.width,"\n",
    //   "crop.height: ",crop.height,"\n",
    //   "crop.x * scaleX: ",crop.x * scaleX,"\n",
    //   "crop.y * scaleY: ",crop.y * scaleY,"\n",
    //   "crop.width * scaleX: ",crop.width * scaleX,"\n",
    //   "crop.width * scaleY: ",crop.width * scaleY,"\n",
    // );
    // console.log("----------------------------------------");
    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width * ownScaleX,
      crop.height * ownScaleY
    );
    // this code for injecting image to template(ready for HTMLToImage library)
    var jpeg = canvas.toDataURL(contentType, 0.9);
    // var jpeg = canvas.toDataURL("image/png");
    this.currentTargets.forEach(target => {
      if (target.type === "image") {
        target.el.src = jpeg;
      }
      this.setState({
        base64CroppedImage: jpeg,
      });
    });  
    // As a blob  (not using croppedImageUrl now)
    return new Promise((resolve, reject) => {
      canvas.toBlob(blob => {
        if (!blob) {
          console.error('Canvas is empty');
          return;
        }
        blob.name = fileName;
        window.URL.revokeObjectURL(this.fileUrl);
        this.fileUrl = window.URL.createObjectURL(blob);
        resolve(this.fileUrl);
      }, contentType);
    });
  }  
  onPDFLoadSuccess = (document) => {
    const { numPages : pdfNumPages} = document;    
    this.setState({
      pdfNumPages,
      PDFLoading: false,
    });
  }
  onChangePdfPage = (step) => {
    this.setState((prevState) => ({
      pdfPageNumber: prevState.pdfPageNumber + step
    }));    
  }
  onChangePdfPrevPage = () => {
    this.onChangePdfPage(-1);
  }
  onChangePdfNextPage = () => {
    this.onChangePdfPage(1);
  }
  onGetImageFromPDF = () => { 
    let imageFromPDF = document
      .getElementsByClassName('dope-canvas-hided')[0]
      .getElementsByClassName('react-pdf__Page__canvas')[0]
      .toDataURL('image/jpg', 0.9);    
    if (this.props.changePostCardStatus) this.props.changePostCardStatus(true);
    this.setState({
      imageType: 'image/jpg',
      pdfCanvasShow: false,
      pdfNumPages: null,
      pdfPageNumber: 1,
      pdfScale: 1,
      pdfSrc: null,
      imageSrc: imageFromPDF
    })
  }
  onCancelImageFromPDF = () => {    
    this.setState({
      pdfCanvasShow: false,
      pdfNumPages: null,
      pdfPageNumber: 1,
      pdfScale: 1,
      pdfSrc: null
    });
  }
  toggleSpectsModal = () => {
    this.setState((prevState) => ({
      spectsModalShow: !prevState.spectsModalShow,
    }));     
  };
  generateBackMarkPlace = () => {     //TODO  
    if(this.props.backMarkPlace){
      return this.props.backMarkPlace      
    }
    else return '';     
  }
  render() {
    const { crop, imageSrc, pdfSrc, pdfCanvasShow, pdfPageNumber, pdfNumPages, pdfScale } = this.state;    
    
    return (
      <React.Fragment>
        <div className={`builder-container size-${this.props.classPref||'xs'}`}>
          <div
            className="template-container"  
            // EJECTED SIZE UNDER TEMPLATE
            // dangerouslySetInnerHTML={{
            //   __html: this.parsingTemplate(this.props.template)
            // }}          
            onClick={this.onClick}
            onKeyDown={this.onPressKey}
            onPaste={this.onPaste}
          >
            <div 
              id={`postcard_${this.props.side}`} 
              className="builder-template-container" 
              style={{
                "WebkitBoxSizing": "border-box", 
                "boxSizing": "border-box",
                "fontFamily": "none"
              }}
            >
              <div 
                className="Morpheus-card-container" 
                style={{
                  "MsFlexDirection": "column", 
                  "WebkitBoxDirection": "normal", 
                  "WebkitBoxOrient": "vertical", 
                  "WebkitBoxSizing": "inherit", 
                  "backgroundColor": "#ffffff", 
                  "boxSizing": "inherit", 
                  "display": "flex", 
                  "flexDirection": "column",
                  "overflow": "hidden", 
                  "pointerEvents": "auto", 
                  "position": "relative", 
                  ...parsePostcardSizeToPX(
                    this.props.postcardSize,
                    this.props.postcardDpi, //300
                    this.props.postcardBleed,  //0.25  LOB
                    ),                  
                }}
                ref={this.myRef}
                dangerouslySetInnerHTML={{
                  __html: `
                    ${this.parsingTemplate(this.props.template)}
                    ${this.generateBackMarkPlace()}
                  `
                }}
              />
            </div>
          </div>
          <div className="spects-container">
            <span className="spects-text">Look to the spects to upload a right postcard
              <span className="spects-linktext"
              onClick={this.toggleSpectsModal}  
              >
                Open&nbsp;spects
              </span>
            </span>
          </div>
          <div className="csi-editor-container">
            <div className="allResize-container">
              {this.state.showFontSizeResizer && (
                <div className="fontSize-container">
                  <FontSizeResizer
                    onFontSizePick={this.onFontSizePick}
                  />
                </div>
              )}
              { (this.state.showResizerHeight||this.state.showResizerWidth) && (
                <div className="resize-container">
                  <Resizer
                    onResizePick={this.onResizePick}
                    show={{h:this.state.showResizerHeight,w:this.state.showResizerWidth}}
                  />
                </div>
              )} 
            </div>
            {this.state.showImageUploader && (
              <div className="upload-img-container">

                <div className="image-action-container">
                  <h3 style={{"fontSize":"16px", "margin":"5px"}}>New Image</h3>
                  {/* { imageSrc ? (
                    <div className="image-action-box">
                      <button 
                        onClick={this.applyCroppedImage}
                        className="dope-button btn btn-primary"
                      >
                        <span>Apply Image</span>
                        <GetImageIcon className="btn-icon"/>                       
                      </button>
                      <button 
                        onClick={this.cancelGetImage}
                        className="dope-button btn btn-white"
                      >
                        <span>Cancel</span>    
                        <CancelIcon className="btn-icon"/>
                      </button>
                    </div>
                  ) : (                     */}
                    <div className="image-action-box">
                      <Label className="dope-button btn btn-primary">
                        Upload Image
                        <DopeInput
                          accept="image/jpeg,image/png,image/jpg,application/pdf"
                          type="file"
                          id="file"
                          onChange={this.onSelectFile}
                          //temporary disable cropper
                          // onChange={e => this.onImageChange(e)}                        
                        />
                        <FileIcon className="btn-icon"/>
                      </Label>
                      <button 
                        onClick={this.onImageDelete}
                        className="dope-button btn btn-white"
                      >
                        <span>Delete Image</span>
                        <DeleteIcon className="btn-icon"/>
                      </button>
                    </div> 
                  {/* )} */}
                </div>
                {imageSrc && (
                  <ReactCrop
                    src={imageSrc}
                    crop={crop}
                    ruleOfThirds
                    onImageLoaded={this.onImageLoaded}
                    onComplete={this.onCropComplete}
                    onChange={this.onCropChange}
                  />
                )}
              </div>             
            )}
            { this.state.showFontColorPicker || this.state.showBackgroundColorPicker || this.state.showBorderColorPicker ? (
              <div className="color-picker-block">            
                {this.state.showFontColorPicker && (
                  <div className="color-picker-item">
                    <h3 style={{"fontSize":"16px", "margin":"5px"}}>Font Color</h3>                   
                    <SketchPicker                    
                      presetColors={this.state.availableColors}
                      color={this.state.selectedFontColor}                       
                      disableAlpha = {false}               
                      onChange={this.onFontColorPick}
                    />
                  </div>
                )}
                {this.state.showBackgroundColorPicker && (
                  <div className="color-picker-item">
                    <h3 style={{"fontSize":"16px", "margin":"5px"}}>Background Color</h3>                 
                    <SketchPicker                  
                      presetColors={this.state.availableColors}
                      color={this.state.selectedBackgroundColor}
                      disableAlpha = {false}               
                      onChange={this.onBackgroundColorPick}
                    />                 
                  </div>
                )}
                {this.state.showBorderColorPicker && (
                  <div className="color-picker-item">
                    <h3 style={{"fontSize":"16px", "margin":"5px"}}>Border Color</h3>
                    <SketchPicker
                      presetColors={this.state.availableColors}
                      color={this.state.selectedBorderColor}
                      disableAlpha = {false}               
                      onChange={this.onBorderColorPick}
                    />                
                  </div>
                )}
              </div>
              ):null
            }
            { pdfCanvasShow ? 
              <React.Fragment>           
                <div className="Canvas-container"> 
                  <Document          
                    file = {pdfSrc}    
                    loading = "Please wait, PDF is loading…"        
                    onLoadSuccess={this.onPDFLoadSuccess}                            
                  >            
                    <Page    
                      className = "dope-canvas-show"  
                      pageNumber={pdfPageNumber}
                      renderAnnotationLayer = { false }
                      renderTextLayer = { false }  
                      scale = {pdfScale}                  
                      width = {320}                       
                      onLoadSuccess = { ()=> (this.setState({PDFRendering: true })) }    
                      onRenderSuccess = { ()=> {this.setState({PDFRendering: false })} }                      
                    /> 
                    {
                      this.state.PDFRendering && (
                        <div className='loader-main'>
                          <FadeLoader size={100} color={'#403d39'} />
                        </div>
                      )
                    }
                    <Page     
                      className = "dope-canvas-hided"                              
                      pageNumber={pdfPageNumber}
                      renderAnnotationLayer = { false }
                      renderTextLayer = { false }  
                      scale = {1}                    
                      width = {this.state.coverCrop.imgContainerWidth}  
                      onRenderSuccess = { ()=> { if(pdfNumPages===1){this.onGetImageFromPDF()} }}                       
                    />           
                  </Document>
                  <div className="pdf-action-container">
                    { pdfNumPages > 1 ? (
                      <React.Fragment>
                        <span className="pdf-num-text"> Page {pdfPageNumber} of {pdfNumPages}</span>
                        <div className="btn-container">
                          <DopeButton
                            color='primary'
                            disabled={pdfPageNumber <= 1}
                            onClick={this.onChangePdfPrevPage}
                          >
                            <LeftIcon className="btn-icon"/>
                          </DopeButton>
                          <DopeButton
                            color='primary'
                            disabled={pdfPageNumber >= pdfNumPages}
                            onClick={this.onChangePdfNextPage}
                          >
                            <RightIcon className="btn-icon"/>
                          </DopeButton>         
                        </div>                     
                        <div className="btn-container">              
                          <DopeButton
                            color='white'                
                            onClick={this.onCancelImageFromPDF}
                          >
                            Cancel
                          </DopeButton>
                          <DopeButton
                            color='primary'                
                            onClick={this.onGetImageFromPDF}
                            disabled={this.state.PDFLoading||this.state.PDFRendering}
                          >
                            Upload
                          </DopeButton>
                        </div>
                      </React.Fragment>
                    ) : null}   
                  </div>
                </div>                
              </React.Fragment>     
              : null
            }
            <div className="reset-btn">
              <button onClick={this.onReset} disabled={this.props.disabled}>
                <Reset />
                Reset
              </button>
            </div>
          </div>         
        </div>
        <SpectsModal          
          // toggle={this.onUploadToggle}
          open={this.state.spectsModalShow}
          toggle={this.toggleSpectsModal}
          // postcardSize={TODO}
          // awsPath={TODO}
        />
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => ({
  configs: state.dbConfigs.configs
});

export default connect(mapStateToProps)(Builder);
