import React, { useState } from 'react';
import LeafletMap from './LeafletMap';
import MapSetting from './MapSetting';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { FormGroup, Form } from 'reactstrap';
import {DopeTable} from "components/DopeTable";
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import ReactTooltip from 'react-tooltip';
import { ReactComponent as HelpIcon } from 'assets/img/icons/help.svg';
import { ReactComponent as SearchIco } from 'assets/img/icons/search.svg';
import { 
  setEddmroutemapErrors,
  getMapRoutesByZipRequest,
  setRouteView,
  setZipFind,  
  setSelectedRoutes,
  setNextStep,
  setTotalSelectedRouteAddr,
  resetSelectedRoutes,
  getRoutesStatByZipRequest,
  } from 'actions/eddm';
import { useSelector, useDispatch } from 'react-redux';
import {columnsFunc, columnsFuncOnlyRes, columnsFuncOnlyBusiness} from "./config";
import { getPriceByCost } from 'utils/helper';
import {useParams} from "react-router-dom";
import AddressTypePoPup from "./AddressTypePoPup/AddressTypePoPup";
import './EddmRouteMap.scss';
import IncomeSortTypePoPup from "./IncomeSortTypePoPup/IncomeSortTypePoPup";


export default function EddmRouteMap({totalRouteAddr, calcTotalSelectedRouteAddr, eddmPostcardPrice}) {

  const { typePreview } = useParams();
  const dispatch = useDispatch();
  const [googleMapType, setGoogleMapType] = useState('m');
  const [isAddressesOpenPopup, setIsAddressesOpenPopup] = useState(false);
  const [isIncomeOpenPopup, setIsIncomeOpenPopup] = useState(false);
  const [incomeSortType, setIncomeSortType] = useState('');

  // FIX SAFARI and Leaflet 1.7 issue with double click
  const [districtClicked, setDistrictClicked] = useState(null);
  // FIX SAFARI and Leaflet 1.7 issue with double click

  const selectedRoutes = useSelector(state => state.eddm.selectedRoutes);
  const zoom = useSelector(state => state.eddm.eddmRouteMap.zoom);
  const mapPosition = useSelector(state => state.eddm.eddmRouteMap.mapPosition);
  const poligonProperties = useSelector(state => state.eddm.eddmRouteMap.poligonProperties);  
  const errors = useSelector(state => state.eddm.eddmRouteMap.errors);
  const routeView = useSelector(state => state.eddm.eddmRouteMap.routeView);
  const featureCollection = useSelector(state => state.eddm.eddmRouteMap.featureCollection);  
  const zipFind = useSelector(state => state.eddm.eddmRouteMap.zipFind);
  const totalSelectedRouteAddr = useSelector(state => state.eddm.totalSelectedRouteAddr);
  const addressesType = useSelector(state => state.eddm.addressesType);
  const mapPositionIsLoading = useSelector(state => state.eddm.eddmRouteMap.mapPositionIsLoading);
  const shipingService = useSelector(state => state.eddm.eddmSummary.shipingService);  
  

  let featureCollectionData = featureCollection.features.slice();
  if(incomeSortType){
    featureCollectionData = incomeSortType === 'highest'
      ? featureCollectionData.sort((a, b) =>  b.attributes.MED_INCOME - a.attributes.MED_INCOME)
      : featureCollectionData.sort((a, b) =>  a.attributes.MED_INCOME - b.attributes.MED_INCOME)
  }

  const openAddressTypePopup = () => {
    setIsAddressesOpenPopup(!isAddressesOpenPopup)
  };

  const openIncomePopup = () => {
    setIsIncomeOpenPopup(!isIncomeOpenPopup)
  };

  let columns;
  if(addressesType === 'residential'){
    columns = columnsFuncOnlyRes(openAddressTypePopup, openIncomePopup);
  }else if(addressesType === 'business'){
    columns = columnsFuncOnlyBusiness(openAddressTypePopup, openIncomePopup)
  }else {
    columns = columnsFunc(openAddressTypePopup, openIncomePopup)
  }


  const getCoordinateByZip = (zip) => {
    dispatch(resetSelectedRoutes());
    //don't used/calculate in getMapRoutesByZipRequest saga(based on features multiPoligon geometry)
    // dispatch(getMapPositionByZipRequest(zip)); 
    dispatch(getMapRoutesByZipRequest(zip));
    // data from backend
    const data = {
      query: {
        zip: zip
      },
    };
    dispatch(getRoutesStatByZipRequest(data));
  }
  
  const handlePoligonClick = (e, route, tableClick) => {
    // FIX SAFARI and Leaflet 1.7 issue with double click    
    if (route === districtClicked) return;
    setDistrictClicked(route);
    setTimeout(() => {
      setDistrictClicked(null);
    }, 800);
    // FIX SAFARI and Leaflet 1.7 issue with double click

    if(typePreview==='view') return;
    if(tableClick){
      e.target.closest('.table-row').classList.toggle('active');
    }
    const isAlreadySelectedRoutes = [...selectedRoutes].includes(route);
    let newSelectedRoutes;
    if (isAlreadySelectedRoutes)
      newSelectedRoutes = [...selectedRoutes].filter(e=>(e!==route));
    else{
      newSelectedRoutes = [...selectedRoutes,route];
    }
    const totalSRAddr = calcTotalSelectedRouteAddr(newSelectedRoutes)    
    dispatch(setSelectedRoutes(newSelectedRoutes));
    dispatch(setTotalSelectedRouteAddr(totalSRAddr));
  }

  const selectAllRoutes = (e) => {    
    const allRoutes = [...featureCollection.features.map(item => item.attributes.ZIP_CRID)];
    dispatch(setSelectedRoutes(allRoutes));
    dispatch(setTotalSelectedRouteAddr(calcTotalSelectedRouteAddr(allRoutes)));
  };
  const unSelectAllRoutes = () => {
    dispatch(setSelectedRoutes([]));
    dispatch(setTotalSelectedRouteAddr(0));
  };

  const rowClasses = ({_id}, index) => {
    return `table-row ${selectedRoutes.includes(_id) ? 'active' : ''}`;
  };

  const eddm1NextStep = () => {    
    dispatch(setNextStep(2));
  }

  const withTooltip = () => {
    return (
      <>
        <span className="itemTitleTooltips" data-tip data-for={`tooltips-residential`}><HelpIcon/></span>
        <ReactTooltip
          className="res-bus-tooltip"
          html={true}
          id={`tooltips-residential`}
        >
          {`<span>Click here for Residential vs Business</span>`}
        </ReactTooltip>
      </>
    );
  };

  const calculatePrice = (qty, shipingService, eddmSize) => {
    if (!qty) return "0.00";
    let pricePerOneClasses = getPriceByCost(eddmPostcardPrice.filter(item => item.name === `${shipingService}`)[0].price[eddmSize], qty);
    // TODO IF first-class/price
    // if (postageType && postageType === 'usps_first_class')
    //   pricePerOne = pricePerOne['first-class-price'];
    // else pricePerOne = pricePerOne.price;    
    // let pricePerOne = pricePerOneClasses.price;
    let pricePerOne = pricePerOneClasses['first-class-price'];
    
    return (pricePerOne * qty / 100).toFixed(2);
  };

  const zipSearchChange = (e) => { 
    dispatch(setZipFind(e.target.value.slice(0,5)));
    dispatch(setEddmroutemapErrors({zip:''}));    
  }

  return (
    <div className="EddmRouteMap">
      <h1 className="main-title">Select Routes</h1>
      <p className="info-text">Select enough routes to meet the 200 piece minimum required by the USPS</p>
      <div className="mapCard">
        <div className="topMapCard">
          <div className="zipSearch">
            <Form 
              onSubmit={e=>{ e.preventDefault(); getCoordinateByZip(zipFind)}}
            >
              <FormGroup className='dope-form-group searchZip'>
                <SearchIco />
                <DopeInput
                  // type="number"
                  // step="1"
                  // max="99999"
                  type="text"
                  mask="99999"
                  maskChar={null}
                  error={errors.zip}                  
                  value={zipFind}
                  placeholder={'Search by ZIP Code'}
                  disabled={typePreview==='view'}
                  onChange={e=>zipSearchChange(e)}                    
                />              
              </FormGroup>
            </Form>
            <DopeButton
              disabled={(typePreview==='view')||mapPositionIsLoading}
              color="primary"
              onClick={() => getCoordinateByZip(zipFind)}>Search</DopeButton>
          </div>
          {featureCollection.features[0] && (
            <div className="cityResult">              
              {featureCollection.features[0].attributes.CITY_STATE||''}              
            </div>
          )}
          <div className="togglemapCardViewBox"> 
            <button 
              className={`togglemapCardViewBtn${routeView === 'map' ? ' active':''}`}
              onClick={e=>dispatch(setRouteView('map'))}
              disabled={mapPositionIsLoading}
            >
              Map View
            </button>    
            <button 
              className={`togglemapCardViewBtn${routeView === 'table' ? ' active':''}`}
              onClick={e=>dispatch(setRouteView('table'))}
              disabled={mapPositionIsLoading}
            >
              Table View
            </button>
          </div>  
        </div>         
        <div className="eddmRoute-container">
          {routeView === 'map' ?(             
            <div className={`eddmRouteMapContainer${mapPositionIsLoading?' eventNone':''}`}>
              <div className={` ${addressesType === 'both' ? 'EddmRouteStat large' : 'EddmRouteStat'}`}>
                <div className="eddmRouteStatItem">
                  <div className="statItemTitle-box"><span className="statItemTitle">Route</span></div>                  
                  <span className={`statItemVal ${poligonProperties.ZIP_CODE ? '' : 'empty'}`}>{poligonProperties.ZIP_CODE ? `${poligonProperties.ZIP_CODE}-${poligonProperties.CRID_ID}`:'\u2013\u2013\u2013'}</span>
                </div>
                {(addressesType === 'both' || addressesType === 'residential') && (
                  <div className="eddmRouteStatItem hasModal" onClick={() => setIsAddressesOpenPopup(!isAddressesOpenPopup)}>
                    <div className="statItemTitle-box">
                      <span className="statItemTitle">Residential</span>
                      {withTooltip()}
                    </div>                    
                    <span className={`statItemVal ${poligonProperties.RES_CNT ? '' : 'empty'}`}>{poligonProperties.RES_CNT ? poligonProperties.RES_CNT : '\u2013\u2013\u2013'}</span>
                  </div>
                )}
                {(addressesType === 'both' || addressesType === 'business') && (
                  <div className="eddmRouteStatItem hasModal" onClick={() => setIsAddressesOpenPopup(!isAddressesOpenPopup)}>
                    <div className="statItemTitle-box">
                      <span className="statItemTitle">Business</span>
                      {withTooltip()}
                    </div>                    
                    <span className={`statItemVal ${poligonProperties.BUS_CNT ? '' : 'empty'}`}>{poligonProperties.BUS_CNT ? poligonProperties.BUS_CNT : '\u2013\u2013\u2013'}</span>
                  </div>
                )}
                <div className="eddmRouteStatItem">
                  <div className="statItemTitle-box"><span className="statItemTitle">Total</span></div>                  
                  <span className={`statItemVal ${ totalRouteAddr(poligonProperties.ZIP_CRID)>0 ? '' : 'empty'}`}>{ totalRouteAddr(poligonProperties.ZIP_CRID) || '\u2013\u2013\u2013'}</span>
                </div>                
                <div className="eddmRouteStatItem">
                  <div className="statItemTitle-box"><span className="statItemTitle">Average income</span></div>
                  <span className={`statItemVal ${poligonProperties.MED_INCOME ? '' : 'empty'}`}>{poligonProperties.ZIP_CODE ? `$${poligonProperties.MED_INCOME/1000}k` :'\u2013\u2013\u2013'}</span>
                </div>                
              </div>       
              <LeafletMap
                center={[mapPosition.lat, mapPosition.lng]}
                zoom={zoom}
                googleMapType={googleMapType}                  
                handlePoligonClick={handlePoligonClick}                 
              />
              <MapSetting
                googleMapType={googleMapType}
                setGoogleMapType={setGoogleMapType}
              />
            </div>
          ):(
            <div className='eddmRouteTableContainer'>
              <div className="select-All-container">
                {featureCollection.features.length === 0 || selectedRoutes.length !== featureCollection.features.length
                ? <DopeButton
                    color="white"
                    onClick={selectAllRoutes}
                    disabled={selectedRoutes.length === featureCollection.features.length || typePreview==='view'}
                  >
                    Select all routes ({featureCollection.features.length})
                  </DopeButton>
                : <DopeButton
                    color="white"
                    onClick={unSelectAllRoutes}
                  >
                    Unselect all routes ({featureCollection.features.length})
                  </DopeButton>
                }
              </div>
              <DopeTable
                woPagination
                keyField="_id"
                columns={columns}
                data={featureCollectionData.map((item) => (                  
                  {
                    _id: item.attributes.ZIP_CRID,
                    route: `${item.attributes.ZIP_CODE}-${item.attributes.CRID_ID}`,                    
                    residential: item.attributes.RES_CNT,
                    business: item.attributes.BUS_CNT,
                    total: addressesType === 'residential'
                      ? item.attributes.RES_CNT
                      : addressesType === 'business'
                        ? item.attributes.BUS_CNT
                        : item.attributes.RES_CNT + item.attributes.BUS_CNT,
                    income: `$${item.attributes.MED_INCOME/1000}k`,
                    // cost: '???',
                  }
                ))}
                rowEvents={{
                  onClick: (e, row) => handlePoligonClick(e, row._id, true)
                }}
                rowClasses={rowClasses}
              />
            </div>
          )}
          <div className='eddmRouteSummary'>
            <div className="eddmRouteSummaryTitle">
              Order Summary
            </div>  
            <div className="eddmRouteSummaryItem">
              <span className="itemTitle">Individual Routes Selected</span>
              <span className={`itemValue${selectedRoutes.length===0?' empty':''}`}>{selectedRoutes.length}</span>
            </div>            
            <div className="eddmRouteSummaryItem">
              <span className="itemTitle">Total Delivery Addresses</span>
              <span className={`itemValue${selectedRoutes.length===0?' empty':''}`}>{totalSelectedRouteAddr}</span>
            </div>
            <div className="eddmRouteSummaryItem">
              <span className="itemTitle">
                Approximate Cost                
              </span>
              <div className="total-price-range">
                <span className={`itemValue${selectedRoutes.length===0?' empty':''}`}>
                  ${calculatePrice(
                    totalSelectedRouteAddr,
                    shipingService,
                    '6,5x9'
                  )}                   
                </span>
                <span className={`itemValue${selectedRoutes.length===0?' empty':''}`}>
                  &nbsp;&ndash;&nbsp;
                </span>                
                <span className={`itemValue${selectedRoutes.length===0?' empty':''}`}>
                  ${calculatePrice(
                    totalSelectedRouteAddr,
                    shipingService,
                    '8,5x11'
                  )}                  
                </span>
              </div>
            </div>
            <div className="btnContainer">
              <DopeButton
                color="primary"
                onClick={e=>eddm1NextStep(e)}
                disabled={(totalSelectedRouteAddr<200)||mapPositionIsLoading}
              >
                Continue
              </DopeButton>
            </div>
          </div>
        </div>
      </div>
      <AddressTypePoPup
        isOpen={isAddressesOpenPopup && typePreview!=='view'}
        toggle={() => setIsAddressesOpenPopup(!isAddressesOpenPopup)}
        addressesType={addressesType}
        calcTotalSelectedRouteAddr={calcTotalSelectedRouteAddr}
      />
      <IncomeSortTypePoPup
        isOpen={isIncomeOpenPopup && typePreview!=='view'}
        toggle={() => setIsIncomeOpenPopup(!isIncomeOpenPopup)}
        incomeSortType={incomeSortType}
        setIncomeSortType={setIncomeSortType}
      />
    </div>
  );
}
  