import React from 'react';
import {
  Card,
  Button,
} from 'reactstrap';
import moment from 'moment';
import 'moment-timezone';
import { ReactComponent as TimeIco } from 'assets/img/icons/time.svg';
import { ReactComponent as MarkerIco } from 'assets/img/icons/location.svg';
import { ReactComponent as PhoneIco } from 'assets/img/icons/telephone.svg';
import { ReactComponent as AddressIco } from 'assets/img/icons/building.svg';
import './MapInfo.scss'
import PlaceholderImg from 'assets/img/map-placeholder.png';

export default function MapInfo(props) {
  return (
    <Card className='map-info-block'>
      {props.openedInfo &&
        props.markers.map(
          (marker, i) =>
            marker._id === props.openedInfo &&
            (marker.icon === 'yard' ? (
              <div style={{ textAlign: 'center' }} key={marker._id}>
                <img src={props.img} alt='house' className='img' />
                <div style={{ fontSize: 20, fontWeight: 'bold' }}>
                  Placed at: {moment(marker.createdAt).format('MM/DD/YY')}
                </div>
                <Button onClick={() => props.onRemovePin(marker._id)}>
                  Remove pin
                </Button>
              </div>
            ) : (
              <div key={i}>
                <div className='map-img-container'>
                  <img src={props.img} alt='house' className='img' />
                </div>
                <div className='contact-info-container'>
                  <div className='contact-info-item'>
                    <p>
                      {marker.firstName} {marker.lastName}
                    </p>
                  </div>
                  <div className='contact-info-item'>
                    <TimeIco/>
                    <p>Call time: {marker.callTime}</p>
                  </div>
                  <div className='contact-info-item'>
                    <MarkerIco/>
                    <p>City: {marker.city}</p>
                  </div>
                  <div className='contact-info-item'>
                    <PhoneIco/>
                    <p>Address: {marker.address}</p>
                  </div>
                  <div className='contact-info-item'>
                    <AddressIco/>
                    <p>Phone number: {marker.phoneNumber}</p>
                  </div>
                </div>
              </div>
            )),
        )}
      {!props.openedInfo && (
        <div style={{ textAlign: 'center' }}>
          <img src={PlaceholderImg} alt='house' className='img' />
          <p> Please select an incoming call to see detailed information </p>
        </div>
      )}
    </Card>
  );
}
