import React from 'react';
import {
    Button,
    Modal,
    ModalHeader,
    Form,
    Input
} from 'reactstrap';
import { connect } from 'react-redux';
import { FadeLoader } from 'react-spinners';
import request from 'utils/request';
import config from 'config';
import { API_URL } from '../../../config/config';

const detailedHeader = [
    { title: 'ID', prop: 'shortID', sortable: true, filterable: true },
    { title: 'Date', prop: 'date' },
    { title: 'List Type', prop: 'listType' },
    { title: 'Target Location', prop: 'targetLocation' },
    { title: 'Description', prop: 'description' },
];

class View extends React.Component {
    state = {
        confirmModal: false,
    }

    onConfirmToggle() {
        this.setState({ confirmModal: !this.state.confirmModal });
    }

    async onConfirm() {
        let csv = this.props.viewModalData.csv;

        try {
            await request(`${API_URL}/contact/addlist/csv`, {
                method: 'post',
                body: csv,
                queries: {
                    account: this.props.currentAccount._id,
                    listRequest: this.props.viewModalData._id
                }
            });
            this.setState({ confirmModal: false });
        } catch (err) {
            console.log(err);
        }
    }

    loadCsv = async (evt) => {
        const reader = new FileReader();
        let photo = evt.target.files[0];
        reader.onloadend = async () => {
            const csv = reader.result;
            try {
                await request(`${API_URL}/list-request/upload-csv`, {
                    method: 'post',
                    body: { csv },
                    queries: { 
                        listRequest: this.props.viewModalData._id,
                        account: this.props.currentAccount._id,
                        addToCrmAutomatically: config.addToCrmAutomatically.toString()
                    }
                });
                this.props.toggle();
            } catch (err) {
                console.log(err);
            }
        };
        try {
            reader.readAsDataURL(photo);
        } catch (e) {
            //loading canceled
        }
    }

    render() {
        return (
            <div style={{ display: 'flex', justifyContent: 'center' }}>
                <Modal isOpen={this.props.open} toggle={this.props.toggle}>
                    <ModalHeader>Detailed Info</ModalHeader>
                    {this.props.loading ?
                        <div style={{ display: 'flex', justifyContent: 'center' }}>
                            <FadeLoader size={100} color={'#403d39'} />
                        </div> :
                        <>
                            <Form style={{ padding: 10 }}>
                                {detailedHeader.map(header => this.props.viewModalData[header.prop] &&
                                    <div style={{ margin: 10 }} key={header.title}>
                                        <i>{header.title}:</i> {this.props.viewModalData[header.prop]}
                                    </div>
                                )}
                                {this.props.isAdmin && this.props.viewModalData.requestStatus !== 'Delivered' &&
                                    <div style={{ margin: 10 }}>
                                        <i>Upload CSV:</i>
                                        <Input
                                            name='clientPrice'
                                            type='file'
                                            onChange={(e) => this.loadCsv(e)}
                                        />
                                    </div>
                                }
                            </Form>
                            {this.props.viewModalData.csv &&
                                <div style={{ marginLeft: 10, marginRight: 10 }}>
                                    {this.props.viewModalData.requestStatus === 'Delivered' &&
                                        <Button
                                            style={{ marginRight: 10 }}
                                            href={this.props.viewModalData.csv}
                                            download='contacts.csv'
                                        >
                                            Download
                                        </Button>
                                    }
                                    {!config.addToCrmAutomatically &&
                                        <Button onClick={() => this.onConfirmToggle()}>Add to CRM</Button>
                                    }
                                </div>
                            }
                        </>
                    }
                </Modal>
                <Modal isOpen={this.state.confirmModal} toggle={() => this.onConfirmToggle()}>
                    <ModalHeader>Are you sure you want to add the file to contacts?</ModalHeader>
                    <div style={{ marginLeft: 10, marginRight: 10 }}>
                        <Button style={{ marginRight: 10, backgroundColor: 'red' }} onClick={() => this.onConfirmToggle()}>No</Button>
                        <Button style={{ backgroundColor: 'green' }} onClick={() => this.onConfirm()}>Yes</Button>
                    </div>
                </Modal>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    currentAccount: state.account.currentAccount
});

export default connect(mapStateToProps)(View);