import {  
  // GET_ALL_MORPHEUS_TEMPLATES_REQUEST_SUCCESS,
  // GET_MY_MORPHEUS_POSTCARDS_REQUEST_SUCCESS, 
  GET_ALL_EDDM_TEMPLATES_SUCCESS,
  GET_ALL_NEIGHBORHOODBLITZ_TEMPLATES_SUCCESS,
  GET_ALL_AUTOMATION_TEMPLATES_SUCCESS,
  RESET_ALL_TEMPLATES,
  SET_LOADING_ALL_TEMPLATES,
} from '../actions/morpheusTemplates';

let initialState = {  
  loading: false,  
  templates: {   
    // total: 5,
    page: 0,
    limit: 10,
    data:[]
  },
};

export default function reducer(state = initialState, action) {
  switch (action.type) {    
    // case GET_ALL_MORPHEUS_TEMPLATES_REQUEST_SUCCESS:
    //   return {
    //     ...state,
    //     templates: action.data,
    //   }; 
    // case GET_MY_MORPHEUS_POSTCARDS_REQUEST_SUCCESS:
    //   return {
    //     ...state,
    //     templates: action.data,
    //   };
    case GET_ALL_EDDM_TEMPLATES_SUCCESS:
      return {
        ...state,
        templates: action.data,
      };  
    case GET_ALL_NEIGHBORHOODBLITZ_TEMPLATES_SUCCESS:
      return {
        ...state,
        templates: action.data,
      }; 
    case GET_ALL_AUTOMATION_TEMPLATES_SUCCESS:
      return {
        ...state,
        templates: action.data,
      }; 
    case SET_LOADING_ALL_TEMPLATES:
      return {
        ...state,
        loading: action.data,
      }; 
    case RESET_ALL_TEMPLATES:
      return {
        ...initialState,
      };
    default:
      return state;
  }
}
