import React from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';

export default function IncomeSortTypePoPup({isOpen, toggle, incomeSortType, setIncomeSortType}) {

  const handleClick = (data) => {
    setIncomeSortType(data);
    toggle();
  };

  return (
    <DopeModal
      isOpen={isOpen}
      centered
      className='addressType'
      toggle={toggle}
    >
      <DopeModalHeader className='dope-modal-header' toggle={toggle}>
        You can select <span>highest to lowest</span> or <span>lowest to highest</span> routes.
      </DopeModalHeader>
      <DopeModalFooter>
        <DopeButton
          color={`${incomeSortType === 'highest' ? 'primary' : 'white'}`}
          onClick={() => handleClick('highest')}
        >
          Highest to lowest
        </DopeButton>
        <DopeButton
          color={`${incomeSortType === 'lowest' ? 'primary' : 'white'}`}
          onClick={() => handleClick('lowest')}
        >
          Lowest to highest
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>
  );
}
