import React, {useState, useEffect} from 'react';
import {useParams, useHistory, Prompt} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import { toast } from 'react-toastify';
import {DopeButton, DopeInput} from 'components/DopeForms';
import { FormGroup } from 'reactstrap';
import {ReactComponent as LeftArrowIcon} from 'assets/img/icons/chevron-left.svg';
import WarningModal from './Modals/WarningModal';
import ConfirmModal from './Modals/ConfirmModal';
import {
  getAdminAccountRequest, 
  resetSelectedAccount, 
  changeAccountDataRequest,
} from 'actions/adminAccounts';

import './AccountGeneral.scss';

export default function AccountGeneral() {
  let history = useHistory();
  const {accountId} = useParams();
  const dispatch = useDispatch();

  const selectedAccount = useSelector(state => state.adminAccounts.selectedAccount);  
  const [confirmModal, setConfirmModal] = useState(false);
  const emailErrorText = "Error in email - email format validation failed:";
  const accountPhoneErrorText = "Error in accountPhone - accountPhone validation failed:";
  
  useEffect(() => {
    dispatch(getAdminAccountRequest(accountId));    
    return() => {
      dispatch(resetSelectedAccount());      
    };
  }, []);  
  const back = () => {
    history.push('/accounts');
  }
  const [accountData, setAccountData] = useState({
    email: selectedAccount.email,
    accountPhone: selectedAccount.accountPhone,
  });
  const [initialAccountData, setInitialAccountData] = useState({
    email: selectedAccount.email,
    accountPhone: selectedAccount.accountPhone,
  });

  const [accountErrors, setAccountErrors] = useState({
    email: '',
    accountPhone: '',
  });

  useEffect(() => {    
    setAccountData({
      email: selectedAccount.email,
      accountPhone: selectedAccount.accountPhone,
    });
    setInitialAccountData({
      email: selectedAccount.email,
      accountPhone: selectedAccount.accountPhone,
    });
  }, [selectedAccount]);

  const validation = () => {
    const { email, accountPhone } = accountData;
    let errors = {...accountErrors};
    const emailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
    const accountPhoneRegex = /^\+?[0-9]{9,15}$/;
    if( !emailRegex.test(email) || !accountPhoneRegex.test(accountPhone) ){
      if( !emailRegex.test(email) ){
        errors.email = `${emailErrorText} ${email}`;
      }else {
        errors.email = ''
      }
      if( !accountPhoneRegex.test(accountPhone) ){
        errors.accountPhone = `${accountPhoneErrorText} ${accountPhone}`;
      }else {
        errors.accountPhone = ''
      }
      setAccountErrors(errors);
      return false;
    }
    setAccountErrors({
      email: '',
      accountPhone: '',
    });
    return true;
  };


  const changeAccountData = ()=>{
    console.log("actual selectedAccount.email: ",selectedAccount.email);
    console.log("actual selectedAccount.accountPhone: ",selectedAccount.accountPhone);
    let data = {
      accountId: accountId,         
      body:{}
    }
    if(selectedAccount.email!==accountData.email.trim()){
      data.body.email=accountData.email.trim();
    }
    if(selectedAccount.accountPhone!==accountData.accountPhone.trim()){
      data.body.accountPhone=accountData.accountPhone.trim();
    }
    if(initialAccountData.email===accountData.email.trim()&&initialAccountData.accountPhone===accountData.accountPhone.trim()){
      toast.warn('Nothing has been changed.') 
      return;
    }
    dispatch(changeAccountDataRequest(data));     
  }
  // PROMPT
  const whenPrompt = ()=>{
    return (initialAccountData.email!==accountData.email||initialAccountData.accountPhone!==accountData.accountPhone)
  }
  const [modalVisible, setModalVisible] = useState(false);
  const [lastLocation, setLastLocation] = useState(null);
  const [confirmedNavigation, setConfirmedNavigation] = useState(false);
  const closeModal = () => {
    setModalVisible(false);
  };
  const handleBlockedNavigation = (nextLocation) => {
    if (!confirmedNavigation) {
      setModalVisible(true);
      setLastLocation(nextLocation);
      return false;
    }
    return true;
  };
  const handleConfirmNavigationClick = () => {       
    setConfirmedNavigation(true);
  }; 
  useEffect(() => {
    if (confirmedNavigation && lastLocation) {   
      history.push(lastLocation.pathname)
    }
  }, [confirmedNavigation, lastLocation]);  
  
  return (
    <div className='AccountGeneral'>

      <div className='back-btn-container'>
        <button className='back-btn'
          onClick={back}>
          <LeftArrowIcon className="back-icon"/>
          Back to accounts
        </button>
      </div>

      <div className='page-title-block'>
        <h2 className='page-title'>
          {
          selectedAccount.name
        }</h2>
      </div>

      <ul className="fictitious-tabs">
        <li className="fictitious-tabs-item">
          <button className="fictitious-tab" onClick={()=>(history.push(`/accounts/${accountId}/preferences`))}>
            Postcard Preferences
          </button>
        </li>
        <li className="fictitious-tabs-item">
          <button className="fictitious-tab active">
            General
          </button>
        </li>
      </ul>
      
      
      <div className='account-card'>
        <div className='page-title-container'>
          <h3 className='account-card-title'>Account general</h3>            
        </div>
        <form className="general-form" onSubmit={changeAccountData}>
          <FormGroup className="dope-form-group">            
            <DopeInput
              value={accountData.email || ''}
              title='account email'              
              name='email'
              error={accountErrors.email}
              onChange={e=>{
                setAccountErrors({...accountErrors.accountPhone, email: ''});
                setAccountData({...accountData, [e.target.name]: e.target.value})
              }}
            />            
          </FormGroup>
          <FormGroup className="dope-form-group">            
            <DopeInput
              value={accountData.accountPhone || ''}
              title='account phone'              
              name='accountPhone'
              error={accountErrors.accountPhone}
              onChange={e=> {
                setAccountErrors({...accountErrors.email, accountPhone: ''});
                setAccountData({...accountData, [e.target.name]: e.target.value})
              }}
            />            
          </FormGroup>
        </form>
      </div>

      <div className="btn-container">
        <DopeButton
          color='primary'
          onClick={() => {
            if(!validation()) return;
            setConfirmModal(true)
          }}
          disabled={!!accountData.email && !!accountData.accountPhone && initialAccountData.email===accountData.email.trim()&&initialAccountData.accountPhone===accountData.accountPhone.trim()}
        >
          Save
        </DopeButton>
      </div>  

      <Prompt
        when={whenPrompt()}
        message={handleBlockedNavigation}        
      /> 

      <WarningModal
        open={modalVisible}
        titleText='Close without saving?'        
        onCancel={closeModal}
        onConfirm={()=>handleConfirmNavigationClick()}
      />
      <ConfirmModal
        isOpen={confirmModal}
        titleText='Save changes?'
        toggle={() => setConfirmModal(!confirmModal)}
        onConfirm={changeAccountData}
        accountData={accountData}
        selectedAccount={selectedAccount}
      />

    </div>        
  );
}