import React, { Component } from 'react';
import AuthContainer from 'components/AuthContainer';
import { DopeInputGroup } from 'components/DopeForms/DopeInputGroup/DopeInputGroup';
import { InputGroupAddon } from 'reactstrap';
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';
import { connect } from 'react-redux';
import {
  createAccountRequest,  
} from 'actions/account';
import {   
  LOG_IN
} from 'utils/constants';
import history from 'history.js';
import { withRouter, Redirect } from 'react-router';
import './SignUpVerifyEmail.scss';
import CheckIco from 'assets/img/icons/check.png';

class SignUpVerifyEmail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      errors: {},
      loading: false,
      load: true,
      delayResend: false,
      email: '',
      accountPhone: '',
      status: '',
      checkCodeVisible: false,
    };    
  }

  componentDidMount() {    
    this.updateData();
  }  

  resendEmail = email => {          
    this.props.auth.sendVerificationEmailRequest(email);
    this.setDelayResend(true);
    setTimeout(() => this.setDelayResend(false), 60000);
  };

  setDelayResend = (val) => {
    this.setState({
      delayResend: val,
    })
  }

  updateData = () => {    
    let email = this.props.auth.getUserEmail()||'';
    let accountPhone = this.props.auth.getUserAccountPhone()||'';
    let status = this.props.auth.getUserStatus()||'';
    this.setState({ 
      email,
      status,
      accountPhone,
      load: false,
      code:'',
    });
  };

  onSignIn = () => {    
    history.replace(LOG_IN);
  };

  onChangeCode = (e) => {        
    this.setState({ 
      code: e.target.value,
    });
  };
  errorHandle = (error) => {    
    this.setState({
      errors: {
        ...this.state.errors,
        ...error
      }
    });
  }

  onSendSMS = async()=>{  
    this.setDelayResend(true);
    setTimeout(() => this.setDelayResend(false), 60000);  
    try {
      this.setState({
        loading: true,
        errors:{
          ...this.setState.errors,
          accountPhone:'',
        }
      })
      await this.props.auth.sendSMS(this.state.accountPhone, this.errorHandle); 
      this.setState({
        checkCodeVisible: true,
      })      
    } catch (err) {
      this.setState({loading: false})
      console.log(err);
    }
  }

  onSendCode = async()=>{
    let data={
      accountPhone: this.state.accountPhone,
      code: this.state.code,
    }    
    try {
      this.setState({loading: true})
      await this.props.auth.sendCode(data, this.errorHandle);        
      this.setState({loading: false})
    } catch (err) {
      this.setState({loading: false})
      console.log(err);
    }
  }

  render() {    
    if (!this.state.load){
      if (this.state.status === "active") return <Redirect to='/' />;
      return (
        <AuthContainer>
          <div className='verify-email'>
            <div className='auth-link-container'>
              <span className='auth-link' onClick={this.onSignIn}>
                Back to Sign In
              </span>
            </div>
            <div className='circle-container'>
              <div className='circle'>
                <img src={CheckIco} alt='Check ico' />
              </div>
            </div>
            <div className='auth-form-title'>              
              You haven't verified<br/> your email yet.
            </div>
            <div className='verify-email-text'>
              We have sent an email to <b>{this.state.email}</b> to confirm the validity of
              your email address. After receiving the email, follow the link
              provided to complete your registration.
            </div> 
            {this.state.accountPhone&&(
              <React.Fragment>
                <div className="phone-verify-container"> 
                  <p>
                    or you can activate your account
                    <button
                      className='link-item'
                      onClick={this.onSendSMS}
                      disabled={this.state.delayResend}                  
                    >
                      via SMS code to {this.state.accountPhone}
                    </button>                    
                  </p>
                  <span className="error-message">{this.state.errors.accountPhone}</span>                  
                </div>
                <div className={`check-code-form${this.state.checkCodeVisible?' active':''}`}>
                  <DopeInputGroup>
                    <DopeInput
                      id='code'
                      name='code'
                      // mask='999999'
                      // alwaysShowMask
                      type='text'
                      onChange={e=>this.onChangeCode(e)}
                      value={this.state.code}
                      error={this.state.errors.code}                      
                    />                
                    <InputGroupAddon
                      addonType='append'
                      onClick={this.onSendCode}
                    >
                      <span className="check-text">send</span>
                    </InputGroupAddon>
                  </DopeInputGroup>
                </div>
              </React.Fragment>
            )}
            
            {this.state.email&&(          
              <div className='verify-email-bottom'>
                <div className='verify-email-bottom-text'>
                  If you haven’t received any email,
                </div>
                <div className='link-block'>
                  <button
                    className='link-item'
                    onClick={() => this.resendEmail(this.state.email)}
                    disabled={this.state.delayResend}                  
                  >
                    Resend confirmation email
                  </button>
                </div>
              </div>
            )}

          </div>
        </AuthContainer>
      );
    }
    else return null;    
  }
}

const mapDispatchToProps = {
  createAccountRequest,  
};

export default connect(null, mapDispatchToProps)(withRouter(SignUpVerifyEmail));
