import React, {useEffect, useRef, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {useHistory, useParams} from "react-router-dom";
import {getHandwrittenOrderRequest, resetCreateHandwritten} from "actions/handwritten";
import {FormGroup} from "reactstrap";
import Select from "react-select";
import {createGetListRequest} from "actions/createPostCard";
import './HandwrittenCreate.scss';
import DopeRadio from "components/DopeForms/DopeRadio";
import {DopeTextarea} from "components/DopeForms";
import {DopeButton} from "components/DopeForms";
import HandwrittenBuyingModal from "./modals/HandwrittenBuyingModal";
import HandwrittenSavingModal from "./modals/HandwrittenSavingModal";

const HandwrittenCreate = ({savedAccountId}) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const {handwrittenId, typePreview} = useParams();
  const currentAccountId = useSelector(state => state.account.currentAccount._id);
  const contactsLists = useSelector(state => state.createPostCard.contactsLists);
  const handwrittenOrder = useSelector(state => state.handwritten.handwrittenOrder);
  const [isBuyingModalOpen, setIsBuyingModalOpen] = useState(false);
  const [isSavingModalOpen, setIsSavingModalOpen] = useState(false);
  const myRef = useRef(null);
  const fonts = useSelector(state => state.dbConfigs.configs).find(item => item.name === "handwrittenFonts").value;

  const [state, setState] = useState({
    size: '9,25x4,125',
    list: '',
    content: '',
    font: fonts[0]
  });

  const [selectedList, setSelectedList] = useState(null);

  const [errors, setErrors] = useState({
    listError: '',
    contentError: ''
  });

  useEffect(() => {
    if(handwrittenId !== 'create'){
      const data = {
        handwrittenId,
        query: {
          account: currentAccountId
        },
      };
      dispatch(getHandwrittenOrderRequest(data));
    }
    dispatch(createGetListRequest(currentAccountId));

    return () => {
      dispatch(resetCreateHandwritten());
    };
  },[]);

  useEffect(() => {
    if((savedAccountId !== currentAccountId) && (savedAccountId !== '')){
      history.push('/handwritten');
    }
  }, [currentAccountId]);

  useEffect(() => {
    if(handwrittenOrder){
      setState({
        size: handwrittenOrder.size,
        list: handwrittenOrder.list,
        content: handwrittenOrder.content,
        font: handwrittenOrder.font ? handwrittenOrder.font : fonts[0]
      })
    }
  },[handwrittenOrder]);

  useEffect(() => {
    if(handwrittenOrder && contactsLists) setSelectedList(contactsLists.find(item => item._id === handwrittenOrder.list));
  },[contactsLists]);

  const openModal = action => {

    if(!state.list){
      setErrors({...errors, listError: "Please select a list of contacts"});
      myRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
      return;
    }
    if(!state.content){
      setErrors({...errors, contentError: "Please type something"});
      return;
    }
    if(state.content.length > 450){
      setErrors({...errors, contentError: "450 characters max"});
      return;
    }

    if(action === 'save') setIsSavingModalOpen(true);
    if(action === 'buy') setIsBuyingModalOpen(true);
  };

  return (
    <div className='Handwritten'>
      <style>
        {`
        @font-face {
          font-family: ${state.font.name};
          src: url(${state.font.src});
        }
      `}
      </style>
      <h1 className="main-title">Build Your Handwritten</h1>

      <div className="HandwrittenCard">
        <div className="title-container">
          <div className="title">Envelope</div>
        </div>
        <div className="cardBody">
          <div className="radio-row">
            <DopeRadio
              name="list"
              checked={state.size === "9,25x4,125"}
              value="9,25x4,125"
              label="#10 (9,25x4,125)"
              disabled={typePreview === 'view'}
              onChange={(e) => setState({...state, envelope: e.target.value})}
            />
          </div>
          <div className="radio-row">
            <DopeRadio
              name="list"
              checked={state.size === "7,25x5,25"}
              value="7,25x5,25"
              label="A7 (7,25x5,25)"
              disabled={typePreview==='view'}
              onChange={(e) => setState({...state, size: e.target.value})}
            />
          </div>
        </div>
      </div>

      <div className="HandwrittenCard">
        <div className="title-container">
          <div className="title">List</div>
        </div>
        <div className="cardBody">
          <FormGroup>
            <Select
              className="dope-select"
              classNamePrefix="dope"
              options={contactsLists}
              getOptionLabel={option => option.name}
              getOptionValue={option => option}
              onChange={option => {
                setState({...state, list: option._id});
                setSelectedList(option);
                setErrors({...errors, listError: ''});
              }}
              isDisabled={typePreview === 'view'}
              value={selectedList}
              isOptionDisabled={(option) => !option.listProperties || ( option.listProperties && (!option.listProperties.verifiedContacts || option.listProperties.verifiedContacts < 100))}
              placeholder='Select ...'
            />
            <div ref={myRef} className='validation-message'>
              {errors.listError}
            </div>
            <p>
              *IF YOU HAVEN’T CREATED YOUR LISTS, CLICK “LISTS” IN THE
              MENU ON THE LEFT
            </p>
          </FormGroup>
        </div>
      </div>

      <div className="HandwrittenCard">
        <div className="title-container">
          <div className="title">Text</div>
        </div>
        <div className="cardBody cardBodyText">
          <FormGroup>
            <Select
              className="dope-select"
              classNamePrefix="dope"
              value={state.font}
              isDisabled={typePreview === 'view'}
              options={fonts}
              getOptionLabel={option => option.name}
              getOptionValue={option => option}
              onChange={option => setState({...state, font: option})}
            />
          </FormGroup>
          <FormGroup className="dope-form-group">
            <DopeTextarea
              style={{fontFamily: state.font.name}}
              disabled={typePreview === 'view'}
              value={state.content}
              name='text'
              error={errors.contentError}
              onChange={(e) => {
                setState({...state, content: e.target.value});
                if(e.target.value.length > 450) setErrors({...errors, contentError: "450 characters max"});
                else setErrors({...errors, contentError: ''});
              }}
            />
            <span
              className={`cardBodyText-indicator ${state.content.length > 450 ? 'error' : ''}`}
            >
              {state.content.length} / 450
            </span>
          </FormGroup>
        </div>
      </div>
      <div className="HandwrittenBtns">
        <DopeButton
          disabled={typePreview === 'view'}
          className='mr-3'
          color="white"
          onClick={() => openModal('save')}
        >
          {typePreview === 'edit' ? 'Edit' : 'Save'}
        </DopeButton>
        <DopeButton
          disabled={typePreview === 'view'}
          color="primary"
          onClick={() => openModal('buy')}
        >
          Buy
        </DopeButton>
      </div>

      {typePreview !== 'view' && (
        <>
          <HandwrittenBuyingModal
            isOpen={isBuyingModalOpen}
            toggle={() => setIsBuyingModalOpen(!isBuyingModalOpen)}
            handwrittenId={handwrittenId}
            count={selectedList ? selectedList.listProperties.verifiedContacts : 0}
            selectedList={selectedList}
            handwrittenOrder={handwrittenOrder}
            state={state}
            typePreview={typePreview}
          />
          <HandwrittenSavingModal
            isOpen={isSavingModalOpen}
            toggle={() => setIsSavingModalOpen(!isSavingModalOpen)}
            state={state}
            handwrittenId={handwrittenId}
            handwrittenOrder={handwrittenOrder}
            typePreview={typePreview}
          />
        </>
      )}
    </div>
  );
};

export default HandwrittenCreate;