import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import Pagination from 'rc-pagination';
import { FadeLoader } from 'react-spinners';
import { ReactComponent as PrevIco } from 'assets/img/icons/chevron-left.svg';
import { ReactComponent as NextIco } from 'assets/img/icons/chevron-right.svg';
import { ReactComponent as DeleteIcon } from 'assets/img/icons/delete.svg';
import DeleteYardSignModal from './DeleteYardSignModal';
import {
  deleteMyYardSignRequest,
  getMyYardSignTemplatesRequest,
} from 'actions/createYardSign';

export default function MyYardSignTemplates({currentAccountId,onSelectTemplate,selectedTemplate,size}) {    
  const dispatch = useDispatch();  
  useEffect(() => {    
    dispatch(getMyYardSignTemplatesRequest({accountId: currentAccountId, query:{ page:0, limit: 15, size: size, types: 'yardsign' }}));    
  },[size, currentAccountId]);

  const templates = useSelector(state => state.createYardSign.yardSignMyTemplates.data);  
  const loading = useSelector(state => state.createYardSign.yardSignMyTemplates.loading);
  const page = useSelector(state => state.createYardSign.yardSignMyTemplates.page);
  const total = useSelector(state => state.createYardSign.yardSignMyTemplates.totalSize);
  const limit = useSelector(state => state.createYardSign.yardSignMyTemplates.limit);

  const [deleteTemplateId,setDeleteTemplateId] = useState('');
  const [showDeleteTemplateModal,setShowDeleteTemplateModal] = useState(false);
  const deleteMyYardSign = (id)=>{    
    dispatch(deleteMyYardSignRequest({accountId: currentAccountId, templateId: id}, ()=>onPageChange(page + 1)));    
    setShowDeleteTemplateModal(false);
  }
  const onPageChange = page => {    
    dispatch(getMyYardSignTemplatesRequest({accountId: currentAccountId, query:{ page: page - 1, limit, size: size, types: 'yardsign'}}));
  };
  const rowClasses = ()=>{
    if(templates&&templates.length<=5){
      return 'oneRow';
    }
    if(templates&&templates.length<=10&&templates&&templates.length>5){
      return 'twoRow';
    }
    return '';    
  }
  return (
    <React.Fragment>
      <div className={`templates-preview-container ${rowClasses()}`}>
        {
          templates && templates.length ? (
            templates.map((template) => (             
              <div className='template-preview-item' key={template._id}>
                <div
                  className='template-item'
                  onClick={() => {                        
                    onSelectTemplate(template);                    
                  }}
                >
                  <div className={`template-preview-content${selectedTemplate === template._id ? ' checked':''}`}>
                    <img
                      className="template-preview-img"
                      src={template.front_thumbnail}
                      alt='yardSigns template'
                    />               
                  </div>
                  <div className='yardSigns-preview-name-block'>
                    <span className="name-text">{template.name}</span>                    
                  </div>
                  <div className='yardSigns-action-block'>                      
                    <DeleteIcon 
                      onClick={ (e)=>{
                        e.stopPropagation();
                        setDeleteTemplateId(template._id);
                        setShowDeleteTemplateModal(true);
                        // setImages(template.images);
                      }}
                    />
                  </div> 
                </div>
              </div>
            ))
          ) : (            
            <div className="no-templates-container">
              No Templates
            </div>            
          )          
        }
        {
          loading && (
            <div className='loader-main'>
              <FadeLoader size={100} color={'#403d39'} />
            </div>
          )
        }
      </div> 
      {templates && templates.length ? (
        <Pagination
          className='dope-pagination'
          current={page + 1}
          defaultPageSize={+limit}
          total={total}
          hideOnSinglePage={true}
          onChange={onPageChange}          
          prevIcon={<PrevIco />}
          nextIcon={<NextIco />}
          jumpPrevIcon={<span>...</span>}
          jumpNextIcon={<span>...</span>}
          showTitle={false}
        />
      ) : null}  
      <DeleteYardSignModal 
        open={showDeleteTemplateModal}
        toggle={()=>setShowDeleteTemplateModal(!showDeleteTemplateModal)}
        deleteMyYardSign={()=>deleteMyYardSign(deleteTemplateId)}        
        // images={images}
      />   
    </React.Fragment>
  );
}
