import React from 'react';
import { name } from 'utils/ajvProperties';
import { pathOr } from 'ramda';

export const CreateListSchema = {
  type: 'object',
  required: ['name', 'list'],
  properties: {
    ...name,
    list: {
      type: 'string',
      minLength: 1,
    },
  },
};

const customActions = (cell, row, rowIndex, formatExtraData) => {
  const checked = formatExtraData.selectedList.list === row._id;
  const verifiedContacts = pathOr(
    0.0,
    ['listProperties', 'verifiedContacts'],
    row,
  );

  return (
    <label className='dope-radio-container'>
      <input
        type='checkbox'
        onChange={() =>
          formatExtraData.selectHandler(row._id, verifiedContacts)}
        checked={checked}
      />
      <span className='checkmark' />
    </label>
  );
};

export const columnsFunc = (onSort, selectHandler, selectedList) => [
  {
    dataField: 'selectList',
    text: '',
    formatter: customActions,
    classes: 'selection-cell',
    formatExtraData: { selectHandler, selectedList },
  },
  {
    dataField: 'name',
    text: 'Name',
    sort: true,
    sortFunc: () => {
      return false;
    },
    onSort: onSort,
  },
  {
    dataField: 'contacts',
    text: 'Validated Contacts',
    sort: true,
    sortFunc: () => {
      return false;
    },
    onSort: onSort,
  },
  {
    dataField: 'createdAt',
    text: 'Date Created',
    sort: true,
    sortFunc: () => {
      return false;
    },
    onSort: onSort,
  },
  {
    dataField: 'listType',
    text: 'List Type',
    sort: true,
    sortFunc: () => {
      return false;
    },
    onSort: onSort,
  },
  {
    dataField: 'description',
    text: 'Description',
    sort: true,
    sortFunc: () => {
      return false;
    },
    onSort: onSort,
  },
];
