import React, {useEffect, useRef, useState} from 'react';
import { DopeModal } from 'components/DopeModal/DopeModal';
import { DopeModalHeader } from 'components/DopeModal/DopeModalHeader/DopeModalHeader';
import { DopeModalFooter } from 'components/DopeModal/DopeModalFooter/DopeModalFooter';
import { DopeButton } from 'components/DopeForms/DopeButton/DopeButton';
import {DopeModalBody} from "components/DopeModal";
import CreditCard from "components/CreditCard";
import {useDispatch, useSelector} from "react-redux";
import {addErrorsPostCard} from "actions/createPostCard";
import {buyHandwrittenRequest, updateHandwrittenRequest} from "actions/handwritten";
import {setDefaultSource} from "actions/account";
import {FormGroup} from "reactstrap";
import { DopeInput } from 'components/DopeForms/DopeInput/DopeInput';

export default function HandwrittenBuyingModal({isOpen, toggle, handwrittenId, count, selectedList, handwrittenOrder, state, typePreview}) {

  const dispatch = useDispatch();
  const currentAccount = useSelector(state => state.account.currentAccount);
  const data = useSelector(state => state.createPostCard.data);
  const prices = useSelector(state => state.dbConfigs.configs).find(item => item.name === "handwrittenOrderPrices").price;
  const [price, setPrice] = useState(0);
  const [handwrittenName, setHandwrittenName] = useState('');
  const [handwrittenNameError, setHandwrittenNameError] = useState('');
  const myRef = useRef(null);

  useEffect(() => {
    if(handwrittenOrder) setHandwrittenName(handwrittenOrder.name);
    return () => {
      setHandwrittenNameError('');
    };
  }, [isOpen]);

  useEffect(() => {
    if(selectedList) setPrice(prices[state.size].find(item => item.min <= count && count <= item.max).price);
  },[selectedList]);

  const buyData = () => {
    if(handwrittenName === ''){
      setHandwrittenNameError("Handwritten's name can't be empty");
      myRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
      return;
    }

    const data = {
      body: {
        ...state,
        name: handwrittenName,
        status: 'in-progress',
        cardId: currentAccount.default_source
      },
      accountId: currentAccount._id,
      handwrittenId
    };
    if(typePreview === 'edit') dispatch(updateHandwrittenRequest(data));
    else dispatch(buyHandwrittenRequest(data));
  };


  return (
    <DopeModal className='HandwrittenModal' isOpen={isOpen} toggle={toggle} centered>
      <DopeModalHeader className='dope-modal-header' toggle={toggle}>
        Order summary
      </DopeModalHeader>
      <DopeModalBody>
        <p>Contacts: {count}</p>
        <p ref={myRef}>Total: ${(count * price / 100).toFixed(2)}</p>
        <FormGroup className="dope-form-group">
          <DopeInput
            value={handwrittenName}
            title='Handwritten name'
            error={handwrittenNameError}
            onChange={e => {
              setHandwrittenNameError('');
              setHandwrittenName(e.target.value);
            }}
          />
        </FormGroup>
        <CreditCard
          currentAccount={currentAccount}
          selected={currentAccount.default_source}
          onChange={(card) => dispatch(setDefaultSource(card.id))}
          data={data}
          errors={{}}
          addErrors={() => dispatch(addErrorsPostCard())}
        >
          Payment Information
        </CreditCard>
      </DopeModalBody>
      <DopeModalFooter>
        <DopeButton color='primary' onClick={buyData} disabled={!currentAccount.default_source}>
          Buy
        </DopeButton>
      </DopeModalFooter>
    </DopeModal>    
  );
}
