import React, {useEffect, useState} from 'react';
import { Switch, Route, useRouteMatch } from 'react-router-dom';

import HandwrittenCreate from "./HandwrittenCreate/HandwrittenCreate";
import {useSelector} from "react-redux";
import HandwrittenList from "./HandwrittenList/HandwrittenList";

export default function Handwritten() {
  const { url } = useRouteMatch();
  const [savedAccountId, setSavedAccountId] = useState('');
  const currentAccountId = useSelector(state => state.account.currentAccount._id);

  useEffect(() => {
    setSavedAccountId(currentAccountId);
  }, [currentAccountId]);

  return (
    <Switch>
      <Route exact path={`${url}`}>
        <HandwrittenList/>
      </Route>
      <Route exact path={`${url}/:handwrittenId/:typePreview?`}>
        <HandwrittenCreate savedAccountId={savedAccountId}/>
      </Route>
    </Switch>
  );
}