import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { getThemeColor } from 'utils/helper';
import { setThemeColor} from 'actions/general';

export default function ThemeCSSVar(props){  
    const dispatch = useDispatch();       
    useEffect(() => { 
        let themeColor = getThemeColor(window.location.host);
        document.querySelector('html').dataset.theme=themeColor;
        dispatch(setThemeColor(themeColor))
    },[]);        
    return <React.Fragment>
        {props.children}
    </React.Fragment>
};
